import * as React from 'react';

import { IBlockConfiguration } from '../../models/block/configuration';
import BaseBlock from '../../components/page/block';
import { Container, Row, Col } from 'react-bootstrap';
import { LClear, LButton } from '../../components';
import { ELButtonType, OrderDataWrapper } from '../../models';
import { MainTexts } from '../../utils/messages';
import { CatKeys } from '../../utils/Defaults';
import { data } from 'jquery';


export enum GigacombiBookOptions {
    undefined = 1,
    internet_Phone_Doppel_Kombi = 2,
    internet_phone_tripple_kombi = 3,
    mobilfunk_doppel_kombi = 4,
    mobilfunk_tripple_kombi = 5,
    tv_doppel_kombi = 6,
    tv_tripple_kombi = 7
}


export interface IGiggacombiBannersProps {
    parent: any;
    advertisementBanner: boolean;
    bookOption: GigacombiBookOptions;
    onCategoryChanged?: any;
    lastSelectedKey? : CatKeys;
    data?: OrderDataWrapper;
};

export class GiggacombiBanners extends React.Component<IGiggacombiBannersProps, any> {
   
    constructor(props: any) {
        super(props);
    };

    public refresh() {
        this.setState({ changed: true });
    };

    public render() {

        let cfg: IBlockConfiguration = {
            title: undefined,
            transparent: false
        };

        let ctrl = (
            <>
                {
                    this.props.advertisementBanner ? this.gigacombi_advertisement(cfg) : this.renderAuto(cfg)
                }
            </>
        );

        return ctrl;
    };

    public gigacombi_advertisement(cfg: IBlockConfiguration) {
        cfg.title = undefined;
        let ctrl = (
            <>
                <BaseBlock config={cfg}>
                    <Container>
                        <Row>
                            <Col xs={2} md={2}>
                                <LButton
                                    icon="info" type={ELButtonType.linkWithIconLeft} onClick={() => { window.open('https://vf-sso.mlconsult.com'); }} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} >
                                <img style={{ width: '100%' }} src="/images/gigaPromo.png" />
                            </Col>
                        </Row>
                        <Row className="justify-content-md-center">
                            <Col xs={10} md={10} >
                                <div>Sichere Deinem Kunden noch mehr Vorteile durch GigaKombi</div>
                                <div >Ist Dein Kunde noch bei einem anderen Anbieter? Alle Infos zum </div>
                            </Col>
                        </Row>
                        <Row className="justify-content-md-center">
                            <Col xs={5} md={5} >
                                <a className="red" href='https://infoportal.vodafone.de/fachhandel/wp-content/uploads/2020/01/Wechselservice-Kommunikation.pdf' target="_blank">Restlaufzeitprogramm</a>
                            </Col>
                        </Row>
                    </Container>
                    <LClear />
                </BaseBlock>
            </>
        );

        return ctrl;
    }

    private renderAuto(cfg: IBlockConfiguration) {
        cfg.title = MainTexts.gigacombiTitle;

        let hasTv = this.props.data!.hasCategory(CatKeys.tv);
        let hasFn = this.props.data!.hasCategory(CatKeys.fn);
        let hasVf = this.props.data!.hasCategory(CatKeys.vodafone);
        let hasOtelo = this.props.data!.hasOtelo();
        let inner = <></>

        if(hasOtelo || (hasTv && hasFn && hasVf)) {
            return <></>
        };

        if(hasTv && !hasFn && !hasVf) {
            inner = <Row>
                <Col xs={6} md={6}>{this.renderButton([CatKeys.tv, CatKeys.fn], CatKeys.fn)}</Col>
                <Col xs={6} md={6}>{this.renderButton([CatKeys.tv, CatKeys.vodafone], CatKeys.vodafone)}</Col>
            </Row>
        } else if (hasFn && !hasTv && !hasVf) {
            inner = <Row>
                    <Col xs={6} md={6}>{this.renderButton([CatKeys.fn, CatKeys.tv], CatKeys.tv)}</Col>
                    <Col xs={6} md={6}>{this.renderButton([CatKeys.fn, CatKeys.vodafone], CatKeys.vodafone)}</Col>
                </Row>
        } else if (hasVf && !hasTv && !hasFn) {
            inner = <Row>
                    <Col xs={6} md={6}>{this.renderButton([CatKeys.vodafone, CatKeys.tv], CatKeys.tv)}</Col>
                    <Col xs={6} md={6}>{this.renderButton([CatKeys.vodafone, CatKeys.fn], CatKeys.fn)}</Col>
                </Row>
        } else if(hasVf && hasTv && !hasFn) { 
            inner = <Row><Col xs={12} md={12}>{this.renderButton([CatKeys.vodafone, CatKeys.tv, CatKeys.fn], CatKeys.fn)}</Col></Row>
        } else if(hasVf && hasFn && !hasTv) { 
            inner = <Row><Col xs={12} md={12}>{this.renderButton([CatKeys.vodafone, CatKeys.tv, CatKeys.fn], CatKeys.tv)}</Col></Row>
        } else if(hasTv && hasVf && !hasFn) { 
            inner = <Row><Col xs={12} md={12}>{this.renderButton([CatKeys.tv, CatKeys.fn, CatKeys.vodafone], CatKeys.fn)}</Col></Row>
        } else if(hasTv && hasFn && !hasVf) { 
            inner = <Row><Col xs={12} md={12}>{this.renderButton([CatKeys.tv, CatKeys.fn, CatKeys.vodafone], CatKeys.vodafone)}</Col></Row>
        } else if(hasFn && hasTv && !hasVf) { 
            inner = <Row><Col xs={12} md={12}>{this.renderButton([CatKeys.fn, CatKeys.tv, CatKeys.vodafone], CatKeys.vodafone)}</Col></Row>
        } else if(hasFn && hasVf && !hasTv) { 
            inner = <Row><Col xs={12} md={12}>{this.renderButton([CatKeys.fn, CatKeys.tv, CatKeys.vodafone], CatKeys.tv)}</Col></Row>
        }

        let ctrl = <BaseBlock config={cfg}>
            <Container className="np">                     
                {inner}
            </Container>
        </BaseBlock>

        return ctrl;
    };

    private renderButton(cats: string[], nextCat: string) {
        let ctrl = <Container className="np">
            <Row>
                <Col className="tac">
                    <img src={`/images/gigacombiCases/${cats.join('_')}.png`} />
                </Col>
            </Row>
            <Row>
                <Col className="tac">
                    <LButton type={ELButtonType.buttonWithIconRight}
                        css="a1"
                        id=""
                        text={MainTexts.gigaShow + ' ' + nextCat}
                        onClick={() => {
                            this.props.onCategoryChanged(nextCat)
                        }} />
                </Col>
            </Row>            
        </Container>

        return ctrl;
    };
};

export default GiggacombiBanners;