import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { LControl, Popup, PopupBody } from '../..';
import { EApiResponseStatus, IApiResponse } from '../../../interfaces';
import { EControlType, ELButtonType, ERequestType, IMnp } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { ICustomer } from '../../../models/ordering/customer';
import { ESalutation } from '../../../models/ordering/order';
import { CustomerScreen } from '../../../screens/insex';
import '../../../screens/test/progressBar.scss';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { ActivationMessages, MainTexts, OrderingTexts } from '../../../utils/messages';
import { EPopupType } from '../../dialogs/popup';
import { CustomerMnp } from '../../ordering/customer';
import BaseBlock from '../../page/block';
import * as page from '../../page/index';

export enum EPortingTime {
    Now = 1,
    AfterContractEnd = 2
}

export interface BelatedPortingModel {
    salutationId: number;
    firstname: string;
    surname: string;
    birthday: Date;
    customerNumber: string;
    toBePorted: string;
    customer: ICustomer;
    consent: boolean;
    portingTime: number;
    mnp: IMnp;
}

export interface IBelatedPortingProps {
    onChange: any;
    parent: CustomerScreen;
}

export interface IBelatedPortingState {
    model: BelatedPortingModel;
    popupTitle: string;
    processing: boolean;
    closablePopup: boolean;
    apiMessage: string;
}

export class BelatedPorting extends React.Component<IBelatedPortingProps, IBelatedPortingState> {
    public static containerId: string = 'belatedPortingScreen';
    private processPopupState: EPopupType = EPopupType.pleaseWait;

    readonly cfg2: IBlockConfiguration = {
        title: OrderingTexts.dataOfLastContract,
        transparent: false,
        styleContainer: '',
        collapsible: false,
        collapsed: false
    };

    constructor(props: any) {
        super(props);

        let data = this.props.parent.data;
        let customer = data.customer;
        const mnp = customer.mnp || { provider: '', network: '', differentCustomer: false };

        this.state = {
            processing: false, closablePopup: false, apiMessage: '', popupTitle: '',
            model: {
                mnp: { network: mnp.network, provider: mnp.provider, differentCustomer: false },
                salutationId: customer.salutation ? (customer.salutation as number) : (ESalutation.mr as number),
                firstname: customer.firstname || '',
                surname: customer.surname || '',
                birthday: customer.birthday || new Date(),
                customerNumber: customer.telMobile ? customer.telMobile.number : '',
                toBePorted: customer.contracts ? customer.contracts[0].msisdn || '' : '',
                portingTime: EPortingTime.Now,
                customer: customer,
                consent: false
            }
        };

        this.onChange = this.onChange.bind(this);
        this._onDismissProcessPopup = this._onDismissProcessPopup.bind(this);
    }

    render() {
        let a: JSX.Element = <page.LButton type={ELButtonType.linkSimple}
            text={MainTexts.backToCustomerDataSheet}
            css="action3 fr"
            onClick={() => {
                this.props.parent.onBackToCustomer(false);
            }} />;

        const cfg: IBlockConfiguration = {
            title: OrderingTexts.activationBNTCustomer,
            transparent: false,
            styleContainer: '',
            collapsible: false,
            collapsed: false,
            action: a
        };

        if (Global.easyApp.head) {
            Global.easyApp.head.setTitle(MainTexts.belatedPortingTitle);
        }

        const { model } = this.state;

        return <div id={BelatedPorting.containerId}>
            <br /><br /><br />
            <Popup
                type={this.processPopupState}
                visible={this.state.processing || this.state.closablePopup}
                closable={this.state.closablePopup}
                id="wait"
                title={this.state.popupTitle}
                height={200}
                onDismiss={this._onDismissProcessPopup}>
                <PopupBody>
                    <div>{this.state.apiMessage}</div>
                    <></>
                </PopupBody>
                <></>
            </Popup>
            <BaseBlock config={cfg}>
                {
                    <Container >
                        <Row>
                            <Col md={2}>
                                <page.LControl type={EControlType.radio}
                                    checked={model.salutationId === (ESalutation.mr as number)}
                                    onChange={this.onChange}
                                    name="salutationId"
                                    id={"salutationId_" + (ESalutation.mr as number).toString()}
                                    label={MainTexts.mr} title={MainTexts.salutation} />
                            </Col>
                            <Col md={2}>
                                <page.LControl type={EControlType.radio}
                                    checked={model.salutationId === (ESalutation.mrs as number)}
                                    onChange={this.onChange}
                                    name="salutationId"
                                    id={"salutationId_" + (ESalutation.mrs as number).toString()}
                                    label={MainTexts.mrs} title={MainTexts.salutation} />
                            </Col>
                        </Row>
                        <Row>
                            <Col md={4}>
                                <LControl type={EControlType.text} style="merged"
                                    name="firstname"
                                    value={model.firstname}
                                    onChange={this.onChange}
                                    label={MainTexts.firstname} />
                            </Col>
                            <Col md={4}>
                                <LControl type={EControlType.text} style="merged"
                                    name="surname"
                                    value={model.surname}
                                    onChange={this.onChange}
                                    label={MainTexts.surname} />
                            </Col>
                        </Row>
                        <Row>
                            <Col md={4}>
                                <page.LDate
                                    value={model.birthday}
                                    datePicker={false}
                                    onChange={this.onChange}
                                    name="birthday"
                                    id="birthday" 
                                    title={MainTexts.dateOfBirth} />
                            </Col>
                            <Col md={4}>
                                <LControl type={EControlType.text} style="merged"
                                    value={model.customerNumber}
                                    onChange={this.onChange}
                                    name="customerNumber"
                                    label={MainTexts.customerNumber} />
                            </Col>
                        </Row>
                        <Row>
                            <Col md={4}>
                                <LControl type={EControlType.text} style="merged"
                                    value={model.toBePorted}
                                    onChange={this.onChange}
                                    name="toBePorted"
                                    label={MainTexts.numberToBePorted} />
                            </Col>
                            <Col md={2}>
                                <page.LControl type={EControlType.radio}
                                    onChange={this.onChange}
                                    name="portingTime"
                                    id={"portingTime_" + (EPortingTime.Now as number).toString()}
                                    checked={model.portingTime === EPortingTime.Now}
                                    label={MainTexts.immediately}
                                    title={MainTexts.portingTime} />
                            </Col>
                            <Col md={2}>
                                <page.LControl type={EControlType.radio}
                                    onChange={this.onChange}
                                    name="portingTime"
                                    id={"portingTime_" + (EPortingTime.AfterContractEnd as number).toString()}
                                    checked={model.portingTime === EPortingTime.AfterContractEnd}
                                    label={MainTexts.afterContractEnds} />
                            </Col>
                        </Row>
                    </Container>
                }
            </BaseBlock>

            <div className="spacer"></div>

            <BaseBlock config={this.cfg2}>
                <Container>
                    <CustomerMnp parent={this} customer={this.state.model.customer} onChange={this.onChange} preview={false} />
                    <Row>
                        <Col md={1}>
                            <page.LControl type={EControlType.checkbox}
                                onChange={this.onChange}
                                name="consent"
                                id="consent"
                                value={true}
                                checked={model.consent} />
                        </Col>
                        <Col md={10}>
                            <page.LControl type={EControlType.textArea}
                                name=""
                                id=""
                                style={"black"}
                            /> {OrderingTexts.dataLastContract}
                        </Col>
                    </Row>

                    {
                        this.state.model.consent ? 
                        <Row>
                            <Col xs={12} md={12} >
                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                    css="a1 fr"
                                    id="save"
                                    text={MainTexts.save}
                                    onClick={this._onSave} />
                            </Col>
                        </Row> : null
                    }
                </Container>
            </BaseBlock>
        </div >;
    }

    onChange(target: any) {
        let model = this.state.model;
  
        Global.onInput(model, target, () => {
            this.setState({ model: model });
        });
    }

    socketCallback(res: IApiResponse) {
       
        const data = res.data;
        if (data.status != EApiResponseStatus.ok) {
            //res.error = data.data;
            this.processPopupState = EPopupType.custom;

            //let sum: IApiSummary = Api.parseSummary(res, undefined);

            this.setState({ apiMessage: data.data.Text, closablePopup: true, processing: false });
        } else {
            this.processPopupState = EPopupType.custom;
            this.setState({ apiMessage: ActivationMessages.execSuccess, closablePopup: true, processing: false });
            /*
            if (data.status === 2) {
                this.setState({ apiMessage: 'Error', closablePopup: true, processing: false });
            } else if (data.status === 1) {
                this.setState({ apiMessage: 'Ok', closablePopup: true, processing: false });
            }
            */
        }
    }

    _onSave = () => {
        this.setState({ processing: true });

        const { model } = this.state;

        const mnp = model.mnp;

        const portingModel = {
            id: this.props.parent.data.id,
            salutationId: model.salutationId,
            firstname: model.firstname,
            surname: model.surname,
            birthday: model.birthday,

            customerNumber: model.customerNumber,
            msisdn: model.toBePorted,

            brandKey: BrandKeys.otelo,
            toBePorted: model.toBePorted,
            portingTime: model.portingTime,

            mnp: {
                networkKey: mnp ? mnp.network : '',
                providerKey: mnp ? mnp.provider : '',
            },

            consent: model.consent
        };

        Global.hub.send(JSON.stringify(portingModel), ERequestType.belatedPortation, () => { });
    }

    _onDismissProcessPopup = () => {
        this.processPopupState = EPopupType.pleaseWait;
        this.setState({ closablePopup: false, processing: false, apiMessage: '' });
    }
}

export default BelatedPorting;