import * as linq from 'linq';
import * as React from 'react';
import { EPopupType } from '../../components/dialogs/popup';
import * as cmp from '../../components/index';
import { EFilterType } from '../../components/page/filter';
import * as page from '../../components/page/index';
import { EPagingType, IColumn } from '../../components/page/table';
import { IApiResponse, IApiSummary } from '../../interfaces';
import * as m from '../../models/index';
import IBrand from '../../models/ordering/brand';
import IProduct from '../../models/ordering/product';
import Api from '../../utils/api';
import Global from '../../utils/Global';
import { MainTexts } from '../../utils/messages';
import StorageHelper from '../../utils/storage';

export interface IDraftsScreenState {
    api?: IApiResponse;
    changed: boolean;
    expanded: boolean;

    apiMessage?: string;
    processing: boolean;
    closablePopup: boolean;
    confirmRemove: boolean;
};

/**
 * Component to handle the history of contracts done by a dealer
 */
export class DraftsScreen extends React.Component<any, IDraftsScreenState> {
    public static containerId: string = 'drafts';
    private filter: m.IFilter = { needle: '' };
    private processPopupState: EPopupType = EPopupType.pleaseWait;
    private currentRecord: any = {};

    constructor(props) {
        super(props);

        this.applyFilter = this.applyFilter.bind(this);
        this.expand = this.expand.bind(this);
        this.itemClick = this.itemClick.bind(this);
        this.onDismissConfirmPopup = this.onDismissConfirmPopup.bind(this);
        this.onDismissPopup = this.onDismissPopup.bind(this);
        this.removeDraft = this.removeDraft.bind(this);

        let to: Date = new Date();
        let from: Date = new Date();
        from = new Date(from.setMonth(from.getMonth() - 1)) as Date;
        this.filter = {
            needle: '',
            from: from,
            to: to
        };

        this.state = {
            expanded: false,
            changed: false,
            processing: true,
            closablePopup: false,
            confirmRemove: false
        };
    };

    public componentDidMount() {
        setTimeout(() => {
            Global.easyApp.setTitle(MainTexts.drafts);

            Global.hub.send('', m.ERequestType.getDrafts, undefined);
        }, 1000);
    };

    public render() {

        let ctrl = <div id={DraftsScreen.containerId}>
            {
                this.state.processing ? <></> :
                    <>
                        <page.Filter
                            filter={this.filter}
                            id="filter"
                            type={EFilterType.default} title={MainTexts.searchTitle}
                            onApply={this.applyFilter}
                            placeholderSearch="z.B. MSISDN oder Vorname" />
                        {this.renderData()}
                    </>
            }

            {this.renderPopup()}
            {this.renderPopupConfirm()}
        </div>;

        return ctrl;
    };

    private renderData(): JSX.Element {
        let cfg = Global.getConfiguration();

        let cols: IColumn[] = [
            { label: MainTexts.employee, binding: 'Employee', size: 2, sortable: true },
            {
                label: MainTexts.date, filterable: true, style: 'text-sub', binding: 'activationDate', size: 2, sortable: true, renderer: (r, c) => {
                    return <>{Global.toGermanDate(r[c.binding])}</>;
                }
            },
            { label: MainTexts.customerName, filterable: true, binding: 'Customer', size: 3, sortable: true, style: '' },
            {
                label: MainTexts.tariff, filterable: true, style: 'text-sub', binding: 'Product', size: 4, sortable: false, renderer: (r, c) => {
                    let br = linq.from<IBrand>(StorageHelper.getBrands())
                        .where(x => x.id == r['brandKey'])
                        .firstOrDefault();

                       

                    if (!br) { return null; }

                    let pr = linq.from<IProduct>(br.products)
                        .where(x => x.id === r['productId'])
                        .firstOrDefault();

                    r['Product'] = pr? pr.name : ''

                    if (!br) { return null; }

                    return <>
                        <div className={'brandi ' + br.id} />
                        <div className="text-sub">{pr? pr.name : ''}</div>
                    </>
                }
            },
            { 
                label: '', binding: '', size: 1, sortable: true,
                renderer: (c, r) => {
                    return <>
                        <page.LButton type={m.ELButtonType.iconImage}
                            icon="trash"
                            clickModel={c}
                            onClickEvent={(e, m) => {
                                e.preventDefault();
                                e.stopPropagation();
                                this.remove(m);
                                return false;
                            }} />
                    </>;
                },
                rendererHead: (c) => {
                    return <>
                        <page.LButton type={m.ELButtonType.iconImage}
                            icon="expand"
                            onClick={this.expand} />
                    </>;
                }
            },
        ];

        const pagingProps = { page: 1, count: cfg.pageSize, type: EPagingType.local };
        
        let ctrl = <div id={DraftsScreen.containerId} className={'c1 container ' + (this.state.expanded ? 'wide' : '')}>
            <div className="row">
                <div className="col-md-12 col-12">
                    <page.Table coulmns={cols} 
                        id={DraftsScreen.containerId + 'Table'}
                        expnaded={this.state.expanded}
                        itemClick={this.itemClick}
                        pagingProps={pagingProps}
                        dateColumn='activationDate'
                        filter={this.filter}
                        data={this.state && this.state.api && this.state.api.data ? this.state.api.data.data : undefined} />
                </div>
            </div>
        </div>;

        return ctrl;
    };

    private renderPopup(): JSX.Element {
        return <cmp.Popup
            type={this.processPopupState}
            visible={this.state.processing || this.state.closablePopup}
            closable={this.state.closablePopup}
            id="wait"
            title={MainTexts.pleaseWait}
            height={200}
            onDismiss={this.onDismissPopup}>
            <cmp.PopupBody>
                <div>{this.state.apiMessage}</div>
                <></>
            </cmp.PopupBody>
            <></>
        </cmp.Popup>
    };

    private renderPopupConfirm(): JSX.Element {
        return <cmp.Popup
            type={EPopupType.confirm}
            visible={this.state.confirmRemove}
            closable={true}
            id="wait"
            title={MainTexts.pleaseWait}
            height={200}
            onDismiss={this.onDismissConfirmPopup}
            onNegative={this.onDismissConfirmPopup}
            onPositive={this.removeDraft}>
            <cmp.PopupBody>
                <></>
                <></>
            </cmp.PopupBody>
            <></>
        </cmp.Popup>
    };

    private onDismissPopup() {

    };

    private onDismissConfirmPopup() {
        this.setState({ confirmRemove: false });
    };

    private applyFilter(f) {
        this.filter = f;
        this.setState({ changed: true });
    };

    public socketCallback(res: IApiResponse) {
        if (res.error) {
            let sum: IApiSummary = Api.parseSummary(res, undefined);
            this.processPopupState = EPopupType.custom;
            this.setState({ changed: true, processing: false, api: res, apiMessage: sum.message });
        } else {
            this.setState({ changed: true, processing: false, api: res });
        }
    };

    private itemClick(ev: any, id: any) {
        this.setState({ processing: true, apiMessage: '' });
        Global.hub.send('{"id": "' + id + '"}', m.ERequestType.loadDraft, undefined);
    };

    private expand() {
        this.setState({ expanded: !this.state.expanded });
    };

    private removeDraft() {
        Global.hub.send('{"id":"' + this.currentRecord['id'] + '"}', m.ERequestType.removeDraft, undefined);
        let data = this.state.api ? this.state.api.data.data : [];
        let a = this.state.api;
        for (let i = 0; i < data.length; i++) {
            if (data[i]['id'] == this.currentRecord['id']) {
                data.splice(i, 1);
                if (a) { a.data.data = data; }
                break;
            };
        };

        this.setState({ confirmRemove: false, api: a });
    };

    private remove(record) {
        this.currentRecord = record;
        this.setState({ confirmRemove: true });
    };
};

export default DraftsScreen;