import * as React from 'react';
import BaseBlock from '../../page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { ECreditDebitType, EBusinessCase, ECustomerType, EActivationType, EMnpType } from '../../../models/ordering/order';
import IProduct from '../../../models/ordering/product';
import ProcessNewScreen from '../../../screens/ordering/process';
import LButton from '../../page/button';
import LLabel from '../../page/label';
import LClear from '../../page/clear';
import { Row, Col, Container, Button } from 'react-bootstrap';
import { ELElementStatus, EPortationType, EControlType } from '../../../models/page/diverse';
import linq from 'linq';
import IBrand from '../../../models/ordering/brand';
import StorageHelper from '../../../utils/storage';
import * as page from '../../page/index';
import { IActivationBlockProps, IActivationBlockState, IActivationBlockOptions} from './activationBlock';
import { MainTexts, OrderingTexts } from '../../../utils/messages';
import { IBaseComponentState } from '../../../interfaces';
import { IActivation } from '../../../models';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { EDateType } from '../../page/date';

export interface IActivationMnpProps 
{
    parent: ProcessNewScreen;
    onChange: any;
};

export class ActivationMnp extends React.Component<IActivationMnpProps, IBaseComponentState> {
    constructor(props: IActivationMnpProps) {
        super(props);
        
        this.state = {
            changed: false
        };
    }

    public componentWillReceiveProps(next: any){
        this.setState(next);
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: MainTexts.portingInformation,
            transparent: false
        };

        let act: IActivation = this.props.parent.current ? this.props.parent.current : new IActivation(this.props.parent.selectedBrand);

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={12} md={12}>
                            <LLabel text={OrderingTexts.mnpDate}/>
                        </Col>
                    </Row>

                    {
                        this.props.parent.current && this.props.parent.current.brandKey === BrandKeys.kabel ?
                        <Row>
                            <Col xs={6} md={6} className="np">
                                <page.LControl onChange={this.props.onChange} checked={act.portationType === EPortationType.asap} 
                                type={EControlType.radio} name="portationType" id="portationType_1" label={OrderingTexts.mnpCable1}/>
                            </Col> 
                            <Col xs={6} md={6} className="np">
                                <page.LControl onChange={this.props.onChange} checked={act.portationType === EPortationType.contractEnd} 
                                type={EControlType.radio} name="portationType" id="portationType_2" label={OrderingTexts.mnpCable2}/>
                            </Col> 
                            <Col xs={6} md={6} className="np">
                                <page.LControl onChange={this.props.onChange} checked={act.portationType === EPortationType.custom} 
                                type={EControlType.radio} name="portationType" id="portationType_3" label={OrderingTexts.mnpCable4}/>
                            </Col> 
                            <Col xs={6} md={6} className="np">
                                <page.LControl onChange={this.props.onChange} checked={act.portationType === EPortationType.date} 
                                type={EControlType.radio} name="portationType" id="portationType_4" label={OrderingTexts.mnpCable3}/>
                            </Col> 
                        </Row>
                        : null
                    }

                    {
                        this.props.parent.current && (this.props.parent.current.brandKey === BrandKeys.otelo || this.props.parent.current.brandKey === BrandKeys.vodafone) ?
                        <Row>
                            <Col xs={4} md={4} className="np">
                                <page.LControl onChange={this.props.onChange} checked={act.portationType === EPortationType.asap} 
                                type={EControlType.radio} name="portationType" id="portationType_1" label={OrderingTexts.mnp1}/>
                            </Col>                         
                            <Col xs={4} md={4} className="npr">
                                <page.LControl onChange={this.props.onChange} checked={act.portationType === EPortationType.contractEnd} 
                                type={EControlType.radio} name="portationType" id="portationType_2" label={OrderingTexts.mnp2}/>
                            </Col>    
                            {
                                this.props.parent.current && this.props.parent.current.brandKey === BrandKeys.vodafone ?
                                <Col xs={4} md={4} className="npr">
                                    <page.LControl onChange={this.props.onChange} checked={act.portationType === EPortationType.custom} 
                                    type={EControlType.radio} name="portationType" id="portationType_3" label={OrderingTexts.mnp3}/>
                                </Col>                 
                                : null           
                            }
                        </Row>
                        : null
                    }


                    {
                        act.portationType === EPortationType.custom ?
                        <Row>
                            <Col xs={8} md={8} className="np">
                                <page.LDate 
                                    onChange={this.props.onChange} 
                                    value={act.portationDate} 
                                    datePicker = {true}
                                    type={EDateType.fixedStart}
                                    from={Global.dateOnly(Global.addDays(new Date(), 0))}
                                    to={Global.dateOnly(Global.addDays(new Date(), 720))}                                    
                                    name="portationDate" 
                                    id="portationDate" title={MainTexts.date}/>
                            </Col>                                                   
                        </Row>
                        : null
                    }            

                     {
                        act.brandKey === BrandKeys.vodafone && act.portationType === EPortationType.contractEnd ?
                        <Row>
                            <Col xs={8} md={8} className="np">
                                <page.LDate 
                                    onChange={this.props.onChange} 
                                    value={act.portationDate} 
                                    datePicker = {true}
                                    type={EDateType.fixedStart}
                                    from={Global.dateOnly(Global.addDays(new Date(), 0))}
                                    to={Global.dateOnly(Global.addDays(new Date(), 720))}                                    
                                    name="portationDate" 
                                    id="portationDate" 
                                    title={MainTexts.contractEndDate}/>
                            </Col>                                                   
                        </Row>
                        : null
                    }                           
                </Container>
                <LClear />
            </BaseBlock>
        );

        return ctrl;
    };
};

export default ActivationMnp;