import * as React from 'react';
import * as linq from 'linq';
import IBrand from '../../models/ordering/brand';
import IActivation from '../../models/ordering/activation';
import IService from '../../models/ordering/service';
import OptionGroup, { EOptionGroupRenderType } from '../process/optionGroup';
import { OrderingTexts, MainTexts } from '../../utils/messages';

export enum ERenderType {
    undefined = 0,
    options = 1
};

export interface ICableRendererProps {
    type: ERenderType;
    brand: IBrand;
    act: IActivation;
};

export class CableRenderer extends React.Component<ICableRendererProps, any> {
    constructor(props) {
        super(props);
    };

    public render() {
        /*
        switch(this.props.type) {
            case ERenderType.options:
                return this.renderOptions();
        };
        */

        return <></>
    };

    public renderOptions() : JSX.Element {
        if(this.props.act.productId.indexOf('kip')>=0){
            let svc = linq.from<IService>(this.props.brand.services)
                .where(x=>x.id.toLowerCase().indexOf('rufnummernneu')>=0)
                .toArray();

            let sel = linq.from<string | any>(this.props.act.services).where(x=>x.indexOf('rufnummernneu')>=0).firstOrDefault();

            return <OptionGroup 
                containerId="telNumberPortation"
                services={svc} brand={this.props.brand} 
                act={this.props.act} 
                title={MainTexts.numberOptions}
                selected={sel}
                type={EOptionGroupRenderType.oneMandatory}
                hint={' (' + MainTexts.optionsMandatory + ')'}/>
        };

        return <></>
    };
};

export default CableRenderer;