import * as React from 'react';
import BaseBlock from '../../../components/page/block';
import ProcessNewScreen from '../../../screens/ordering/process';
import IActivation from '../../../models/ordering/activation';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { ActivationContractBlock } from './activationContractBlock';

export interface IActivationContractContainerProps {
    parent: ProcessNewScreen; 
    activations: IActivation[];
};

export class ActivationContractContainer extends React.Component<IActivationContractContainerProps,any> {
    constructor(props: IActivationContractContainerProps) {
        super(props);
    };

    public render() {
        let ctrl = (<></>);

        if(this.props.activations && this.props.activations.length > 0) {
            let cfg: IBlockConfiguration = {
                title: 'Vertrag',
                transparent: true,
                style: '',
                styleContainer: 'block-full'
            };
    
            ctrl = (
                <>
                <BaseBlock config={cfg}>
                    {this.props.activations.map((i,idx)=>
                            (this.renderActivation(i, idx))
                    )}
                </BaseBlock>
                </>
            );
        }

        return ctrl;
    };

    private renderActivation(act: IActivation, idx: number) : JSX.Element {
        let ctrl = (
            <ActivationContractBlock 
                activation={act}
                key={idx}
                parent={this.props.parent}
                />
        );

        return ctrl;
    };
};

export default ActivationContractContainer;