import * as React from 'react';
import * as linq from 'linq';
import IBrand from '../../models/ordering/brand';
import { ECreditDebitType, EServiceType, ECustomerType, EActivationType, EBusinessCase } from '../../models/ordering/order';
import IProduct, {  ISubConfig, ISubServiceConfig } from '../../models/ordering/product';
import OrderingHelper from '../../utils/OrderingHelper';
import TariffThumb from '../ordering/tariffThumb';
import * as page from '../page/index';
import IProcessNewSelection from '../../interfaces/ordering/IProcessNewSelection';
import { EControlType, IActivation, OrderDataWrapper, ICustomerAction, ELButtonType, IOrderData } from '../../models';
import { MainTexts, OrderingTexts } from '../../utils/messages';
import ProductWrapper from '../../models/ordering/productWrapper';
import IService from '../../models/ordering/service';
import { BrandKeys, ConstantKeys } from '../../utils/Defaults';
import StorageHelper from '../../utils/storage';
import Global from '../../utils/Global';
import { IAvailabilityBrand, AvailabilityProduct } from '../../models/ordering/availability';
import { IBlockConfiguration } from '../../models/block/configuration';
import BlockBase from '../page/block';
import { ActivationServiceSelector } from '../ordering/process';
import OteloStatic from '../../utils/static';
import ISub from '../../models/ordering/sub';
import { runInThisContext } from 'vm';
import { IControlValue } from '../../models/page/interfaces';
import {ECustomerActionType} from '../../models/ordering/customerAction';
import { ICustomer } from '../../models/ordering/customer';

export interface ITariffConfigurationProps {
    brand: IBrand;
    onConfigurationChanged: any;
    availableProducts: IProduct[];
    selectedTariff: string;
    activation: IActivation;
    //customer: ICustomer;
    businessCase: EBusinessCase;
    selection: IProcessNewSelection;
    action?: ICustomerAction;
    backToCustomer?: any;
};

export interface ITariffConfigurationState {
    selection: IProcessNewSelection;
    changed: boolean;
    canChangeProduct: boolean;
    canChangeSub: boolean;
};

export class TariffConfiguration extends React.Component<ITariffConfigurationProps, ITariffConfigurationState> {
    private current: IProduct | any;
    private businessCase: EBusinessCase;
    private processParent: IOrderData | any;
    private availableProducts: IProduct[];

    constructor(props:ITariffConfigurationProps) {
        super(props);

        this.onSelectTariff = this.onSelectTariff.bind(this);
        this.onSelectOption = this.onSelectOption.bind(this);
        this.onSelectSub = this.onSelectSub.bind(this);

        this.businessCase = this.props.businessCase;

        this.processParent = OrderDataWrapper.getParentOfProcess(this.props.activation.processId);

        /** 2019-09-24
         * All VF VVL Products and Subs delivered by the SubHandy are being displayed
         * Services are filtered bei the respective businessCase
         */
        if(this.props.activation.activationType === EActivationType.existingCustomer 
            && this.props.activation.brandKey === BrandKeys.vodafone) {
            this.businessCase = EBusinessCase.bnt;
        };

        /**
         * 2020-03-17 the real businessCase for EC is stored inside the action
         */
        //if(this.props.action) {
        //    this.businessCase = this.props.action.bc;
        //};        

        /** 2020-02-24 
         * if gigakombi buchung then only one product
        */
       this.availableProducts = this.props.availableProducts;
       if(this.props.activation.activationType === EActivationType.bookService) {
            this.availableProducts = linq.from<IProduct>(this.props.brand.products)
                .where(x=>x.id === this.props.activation.productId)
                .toArray();
       }

        this.state = {
            selection: this.props.selection,
            changed: false,
            canChangeProduct: !(this.props.activation.activationType === EActivationType.bookService),
            canChangeSub: !(this.props.activation.activationType === EActivationType.bookService)
        };
    };

    public render() {
        let a: JSX.Element = <>
            <page.LButton type={ELButtonType.linkSimple}
                text={MainTexts.backToCustomerDataSheet}
                css="action3 fr"
                onClick={() => {
                    if(this.props.backToCustomer) {
                        this.props.backToCustomer();
                    }
                }} />
        </>;

        let cfg: IBlockConfiguration = { title: OrderingTexts.activationBNTTariff, 
            transparent: false ,
            action: a
        }; 
        
        let preselectedServices: string[] = [];

        //todo: check of VVL/tw
        if(this.processParent.customer.contracts && this.processParent.customer.contracts[0].bookedProduct) {
            preselectedServices = this.processParent.customer.contracts[0].bookedProduct.services;
        };

        this.current = linq.from(this.availableProducts)
            .where(x=>x.id === this.props.activation.productId)
            .firstOrDefault();

        let ctrl = (
            <BlockBase config={cfg}>
                <></>
                {
                    this.props.activation.brandKey === BrandKeys.vodafone ? 
                    <>
                        {this.renderCurrent(this.props.action)}
                        <div className="spacer-big clear"></div>
                    </>
                    : <></>
                }
                {this.renderGroups()}
                {
                    this.props.activation.inCurrent ? <></> : 
                    <>
                        <div className="spacer-big clear"></div>
                        {
                            (
                                (this.current && this.current.creditDebitType)
                                ||
                                this.props.activation.activationType === EActivationType.bookService
                            ) ? 
                            this.renderSub() : <></>
                        }

                        <div className="spacer-big clear"></div>
                        {
                            this.props.brand.id === BrandKeys.vodafone ?
                                <ActivationServiceSelector 
                                    activation={this.props.activation} 
                                    bc={EBusinessCase.vvl}
                                    preselected={preselectedServices}
                                    blockAddRemove={this.props.activation.activationType == EActivationType.bookService ? true : false}
                                    canEdit={true}
                                    onChanged={()=>{ this.props.onConfigurationChanged() }}
                                    brand={this.props.brand}/> :
                            <>
                                {
                                    this.props.activation && this.props.activation.brandKey != BrandKeys.kabel ?
                                    this.renderPromotions()
                                    : null
                                }

                                <div className="spacer-big clear"></div>
                                {this.renderOptions()}
                                <div className="spacer-big clear"></div>
                            </>
                        } 
                    </>
                }


            </BlockBase>
        );        
        
        return ctrl;
    };

    private renderCurrent(a: ICustomerAction | undefined ) : JSX.Element {
        if(a && (a.type != ECustomerActionType.STD || a.name.toLowerCase().indexOf("tarifwechsel")>=0)) {
            return <></>
        }   

        if(this.processParent.customer.contracts && this.processParent.customer.contracts[0].bookedProduct && this.processParent.customer.contracts[0].bookedProduct.sub == 'OS') {
            return <></>
        };        

        let ctrl = <div>
                <TariffThumb
                        key={'current-tar'}
                        id={ConstantKeys.current} 
                        text={OrderingTexts.prolongateCurrentTariff}
                        idx={0}
                        brandKey={this.props.brand.id} 
                        selected={this.props.activation.inCurrent ? true : false} 
                        styleBrand={false}
                        cssStyle={this.props.activation.inCurrent ? 'selected' : ''}
                        onClick={this.onSelectTariff}/>
        </div>

        return ctrl;
    };

    private renderSub() : JSX.Element {
        if(!this.current || !this.current.subs) { return <></> }

        let subs: string[] = linq.from<ISubConfig>(this.current.subs)
            .join(this.props.brand.subs, cs=>cs.id, bs=>bs.id, (cs,bs)=>{ return {CS:cs, BS:bs}})
            .orderBy(x=>x.BS.sort)
            .select(x=>x.CS.id).toArray();

        if(this.props.activation.isNba()) {
            subs = [this.props.activation.sub];
        };

        if(this.props.activation.brandKey === BrandKeys.otelo 
            && this.props.selection 
            && this.props.businessCase === EBusinessCase.bnt
            && this.props.selection.creditDebitType === ECreditDebitType.postpaid ) {
            if(linq.from<string>(subs).where(x=>x === OteloStatic.simOnly).count() === 0) {
                subs.push(OteloStatic.simOnly);
            }
        };

        //vf gigskombi
        if(this.props.activation.activationType === EActivationType.bookService) {
            subs = [this.props.activation.sub];
        };

        let subList : JSX.Element[] = [];

        subs = linq.from<string>(subs).distinct().toArray();

        if(this.props.activation.brandKey === BrandKeys.vodafone && this.props.action && this.props.action.type == ECustomerActionType.STD) {
            subs.splice(0, 0, 'OS');
        };

        subs.map((i,idx)=> {
            let tt = linq.from<ISub>(this.props.brand.subs).where(x=>x.id === i).first();
 
            let s = <TariffThumb 
                id={i} 
                text={OrderingHelper.getSub(this.props.brand, i).name}
                key={idx}
                idx={idx}
                tooltip={ tt.tooltip }
                tooltipModel={i}                    
                brandKey={this.props.brand.id} 
                selected={false} 
                styleBrand={false}
                cssStyle={this.props.activation.sub === i ? 'selected sub ' + this.props.brand.id : 'sub ' + this.props.brand.id}
                onClick={this.onSelectSub}/>
                subList.push(s);
        });

        let ctrl = (
            <>
            <div className="subtitle">{OrderingTexts.sub}</div>
            {subList}
            </>
        );

        return ctrl;
    };

    private renderGroups() : JSX.Element {
        let pt = this.state.selection.creditDebitType// : ECreditDebitType.postpaid;
        let ct = this.state.selection.customerType// : ECustomerType.personal;
        let at = this.state.selection.activationType// : EActivationType.actDefault;
        //let bc = this.state.selection.businessCase;

        let plist = linq.from(this.availableProducts).select(x=>x.id).toArray();

        var groups = linq.from<IProduct>(OrderingHelper.filterProducts(this.props.brand.id, this.props.brand.products, this.businessCase))
            .where(x=>(x.creditDebitType & pt) === pt && x.bookable && (x.customerType & ct) === ct && (x.activationType & at) === at)
            .where(x=>plist.indexOf(x.id) >= 0)
            .select(x=> {return {familyName: x.familyName, familySort:x.familySort}} )
            .distinct()
            .orderBy(x=>x.familySort)
            .select(x=>x.familyName)
            .distinct()
            .toArray();


        //vf gigskombi
        if(this.props.activation.activationType === EActivationType.bookService) {
            groups = linq.from<IProduct>(this.props.brand.products)
                .where(x=>x.id === this.props.activation.productId)
                .select(x=> {return {familyName: x.familyName, familySort:x.familySort}} )
                .distinct()
                .orderBy(x=>x.familySort)
                .select(x=>x.familyName)
                .distinct()
                .toArray();
        };

        let ctrl = (
            <>
                {groups.map((i,idx)=>
                    (this.renderGroup(i,idx))
                )}
                
            </>
        );

        return ctrl;
    };

    private renderGroup(name: string, idx: number) {
        let pt = this.state.selection? this.state.selection.creditDebitType : ECreditDebitType.postpaid;
        let ct = this.state.selection? this.state.selection.customerType : ECustomerType.personal;
        let at = this.state.selection? this.state.selection.activationType : EActivationType.actDefault;

        let plist = linq.from(this.availableProducts).select(x=>x.id).toArray();

        var products = linq.from<IProduct>(OrderingHelper.filterProducts(this.props.brand.id, this.props.brand.products, this.businessCase))
            .where(x=>x.familyName === name && (x.creditDebitType & pt) === pt && x.bookable && (x.customerType & ct) === ct && (x.activationType & at) === at)
            .where(x=>plist.indexOf(x.id) >= 0)
            .orderBy(x=>x.sort)
            .toArray();

        if(this.props.activation.isNba()) {
            products = linq.from<IProduct>(products).where(x=>x.id == this.props.activation.productId).toArray();
        };

        //vf gigskombi
        if(this.props.activation.activationType === EActivationType.bookService) {
            var products = linq.from<IProduct>(this.props.brand.products)
                .where(x=>x.id === this.props.activation.productId)
                .toArray();  
        };

        //no products found so return empty
        if(products.length === 0) {
            return <></>;
        };

        let ctrl = (
            <div key={idx}>
                <div className="subtitle">{name}</div>
                {products.map((i,idx)=>  
                    (<TariffThumb
                        key={idx}
                        id={i.id} 
                        text={i.name}
                        tooltip={i.tooltip}
                        tooltipModel={i}
                        idx={idx}
                        brandKey={this.props.brand.id} 
                        selected={i.id === this.props.activation.productId && !this.props.activation.inCurrent} 
                        styleBrand={false}
                        cssStyle={this.props.activation.productId === i.id && !this.props.activation.inCurrent? 'selected' : ''}
                        onClick={this.onSelectTariff}/>)
                    )}
                
                <page.LClear/>
            </div>
        );

        return ctrl;
    };    

    private renderPromotions() : JSX.Element {
        let ctrl = (
            <div className="subtitle">{MainTexts.noPromotions}</div>
        );  

        if(this.current) {
            //this.current = linq.from<IProduct>(this.availableProducts).firstOrDefault(x=>x.id == (this.current ? this.current.id : ''));

            let pw = new ProductWrapper(this.props.brand.id, this.current);
            let sub = linq.from<ISubConfig>(this.current.subs)
                .where(x=>x.id == this.props.activation.sub)
                .firstOrDefault();

            let svc = linq.from(pw.getServices(this.props.activation, this.props.businessCase, false))
                .toArray();

            if(svc.length > 0) {
                let all: JSX.Element[] = [];
                for(let i = 0; i < sub!.sl.length; i++) {
                    let data = linq.from<IService>(this.props.brand.services)
                        .where(x=>x.id === sub!.sl[i].id && x.type == EServiceType.promotion ) // && ((x.businessCase & EBusinessCase.bnt) === EBusinessCase.bnt))
                        .firstOrDefault();

                    if(data) {
                        all.push(<page.LControl type={EControlType.checkbox} 
                            label={data.name} 
                            key={i} id={data.id} 
                            onChange={this.onSelectOption}
                            hideHint={true} hideTitle={false}/>);
                    }
                }

                ctrl = 
                    <>
                        <div className="subtitle">Optionale Promotion</div>
                        {all}
                    </>
            }
        }
        
        return ctrl;
    };

    private renderOptions() : JSX.Element {
        let ctrl = (
            <div className="subtitle">{MainTexts.noOptions}</div>
        );  

        if(this.current) {
            let selectable:string[] = this.getSelectableServices();

            if(this.props.activation) {
                let pw = new ProductWrapper(this.props.activation.brandKey, 
                    this.current);

                //kabel services are not bound to hardware
                let svc: string[] = [];

                if(this.businessCase === EBusinessCase.bnt) {
                    svc = this.props.activation.brandKey == BrandKeys.kabel ? selectable
                        : linq.from(pw.getServices(this.props.activation, this.businessCase, false))
                            .where(x=>selectable.indexOf(x)>=0)
                            .toArray(); 
                } else {
                    svc = selectable;
                }

                this.props.activation.availability = StorageHelper.getAvailability();

                if(svc.length > 0) {
                    let brandServices = this.props.brand.services;
                    let all: JSX.Element[] = [];
                    for(let i = 0; i < svc.length; i++) {
                        let data = linq.from(brandServices)
                            .where(x=>x.id == svc[i] && x.type === EServiceType.option)
                            .firstOrDefault();

                        if(data) {                            
                            if(this.current 
                                && this.props.activation 
                                && this.props.activation.availability 
                                && this.props.brand.id === BrandKeys.dsl) {
                                var brandProducts = linq.from<IAvailabilityBrand>(this.props.activation.availability.brands)
                                    .where(x=>x.key === this.props.activation.brandKey)
                                    .first();

                                var avprod = linq.from<AvailabilityProduct>(brandProducts.products)
                                    .where(x=>x.productId === (this.current ? this.current.id : ''))
                                    .firstOrDefault();

                                /**
                                 * comfort option only if provided in the availability
                                 */
                                if(avprod) {
                                    if(!avprod.hasComfort && data.id === "svc_dsl_comfort") {
                                        continue;
                                    }
                                }
                            }

                            let text = data.name + 
                                ''; //(data.monthlyFee ? '( ' + Global.formatNumber(data.monthlyFee.price, 2, 2, data.monthlyFee.unit) + ' ' + data.id + ')' : '');
                            let name = 'cb-' + data.id;

                            all.push(<page.LControl  
                                type={EControlType.checkbox} 
                                disabled={this.serviceDisabled(data.id)} 
                                name={name } 
                                label={text + ':' + data.id} 
                                key={i} 
                                id={data.id} 
                                hideHint={true} 
                                checked={this.props.activation.services.indexOf(data.id) >= 0}
                                onChange={this.onSelectOption}/>);
                        } 
                    }
                    ctrl = 
                    (
                        <>
                        {all}
                        </>
                    );
                    
                    return ctrl;
                }
            }
        }
        
        return ctrl;
    };    

    private getSelectableServices() : string[] {
        let res: string[] = [];
        /*
                - BNT get the services from helper
                - VVL get them from curren action product
        */
        if(this.businessCase === EBusinessCase.bnt) {
           res = OrderingHelper.filterServices(this.props.brand, 
               this.props.activation ? this.props.activation : new IActivation(this.props.brand.id), 
               EServiceType.option);
        } else if (this.props.action) {
           let prod = linq.from<IProduct>(this.props.action.products)
               .where(x=>x.id === this.props.activation.productId)
               .first()
            
            let sub = linq.from<ISubConfig>(prod.subs)
                .where(x=>x.id === this.props.activation.sub)
                .firstOrDefault();

            if(sub) {
                res = linq.from<ISubServiceConfig>(sub.sl).select(x=>x.id).toArray();
            }
        };

        return res;
    };

    private onSelectTariff(value: string) {     
        if(!this.state.canChangeProduct) { return;};

        if(this.props.activation.isNba()) { return; }

        this.props.activation.servicesRemove = [];
        this.props.activation.productId = value;
        this.current = linq.from(this.availableProducts)
            .where(x=>x.id === this.props.activation.productId)
            .firstOrDefault();           
        
        this.props.activation.resetSeetedsubsAndServices();

        if(value == ConstantKeys.current) {
            if(this.processParent.customer.contracts && this.processParent.customer.contracts[0].bookedProduct) {
                this.props.activation.productId = this.processParent.customer.contracts[0].bookedProduct.id;
                this.props.activation.sub = this.processParent.customer.contracts[0].bookedProduct.sub;
                this.props.activation.inCurrent = true;
            }
        } else {
            this.props.activation.inCurrent = false;
        }

        StorageHelper.updateActivation(this.props.activation);

        if(this.props.onConfigurationChanged) {
            this.props.onConfigurationChanged(value);
        };

        this.setState({changed: true});
    };   
    
    private onSelectSub(value: string) {
        if(!this.state.canChangeSub) { return;};

        this.props.activation.servicesRemove = [];
        this.props.activation.sub = value;
        StorageHelper.updateActivation(this.props.activation);
        //this.setState({changed: true});
        if(this.props.onConfigurationChanged) {
            this.props.onConfigurationChanged(value);
        };
    }; 

    private onSelectOption(e: IControlValue) {
        if(e.checked) {
            if(this.props.activation) {
                this.props.activation.addService(e.id, undefined);
            }
        } else {
            if(this.props.activation) {
                this.props.activation.removeService(e.id,undefined);
            }
        }
        
        StorageHelper.updateActivation(this.props.activation);
        this.setState({changed: true});
    };

    private serviceDisabled(id: string) : boolean {
        return this.props.activation.validateService(id);
    };    
};

export default TariffConfiguration;