import * as React from 'react';
import { Container, Row, Col } from 'react-bootstrap';

export interface IInnerSingleProps {
    children: React.ReactChild
};

export class InnerSingle extends React.Component<IInnerSingleProps, any>{
    constructor(props: IInnerSingleProps){
        super(props);
    }

    public render() {
        let ctrl = (
        <>
            <Container>
                <Row>
                    <Col xs={12} md={12}>
                        {this.props.children}
                    </Col>
                </Row>
            </Container>
        </>
        );

        return ctrl;
    }
};

export default InnerSingle;