import * as React from 'react';
import '../../../screens/test/ProgressBar.tsx';

import { Container, Row, Col } from 'react-bootstrap';
import { LControl } from '../..';
import { MainTexts, OrderingTexts, OrderStages, ActivationMessages, ValidationMessages } from '../../../utils/messages';
import BaseBlock from '../../page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { EControlType, ELButtonType, ICancellation, ERequestType, ERequestStatus } from '../../../models';
import * as page from '../../page/index';

import Global from '../../../utils/Global';
import { ICustomer } from '../../../models/ordering/customer';
import { ActivationProgress } from '../../ordering/process';
import { EOrderProcessView } from '../../../models/ordering/order';
import { CustomerScreen } from '../../../screens/insex';
import { IControlValue, IKeyText, IKeyTextNumber } from '../../../models/page/interfaces';
import { IPopupConfig, EPopupType } from '../../dialogs/popup';
import StorageHelper from '../../../utils/storage';
import { EPreviewState } from '../../ordering/preview/contract';
import { IProcessStatus, IApiStep, IApiSummary } from '../../../interfaces';
import * as linq from 'linq';
import { IContract } from '../../../models/existing/contract';
import Api from '../../../utils/api';
import ActivationApi from '../../../utils/apiActivation';
import { ECancellationType, EValueType } from '../../../models/page/diverse';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import IUser from '../../../models/environment/user';

export interface ICancellationProps {
    onChange: any;
    parent: CustomerScreen;
};

export interface ICancellationState {
    changed: boolean;
    progress: EOrderProcessView;
};

//Ihre ausserordentliche Stornierung wird an den Vertrieb zu prfung weitergeleitet. Den aktuellen Status knnen sie jederzeit ber den otelo-EASY Auftragsmanger abrufen.

export class Cancellation extends React.Component<ICancellationProps, ICancellationState> {
    private cancel: ICancellation= new ICancellation();
    private readonly: boolean = false;
    private viewState: EPreviewState = EPreviewState.undefined;
    private processStatus:IProcessStatus = {steps: []};
    private contract:IContract;
    private printedDocument: string = '';
    private printed: boolean = false;
    private user?:IUser;

    constructor(props: any) {
        super(props);

        this.state = {
           changed: false,
           progress: EOrderProcessView.cancellation
        };

        this.user = StorageHelper.getUser();

        this.contract = this.props.parent.data.customer.contracts?this.props.parent.data.customer.contracts[0] : {};

        if(this.contract.cancel) {
            this.cancel = this.contract.cancel;
        };

        this.cancel.cancellationTypeId = 7;
        this.cancel.cancellationSpecialTypeId = 1;
        this.cancel.reasonId = 1;
        this.cancel.provisionRenounce = false; 
        this.cancel.employeeName = this.user ?  this.user.firstname + ' ' + this.user.surname : '';

        this.onChange = this.onChange.bind(this);
    };

    public render() {
        this.parseStatus();

        let a: JSX.Element = <>
            <page.LButton type={ELButtonType.linkSimple}
                text={MainTexts.backToCustomerDataSheet}
                css="action3 fr"
                onClick={() => {
                    this.props.parent.onBackToCustomer(true);
                }} />
        </>;

        let cfg: IBlockConfiguration = {
            title: MainTexts.cancellation,
            transparent: false,
            style: '',
            styleContainer: '',
            collapsible: false,
            collapsed: false,
            action: a
        };

        let title: JSX.Element = <h3>{OrderingTexts.cancellation}</h3>;

        let cancelProgress: JSX.Element = 
            <ActivationProgress 
                progress={this.state.progress} 
                states={[OrderStages.cancellation,OrderStages.overview,OrderStages.summary]} />;

        if(Global.easyApp.head) {
            Global.easyApp.head.setTitle(OrderingTexts.cancellation.toUpperCase());
            Global.easyApp.head.appendSub(cancelProgress);
        }

        let ctr = <></>;

        if(this.viewState === EPreviewState.activationDone) {
            let step = linq.from<IApiStep>(this.processStatus.steps).where(x=>x.type === ERequestType.activation).first();
            ctr = (
                <BaseBlock config={cfg}>
                    <Container>
                        <Row>
                            <Col xs={12} md={12} >
                                {step.message}
                            </Col>
                        </Row>
                    </Container>
                </BaseBlock>
            )
        } else {
            ctr = (
                <BaseBlock config={cfg}>
                        {
                            <Container >
                                <Row>
                                    <Col md={4}>
                                        <page.LControl type={EControlType.dropdown} 
                                            value={this.cancel.cancellationTypeId}
                                            onChange={this.onChange} 
                                            valueType={EValueType.numberInt}
                                            preview={this.state.progress === EOrderProcessView.overview}
                                            name="cancellationTypeId" id="cancellationTypeId" title={MainTexts.orderType}
                                            options={this.getOrderType(this.contract)} />
                                    </Col>
                                    {
                                        this.cancel.cancellationTypeId == ECancellationType.extraordinary ? 
                                        <Col md={6}>
                                            <page.LControl type={EControlType.dropdown} 
                                                value={this.cancel.cancellationSpecialTypeId}
                                                onChange={this.onChange} 
                                                valueType={EValueType.numberInt}
                                                preview={this.state.progress === EOrderProcessView.overview}
                                                name="cancellationSpecialTypeId" 
                                                id="cancellationSpecialTypeId" 
                                                title={MainTexts.extraordinaryCancellation}
                                                options={this.getExtraodrinaryCancellation()} />
                                        </Col>                                        
                                        : null
                                    }
                                </Row>
                                
                                <Row>
                                    <Col md={12} className="title">{MainTexts.reasoningPOS}</Col>
                                </Row>
                                
                                <Row>
                                    <Col md={4}>
                                        <page.LControl type={EControlType.dropdown} 
                                            value={this.cancel.reasonId}
                                            valueType={EValueType.numberInt}
                                            preview={this.state.progress === EOrderProcessView.overview}
                                            onChange={this.onChange} name="reasonId" id="reasonId" 
                                            title={MainTexts.typeOfCancellation}
                                            options={this.getCancellationReasons()} />
                                    </Col>
                                    <Col md={4}></Col>
                                </Row>
                                
                                <Row>
                                    <Col md={12}>
                                        <LControl hint={"Mind. 80 und Max. 140 Zeichen"} 
                                            type={EControlType.text} 
                                            style="merged"
                                            id="reason"
                                            name="reason"
                                            preview={this.state.progress === EOrderProcessView.overview}
                                            value={this.cancel.reason}
                                            onChange={this.onChange}
                                            label={MainTexts.reason} 
                                            validationOptions={ 
                                                [
                                                    new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                                    new ValidationOption({type: EValidationOption.minLength, targetValue: 80, message: 'Min 80' }),
                                                    new ValidationOption({type: EValidationOption.maxLength, targetValue: 120, message: 'Max 120' })
                                                ]
                                            }   
                                            />
                                    </Col>
                                </Row>
                                
                                <Row>
                                    <Col md={12} className="title">{MainTexts.dealerInfo}</Col>
                                </Row>
                                
                                <Row>
                                    <Col md={12}>
                                        <LControl type={EControlType.text} style="merged" 
                                            id="employeeName"
                                            name="employeeName"    
                                            preview={this.state.progress === EOrderProcessView.overview}                                
                                            value={this.cancel.employeeName}
                                            onChange={this.onChange}                                    
                                            label={MainTexts.dealerName} />
                                    </Col>
                                </Row>
                                
                                {
                                    this.state.progress === EOrderProcessView.overview ? 
                                    <Row>
                                        <Col md={12}> 
                                            <LControl type={EControlType.checkbox} 
                                                style="merged tal" 
                                                id="provisionRenounce"
                                                name="provisionRenounce"    
                                                preview={this.state.progress === EOrderProcessView.overview}                                
                                                checked={this.cancel.provisionRenounce}
                                                onChange={this.onChange} 
                                                label={'Mit dieser Auftragsstornierung verzichten wir auf unsere Provision fr die <br/>ausgewhlten Neuauftrge bzw. Vertragsverlngerungen.'} />
                                        </Col>
                                        <Col md={12}> 
                                            <div className="text-sub">
                                                <br/><br/>Achtung: Bei Formularausdruck ffnet sich eine neuer Reiter un Ihrem Broeser. Nach dem Ausdruck, mssen Sie den Button "Absenden" drcken. Formular vom Kunden unterzeichnet fr mgliche Rckfragen durch Vodafone oder Ihren L1 Hndelr lokal archivieren.
                                            </div>
                                        </Col>                                    
                                    </Row>
                                    : null
                                }

                                <Row>
                                    <Col xs={12} md={12} >
                                        {this.renderAction()}
                                    </Col>
                                </Row>
                            </Container>
                        }
                    </BaseBlock>
            )
        };

        return ctr;
    };

    private parseStatus() {
        this.viewState = EPreviewState.requestNeeded;

        if(this.processStatus.steps && this.processStatus.steps.length > 0) {
            let prStep = linq.from<IApiStep>(this.processStatus.steps).where(x=>x.type === ERequestType.print).first();
            let acStep = linq.from<IApiStep>(this.processStatus.steps).where(x=>x.type === ERequestType.activation).first();

            if(prStep.status === ERequestStatus.done) {

                this.viewState = EPreviewState.printNeeded;
                this.printedDocument = prStep.message
            };

            if(this.printed) {
                this.viewState = EPreviewState.printReady;
            };

            if(acStep.status === ERequestStatus.done) {
                this.viewState = EPreviewState.activationDone;
            };
        };        
    };

    private renderAction() : JSX.Element {
        let ctrl:JSX.Element = <></>;

        if(this.viewState === EPreviewState.activationDone) {
            return ctrl;
        };

        if(this.state.progress === EOrderProcessView.cancellation) {
            ctrl = <>
                <page.LButton type={ELButtonType.buttonWithIconRight}
                    css="a1 fr"
                    id=""
                    text={MainTexts.next}
                    onClick={() => {
                        if(this.props.parent.data.customer.contracts) {
                            this.props.parent.data.customer.contracts[0].cancel = this.cancel;
                            StorageHelper.addOrder(this.props.parent.data);
                        };

                        let cfg:IPopupConfig = {
                            props: {
                                id: 'dynamicPopup',
                                title: 'Stornoprozess wird vorbereitet',
                                visible: true,
                                closable: false,
                                type: EPopupType.pleaseWait
                            },
                            closeOnDone: true,
                            content: <div>{OrderingTexts.customerData}</div>,
                            callback: (d)=>{
                                this.setState({progress: EOrderProcessView.overview});
                                this.processStatus = d;
                                this.setState({changed: true});
                            }
                        };

                        this.props.parent.processCustomer(()=>{
                            Global.hub.send(JSON.stringify(this.props.parent.data),
                                ERequestType.createCancellationProcess,
                                undefined);
                        },cfg);
                    }} />        
            </>
        } else if(this.state.progress === EOrderProcessView.overview) {
            ctrl = <>
                {
                    this.viewState === EPreviewState.requestNeeded ?
                    <page.LButton type={ELButtonType.buttonWithIconRight}
                        css="action3 fr"
                        id=""
                        text={ActivationMessages.requestContract}
                        onClick={() => {
                            StorageHelper.addOrder(this.props.parent.data);

                            this.printed = false;
                            let cfg:IPopupConfig = {
                                props: {
                                    id: 'dynamicPopup',
                                    title: 'Ausdruck wird vorbereitet',
                                    visible: true,
                                    closable: false,
                                    type: EPopupType.pleaseWait
                                },
                                callback: (api)=>{ 
                                    this.processStatus = api;
                                    this.setState({changed: true});
                                },
                                content: <div>{OrderingTexts.customerData}</div>
                            };
    
                            let t = {
                                innerType: ERequestType.customerSetCancellation,
                                brandKey: this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0].brandKey : '',
                                id: this.props.parent.data.id,
                                orderId: this.props.parent.data.activations[0].id,
                                receiverId: CustomerScreen.containerId
                            };

                            this.props.parent.processCustomer(()=>{
                                Global.hub.send(JSON.stringify(t),
                                    ERequestType.print,
                                    undefined);
                            },cfg);
                        }} />  
                    : null
                }         
                {
                    this.viewState === EPreviewState.printReady ?
                    <page.LButton type={ELButtonType.buttonWithIconRight}
                        css="action3 fr"
                        id=""
                        text={ActivationMessages.run}
                        onClick={() => {
                            StorageHelper.addOrder(this.props.parent.data);

                            let cfg:IPopupConfig = {
                                props: {
                                    id: 'dynamicPopup',
                                    title: 'Stornierung wird ausgefhrt',
                                    visible: true,
                                    closable: false,
                                    type: EPopupType.pleaseWait
                                },
                                callback: (api)=>{
                                    this.setState({progress: EOrderProcessView.summary});
                                    this.processStatus = api;
                                    this.setState({changed: true});
                                },
                                closeOnDone: true,
                                content: <div>{OrderingTexts.customerData}</div>
                            };
    
                            let t = {
                                innerType: ERequestType.customerSetCancellation,
                                brandKey: this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0].brandKey : '',
                                id: this.props.parent.data.id,
                                orderId: this.props.parent.data.activations[0].id,
                                receiverId: CustomerScreen.containerId
                            };

                            this.props.parent.processCustomer(()=>{
                                Global.hub.send(JSON.stringify(t),
                                    ERequestType.activation,
                                    undefined);
                            },cfg);
                        }} />  
                    : null
                }                   
                {
                    this.viewState === EPreviewState.printNeeded || this.viewState === EPreviewState.printReady ?
                    <page.LButton type={ELButtonType.buttonWithIconRight}
                        css="action3 fr"
                        id=""
                        text={ActivationMessages.print}
                        onClick={() => {
                            let prStep = linq.from<IApiStep>(this.processStatus.steps).where(x=>x.type === ERequestType.print).first();
                            let doc = JSON.parse(prStep.message);
                            ActivationApi.download(this.props.parent.data.activations[0], '', null);
                            this.viewState = EPreviewState.printReady;
                            this.printed = true;
                            this.setState({changed: true});
                        }} />  
                    : null
                }                                
            </>
        }

        return ctrl;
    };
 
    private onChange(e: IControlValue) {
        Global.setValueByPath(this.cancel, e.name, e.type === EControlType.checkbox ? e.checked : e.value);
        
        if(this.props.parent.data.customer.contracts) {
            this.props.parent.data.customer.contracts[0].cancel = this.cancel;
            StorageHelper.addOrder(this.props.parent.data);
        };        

        this.setState({changed: true});
    };

    private getOrderType(contract: IContract): IKeyTextNumber[] {
        let op: IKeyTextNumber[] = new Array();

        if(this.user && this.user.accountLevel === 1) {
            if(contract.cancel && contract.cancel.bntCancelable) {
                op.push({ key: 1, text: "Neuvertrag " });
            };
            if(contract.cancel && contract.cancel.vvlCancelable) {
                op.push({ key: 2, text: "VVL" });
            };        
            if(contract.cancel && contract.cancel.twCancelable) {
                op.push({ key: 3, text: "TW" });
            };
        } else {
            if(contract.cancel && contract.cancel.bntCancelable) {
                op.push({ key: 4, text: "Neuvertrag (Freigabe Level 1 erforderlich)" });
            };
            if(contract.cancel && contract.cancel.vvlCancelable) {
                op.push({ key: 5, text: "VVL (Freigabe Level 1 erforderlich)" });
            };        
            if(contract.cancel && contract.cancel.twCancelable) {
                op.push({ key: 6, text: "TW (Freigabe Level 1 erforderlich)" });
            };
        }

        op.push({ key: 7, text: "Ausserordenliche Stornierung" });
        return op;
    };

    private getExtraodrinaryCancellation(): IKeyTextNumber[] {
        let op: IKeyTextNumber[] = new Array();
        op.push({ key: 1, text: "Neuvertrag (NV)" });
        op.push({ key: 2, text: "Neuvertrag mit Portierung (NMP)" });
        op.push({ key: 3, text: "Tarifwechsel (TW)" });
        op.push({ key: 4, text: "Vertragsverlngerung (VVL)" });
        op.push({ key: 5, text: "Bitte auswhlen" });

        return op;
    };

    private getCancellationReasons(): IKeyTextNumber[] {
        let op: IKeyTextNumber[] = new Array();
        op.push({ key: 1, text: "Storno mit Provisionsverzicht" });
        op.push({ key: 2, text: "Widerruf mit Provisionsverzicht" });
        return op;
    };    
};

export default Cancellation;