import IProduct from './product';
import ISub from './sub';
import IService, { IServiceRule } from './service';
import IServiceValidationGroup from './validationGroup';
import { InfoItem } from '../page/infoItem';

export class Brand extends InfoItem {
    public static fromInterface(i: IBrand) {
        let b = new Brand();
        b.id = i.id;

        return b;
    };
};

export interface IBrand {
    id:string;
    categories: string[];
    available: boolean;
    name:string;
    subs: ISub[];
    products: IProduct[];
    services: IService[];
    serviceRules: IServiceRule[];
    //info: IInfoItem;
    visible: boolean;
    loading?: boolean;

    style?: string;

    serviceGroups: IServiceValidationGroup[];
};

export default IBrand;