import * as React from 'react';


export class Footer extends React.Component<any, any> {
    constructor(props: any) {
        super(props);
    };

    public render() {
        return (
        <div className="footer">
            <div className="wrapper">
                <div className="logo">
                    <img src="/images/easy.png"/>
                </div>
            </div>
        </div>
        );
    };
};

export default Footer;