import * as React from 'react';
// import { EControlType, EComponentState } from '../../../models/page/diverse';
import { Col, Container, Row } from 'react-bootstrap';
import BlockBase from '../../../components/page/block';
import * as i from '../../../interfaces/index';
import { IApiResponse, ISocketPushModel } from '../../../interfaces/index';
import { IBlockConfiguration } from '../../../models/block/configuration';
import * as m from '../../../models/index';
import * as s from '../../../screens/insex';
import { ValidationWrapper } from '../../../utils';
import { StorageKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { MainTexts, ValidationMessages } from '../../../utils/messages';
import StorageHelper from '../../../utils/storage';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import * as page from '../../page/index';
import Status, { EStatusState } from '../../support/status';
import ActivationResponseStatus from './activationResponseStatus';
 
export interface IActivationAvailabilityProps 
{
    parent: s.WelcomeScreen;
    status: m.EComponentState
};

export interface IActivationAvailabilityState 
{
    status: m.EComponentState;
    corrected: boolean;
    closed: boolean;
};

export class ActivationAvailability extends React.Component<IActivationAvailabilityProps, IActivationAvailabilityState>
{   
    public static containerId: string = 'availability';
    private api?: i.IApiResponse;
    private process?: i.IProcessStatus;
    private apiState: EStatusState;    
    private current: m.Availability;
    private currentId: string = '';
    private timerId: string = 'availabilityTimer';
    constructor(props: any) {
        super(props);

        this.onStart = this.onStart.bind(this);
        this.onDelete = this.onDelete.bind(this);
        this.onCompleted = this.onCompleted.bind(this);
        this.socketCallback = this.socketCallback.bind(this);
        this.timerCallback = this.timerCallback.bind(this);
        this.timerCompletedCallback = this.timerCompletedCallback.bind(this);

        this.apiState = EStatusState.undefined;
        this.current = StorageHelper.getAvailability();

        if(this.current.executed != m.ERequestStatus.done) {
            this.current = StorageHelper.resetAvailability(undefined, m.ERequestStatus.undefined);
        }
        
        let cfg = Global.getConfiguration();

        

        if(cfg.debug) {
             this.current.address = {
                //street: 'Am Seestern', city:'Dsseldorf', zip:'40547', nr: '1', house_number: '1', corrected: false, additional: ''
                //// unity
                //street: 'Pingsdorfer Str.', city:'Brhl', zip:'50321', nr: '108', house_number: '108', corrected: false, additional: ''
                //// unity promo
                //street: 'Im Fuhlenbrock', city:'Bottrop', zip:'46242', nr: '187', house_number: '187', corrected: false, additional: ''
                ////  
              //  street: "Im Wolfsfeld", nr: "19", house_number: '19', city:"Bochum", zip:"44879", corrected: false, additional: ''
                //// mix
                // street: 'Goethestr. ', city:'Wolfsburg', zip:'38440', nr: '54', house_number: '54', corrected: false, additional: ''                
                //// cable
                //street: 'Ludwigstr.', city:'Wrzburg', zip:'97070', nr: '22', house_number: '22', corrected: false, additional: ''
                //// dsl regio
                //street: 'Schillstr.', city:'Saarbrcken', zip:'66113', nr: '3', house_number: '3', corrected: false, additional: ''
                 street: 'Edith-Stein-Str.', city:'Kln', zip:'51063', nr: '12', house_number: '12', corrected: false, additional: ''
             };
        };

        this.state = {
            status: this.props.status,
            corrected: false,
            closed: false
        };
    };

    public componentWillReceiveProps(next: IActivationAvailabilityProps) {
        this.setState({status: next.status});
    };

    public render () {
        let cfg: IBlockConfiguration = {
            title: MainTexts.textAvailability,
            transparent: false
        };

        let ctrl = <></>;

        if(this.current.executed == m.ERequestStatus.done) {
            let info: string = 'Adresse: ' + this.current.forUI();
            ctrl = (
                <BlockBase config={cfg}>
                    <Container>
                        <Row>
                            <Col xs={12} md={12} className="np black">
                                {info}
                            </Col>                      
                        </Row>
                        <Row>
                            <page.LButton text="Prfung fr eine neue Adresse starten" 
                                onClick={this.onDelete} 
                                css="fr"
                                type={m.ELButtonType.buttonWithIconRight} 
                                icon="arrow-right"/> 
                        </Row>                           
                    </Container>
                    <page.LClear />
                </BlockBase>
            );

            return ctrl;
        };

        if(this.state.status != m.EComponentState.hidden) {
            ctrl = (
                <div id={ActivationAvailability.containerId}>
                <BlockBase config={cfg}>
                    <Container>
                        <Row>
                            <Col xs={9} md={9} className="np">
                                {/* <a href="javascript:void()" onClick={(a)=>{this.setState({closed: true})}}>close</a> */}

                                <page.LControl type={m.EControlType.text} 
                                    value={this.current.address.street} 
                                    onChange={ (t)=> { Global.AutoInput(this, this.current, t); } } 
                                    title="Anschrift" name="address.street" id="street" label={MainTexts.street}
                                    validationOptions={ 
                                        [
                                            new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError })
                                        ]
                                    }/>
                            </Col>
                            <Col xs={3} md={3} className="npr">
                                <page.LControl type={m.EControlType.text} value={this.current.address.nr} 
                                onChange={(t)=> { Global.AutoInput(this, this.current, t); }} name="address.nr" id="nr" label={MainTexts.stnr}
                                validationOptions={ 
                                    [
                                        new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError })
                                    ]
                                }/>
                            </Col>                        
                        </Row>

                        <Row>
                            <Col xs={3} md={3} className="np">
                            <page.LControl type={m.EControlType.text} value={this.current.address.zip} 
                                onChange={(t)=> { Global.AutoInput(this, this.current, t); }} name="address.zip" id="zip" label="PLZ" hideTitle={true} 
                                validationOptions={ 
                                    [
                                        new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                        new ValidationOption({type: EValidationOption.exactLength, targetValue: 5, message: ValidationMessages.errorExactLength })
                                    ]
                                }/>
                            </Col>                       

                            <Col xs={9} md={9} className="npr">
                            <page.LControl type={m.EControlType.text} value={this.current.address.city} 
                                onChange={(t)=> { Global.AutoInput(this, this.current, t); }} name="address.city" id="city" label="Ort" hideTitle={true} 
                                validationOptions={ 
                                    [
                                        new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError })
                                    ]
                                }/>
                            </Col>                                 
                        </Row>  

                        <Row>
                            {
                                (this.state.status == m.EComponentState.visible || this.state.status === m.EComponentState.nextStep) && this.apiState != EStatusState.inprocess ? 
                                <page.LButton text="Starten" 
                                    onClick={this.onStart} 
                                    css="fr"
                                    type={m.ELButtonType.buttonWithIconRight} 
                                    icon="arrow-right"/>                       
                                    :
                                null
                            }    
                                 
                            {
                                (this.api && this.state.status == m.EComponentState.visible) || this.state.status == m.EComponentState.processing || this.state.status === m.EComponentState.nextStep ? 
                                <ActivationResponseStatus
                                    timerId={this.timerId}
                                    parent = {this}
                                    apiState={this.apiState}
                                    api={this.api} 
                                    process={this.process} 
                                    completedCallback={this.timerCompletedCallback}
                                    processCallback={this.timerCallback}/>                
                                    :
                                null
                            }               
                        </Row>                           
                    </Container>
                    <page.LClear />
                </BlockBase>
                </div>
            );
        }

        return ctrl;
    };

    private timerCallback() {

    };

    private timerCompletedCallback() {
        this.apiState = EStatusState.undefined;
        this.setState({status: m.EComponentState.visible});
    };    

    public socketCallback(sock: ISocketPushModel) {
        let api: IApiResponse = JSON.parse(sock.data);
        this.api = api;
        let inner = api.data;

       
        if (this.api.error != null) {

            this.apiState = EStatusState.failed;
            this.current.executed = m.ERequestStatus.undefined;
            this.setState({corrected: false, status: m.EComponentState.visible});
            return;
        };

        if(inner == null) {
            return;
        };

            try {
                let step: i.IApiStep = inner.steps[0];// ActivationApi.processStep(api, state);

                if(step.status == m.ERequestStatus.done || step.status == m.ERequestStatus.tempResult) {
                    this.apiState = step.status == m.ERequestStatus.done ? EStatusState.done : EStatusState.inprocess;
                    this.current = StorageHelper.getAvailability();

                    this.setState({corrected: false, 
                        status: this.current.executed == m.ERequestStatus.done ? m.EComponentState.visible : m.EComponentState.processing});
                    this.props.parent.refresh();
                } else if (step.status === m.ERequestStatus.corrected) {
                    this.apiState = EStatusState.warning;
                    this.process = {steps: [step]};
                    let adr: m.Address = JSON.parse(step.message);

                    if(adr) {
                        this.current.address = adr;
                        this.current.address.nr = adr.house_number;
                        this.setState({corrected: true, status: m.EComponentState.nextStep});
                    }
                } else if (step.status === m.ERequestStatus.failed || step.status === m.ERequestStatus.invalid) {
                    this.apiState = EStatusState.failed;
                    this.current.executed = m.ERequestStatus.undefined;
                    this.setState({corrected: false, status: m.EComponentState.visible});
                };

                Global.categoryWrapper.reset();
            } catch(e) {
                console.error(e);
            }
    };

    private onCompleted(){
        this.setState({status: m.EComponentState.visible});
    };

    private onDelete() {
        this.current = new m.Availability();

        this.api = undefined;

        StorageHelper.set(StorageKeys.keyAvailability, this.current);

        Global.categoryWrapper.resetCategory();

        this.props.parent.refresh();

        this.setState({status: m.EComponentState.visible});
    };

    private validate() : boolean {
        let all = ValidationWrapper.getControls(ActivationAvailability.containerId, 'oe-control');
        
        for(let i = 0; i< all.length; i++) {
            let element = document.getElementById(all[i].id);
            let ctrl:page.LControl = Global.FindReact(element);
                if(!ctrl.validate(undefined, true)) {
                    return false;
                }   
        };
        return true;
    };

    private onStart() {
        if(!this.validate()) {
            return;
        }

        if(!Global.easyApp.checkDefCon()) {
            return;
        };

        this.process = {steps: []};
        this.apiState = EStatusState.inprocessTimer;
        this.api = undefined;

        this.current = StorageHelper.resetAvailability(this.current.address, m.ERequestStatus.running);

        Global.hub.send(JSON.stringify(this.current), 
            m.ERequestType.checkAvailability,
            undefined);

        this.setState({status: m.EComponentState.processing}, ()=>{
            var timer = Global.GetComponent<Status>(this.timerId);
            timer.resetTimer();   
        });

        //this.props.parent.refresh();
    };
};


export default ActivationAvailability;