export interface IConsumption {
    data:ConsumptionItem;
};

export class ConsumptionItem {
    Consumed: number=0;
    Remaining: number=0;
    Total: number=0;
    Unit: string='';

    public getValue() : number {
        let v:number = 100 * (this.Consumed / this.Total);
        return v;
    };
    public getValueReverse() : number {
        let v:number = 100 - 100 * (this.Consumed / this.Total);
        return v;
    };
    public getLabel() : string {
        return this.Consumed + ' von ' + this.Total + ' ' + this.Unit + ' verbraucht';
    };
};
