import * as React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { MainTexts, OrderingTexts, OrderProductStages } from '../../utils/messages';
import BaseBlock from '../../components/page/block';
import { IBlockConfiguration } from '../../models/block/configuration';
import {ELButtonType, IOrderProductViewModel } from '../../../src/models';
import * as page from '../../../src/components/page/index';
import Global from '../../utils/Global';
import * as m from '../../models/index';
import StorageHelper from '../../utils/storage';

import {IApiResponse } from '../../interfaces';
import IUser from '../../models/environment/user';
import { EOrderProcessView } from '../../models/ordering/order';
import { ActivationProgress } from '../../components/ordering/process';
import { IColumn, EPagingType } from '../../components/page/table';
import { StorageKeys } from '../../utils/Defaults';
import EasyRouter from '../../utils/router';
import { EPopupType} from '../../components/dialogs/popup';
import * as cmp from '../../components/index';


export interface IOrderProps {
    onChange: any;
};

export interface IOrderState {
    changed: boolean;
    progress: EOrderProcessView;
    table1;
    popup: boolean;
    processingDone: boolean;
};

export interface IDataTable1 {
    ProductGroup: string,
    Picture: string,
    Id: string,
    Name: string,
    ProductAvailability: string,
    PriceString: string,
    Amount: any
};

export class OrderOverview extends React.Component<IOrderProps, IOrderState> {
    public static containerId: string = 'orderOverview';
    public dataTable1: IDataTable1[] = [];
    public orderProductsModel: IOrderProductViewModel = { products: [], address: new m.Address() };
    private processPopupStateCustom: EPopupType = EPopupType.custom;
    private processPopupStateWait: EPopupType = EPopupType.pleaseWait;

    public user?: IUser;

    constructor(props: any) {
        super(props);

        this.state = {
            changed: false,
            progress: EOrderProcessView.orderOverview,
            table1: true,
            popup: false,
            processingDone: false
        }

        this.user = StorageHelper.getUser();
        var data = sessionStorage.getItem(StorageKeys.keyOrdersProducts);

        if (data !== null) {
            this.orderProductsModel = JSON.parse(data);
        }

        this.socketCallback = this.socketCallback.bind(this);
        this.calculateAmount = this.calculateAmount.bind(this);
        this.calculatePriceTotal = this.calculatePriceTotal.bind(this);
    };

    public render() {
        let cfg = Global.getConfiguration();
        const pagingProps = { page: 1, count: cfg.pageSize, type: EPagingType.local };

        let colsTable1: IColumn[] = [
            { label: MainTexts.productGroup, binding: 'ProductGroup', size: 3, sortable: true },
            { label: MainTexts.picture, binding: 'Picture', size: 1, sortable: true },
            { label: MainTexts.surname, binding: 'Name', size: 2, sortable: true },
            { label: MainTexts.availability, binding: 'ProductAvailability', size: 2, sortable: true },
            { label: MainTexts.priceInEuro, binding: 'PriceString', size: 2, sortable: true },
            { label: MainTexts.amount, binding: 'Amount', size: 2, sortable: true }
        ];

        let a: JSX.Element = <>
            <page.LButton type={ELButtonType.linkSimple}
                text={MainTexts.back}
                css="action3 fr"
                onClick={() => {
                    EasyRouter.route({ url: '/apps/orderproducts/selectdeliveryaddress', title: MainTexts.selectDeliveryAddress });
                }} />
        </>;

        let cfg1: IBlockConfiguration = {
            title: MainTexts.yourOrder,
            transparent: false,
            style: '',
            styleContainer: 'block-orderProducts',
            collapsible: false,
            collapsed: false,
            action: a
        };

        let cancelProgress: JSX.Element =
            <ActivationProgress
                progress={this.state.progress}
                states={[OrderProductStages.selectProducts, OrderProductStages.deliveryAddress, OrderProductStages.overviewOrder]} />;

        if (Global.easyApp.head) {
            Global.easyApp.head.setTitle(OrderingTexts.cancellation.toUpperCase());
            Global.easyApp.head.appendSub(cancelProgress);
        }

        let ctr = <></>;

        ctr = <div id={OrderOverview.containerId}>
            <page.Inner1C style="   ">
                {this.renderProgress()}
            </page.Inner1C>

            <BaseBlock config={cfg1}>
                {
                    <Container>
                        <div className="spacer"></div>
                        <Row>
                            <Col xs={4} md={4} >
                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                    css="a1 fr"
                                    id=""
                                    text={MainTexts.orderProducts}
                                    onClick={() => {

                                        EasyRouter.route({ url: '/apps/orderproducts', title: MainTexts.orderProductsTitle });
                                    }} />
                            </Col>
                            <Col xs={4} md={4} >
                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                    css="a1 fr"
                                    id=""
                                    text={MainTexts.yourOrders}
                                    onClick={() => {
                                        let t = {
                                            id: 'test',
                                            url: 'test'
                                        };

                                        Global.hub.send(JSON.stringify(t), m.ERequestType.getOrdersList, undefined);
                                        EasyRouter.route({ url: '/apps/orderproducts', title: MainTexts.yourOrders });
                                    }} />
                            </Col>
                        </Row>

                        <Row>
                            <Col >
                                {this.state.table1 ? <page.Table coulmns={colsTable1}
                                    id={'1'}
                                    expnaded={false} //to do adding datetime field
                                    pagingProps={pagingProps}
                                    data={this.orderProductsModel.products} /> : <></>
                                }
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={8} md={8}>
                            </Col>
                            <Col xs={2} md={2}>
                                <page.LLabel text={MainTexts.orderText+':  ' + this.calculatePriceTotal()} />
                            </Col>
                            <Col xs={2} md={2}>
                                <page.LLabel text={MainTexts.amount+':  ' + this.calculateAmount()} />
                            </Col>
                        </Row>

                        <Col xs={12} md={12} >
                            <page.LButton type={ELButtonType.buttonWithIconRight}
                                css="a1 fr"
                                id=""
                                text={MainTexts.next}
                                onClick={() => {
                                    this.setState({ popup: true, processingDone: false });
                                    Global.hub.send(JSON.stringify(this.orderProductsModel), m.ERequestType.saveOrderPoducts, undefined);
                                }} />
                        </Col>

                        <Row>
                            <Col md={4}>
                                <page.LLabel text={OrderProductStages.deliveryAddress} />
                            </Col>
                        </Row>

                        <Row>
                            <Col md={4}>
                                <page.LLabel text={this.orderProductsModel.address.name} />
                            </Col>
                        </Row>

                       

                        <Row>
                            <Col md={4}>
                                <page.LLabel text={(this.orderProductsModel.address.house_number===''? "N/A":this.orderProductsModel.address.house_number) + ", " + this.orderProductsModel.address.zip + ", " + this.orderProductsModel.address.city} />
                            </Col>
                        </Row>

                    </Container>
                }
                {this.state.popup ? this.renderPopup() : <></>}
            </BaseBlock>
        </div>

        return ctr;
    };

    public socketCallback(res: IApiResponse) {
        if (res.data.status === 1) {
            this.setState({ processingDone: true });
        }
    }

    private renderProgress(): JSX.Element {
        let cancelProgress: JSX.Element =
            <ActivationProgress
                progress={this.state.progress}
                states={[OrderProductStages.selectProducts, OrderProductStages.deliveryAddress, OrderProductStages.overviewOrder]} />;

        if (Global.easyApp.head) {
            Global.easyApp.head.setTitle(MainTexts.yourOrder);
            Global.easyApp.head.appendSub(cancelProgress);
        }
        return <></>;
    }

    private renderPopup(): JSX.Element {
        let a: JSX.Element = <>
            <page.LButton type={ELButtonType.linkSimple}
                text={MainTexts.backToOrderProducts}
                css="action3 fr"
                onClick={() => {
                    EasyRouter.route({ url: '/apps/orderproducts', title: MainTexts.orderProductsTitle });
                }} />
        </>;

        let b: JSX.Element = <>
            <page.LButton type={ELButtonType.linkSimple}
                text={MainTexts.monitorYourOrders}
                css="action3 fr"
                onClick={() => {
                    let t = {
                        id: 'test',
                        url: 'test'
                    };
                    Global.hub.send(JSON.stringify(t), m.ERequestType.getOrdersList, undefined);
                    EasyRouter.route({ url: '/apps/orderproducts', title: MainTexts.yourOrders });
                }} />
        </>;

        let cfg1: IBlockConfiguration = {
            title: '',
            transparent: false,
            style: '',
            styleContainer: 'block-innerMiddle',
            collapsible: false,
            collapsed: false

        };

        return <cmp.Popup
            type={this.state.processingDone ? this.processPopupStateCustom : this.processPopupStateWait}
            visible={this.state.popup}
            closable={false}
            id="wait"
            title={''}
            height={400}
            width={600}
            onDismiss={this.onDismissPopup}>
            <cmp.PopupBody>

                <BaseBlock config={cfg1}>
                    {
                        <Container>
                            <Row>
                                <Col>
                                    <h3>{MainTexts.thankYouFor} BESTELLUNG.</h3>
                                    {a}
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                  {b}
                                </Col>
                            </Row>
                        </Container>
                    }
                </BaseBlock>
                <></>
            </cmp.PopupBody>
            <></>
        </cmp.Popup>
    };

    private onDismissPopup() {
        this.setState({ popup: false });
    };

    private calculateAmount() {
        let count: number = 0;
        this.orderProductsModel.products.forEach(product => {
            count += Number(product.Amount);
        })
        return count.toString();
    }

    private calculatePriceTotal() {
        let priceTotal: number = 0.00;
        this.orderProductsModel.products.forEach(product => {
            var priceFormat: string = product.PriceString.substring(0, 3);
            priceTotal += Number(product.Amount) * Number(priceFormat);
        })

        return priceTotal.toFixed(2).toString() + " ";
    }

};

export default OrderOverview;