import Api from './api';
import Global from './Global';
import linq from 'linq';
import * as i from '../interfaces/index';
import { HttpMethods } from './Defaults';
import IActivation from '../models/ordering/activation';
import { ERequestStatus, ERequestType } from '../models/page/diverse';


export class ActivationApi  {
    public static runAction(step: ERequestType, model: any, callback: any) {
        let app = Global.getConfiguration();
        let url = app.apiUrl + 'activation/';
        let m: any = model;

        if(step === ERequestType.create) {
            url+='create';
        } else if (step === ERequestType.print) {
            url+='print/' + model.id;
            m = undefined;
        } else if (step === ERequestType.status) {
            url+='status/' + model.id;
            m = undefined;
        } else if (step === ERequestType.statusRequest) {
            url+='statusrequest/' + model.id;            
            m = undefined;
        } else if (step === ERequestType.activation) {
            url+='activate/' + model.id;;
            m = undefined;
        } 

        Api.fetch(url, 
            m,
            HttpMethods.post,
            (a:i.IApiResponse) => {
                Api.parseResponse(a, (api: i.IApiResponse)=>{
                    if(callback) { callback(api); }
                });                
            });        
    };

    public static processStep(api: i.IApiResponse, type: ERequestType) : i.IApiStep {
        let result:i.IApiStep = { type: ERequestType.undefined, status: ERequestStatus.failed, message: 'Fehler'};

        if(api && ! api.error) {
            if(api && !api.error && api.data && api.data.steps) {
                let step:i.IApiStep = linq.from<i.IApiStep | any>(api.data.steps)
                    .where(x=>x.type == type)
                    .firstOrDefault();
                
                    result = step;
            }
        } 
        
        if (api && api.error){
            result.message = api.error.message;
        }

        return result;
    };

    public static getSteps(api: i.IApiResponse) : i.IApiStep[] {
        let result:i.IApiStep = { type: ERequestType.undefined, status: ERequestStatus.failed, message: 'Fehler'};

        if(api && ! api.error) {
            if(api && !api.error && api.data && api.data.steps) {
                let step:i.IApiStep[] = linq.from<i.IApiStep>(api.data.steps)
                    .toArray();
                
                    return step;
            }
        } 
        
        if (api && api.error){
            result.message = api.error.message;
        }

        return [];
    };

    public static download(act: IActivation, 
        file: string,
        callback: any) {
        let app = Global.getConfiguration();

        let url = app.apiUrl + 'activation/download/' + act.id + '?file=' + file;

        window.open(url, "_blank");
    };
};

export default ActivationApi;