import * as linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBaseComponentState } from '../../../interfaces';
import { EExistingCustomerLoginMode } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { IContract } from '../../../models/existing/contract';
import * as d from '../../../models/existing/document';
import { ICustomer } from '../../../models/ordering/customer';
import { EDocumentType } from '../../../models/ordering/order';
import CustomerScreen from '../../../screens/existing/customer';
import { BrandKeys } from '../../../utils/Defaults';
import { MainTexts, OrderingTexts } from '../../../utils/messages';
import ExistingCustomerPayment from '../../existing/data/customerPayment';
import BaseBlock from '../../page/block';
import Download from './download';

export interface IExistingCustomerBillsProps {
    parent: CustomerScreen;
};

export class ExistingCustomerBills extends React.Component<IExistingCustomerBillsProps, IBaseComponentState> {
    private contract: IContract;
    private customer: ICustomer;
    constructor(props: IExistingCustomerBillsProps) {
        super(props);

        this.state = {
            changed: false
        };

        this.customer = this.props.parent.data.customer;
        this.contract = this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0] : {};
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.customerBills,
            transparent: false,
            style: '',
            styleContainer: '',
            collapsible: true,
            collapsed: false
        };

        let bills: d.Document[] = this.contract.documents ? linq.from<d.Document>(this.contract.documents).where(x => x.type === EDocumentType.bill).toArray() : [];
        let evn: d.Document[] = this.contract.documents ? linq.from<d.Document>(this.contract.documents).where(x => x.type === EDocumentType.evn).toArray() : [];

        let ctrl =
            <Container className="">
                <Row>
                    {this.props.parent.data.activations[0].brandKey === BrandKeys.otelo && this.props.parent.data.customer.loginModel && this.props.parent.data.customer.loginModel.mode === EExistingCustomerLoginMode.birthday ?
                        <Col md={12}>
                            <p><b>{MainTexts.exCustBillsWithBday}</b></p>

                        </Col> : <Col md={12} className="np">
                            {
                                bills.map((i, idx) => (<Download doc={i} brandKey={this.props.parent.data.activations[0].brandKey} activationId={this.props.parent.data.activations[0].id} key={'b-' + idx} />))
                            }
                        </Col>}
                </Row>
                <Row>
                    <Col md={12} className="np">
                        <ExistingCustomerPayment parent={this.props.parent}
                            contract={this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0] : undefined} />
                    </Col>
                </Row>
            </Container>;

        return <BaseBlock config={cfg}>
            {ctrl}
        </BaseBlock>;
    };
};

export default ExistingCustomerBills;