import * as React from 'react';

export interface IInner2CLeftProps {
    children: React.ReactChild
};

export class Inner2CLeft extends React.Component<IInner2CLeftProps, any>{
    constructor(props: IInner2CLeftProps){
        super(props);
    }

    public render() {
        let ctrl = (
        <>
            {this.props.children}
        </>
        );

        return ctrl;
    }
};

export default Inner2CLeft;