import * as React from 'react';
import EasyRouter from '../../utils/router';
import App from '../../App';
import Global from '../../utils/Global';
import StorageHelper from '../../utils/storage';
import Api from '../../utils/api';
import Teaser from '../tools/teaser';

export interface IHeadProps {
    changed: boolean;
    title?: string;
};

export interface IHeaderState {
    changed: boolean;
    title?: string;
    fixed?: boolean;
    subContent?: React.Component;
};

let tgl: HTMLDivElement;

export class Head extends React.Component<IHeadProps, IHeaderState> {
    constructor(props: IHeadProps) {
        super(props);

        let cfg = Global.getConfiguration();
        
        Global.easyApp.head = this;

        this.state = {
            changed: false,
            title: cfg.appTitle
        };

        this.appendSub = this.appendSub.bind(this);
    };

    public fix(value: boolean) {
        this.setState({fixed: value});
    };

    public appendSub(value: any) {
        this.setState({ subContent: value, changed: true});
    };

    public render() {
        let css: string = "header";

        if(this.state.fixed) {
            css+= ' fix';
        }

        if(this.state.subContent) {
            css+= ' hassub';
        }

        return (
            <div className={css} id="header">
                <Teaser />
                <div className="wrapper">
                    <div className="logo">
                        <a href={Api.getAppUrl()}>
                            <img src="/images/vfeasy.png"/>
                        </a>
                    </div>

                    <div className="title">
                        {this.state.title}
                    </div>

                    {this.state.subContent ? (<div className="sub">{this.state.subContent}</div>) : null}
                </div>
            </div>
        );
    };

    public setTitle(value: string) {
        this.setState({title: value});
    }; 
};

export default Head;