import * as linq from 'linq';

export class TreeItem {
    id: string = '';
    prio: number = 0;
    children: TreeItem[] = [];

    constructor(id: string | undefined) {
        this.id = id || 'root';
        this.children = [];
    };

    static createInstance(id: string, children: TreeItem[], prio: number = 0) {
        let i = new TreeItem(id);
        i.children = children;
        i.prio = prio || 0;
        return i;
    }

    static ensure(source:TreeItem) : TreeItem{
        let aw =  Object.assign(new TreeItem(source.id), source);
        aw.children = [];
        source.children.map((i)=>{
            aw.children.push(TreeItem.ensure(i));
        });

        return aw;
    };

    getChildIndex(id: string) : number {
        for(let i:number = 0;i < this.children.length; i++) {
            if(this.children[i].id == id) {
                return i;
            };
        };

        return -1;
    };

    //**will remove all children */
    reset() {
        this.children = [];
    };

    //**will remove all children */
    resetChildren() {
        this.children.map((i)=>{
            i.reset();
        });
        this.children = [];
    };

    find(id: string) : TreeItem | undefined{
        return linq.from<TreeItem>(this.children).firstOrDefault(x=>x.id == id);
    };

    static findChild(parent: TreeItem, id: string) {
        
        let idx = parent.getChildrenIds().indexOf(id);
        
        if(idx>=0) {
            return parent.children[idx];
            
        } else if (!parent.children || parent.children.length == 0) {
            
            return undefined;
        } else {
            for(let i: number = 0; i < parent.children.length; i++) {
                
                let el = TreeItem.findChild(parent.children[i], id);

                if(el) {
                    
                    return el;
                }
            };

            return undefined;
        };
    };

    add(id: string) {
        let tr = new TreeItem(id);
        this.children.push(tr); 
        return tr;
    };

    hasChild(id: string) : boolean {
        if(this.children) {
            return this.getChildrenIds().indexOf(id) >= 0;
        };

        return false;
    };

    addChildren(items: TreeItem[]) {
        items.map((i)=>{
            this.children.push(i);
        });
    };

    remove(id: string) {
        for(let i=0; i < this.children.length; i++) {
            if(this.children[i].id == id) {
                if(i == 0) {
                    this.children.shift();
                } else if (i == this.children.length - 1) {
                    this.children.pop();
                } else {
                    this.children.splice(i, 1);
                }
                break;
            }
        };
    };

    addOrRemove(id: string) {
        let el = this.find(id);
        if(el) {
            this.remove(id);
        } else {
            this.add(id);
        }
    };

    findPath(path: string[]) : TreeItem | undefined{
        let e:TreeItem | undefined = this;
        for(let i = 0; i < path.length; i++ ){
            e = e!.find(path[i]);
            if(e) {
                if(i == path.length - 1) {
                    return e;
                }
            } 
        };

        return undefined;
    };

    static addRemovePath(item: TreeItem, path: string[]) {
        if(!item.find) {
            item = TreeItem.ensure(item); 
        }

        let el = item.find(path[0]);

        if(path.length > 1) {
            path.shift();
            TreeItem.addRemovePath(el!, path);
        } else {
            item.addOrRemove(path[0]);
        };
    };

    static addRemoveTo(id: string, item: TreeItem, path: string[]) {
        if(!item.find) {
            item = TreeItem.ensure(item); 
        }

        let el = item.find(id);

        if(el) {
            TreeItem.addRemovePath(el, path);
        }
    };

    getChildrenIds() : string[] {
        return linq.from<TreeItem>(this.children).select(x=>x.id).toArray();
    };

    getChildrenIdSub() : string[] {
        let res: string[] = [];
        this.children.map((i)=>{
            res = res.concat(i.getChildrenIds());
        });

        //console.log('subs', res);

        return res;
    };

    //** will deliver the tree as array */
    toArray(withPath: boolean, id: string | undefined) : string[] {
        let sa:string[] = [];

        if(id) {
            let el = linq.from<TreeItem>(this.children).where(x=>x.id == id).firstOrDefault();

            if(el) {
                this.toArrayInner(sa, withPath, el.id, el.children);
            }
        }
        else {
            this.toArrayInner(sa, withPath, '', undefined);
        }

        return sa;
    };

    private toArrayInner(target: string[], withPath: boolean, path: string, children: TreeItem[] | undefined) {
        let el: TreeItem[] = children ? children : this.children;
        el.map((i)=>{
            let s: string =  withPath && path ? path + '/' + i.id : i.id;
            
            target.push(s);

            if(i.children && i.children.length >0) {
                this.toArrayInner(target, withPath, s, i.children);
            };
        });
    };    
};

export class ProductTree extends TreeItem {
    public cat: string = '';
    public brand: string = '';
    public idx: number = -1;
    familyId: number = -1;
    public children: ProductTree[] = [];

    constructor(id: string | undefined, cat: string) {
        super(id);
        this.cat = cat;
    };

    public getCats() : string[] {
        return linq.from<ProductTree>(this.children).select(x=>x.cat).distinct().toArray();
    };

    addChildren(ch: ProductTree[]) {
        if(!this.children) { this.children = [] };

        ch.map((i)=>{
            this.children.push(i);
        });
    };

    public static FromTree(ti: TreeItem) {
        let r = ti as ProductTree;

        return r;
    };

    find(id: string) : ProductTree | undefined{
        return linq.from<ProductTree>(this.children).firstOrDefault(x=>x.id == id);
    };
};