import * as React from 'react';
import { Action, PopupBody } from '..';
import { any } from 'prop-types';
import GlobalJSX from '../../utils/GlobalJSX';
import { LButton } from '../page';
import { ELButtonType } from '../../models/page/diverse';
import { MainTexts, ErrorMessages } from '../../utils/messages';
import Global from '../../utils/Global';
import ReactDOMServer from 'react-dom/server';

export interface IPopupProps {
    id: string;
    visible: boolean;
    closable: boolean;
    title?: string;
    style?: string;
    children?: JSX.Element[];
    width?: number | string;
    height?: number | string;
    onDismiss?: any;
    onPositive?: any;
    onNegative?: any;
    type: EPopupType;

    textPositive?: string;
    textNegative?: string;
};

export interface IPopupConfig {
    props: IPopupProps;
    action?: JSX.Element;
    content: JSX.Element;
    callback?: any;
    closeOnDone?: boolean;
};

export interface IPopupState {
    visible: boolean;
    closable: boolean;
    type: EPopupType;

    content?: JSX.Element;
};

export enum EPopupType {
    info = 0,
    pleaseWait = 1,
    confirm = 2,
    custom = 3,
    confirmCustom = 4
};

export class Popup extends React.Component<IPopupProps, IPopupState> {
    private hasAction: boolean = false;
    private title: string = '';

    constructor(props: IPopupProps) {
        super(props);

        this.onDismiss = this.onDismiss.bind(this);
        this.confirmNegative = this.confirmNegative.bind(this);
        this.confirmPositive = this.confirmPositive.bind(this);
        this.clickOverlay = this.clickOverlay.bind(this);
        this.refresh = this.refresh.bind(this);

        this.state = {
            visible: this.props.visible,
            type: this.props.type,
            closable: this.props.closable
        };
    };

    public componentWillReceiveProps(next: IPopupProps) {
        this.setState({ visible: next.visible, closable: next.closable, type: next.type });
    };

    public render() {
        if (!this.state.visible) { return <></> };

        let s: string = this.props.id + '-over';
        let cssover: string = 'popup-overlay dn';
        let css: string = 'popup';
        let styles: any = {};
        this.title  = this.props.title ? this.props.title : '';

        if (this.props.type === EPopupType.confirm && !this.title) {
            this.title = 'Bitte besttigen';
        } else if (this.props.type === EPopupType.pleaseWait && !this.title) {
            this.title = MainTexts.pleaseWait;
        }

        if (this.state.visible) {
            cssover = 'popup-overlay';
        }

        let a = this.renderAction();
        
        if (!this.hasAction) {
            css += ' noact';
        };

        if (this.props.width) {
            if (typeof this.props.height === "number") {
                styles['width'] = this.props.width + 'px';
            } else {
                styles['width'] = this.props.width;
            }
        };

        if (this.props.height) {
            if (typeof this.props.height === "number") {
                let h: number = this.props.height === 200 ? 300 : this.props.height;
                styles['height'] = h + 'px';
            } else {
                styles['height'] = this.props.height;
            }
        };

        if (this.props.style) {
            css += ' ' + this.props.style;
        };

        let content = this.renderContent();

        let ctrl = (
            <div id={this.props.id}>
                <div id={s} className={cssover} onClick={this.clickOverlay}></div>
                <div className={css} id={this.props.id} style={styles}>
                    <div className="inner">
                        {
                            this.props.onDismiss && this.state.closable ? <div className="close" onClick={this.onDismiss} /> : null
                        }
                        <div className="title">{this.title}</div>
                        {/* <div className="sep"/> */}
                        {content}
                        {a ?
                            (
                                <div className="action-container">
                                    {/* <div className="sep"/> */}
                                    <div className="popup-action">
                                        {a}
                                    </div>
                                </div>
                            )
                            : null
                        }
                    </div>
                </div>
            </div>
        );

        return ctrl;
    };

    public refresh(visible: boolean,
        closable: boolean,
        type: EPopupType,
        content?: JSX.Element) {
        
        this.setState({
            visible: visible,
            closable: closable,
            type: type,
            content: content
        });
    };

    private renderContent(): JSX.Element {
        let css: string = 'content text-body';
        //console.log('rendercontent', this.props.children)
        //todo: not working in prod, tempfix [0]
        let b = this.props.children ? this.props.children[0] : <div>error</div>; //this.getChild<PopupBody>('cmp.PopupBody');

        if (this.state.type == EPopupType.pleaseWait) {
            css += ' center';
            b = GlobalJSX.GetLoaderWithText("Die Anfrage wird verarbeitet ... ");
        } else if (this.state.type == EPopupType.confirm) {
            css += ' center';
            b = <div>Sind Sie sicher?</div>
        } else if (this.state.type == EPopupType.custom) {
            css += ' center';

            if(this.state.content) {
                b = this.state.content;
            }
        };

        let bs = ReactDOMServer.renderToString(b);
        if(bs.indexOf('answerError') > 0) {
            this.title = ErrorMessages.general;
        };

        let ctrl =
            (
                <div className={css}>
                    {b}
                </div>
            );

        return ctrl;
    };

    private renderAction(): JSX.Element {
        let a = this.props.children && this.props.children.length > 0 ?
            this.props.children[this.props.children.length - 1] : null;

        let ctrl: JSX.Element = <></>;

        this.hasAction = a !== undefined;

        if (this.props.type === EPopupType.confirm || this.props.type === EPopupType.confirmCustom) {
            ctrl = (
                <Action>
                    <LButton text={this.props.textNegative ? this.props.textNegative : MainTexts.cancel}
                        css="fl" icon="cancel" type={ELButtonType.linkWithIconLeft} onClick={this.confirmNegative} />
                    <LButton text={this.props.textPositive ? this.props.textPositive : MainTexts.ok}
                        css="fr" icon="ok" type={ELButtonType.buttonWithIconRight} onClick={this.confirmPositive} />
                </Action>
            );
        } else {
            if (a) {
                ctrl = a;
            }
        }

        return ctrl;
    };

    private clickOverlay() {
        if (this.props.closable) {
            this.setState({ visible: false });
            this.onDismiss();
        };
    };

    private identify<T>(arg: T): T {
        return arg;
    };

    private onDismiss() {
        if (this.props.onDismiss) {
            this.props.onDismiss();
        }
    };

    private confirmPositive() {
        if (this.props.onPositive) {
            this.props.onPositive();
        };
    };

    private confirmNegative() {
        if (this.props.onNegative) {
            this.props.onNegative();
        };
    };
};

export default Popup;