import * as React from 'react';
import { Row, Col, Container } from 'react-bootstrap';
import Global from '../../../utils/Global';
import { EControlType, ETelType } from '../../../models/page/diverse';
import * as page from '../../page/index';
import { MainTexts } from '../../../utils/messages';
import { IBaseComponentState } from '../../../interfaces/index';
import GlobalJSX from '../../../utils/GlobalJSX';
import { IControlValue } from '../../../models/page/interfaces';
import { Tel as TelModel}  from '../../../models/ordering/tel';

export interface ITelProps {
    telTypeHidden?: boolean;
    telModel?: TelModel;
    prefix: string;
    onChange: any;
    source: any;
    preview: boolean; 
    valueChange?: boolean;
    countryCodeVisible?: boolean;
    withPading?: boolean;
    id?: string;
    validationOptions?: any;
    titleTel?: string;
    disablePrefix?: boolean;
};

export class Tel extends React.Component<ITelProps, IBaseComponentState> {
    private tel : TelModel;
    private titleTel: string;

    constructor(props: ITelProps) {
        super(props);

        this.changeValue = this.changeValue.bind(this);

        this.titleTel = this.props.titleTel ?  this.props.titleTel : 'Telefon';

        this.tel = {
            telType: ETelType.mobile,
            prefix: '',
            number: ''
        };

        if(this.props.telModel){
            this.tel = this.props.telModel;
        };

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: ITelProps) {
        if(next.titleTel) {
            this.titleTel = next.titleTel;
        }
        this.setState({ changed: true });
    };

    public render() {
        let type: ETelType = ETelType.home;

        let name: string = this.props.prefix + '.telType';

        type = Global.getValueByPath(this.props.source, name);

        if (!type) {
            type = ETelType.home;
        };

        let countryCodeControl = <></>;

        if (this.props.countryCodeVisible === true) {
            countryCodeControl =
                <Col xs={3} md={3} >
                    {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'countryCode', " ", MainTexts.countryCode, this.props.preview)}
                </Col>
        }

        let ctrl = (<div id={this.props.id}>
            <Container className={this.props.withPading ? '' : 'np'}>
                {
                    this.props.preview || this.props.telTypeHidden === true ? null :
                        <Row>
                            <Col xs={6} md={6} className="np">
                                <page.LControl 
                                    type={EControlType.radio}
                                    onChange={this.props.onChange} 
                                    name={name}
                                    id={this.props.prefix  + ".telType-1"}
                                    //id={this.props.prefix.replace('.', '') + "telType-1"}
                                    value={ETelType.mobile}
                                    checked={type == ETelType.mobile}
                                    label="Mobil" 
                                    title={this.titleTel} />
                            </Col>
                            <Col xs={6} md={6}>
                                <page.LControl 
                                    type={EControlType.radio}
                                    onChange={this.props.onChange} 
                                    name={name}
                                    id={this.props.prefix  + ".telType-2"}
                                    //id={this.props.prefix.replace('.', '') + "telType-2"}
                                    checked={type == ETelType.home}
                                    value={ETelType.home} 
                                    label="Festnetz" />
                            </Col>
                        </Row>
                }

                <Row>
                    {countryCodeControl}

                    <Col xs={3} md={3} className="np">
                        {
                            GlobalJSX.renderInner(this.props.prefix, 
                                this.props.source, 
                                this.props.valueChange ? (e)=>{this.tel.prefix = e.value;
                                    this.changeValue(); } : this.props.onChange, 
                                'prefix' , " ", MainTexts.telPrefix, this.props.preview, this.tel.prefix, this.props.disablePrefix)

                               
                        }
                    </Col>
                    <Col xs={9} md={9}>
                        {
                            GlobalJSX.renderInner(this.props.prefix, 
                                this.props.source, 
                                this.props.valueChange ? (e)=>{this.tel.number = e.value; this.changeValue(); } : this.props.onChange, 
                                'number', " ", MainTexts.telnummer, this.props.preview, this.tel.number)
                        }
                    </Col>
                </Row>

            </Container>
            <page.LClear />
        </div>
        );

        return ctrl;
    };

    public getValue() {
        return {
            type: EControlType.tel,
            name: this.props.id ?  this.props.id : '',
            id: this.props.id ?  this.props.id : '',
            value: this.tel.prefix + '-' + this.tel.number,
             
        };        

    };

    private changeValue() {
        let val = this.getValue();

        this.props.onChange(val);
    };
};

export default Tel;