import * as React from 'react';
import { IDefCon, EAppState } from '../../models';
import StorageHelper from '../../utils/storage';
import Global from '../../utils/Global';
import App from '../../App';

export class DefCon extends React.Component<any, any> {
    private model: IDefCon = new IDefCon();// {state: EAppState.undefined, brands: []};

    constructor(props) {
        super(props);

        this.state = {
        };
    }; 

    public render() {
        this.model = Global.easyApp.defcon;
        
        if(!this.model || (this.model.state & EAppState.warningVisible) !== EAppState.warningVisible) {
            return <></>
        };

        let block = (this.model.state & EAppState.blockScreen) === EAppState.blockScreen;

        let ctrl = (
            <>
            <div className="defcon-container">
                <div className="defcon">
                    <div dangerouslySetInnerHTML={{__html: this.model.notification ? this.model.notification : ''}}/>
                </div>
            </div>
            {
                block ?
                <div className="defcon-blocked"></div> 
                : <></>
            }
            </>
        );

        return ctrl;
    };
};

export default DefCon;