import * as linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { EPopupType } from '../../components/dialogs/popup';
import * as cmp from '../../components/index';
import { LControl } from '../../components/index';
import BaseBlock from '../../components/page/block';
import * as page from '../../components/page/index';
import { IApiResponse } from '../../interfaces';
import IAppDefinition from '../../interfaces/app/IAppDefinition';
import { EControlType, ELButtonType, ERequestType } from '../../models';
import { UserRole } from '../../models/am/userRole';
import { IBlockConfiguration } from '../../models/block/configuration';
import { IControlValue } from '../../models/page/interfaces';
import { Roles } from '../../utils/Defaults';
import Global from '../../utils/Global';
import { MainTexts } from '../../utils/messages';
import StorageHelper from '../../utils/storage';

export interface BaseStruct {
    id: string,
    text: string;
    value: string;
    selected: boolean;
}

export class UserData {
    constructor() {
        this.id = '';
        this.firstname = '';
        this.surname = '';
        this.email = '';
        this.mobilePhone = '';
        this.birthDate = undefined;
        this.voId = '';
        this.portalStatus = '';

        this.salutationId = 0;
        this.positionId = 0;
        this.newsletterId = 0;

        this.salutations = [];
        this.roles = [];
        this.positions = [];
        this.newsletters = [];
        this.portalStatuses = [];
    }

    id: string;
    firstname: string;
    surname: string;
    email: string;
    mobilePhone: string;
    birthDate?: Date;
    voId: string;
    portalStatus: string;

    salutationId: number;
    positionId: number;
    newsletterId: number;

    salutations: Array<BaseStruct>;
    roles: Array<UserRole>;
    positions: Array<BaseStruct>;
    newsletters: Array<BaseStruct>;
    portalStatuses: Array<BaseStruct>;
}

enum UserActivationStatus {
    Unknown,
    Active,
    InActive
}

export interface IUserScreenProps {
    contactId: number;
}

export interface IUserScreenState {
    contactId: number;
    popupTitle: string;
    processing: boolean;
    closblePopup: boolean;
    apiMessage: string;
    model: UserData;
    cancelRequest: boolean;
    userActivationStatus: UserActivationStatus;
}

export class EditUserScreen extends React.Component<IUserScreenProps, IUserScreenState> {
    public static containerId: string = 'editUserScreen';
    private processPopupState: EPopupType = EPopupType.pleaseWait;

    readonly globalCfg: IAppDefinition = Global.getConfiguration();
    readonly cfg: IBlockConfiguration = {
        title: MainTexts.edit,
        transparent: false,
        styleContainer: ''
    };
    readonly cfgConfiguration: IBlockConfiguration = {
        title: MainTexts.configuration,
        transparent: false,
        styleContainer: ''
    };
    readonly cfgRoles: IBlockConfiguration = {
        title: MainTexts.roles,
        transparent: false,
        styleContainer: ''
    };

    constructor(props) {
        super(props);

        this.onDismissProcessPopup = this.onDismissProcessPopup.bind(this);
        this._onChange = this._onChange.bind(this);
        this._onSave = this._onSave.bind(this);
        this.loadUserCallback = this.loadUserCallback.bind(this);
        this.saveUserCallback = this.saveUserCallback.bind(this);

        this._activateUser = this._activateUser.bind(this);
        this._deactivateUser = this._deactivateUser.bind(this);
        this._reload = this._reload.bind(this);

        this.state = { contactId: this.props.contactId, processing: true, closblePopup: false, apiMessage: '', popupTitle: '', model: new UserData(), cancelRequest: false, userActivationStatus: UserActivationStatus.Unknown };

        Global.hub.send(JSON.stringify({ id: this.state.contactId }),
            ERequestType.loadContact,
            undefined);
    }

    render() {
        const { model } = this.state;
        let user = StorageHelper.getUser();

        

     
        let activationDeactivation = <></>;

        if (Global.checkAccessRoles([Roles.XTND_ShopManager,Roles.XTND_Nutzerverwaltung]) && this.state.contactId !== 0 && model !== undefined) { // we show activate or deactivate button
            if (this.state.userActivationStatus === UserActivationStatus.Active) {
                activationDeactivation = <Col xs={4} md={4} >
                    {/* <page.LButton
                                        text={MainTexts.save}
                                        onClick={this._onSave}
                                        type={ELButtonType.buttonWithIconRight}
                                        css={"action fr"}
                                        icon="chevron" /> */}
                    <page.LButton
                        text={MainTexts.deactivate}
                        onClick={() => this._deactivateUser(this.state.contactId)}
                        type={ELButtonType.buttonWithIconRight}
                        css={"action fr"} />
                </Col>;
            } else if (this.state.userActivationStatus === UserActivationStatus.InActive) {
                activationDeactivation = <Col xs={4} md={4} >
                    <page.LButton
                        text={MainTexts.activate}
                        onClick={() => this._activateUser(this.state.contactId)}
                        type={ELButtonType.buttonWithIconRight}
                        css={"action fr"} />
                </Col>;
            }
        }

        return <div id={EditUserScreen.containerId}>
            <page.Inner1C>
                <div>
                    <cmp.Popup
                        type={this.processPopupState}
                        visible={this.state.processing || this.state.closblePopup}
                        closable={this.state.closblePopup}
                        id="wait"
                        title={this.state.popupTitle}
                        height={200}
                        onDismiss={this.onDismissProcessPopup}>
                        <cmp.PopupBody>
                            <div>{this.state.apiMessage}</div>
                            <></>
                        </cmp.PopupBody>
                        <></>
                    </cmp.Popup>
                    <BaseBlock config={this.cfg}>
                        <Container className="container-action">
                            <Row>
                                <Col xs={6} md={6}>
                                    <LControl type={EControlType.dropdown}
                                        value={model.salutationId}
                                        onChange={this._onChange}
                                        id='salutationId'
                                        name='salutationId'
                                        title={MainTexts.salutation}
                                        options={this.structToKeyTexts(model.salutations)} />
                                </Col>
                                <Col xs={6} md={6}>
                                    <LControl type={EControlType.text}
                                        value={model.email}
                                        onChange={this._onChange}
                                        id='email'
                                        name='email'
                                        title={MainTexts.email} />
                                </Col>
                            </Row>
                            <Row>
                                <Col xs={6} md={6}>
                                    <LControl type={EControlType.text}
                                        value={model.firstname}
                                        onChange={this._onChange}
                                        id='firstname'
                                        name='firstname'
                                        title={MainTexts.firstname} />
                                </Col>
                                <Col xs={6} md={6}>
                                    <LControl type={EControlType.text}
                                        value={model.mobilePhone}
                                        onChange={this._onChange}
                                        id='mobilePhone'
                                        name='mobilePhone'
                                        title={MainTexts.mobilePhoneNumber} />
                                </Col>
                            </Row>
                            <Row>
                                <Col xs={6} md={6}>
                                    <LControl type={EControlType.text}
                                        value={model.surname}
                                        onChange={this._onChange}
                                        id='surname'
                                        name='surname'
                                        title={MainTexts.surname} />
                                </Col>
                                <Col xs={6} md={6}>
                                    <page.LDate onChange={this._onChange}
                                        datePicker={false}
                                        value={model.birthDate}
                                        name="birthDate" id="birthDate" title={MainTexts.dateOfBirth} />
                                </Col>
                            </Row>
                        </Container>
                    </BaseBlock>

                    <BaseBlock config={this.cfgConfiguration}>
                        <Container className="container-action">
                            <Row>
                                <Col xs={6} md={6}>
                                    <LControl type={EControlType.text}
                                        value={model.voId}
                                        id='void'
                                        title={'VOID'}
                                        preview={true} />
                                </Col>
                                <Col xs={6} md={6}>
                                    <LControl type={EControlType.dropdown}
                                        value={model.positionId}
                                        onChange={this._onChange}
                                        id='positionId'
                                        name='positionId'
                                        title='Position'
                                        options={this.structToKeyTexts(model.positions)} />
                                </Col>
                            </Row>
                            <Row>
                                <Col xs={6} md={6}>
                                    <LControl type={EControlType.text}
                                        value={model.portalStatus}
                                        id='portalStatus'
                                        name='portalStatus'
                                        title='Portal Status'
                                        preview={true} />
                                </Col>
                                <Col xs={6} md={6}>
                                    <LControl type={EControlType.dropdown}
                                        value={model.newsletterId}
                                        onChange={this._onChange}
                                        id='newsletterId'
                                        name='newsletterId'
                                        title='Newsletter'
                                        options={this.structToKeyTexts(model.newsletters)} />
                                </Col>
                            </Row>
                        </Container>
                    </BaseBlock>

                    <BaseBlock config={this.cfgRoles}>
                        <Container className="container-action">
                            <Row>
                                <Col xs={12} md={12}>
                                    {model.roles.map((r, i) => {
                                        if (r.isVisible) {
                                            //please do not change
                                            if (r.name === Roles.Stornofreigabe && user && user.accountLevel === 2) {
                                                return <></>;
                                            } else {

                                                return <LControl type={EControlType.checkbox}
                                                    value={r.id}
                                                    checked={r.selected ? true : false}
                                                    onChange={this._onChange}
                                                    id={'roles-' + i}
                                                    name={'roles[' + i + '].selected'}
                                                    key={'roles-' + i}
                                                    disabled={!r.isSelectable}
                                                    label={r.text} />;
                                            }
                                        }
                                    })}
                                </Col>
                            </Row>
                            <Row>
                                <Col xs={4} md={4} >
                                    <page.LButton
                                        text={MainTexts.cancel}
                                        onClick={() => window.location.href = '/am/administration'}
                                        type={ELButtonType.linkSimple}
                                        css={"fl"}
                                        icon="chevron" />
                                </Col>
                                {activationDeactivation}
                                <Col xs={4} md={4} >
                                    <page.LButton
                                        text={MainTexts.save}
                                        onClick={this._onSave}
                                        type={ELButtonType.buttonWithIconRight}
                                        css={"action fr"} />
                                </Col>
                            </Row>
                        </Container>
                    </BaseBlock>
                </div>
            </page.Inner1C>
        </div>;
    }

    loadUserCallback(api: IApiResponse) {
        let model = api.data.data as UserData;

        let actStatus = UserActivationStatus.Unknown;

        if (model.portalStatus === 'Active') {
            actStatus = UserActivationStatus.Active;
        } else if (model.portalStatus === 'Inactive') {
            actStatus = UserActivationStatus.InActive;
        }

        this.setState({ model, processing: false, userActivationStatus: actStatus });
    }
    saveUserCallback(api: IApiResponse) {
        if (!this.state.cancelRequest) {
            this.processPopupState = EPopupType.info;

            if (api.error) {
                // this.showPopup(EPopupType.info, MainTexts.textError, api.error.message, false);
                this.setState({
                    apiMessage: api.error.message,
                    closblePopup: true,
                    processing: false,
                    popupTitle: MainTexts.textError,
                    userActivationStatus: UserActivationStatus.Unknown
                });
            } else {
                const cntId = api.data.data;

                this._reload(cntId);
            }
        }
    }

    activateUserCallback(api: IApiResponse) {
        this._reload(this.state.contactId);
    }
    deactivateUserCallback(api: IApiResponse) {
        this._reload(this.state.contactId);
    }

    _activateUser = (contactId: number) => {
        this.setState({
            processing: true, closblePopup: false
        });

        Global.hub.send(JSON.stringify({ id: this.state.contactId }),
            ERequestType.activateUser,
            undefined);
    }
    _deactivateUser = (contactId: number) => {
        this.setState({
            processing: true, closblePopup: false
        });

        Global.hub.send(JSON.stringify({ id: this.state.contactId }),
            ERequestType.deactivateUser,
            undefined);
    }

    _reload = (contactId: number) => {
        // after save reload so we can obtain the portal status of the user
        this.setState({
            contactId: contactId, processing: true, closblePopup: false, apiMessage: '', popupTitle: '', model: new UserData(), cancelRequest: false, userActivationStatus: UserActivationStatus.Unknown
        });

        Global.hub.send(JSON.stringify({ id: contactId }),
            ERequestType.loadContact,
            undefined);
    }

    private _onChange(e: IControlValue) {

        let model = this.state.model;

        let path = e.name;

        if (path && path[0] !== '[') {
            path = '.' + path;
        }

        let val = e.value;

        if (e.type === EControlType.date) {
            val = e.dateValue;
        } else if (e.type === EControlType.checkbox) {
            val = e.checked;
        }

        eval("model" + path + '=val');

        this.setState({ model });
    }

    private structToKeyTexts(struct: BaseStruct[]) {
        return linq.from<BaseStruct>(struct)
            .select((x, i) => {
                return { key: x.id, text: x.text };
            })
            .toArray();
    }

    private onDismissProcessPopup() {
        this.processPopupState = EPopupType.pleaseWait
        this.setState({ processing: false, closblePopup: false, cancelRequest: true, apiMessage: '', popupTitle: '' });
    }

    private _onSave() {
        const { model } = this.state;

        this.setState({ processing: true, closblePopup: true, cancelRequest: false });

        model.firstname = model.firstname.trim();
        model.surname = model.surname.trim();

        Global.hub.send(JSON.stringify(model),
            ERequestType.saveContact,
            undefined);
    }
}