import * as React from 'react';
import BaseBlock from '../../page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import ProcessNewScreen from '../../../screens/ordering/process';
import { Row, Col, Container } from 'react-bootstrap';
import * as page from '../../page/index';
import * as m from '../../../models/index';
import { MainTexts, ValidationMessages } from '../../../utils/messages';
import { ECreditDebitType, EActivationType } from '../../../models/ordering/order';
import { BrandKeys } from '../../../utils/Defaults';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import Tel from '../global/tel';
import { Tel as TelModel } from '../../../models/ordering/tel';
import { IControlValue, IKeyTextNumber, IKeyValue } from '../../../models/page/interfaces';
import Global from '../../../utils/Global';
import StorageHelper from '../../../utils/storage';
import * as linq from 'linq';
import { IAvailabilityBrand } from '../../../models/ordering/availability';

export interface IActivationTelProps {
    parent: ProcessNewScreen;
    onChange: any;
    numbersToPort: number;
};

export interface IActivationTelState {
    changed: boolean;
    numbersToPort: number;
};

export class ActivationTel extends React.Component<IActivationTelProps, IActivationTelState> {
    private tel: TelModel = new TelModel();
    private msisdnArray:string[] = [];
    private brandKey: string = '';

    constructor(props: IActivationTelProps) {
        super(props);

        this.onChange = this.onChange.bind(this);

        let avl = StorageHelper.getAvailability();
        this.tel = new TelModel();

        this.brandKey = this.props.parent.current ? this.props.parent.current.brandKey : '';

        if (avl && avl.brands) {
            let br = linq.from<IAvailabilityBrand>(avl.brands).where(x => x.key === BrandKeys.dsl).firstOrDefault();
            if (br && br.products && br.products.length > 0) {

                let prod = br.products[0];
                this.tel.prefix = prod.areaCode;
            };
        };

        this.state = {
            changed: false,
            numbersToPort: this.props.numbersToPort
        };
    };

    public componentWillReceiveProps(next: IActivationTelProps) {
        this.setState({numbersToPort: next.numbersToPort});
    };

    public render() {
        if(this.props.parent.current){
            this.msisdnArray =  this.props.parent.current.msisdn.split(";");
        } else {
            this.msisdnArray = new Array(this.state.numbersToPort);
        }

        if(!this.msisdnArray) {
            this.msisdnArray = [];
        };

        let businessType: ECreditDebitType = this.props.parent.selection ? this.props.parent.selection.creditDebitType : ECreditDebitType.postpaid;

        let cfg: IBlockConfiguration = {
            title: MainTexts.numberToBePorted,
            transparent: false
        };

        let act: m.IActivation = this.props.parent.current ? this.props.parent.current : new m.IActivation(this.props.parent.selectedBrand);

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container id="allNumbersToPort">
                    {this.howManyElToDisplay()}
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };

    private howManyElToDisplay(): JSX.Element[] {
        let elements: any = [];
        
        for (let i = 0; i < this.state.numbersToPort; i++) {
            let temp = (i >= this.msisdnArray.length ? this.tel.prefix + '-' : this.msisdnArray[i]).split("-");
            let tel:TelModel = {
                telType: m.ETelType.mobile,
                prefix: temp[0] ? temp[0] : this.tel.prefix,
                number: temp[1]
            };

            let pr = 'tel-' + i;
            let ctrl = (
                <Row>
                    {
                        this.brandKey == BrandKeys.dsl || this.brandKey == BrandKeys.kabel ? 
                        <div>
                            <page.LControl 
                                    type={m.EControlType.info}
                                    name={name}
                                    id={"tel-prefix-" + i }
                                    value={"+49"}
                                    label="+49" 
                                    style="ccode"/>
                        </div>                        
                        : null
                    }

                    <Col xs={6} md={6} >
                        <Tel 
                            source={this.tel}
                            telModel = {tel}
                            id={"msisdn-" + i.toString()}
                            withPading={true}
                            telTypeHidden={true}
                            onChange={this.onChange}
                            valueChange={true}
                            prefix={pr}
                            preview={false}
                            disablePrefix={this.brandKey == BrandKeys.kabel || this.brandKey == BrandKeys.dsl ? true : false}/>
                    </Col>
                </Row>
            );
            elements.push(ctrl);
        }

        return elements;
    }

    private onChange(e: IControlValue) {
        let sa = e.id.split('-');
        let id = 'msisdn-' + sa[1];
        let msisdn = Global.GetComponent<Tel>(id);
       
        this.msisdnArray[sa[1]] = msisdn.getValue().value;

        if (this.props.parent.current) {
            this.props.parent.current.msisdn = this.msisdnArray.join(";");
            if (this.props.parent.data.customer.mnp) {
                this.props.parent.data.customer.mnp.countNumbers = this.state.numbersToPort;
                this.props.parent.data.customer.mnp.numberPortingType = this.state.numbersToPort === 1 ? 1 : 2;

            }
            StorageHelper.addOrder(this.props.parent.data);
        }

        this.setState({ changed: true });
    };
};

export default ActivationTel;