import linq from 'linq';
import { IActivation } from '..';
import IProduct, { ISubConfig, ISubServiceConfig } from '../../models/ordering/product';
import StorageHelper from '../../utils/storage';
import IBrand from './brand';
import { EBusinessCase, ECreditDebitType } from './order';

export class ProductWrapper {
    private prod: IProduct;
    private brand: IBrand;
    constructor(brandKey: string, valProduct: IProduct) {
        this.prod = valProduct;
        this.brand = linq.from<IBrand>(StorageHelper.getBrands())
            .where(x => x.id == brandKey)
            .first();
    };

    public getServices(act: IActivation | undefined,
        bc: EBusinessCase,
        withMandatory: boolean): string[] {
        let svc: string[] = [];

        let sub: string = act ? act.sub : '';
        
        if (act && act.creditDebitType === ECreditDebitType.prepaid) {
            return this.prod.services;
        };

        if (!this.prod) return [];
     
        let t = linq.from<ISubConfig>(this.prod.subs)
            .where(x => x.id == sub && x.b == bc)
            .firstOrDefault();
       
        if (t) {
            if (withMandatory) {
               
                svc = linq.from<ISubServiceConfig>(t.sl)
                   
                    .select(x => x.id)
                    .toArray();
            } else {
                svc = linq.from<ISubServiceConfig>(t.sl)
                    .where(x => !x.m)
                    .select(x => x.id)
                    .toArray();
            }
        }

        return svc;
    };

    public getServicesAndMandatory(sub: string, bc: EBusinessCase): string[] {
        let svc: string[] = [];

        let t = linq.from<ISubConfig>(this.prod.subs)
            .where(x => x.id == sub && x.b == bc)
            .firstOrDefault();

        if (t) {
            svc = linq.from<ISubServiceConfig>(t.sl)
                .select(x => x.id)
                .toArray();
        }

        return svc;
    };

    public getAllServicesFromConfig(sub: string, bc: EBusinessCase): string[] {
        let svc: string[] = [];

        let t = linq.from<ISubConfig>(this.prod.subs)
            .where(x => x.id == sub && x.b == bc)
            .firstOrDefault();

        svc = linq.from<ISubServiceConfig>(t!.sl)
            .select(x => x.id)
            .toArray();

        return svc;
    };
};

export default ProductWrapper;