import * as React from 'react';
import Global from '../../utils/Global';
import { MainTexts } from '../../utils/messages';
import { IBlockConfiguration } from '../../models/block/configuration';
import BaseBlock from '../../components/page/block';
import { Row, Col, Container } from 'react-bootstrap';
import { LClear, LControl, LButton } from '../../components';
import { EControlType, ELButtonType } from '../../models';
import { IControlValue } from '../../models/page/interfaces';
import StorageHelper from '../../utils/storage';
import IUser from '../../models/environment/user';
import dateformat from 'dateformat';
import md5 from 'md5';
import Api from '../../utils/api';
import { IApiResponse } from '../../interfaces';

export interface IIDScannerScreenProps {};

export interface IIDScannerScreenState {
    changed: boolean;
    run: number;
    visible: boolean;
};

export interface IIDScannerModel {
    msisdn: string;
    url: string;
};

export class IDScannerScreen extends React.Component<IIDScannerScreenProps, IIDScannerScreenState> {
    public static containerId: string = 'idscanner';

    private model: IIDScannerModel = {msisdn:'', url:''};
    private msisdn: string = '';

    constructor(props:IIDScannerScreenProps) {
        super(props);
        this.state = {
            changed: false, run: 0, visible: false
        };
        Global.easyApp.setTitle(MainTexts.documentScanner);
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: undefined,
            transparent: false,
            styleContainer: 'withicon',
            teaser: <div className="block-icon"><img className="fit"  /></div>
        };

        let ctrl = (
            <>
            <Container id={'container-main'} className={'c1 container '}>
                <Row>
                    <Col md={12}>
                    <BaseBlock config={cfg} key="b1">
                        <Container key="c1">
                            <Row>
                                <Col xs={4} md={4}>
                                    <LControl type={EControlType.text} title="MSISDN" label="MSISDN" 
                                        hint="Bitte geben Sie die MSISDN fr den Scannerdokument ein."
                                        onChange={(e:IControlValue)=>{
                                            this.msisdn = e.value;
                                            this.setState({changed: true, visible: false});
                                        }}/>
                                </Col>
                                <Col xs={8} md={8}>

                                </Col>                        
                            </Row>      
                            <Row>
                                <Col>
                                    <LButton text={MainTexts.start}
                                            css="fl"
                                            onClick={() => { 
                                                Api.fetch('environment/docscan/?msisdn=' + this.msisdn, null, 'GET', (api:IApiResponse)=>{
                                                    this.model = api.data;
                                                    this.setState({visible: true });
                                                });
                                            }}
                                            type={ELButtonType.buttonWithIconRight}
                                            icon="arrow-right" />
                                </Col>    
                            </Row>                          
                        </Container>
                        <LClear />
                        <Container className="items-container"  key="c2">
                            <Row>
                                                
                            </Row>
                        </Container>
                    </BaseBlock>
                    </Col>
                </Row>
                <Row>
                    <Col>
                    {this.renderScanner()}
                    </Col>
                </Row>
            </Container>
            
            </>
        );

        return ctrl;
    };

    private renderScanner() {
        let cfg: IBlockConfiguration = {
            title: undefined,
            transparent: false
        };
        let user = StorageHelper.getUser();
        
        if(!user || !this.state.visible) { return <></>}


        let ctrl = (
            <>
                <BaseBlock config={cfg} key="b1">
                    <Container key="c2">
                        <Row>
                            <Col xs={12} md={12}>
                                <iframe src={this.model.url} className="scanner"/>
                            </Col>                   
                        </Row>                          
                    </Container>
                    <LClear />
                </BaseBlock>
            </>
        );

        return ctrl;
    };
};

export default IDScannerScreen;