import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBlockConfiguration } from '../../../models/block/configuration';
import * as m from '../../../models/index';
import { EActivationType, ECreditDebitType } from '../../../models/ordering/order';
import ProcessNewScreen from '../../../screens/ordering/process';
import { ValidationWrapper } from '../../../utils';
import { MainTexts, ValidationMessages } from '../../../utils/messages';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import BaseBlock from '../../page/block';
import * as page from '../../page/index';

export interface IActivationSimProps {
    parent: ProcessNewScreen;
    onChange: any;
};

export interface IActivationSimState {
    sim: string;
    businessType: ECreditDebitType;
};

export class ActivationSim extends React.Component<IActivationSimProps,IActivationSimState> {
    constructor(props: IActivationSimProps) {
        super(props);
        
        this.state = {
            sim: '', 
            businessType: ECreditDebitType.postpaid
        };
    };

    public componentWillReceiveProps(next: IActivationSimProps) {
        if(next.parent.selection) {
            this.setState({businessType: next.parent.selection.creditDebitType});
        };
    };    

    public render() {
        let cfg: IBlockConfiguration = {
            title: MainTexts.textSIMCards,
            transparent: false
        };

        if(this.props.parent.selection.activationType === EActivationType.portDefault) {
            cfg.title = MainTexts.numberToBePorted;
        } else if(this.props.parent.selection.activationType === EActivationType.cdChange) {
            cfg.title = MainTexts.currentPrepaidNumber;
        };

        let act: m.IActivation = this.props.parent.current ? this.props.parent.current : new m.IActivation(this.props.parent.selectedBrand);
        let lbl = this.props.parent.selection.activationType == EActivationType.actDefault || this.props.parent.selection.activationType == EActivationType.partnerCard ? 
            MainTexts.vfserial : MainTexts.msisdn;
        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={6} md={6}>
                            <Row>
                                <Col xs={12} md={12}>
                                    {
                                        <page.LControl 
                                            preview={!this.props.parent.dataWrapper.canEditActivation(this.props.parent.currentIndex)}
                                            error={ValidationWrapper.getErrorFromState(this.props.parent.modelState, 'msisdn')} 
                                            maxLength={lbl==MainTexts.vfserial? 14 : undefined}
                                            type={m.EControlType.text} label={lbl} id="msisdn" name="msisdn" 
                                            onChange={this.props.onChange}
                                            value={act.msisdn}
                                            validationOptions={ 
                                                [
                                                    new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                                    new ValidationOption({type: EValidationOption.numeric, message: ValidationMessages.errorNumeric })
                                                ]
                                            }/>
                                    }
                                </Col>                           
                            </Row>
                        </Col>
                        {
                            act.activationType === EActivationType.actDefault ?
                            <Col xs={6} md={6}><img className="fit" src={"/images/act-" + act.brandKey + ".jpg"} /></Col> 
                            : null
                        }
                    </Row>                                
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };
};

export default ActivationSim;