import * as React from 'react';
import * as act from '../../../src/components/ordering/process/index';
import BrandsList from '../../components/ordering/brandList';
import { EBrandThumbType } from '../../components/ordering/brandThumb';
import Inner1C from '../../components/page/inner1';
import * as m from '../../models/index';
import Global from '../../utils/Global';
import StorageHelper from '../../utils/storage';
import { GigacombiBookOptions, GiggacombiBanners } from './advertisement';

export class WelcomeScreen extends React.Component {
    public componentId: string = 'welcomescreen'
    constructor(props: any) {
        super(props);

        this.refresh = this.refresh.bind(this);

        Global.easyApp.setTitle("");

        //Api.init();
    };

    public refresh() {
        this.setState({ changed: true });
    };

    public render() {
        let brands = StorageHelper.getBrands();
        let user = StorageHelper.getUser();
        let dc = StorageHelper.getDefCon();

        let ctrl = (
            <>
                <Inner1C style="tiny">
                    <>

                        {user ?
                            <div className="inner">
                                <h1>Guten Tag {user.firstname} {user.surname}</h1>
                            </div>
                            : null}

                           
                        {
                            StorageHelper.needAvailability(dc) ?
                                <act.ActivationAvailability
                                    status={m.EComponentState.visible}
                                    parent={this} />
                                : null
                        }
                    
                        {StorageHelper.needAvailability(dc) ? <GiggacombiBanners bookOption={GigacombiBookOptions.undefined} parent={this} advertisementBanner={true} /> : null}

                        <BrandsList
                            selected={'vf'}
                            thumbSelectable={true}
                            needSelection={true}
                            thumbType={EBrandThumbType.blister}
                            withTariffs={true}
                            block={{ title: 'Whlen Sie eine Marke aus', transparent: true }} />
                    </>
                    <div>empty</div>

                </Inner1C>
            </>
        );

        return ctrl;
    };
};

export default WelcomeScreen;