import linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import * as page from '../../../src/components/page/index';
import { EPopupType, IPopupConfig } from '../../components/dialogs/popup';
import ExistingCustomerActivities from '../../components/existing/data/activities';
import BelatedPorting from '../../components/existing/data/Belatedporting';
import ExistingCustomerBills from '../../components/existing/data/bills';
import Cancellation from '../../components/existing/data/Cancellation';
import ExistingCustomerContract from '../../components/existing/data/contract';
import ExistingContractData from '../../components/existing/data/contractData';
import ExistingCustomerCrossSelling from '../../components/existing/data/crossSelling';
import ExistingCustomerData from '../../components/existing/data/customerData';
import ExistingCustomerPayment from '../../components/existing/data/customerPayment';
import ExistingCustomerNba from '../../components/existing/data/nba';
import ProtectionAndConsent from '../../components/existing/data/protectionAndConsent';
import ExistingCustomerServices from '../../components/existing/data/services';
import SimReplacement from '../../components/existing/data/simReplacement';
import * as cmp from '../../components/index';
import { CustomerConsent } from '../../components/ordering/customer';
import PreviewContract from '../../components/ordering/preview/contract';
import { ActivationHardware, ActivationProgress } from '../../components/ordering/process';
import TariffConfiguration from '../../components/process/tariffConfiguration';
import { IApiSummary, ISocketPushModel } from '../../interfaces';
import * as i from '../../interfaces/index';
import IProcessNewSelection from '../../interfaces/ordering/IProcessNewSelection';
import * as m from '../../models/index';
import { EFormMode, IOrderData, OrderDataWrapper } from '../../models/index';
import { ActivationWrapper } from '../../models/ordering/activation';
import IBrand from '../../models/ordering/brand';
import { EActivationType, EBusinessCase, ECreditDebitType, ECustomerType, EOrderProcessView } from '../../models/ordering/order';
import { IControlValue } from '../../models/page/interfaces';
import Api from '../../utils/api';
import { BrandKeys } from '../../utils/Defaults';
import Global from '../../utils/Global';
import GlobalJSX from '../../utils/GlobalJSX';
import * as u from '../../utils/index';
import { ActionTexts, MainTexts, OrderStages, ValidationMessages } from '../../utils/messages';
import EasyRouter from '../../utils/router';
import StorageHelper from '../../utils/storage';
import ImeiScreen, { ImeiUploadType } from '../am/imei';
import ErrorScreen, { IErrorScreenData } from '../global/error';

export interface ICustomerScreenProps {
    id: string;
    dataMode: EExistingCustomerMode;
};

export enum EExistingCustomerMode {
    main = 0,
    sim = 1,
    imei = 2,
    belatedPorting = 3,
    cancellation = 4
};

export interface ICustomerScreenState {
    changed: boolean;
    processing: boolean;
    loading: boolean;
    closablePopup: boolean;
    apiMessage: string;
    clear: boolean;
    dataMode: EExistingCustomerMode;
    showConsentPopUp: boolean;
};

export class CustomerScreen extends React.Component<ICustomerScreenProps, ICustomerScreenState> {
    public static containerId = 'existingCustomer';
    public static uri: string = '/customer/dashboard/';
    public modelState: u.ModelStete = new u.ModelStete();
    public data: IOrderData;
    public dataWrapper: OrderDataWrapper;
    public brand: IBrand;
    private processId: string = '';
    private parentId: string = '';
    private progress: EOrderProcessView = EOrderProcessView.tariff;
    private lastApi?: i.IApiResponse;
    private processPopupState: EPopupType = EPopupType.pleaseWait;
    private dynamicPopup: IPopupConfig | undefined;

    constructor(props: ICustomerScreenProps) {
        super(props);

        this.state = {
            changed: false,
            processing: false,
            loading: false,
            closablePopup: false,
            apiMessage: '',
            clear: false,
            dataMode: props.dataMode,
            showConsentPopUp: false
        };

        this.onConfigurationChanged = this.onConfigurationChanged.bind(this);
        this.onChangeHardware = this.onChangeHardware.bind(this);
        this.onNext = this.onNext.bind(this);
        this.onBack = this.onBack.bind(this);
        this.refresh = this.refresh.bind(this);
        this.onBackToCustomer = this.onBackToCustomer.bind(this);
        this.onDismissProcessPopup = this.onDismissProcessPopup.bind(this);
        this.onDismissValidation = this.onDismissValidation.bind(this);
        this._imeiCancel = this._imeiCancel.bind(this);
        this.openImei = this.openImei.bind(this);

        this.data = StorageHelper.getOrder(this.props.id);
        //StorageHelper.cleanAndPreserve(this.props.id);
        this.dataWrapper = new OrderDataWrapper(this.data);

        this.brand = linq.from<IBrand>(StorageHelper.getBrands())
            .where(x => x.id === this.data.activations[0].brandKey)
            .first();
    };

    public componentWillReceiveProps(next: ICustomerScreenProps) {
        this.processId = next.id;

        this.setState({ clear: true }, () => {
            this.setState({ changed: true, clear: false });
        });
    };

    public render() {
        if (this.state.clear) {
            return <></>;
        };

        this.processId = this.props.id;

        this.data = StorageHelper.getOrder(this.processId);
        this.dataWrapper = new OrderDataWrapper(this.data);

        let ord = StorageHelper.getOrders();
        let d: IErrorScreenData = { code: 404 };

        Global.easyApp.setTitle(MainTexts.existingCustomerActivation);
        OrderDataWrapper.ensure(this.data);

        this.brand = linq.from<IBrand>(StorageHelper.getBrands())
            .where(x => x.id === this.data.activations[0].brandKey)
            .first();

        if (this.data.customer.action) {
            Global.easyApp.setTitle(this.data.customer.action.name);
        };

        this.parentId = this.data.parentId ? this.data.parentId : '';

        let dyn: JSX.Element = <></>
       
        if (this.dynamicPopup) {
            this.dynamicPopup.props.onDismiss = () => {
                this.dynamicPopup = undefined;
                this.setState({ changed: true, processing: false, closablePopup: false });
            };

            dyn = GlobalJSX.GetPopup(this.dynamicPopup.props, this.dynamicPopup.content, this.dynamicPopup.action);
        };

        let ctrl =
            <div id={CustomerScreen.containerId}>
                <page.Inner1C style="   ">
                    <page.Inner2CLeft>
                        {this.data.activations[0].processId === "undefined Process" ? <ErrorScreen data={d} /> :
                            <>
                                {dyn}
                                {/* {this.renderPopup()} */}
                                {this.renderProgress()}
                            </>}
                    </page.Inner2CLeft>
                    <></>
                </page.Inner1C>
            </div>;

        return ctrl;
    };

    public getCurrent() {
        return this.data;
    };

    public refresh() {
        this.setState({ changed: true });
    };

    public getProgress() {
        if (window.location.href.indexOf('/' + OrderStages.done + '/') > 0) {
            return EOrderProcessView.done;
        } else if (window.location.href.indexOf('/' + OrderStages.tariff + '/') > 0) {
            return EOrderProcessView.tariff;
        } else {
            return EOrderProcessView.dashboard;
        };
    };

    public socketCallback(sock: ISocketPushModel) {

        let data: any = sock.data;

        let api: i.IApiResponse = typeof data == 'string' ? JSON.parse(data) : data;

        this.lastApi = api;


        let sum: IApiSummary = Api.parseSummary(api, undefined);
        if (api && !api.error) {
            if (this.dynamicPopup) {

                if (this.dynamicPopup.closeOnDone) {
                    this.dynamicPopup.props.visible = false;
                };
                this.dynamicPopup.props.closable = true;
                this.dynamicPopup.props.type = EPopupType.info;
                this.dynamicPopup.content = <div>Erfolgreich ausgefhrt</div>

                if (!this.dynamicPopup.callback) {
                    try {

                        let pr = JSON.parse(api.data.data);

                        this.data = JSON.parse(pr.steps[0].message);

                    } catch {
                        this.data = api.data;
                    };

                    StorageHelper.addOrder(this.data);
                };

                if (sock.type === m.ERequestType.customerGetCanellation) {
                    this.lastApi = undefined;
                    let d = OrderDataWrapper.createProcess(EBusinessCase.cancel, EActivationType.cancellation, this.data, undefined);
                    EasyRouter.route({ url: '/customer/cancellation/' + d.id, title: ActionTexts.cancel });
                } else if (sock.type === m.ERequestType.customerChangePayment) {
                    let receiver = Global.GetComponent<ExistingCustomerPayment>(ExistingCustomerPayment.containerId);
                    receiver.switchMode(EFormMode.view);
                } else if (sock.type === m.ERequestType.customerChangeData || sock.type === m.ERequestType.customerChangePassword) {
                    let receiver = Global.GetComponent<ExistingCustomerData>(ExistingCustomerData.containerId);
                    ////console.log(receiver);
                    receiver.switchMode(EFormMode.view);
                } else if (sock.type === m.ERequestType.customerChangeRemoveService
                    || sock.type === m.ERequestType.customerChangeAddService
                    || sock.type === m.ERequestType.customerChangeAddRemoveServices) {
                    let receiver = Global.GetComponent<ExistingCustomerServices>(ExistingCustomerServices.containerId);
                    receiver.switchMode(EFormMode.view);
                } else if (this.dynamicPopup.callback) {
                    this.dynamicPopup.callback(this.lastApi);
                }

                if (this.dynamicPopup.closeOnDone) {
                    this.dynamicPopup = undefined;
                };

                this.setState({ processing: false, closablePopup: (this.dynamicPopup != undefined), apiMessage: sum.message });

                return;
            };

            let actionName = this.data.customer.action ? this.data.customer.action.name : '';

            StorageHelper.updateActivation(api.data.activations[0]);

            EasyRouter.routeUrl({ url: '/customer/' + OrderStages.done + '/' + this.processId, title: actionName + ' ' + OrderDataWrapper.getMsisdnFromContract(this.data) });

            this.setState({ changed: true, processing: false });
        } else {
            if (this.dynamicPopup) {
                this.dynamicPopup.props.closable = true;
                this.dynamicPopup.props.type = EPopupType.info;
                this.dynamicPopup.content = <div dangerouslySetInnerHTML={{__html:sum.message}}></div>
            };
            this.processPopupState = EPopupType.custom;
            this.setState({ processing: false, closablePopup: true, apiMessage: sum.message });

            if (api.validation) {
                Api.applyValidation(api);
            };
        }
    };

    public applyAvailability() {
        this.setState({changed: true});
    };

    public processCustomer(todo: any, config: IPopupConfig) {
        this.dynamicPopup = config;

        config.props.onDismiss = () => {
            this.dynamicPopup = undefined;

        };

        this.setState({ changed: true }, () => {
            todo();
        });
    };

    public resetDynamicPopup(callback: any) {
        this.dynamicPopup = undefined;
        this.setState({changed: true});
        if(callback) {callback()};
    };

    public openImei(msisdn?: string) {
        EasyRouter.route({ url: '/customer/imei/' + this.data.id, title: MainTexts.imei });
    }

    public onBackToCustomer(external: boolean = true) {
        if (external) {
            StorageHelper.removeOrder(this.data.id, () => {
                EasyRouter.route({ url: '/customer/dashboard/' + this.data.parentId, title: MainTexts.existingCustomerActivation });
            });
        } else {
            EasyRouter.route({ url: '/customer/dashboard/' + this.data.id, title: MainTexts.existingCustomerActivation });
        };
    };

    private renderProgress(): JSX.Element {
        this.progress = this.getProgress();

        if (this.progress !== EOrderProcessView.dashboard) {
            let actProgress: JSX.Element = <ActivationProgress progress={this.progress} states={[OrderStages.tariff, OrderStages.done]} />;

            if (Global.easyApp.head) {
                Global.easyApp.head.appendSub(actProgress);
            }
        };

        if (this.props.dataMode === EExistingCustomerMode.sim) {
            return this.renderSimReplace();
        } else if (this.props.dataMode === EExistingCustomerMode.cancellation) {
            return this.renderCancellation();
        } else if (this.props.dataMode === EExistingCustomerMode.belatedPorting) {
            return this.renderBelatedportingReplace();
        } else if (this.props.dataMode === EExistingCustomerMode.imei) {
            return this.renderImei();
        }
        else {
            /**
             * process is VVL/TW and activation is done,
             * force user to use BackTocustomer
             */
            if (this.data.type != EBusinessCase.bnt) {
                if (this.dataWrapper.getAllActivationsDone()) {
                    this.progress = EOrderProcessView.done;
                };
            };


            if (this.progress === EOrderProcessView.dashboard) {
                return this.rednerDashboard();
            } else if (this.progress === EOrderProcessView.done) {
                return this.renderDone();
            } else {
                return this.renderTariff();
            }
        }
    };

    private _imeiCancel = () => {
        this.setState({ dataMode: EExistingCustomerMode.main });
    };

    private rednerDashboard(): JSX.Element {
        return <>
            <ExistingCustomerContract parent={this} />
            <ExistingContractData parent={this} />
            <ExistingCustomerActivities parent={this} />
            <ExistingCustomerNba parent={this} brand={this.brand}/>
            <ExistingCustomerCrossSelling parent={this} />
            <ExistingCustomerData parent={this} />
            <ExistingCustomerServices parent={this} />
            {
                this.brand.id === BrandKeys.otelo ? <ExistingCustomerBills parent={this} /> : null
            }
            {
                this.brand.id === BrandKeys.otelo ? <ProtectionAndConsent parent={this} collapsed={false}/> : null
            }
        </>;
    };

    private renderImei(): JSX.Element {
        const msisdn = OrderDataWrapper.getMsisdnFromContract(this.data);
        return <>
            <ExistingCustomerContract parent={this} />
            <ImeiScreen uploadType={ImeiUploadType.Manual} parent={this} msisdn={msisdn} onCancel={this._imeiCancel} />
        </>;
    };

    private renderSimReplace(): JSX.Element {
        return <>
            <ExistingCustomerContract parent={this} />
            <SimReplacement parent={this} />
        </>;
    };

    private renderCancellation(): JSX.Element {
        return <>
            <ExistingCustomerContract parent={this} />
            <Cancellation onChange={true} parent={this} />
        </>;
    };

    private renderBelatedportingReplace(): JSX.Element {
        return <>
            <ExistingCustomerContract parent={this} />
            <BelatedPorting onChange={true} parent={this} />
        </>;
    };

    private renderDone(): JSX.Element {
        let a: m.IActivation = OrderDataWrapper.ensureActivationIsObject(this.data.activations[0]);

        let aw = new ActivationWrapper(this.dataWrapper.data);

        return <>
            {
               // a.activationType == EActivationType.bookService ? null :
                    <CustomerConsent
                        customer={this.dataWrapper.data.customer}
                        parentRefresh={() => {
                            StorageHelper.addOrder(this.dataWrapper.data);
                            this.setState({ changed: true });
                        }} />
            }

            <PreviewContract
                key={"pc-" + a.id}
                bc={EBusinessCase.vvl}
                ah={aw.items[0]}
                customer={this.data.customer}
                preview={false}
                refreshParent={() => {
                    this.setState({ changed: true });
                }} />

            {this.renderAction()}
        </>;
    };

    private renderTariff(): JSX.Element {
        let tarif = this.data.customer.contracts && this.data.customer.contracts[0].bookedProduct
            ? this.data.customer.contracts[0].bookedProduct.id : '';

        let selection: IProcessNewSelection = {
            businessCase: EBusinessCase.vvl,
            creditDebitType: ECreditDebitType.postpaid,
            activationType: EActivationType.actDefault,
            customerType: ECustomerType.personal
        };

        return <>
            <cmp.Popup
                type={this.processPopupState}
                visible={this.state.processing || this.state.closablePopup}
                closable={this.state.closablePopup}
                id="wait"
                title={MainTexts.pleaseWait}
                height={200}
                onDismiss={this.onDismissProcessPopup}>
                <cmp.PopupBody>
                    <div>{this.state.apiMessage}</div>
                    <></>
                </cmp.PopupBody>
                <></>
            </cmp.Popup>

            <cmp.Popup
                type={EPopupType.custom}
                visible={!this.modelState.isValid()}
                closable={true}
                id="validate"
                title={ValidationMessages.titleValidationError}
                height={200}
                onDismiss={this.onDismissValidation}>
                <cmp.PopupBody>
                    <div>{this.modelState.errorMessage}</div>
                    <page.LClear />
                </cmp.PopupBody>
                <></>
            </cmp.Popup>

            <ExistingCustomerContract parent={this} />

            <TariffConfiguration
                brand={this.brand}
                businessCase={this.data.customer.action ? this.data.customer.action.bc : EBusinessCase.vvl}
                activation={this.data.activations[0]}
                selection={selection}
                selectedTariff={tarif}
                action={this.data.customer.action}
                backToCustomer={this.onBackToCustomer}
                availableProducts={OrderDataWrapper.getAvailableProducts(this.brand, this.data.customer.action, this.data.customer, this.data.customer.action ? this.data.customer.action.bc : EBusinessCase.vvl)}
                onConfigurationChanged={this.onConfigurationChanged} />

            <ActivationHardware preview={false}
                activation={this.data.activations[0]}
                onChange={this.onChangeHardware} />

            {this.renderAction()}
        </>;
    };

    private onConfigurationChanged() {
        this.setState({ changed: true });
    };

    private onDismissProcessPopup() {
        this.setState({ processing: false, closablePopup: false });
    };

    private onDismissValidation() {
        this.modelState.reset();

        this.setState({ changed: true });
    };

    private onChangeHardware(val: IControlValue) {
        this.data.activations[0].imei = val.value;
        StorageHelper.updateActivation(this.data.activations[0]);
        this.setState({ changed: true });
    };

    public onNext(external: IOrderData | undefined) {
        if (!this.validate()) {
            return;
        }
        let d = JSON.parse(JSON.stringify(this.data)) as IOrderData;
        d.customer.contracts = [];
        d.customer.contracts!.map((i)=>{
            i.actions = [];
        });
        
        Global.hub.send(JSON.stringify(external ? external : d), m.ERequestType.createCustomerProcess, undefined);

        this.setState({ processing: true, apiMessage: '' });
    };

    private validate(): boolean {
        this.modelState.reset();

        this.modelState = u.ValidationWrapper.startExistingProcessValidation(this.data.activations[0]);

        if (!this.modelState.isValid()) {
            this.setState({ changed: true });
            return false;
        }

        return true;
    };

    private onBack() {
        let pr = this.getProgress();

        let actionName = this.data.customer.action ? this.data.customer.action.name : '';

        if (this.data.activations[0].activationType == EActivationType.bookService) {
            StorageHelper.removeOrder(this.processId, () => {
                EasyRouter.route({ url: '/customer/' + OrderStages.dashboard + '/' + this.parentId, title: MainTexts.existingCustomerActivation });
            });
            return;
        };

        if (pr == EOrderProcessView.done) {
            EasyRouter.route({ url: '/customer/' + OrderStages.tariff + '/' + this.processId, title: actionName + ' ' + OrderDataWrapper.getMsisdnFromContract(this.data) });
        } else if (pr === EOrderProcessView.tariff) {
            StorageHelper.removeOrder(this.processId, () => {
                EasyRouter.route({ url: '/customer/' + OrderStages.dashboard + '/' + this.parentId, title: MainTexts.existingCustomerActivation });
            });
        };

        //this.setState({changed: true});
    };

    private renderAction(): JSX.Element {
        let pr = this.getProgress();

        if (this.data.type != EBusinessCase.bnt && this.dataWrapper.getAllActivationsDone()) {
            return <></>;
        };

        let ctrl = (
            <Container className="container-action">
                <Row>
                    {
                        this.data.activations[0].isActivated() ?
                            <Col xs={4} md={4}>
                                <page.LButton text={MainTexts.backToCustomer}
                                    onClick={this.onBackToCustomer}
                                    type={m.ELButtonType.linkSimple}
                                    css="action3"
                                    icon="chevron" />
                            </Col>
                            :
                            <Col xs={4} md={4}>
                                <page.LButton text={MainTexts.back}
                                    onClick={this.onBack}
                                    type={m.ELButtonType.linkSimple}
                                    css="action3"
                                    icon="chevron" />
                            </Col>
                    }
                    {
                        pr != EOrderProcessView.done ?
                            <Col xs={8} md={8} className="toright">
                                <page.LButton text="WEITER ZUR BERSICHT"
                                    onClick={this.onNext}
                                    css="fr"
                                    type={m.ELButtonType.buttonWithIconRight}
                                    icon="arrow-right" />
                            </Col>
                            : null
                    }
                </Row>
            </Container>
        );

        return ctrl;
    };
};

export default CustomerScreen;
