import * as React from 'react';
import * as linq from 'linq';
import * as d from '../../../models/existing/document';
import { EDocumentType } from '../../../models/ordering/order';
import {MainTexts} from '../../../utils/messages';
import { LButton } from '../../page';
import { ELButtonType } from '../../../models';
import Api from '../../../utils/api';
import { BrandKeys } from '../../../utils/Defaults';

export interface IDownloadProps {
    doc: d.Document;
    brandKey: string;
    activationId: string;
};

export class Download extends React.Component<IDownloadProps, any> {
    constructor(props: IDownloadProps) {
        super(props);
    };

    public render() {
        let ctrl = <div className="block-download">
            <div className="text-h2">{this.props.doc.name}</div>
            <div className="text-tiny">
                {
                    this.props.doc.type === EDocumentType.evn ? MainTexts.evn : MainTexts.bill
                }
            </div>
            <div className="action">
            <LButton type={ELButtonType.iconImage} 
                    icon="download" 
                    onClick={(e:d.Document)=>{
                        Api.follow('environment/dd/?brand=' + this.props.brandKey + '&activationId=' + this.props.activationId + '&file=' + e.file);
                    }} 
                    clickModel={this.props.doc}/>     
            </div>
        </div>

        return ctrl;
    };
};

export default Download;