import linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import BaseBlock from '../../../components/page/block';
import { IActivation, IOrderData } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { IAvailabilityBrand } from '../../../models/ordering/availability';
import { ICustomer } from '../../../models/ordering/customer';
import { EActionAddressType, EActivationType, ECreditDebitType, EPreviousAccessOwner, ESalutation } from '../../../models/ordering/order';
import PersonAndAddress, { CallbackTimeSplitted } from '../../../models/ordering/personWithAddress';
import { AccessNumber, AccessOwner } from '../../../models/ordering/previousAccessOwner';
import { EControlType } from '../../../models/page/diverse';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { MainTexts, OrderingTexts, ValidationMessages } from '../../../utils/messages';
import StorageHelper from '../../../utils/storage';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import { EDateInputMode } from '../../page/date';
import * as page from '../../page/index';
import Address from '../global/address';
import Person from '../global/person';
import Tel from '../global/tel';
import ActivationCustomerData from '../process/activationCustomerData';

export interface ICustomerDataMainProps {
    parent: ActivationCustomerData;
    data: IOrderData;
    customer: ICustomer;
    onChange: any;
};

export interface ICustomerDataMainState {
    changed: boolean;
};

export class CustomerDataMain extends React.Component<ICustomerDataMainProps, ICustomerDataMainState> {
    private customer: ICustomer;
    private activations: IActivation[];

    constructor(props: ICustomerDataMainProps) {
        super(props);

        this.customer = props.customer;
        this.activations = props.data.activations;

        //this.customer.salutation = ESalutation.undefined;

        if(props.customer.email){
            this.customer.email2 = props.customer.email;
        }

        // if(props.customer.payment!.iban){
        //     this.customer.payment!.iban = '';
        // }

        let order2 = linq.from<IOrderData>(StorageHelper.getOrders())
            .where(x => x.id == this.props.data.id)
            .firstOrDefault();


        if (new Date(this.customer.birthday!).getFullYear() === new Date().getFullYear()) {
            this.customer.birthday = this.createDate(0, 0, this.props.parent.props.parent.current!.creditDebitType == ECreditDebitType.prepaid && this.props.parent.props.parent.current!.brandKey === BrandKeys.otelo ? 16 : 0);
        }

        if (this.customer.delivery) {
            if (this.customer.delivery && new Date(this.customer.delivery.deliveryDate).setHours(0, 0, 0, 0) == new Date().setHours(0, 0, 0, 0)) {
                this.customer.delivery.deliveryDate = Global.dateOnly(Global.addDays(new Date(this.activations[0].activationDate), 1))
            } else if (this.customer.delivery && new Date(this.customer.delivery.deliveryDate).setHours(0, 0, 0, 0) < new Date(this.activations[0].activationDate).setHours(0, 0, 0, 0)) {
                this.customer.delivery.deliveryDate = Global.dateOnly(Global.addDays(new Date(this.activations[0].activationDate), 1))
            } else if (this.customer.delivery && new Date(this.activations[0].activationDate).setHours(0, 0, 0, 0) == new Date().setHours(0, 0, 0, 0)) {
                this.customer.delivery.deliveryDate = Global.dateOnly(Global.addDays(new Date(this.activations[0].activationDate), 1))
            }

            this.customer.delivery!.deliveryDate = new Date(new Date(this.customer.delivery!.deliveryDate).setHours(4, 0, 0, 0));

            this.customer.delivery!.callbackTimeSplitted = new CallbackTimeSplitted();

            // if(this.customer.delivery && !this.customer.delivery.callbackTime && this.props.parent.props.parent.dataWrapper.hasKabel()){
            //     this.customer.delivery.callbackTime = "12:00";
            //     this.customer.delivery.callbackTimeSplitted.hour = "12"
            //     this.customer.delivery.callbackTimeSplitted.minutes = "0o0";
            // }


            StorageHelper.updateCustomer(this.props.parent.props.parent.data.id, this.customer);
        }

        //split strnumber and extra in two fields
        if (this.props.parent.props.parent.dataWrapper.hasKabelOnly()) {
            let avl = Global.categoryWrapper.avl.getBrand(BrandKeys.kabel);
            if (avl && avl.address) {
                this.customer.address!.nr = avl.address.nr;
                this.customer.address!.nre = avl.address.nre;
            };
        };

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: ICustomerDataMainProps) {
        this.customer = next.customer;
        this.activations = next.data.activations;


        this.setState({ changed: true });
    };

    public render() {

        let cfg: IBlockConfiguration = {
            title: OrderingTexts.customerData,
            transparent: false
        };

        let needTel: boolean = false;
        //for the future if they want this for young tariffs make it dynamic, for now only otelo prepaid
        let minYear = new Date().getFullYear() - 16;
        let pwAdditional = this.props.parent.props.parent.dataWrapper.getPasswordForAdditionalContract();
        let noPasswordNeeded = this.props.parent.props.parent.dataWrapper.hasKabelOnly()
            || this.props.parent.props.parent.dataWrapper.hasUnityOnly()
            || this.props.parent.props.parent.current!.activationType == EActivationType.partnerCard
            || this.props.parent.props.parent.current!.activationType == EActivationType.partnerCardPortation;

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <div style={{ marginBottom: '30px' }}>
                        <Person prefix="+customer" onChange={this.props.onChange} source={this.customer}
                            salutation={this.customer.salutation ? this.customer.salutation : ESalutation.undefined}
                            firstname={this.customer.firstname ? this.customer.firstname : ''}
                            surname={this.customer.surname ? this.customer.surname : ''}
                            title={this.customer.title ? this.customer.title : ''} />
                    </div>

                    <Row >
                        <Col xs={7} md={7} className="np">
                            <page.LDate
                                onChange={this.props.onChange}
                                datePicker={false}
                                mode={EDateInputMode.withToggle}
                                value={this.customer.birthday}
                                key={"birthday"}
                                minimumBdayYear={this.props.parent.props.parent.current!.creditDebitType == ECreditDebitType.prepaid && this.props.parent.props.parent.current!.brandKey === BrandKeys.otelo ? minYear : undefined}
                                name={"birthday"} id={"birthday"}
                                title={MainTexts.dateOfBirth}
                            />
                        </Col>

                        <Col xs={5} md={5} className="npr">
                            <page.LControl type={EControlType.dropdown}
                                value={this.customer.nationality}
                                style="nationality"
                                onChange={this.props.onChange} name="nationality" id="nationality" title={MainTexts.nationality}
                                options={Global.getNationalities()}
                                validationOptions={
                                    [
                                        new ValidationOption({
                                            type: EValidationOption.nationalityError,
                                            message: ValidationMessages.nationalityError
                                        })
                                    ]
                                }
                            />
                        </Col>
                    </Row>

                    <div className="spacer-big" />

                    <Address onChange={this.props.onChange}
                        source={this.props.customer}
                        prefix="address"
                        withAditional={false}
                        showStrnAdditional={this.props.parent.props.parent.dataWrapper.hasKabel()}
                        preview={this.props.parent.props.parent.dataWrapper.hasKabel() || this.props.parent.props.parent.dataWrapper.hasDsl() || this.props.parent.props.parent.dataWrapper.hasUnity()} />

                    <div className="spacer-big" />

                    {
                        this.props.parent.props.parent.dataWrapper.hasUnity() ?
                            <>
                                <Tel titleTel={MainTexts.contactNumber} prefix="tel" onChange={this.props.onChange} source={this.customer} preview={false} />
                                <div className="spacer-big" />
                            </>
                            : null
                    }

                    {
                        this.props.parent.props.parent.dataWrapper.hasDsl() || this.props.parent.props.parent.dataWrapper.hasKabel() || this.props.parent.props.parent.dataWrapper.hasUnity() ?
                            <>
                                {this.renderDelivery()}
                                <div className="spacer-big" />
                            </>
                            : null
                    }

                    {
                        this.props.parent.props.parent.dataWrapper.hasDsl() ?
                            <>
                                {this.renderInst()}
                                <div className="spacer-big" />
                            </>
                            : null
                    }

                    {
                        this.props.parent.props.parent.dataWrapper.hasDsl() ?
                            <>
                                {this.renderPreviousAccessOwner()}
                                <div className="spacer-big" />
                            </>
                            : null
                    }

                    <Row>
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.text} value={this.customer.email}
                                onChange={this.props.onChange} name="email" id="email" label="Email"
                                title="E-Mail-Adresse fr Besttigungs E-Mail"
                                validationOptions={
                                    [
                                        new ValidationOption({
                                            type: EValidationOption.email,
                                            message: ValidationMessages.errorEmail
                                        })
                                    ]
                                } />
                        </Col>
                        <Col xs={6} md={6} className="npr">
                            <page.LControl type={EControlType.text} value={this.customer.email2}
                                onChange={this.props.onChange} name="email2" id="email2" label="Email"
                                pasteDisabled={true}
                                validationOptions={
                                    [
                                        new ValidationOption({ type: EValidationOption.email, message: ValidationMessages.errorEmail }),
                                        new ValidationOption({ type: EValidationOption.match, message: ValidationMessages.errorEmailMatch, source: this.customer, targetMatch: 'email' })
                                    ]
                                } />
                        </Col>
                    </Row>

                    {this.props.data.currentBrand === BrandKeys.vodafone || this.props.data.currentBrand === BrandKeys.dsl || this.props.data.currentBrand === BrandKeys.otelo ? <Row>
                        <Col xs={12} md={12} className="np">
                            <page.LControl hideTitle={true} labelStyle={'tc-hint mwa'} type={EControlType.hint} label={this.props.data.currentBrand === BrandKeys.vodafone || this.props.data.currentBrand === BrandKeys.dsl ? MainTexts.vf_dsl_email_text : MainTexts.otelo_email_text} />
                            <page.LClear />
                        </Col>
                    </Row> : <></>}

                    <Row>
                        <Col xs={12} md={12} className="np">
                            <page.LControl type={EControlType.checkbox} onChange={this.props.onChange}
                                checked={this.customer.emailApproved ? (this.customer.emailApproved > 0) : false}
                                name="emailApproved" id="emailApproved"
                                label="Bitte besttigen Sie die E-Mail Adresse"
                                labelStyle="blue" />
                        </Col>
                    </Row>

                    {
                        //if only cable then no pw required
                        noPasswordNeeded ? null :
                            <Row>
                                <Col xs={12} md={12} className="np">
                                    <page.LControl type={EControlType.text}
                                        disabled={pwAdditional ? true : false}
                                        value={pwAdditional ? pwAdditional : (this.customer.password ? this.customer.password : '')}
                                        onChange={this.props.onChange} name="password" id="password"
                                        validationOptions={[
                                            new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                            new ValidationOption({ type: EValidationOption.password, message: ValidationMessages.errorPassword })
                                        ]}
                                        title={MainTexts.customerPassword} />
                                </Col>
                            </Row>
                    }

                    {
                        this.props.parent.props.parent.current && !noPasswordNeeded && this.props.parent.props.parent.current.activationType != EActivationType.partnerCard ?
                            <Row>
                                <Col xs={12} md={12} className="np">
                                    <page.LControl hideTitle={true} labelStyle={'tc-hint mwa'} maxLength={30} type={EControlType.hint} label="Das Kunden-Kennwort muss aus mindestens 8 und hchstens 10 Zeichen bestehen. Es muss Buchstaben und Ziffern enthalten. , ,  und  sind nicht zulssig" />
                                    <page.LClear />
                                </Col>
                            </Row>
                            : null
                    }

                    <Row>
                        <Col xs={12} md={12} className="np">
                            <page.LControl hideTitle={true} type={EControlType.hint} label="Hinweis: Die Vodafone GmbH darf meine Telefonnummer sowie meine Post- und E-Mail-Adresse nutzen, um mir per Messenger, SMS, MMS, Post und E-Mail Infos zur Beratung, zur Werbung fr eigene hnliche Angebote und zur Marktforschung zu schicken. Ich kann der Nutzung meiner Telefonnummer und Adressen zu diesem Zwecke jederzeit ganz oder teilweise schriftlich oder elektronisch fr die Zukunft widersprechen." />
                            <page.LClear />
                        </Col>
                    </Row>
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };

    public createDate(days, months, years) {
        var date = new Date();
        date.setDate(date.getDate() + days);
        date.setMonth(date.getMonth() + months);
        date.setFullYear(date.getFullYear() - years);
        return date;
    }

    public renderDelivery() {
        let dt: EActionAddressType = this.props.customer.deliveryType ? this.props.customer.deliveryType : EActionAddressType.sameAddress;
        let bt: EActionAddressType = this.props.customer.payment && this.props.customer.payment.addressType ? this.props.customer.payment.addressType : EActionAddressType.sameAddress;
        let del: PersonAndAddress = this.customer.delivery ? this.customer.delivery : new PersonAndAddress();

        if (!del.person) {
            del.person = { salutation: ESalutation.undefined, firstname: '', surname: '', birthday: new Date(), title: '' }
        }

        if (this.props.parent.props.parent.dataWrapper.hasUnityOnly()) {
            return <></>
        };

        let ctrl = (
            <>
                <Row>
                    <Col xs={12} md={12} className="np">
                        <page.LControl type={EControlType.title} title={OrderingTexts.productDeliveryAddress}
                        />
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} md={12} className="np">
                        <page.LControl type={EControlType.radio} hideTitle={true}
                            checked={dt === EActionAddressType.sameAddress}
                            onChange={this.props.onChange} name="deliveryType" id="deliveryType_1"
                            label={OrderingTexts.deliveryType1} />
                    </Col>
                    <Col xs={12} md={12} className="np">
                        <page.LControl type={EControlType.radio} hideTitle={true}
                            checked={dt === EActionAddressType.differentAddress}
                            onChange={this.props.onChange} name="deliveryType" id="deliveryType_2"
                            label={OrderingTexts.deliveryType2} />
                    </Col>
                </Row>

                {
                    dt === EActionAddressType.differentAddress ?
                        <>
                            <Address onChange={this.props.onChange} showStrnAdditional={this.props.parent.props.parent.dataWrapper.hasKabel()} source={this.customer} prefix="delivery.address"
                                preview={false} />
                            <Person prefix="delivery.person" onChange={this.props.onChange}
                                source={this.customer}
                                salutation={del.person.salutation}
                                firstname={del.person.firstname}
                                surname={del.person.surname}
                                title={del.person.title} />
                            <Col xs={7} md={7} className="np">
                                <page.LDate
                                    onChange={this.props.onChange}
                                    datePicker={false}
                                    mode={EDateInputMode.undefined}
                                    value={this.customer.delivery ? this.customer.delivery.person.birthday : new Date()}
                                    key={"birthdayDelivery"}
                                    name={"delivery.person.birthday"} id={"birthdayDelivery"}
                                    title={MainTexts.dateOfBirth}
                                />
                            </Col>
                            <Tel prefix="delivery.tel" onChange={this.props.onChange} source={this.customer} preview={false} />
                            <Row>

                            </Row>
                            {this.props.parent.props.parent.dataWrapper.hasDsl() ? <></> : <Row>
                                <Col xs={12} md={12} className="np">
                                    <page.LControl type={EControlType.text} title={MainTexts.telCallback}
                                        value={del.callbackTime}
                                        onChange={this.props.onChange} name="delivery.callbackTime" id="delivery-callbackTime"
                                        label='z.b. gegen 9:00' />
                                </Col>
                            </Row>}
                            {this.props.parent.props.parent.dataWrapper.hasDsl() ? <></> : <Row>
                                <Col xs={6} md={6} className="np">
                                    <page.LDate title={MainTexts.deliverydate}
                                        id={'deliveryDate'}
                                        datePicker={true}
                                        from={Global.dateOnly(Global.addDays(new Date(), 0))}
                                        to={Global.dateOnly(Global.addDays(new Date(), 1827))}
                                        hasKabel={this.props.parent.props.parent.dataWrapper.hasKabel()}
                                        name={'delivery.deliveryDate'}
                                        onChange={this.props.onChange}
                                        value={this.customer.delivery ? this.customer.delivery.deliveryDate : new Date()}
                                    />
                                </Col>
                            </Row>}
                        </>
                        : null
                }

                {
                    this.props.parent.props.parent.dataWrapper.hasUnity() ?
                        <>
                            <Row>
                                <Col xs={12} md={12} className="np">
                                    <page.LControl type={EControlType.title} title={OrderingTexts.billAddress}
                                    />
                                </Col>
                            </Row>
                            <Row>
                                <Col xs={12} md={12} className="np">
                                    <page.LControl type={EControlType.radio} hideTitle={true}
                                        checked={bt === EActionAddressType.sameAddress}
                                        onChange={this.props.onChange} name="payment.addressType" id="addressType_1"
                                        label={OrderingTexts.billAddress1} />
                                </Col>
                                <Col xs={12} md={12} className="np">
                                    <page.LControl type={EControlType.radio} hideTitle={true}
                                        checked={bt === EActionAddressType.differentAddress}
                                        onChange={this.props.onChange} name="payment.addressType" id="addressType_2"
                                        label={OrderingTexts.billAddress2} />
                                </Col>
                            </Row>
                            {
                                this.customer.payment!.addressType == EActionAddressType.differentAddress ?
                                    <Address onChange={this.props.onChange} source={this.customer} prefix="payment.address"
                                        preview={false} />
                                    : null
                            }
                        </>
                        : null
                }

                {/* {
                    // numberprotation option is selected
                    // 2020-03-21 sm not needed anymore but !!!!! dont delete !!!!
                    this.props.parent.props.parent.dataWrapper.hasNumberProtability() ?
                        <>
                            <div className="spacer" />
                            <Row>
                                <Col xs={12} md={12} className="">
                                    <page.LControl type={EControlType.title} title={MainTexts.numberPortability}
                                         />
                                </Col>
                            </Row>
                            <Row>
                                <Col xs={12} md={12} className="">
                                    <Tel prefix="numberPort"
                                        telTypeHidden={true}
                                        onChange={this.props.onChange}
                                        source={this.customer} preview={false} />
                                </Col>
                            </Row>
                        </>
                        : null
                } */}

                {
                    //special case tel is needed for Kabel
                    dt === EActionAddressType.sameAddress && this.props.parent.props.parent.dataWrapper.hasKabel() ?
                        <>
                            <Tel prefix="delivery.tel" onChange={this.props.onChange} source={this.customer} preview={false} />
                            <Row>
                                {/* <Col xs={12} md={12} className="np">
                                    <page.LControl type={EControlType.text} title={MainTexts.telCallback}
                                        value={del.callbackTime}
                                        onChange={this.props.onChange} name="delivery.callbackTime" id="delivery-callbackTime"
                                        label='z.b. gegen 9:00' />
                                </Col> */}
                                <page.LTime value={del.callbackTime} name={"delivery.callbackTime"} id={'delivery-callbackTime'} onChange={this.props.onChange} />
                            </Row>
                            <Row>
                                <Col xs={6} md={6} className="np">
                                    <page.LDate title={MainTexts.deliverydate}
                                        datePicker={true}
                                        from={Global.dateOnly(Global.addDays(new Date(this.activations[0].activationDate), 1))}
                                        to={Global.dateOnly(Global.addDays(new Date(), 1827))}
                                        id={'deliveryDateKabel'}
                                        name={'delivery.deliveryDate'}
                                        hasKabel={this.props.parent.props.parent.dataWrapper.hasKabel()}
                                        onChange={this.props.onChange}
                                        value={this.customer.delivery ? this.customer.delivery.deliveryDate : Global.dateOnly(Global.addDays(new Date(this.activations[0].activationDate), 1))}
                                    />
                                </Col>
                            </Row>
                        </>
                        : null
                }
            </>
        );

        return ctrl;
    };

    public renderPreviousAccessOwner() {

        let prev: EPreviousAccessOwner = this.props.customer.prevOwner ? this.props.customer.prevOwner : EPreviousAccessOwner.noPreviousOwner;
        let prevOwner: AccessOwner = this.props.customer.previousAccessOwner ? this.props.customer.previousAccessOwner : new AccessOwner();
        let prevOwnerNumber: AccessNumber = this.props.customer.previousAccessNumber ? this.props.customer.previousAccessNumber : new AccessNumber();
       
        var avl = StorageHelper.getAvailability();
        let br = linq.from<IAvailabilityBrand>(avl.brands).where(x => x.key === BrandKeys.dsl).firstOrDefault();

        if (br && br.products && br.products.length > 0 && !this.props.customer.previousAccessNumber) {
            this.props.customer.previousAccessNumber = new AccessNumber();
            this.props.customer.previousAccessNumber.prefix = br.products[0].areaCode;
        };

        let ctrl = (
            <>
                <Row>
                    <Col xs={12} md={12} className="np">
                        <page.LControl type={EControlType.title} title={OrderingTexts.previousAccessOwner} />
                    </Col>
                </Row>
                <Row>
                    <Col xs={6} md={6} className="np">
                        <page.LControl type={EControlType.radio} hideTitle={true}
                            checked={prev == EPreviousAccessOwner.previousOwner}
                            onChange={this.props.onChange} name="prevOwner" id="prevOwner-1"
                            label={OrderingTexts.previousAccessOwnerLabelYes} />
                    </Col>
                    <Col xs={6} md={6} className="np">
                        <page.LControl type={EControlType.radio} hideTitle={true}
                            checked={prev == EPreviousAccessOwner.noPreviousOwner}
                            onChange={this.props.onChange} name="prevOwner" id="prevOwner-2"
                            label={OrderingTexts.previousAccessOwnerLabelNo} />
                    </Col>
                </Row>
                {
                    prev === EPreviousAccessOwner.previousOwner ?
                        <>
                            <Person prefix="previousAccessOwner"
                                onChange={this.props.onChange}
                                source={this.customer}
                                salutation={prevOwner.salutation}
                                firstname={prevOwner.firstname}
                                birthday={prevOwner.birthday}
                                showBirthday={true}
                                surname={prevOwner.surname}
                                validationOptions={[
                                    new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                    new ValidationOption({ type: EValidationOption.validateSurname, message: 'Es sind nur Buchstaben, Bindestriche und Leerzeichen zulssig und die Lnge liegt zwischen 2 und 30 Zeichen.' })
                                ]}
                                hideTitle={true}
                                title={''} />
                            <Tel
                                telTypeHidden={true}
                                source={this.props.customer}
                                onChange={this.props.onChange}
                                prefix="previousAccessNumber"
                                preview={false}
                                disablePrefix={true}
                                titleTel={"Festnetz"}
                            />
                        </>
                        : null
                }
            </>
        );

        return ctrl;
    };

    public renderInst() {
        let dt: EActionAddressType = this.props.customer.instType ? this.props.customer.instType : EActionAddressType.sameAddress;
        let inst: PersonAndAddress = this.props.customer.inst ? this.props.customer.inst : new PersonAndAddress();

        let ctrl = (
            <>
                <Row>
                    <Col xs={12} md={12} className="np">
                        <page.LControl type={EControlType.title} title={OrderingTexts.installationAddress} />
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} md={12} className="np">
                        <page.LControl type={EControlType.radio} hideTitle={true}
                            checked={dt === EActionAddressType.sameAddress}
                            onChange={this.props.onChange} name="instType" id="instType_1"
                            label={OrderingTexts.installationType1} />
                    </Col>
                    <Col xs={12} md={12} className="np">
                        <page.LControl type={EControlType.radio} hideTitle={true}
                            checked={dt === EActionAddressType.differentAddress}
                            onChange={this.props.onChange} name="instType" id="instType_2"
                            label={OrderingTexts.installationType2} />
                    </Col>
                </Row>
                {
                    dt === EActionAddressType.differentAddress ?
                        <>
                            <Address onChange={this.props.onChange} source={this.customer}
                                prefix="inst.address" preview={false} />

                            <Person prefix="inst.person"
                                onChange={this.props.onChange}
                                source={this.customer}
                                salutation={inst.person.salutation}
                                firstname={inst.person.firstname}
                                surname={inst.person.surname}
                                title={inst.person.title} />
                        </>
                        : null
                }
            </>
        );

        return ctrl;
    };
};

export default CustomerDataMain;