import * as React from 'react';
import * as linq from 'linq';
import CustomerScreen from '../../../screens/existing/customer';
import ExistingCustomerPayment from '../../existing/data/customerPayment';
import { IBaseComponentState, IApiResponse, EApiResponseStatus } from '../../../interfaces';
import { Container, Row, Col } from 'react-bootstrap';
import { IBlockConfiguration } from '../../../models/block/configuration';
import BaseBlock from '../../page/block';
import { IContract } from '../../../models/existing/contract';
import { OrderingTexts, MainTexts, ActivationMessages } from '../../../utils/messages';
import { ICustomer } from '../../../models/ordering/customer';
import { EDocumentType } from '../../../models/ordering/order';
import * as d from '../../../models/existing/document';
import Download from './download';
import { LControl, LButton } from '../../page';
import { EControlType, EFormMode } from '../../../models';
import Global from '../../../utils/Global';
import * as m from '../../../models/index';
import { ConsentText, AddChanel, ConsentGet } from '../../../models/ordering/bew';
import * as page from '../../page/index';
import { IControlValue } from '../../../models/page/interfaces';
import StorageHelper from '../../../utils/storage';
import { BrandKeys } from '../../../utils/Defaults';
import { IPopupConfig, EPopupType } from '../../dialogs/popup';
import Api from '../../../utils/api';

export interface IExistingCustomerConsentProps {
    parent: CustomerScreen;
    collapsed: boolean;
};

export interface IExistingCustomerConsentState {
    mode: EFormMode,
    changed: boolean,
    activateAll: boolean,
    deactivateAll: boolean,
}



export class Consents {
    email_EMA: AddChanel = { selected: false, ad_channel_type: "", change_date: new Date(), channel: "" };
    emailLastChange: AddChanel = { selected: false, ad_channel_type: "", change_date: new Date(), channel: "" };
    sms_mms_SMS: AddChanel = { selected: false, ad_channel_type: "", change_date: new Date(), channel: "" };
    post_WHM: AddChanel = { selected: false, ad_channel_type: "", change_date: new Date(), channel: "" };
    telefon_OBP: AddChanel = { selected: false, ad_channel_type: "", change_date: new Date(), channel: "" };
    telefonContractExt_PVL: AddChanel = { selected: false, ad_channel_type: "", change_date: new Date(), channel: "" };
    checkoutTerms: AddChanel = { selected: false, ad_channel_type: "", change_date: new Date(), channel: "" };
    data_handling_terms: AddChanel = { selected: false, ad_channel_type: "", change_date: new Date(), channel: "" };
}

export class ProtectionAndConsent extends React.Component<IExistingCustomerConsentProps, IExistingCustomerConsentState> {
    private contract: IContract;
    private customer: ICustomer;
    private allConsents: Consents;
    private collapsed?: boolean;
    public static containerId: string = 'containerConsentsExisting';
    private oneConsent: ConsentGet = { consent_date: new Date(), consent_type: "", not_selected_ad_channels: [], selected_ad_channels: [] };

    private canPrint?: boolean = false;
    private canSave?: boolean = false;
    private printed: boolean = false;
    private needPrint: boolean = false;

    constructor(props: IExistingCustomerConsentProps) {
        super(props);

        this.collapse = this.collapse.bind(this);

        this.collapsed = this.props.collapsed;

        this.state = {
            changed: false,
            mode: EFormMode.view,
            activateAll: false,
            deactivateAll: false
        }; 

        this.allConsents = new Consents();
        this.customer = StorageHelper.getCustomer(this.props.parent.data.id);
        this.contract = this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0] : {};
    }; 

    public render() {
        let cfg: IBlockConfiguration = { 
            title: OrderingTexts.customerConsent,  
            transparent: false,
            style: '',
            styleContainer: '', 
            collapsible: true,
            id: ProtectionAndConsent.containerId + "-inner",
            collapsed: this.collapsed,
            onCollapse: (collapsed)=>{
                this.collapsed = collapsed;
                this.setState({changed: true});
            }
        };

        this.prepareConsents();

        this.canPrint = false;
        this.canSave = false;

        if(this.customer.bew) {
            if(this.customer.bew.consulting1 || this.customer.bew.consulting2) {
                this.canPrint = true;
            };

            if(this.customer.bew.consulting1 && this.printed) {
                this.canSave = true;
            };

            if(!this.customer.bew.consulting1 && !this.customer.bew.consulting2 && ((this.customer.bew.adChannels && this.customer.bew.adChannels.length == 0))) {
                this.canSave = true;
            };

            if(!this.customer.bew.consulting1 && this.printed && this.customer.bew.consulting2) {
                this.canSave = true;
            };
            
            this.needPrint = false;
            if(this.customer.bew.consulting1 || this.customer.bew.consulting2 || ((this.customer.bew.adChannels && this.customer.bew.adChannels.length == 0))) {
                this.needPrint = true;
            };

            if(this.state.deactivateAll) {
                this.needPrint = false;
            };
        };

        let ctrl =
            <Container >
                {this.state.mode == EFormMode.view ? <>
                    <Row>
                        <Col xs={4} md={4}>
                            <LControl preview={true}
                                type={EControlType.text} style="merged"
                                label={MainTexts.email}
                                id={""}
                                name={""}
                                value={Global.toGermanDate(this.allConsents.email_EMA.change_date)} />
                        </Col>
                        <Col xs={1} md={1}>
                            <br/>
                            <LControl type={EControlType.checkbox}
                                hideTitle={true}
                                checked={this.allConsents.email_EMA.selected}
                                disabled={true}
                                name=""
                                id={""}
                            />
                        </Col>
                        <Col xs={2} md={2} />

                        <Col xs={4} md={4}>
                            <LControl preview={true}
                                type={EControlType.text} style="merged"
                                label={MainTexts.sms_mms}
                                id={""}
                                name={""}
                                value={Global.toGermanDate(this.allConsents.sms_mms_SMS.change_date)} />
                        </Col>
                        <Col xs={1} md={1}>
                            <br/>
                            <LControl type={EControlType.checkbox}
                                hideTitle={true}
                                style="merged"
                                checked={this.allConsents.sms_mms_SMS.selected}
                                disabled={true}
                                name=""
                                id={""}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={4} md={4}>
                            <LControl preview={true}
                                type={EControlType.text} style="merged"
                                label={MainTexts.emailLastChange}
                                id={""}
                                name={""}

                                value={Global.toGermanDate(this.allConsents.emailLastChange.change_date)} />
                        </Col>
                        <Col xs={1} md={1}>
                            <br/>
                            <LControl type={EControlType.checkbox}
                                checked={this.allConsents.emailLastChange.selected}
                                disabled={true}
                                hideTitle={true}
                                name=""
                                id={""}
                            />
                        </Col>
                        <Col xs={2} md={2} />

                        <Col xs={4} md={4}>
                            <LControl preview={true}
                                type={EControlType.text} style="merged"
                                label={MainTexts.phone}
                                id={""}
                                name={""}

                                value={Global.toGermanDate(this.allConsents.telefon_OBP.change_date)} />
                        </Col>
                        <Col xs={1} md={1}>
                            <br/>
                            <LControl type={EControlType.checkbox}
                                checked={this.allConsents.telefon_OBP.selected}
                                disabled={true}
                                hideTitle={true}
                                name=""
                                id={""}
                            />
                        </Col>
                    </Row>
                    <Row>

                        <Col xs={4} md={4}>
                            <LControl preview={true}
                                type={EControlType.text} style="merged"
                                label={MainTexts.post}
                                id={""}
                                name={""}

                                value={Global.toGermanDate(this.allConsents.post_WHM.change_date)} />
                        </Col>
                        <Col xs={1} md={1}>
                            <br/>
                            <LControl type={EControlType.checkbox}
                                checked={this.allConsents.post_WHM.selected}
                                disabled={true}
                                hideTitle={true}
                                name=""
                                id={""}
                            />
                        </Col>
                        <Col xs={2} md={2} />

                        <Col xs={4} md={4}>
                            <LControl preview={true}
                                type={EControlType.text} style="merged"
                                label={MainTexts.phoneContractExtension}
                                id={""}
                                name={""}

                                value={Global.toGermanDate(this.allConsents.telefonContractExt_PVL.change_date)} />
                        </Col>
                        <Col xs={1} md={1}>
                            <br/>
                            <LControl type={EControlType.checkbox}
                                checked={this.allConsents.telefonContractExt_PVL.selected}
                                disabled={true}
                                hideTitle={true}
                                name=""
                                id={""}
                            />
                        </Col>
                    </Row>
                </>
                    :
                    <>
                        <Row>
                            <Col xs={12} md={12}  >
                                <LControl type={EControlType.hint} label={"Hier bestimmen Sie fr Ihren Kunden ob und wie Sie zur Beratung kontaktiert werden drfen."} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} >
                                <LControl type={EControlType.hint} label={"Mit den Checkboxen knnen Sie Ihre Beratungseinwilligung. geben und widerrufen und Ihr Widerspruchsrecht nutzen"} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} >
                                <LControl type={EControlType.hint} label={"ACHTUNG: Im easy Portal knnen Sie die Einstellungen nur fr alle Kommunikationswege vornehmen."} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={1} md={1} className="np">
                                <LControl type={EControlType.checkbox} onChange={() => {
                                    this.selectAll();
                                }} name="selectAll" id="selectAllId"
                                    hideTitle={true} label=""

                                    checked={this.state.activateAll} />
                            </Col>
                            <Col xs={11} md={11} className="npr">
                                <LControl type={EControlType.hint} label={"Alle Kommunikationskanle aktivieren"} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={1} md={1} className="np">
                                <page.LControl
                                    type={EControlType.checkbox}
                                    onChange={(e) => {
                                        this.deselectAll()
                                    }}
                                    name="deactivateAll"
                                    id="deactivateAllId"
                                    hideTitle={true}
                                    label=""
                                    checked={this.state.deactivateAll} />
                            </Col>
                            <Col xs={11} md={11} className="npr">
                                <LControl type={EControlType.hint} label={"Alle Kommunikationskanle deaktivieren"} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <div className="spacer-medium" />
                                <div className="spacer-line" />
                                <div className="spacer-medium" />
                            </Col>
                        </Row>
                    </>}

                {this.state.deactivateAll ? <></> : <Row>
                    <Col xs={1} md={1} className="np">
                        <LControl type={EControlType.checkbox} onChange={(v:IControlValue) => {
                            if(this.customer.bew) {
                                this.customer.bew.consulting1 = v.checked;
                                this.printed = false;
                                ////StorageHelper.updateCustomer(this.props.parent.data.id, this.customer);
                            };                            
                            // this.allConsents.checkoutTerms.selected = this.allConsents.checkoutTerms.selected ? false : true;
                            this.setState({ changed: true });
                        }} name="checkoutTerms" id="checkoutTermsId"
                            hideTitle={true} label=""
                            disabled={this.state.mode == EFormMode.view}
                            checked={this.customer.bew ? this.customer.bew.consulting1 : false} />
                    </Col>
                    <Col xs={11} md={11} className="npr">
                        <LControl type={EControlType.hint} label={this.customer.bew && this.customer.bew.consentExisting && this.customer.bew.consentExisting.ActualTexts.bew_checkout_terms !== undefined ? this.customer.bew.consentExisting.ActualTexts.bew_checkout_terms[0].text : "n/a"} />
                    </Col>
                </Row>}

                <Row>
                    <Col xs={1} md={1} className="np">
                        <LControl type={EControlType.checkbox} onChange={(v:IControlValue) => {
                            if(this.customer.bew) {
                                this.customer.bew.consulting2 = v.checked;

                                this.printed = false;

                                ////StorageHelper.updateCustomer(this.props.parent.data.id, this.customer);
                            };
                            this.setState({ changed: true })
                        }} name="dataHandlingTerms" id="dataHandlingTerms"
                            hideTitle={true} label=""
                            disabled={this.state.mode == EFormMode.view}
                            checked={this.customer.bew ? this.customer.bew.consulting2 : false} />
                    </Col>
                    <Col xs={11} md={11} className="npr">
                        <LControl type={EControlType.hint} 
                            label={this.customer.bew && this.customer.bew.consentExisting && this.customer.bew.consentExisting.ActualTexts.data_handling_terms !== undefined ? this.customer.bew.consentExisting.ActualTexts.data_handling_terms[0].text : "n/a"} />
                    </Col>
                </Row>

                {this.state.mode == EFormMode.edit ?
                    <>
                        <LButton type={m.ELButtonType.buttonWithIconRight}
                            icon="print"
                            css="a1"
                            id=""
                            text={MainTexts.cancel}
                            onClick={() => {
                                this.printed = false;
                                this.customer = StorageHelper.getCustomer(this.props.parent.data.id);
                                this.setState({ mode: EFormMode.view, activateAll: false, deactivateAll: false })
                            }} />

                        {
                            this.canPrint ? 
                            <LButton type={m.ELButtonType.buttonWithIconRight} //show only if at least one checkbox is selected
                                icon="print"
                                css="a1 fr"
                                id=""
                                text={MainTexts.print}
                                onClick={() => {
                                    if(!this.canProcess()) {
                                        return;
                                    };

                                    let update  = {
                                        type: m.ERequestType.customerPrintBEW,
                                        brandKey: BrandKeys.otelo,
                                        id: this.props.parent.data.id,
                                        bew: this.customer.bew
                                    };

                                    let cfg:IPopupConfig = {
                                        props: {
                                            id: 'dynamicPopup',
                                            title: OrderingTexts.customerConsent,
                                            visible: true,
                                            closable: false,
                                            type: EPopupType.pleaseWait
                                        },
                                        content: <div>{OrderingTexts.customerData}</div>,
                                        callback: (api: IApiResponse)=>{

                                        }
                                    };
                                    
                                    this.props.parent.processCustomer(()=>{
                                        Api.fetch('existing/printbew', update, 'post', (api)=>{
                                            let msg: any = ActivationMessages.saveSuccess;
                                            
                                            if(api.status != EApiResponseStatus.ok) {
                                                var sum = Api.parseSummary(api, undefined);
                                                msg = sum.message;
                                            } else {
                                                msg = <div className="tal">
                                                    <span>Das Auftragsdokument zur BEW-nderung wurde erstellt. Zum Download klicke auf den nachstehenden Link.</span>
                                                    <br/> <br/>
                                                    <div className="tac">
                                                        <LButton type={m.ELButtonType.buttonWithIconRight} //show only if at least one checkbox is selected
                                                            icon="print"
                                                            css="a1"
                                                            id=""
                                                            text={"Dokument anzeigen"}
                                                            onClick={() => {
                                                                this.printed = true;
                                                                this.setState({changed: true});
                                                                Api.follow('environment/dd/?brand=' + this.props.parent.data.activations[0].brandKey + '&activationId=' + this.props.parent.data.activations[0].id + '&file=' + api.data.Binary);
                                                                this.props.parent.resetDynamicPopup(undefined);
                                                            }}
                                                        />
                                                    </div>
                                                </div>
                                            };

                                            let cfg:IPopupConfig = {
                                                props: {
                                                    id: 'dynamicPopup',
                                                    title:  OrderingTexts.customerConsent,
                                                    visible: true,
                                                    closable: true,
                                                    type: EPopupType.custom
                                                },
                                                content: <div>{msg}</div>
                                            };

                                            this.props.parent.processCustomer(()=>{ },cfg)
                                        });
                                    },cfg);                                

                                }} />                           
                                : null
                        }

                        {
                            this.canSave ? 
                            <LButton type={m.ELButtonType.buttonWithIconRight} //show only if at least one checkbox is selected
                                icon="print"
                                css="a1 fr"
                                id=""
                                text={MainTexts.save}
                                onClick={() => {
                                    if(!this.canProcess()) {
                                        return;
                                    };
                                    
                                    if(this.needPrint) {
                                        let cfgHint:IPopupConfig = {
                                            props: {
                                                id: 'dynamicPopup',
                                                title:  OrderingTexts.customerConsent,
                                                visible: true,
                                                closable: true,
                                                type: EPopupType.custom
                                            },
                                            content: <div>Bitte vergewissere dich, dass unser Kunde das Dokument zur nderung seiner Beratungseinwilligung unterschrieben hat.</div>,
                                            action: <>
                                                <page.LButton text={MainTexts.next} css="fr" icon="ok"
                                                    type={m.ELButtonType.buttonWithIconRight}
                                                    onClick={() => { 
                                                        this.runSave();
                                                    }} />
                                            </>
                                        };
                                        
                                        this.props.parent.processCustomer(()=>{ }, cfgHint);
                                    } else {
                                        this.runSave();
                                    };
                                }} />                            
                            : null
                        }

                    </>

                    : <LButton type={m.ELButtonType.buttonWithIconRight}
                        icon="print"
                        css="a1 fr"
                        id=""
                        text={MainTexts.edit}
                        onClick={() => {
                            this.setState({ mode: EFormMode.edit })
                        }} />
                }
            </Container>;

        return <div id={ProtectionAndConsent.containerId}>
            <BaseBlock config={cfg}>
                {ctrl}
            </BaseBlock>
            </div>;
    };



    public collapse(state: boolean) {
        this.collapsed = state;
        this.setState({mode: EFormMode.view}, ()=>{
            setTimeout(()=> {
                this.setState({mode: EFormMode.view});
                Global.FocusComponent('oecselectAllId');
            }, 1000);
        });
    };

    private runSave() {
        this.props.parent.resetDynamicPopup(undefined); 

        let update  = {
            type: m.ERequestType.customerUpdateConsents,
            brandKey: BrandKeys.otelo,
            id: this.props.parent.data.id,
            bew: this.customer.bew
        };

        let cfg:IPopupConfig = {
            props: {
                id: 'dynamicPopup',
                title:  OrderingTexts.customerConsent,
                visible: true,
                closable: false,
                type: EPopupType.pleaseWait
            },
            content: <div>{OrderingTexts.customerData}</div>,
            callback: (api: IApiResponse)=>{
                let msg = ActivationMessages.saveSuccess;
        

                if(api.status != EApiResponseStatus.ok) {
                    var sum = Api.parseSummary(api, undefined);
                    msg = sum.message;
                } else {
                    StorageHelper.updateCustomer(this.props.parent.data.id, this.customer);
                    this.setState({ mode: EFormMode.view })
                };
                
                let cfg:IPopupConfig = {
                    props: {
                        id: 'dynamicPopup',
                        title:  OrderingTexts.customerConsent,
                        visible: true,
                        closable: true,
                        type: EPopupType.pleaseWait
                    },
                    content: <div>{msg}</div>,
                    action: <>
                    <page.LButton text={MainTexts.close} css="fr" icon="ok"
                        type={m.ELButtonType.buttonWithIconRight}
                        onClick={() => { 
                            this.props.parent.resetDynamicPopup(undefined); 
                        }}/>
                    </>
                };

            this.props.parent.processCustomer(()=>{ },cfg)
            }
        };
        
        this.props.parent.processCustomer(()=>{
            Global.hub.send(JSON.stringify(update),
            m.ERequestType.customerUpdateConsents,
                undefined);
        },cfg);                                

        Global.hub.send(JSON.stringify(update),
                        m.ERequestType.customerUpdateConsents,
                        undefined);        
    };

    private prepareConsents() {
        this.allConsents.email_EMA.selected = false;
        this.allConsents.emailLastChange.selected = false;
        this.allConsents.sms_mms_SMS.selected = false;
        this.allConsents.telefon_OBP.selected = false;
        this.allConsents.telefonContractExt_PVL.selected = false;
        this.allConsents.post_WHM.selected = false;

        if (this.customer.bew && this.customer.bew.consentExisting) {
            //let sel = this.customer.bew.adChannels ? this.customer.bew.adChannels.length > 0 : false;
            this.customer.bew.consentExisting.consents.forEach(consent => {
                if (consent.consent_type = "BEW") {
                        if (consent.hasOwnProperty("selected_ad_channels") && consent.selected_ad_channels && consent.selected_ad_channels.length > 0) {
                            this.allConsents.checkoutTerms.selected = true;
                            if(consent.selected_ad_channels){
                            consent.selected_ad_channels.forEach(channel => {
                                let sel = true;
                                //if(channel.selected) {
                                //    sel = true;
                                //};
                                switch (channel.ad_channel_type) {
                                    case "EMA":
                                        this.allConsents.email_EMA.selected = sel;
                                        this.allConsents.email_EMA.change_date = channel.change_date;
                                        break;
                                    case "ECU":
                                        this.allConsents.emailLastChange.selected = sel;
                                        this.allConsents.emailLastChange.change_date = channel.change_date;
                                        break;
                                    case "SMS":
                                        this.allConsents.sms_mms_SMS.selected = sel;
                                        this.allConsents.sms_mms_SMS.change_date = channel.change_date;
                                        break;
                                    case "OBP":
                                        this.allConsents.telefon_OBP.selected = sel;
                                        this.allConsents.telefon_OBP.change_date = channel.change_date;
                                        break;
                                    case "PVL":
                                        this.allConsents.telefonContractExt_PVL.selected = sel;
                                        this.allConsents.telefonContractExt_PVL.change_date = channel.change_date;
                                        break;
                                    case "WHM":
                                        this.allConsents.post_WHM.selected = sel;
                                        this.allConsents.post_WHM.change_date = channel.change_date;
                                        break;
                                }
                            
                            });
                        }
                    } 
                }
                if (consent.consent_type == "DAT") {
                    this.allConsents.data_handling_terms.selected = true;
                }
            })
        }
    };

    private selectAll() {
        this.printed = false;

        if(this.customer.bew && this.customer.bew.consentExisting) {
            this.customer.bew.adChannels = [];
            let bew = linq.from<ConsentGet | any>(this.customer.bew.consentExisting.consents).where(x=>x.consent_type == 'BEW').firstOrDefault();
            let idx = this.customer.bew.consentExisting.consents.indexOf(bew);
            if(bew) {
                if(this.customer.bew.consentExisting) {

                    bew.selected_ad_channels = bew.not_selected_ad_channels;
                    bew.not_selected_ad_channels = [];

                    if(!bew.selected_ad_channels || bew.selected_ad_channels.length == 0) {
                        bew.selected_ad_channels = [
                            {ad_channel_type: 'EMA', channel: 'RET', change_date: new Date()},
                            {ad_channel_type: 'ECU', channel: 'RET', change_date: new Date()},
                            {ad_channel_type: 'SMS', channel: 'RET', change_date: new Date()},
                            {ad_channel_type: 'OBP', channel: 'RET', change_date: new Date()},
                            {ad_channel_type: 'PVL', channel: 'RET', change_date: new Date()},
                            {ad_channel_type: 'WHM', channel: 'RET', change_date: new Date()},
                        ];
                    };

                    if(bew.selected_ad_channels) {
                        bew.selected_ad_channels.map((i)=>{
                            i.change_date = new Date();
                            i.selected = true;
                            if(this.customer.bew && this.customer.bew.adChannels) {
                                this.customer.bew.adChannels.push(i.ad_channel_type);
                            }
                        });
                    };

                    this.customer.bew.consentExisting.consents[idx] = bew;
                };                

            }
        };

        ////StorageHelper.updateCustomer(this.props.parent.data.id, this.customer);
         
        this.setState({ changed: true, activateAll: true, deactivateAll: false });
    };

    private deselectAll() {
        this.printed = false;
        if(this.customer.bew) {
            this.customer.bew.adChannels = [];
            this.customer.bew.consulting1 = false;
            if(this.customer.bew.consentExisting) {
                let bew = linq.from<ConsentGet>(this.customer.bew.consentExisting.consents).where(x=>x.consent_type == 'BEW').firstOrDefault();
                let idx = this.customer.bew.consentExisting.consents.indexOf(bew);
                if(bew) {
                    bew.not_selected_ad_channels = bew.selected_ad_channels;
                    if(bew.not_selected_ad_channels) {
                        bew.not_selected_ad_channels.map((i)=>{
                            i.selected = false;
                        });
                    };
                    bew.selected_ad_channels = [];
                };
                this.customer.bew.consentExisting.consents[idx] = bew;
            };
        };

        ////StorageHelper.updateCustomer(this.props.parent.data.id, this.customer);

        this.setState({ changed: true, activateAll: false, deactivateAll: true });

    };

    private canProcess() : boolean {
        if(this.customer.bew) {
            if(this.customer.bew.consulting1 && this.customer.bew.adChannels && this.customer.bew.adChannels.length > 0) {
                return true;
            } else if (this.state.deactivateAll) {
                return true;                
            } else if (!this.customer.bew.consulting1 && this.customer.bew.consulting2 && this.customer.bew.adChannels && this.customer.bew.adChannels.length == 0) {
                return true;
            } else if (!this.customer.bew.consulting1 && !this.customer.bew.consulting2 && this.customer.bew.adChannels && this.customer.bew.adChannels.length == 0) {
                return true;
            };
        };

        if(this.customer.bew) {
            if(!this.customer.bew.consulting1 && this.customer.bew.adChannels && this.customer.bew.adChannels.length > 0) {
                let cfg:IPopupConfig = {
                    props: {
                        id: 'dynamicPopup',
                        title:  OrderingTexts.customerConsent,
                        visible: true,
                        closable: true,
                        type: EPopupType.custom
                    },
                    content: <div>Sie mssen die Beratungseinwilligung besttigen.</div>,
                    action: <>
                        <page.LButton text={MainTexts.ok} css="fr" icon="ok"
                            type={m.ELButtonType.buttonWithIconRight}
                            onClick={() => { 
                                this.props.parent.resetDynamicPopup(undefined); 
                            }} />
                    </>
                };
        
                this.props.parent.processCustomer(()=>{ },cfg);

                return false;
            };
        };

        let cfg:IPopupConfig = {
            props: {
                id: 'dynamicPopup',
                title:  OrderingTexts.customerConsent,
                visible: true,
                closable: true,
                type: EPopupType.custom
            },
            content: <div>Bitte whle alle Kommunikationskanle aus, bevor du das Dokument ausdruckst und speicherst.</div>,
            action: <>
                <page.LButton text={MainTexts.ok} css="fr" icon="ok"
                    type={m.ELButtonType.buttonWithIconRight}
                    onClick={() => { 
                        this.props.parent.resetDynamicPopup(undefined); 
                    }} />
            </>
        };

        this.props.parent.processCustomer(()=>{ },cfg);

        return false;
    };
};

export default ProtectionAndConsent;