import * as React from 'react';
import CustomerScreen from '../../../screens/existing/customer';
import { IBaseComponentState, ISocketPushModel, IApiResponse, EApiResponseStatus, IProcessStatus, IApiSummary, IApiStep } from '../../../interfaces';
import { Container, Row, Col } from 'react-bootstrap';
import * as i from '../../../interfaces/index';
import * as m from '../../../models/index';
import { IContract } from '../../../models/existing/contract';
import StorageHelper from '../../../utils/storage';
import * as linq from 'linq';
import * as page from '../../page/index';
import ActivationResponseStatus from '../../ordering/process/activationResponseStatus';
import Status, { EStatusState } from '../../support/status';
import Global from '../../../utils/Global';
import { IPopupProps, EPopupType } from '../../dialogs/popup';
import { MainTexts, ActionTexts } from '../../../utils/messages';
import GlobalJSX from '../../../utils/GlobalJSX';
import Api from '../../../utils/api';
import { PartnerCardGet, CustomerGroup, IGroupMember, EGroupMemeberType, MobileNumber, TariffAndSub } from '../../../models/index';
import { BrandKeys } from '../../../utils/Defaults';
import IBrand from '../../../models/ordering/brand';
import ProductWrapper from '../../../models/ordering/productWrapper';

export interface IExistingPartnerCardProps {
    parent: CustomerScreen;
};

export interface IExistingPartnerCardState {
    status: m.ERequestStatus;
};

export class ExistingPartnerCard extends React.Component<IExistingPartnerCardProps, IExistingPartnerCardState> {
    private contract:IContract;
    private lastApi?: i.IApiResponse;
    private process?: i.IProcessStatus;
    private apiState: EStatusState = EStatusState.undefined;
    private current: m.PartnerCardGet = new m.PartnerCardGet();
    public static containerId: string = 'partnerCard';
    private brand: IBrand;

    constructor(props:IExistingPartnerCardProps) {
        super(props);

        this.onStart = this.onStart.bind(this);
        this.onDismissPopup = this.onDismissPopup.bind(this);
        this.socketCallback = this.socketCallback.bind(this);

        this.state = {
            status: m.ERequestStatus.undefined
        };

        this.current.id = this.props.parent.data.id;
        this.current.brandKey = BrandKeys.vodafone;

        this.brand = StorageHelper.getBrand(BrandKeys.vodafone);

        this.contract = this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0] : {};
    };

    public render() {
        let ctrl = <div id={ExistingPartnerCard.containerId}>
            {this.renderControl()}
        </div>;

        return ctrl;
    };

    private onStart(type: m.ERequestType) {
        this.process = { steps: [] };
        this.apiState = EStatusState.inprocessTimer;
        this.lastApi = undefined;

        this.current.type = type;

        Global.hub.send(JSON.stringify(this.current), 
            type,
            undefined);

        this.setState({status: m.ERequestStatus.running}, ()=>{       
        });
    };

    private onDismissPopup() {
        this.lastApi = undefined;
        this.setState({status: m.ERequestStatus.undefined});
    };

    private renderPopup() : JSX.Element {
        let cfg:IPopupProps = {
            title: ActionTexts.requestInProcess,
            closable: this.state.status !== m.ERequestStatus.running,
            visible: this.state.status !== m.ERequestStatus.undefined && this.state.status !== m.ERequestStatus.done,
            id: "partnerCardLoaderPopup",
            type: EPopupType.pleaseWait,
            onDismiss: this.onDismissPopup
        };

        let content:JSX.Element = <></>;

        if(this.lastApi && this.lastApi.status != EApiResponseStatus.ok) {
            let pr: IProcessStatus = JSON.parse(this.lastApi.data);
            let sum: IApiSummary = Api.parseSummary(this.lastApi, pr);

            cfg.closable = true;
            cfg.visible = true;
            cfg.type = EPopupType.info;
            content = <div>{sum.message}</div>
        };

        let ctrl = GlobalJSX.GetPopup(cfg, content, undefined);

        return ctrl;
    };    

    private renderGroups() : JSX.Element {
        let gr = this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0].groups : [];
        let jsx : JSX.Element[] = [];

        if(gr && gr.length > 0) {
            gr.map((i,idx)=>{
                jsx.push(this.renderGroup(i, idx));
            });
        };

        return <div className="">
            {jsx}
        </div>;
    };

    private renderGroup(group: CustomerGroup, idx: number) : JSX.Element {
        let gr = CustomerGroup.createInstanceFromJson(group);
        let owner = gr.getOwner();
        let members = gr.getMembers();
        //let m1: IGroupMember = JSON.parse(JSON.stringify(owner));
        //m1.type = EGroupMemeberType.partner;
        //members.push(m1);

        let ctrl = <div className={"group"} key={"group-" + idx}>
                <div className="text-sub uc">{MainTexts.group + ' ' + group.groupName}</div>
                <div className="spacer"/>
                {
                    this.renderMember(owner)
                }

                {
                    members && members.length > 0 ?
                    members.map((i, idx)=>(
                        this.renderMember(i)
                    ))
                    : null
                }
            </div>;

        return ctrl;
    };

    private renderMember(m: IGroupMember) : JSX.Element {
        let mobile:MobileNumber = MobileNumber.createInstanceFromJson(m.subscriberNumber);
        let css = (m.type === EGroupMemeberType.owner ? 'cardowner' : 'cardmember');
        
        //m.tariffOption = 'RED4SH30';
        let pr = TariffAndSub.createInstanceFromId(this.brand, m.tariffOption ? m.tariffOption : '');

        return <div className="group-member">
            <Container>
                <Row>
                    <Col md={1}>
                        <div className={"icon32 icon " + css}></div>
                    </Col>
                    <Col md={2}>
                        <div className={"text-tiny uc group-text " + css}>{m.type === EGroupMemeberType.owner ? MainTexts.owner : MainTexts.member}</div>
                    </Col>
                    <Col md={3}>
                        <div className={"text-body uc group-text group-text-big " + css}>{mobile.toString()}</div>
                    </Col>
                    <Col md={6}>
                        <div className={"text-body uc group-text group-text-big " + css}>{pr.productName}</div>
                    </Col>                    
                    {/* <Col md={2}>
                        <div className={"text-body uc group-text group-text-big " + css}>{Global.toGermanDate(m.effectiveDate)}</div>
                    </Col>                                         */}
                </Row>
            </Container>
        </div>;
    };

    private renderControl() : JSX.Element {
        let ctrl = 
            <>
            <Container>
                <Row>
                    {
                        this.state.status !== m.ERequestStatus.running ? 
                        <>
                            {/* <page.LButton text="gruppeninformation abrufen" 
                                onClick={()=>{this.onStart(m.ERequestType.customerGroupGet);}} 
                                css="fr"
                                type={m.ELButtonType.buttonWithIconRight} 
                                icon="arrow-right"/> */}
                            {
                                !this.props.parent.dataWrapper.hasPartnerGroups() ? 
                                <>
                                    <Col md={9}>Der Kunde besitzt keine Gruppenzugehrigkeit. <br/>* Um Partnerkarten (RED+) zu buchen, muss der Kunde einer Gruppe angehren.</Col>
                                    <Col md={3}>
                                        <page.LButton text="Gruppe erstellen" 
                                            onClick={()=>{this.onStart(m.ERequestType.customerGroupCreate);}} 
                                            css="fr"
                                            type={m.ELButtonType.buttonWithIconRight} 
                                            icon="arrow-right"/>
                                    </Col>
                                </>
                                : null
                            }
                        </>
                            :
                        null
                    }    
                </Row>      
                                     
                <Row>
                    <Col md={12} className="np">
                        {this.renderGroups()}
                    </Col>
                </Row>
            </Container>

            { this.renderPopup() }

            </>;      

        return ctrl;
    };

    public socketCallback(sock: ISocketPushModel) {
        let api: IApiResponse = JSON.parse(sock.data);
        this.lastApi = api;
        let inner = api.data;

        if(api.error) {
            this.setState({status: m.ERequestStatus.failed});
            return;
        };

        if(inner == null ) {
            return;
        };

        let wrapper = JSON.parse(inner.data);

        let customer  = JSON.parse(wrapper.steps[0].message);

        this.props.parent.data.customer = customer;

        StorageHelper.addOrder(this.props.parent.data);

        this.setState({status: m.ERequestStatus.done});

        this.props.parent.refresh();
    };
};

export default ExistingPartnerCard;