import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import BaseBlock from '../../../components/page/block';
import LClear from '../../../components/page/clear';
import { EApiResponseStatus, IApiResponse } from '../../../interfaces';
import { IActivation, OrderDataWrapper } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { EOrderProcessView } from '../../../models/ordering/order';
import { ELButtonType, ERequestType } from '../../../models/page/diverse';
import { CombiWrapper } from '../../../models/support/ISupportWrapper';
import ProcessNewScreen from '../../../screens/ordering/process';
import { ValidationWrapper } from '../../../utils';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { MainTexts } from '../../../utils/messages';
import EasyRouter from '../../../utils/router';
import StorageHelper from '../../../utils/storage';
import PopupBody from '../../dialogs/body';
import Popup, { EPopupType } from '../../dialogs/popup';
import * as page from '../../page/index';

export interface IActivationActionsProps {
    parent: ProcessNewScreen;
    progress: EOrderProcessView;
    onNext?: any;
    onPrev?: any;
};

export interface IActivationActionsState {
    imei: string,
    areYouSureVisibility: boolean;
};

export class ActivationActions extends React.Component<IActivationActionsProps, IActivationActionsState> {
    private processPopupState: EPopupType = EPopupType.custom;
    constructor(props: IActivationActionsProps) {
        super(props);
        this._onDismissProcessPopup = this._onDismissProcessPopup.bind(this);
        this.onYesClicked = this.onYesClicked.bind(this);

        this.state = {
            imei: '',
            areYouSureVisibility: false
        };
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: '',
            transparent: true
        };

        let ctrl = (
            <>
                <Popup
                    type={this.processPopupState}
                    visible={this.state.areYouSureVisibility}
                    closable={true}
                    id="areYouSure"
                    title={"Warnung"}
                    height={200}
                    onDismiss={this._onDismissProcessPopup}>
                    <PopupBody>
                        <h5>Sie gelangen jetzt zurck zum Dashboard. Dadurch werden die von Ihnen eingegebenen Daten gelscht.</h5>
                        <div className="spacer clear"></div>
                        <div className="spacer clear"></div>
                        <div className="spacer clear"></div>
                        <page.LButton
                            text={"Ja"}
                            onClick={this.onYesClicked}
                            type={ELButtonType.buttonWithIconRight}
                        />

                        <page.LButton
                            text={"Nein"}
                            onClick={this._onDismissProcessPopup}
                            type={ELButtonType.buttonWithIconRight}
                        />
                        <></>
                    </PopupBody>
                    <></>
                </Popup>
                <BaseBlock config={cfg}>
                    <Container className="container-action">
                        {this.renderActions()}
                    </Container>
                    <LClear />
                </BaseBlock>
            </>
        );

        return ctrl;
    };

    private onYesClicked() {
        StorageHelper.removeOrder(this.props.parent.data.id, () => {
            EasyRouter.route({ url: '/', title: Global.getConfiguration().appTitle });
        });
    }


    _onDismissProcessPopup = () => {
        this.setState({ areYouSureVisibility: false });
    }

    private renderActions(): JSX.Element {
        if (this.props.progress == EOrderProcessView.customer) {
            return this.renderCustomerActions();
        } else if (this.props.progress == EOrderProcessView.done) {
            return this.renderCustomerPreview();
        } else if (this.props.progress == EOrderProcessView.tariff) {
            return this.renderCustomerTariff();
        }

        return <></>;
    };

    private renderCustomerPreview(): JSX.Element {
        let ctrl = (
            <Row>
                <Col xs={4} md={4}>
                    <page.LButton text={MainTexts.back}
                        onClick={this.onBack}
                        type={ELButtonType.linkSimple}
                        css="action3"
                        icon="chevron" />
                </Col>
                <Col xs={8} md={8}>
                    <page.LButton text={MainTexts.toDashboard}
                        css="fr"
                        onClick={() => {
                            this.setState({ areYouSureVisibility: true })
                            // StorageHelper.removeOrder(this.props.parent.data.id, ()=>{
                            //     EasyRouter.route({url: '/', title: Global.getConfiguration().appTitle});
                            // });
                        }}
                        type={ELButtonType.buttonWithIconRight}
                        icon="" />
                </Col>
            </Row>
        );

        return ctrl;
    }

    private renderCustomerActions(): JSX.Element {
        let ctrl = (
            <Row>
                <Col xs={4} md={4}>
                    <page.LButton text="ZURCK"
                        onClick={this.onBack}
                        type={ELButtonType.linkSimple}
                        css="action3"
                        icon="chevron" />
                </Col>
                <Col xs={8} md={8} className="toright">
                    <page.LButton text="WEITER"
                        onClick={this.onNext}
                        css="fr"
                        type={ELButtonType.buttonWithIconRight}
                        icon="arrow-right" />
                </Col>
            </Row>
        );

        return ctrl;
    }

    private renderCustomerTariff(): JSX.Element {
        let ctrl = (
            <Row>
                <Col xs={4} md={4}>
                    {/* <page.LButton text="FERTIG" 
                        onClick={this.onDone} 
                        type={ELButtonType.linkSimple} 
                        css="action3"
                        icon="chevron"/> */}
                </Col>
                <Col xs={8} md={8} className="toright">
                    <page.LButton text="WEITER ZU DEN KUNDENDATEN"
                        onClick={()=>{
                            this.onNext();
                        }}
                        css="fr"
                        type={ELButtonType.buttonWithIconRight}
                        icon="arrow-right" />
                </Col>
            </Row>
        );

        return ctrl;
    };

    private onBack = () => {
        this.props.parent.dataWrapper.resetUnfinishedActivations();
        if (this.props.progress == EOrderProcessView.done) {
            this.props.parent.gotoView(EOrderProcessView.customer);
        } else if (this.props.progress == EOrderProcessView.customer) {
            this.props.parent.gotoView(EOrderProcessView.tariff);
        }; 
    };

    private onNext = () => {
        if (!ValidationWrapper.validateFormular(this.props.parent.dataWrapper, true, 'container-main')) {
            return;
        };

        let cw = CombiWrapper.GetInstance();

        cw.validate(this.props.parent.dataWrapper, (dw : OrderDataWrapper, changed : boolean)=>{
            //**if basket has unity we need to resync it with the backend */
            if(dw.hasUnity()) {
                let a:IActivation|undefined = dw.getActivationByBrand(BrandKeys.unity);
                this.props.parent.syncOrder(a!, (res: IApiResponse) => {
                    if (res.status == EApiResponseStatus.ok) {
                        StorageHelper.updateAvailability(BrandKeys.unity, res.data.avl);
                        this.onNextInternal();
                    };
                });
            } else {
                this.onNextInternal();
            }
        });            
    };

    private onNextInternal = () => {
        if (this.props.onNext) {
            this.props.onNext();
            return;
        }

        if (this.props.progress == EOrderProcessView.tariff) {
            //** if otelo in the batch validate the msisdn first */
            if (this.props.parent.dataWrapper.hasOtelo()) {
                this.props.parent.onCreateProcess(ERequestType.checkMsisdn);
            } else {
                this.props.parent.gotoView(EOrderProcessView.customer);
            }
        } else if (this.props.progress == EOrderProcessView.customer) {
            this.props.parent.gotoView(EOrderProcessView.done);
        };
    };
};

export default ActivationActions;