import * as linq from 'linq';
import { BrandKeys, CatKeys } from "../../utils/Defaults";
import Global from "../../utils/Global";
import StorageHelper from "../../utils/storage";
import IBrand from "../ordering/brand";
import { OrderDataWrapper } from "../ordering/data";
import { ICountry, IFocusBox, IListItem } from "../page/interfaces";
import { IField } from './field';
import { ProductTree, TreeItem } from "./tree";

export interface ISupportWrapper {
    countries: ICountry[];
    networks: IListItem[];
    fields: IField[];
    boxes: IFocusBox[];
};

export class CombiWrapper {
    items: Combi[] = [];
    pt: ProductTree = new ProductTree('', '');
   public static combisWereAdded: boolean = false;

    combis: Combi[] = []

    public static GetInstance(): CombiWrapper {
        let c = StorageHelper.getCombis();

        Object.assign(new CombiWrapper(), c);

        return c;
    };

    public validate(dw: OrderDataWrapper, callback: any) {
        this.pt = dw.getProducts();

        let changed : boolean = false;

        if (this.needGigaCombi()) {
            let c = this.findCombi(false);

            if (c) {
                c.items.map((i) => {
                    var a = dw.getActivationByProduct(i.product!.id);
                    let b = linq.from<IBrand>(Global.categoryWrapper.bra)
                        .where(x => x.id == i.product!.brand)
                        .first();

                    //try add services if not present
                    i.services.map((j) => {
                        if (a && !a.hasService(j, i.product!.id)) {
                            let sa: string[] = a.checkServiceCompatibility(j, b);

                            if (sa.length) {
                                sa.map((k) => {
                                    a!.removeService(k, i.product!.id);
                                    CombiWrapper.combisWereAdded = false;
                                })
                            };
                            CombiWrapper.combisWereAdded = true;
                            changed = true;
                            a.addService(j, i.product!.id);
                        };
                    });

                    //append benefits if any
                    if (c && c.benefits && c.benefits.length > 0 && a && a.catKey == CatKeys.fn) {
                       
                        c.benefits.map((j) => {
                            if (a && !a.hasService(j, i.product!.id)) {
                                let sa: string[] = a.checkServiceCompatibility(j, b);

                                if (sa.length) {
                                    sa.map((k) => {
                                        a!.removeService(k, i.product!.id);
                                    })
                                };

                                //** for cable we shold add the fn.benefit to the gigakombi item */
                                if (a.brandKey == BrandKeys.kabel) {
                                   
                                    let citem = linq.from<CombiItem>(c!.items).where(x => x.cat == CatKeys.fn).first();
                                    if(i.product!.cat == CatKeys.fn) {
                                        
                                        let pindex = a.selection.getChildIndex(i.product!.id);
                                        TreeItem.addRemovePath(a.selection.children[pindex], [citem.services[0], j]);
                                        StorageHelper.updateActivation(a);
                                    }
                                } else {
                                    a.addService(j, i.product!.id);
                                    StorageHelper.updateActivation(a);
                                };

                            };
                        });
                    };
                });
            };
        }

        if (callback) {
            callback(dw, changed);
        };
    };

    public needGigaCombi() {
        let cats = this.pt.getCats();

        return linq.from<string>(cats).where(x => x == CatKeys.vodafone || x == CatKeys.tv || x == CatKeys.fn).distinct().count() > 1;
    };

    public getFirstSuitableCombi(dw: OrderDataWrapper,
        matchServices: boolean) {
        this.pt = dw.getProducts();

        if (!this.needGigaCombi()) {          
            return undefined;
        };

        let c = this.findCombi(matchServices);

        return c ? Combi.ensure(c) : undefined;
    };

    private findCombi(matchServices: boolean): Combi | undefined {
        for (let i: number = 0; i < this.items.length; i++) {
            let all: number = 0;
            for (let j: number = 0; j < this.pt.children.length; j++) {
                let ci = this.fitInCombi(this.items[i], this.pt.children[j]);
                if (ci) {
                    if (matchServices) {
                        if (TreeItem.findChild(this.pt.children[j], ci.services[0])) {
                            all++;
                        };
                    } else {
                        all++;
                    };
                } else {
                    continue;
                }
            };

            if (all == this.pt.children.length && all == this.items[i].items.length) {             
                return this.items[i]; }
        };

        return undefined;
    };

    //** if matchServices true, then the services in selection are being checked too */
    private fitInCombi(c: Combi, p: ProductTree) {
      //  let ci = linq.from<CombiItem>(c.items).where(x => x.cat == p.cat && x.familyId == p.familyId ).firstOrDefault();
      let ci ;

        c.items.forEach(kombi => {
            if(kombi.cat==CatKeys.vodafone){
                if(kombi.cat== p.cat && kombi.productId == p.id){
                    ci = kombi;
                }
            }else {
                if(kombi.cat == p.cat && kombi.familyId == p.familyId){
                    ci = kombi
                }
            }
        });
        
        if (ci) {         
            ci.product = p;
        }

        return ci;
    };
};

export class Combi {
    items: CombiItem[] = [];
    benefits: string[] = [];
    markup: string = '';

    public static ensure(c: Combi) {
        c = Object.assign(new Combi(), c);

        return c;
    };

    public getBlister() {

        if (this.markup) {
            let json = JSON.parse(this.markup);
            return json ? json.product_ui.ui_blister : null;
        }

        return null;
    };
};

export class CombiItem {
    cat: string = '';
    familyId: number = 0;
    productId: string = '';
    services: string[] = [];

    product?: ProductTree;
};
