import * as React from 'react';
import { LClear } from '.';
import { IFilter } from '../../models';
import { TablePaging } from './paging';
import { MainTexts } from '../../utils/messages';
import { ErrorMessage } from '../dialogs/errorMessage/errorMessage';

export enum EPagingType {
    none,
    local,
    server
}

export interface ITablePagingProps {
    type: EPagingType
    page: number;
    count: number;
}

export interface IPagingModel {
    page: number,
    count: number
}
export enum ESortDirection {
    Asc, Desc
}

export interface ITable {
    coulmns: IColumn[];
    data?: any;
    id: string;
    expnaded: boolean;
    itemClick?: any;
    dateColumn?: string;
    filter?: IFilter;
    pagingProps: ITablePagingProps;
}

export interface ITableState {
    sortSource?: string;
    pagingModel: IPagingModel;
    sortCol?: IColumn;
    columns: IColumn[];
}

export interface IColumn {
    label: string;
    labelShort?: string;
    binding: string;
    size: number;
    sortable: boolean;
    filterable?: boolean;
    renderer?: any;
    rendererHead?: any;
    style?: string;
    showTitle?: boolean;
    isHtml?: boolean;
    sortDirection?: ESortDirection
}

export class Table extends React.Component<ITable, ITableState> {
    private me: any;
    public currentId: string = '';
    private data?: any[];
    private hasMoreItems: boolean = true;
    private totalItemsCount: number = 0;

    constructor(props) {
        super(props);

        this.me = React.createRef()

        this.itemClick = this.itemClick.bind(this);
        this.onPaginationPageChanged = this.onPaginationPageChanged.bind(this);

        this.data = this.props.data;

        this.state = {
            columns: this.props.coulmns,
            sortCol: undefined,
            pagingModel: { page: this.props.pagingProps.page, count: this.props.pagingProps.count }
        };
    }

    render() {
        this.data = this.props.data;
        this.filterAndPaginate();
        let css: string = 'oe-table';

        let paging = <></>;

        if (this.props.pagingProps.type !== EPagingType.none) {
            const { pagingModel } = this.state;
            paging = <TablePaging totalItemsCount={this.totalItemsCount}
                parent={this}
                pagesToRender={5}
                model={pagingModel} />
        }
        
        let ctrl = <div id={this.props.id} className={css} >
            <div>
                <ErrorMessage visible={this.totalItemsCount === 501 ? true : false} text="Es werden mehr als 500 Datenstze geliefert. Bitte Filter anwenden." />

                {this.renderTable(paging)}
            </div>
        </div>;

        return ctrl;
    }

    onPaginationPageChanged(page: number) {
        let { pagingModel } = this.state;
        pagingModel.page = page;
        this.setState({ pagingModel });
    }

    private filterAndPaginate() {
        let size = this.me.current;

        this.data = this.props.data;

        if (this.state.sortCol) {
            this._sort(this.state.sortCol, this.data);
        }

        // apply filter
        if (this.props.filter) {
            this.data = this.applyFilter(this.data);
        }

        // apply paging
        if (this.props.pagingProps.type === EPagingType.local) {
            this.data = this.applyLocalPagging(this.data);
        } else if (this.props.pagingProps.type === EPagingType.server) {
            this.data = this.applyServerPagging(this.data);
        }
    }

    private renderTable(pager: JSX.Element): JSX.Element[] {
        let html: JSX.Element[] = [];

        html.push(this.renderRow(0, true));

        if (this.data && this.data.length > 0) {
            for (let i: number = 0; i < this.data.length; i++) {
                html.push(this.renderRow(i, false));
            };
        } else {
            html.push(<div className="oe-row odd">
                <div className="col data" style={{ width: this.getWidth(12) + "%" }}>
                    <p className="text-center"><strong>{MainTexts.noData}</strong></p>
                </div>
                <LClear />
            </div>);
        }

        html.push(this.renderFooter(pager));

        return html;
    };

    private renderHead(col: IColumn, index: number): JSX.Element {
        let css: string = 'col head';

        let val = col.rendererHead ? col.rendererHead(col) : 
        (!this.props.expnaded && col.labelShort ? col.labelShort : col.label);

        let sortableHtml = <></>;

        let sortButton = <div className="icon arrow-up" onClick={() => { this._sortAscending(col) }} />;
        if (col.sortDirection === ESortDirection.Asc) {
            sortButton = <div className="icon arrow-down" onClick={() => { this._sortDescending(col) }} />;
        }

        if (col.sortable) {
            sortableHtml = <div className="col-head-sort-icons">
                {sortButton}
            </div>;
        }

        return <div className={css} title={col.showTitle ? val : ''} key={"head-" + index} style={{ width: this.getWidth(col.size) + "%" }}>
            <div className="col-head-text">
                {val}
            </div>
            {sortableHtml}
        </div>;
    }

    _resetSortIcons = (sortCol) => {
            let cols = this.state.columns;
            for (let i: number = 0; i < cols.length; i++) {
                let col = cols[i];
                if (sortCol !== col && col.sortable) {
                    col.sortDirection = ESortDirection.Desc;
                }
            }

            return cols;
    };

    _sortDescending = (col: IColumn) => {
        col.sortDirection = ESortDirection.Desc;
        let cols = this._resetSortIcons(col);
        this.setState({ sortCol: col, columns: cols });
    };

    _sortAscending = (col: IColumn) => {
        col.sortDirection = ESortDirection.Asc;
        let cols = this._resetSortIcons(col);
        this.setState({ sortCol: col, columns: cols });
    };

    _sort = (col: IColumn, data: any[] | undefined) => {
        
        if (col && data && data.length > 0) {
            if (col.sortDirection === ESortDirection.Asc) {
                data.sort((a, b) => (a[col.binding] >= b[col.binding]) ? 1 : -1)
            } else { // desc
                data.sort((a, b) => (a[col.binding] <= b[col.binding]) ? 1 : -1)
            }
            
            this.data = data;
        }
    };

    private renderFooter(pager: JSX.Element): JSX.Element {
        return <div key="footer" className="oe-row last">
            {pager}
        </div>;
    };

    private renderRow(idx: number, head: boolean): JSX.Element {
        let html: JSX.Element[] = [];
        let id: string = '';

        for (let i: number = 0; i < this.state.columns.length; i++) {
            if (head) {
                html.push(this.renderHead(this.state.columns[i], i));
            } else if (this.data !== undefined) {
                let row = this.data[idx];
                id = this.data[idx].id;

                if(!id) {
                    id = this.data[idx].Id;
                };

                html.push(this.renderColumn(row, this.state.columns[i], head, i));
            }
        }

        let css: string = idx % 2 == 0 ? 'odd' : 'even';

        //css += last ? ' last' : '';

        return <div key={'key-' + (head ? 'head' : idx)}
            id={id}
            className={'oe-row ' + css}
            onClick={head ? undefined : (e) => { this.itemClick(e, id) }}>
            {html}
            <LClear />
        </div>;
    };

    private renderColumn(row: any, col: IColumn, head: boolean, index: number): JSX.Element {
        let css: string = 'col data ' + (col.style ? col.style : '');

        if (!col.renderer) {
            css += ' text-sub';
        };

        let val = col.renderer && !head ? col.renderer(row, col) : row[col.binding];

        // if(val && (typeof val === 'string' || val instanceof String)) {
        //     val = val.toString().replace(/<br\/>/g, '');
        // }

        if (col.showTitle) {
            if (col.isHtml) {
                return <div key={"col" + index} title={val} className={css} style={{ width: this.getWidth(col.size) + "%" }}
                    dangerouslySetInnerHTML={{ __html: val }} />;
            } else {
                return <div key={"col" + index} title={val} className={css} style={{ width: this.getWidth(col.size) + "%" }}>{val}</div>;
            }
        }

        return <div key={"col" + index} className={css} style={{ width: this.getWidth(col.size) + "%" }}>{val}</div>;
    };

    private applyLocalPagging(data?: any[]) {
        if (data === undefined) {
            return;
        }

        const { pagingModel } = this.state;
        let totalCount = data.length || 0;

        this.totalItemsCount = totalCount;

        let page = pagingModel.page;
        let count = pagingModel.count;

        this.hasMoreItems = (page * count) < totalCount;
        // if user wants page that is negative we return first page
        if (page <= 1) {
            page = 0;
        }
        else { // we need to decrement the page always by 1 
            page--;
        }

        if (count <= 0) {
            count = 10;
        } else if (count > totalCount) {
            count = totalCount;
        }

        if (page * count >= totalCount) // we need to return last page
        {
            if ((totalCount / count) % 2 == 0) {
                page = (totalCount / count) - 1;
            }
            else {
                page = (totalCount / count);
            }
        }

        // do the paging
        let result: any[] = [];
        let i = page * count; // skip
        let take = i + count;

        if (take > data.length) {
            take = data.length;
        }


        for (i; i < take; i++) { // take
            result.push(data[i])
        }

        return result;
    };

    private applyServerPagging(data) {
        return data; // TODO implement server paging
    };

    private applyFilter(data) {
        let d = [] as any;

        if (this.props.filter && this.data !== undefined) {
            if (this.props.filter.to) { this.props.filter.to.setHours(0, 0, 0, 0); }
            if (this.props.filter.from) { this.props.filter.from.setHours(0, 0, 0, 0); }
            let f = this.props.filter;
            for (let i = 0; i < data.length; i++) {
                let row = data[i];
                let dateok: boolean = true;

                if (this.props.dateColumn) {
                    let v = new Date(Date.parse(row[this.props.dateColumn].substring(0, 10)));

                    v.setHours(0, 0, 0, 0);

                    if (this.props.filter.from && this.props.filter.to && v >= this.props.filter.from && v <= this.props.filter.to) {
                        dateok = true;
                    };
                };

                for (let j = 0; j < this.state.columns.length; j++) {
                    if (!this.state.columns[j].filterable) {
                        continue;
                    };

                    let c = this.state.columns[j];

                    if (!c.binding) { continue; };

                    if (dateok && row[c.binding] && (typeof row[c.binding] === 'string') && row[c.binding].toLowerCase().indexOf(f.needle.toLowerCase()) >= 0) {
                        d.push(row);
                        break;
                    };
                };
            };
        } else {
            return data;
        }

        return d;
    };

    private getWidth(size: number): number {
        return 100 * size / 12;
    };

    private itemClick(ev: any, id: string) {
        this.currentId = id;
        if (this.props.itemClick && id) {
            this.props.itemClick(ev, id);
        } else {
            ev.preventDefault();
            ev.stopPropagation();
        };
    };
};

export default Table;