import * as React from 'react';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { Row, Col, Container } from 'react-bootstrap';
import BaseBlock from '../../../components/page/block';
import * as page from '../../page/index';
import { EControlType } from '../../../models/page/diverse';
import { ProcessNewScreen } from '../../../screens/insex';
import { IControlValue } from '../../../models/page/interfaces';
import StorageHelper from '../../../utils/storage';
import { ICustomer } from '../../../models/ordering/customer';
import { MainTexts } from '../../../utils/messages';

export interface ICustomerConsentProps {
    //parent: ProcessNewScreen;
    customer: ICustomer;
    parentRefresh: any;
    //onChange: any;
};

export interface ICustomerConsentState {
    changed: boolean;
};

export class CustomerConsent extends React.Component<ICustomerConsentProps, ICustomerConsentState> {
    public static containerId: string = 'customerConsentContainer';
    constructor(props: ICustomerConsentProps) {
        super(props);

        this.state = {
            changed: false
        };
    };

    public render() {
        let cfg: IBlockConfiguration = {
            id: CustomerConsent.containerId,
            title: MainTexts.letterOfAceptance,
            transparent: false
        };

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={1} md={1} className="np">
                            <page.LControl type={EControlType.checkbox} onChange={(v:IControlValue)=>{
                                this.props.customer.bew!.consent = v.checked ? v.checked : false;
                                if(this.props.parentRefresh) {
                                    this.props.parentRefresh();
                                };
                                //StorageHelper.addOrder(this.props.parent.data);
                                //this.props.parent.refresh();
                            }} name="consent" id="consent_true" 
                            hideTitle={true} label="" 
                            checked={this.props.customer.bew ? this.props.customer.bew.consent : false}/>
                        </Col>
                        <Col xs={11} md={11} className="npr">
                            <page.LControl type={EControlType.hint} label="Bestandteil des Vertrages sind die Allgemeinen Geschftsbedingungen der Vodafone GmbH sowie die Preisliste fr otelo Mobilfunkdienstleistungen. <br/><br/>Vodafone Kabel Deutschland/Vodafone tauscht Ihre personenbezogenen Daten zur Durchfhrung von Bonittsprfungen (Bonittsdaten) vor Auftragsannahme mit der Schufa GmbH sowie mit sonstigen Auskunfteien gem. Ziff. 7 der Datenschutz-Hinweise aus."/>
                        </Col>                        
                    </Row>

                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };    
};

export default CustomerConsent;