import * as linq from 'linq';
import * as React from 'react';
import BaseBlock from '../../components/page/block';
import { IBlockConfiguration } from '../../models/block/configuration';
import { EBusinessCase } from '../../models/ordering/order';
import IProduct from '../../models/ordering/product';
import Global from '../../utils/Global';
import OrderingHelper from '../../utils/OrderingHelper';

export interface IBrandTariffsListProps {
    brandKey: string;
    products: IProduct[];
    block: IBlockConfiguration;
    onClick: any;
    style?: string;
    styleContainer?: string;
};

export interface IBrandTariffsListState {
    selected: string;
};

export class BrandTariffsList extends React.Component<IBrandTariffsListProps, IBrandTariffsListState> {
    constructor(props: IBrandTariffsListProps) {
        super(props);

        this.onSelect = this.onSelect.bind(this);
        this.onClickBlister = this.onClickBlister.bind(this);

        this.state = {
            selected: ''
        };
    }

    public render() {
        if(!Global.categoryWrapper.availableBrands || Global.categoryWrapper.availableBrands.length == 0) {
            return <></>
        };

        let cfg: IBlockConfiguration = {
            title: this.props.block.title,
            transparent: true,
            styleContainer: this.props.styleContainer,
            action: this.props.block.action
        };

        let prod: IProduct[] = OrderingHelper.filterProducts(this.props.brandKey,
            this.props.products,
            EBusinessCase.bnt);

        prod = prod.filter(x => x.sortFocus! !== 0);

        prod = prod.sort((a, b) => a.sortFocus! - b.sortFocus!);

        prod = linq.from<IProduct>(prod).take(4).toArray();

        let thumbs: JSX.Element[] = [];

        prod.map((i, idx) => {
            let pr = i.monthlyFee.price.toString();

            let html = <div key={'btl-' + idx} onClick={this.onClickBlister} dangerouslySetInnerHTML={{ __html: i.blister }} />
            thumbs.push(html);
        })

        let ctrl = (
            <div>
                <BaseBlock config={cfg}>
                    {thumbs}
                </BaseBlock>
            </div>
        );

        return ctrl;
    };

    private onClickBlister(e) {
        let dom: any;
        if (e.target) { dom = e.target; } else { dom = e; }
        if (!dom) { return; };

        if (dom.classList.contains('thumb-blister')) {
            this.onSelect(e.getAttribute('blister-action'));
            return;
        } else {
            this.onClickBlister(dom.parentNode);
        }
    };

    private onSelect(key: string) {
        if (this.props.onClick) {
            this.props.onClick(key);
        }
    };

    private onClick(e: any) {
        if (this.props.onClick) {
            this.props.onClick(e);
        }
    };
};

export default BrandTariffsList;