import * as React from 'react';
import { IDefCon, EAppState, ELButtonType } from '../../models';
import StorageHelper from '../../utils/storage';
import Global from '../../utils/Global';
import App from '../../App';
import { Container, Row, Col } from 'react-bootstrap';
import { IPopupConfig, EPopupType } from '../dialogs/popup';
import { LButton } from '../page';
import GlobalJSX from '../../utils/GlobalJSX';

export interface ITeaserState 
{
    popupVisible: boolean;
};

export class Teaser extends React.Component<any, ITeaserState> {
    constructor(props) {
        super(props);

        this.state = {
            popupVisible: false
        };
    }; 

    public render() {
        let ctrl = <>
            <div className="teaserTop">
                <div className="tac inner" onClick={()=>{
                    this.setState({popupVisible: true});
                }}>
                Gesenkte MwSt.? Geben wir weiter! Hier erfhrst du mehr.
                </div>
            </div>

            {
                this.state.popupVisible && this.renderPopup()
            }
        </>

        return ctrl;
    };

    private renderPopup() {
        let ctrl = (
            <Container className="full tal">
              <Row>
                <Col xs={12} md={12} className="npr">
                    <b>Die dargestellten Preise enthalten noch die MwSt. von 19 %. Fr vom 01.07. bis 31.12.2020 erbrachte Leistungen findest Du auf Deiner Rechnung eine entsprechende Senkung der MwSt. auf 16 % fr Leistungen, die der regulren MwSt. unterliegen. Gilt fr alle Telekommunikationstarife mit Mindestlaufzeit und Mietentgelte fr Gerte. </b>
                    <br/><br/><span className="small">Fr Prepaid gilt: Fr Aufladungen, die in der Zeit vom 1.7. bis zum 31.12.2020 vorgenommen werden, schreiben wir je 2,52 % des Auflade-Betrags dem Guthaben zustzlich gut. Beispiel: Bei einer Aufladung von 15 Euro schreiben wir dem Kundenkonto 15,38 Euro gut. Gilt nicht fr CallNow Transfer. Die Gutschrift des zustzlich gewhrten Guthabens bei den Auflade-Wegen Voucher, Bankautomat, Bezahl-Terminal und Bankeinzug/Komfort-Aufladung erfolgt zeitgleich mit der Aufladung. Bei anderen Auflade-Wegen (Paypal, Kreditkarte, Sofortberweisung und Bankberweisung) erfolgt die Gutschrift innerhalb von 24 Stunden nach Geldeingang. Die Gutschrift des zustzlich gewhrten Guthabens kann bei otelo bis zu 72 Stunden dauern. </span>
                </Col>
              </Row>
            </Container>
          )
      
          let cfg: IPopupConfig = {
            props: {
              id: 'dynamicPopup',
              title: '',
              visible: true,
              closable: false,
              style: 'tal',
              type: EPopupType.custom,
              height: 400,
              width: 700
            },
            content: ctrl,
            action: <>
              <LButton type={ELButtonType.buttonWithIconRight}
                css="a1 fr"
                id=""
                text={"OK"}
                onClick={() => {
                  this.setState({popupVisible: false});
                }} />
            </>
          };

          return GlobalJSX.GetPopup(cfg.props, cfg.content, cfg.action);
    };
};

export default Teaser;