import * as React from 'react';
import * as linq from 'linq';
import { Spinner, Container, Row, Col } from 'react-bootstrap';
import Global from '../../utils/Global';
import { MainTexts, ActionTexts } from '../../utils/messages';
import { IColumn, EPagingType } from '../../components/page/table';
import * as page from '../../components/page';
import { ELButtonType } from '../../models';
import Api from '../../utils/api';
import { IPopupProps, EPopupType } from '../../components/dialogs/popup';
import GlobalJSX from '../../utils/GlobalJSX';
import * as m from '../../models/index';
import { EApiResponseStatus, IApiResponse, IApiSummary } from '../../interfaces';
import { IControlValue } from '../../models/page/interfaces';
import { IBlockConfiguration } from '../../models/block/configuration';
import { BaseBlockAction } from '../../components/page/block';
import BaseBlock from '../../components/page/block';

export interface ICancellationScreenProps {};

export interface ICancellationScreenState {
    expanded: boolean;
    status: m.ERequestStatus;
    data?: any;
    selection: number[];
    accept?: boolean;
    loading?: boolean;
    processing?: boolean;
};

export class CancellationScreen extends React.Component<ICancellationScreenProps,ICancellationScreenState> {
    public static containerId: string = 'cancellations';
    private lastApi?: IApiResponse;

    constructor(props) {
        super(props);

        this.state = {
            expanded: false,
            status: m.ERequestStatus.undefined,
            selection: []
        };
    };

    public componentDidMount() {
        this.setState({status: m.ERequestStatus.running, loading: true});
        this.refresh();
        setTimeout(() => {
            Global.easyApp.setTitle(MainTexts.cancellations);
        }, 1000);
    };

    render () {
        return <div id={CancellationScreen.containerId}>
                {this.renderData()}
                {this.renderPopup()}
        </div>
    };

    private renderData(): JSX.Element {
        let cfg = Global.getConfiguration();

        let cols: IColumn[] = [
            { label: 'Auftragsart', binding: 'x1', size: 2, sortable: true, renderer: (c, r) =>{
                let s = '';
                if(c.TypeId == 7) { s=  'Ausserordentliche Stornierung'}
                else {
                    s = c.TypeText;
                }

                s += ' [' + c.Id + ']'; 

                return s;
            } },

            { label: 'Vertrag', binding: 'ContractId', size: 2, sortable: true },
            { label: 'MSISDN', binding: 'MSISDN', size: 2, sortable: true },
            { label: 'VOID', binding: 'Void', size: 2, sortable: true },
            { label: 'Hndlername', binding: 'AccountName', size: 2, sortable: true },
            {
                label: 'Stornierungsdatum', filterable: true, style: 'text-sub', binding: 'CancellationDate', size: 1, sortable: true, renderer: (r, c) => {
                    return <>{Global.toGermanDate(r[c.binding])}</>;
                }
            },
            {
                label: '', binding: '', size: 1, sortable: false,
                renderer: (c, r) => {
                    return <>
                        <page.LControl 
                            type={m.EControlType.checkbox}
                            id={r.Id}
                            value={r.Id}
                            checked={this.state.selection.indexOf(c.Id)>=0}
                            onChange={(v:IControlValue)=>{
                                this.addRemove(v.value);
                            }} />
                    </>;
                },
                rendererHead: (c) => {
                    return <>
                        <page.LButton type={ELButtonType.iconImage}
                            icon="expand"
                            onClick={()=>{
                                this.setState({expanded: !this.state.expanded});
                            }} /> 
                    </>;
                }
            },
        ];

        const pagingProps = { page: 1, count: cfg.pageSize, type: EPagingType.local };
        
        let ctrl = <div id="container-main" className={'c1 container ' + (this.state.expanded ? 'wide' : '')}>
            <div className="row">
                <div className="col-md-12 col-12">
                    <page.Table coulmns={cols} 
                        id={CancellationScreen.containerId + 'Table'}
                        itemClick={this.itemClick}
                        expnaded={this.state.expanded}
                        pagingProps={pagingProps}
                        data={this.state.data}
                        dateColumn='activationDate' />
                </div>
            </div>
            {
                this.state.selection.length > 0 ?
                <div className="row">
                    <div className="col-md-12 col-12">
                        {this.renderAction()}
                    </div>
                </div>                     
                : null 
            }           
        </div>

        return ctrl;
    };

    private renderAction() : JSX.Element {
        let cfg: IBlockConfiguration = {
            title: 'HNDLERERKLRUNG',
            transparent: false,
            styleContainer: '',
        };

        let ctrl = (
            <BaseBlock config={cfg} key="b1">
                <Container key="c1">
                    <Row>
                        <Col xs={12} md={12}>
                            <page.LControl
                                type={m.EControlType.checkbox}
                                checked={this.state.accept}
                                id="acceptRules"
                                style="tal"
                                onChange={(e)=>{
                                    this.setState({accept: !this.state.accept});
                                }}
                                label="Mit dieser Auftragsstornierung verzichten wir auf unsere Provision fr die ausgewhlten <br/>Neuauftrge bzw. Vertragsverlngerungen."
                                />
                        </Col>                        
                    </Row>      
                    {
                        this.state.accept ? 
                        <Row>
                            <Col xs={12} md={12}>
                                <div className="action">
                                <page.LButton text="Ablehnen" 
                                    type={ELButtonType.buttonWithIconRight}
                                    css="action3 fr"
                                    onClick={this.reject}/>
                                <page.LButton text="Freigeben" 
                                    type={ELButtonType.buttonWithIconRight}
                                    css="action3 fr"
                                    onClick={this.approve}/>                                    
                                </div>
                            </Col>                        
                        </Row>
                        : null
                    }                          
                </Container>
            </BaseBlock>
        );

        return ctrl;
    };

    private approve= ()=>{
        this.process(true);
    };

    private reject= ()=>{
        this.process(false);
    };

    private process(ok: boolean) {
        let model = {
            cancellationIds: this.state.selection,
            approve: ok,
            cancelationConfirm: this.state.accept
        };

        this.lastApi = undefined;

        this.setState({
            processing: true,
            status: m.ERequestStatus.running
        });
        
        Api.fetch('cancellation/process', model, 'post', (api:IApiResponse)=>{
            if(api.status == EApiResponseStatus.ok) {
                this.lastApi = undefined;
                this.setState({data: api.data, selection: [], accept: false, status: m.ERequestStatus.undefined});
            } else {
                this.lastApi = api;
                this.setState({status: m.ERequestStatus.done, accept: false});
            }
        });
    };

    private itemClick = (ev: any, id: any) => {
        ////console.log('item selected:' + id);
        this.addRemove(id);
        ev.preventDefault();
        ev.stopPropagation();
        ////console.log('selection', this.state.selection);
    };

    private refresh() {
        Api.fetch('cancellation', undefined, 'get', (api:IApiResponse)=>{
            if(api.status == EApiResponseStatus.ok) {
                this.lastApi = undefined;
                this.setState({data: api.data, loading: false, status: m.ERequestStatus.undefined});
            } else {
                this.lastApi = api;
                this.setState({status: m.ERequestStatus.done, loading: false});
            }
        });
    };

    private renderPopup() : JSX.Element {
        let title = ActionTexts.requestInProcess;
        let type = EPopupType.pleaseWait;
        if(this.state.loading) {
            title = ActionTexts.loadData;
        };

        let cfg:IPopupProps = {
            title: title,
            closable: this.state.status !== m.ERequestStatus.running,
            visible: this.state.status !== m.ERequestStatus.undefined && this.state.status !== m.ERequestStatus.done,
            id: "cancellationPopup",
            type: type,
            onDismiss: ()=>{
                this.lastApi = undefined;
                this.setState({
                    status: m.ERequestStatus.undefined,
                    loading: false,
                    processing: false
                });
            }
        };

        let content:JSX.Element = <></>;

        if(this.lastApi && this.lastApi.status != EApiResponseStatus.ok) {
            let sum: IApiSummary = Api.parseSummary(this.lastApi, undefined);

            cfg.closable = true;
            cfg.visible = true;
            cfg.type = EPopupType.info;
            content = <div>{sum.message}</div>
        };

        let ctrl = GlobalJSX.GetPopup(cfg, content, undefined);

        return ctrl;
    };   

    private addRemove = (id: number) => {
        let c = this.state.selection;
        let i = c.indexOf(id);
        if( i >= 0) {
            c.splice(i, 1);
        } else {
            c.push(id);
        }

        this.setState({selection: c});
    };
};

export default CancellationScreen;