import * as React from 'react';
import { Container, Row, Col } from 'react-bootstrap';

export interface IInner2HeadProps {
    children: React.ReactChild[];
    title?: string;
};

export class Inner2Head extends React.Component<IInner2HeadProps, any>{
    constructor(props: IInner2HeadProps){
        super(props);
    }

    public render() {
        let ctrl = (
        <>
            <Container className="c2" id="container-main">
                {this.props.title ?
                    (
                    <Row>
                        <Col xs={12} md={12}>
                            <h1>{this.props.title}</h1>
                        </Col>
                    </Row>
                    ) : null 
                }
                <Row>
                    <Col xs={12} md={12}>
                        {this.props.children[0]}
                    </Col>
                </Row>                
                <Row>
                    <Col xs={8} md={8}>
                        {this.props.children[1]}
                    </Col>
                    <Col xs={4} md={4}>
                        {this.props.children[2]}
                    </Col>
                </Row>
            </Container>            
        </>
        );

        return ctrl;
    }
};

export default Inner2Head;