import * as React from 'react';
import { Col, Row } from 'react-bootstrap';
import '../../../src/styles/managerDetails.scss';
import { EPopupType } from '../../components/dialogs/popup';
import * as cmp from '../../components/index';
import * as page from '../../components/page/index';
import { EPagingType, IColumn } from '../../components/page/table';
import { IApiResponse, IApiSummary } from '../../interfaces';
import IAppDefinition from '../../interfaces/app/IAppDefinition';
import { IBlockConfiguration } from '../../models/block/configuration';
import * as m from '../../models/index';
import Api from '../../utils/api';
import Global from '../../utils/Global';
import { MainTexts } from '../../utils/messages';
import ContractManagerScreen from './manager';

export interface IContractDetailsModel {
    customer: string;
    msisdn: string;
    contractId: string;
    customerId: string;
    details?: IContractDetailModel[];
}
export interface IContractDetailModel {
    date: Date,
    status: string;
    details: string;
}

export interface IContractManagerDetailsProps {
    voId: string;
    contractId: string;
    parent: ContractManagerScreen;
}
export interface IContractManagerDetailsState {
    model?: IContractDetailsModel;

    apiMessage?: string;
    processing: boolean;
    closablePopup: boolean;
}

export class ContractManagerDetailsScreen extends React.Component<IContractManagerDetailsProps, IContractManagerDetailsState> {
    public static containerId: string = 'contractManagerDetails';
    private processPopupState: EPopupType = EPopupType.pleaseWait;
    readonly globalCfg: IAppDefinition = Global.getConfiguration();
    readonly cfg: IBlockConfiguration = {
        title: undefined,
        transparent: false,
        styleContainer: ''
    };
    readonly cols: IColumn[] = [
        {
            label: MainTexts.date, showTitle: true, style: 'text-sub', binding: 'date', size: 3, sortable: true, renderer: (r, c) => {
                const d = r.date as Date;
                if (d) {
                    return Global.toGermanDate(d);
                }

                return '';
            }
        },
        { label: "Status", showTitle: true, filterable: false, binding: 'status', size: 4, sortable: true },
        { label: "Details", showTitle: true, filterable: false, binding: 'details', size: 5, sortable: true }
    ];

    constructor(props) {
        super(props);

        this.state = {
            model: undefined,

            apiMessage: undefined,
            processing: true,
            closablePopup: false
        };

        this._onDismissPopup = this._onDismissPopup.bind(this);
        this._onDismissMainPopup = this._onDismissMainPopup.bind(this);

        this._loadData(props.contractId, props.voId);
    }

    componentWillReceiveProps(nextProps) {
        this.processPopupState = EPopupType.pleaseWait;
        this.setState({ processing: true, model: undefined });
        this._loadData(nextProps.contractId, nextProps.voId);
    }

    render() {
        const { model } = this.state;

        const pagingProps = { page: 1, count: this.globalCfg.pageSize, type: EPagingType.none };

        return <div id={ContractManagerDetailsScreen.containerId}>
            {this._renderPopup()}
            <cmp.Popup title={MainTexts.details} onDismiss={this._onDismissMainPopup} visible={model !== undefined} width="1200px" height="700px" closable={true} id="managerDetailsMainPopup" type={EPopupType.custom}>
                <div>
                        <Row>
                            <Col xs={3} md={3} className="np">
                                <page.LControl type={m.EControlType.text}
                                    value={model !== undefined ? model.customer : ''}
                                    onChange={() => { }}
                                    preview={true}
                                    name="customer" id="customer"
                                    label={MainTexts.customer} />
                            </Col>
                            <Col xs={3} md={3} className="np">
                                <page.LControl type={m.EControlType.text}
                                    value={model !== undefined ? model.msisdn : ''}
                                    onChange={() => { }}
                                    preview={true}
                                    name="msisdn" id="msisdn"
                                    label="MSISDN" />
                            </Col>
                            <Col xs={3} md={3} className="np">
                                <page.LControl type={m.EControlType.text}
                                    value={model !== undefined ? model.contractId : ''}
                                    onChange={() => { }}
                                    preview={true}
                                    name="contract" id="contract"
                                    label={MainTexts.contract} />
                            </Col>
                            <Col xs={3} md={3} className="np">
                                <page.LControl type={m.EControlType.text}
                                    value={model !== undefined ? model.customerId : ''}
                                    onChange={() => { }}
                                    preview={true}
                                    name="customerid" id="customerid"
                                    label={MainTexts.customerId} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12}>
                                <page.Table coulmns={this.cols} id={ContractManagerDetailsScreen.containerId + 'Table'}
                                    expnaded={false}
                                    pagingProps={pagingProps}
                                    data={model !== undefined && model.details !== undefined ? model.details : undefined} />
                            </Col>
                        </Row>
                </div>
                <></>
            </cmp.Popup>
        </div>;
    }

    socketCallback(res: IApiResponse) {
        if (res.error) {
            this.processPopupState = EPopupType.custom;
            let sum: IApiSummary = Api.parseSummary(res, undefined);
            this.processPopupState = EPopupType.custom;

            this.setState({ apiMessage: sum.message, closablePopup: true, processing: false });
        } else {
            const resModel: IContractDetailsModel = res.data.data;
            this.setState({ processing: false, closablePopup: false, model: resModel });
        }
    }

    _loadData = (contractId, voId) => {
        const reqModel = { contractId, voId };
        Global.hub.send(JSON.stringify(reqModel), m.ERequestType.getContractDetails, undefined);
    };

    _renderPopup = () => {
        return <cmp.Popup
            type={this.processPopupState}
            visible={this.state.processing || this.state.closablePopup}
            closable={this.state.closablePopup}
            id="wait"
            title={MainTexts.pleaseWait}
            height={200}
            onDismiss={this._onDismissPopup}>
            <cmp.PopupBody>
                <div>{this.state.apiMessage}</div>
                <></>
            </cmp.PopupBody>
            <></>
        </cmp.Popup>
    }

    _onDismissPopup = () => {
        this.setState({ processing: false, closablePopup: false });
    }

    _onDismissMainPopup = () => {
        this.setState({ processing: false, closablePopup: false, model: undefined });
        this.props.parent.onDismissDetailsScreen();
    }
}

export default ContractManagerDetailsScreen;