import {IBrand} from '../ordering/brand';
import * as linq from 'linq';
import { BrandKeys } from '../../utils/Defaults';
import Global from '../../utils/Global';
import StorageHelper from '../../utils/storage';

export enum EAppState
{
    undefined = 0,
    ok = 1,
    okDb = 2,
    bookingAllowed = 4,
    warningVisible = 8,
    blockScreen = 16
};

export class IDefCon {
    state: EAppState = EAppState.ok;
    isAvailable?: boolean;
    brands: IBrand[] = [];
    voidAllowed?: string[];
    notification?: string;

    canRunAvailability() {
        var cab = linq.from<IBrand>(this.brands).where(x=>x.id == BrandKeys.kabel).firstOrDefault();
        var dsl = linq.from<IBrand>(this.brands).where(x=>x.id == BrandKeys.dsl).firstOrDefault();
        if(
            (!cab || (cab && !cab.available))
            &&
            (!dsl || (dsl && !dsl.available))
         ) {
            return false;
         }

         return true;
    };

    canAccess() {
        if(this.state != EAppState.ok && (this.state & EAppState.bookingAllowed) != EAppState.bookingAllowed) {
            let usr = StorageHelper.getUser();

            if(this.voidAllowed && usr) {
                return this.voidAllowed.indexOf(usr.voId) >= 0;
            };
        };

        return true;
    };

    public static ensure(source: IDefCon) : IDefCon {
        return Object.assign(new IDefCon(), source);
    };
};