import React from 'react';
import Global from './Global';
import * as i from '../interfaces/index';
import { HttpMethods, StorageKeys, AppIds, ErrorGroup } from '../utils/Defaults';
import StorageHelper from '../utils/storage';
import EasyRouter from './router';
import { ERequestStatus, IAnswerInfo } from '../models';
import linq from 'linq';
import * as page from '../components/page/index';
import * as dc from '../models/global/defcon';
import "isomorphic-fetch";
import { IAnswerError } from '../models/global/answerError';
import UserFeedback from '../screens/test/feedback';
import { Container, Row, Col } from 'react-bootstrap';
import ReactDOMServer from 'react-dom/server';

export class Api {
    public static follow(url: string) {
        let cfg = Global.getConfiguration();

        if (!url.startsWith("http")) {
            url = cfg.apiUrl + url;
        }

        window.open(url, "_blank");
    };

    public static fetch(url: string,
        model: any,
        method: string,
        callback: any) {

        let cfg = Global.getConfiguration();

        if (!url.startsWith("http")) {
            url = cfg.apiUrl + url;
        }

        let what: any = {
            method: !method ? HttpMethods.get : method,
            credentials: 'include',
            headers:
            {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            mode: 'cors'
        };

        if (method != HttpMethods.get) {
            what['body'] = JSON.stringify(model);
        }

        fetch(url, what)
            .then(res => res.json())
            .then((res) => { return res; })
            .then(
                (res) => {
                    Api.parseResponse(res, (a) => {
                        callback(res as i.IApiResponse);
                    });
                },
                (res) => {
                    console.error(res);
                    callback(res as i.IApiResponse);
                }
            );
    };

    public static logout() {
        let user = StorageHelper.getUser();
        if (user) {
            StorageHelper.clear();
            window.location.href = user.logoutUrl + '?app=' + Api.getAppUrl();
        }
    };

    public static getAppUrl() {
        let path = location.protocol + '//' + location.host;

        return path;
    };

    public static parseDefcon(api: i.IApiResponse, callback: any) {
        if (api) {
            let dcon: dc.IDefCon = api.data;

            callback(dcon);
        }
    };

    public static parseResponse(api: i.IApiResponse, callback: any) {
        if (api) {
            if (api.error && api.error.action) {
                if (api.error.action.type == 1) {
                    StorageHelper.clear();
                    window.location.href = api.error.action.url + '?app=' + Api.getAppUrl();
                    return;
                };
            }

            if (callback) {
                callback(api);
            }
        }
    };

    public static parseModelState(api: i.IApiResponse): i.IApiSummary {
        let sum: i.IApiSummary = { status: ERequestStatus.undefined, message: '', code: '' };
        if (api && api.validation && api.validation.items.length > 0) {

            sum.status = ERequestStatus.failed;

            api.validation.items.map((i, id) => {
                if (i.Key === ErrorGroup.form || i.Key === ErrorGroup.api) {
                    sum.message += '<li>' + i.Value + '</li>'
                }
            });
        };

        return sum;
    };

    public static parseSummary(api: i.IApiResponse | undefined, 
        process: i.IProcessStatus | undefined): i.IApiSummary {
        
        let sum: i.IApiSummary = { status: ERequestStatus.undefined, message: '', code: '' };

        if (api) {
            //console.log('parseSummary', api.error);
            if (api.error) {
                sum.code = api.error.code;
                sum.status = ERequestStatus.failed;
                sum.message = api.error.message;
                sum.validation = api.validation;
                
                Api.extractErrorInfo(sum, api.error.message);

                return sum;
            };

            if (api.data && process && process.steps.length == 0) {
                process.steps = api.data.steps;
            };
        };

        if (process) {
            try {
                //console.log('processs');
                //console.log(process);
                let step = linq.from<i.IApiStep>(process.steps)
                    .where(x => x.status != ERequestStatus.undefined)
                    .orderByDescending(x => x.type)
                    .firstOrDefault();

                if (step) {
                    
                    sum.status = step.status;
                    let msg: IAnswerInfo = JSON.parse(step.message);
                    //console.log('answerinfo', msg);
                    sum.message = msg.Info ? msg.Info : msg.Text;
                    if (step.status !== ERequestStatus.done && msg.ErrorCode) {
                        sum.message = (msg.ErrorCode) + sum.message;
                    };

                    if(msg.answerError) {
                        sum.answerError = msg.answerError; 
                    }

                    return sum;
                }
            } catch { }
        } else {
            if(api && !api.error) {
                sum.status = ERequestStatus.done;

                if(api.data) {
                    let answer = api.data as IAnswerInfo;
                    sum.message = answer.Text;
                };
            }
        };

        return sum;
    };

    private static extractErrorInfo(sum: i.IApiSummary, message: string) {
        try {
            let err = JSON.parse(message);
            let ae: IAnswerError = err.answerError;
            
            if(err.code) {
                ae = err as IAnswerError;
            };

            if(ae) {
                let jsx:JSX.Element =  <Container className="full tal answerError">
                    <Row>
                        <Col xs={12} md={12} className="np">
                            <div className="font-weight-bold" >Das ist passiert</div>
                        </Col>
                    </Row>
                    
                    <Row>
                        <Col xs={12} md={12}>
                                {ae.message}
                        </Col>
                    </Row>
                    {
                        ae.suggestion ? 
                        <>
                            <Row>
                                <Col xs={12} md={12} className="np">
                                    <div className="font-weight-bolder">Was kannst du tun?</div>
                                </Col>
                            </Row>

                            <Row>
                                <Col xs={12} md={12}>
                                        {ae.suggestion}
                                </Col>
                            </Row>  
                        </>
                        : null
                    }
                  
                </Container>

                sum.message = ReactDOMServer.renderToStaticMarkup(jsx);
            };
        } catch(e) {

        };
    };

    public static applyValidation(api: i.IApiResponse) {
        //console.log('applyValidation');
        if (api && api.validation) {
        

            for (let i = 0; i < api.validation.items.length; i++) {

                let id = api.validation.items[i].Key;
                let element = document.getElementById(id);

                if (!element) {
                    let sa: string[] = api.validation.items[i].Key.split('.');

                    id = 'oec' + sa[1];

                    element = document.getElementById(id);
                };

                if (!element) {
                    id = api.validation.items[i].Key;
                    id = 'oec' + id.replace('.', '-');

                    element = document.getElementById(id);
                };

                if (!element) {
                    let arr = document.getElementsByName(api.validation.items[i].Key);
                    if (arr && arr.length) {
                        element = arr[0];
                        id = element.id;
                    };
                };

                if (!element) {
                    id = 'oeccustomer-' + api.validation.items[i].Key;

                    element = document.getElementById(id);
                };

                if (!element) { continue; }
             
                let ctrl: page.LControl = Global.GetComponent(id); //Global.FindReact(element);

                if (!ctrl) { continue; }
              
                ctrl.setError(api.validation.items[i].Value);

            };
        };
    };

    public static init() {
        let us = StorageHelper.getUser();
    };
};

export default Api;