import * as React from 'react';
import * as linq from 'linq';
import IBrand from '../../models/ordering/brand';
import IActivation from '../../models/ordering/activation';
import IService from '../../models/ordering/service';
import { IValidationOption } from '../../utils/validation/validationOption';
import TariffThumb from '../ordering/tariffThumb';
import Global from '../../utils/Global';
import StorageHelper from '../../utils/storage';
import { LClear } from '../page';

export enum EOptionGroupRenderType {
    undefined = 0,
    oneMandatory = 1
};

export interface IOptionGroupProps {
    containerId: string;
    services: IService[];
    validation?: IValidationOption[];
    brand: IBrand;
    act: IActivation;
    title: string;
    hint?: string;
    selected: string;
    type: EOptionGroupRenderType;
};

export interface IOptionGroupState {
    selected: string;
    invalid: boolean;
};

export class OptionGroup extends React.Component<IOptionGroupProps, IOptionGroupState> {
    public static containerClass = 'optiongroup';
    private ids:string[] = [];

    constructor(props) {
        super(props);

        this.onSelect = this.onSelect.bind(this);

        this.ids = linq.from<IService>(this.props.services).select(x=>x.id).toArray();

        this.state = {
            selected: this.props.selected, invalid: false
        };
    };

    public componentWillReceiveProps(next: IOptionGroupProps){
        this.setState({selected: next.selected});
    };

    public render() {
        let ctrl = ( 
            <div className={OptionGroup.containerClass} id={this.props.containerId}>
                    <div className="subtitle">
                        {this.props.title}
                        {
                            this.props.hint ? <span className={'hint' + (this.state.invalid ? ' error': '')}> {this.props.hint}</span> : null
                        }
                    </div>
                    <div className="">
                        {
                            this.props.services.map((i, idx)=>(
                                (<TariffThumb
                                    key={idx}
                                    id={i.id} 
                                    text={i.name}
                                    tooltip={i.tooltip}
                                    tooltipModel={i}
                                    idx={idx}
                                    brandKey={this.props.brand.id} 
                                    selected={i.id === this.state.selected} 
                                    styleBrand={false}
                                    cssStyle={this.state.selected === i.id ? 'selected' : ''}
                                    onClick={this.onSelect}/>)
                            ))
                        }
                    </div>
                    <LClear/>
            </div>);

        return ctrl;
    };

    public validate() : boolean {
        let res : boolean = true;

        if(this.props.type === EOptionGroupRenderType.oneMandatory) {
            res = this.state.selected ? true : false;
        };

        this.setState({invalid: !res});

        return res;
    };

    private onSelect(value: string) {
        //remove all prev selected
        let newSvc =  linq.from<string>(this.props.act.services)
            .where(x=> this.ids.indexOf(x) < 0 )
            .toArray();

        newSvc.push(value);

        this.props.act.services = newSvc;

        StorageHelper.updateActivation(this.props.act);

        this.setState({selected:value});
    };
};

export default OptionGroup;