export class CustomerGroup {
    groupId:string = '';

    numberOfOwner:number = 0;

    numberOfMember:number = 0; 

    groupName:string = '';

    groupStatus:string = '';

    groupType:string = '';

    marketCode:string = '';

    effectiveDate:Date = new Date();

    members: IGroupMember[] = [];

    public static createInstanceFromJson(gr:CustomerGroup) : CustomerGroup {
        return Object.assign(new CustomerGroup(), gr);
    };

    public getOwner() : IGroupMember {
        let m = linq.from<IGroupMember>(this.members)
            .where(x=>x.type === EGroupMemeberType.owner)
            .firstOrDefault();
        
        return m ? m : {};
    };

    public getMembers() : IGroupMember[] {
        return linq.from<IGroupMember>(this.members)
            .where(x=>x.type === EGroupMemeberType.partner)
            .toArray();
    };    
};

