import * as React from 'react';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { Row, Col, Container } from 'react-bootstrap';
import BaseBlock from '../../page/block';
import ActivationCustomerData from '../process/activationCustomerData';
import * as page from '../../page/index';
import { EControlType } from '../../../models/page/diverse';
import * as m from '../../../models/index';
import StorageHelper from '../../../utils/storage';
import linq from 'linq';
import { IListItem, IKeyText } from '../../../models/page/interfaces';
import { MainTexts, OrderingTexts, ValidationMessages } from '../../../utils/messages';
import { ICustomer } from '../../../models/ordering/customer';
import { EPaymentType, EPhonebookType, ETopupType } from '../../../models/ordering/order';
import { IBaseComponentState } from '../../../interfaces';
import { ITopup } from '../../../models/index';
import Global from '../../../utils/Global';
import { EPagingType } from '../../page/table';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';

export interface ICustomerTopupProps {
    parent: ActivationCustomerData;
    topup?: ITopup;
    onChange: any;
};

export class CustomerTopup extends React.Component<ICustomerTopupProps, IBaseComponentState> {
    constructor(props: ICustomerTopupProps) {
        super(props);

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: ICustomerTopupProps) {
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.autotopup,
            titleDesc:OrderingTexts.forOtelo,
            transparent: false
        };

        if(!this.props.parent.props.parent.dataWrapper.needTopUp()) {
            return <></>
        };

        let topup: ITopup = this.props.topup ? this.props.topup : {amount:0, amountSingle: 0, trigger: 0, type: ETopupType.undefined};
        
        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>            
                    <div className="spacer-dim2"></div>
                    <Row>
                        <Col className="np">
                            <page.LControl type={EControlType.hint} label={OrderingTexts.autotopup}/>
                        </Col>
                    </Row>
                    <div className="spacer-dim2"></div>
                    <Row>
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.dropdown} value={topup.amount} 
                                onChange={(e)=>{
                                    if(e.value > 0) {
                                        this.props.parent.props.parent.data.customer.payment!.paymentType = EPaymentType.iban;
                                    } 

                                    this.props.onChange(e);
                                }} name="topup.amount" id="amount" title={OrderingTexts.amount} 
                                options={this.getAmounts()}/>
                        </Col>

                        {
                            this.props.parent.props.parent.data.customer.topup && this.props.parent.props.parent.data.customer.topup.amount > 0 ?
                            <Col xs={6} md={6} className="npr">
                                <page.LControl type={EControlType.dropdown} value={topup.trigger} 
                                    onChange={this.props.onChange} name="topup.trigger" id="trigger" title={OrderingTexts.atTrigger} 
                                    options={this.getTriggers()}
                                    validationOptions={[
                                        new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError })
                                    ]}/>
                            </Col> 
                            : null
                        }                                      
                    </Row>   
                    <div className="spacer-dim2"></div>        
                    <Row>
                        <Col className="np">
                            <page.LControl type={EControlType.hint} label={OrderingTexts.singeltopup}/>
                        </Col>
                    </Row>
                    <div className="spacer-dim2"></div>
                    <Row>
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.dropdown} value={topup.amountSingle} 
                                onChange={this.props.onChange} name="topup.amountSingle" id="amount" title={OrderingTexts.amount} 
                                options={this.getNow()}/>
                        </Col>                                   
                    </Row>                        
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };     
    
    private getAmounts() : IKeyText[] {
        let op: IKeyText[] = new Array();
        op = [
            {key: '0', text: OrderingTexts.noComfort},
            {key: '9', text: '9 '},
            {key: '15', text: '15 '},
            {key: '19', text: '19 '},
            {key: '25', text: '25 '},
            {key: '29', text: '29 '},
        ];
        return op;
    };  
    
    private getTriggers() : IKeyText[] {
        let op: IKeyText[] = new Array();
        op = [
            {key: '0', text: MainTexts.pleaseSelect},
            {key: '5', text: '5 '},
            {key: '15', text: '15 '},
            {key: '50', text: '50 '},
        ];
        return op;
    };      

    private getNow() : IKeyText[] {
        let op: IKeyText[] = new Array();
        op = [
            {key: '0', text: OrderingTexts.noSofort},
            {key: '5', text: '5 '},
            {key: '15', text: '15 '},
            {key: '50', text: '50 '},
        ];
        return op;
    };     
};

export default CustomerTopup;