import * as linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBaseComponentState } from '../../../interfaces';
import { Address, Availability, ELButtonType, ERequestStatus, ERequestType, IDefCon, OrderDataWrapper } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import IBrand from '../../../models/ordering/brand';
import { EActionAddressType, EActivationType, EBusinessCase, ECreditDebitType, EIDCardType, EPaymentType } from '../../../models/ordering/order';
import { TreeItem } from '../../../models/support/tree';
import CustomerScreen from '../../../screens/existing/customer';
import { BrandKeys, StorageKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { OrderingTexts } from '../../../utils/messages';
import EasyRouter from '../../../utils/router';
import StorageHelper from '../../../utils/storage';
import BrandsList from '../../ordering/brandList';
import { EBrandThumbType } from '../../ordering/brandThumb';
import { LButton } from '../../page';
import BaseBlock from '../../page/block';
import { ECategoryStatus } from '../../tools/categoryWrapper';

export interface IExistingCustomerCrossSellingProps {
    parent: CustomerScreen;
}

export interface IExistingCustomerCrossSellingState extends IBaseComponentState {

}

export class ExistingCustomerCrossSelling extends React.Component<IExistingCustomerCrossSellingProps, IExistingCustomerCrossSellingState> {
    private dc: IDefCon;
   
    constructor(props: IExistingCustomerCrossSellingProps) {
        super(props);

        this.dc = StorageHelper.getDefCon();

        this.state = {
            changed: false
        };
    };

    componentWillReceiveProps(next: IExistingCustomerCrossSellingProps) {
        this.setState({ changed: true });
    };

    

    render() {
        if (!this.dc.canRunAvailability()) { return <></> }

        let avl = StorageHelper.getAvailability();

        let cfg: IBlockConfiguration = {
            title: OrderingTexts.crossSelling,
            transparent: false,
            style: 'crossselling',
            styleContainer: '',
            collapsible: true,
            collapsed: false
        };

        let br = linq.from<IBrand>(StorageHelper.getBrands())
            .where(x => x.id == BrandKeys.kabel || x.id == BrandKeys.dsl)
            .toArray();

        let tocheck = avl.executed == ERequestStatus.undefined;

        if (this.props.parent.data.customer.address && avl.address.city != this.props.parent.data.customer.address.city) {
            tocheck = true;
        }

        if (avl.executed == ERequestStatus.tempResult || avl.executed == ERequestStatus.done) {
            tocheck = false;
        }

        let inner = tocheck ? this.renderAction(avl) : this.renderBrands(br, avl);

        let ctrl = <BaseBlock config={cfg}>
            <Container>
                <Row>
                    <Col md={12} className="np">
                        {inner}
                        {/* {this.renderAction(avl)} */}
                    </Col>
                </Row>
            </Container>
        </BaseBlock>

        return ctrl;
    };

    private renderAction(avl: Availability) {
        let cust = this.props.parent.data.customer;
        let ctrl = <LButton type={ELButtonType.buttonWithIconRight}
            css="action3 fl"
            id=""
            text={"Festnetz und TV Verfgbarkeit prfen"}
            onClick={() => {
                avl.address = cust.address!;
                avl.isCrossSelling = true;
                avl.executed = ERequestStatus.running;
               

                StorageHelper.set(StorageKeys.keyAvailability, avl);

                Global.hub.send(JSON.stringify(avl),
                    ERequestType.checkAvailability,
                    undefined);

                this.setState({ changed: true });
            }} />

        return ctrl;
    };


    private renderBrands(br: IBrand[], avl: Availability) {
        let cust = this.props.parent.data.customer;
        avl.address = cust.address!;
        avl.isCrossSelling = true;
        avl.executed = ERequestStatus.done;

        StorageHelper.set(StorageKeys.keyAvailability, avl);

        let inner = <BrandsList
            selected={BrandKeys.kabel}
            thumbSelectable={true}
            needSelection={false}
            thumbType={EBrandThumbType.blister}
            withTariffs={false}
            forceVisible={true}
            crossSelling={true}
            checkAvailabilityProductsUponClick={true}
            clickHandler={(catKey) => {
                
                let c = Global.categoryWrapper.getCategory(catKey);

                if (!c || c.status != ECategoryStatus.available) {
                    return;
                }

                let d = OrderDataWrapper.createProcess(EBusinessCase.bnt,
                    EActivationType.actDefault,
                    this.props.parent.data,
                    undefined);
                Global.categoryWrapper.switchCat(catKey);
                d.currentCat = catKey;

                d.currentBrand = d.activations[0].brandKey = Global.categoryWrapper.getCategoryBrandKey(undefined);
                d.activations[0].reset();
                d.activations[0].catKey = catKey;
                d.activations[0].additional = true;
                d.activations[0].msisdn = '';
                d.activations[0].activationType = EActivationType.actDefault;
                d.activations[0]['businessCaseId'] = EBusinessCase.bnt;
                d.activations[0].creditDebitType = ECreditDebitType.postpaid;
                d.customer.legitimation = { type: EIDCardType.german, id: '', authority: '' };
                if(!d.customer.payment) {
                    d.customer.payment =  {
                        paymentType: EPaymentType.iban,
                        addressType: EActionAddressType.sameAddress,
                        address: new Address()
                    };
                };
                d.activations[0].selection = new TreeItem('root');

                // d.activations[0].availability.isCrossSelling = true;
                // d.activations[0].availability = avl;
               

                d.isCrossSelling = true;

                if (!this.props.parent.data.customer.mnp) {
                    d.customer.mnp = { provider: '', network: '', differentCustomer: false, birthday: new Date() };
                    d.customer.mnp.differentCustomer = false;
                    d.customer.mnp.birthday = new Date();
                    d.customer.mnp.cancellationDate = new Date();
                    d.customer.mnp.contractEndDate = new Date();

                } else {
                    d.customer.mnp = this.props.parent.data.customer.mnp;
                }

                if (this.props.parent.data.customer.contracts && this.props.parent.data.customer.contracts[0].customerId) {
                    d.activations[0].customerId = this.props.parent.data.customer.contracts[0].customerId;
                }

                StorageHelper.addOrder(d, () => {
                    EasyRouter.route({ url: '/ordering/new/tariff/' + d.id, title: OrderingTexts.activation });
                });
            }}
            block={{ title: '', transparent: true }} />

        return inner;
    };
};

export default ExistingCustomerCrossSelling;