import * as React from 'react';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { Row, Col, Container } from 'react-bootstrap';
import BaseBlock from '../../page/block';
import ActivationCustomerData from '../process/activationCustomerData';
import * as page from '../../page/index';
import { EControlType } from '../../../models/page/diverse';
import * as m from '../../../models/index';
import StorageHelper from '../../../utils/storage';
import linq from 'linq';
import { IListItem, IKeyText } from '../../../models/page/interfaces';
import { MainTexts, OrderingTexts } from '../../../utils/messages';
import { ICustomer } from '../../../models/ordering/customer';
import { EPhonebookType } from '../../../models/ordering/order';
import { IDesiredSchedule } from '../../../models/ordering/desiredSchedule';

export interface ICustomerDesiredScheduleProps {
    parent: ActivationCustomerData;
    customer: ICustomer;
    onChange: any;
};

export interface ICustomerDesiredScheduleState {
    changed: boolean;
};

export class CustomerDesiredSchedule extends React.Component<ICustomerDesiredScheduleProps, ICustomerDesiredScheduleState> {
    constructor(props: ICustomerDesiredScheduleProps) {
        super(props);

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: ICustomerDesiredScheduleProps) {
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.desiredSchedule,
            // titleDesc:OrderingTexts.forDSL,
            transparent: false
        };

        let ds: IDesiredSchedule = this.props.customer.installationSchedule ? this.props.customer.installationSchedule : { dateType: m.EDesiredSchedule.asap, date: new Date() };
        let dateVisible:boolean = ds.dateType === m.EDesiredSchedule.custom;

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>            
                    <Row>
                        <Col xs={12} md={12} className="np">
                            <page.LControl type={EControlType.radio} hideTitle={true}
                                    checked={ds.dateType === m.EDesiredSchedule.asap}
                                    onChange={this.props.onChange} name="installationSchedule.dateType" id="dateType_1" label={OrderingTexts.desiredSchedule1}/>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={12} className="np">
                            <page.LControl type={EControlType.radio} hideTitle={true}
                                    checked={ds.dateType === m.EDesiredSchedule.custom}
                                    onChange={this.props.onChange} name="installationSchedule.dateType" id="dateType_2" label={OrderingTexts.desiredSchedule2}/>
                        </Col>
                    </Row>      
                    {
                        dateVisible ? 
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LDate value={ds.date} title={""} 
                                datePicker = {true}
                                    onChange={this.props.onChange} name="installationSchedule.date" id="installationSchedule-date"/>
                            </Col>                         
                        </Row>
                        : null
                    }
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };               
};

export default CustomerDesiredSchedule;