import * as React from 'react';
import { IBlockConfiguration } from '../../models/block/configuration';
import { UserMessages, MainTexts, ValidationMessages } from '../../utils/messages';
import BaseBlock from '../../components/page/block';
import { Row, Col, Container } from 'react-bootstrap';
import { LClear } from '../../components';
import * as page from '../../components/page/index';
import { EControlType, ELButtonType, ERequestType } from '../../models';
import { IControlValue } from '../../models/page/interfaces';
import ValidationOption, { EValidationOption } from '../../utils/validation/validationOption';
import Global from '../../utils/Global';
import { EPopupType } from '../../components/dialogs/popup';
import { IApiResponse } from '../../interfaces';
import * as cmp from '../../components/index';
import { EDateType } from '../../components/page/date';

export interface IEditProfileProps {

}
export interface IEditProfileState {
    firstname: string;
    surname: string;
    birthDate: Date | undefined;
    mobilePhoneNumber: string;
    email: string;

    popupTitle: string;
    processing: boolean;
    closblePopup: boolean;
    apiMessage: string;
}

class EditProfileScreen extends React.Component<IEditProfileProps, IEditProfileState> {
    private processPopupState: EPopupType = EPopupType.pleaseWait;
    public static containerId: string = 'editProfile';
    readonly cfg: IBlockConfiguration = {
        title: UserMessages.profileEdit,
        transparent: false,
        styleContainer: ''
    }

    constructor(props) {
        super(props);

        this._onSave = this._onSave.bind(this);
        this._onChange = this._onChange.bind(this);
        this.onDismissProcessPopup = this.onDismissProcessPopup.bind(this);
        this.showPopup = this.showPopup.bind(this);

        this.state = { firstname: '', surname: '', birthDate: undefined, mobilePhoneNumber: '', email: '', closblePopup: false, apiMessage: '', processing: false, popupTitle: '' };

        Global.hub.send(JSON.stringify(''),
            ERequestType.loadProfile,
            undefined);
    }

    render() {
        return <div id={EditProfileScreen.containerId}>
            <page.Inner1C>
                <BaseBlock config={this.cfg}>
                    <cmp.Popup
                        type={this.processPopupState}
                        visible={this.state.processing || this.state.closblePopup}
                        closable={this.state.closblePopup}
                        id="wait"
                        title={this.state.popupTitle}
                        height={200}
                        onDismiss={this.onDismissProcessPopup}>
                        <cmp.PopupBody>
                            <div>{this.state.apiMessage}</div>
                            <></>
                        </cmp.PopupBody>
                        <></>
                    </cmp.Popup>
                    <Container className="container-action">
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.hint} label={UserMessages.profileEditDesc} />
                            </Col>
                        </Row>

                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.text}
                                    value={this.state.firstname}
                                    validationOptions={
                                        [
                                            new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError })
                                        ]
                                    }
                                    onChange={this._onChange}
                                    name="firstname" id="firstname"
                                    label={MainTexts.firstname} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.text}
                                    value={this.state.surname}
                                    validationOptions={
                                        [
                                            new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError })
                                        ]
                                    }
                                    onChange={this._onChange}
                                    name="surname" id="surname"
                                    label={MainTexts.surname} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LDate 
                                    title={MainTexts.dateOfBirth} 
                                    datePicker = {false}
                                    name="birthDate" 
                                    id="birthDate" 
                                    type={EDateType.default}
                                    onChange={this._onChange} 
                                    value={this.state.birthDate} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.text}
                                    value={this.state.mobilePhoneNumber}
                                    onChange={this._onChange}
                                    name="mobilePhoneNumber" id="mobilePhoneNumber"
                                    label={MainTexts.mobilePhoneNumber} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.text}
                                    value={this.state.email}
                                    onChange={this._onChange}
                                    name="email" id="email"
                                    label={MainTexts.email} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.hint} label={UserMessages.profileSecurityTanValDesc} />
                            </Col>
                        </Row>

                        <Row>
                            <Col xs={12} md={12} >
                                <page.LButton
                                        text={MainTexts.save}
                                        onClick={this._onSave}
                                        type={ELButtonType.buttonWithIconRight}
                                        css={"action fr"}
                                        icon="arrow-right" />

                                <page.LButton
                                    onClick={()=>{
                                        Global.easyApp.gotoDashboard()
                                    }}
                                    text={MainTexts.cancel}
                                    type={ELButtonType.linkSimple}
                                    css="action3 fr"
                                    icon="chevron" />
                            </Col>
                        </Row>
                    </Container>
                    <LClear />
                </BaseBlock>
            </page.Inner1C>
        </div>;
    }

    public loadProfileCallback(api: IApiResponse) {
        var data = api.data.data as IEditProfileState;
        this.setState(data);
    }

    public saveProfileCallback(api: IApiResponse) {
        if (api.error) {
            this.showPopup(EPopupType.info, MainTexts.textError, api.error.message, true);
        } else {
            this.showPopup(EPopupType.info, MainTexts.saveSuccess, '', true);
        }
    }

    private _onChange(e: IControlValue) {
        let st = this.state;
        Global.onInput(st, e, () => {
            this.setState(st);
        });
    }

    private _onSave() {
        const model = this.state;
        Global.hub.send(JSON.stringify(model),
            ERequestType.saveProfile,
            undefined);
    }

    private showPopup(type: EPopupType, title: string, message: string, inprocess: boolean) {
        this.processPopupState = type;
        this.setState({
            apiMessage: message,
            closblePopup: true,
            processing: inprocess,
            popupTitle: title
        });
    }

    private onDismissProcessPopup() {
        this.setState({ processing: false, closblePopup: false });
    }
}

export default EditProfileScreen;
