import * as React from 'react';
import * as cmp from '../../../components/index';
import * as i from '../../../interfaces/index';
import { ERequestStatus } from '../../../models/page/diverse';
import UserFeedback from '../../../screens/test/feedback';
import Api from '../../../utils/api';
import Global from '../../../utils/Global';
import { OrderingTexts } from '../../../utils/messages';
import { LClear } from '../../page';
import { EStatusState } from '../../support/status';

export interface IActivationResponseStatusProps {
    api?: i.IApiResponse;
    process?: i.IProcessStatus;
    apiState: EStatusState;
    message?: any;
    title?: any;
    processCallback: any;
    completedCallback?: any;
    timerId?: string;
    resetTimer?: boolean;
    autoRestart?: boolean;
    parent?: any;
};

export class ActivationResponseStatus extends React.Component<IActivationResponseStatusProps, i.IBaseComponentState> {
    private api?: i.IApiResponse;
    private process?: i.IProcessStatus;

    constructor(props: IActivationResponseStatusProps) {
        super(props);

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: IActivationResponseStatusProps) {
        this.api = next.api;
        this.process = next.process;
        this.setState({ changed: true });
    };

    public render() {
        let ctrl = <></>;
        let action: JSX.Element = <></>;
        let info: string = '';
        let css: string = '';

        //this.preapre();

        this.process = this.props.process;

        if (this.props.apiState === EStatusState.undefined) {
            return <div>status: {this.props.apiState}</div>
        }
        else if (this.props.apiState === EStatusState.inprocessTimer || this.props.apiState === EStatusState.inprocess) {
            info = OrderingTexts.inProcess;
        } else if (this.props.apiState == EStatusState.done) {
            info = OrderingTexts.success;
        } else if (this.props.apiState === EStatusState.warning) {
            info = OrderingTexts.info;
        }
        else {
            info = OrderingTexts.error;
        };

        action = <cmp.Status
            counter={this.props.resetTimer ? cmp.Status.defaultTimer : undefined}
            id={this.props.timerId ? this.props.timerId : 'status_' + Global.getUniqueId()}
            status={this.props.apiState}
            autoRestart={true}
            completedCallback={() => { this.props.completedCallback!(); }}
            stepCallback={this.props.processCallback} />;

        let sum: i.IApiSummary = Api.parseSummary(this.api, this.process);

        //console.log('summary:' + this.props.apiState, sum);

        if (sum.status === ERequestStatus.failed) {
            css += ' error';
        } else if (sum.status === ERequestStatus.done) {
            css += ' success';
        } else if (sum.status === ERequestStatus.corrected) {
            css += ' warn';
        }

        let text = (sum.code ? sum.code + ': ' : '') + sum.message;

        if(this.props.message) {
            info = this.props.title;
            text = this.props.message;
        }

        text = text.replace('undefined', '').replace('null', '');

        ctrl = <div className={'apistatus ' + css}>
            <div className='info'>
                {info}
            </div>

            <div className="act">
                {action}
            </div>

            {

                sum.status != ERequestStatus.tempResult &&
                <div className='details'>
                    {text}
                </div>
            }

            <LClear />
        </div>;

        if (sum.status === ERequestStatus.failed) {
        
            if ( this.props.parent && this.props.parent.props.parent.componentId === 'welcomescreen') {
                ctrl = (<div className={'apistatus ' + css}>
                    <div className='info'>
                        {info}
                    </div>

                    <div className="act">
                        {action}
                    </div>

                    <div className='details'>
                        {sum.message}
                    </div>
                </div>)
            } else {
                ctrl = <UserFeedback summary={sum} />
            }

        };

        return ctrl;
    };
};

export default ActivationResponseStatus;