import * as React from 'react';
import BaseBlock from '../../../components/page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import ProcessNewScreen from '../../../screens/ordering/process';
import { Row, Col, Container } from 'react-bootstrap';
import { EControlType } from '../../../models/page/diverse';
import * as page from '../../page/index';
import { IControlValue } from '../../../models/page/interfaces';
import { MainTexts, OrderingTexts, ValidationMessages } from '../../../utils/messages';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import { IActivation } from '../../../models';
import { IBaseComponentState } from '../../../interfaces';
import { EActivationType } from '../../../models/ordering/order';

export interface IActivationHardwareProps {
    //parent: ProcessNewScreen;
    onChange: any;
    preview: boolean;
    activation: IActivation;
};

export class ActivationHardware extends React.Component<IActivationHardwareProps, IBaseComponentState> {
    constructor(props: IActivationHardwareProps) {
        super(props);
    };

    public render() {
        //vf gigakombi
        if(this.props.activation.activationType == EActivationType.bookService) {
            return <></>
        };

        let cfg: IBlockConfiguration = {
            title: OrderingTexts.sub,
            id: "hardwareSelection",
            transparent: false
        };

        let act: IActivation = this.props.activation;
        
        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={12} md={12}>
                            <Row>
                                <Col xs={12} md={12}>
                                    <page.LControl 
                                        //preview= {!this.props.parent.dataWrapper.canEditActivation(this.props.parent.currentIndex)}
                                        preview={this.props.preview}
                                        type={EControlType.text} label="IMEI" id="imei" name="imei"
                                        maxLength = {15}
                                        onChange={this.props.onChange}
                                        value={act.imei}
                                        validationOptions={ 
                                            [
                                                new ValidationOption({type: EValidationOption.exactLength, nullable: true, targetValue: 15, message: ValidationMessages.errorExactLength }),
                                                new ValidationOption({type: EValidationOption.numeric, nullable: true, message: ValidationMessages.errorNumeric })
                                            ]
                                        }/>
                                </Col>                             
                            </Row>
                        </Col>
                    </Row>                                
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };  
};

export default ActivationHardware;