import {isIE, isEdge, isSafari, isFirefox} from 'react-device-detect';

export class Browser {
    public static getBrowserString() : string {
        if(isIE) { 
            return 'ie'
        } else if(isSafari) {             
            return 'safari';
        } else if(isFirefox) {             
            return 'firefox';            
        } else if(isEdge) { 
            return 'edge'
        };

        return 'chrome';
    };
};