import * as React from 'react';
import BaseBlock from '../../../components/page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { ECreditDebitType, EBusinessCase, ECustomerType, EActivationType, EMnpType } from '../../../models/ordering/order';
import IProduct from '../../../models/ordering/product';
import ProcessNewScreen from '../../../screens/ordering/process';
import LButton from '../../../components/page/button';
import LLabel from '../../../components/page/label';
import LClear from '../../../components/page/clear';
import { Row, Col, Container } from 'react-bootstrap';
import { ELElementStatus, ELButtonType } from '../../../models/page/diverse';
import linq from 'linq';
import IBrand from '../../../models/ordering/brand';
import StorageHelper from '../../../utils/storage';
import { MainTexts, OrderingTexts, ActionTexts } from '../../../utils/messages';
import EasyRouter from '../../../utils/router';
import { CustomerScreen } from '../../../screens/insex';
import Global from '../../../utils/Global';
import { BrandKeys } from '../../../utils/Defaults';

export interface IActivationBlockProps {
    product?: IProduct;
    parent: ProcessNewScreen;
};

export interface IActivationBlockOptions {
    canPrepaid: boolean;
    canSoho: boolean;
    canMnpStandard: boolean;
    canMnpPredate: boolean;
};

export interface IActivationBlockState {
    businessType: ECreditDebitType;
    businessCase: EBusinessCase;
    customerType: ECustomerType;
    activationType: EActivationType;
};

export class ActivationBlock extends React.Component<IActivationBlockProps, IActivationBlockState> {
    private mounted: boolean = false;
    private config: IActivationBlockOptions;
    constructor(props: IActivationBlockProps) {
        super(props);
        
        this.handlePayment = this.handlePayment.bind(this);
        this.handleActivationType = this.handleActivationType.bind(this);
        this.handleCusomerType = this.handleCusomerType.bind(this);

        this.config = { canMnpPredate: false, canMnpStandard: false, canSoho: false, canPrepaid: false };

        this.state = {
            businessType: ECreditDebitType.postpaid,
            businessCase: EBusinessCase.bnt,
            customerType: ECustomerType.personal,
            activationType: EActivationType.actDefault
        };
    }

    public componentDidMount() {
        this.mounted = true;
    };

    public componentWillReceiveProps(next: any){
        this.setState(next);
    };

    public render() {
        let act:JSX.Element = <>
        <LButton text={MainTexts.ready} css="fr" icon="ok"
            type={ELButtonType.buttonWithIconRight}
            onClick={() => {
                EasyRouter.route({ url: CustomerScreen.uri + this.props.parent.data.id, title: MainTexts.existingCustomerActivation });
            }} />
        </>

        let cfg: IBlockConfiguration = {
            title: MainTexts.productSelection,
            transparent: false,
            action: act
        };

        this.prepare();       

        //let brand = Global.categoryWrapper.getCategoryBrandKey(this.props.parent.data);

        let ctrl = (
            <>
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={6} md={6}>
                            <LLabel text={MainTexts.productSelection}/>
                        </Col>
                        <Col xs={6} md={6}>
                            <LLabel text={MainTexts.businessCase}/>
                        </Col>
                    </Row>

                    <Row>
                        <Col xs={3} md={3}>
                            <LButton 
                                text="Postpaid" 
                                onClick={this.handlePayment}
                                clickModel={ECreditDebitType.postpaid}
                                status={this.props.parent.selection.creditDebitType === ECreditDebitType.postpaid ? ELElementStatus.selected : ELElementStatus.normal}/>
                        </Col>
                        <Col xs={3} md={3}> 
                            {
                                this.config.canPrepaid ?
                                <LButton text="Prepaid"
                                    onClick={this.handlePayment}
                                    clickModel={ECreditDebitType.prepaid}
                                    status={this.props.parent.selection.creditDebitType === ECreditDebitType.prepaid ? ELElementStatus.selected : ELElementStatus.normal}/>
                                : null
                            }
                        </Col>
                        <Col xs={6} md={6}>
                            <LButton text={OrderingTexts.activation}
                                    onClick={this.handleActivationType}
                                    clickModel={EActivationType.actDefault}
                                    status={this.state.activationType === EActivationType.actDefault ? ELElementStatus.selected : ELElementStatus.normal}/> 
                        </Col>                        
                    </Row>

                    <Row>
                        <Col xs={3} md={3}>
                            
                        </Col>
                        <Col xs={3} md={3}>
                            
                        </Col>
                        <Col xs={6} md={6}>
                        {
                                this.config.canMnpStandard ?                                
                                <LButton text={OrderingTexts.portation}
                                        onClick={this.handleActivationType}
                                        clickModel={EActivationType.portDefault}
                                        status={this.state.activationType === EActivationType.portDefault ? ELElementStatus.selected : ELElementStatus.normal}/>
                                : null
                        }
                        </Col>                        
                    </Row>  

                    <Row>
                        <Col xs={3} md={3}>
                            <LButton 
                                text="Privat" 
                                onClick={this.handleCusomerType}
                                clickModel={ECustomerType.personal}
                                status={this.state.customerType === ECustomerType.personal ? ELElementStatus.selected : ELElementStatus.normal}/>
                        </Col>
                        <Col xs={3} md={3}> 
                        {
                                this.config.canSoho ?                        
                                <LButton 
                                        text="Geschftlich" 
                                        onClick={this.handleCusomerType}
                                        clickModel={ECustomerType.soho}
                                        status={this.state.customerType === ECustomerType.soho ? ELElementStatus.selected : ELElementStatus.normal}/>
                                : null
                        }
                        </Col>
                        {/* <Col xs={6} md={6}>
                        {
                                this.config.canMnpPredate ?                              
                                <LButton text="Vorzeitige Portierung"
                                        onClick={this.handleActivationType}
                                        clickModel={EActivationType.portPre}
                                        status={this.state.activationType === EActivationType.portPre ? ELElementStatus.selected : ELElementStatus.normal}/>
                                : null
                        }
                        </Col>                         */}
                    </Row>                                       
                </Container>
                <LClear />
            </BaseBlock>
            </>
        );

        return ctrl;
    };

    private prepare() {
        var brand = linq.from<IBrand>(StorageHelper.getBrands())
            .where(x=>x.id == this.props.parent.selectedBrand)
            .first();

        this.config.canPrepaid = linq.from<IProduct>(brand.products)
            .where(x=>x.creditDebitType === ECreditDebitType.prepaid)
            .count() > 0;

        this.config.canSoho = linq.from<IProduct>(brand.products)
            .where(x=> (x.customerType & ECustomerType.soho) === ECustomerType.soho) 
            .count() > 0;   

        this.config.canMnpStandard = linq.from<IProduct>(brand.products)
            .where(x=> (x.mnpType & EMnpType.standard) === EMnpType.standard) 
            .count() > 0;            

        this.config.canMnpPredate= linq.from<IProduct>(brand.products)
            .where(x=> (x.mnpType & EMnpType.predate) === EMnpType.predate) 
            .count() > 0;                        
    };

    private handleCategoryBrand = (id: string) =>{

    };

    private handleCusomerType(value: ECustomerType) {
        if(this.mounted) {
            this.setState({customerType: value});
            this.props.parent.applyCustomerType(value);
        }
    };

    private handleActivationType(value: EActivationType) {
        if(this.mounted) {
            this.setState({activationType: value});
            this.props.parent.applyActivationType(value);
        }
    };

    private handlePayment(value: ECreditDebitType) {
        if(this.mounted) {
            this.setState({businessType: value});
            this.props.parent.applyPayment(value);
            // this.props.parent.handleActivation( this.state );
        }
    };
};

export default ActivationBlock;