import * as React from 'react';
import '../errorMessage/errorMessage.scss';

export interface IErrorMessageProps {
    visible: boolean;
    text: string;
};




export class ErrorMessage extends React.Component<IErrorMessageProps> {
    private hasAction: boolean = false;
    constructor(props: IErrorMessageProps) {
        super(props);



        this.state = {
            visible: this.props.visible,
            text: this.props.text
        };
    };

    public componentWillReceiveProps(next: IErrorMessageProps) {
        this.setState({ visible: next.visible });
    };

    public render() {


        let ctrl = <></>;

        this.props.visible? ctrl=(<div className="errorMessageContainer"><h3 >{this.props.text}</h3></div>) : ctrl=<></>

        return ctrl;
    };




};

export default ErrorMessage;