import * as React from 'react';
import App from '../App';
import { INavLink } from "../interfaces/nav/INavLink";
import { ImeiUploadType } from '../screens/am/imei';
import { MnpStatusScreen } from '../screens/am/mnpStatus';
import { EExistingCustomerMode } from '../screens/existing/customer';
import { IErrorScreenData } from '../screens/global/error';
import * as scr from '../screens/insex';
import { ImeiScreen } from '../screens/insex';
import TestScreen from '../screens/test/form';
import Api from './api';
import { AppIds } from './Defaults';
import Global from './Global';
import StorageHelper from './storage';

export class EasyRouter {
    public static last?: INavLink;

    public static routeUrl(item: INavLink) {
        window.history.pushState("object or string", "Title", item.url);
    };

    public static route(item?: INavLink): JSX.Element {
        EasyRouter.last = item;

        let found: boolean = false;

        const queryString = require('query-string');

        let cfg = Global.getConfiguration();

        if (Global.easyApp.head) {
            Global.easyApp.head.appendSub(null);
        }

        let ctrl: JSX.Element = <></>;

        if (item) {
            let uri: string = item.url.replace(Api.getAppUrl(), '');
            let sa: string[] = uri.split('/');

            if (uri.toLowerCase().indexOf('/test') >= 0) {
                found = true;
                ctrl = <TestScreen/>;
            } else if (uri.toLowerCase().indexOf('/am/drafts') >= 0) {
                found = true;
                ctrl = <scr.DraftsScreen />;
            } else if (uri.toLowerCase().indexOf('/am/cancellation') >= 0) {
                found = true;
                ctrl = <scr.CancellationScreen />;
            } else if (uri.toLowerCase().indexOf('/am/mnp/portingstatus') >= 0) {
                found = true;
                ctrl = <scr.StatusPorting onChange={''} />;
            } else if (uri.toLowerCase().indexOf('/apps/orderproducts/selectdeliveryaddress') >= 0) {
                let sa = uri.toLocaleLowerCase().split('/');
                if (sa.length === 5) {
                    found = true;
                    ctrl = <scr.OrderOverview onChange={''} />;
                } else {
                    found = true;
                    ctrl = <scr.DeliveryAddress onChange={onchange} />;
                }
            } else if (uri.toLowerCase().indexOf('/am/contractmanager') >= 0) {
                found = true;
                ctrl = <scr.ContractManagerScreen />;
            } else if (uri.toLowerCase().indexOf("/apps/orderproducts") >= 0) {
                found = true;
                ctrl = <scr.OrderProduct onChange={''} />;

            } else if (uri.toLowerCase().indexOf('/am/idscanner') >= 0) {
                found = true;
                ctrl = <scr.IDScannerScreen />;
            }
            else if (uri.toLowerCase().indexOf('/am/imei') >= 0) { // todo Imei
                found = true;
                ctrl = <ImeiScreen uploadType={ImeiUploadType.List} />;
            } else if (uri.toLowerCase().indexOf('/am/contractmanager') >= 0) {
                found = true;
                ctrl = <scr.ContractManagerScreen />;
            } else if (uri.toLowerCase().indexOf('/ordering/clear') >= 0) {
                StorageHelper.resetOrders();
                ctrl = <scr.WelcomeScreen />
            } else if (uri == '' || uri == '/' || uri.toLowerCase().indexOf('/ordering/welcome') >= 0) {
                StorageHelper.resetOrders();
                found = true;
                ctrl = <scr.WelcomeScreen />;
            } else if (uri.toLowerCase().indexOf('/ordering/preview') >= 0) {
                found = true;
                ctrl = <scr.ProcessNewScreen id={sa[3]} preview={true} />;
            } else if (uri.toLowerCase().indexOf('/ordering/new/') >= 0) {
                found = true;
                let processId = EasyRouter.getQueryVariable(item.url, 'id');
                const prod = EasyRouter.getQueryVariable(item.url, 'product');
                const brand = EasyRouter.getQueryVariable(item.url, 'brand');

                ctrl = <scr.ProcessNewScreen id={processId} productId={prod} brandKey={brand} preview={false} />;
            } else if (uri.toLowerCase().indexOf('/customer/') >= 0) {
                found = true;
                let sa = uri.toLocaleLowerCase().split('/');
                let dm: EExistingCustomerMode = EExistingCustomerMode.main;
                if (sa[2] == 'sim') { dm = EExistingCustomerMode.sim; }
                else if (sa[2] == 'cancellation') { dm = EExistingCustomerMode.cancellation; }
                else if (sa[2] == 'belatedporting') { dm = EExistingCustomerMode.belatedPorting; }
                else if (sa[2] == 'imei') { dm = EExistingCustomerMode.imei; }

                ctrl = <scr.CustomerScreen id={sa[3]} dataMode={dm} />;
            } else if (uri.toLowerCase().indexOf('/user/password') >= 0) {
                found = true;
                ctrl = <scr.EditPasswordScreen />;
            } else if (uri.toLowerCase().indexOf('/user/profile') >= 0) {
                found = true;
                ctrl = <scr.EditProfileScreen />;
            } else if (uri.toLowerCase().indexOf('/user/dealer') >= 0) {
                found = true;
                ctrl = <scr.DealerScreen />;
            } else if (uri.toLowerCase().indexOf('/am/administration') >= 0) {
                found = true;
                ctrl = <scr.AdministrationScreen />;
            } else if (uri.toLowerCase().indexOf('/am/mnp/status') >= 0) {
                found = true;
                ctrl = <MnpStatusScreen />;
            }

            if (!Global.isDelaer(false)) {
                App.setContainer(<scr.ErrorScreen data={{ code: 403.1 }} />, AppIds.container);

                return <></>
            };

            if (found) {
                let t = item.title ? item.title : cfg.appTitle;
                document.title = t;
                if (!item.skipState) {
                    window.history.pushState(t, t, item.url);
                }

                App.setContainer(ctrl, AppIds.container);

                return <></>;
            } else {
                let d: IErrorScreenData = { code: 404 };
                App.setContainer(<scr.ErrorScreen data={d} />, AppIds.container);
            }
        }

        var app = Global.FindReact(document.getElementById(AppIds.container));
        return <scr.InitScreen parent={app} />
    };

    public static reload() {
        EasyRouter.route(EasyRouter.last);
    };

    public static routeAndCallback(item: INavLink, callback: any) {
        EasyRouter.route(item);

        if (callback) {
            callback();
        };
    };

    public static getQueryVariable(url: string, name: string, fallback?: string): string {
        var query = url;

        if (url.indexOf('?') > 0) {
            query = url.substring(url.indexOf('?') + 1);
        }

        var vars = query.split('&');
        for (var i = 0; i < vars.length; i++) {
            var pair = vars[i].split('=');
            if (decodeURIComponent(pair[0]) == name) {
                return decodeURIComponent(pair[1]);
            }
        }

        return fallback ? fallback : '';
    }

    public static isBlank(): boolean {
        if (window.location.pathname.toLowerCase().indexOf('ordering/preview') >= 0) {
            return true;
        } else {
            return false;
        }
    }
};

export default EasyRouter;