import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import ReactTooltip from 'react-tooltip';
import * as cmp from '../../../components/index';
import { OrderDataWrapper } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import IActivation, { ActivationHelper, ActivationUI, ActivationWrapper } from '../../../models/ordering/activation';
import IBrand from '../../../models/ordering/brand';
import { EActivationType, EOrderProcessView } from '../../../models/ordering/order';
import { ELButtonType, ERequestType } from '../../../models/page/diverse';
import { CombiWrapper } from '../../../models/support/ISupportWrapper';
import { CustomerScreen } from '../../../screens/insex';
import ProcessNewScreen from '../../../screens/ordering/process';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { ActivationMessages, MainTexts, OrderingTexts } from '../../../utils/messages';
import EasyRouter from '../../../utils/router';
import StorageHelper from '../../../utils/storage';
import { EPopupType } from '../../dialogs/popup';
import * as page from '../../page/index';
import LLabel from '../../page/label';

export interface IActivationSummaryProps {
    activations: IActivation[];
    parent: ProcessNewScreen
};

export interface IActivationSummaryState {
    confirmRemove: boolean;
    changed: boolean;
    htmlDiscount: string;
    vodafoneIsChecked: boolean;
};

export class ActivationSummary extends React.Component<IActivationSummaryProps, IActivationSummaryState> {
    private brands: IBrand[];
    private brutto: number = 0;
    private bruttoAct: number = 0;
    private hasGiga: boolean = false;
    private pr: EOrderProcessView = EOrderProcessView.tariff;
    private currentIndex: number = -1;
    public static containerId: string = 'activationsummary';
    private aw: ActivationWrapper;

    constructor(props: IActivationSummaryProps) {
        super(props);

        this.socketCallback = this.socketCallback.bind(this);

        this.brands = StorageHelper.getBrands();

        this.aw = new ActivationWrapper(this.props.parent.data);

        this.state = {
            confirmRemove: false, changed: false, htmlDiscount: '', vodafoneIsChecked: false
        };
    };

    public componentWillReceiveProps(next: IActivationSummaryProps) {
        this.setState({ changed: true });
    };

    public render() {
        this.pr = this.props.parent.getProgress();

        this.aw = new ActivationWrapper(this.props.parent.data);

        let css: string = this.props.activations.length < 3 ? 'fixed' : '';

        let cw = CombiWrapper.GetInstance();
        var c = cw.getFirstSuitableCombi(this.props.parent.dataWrapper, true);
        
        let ctrl = (
            <>
                {this.renderPopupConfirm()}
                <div className={"infobox " + css} id={ActivationSummary.containerId}>
                    <page.LButton type={ELButtonType.icon} icon="summary" text="ZUSAMMENFASSUNG" />
                    <div className="spacer-big"></div>
                    {this.renderActivations()}
                    { 
                        c && c.getBlister() ? 
                            <>
                                <div className="spacer-medium" />
                                <div className="" dangerouslySetInnerHTML={{ __html: c.getBlister()}}></div>
                            </>
                         : null
                    }
                </div>
            </>
        );

        return ctrl;
    };

    private renderActivations(): JSX.Element {
        this.brutto = 0;
        this.bruttoAct = 0;

        let cfg1: IBlockConfiguration = {
            title: '',
            transparent: false,
            style: '',
            styleContainer: 'block-innerMiddle',
            collapsible: false,
            collapsed: false
        };

        this.hasGiga = OrderDataWrapper.hasGiga(this.props.parent.data);

        if (!OrderDataWrapper.previewAvailableForAtLeastOne(this.props.activations)) {
            return <div>Keine Buchungen</div>
        };

        let ctrl = (
            <>
                <div className="inner">
                    {this.aw.items.map((i, idx) => (
                        <div key={'act-' + idx}>
                            {this.renderActivation(i, idx)}
                            {
                                idx == this.aw.items.length - 1 ? null : <div key={'sep-' + i} className="sep" />
                            }
                        </div>
                    ))}

                    {this.renderSummary()}
                </div>
            </>
        );

        return ctrl;
    };

    private renderSummary(): JSX.Element {
        let ctrl = (
            <Container>
                <Row>
                    <Col xs={12} md={12} className="info infor">
                        <LLabel text={Global.feeToString(this.aw.getMonthlyFee())} css="big" />
                    </Col>
                    <Col xs={12} md={12} className="info infor">
                        <LLabel text={MainTexts.feeMonthTotal} />
                    </Col>
                </Row>

                <Row>
                    <Col xs={12} md={12} className="info infor">
                        <LLabel text={Global.feeToString(this.aw.getActivationFee())} css="big" />
                    </Col>
                    <Col xs={12} md={12} className="info infor">
                        <LLabel text={MainTexts.feeActTotal} />
                    </Col>
                </Row>
            </Container>
        );

        return ctrl;
    };

    public socketCallback(api: any) {
       
        if (api) {
           
            if (api.error) {
                this.props.parent.showPopup(EPopupType.custom, api.error.message, true);
            } else {
                this.props.parent.showPopup(EPopupType.custom, ActivationMessages.saveSuccess, true);
            }
        }
    }

    // private getTooltipOptions(brand: IBrand, act: IActivation): string {
    //     if (act.services && act.services.length) {
    //         let res = linq.from<IService>(brand.services)
    //             .where(x => act.services.indexOf(x.id) >= 0)
    //             .select(x => x.tooltipExtra ? '<li>' + x.tooltipExtra + '</li>' : '')
    //             .toArray();

    //         return res.join('');
    //     };

    //     return '';
    // };

    private renderActivation(ah: ActivationHelper, idx: number) {

        if (ah.hasMultiple()) {
            let all: JSX.Element[] = [];
           
            ah.items.map((i, j) => {
                all.push(this.renderActivationSingle(i, j, true, idx));
            })

            return all;
        } else {
            return this.renderActivationSingle(ah.items[0], idx, false, idx);
        }
    };

    private renderActivationSingle(item: ActivationUI, 
        idx: number, /**index of activation inside selection */
        isMultiple: boolean, 
        actIndex: number /** unique activation index*/): JSX.Element {
        
        if (!item.productId) { return <></>; };

        let cssi = 'brandi ' + item.brandKey;
        let ctrl = (
            <div key={'act-' + Global.getUniqueId()}>
                <div className={cssi}></div>

                <div className="action">
                    {
                        item.isDone() === false && (!isMultiple || (isMultiple && idx == 0)) ?
                            <>
                                {
                                    <page.LButton type={ELButtonType.iconImage}
                                        icon="edit"
                                        onClick={this.editContract}
                                        clickModel={actIndex} />
                                }
                                {
                                    item.brandKey == BrandKeys.unity ? null : 
                                    <page.LButton type={ELButtonType.iconImage}
                                        icon="save"
                                        onClick={this.saveDraft}
                                        clickModel={actIndex} />

                                }
                                {
                                    this.pr === EOrderProcessView.tariff || this.pr === 0 ?
                                        <page.LButton type={ELButtonType.iconImage}
                                            icon="trash"
                                            onClick={this.removeContract}
                                            clickModel={actIndex} />
                                        : null
                                }
                            </>
                            : null
                    }
                </div>

                <div className="product">
                    <div className="title">{item.product!.name}</div>

                    <div className="spacer" />

                    <Container>
                        <Row>
                            <Col xs={6} md={6} className="info infol np">
                                {
                                    item.ttProduct ?
                                        <ReactTooltip place="top" type="light" effect="solid" id={'tt-pr-' + item.id}>
                                            <div dangerouslySetInnerHTML={{ __html: item.ttProduct }} />
                                        </ReactTooltip>
                                        : null
                                }

                                <label data-tip={item.ttProduct} data-for={'tt-pr-' + item.id}>{OrderingTexts.tariff}</label>
                            </Col>

                            <Col xs={6} md={6} className="info infor">
                                <LLabel text={Global.feeToString(item.getProductMonthlyFee())} />
                            </Col>

                            <Col xs={6} md={6} className="info infol np">
                                {
                                    item.ttHardware ?
                                        <ReactTooltip place="top" type="light" effect="solid" id={'tt-sub-' + item.id}>
                                            <div dangerouslySetInnerHTML={{ __html: item.ttHardware }} />
                                        </ReactTooltip>
                                        : null
                                }

                                <label data-tip={item.ttHardware} data-for={'tt-sub-' + item.id}>{OrderingTexts.sub}</label>
                            </Col>
                            <Col xs={6} md={6} className="info infor">
                                <LLabel text={Global.feeToString(item.getSubMonthlyFee())} />
                            </Col>

                            <Col xs={6} md={6} className="info infol np">
                                {
                                    item.ttOptions ?
                                        <ReactTooltip place="top" type="light" effect="solid" id={'tt-op-' + item.id}>
                                            <div dangerouslySetInnerHTML={{ __html: item.ttOptions }} />
                                        </ReactTooltip>
                                        : null
                                }
                                <label data-tip={item.ttOptions} data-for={'tt-op-' + item.id}>{OrderingTexts.options}</label>
                            </Col>
                            <Col xs={6} md={6} className="info infor">
                                <LLabel text={Global.feeToString(item.getOptionMonthlyFee())} />
                            </Col>

                            <Col xs={6} md={6} className="info infol np">
                                {
                                    item.ttActivation ?
                                        <ReactTooltip place="top" type="light" effect="solid" id={'tt-ac-' + item.id}>
                                            <div dangerouslySetInnerHTML={{ __html: item.ttActivation }} />
                                        </ReactTooltip>
                                        : null
                                }
                                <label data-tip={item.ttActivation} data-for={'tt-ac-' + item.id}>{MainTexts.activationFee}</label>
                            </Col>

                            <Col xs={6} md={6} className="info infor">
                                <LLabel text={Global.feeToString(item.getActivationFee())} />
                            </Col>

                            <Col xs={6} md={6} className="info infol np">
                                <LLabel text={MainTexts.feeMonthTotal} css="best" />
                            </Col>
                            <Col xs={6} md={6} className="info infor">
                                <LLabel text={Global.feeToString(item.getMonthlyFee())} css="best" />
                            </Col>
                        </Row>
                    </Container>
                    <div className="sline"></div>
                </div>
                <page.LClear />
            </div>
        );

        return ctrl;
    };

    private removeContract = (idx: number) => {
        let a = this.props.activations[idx];

        if (a.activationType === EActivationType.partnerCard) {

            StorageHelper.removeOrder(a.processId, () => {
                EasyRouter.route({
                    url: CustomerScreen.uri + this.props.parent.data.parentId,
                    title: MainTexts.existingCustomerActivation
                });
            });

            return;
        } else {
            this.currentIndex = idx;
            this.setState({ confirmRemove: true });
        }

    };

    private onDismissConfirmPopup = () => {
        this.setState({ confirmRemove: false });
    };

    private renderPopupConfirm(): JSX.Element {
        return <cmp.Popup
            type={EPopupType.confirm}
            visible={this.state.confirmRemove}
            closable={true}
            id="wait"
            key="popup-confirm"
            title={MainTexts.pleaseWait}
            height={200}
            onDismiss={this.onDismissConfirmPopup}
            onNegative={this.onDismissConfirmPopup}
            onPositive={() => {
                this.props.parent.removeActivation(this.currentIndex);
                this.currentIndex = -1;
                this.setState({ confirmRemove: false });
            }}>
            <cmp.PopupBody>
                <></>
                <></>
            </cmp.PopupBody>
            <></>
        </cmp.Popup>
    };

    private saveDraft = (idx: number) => {
        this.props.parent.showPopup(EPopupType.pleaseWait, '', true);

        let draft = this.props.parent.dataWrapper.getDraftFromActivation(this.props.parent.data.activations[idx].id);

        setTimeout(() => {
            Global.hub.send(JSON.stringify(draft),
                ERequestType.saveDraft, undefined
            );
        }, 1000);
    };

    private editContract = (idx: number) => {
        let pr = this.props.parent.getProgress();

        if (pr !== EOrderProcessView.tariff) {
            this.props.parent.gotoView(EOrderProcessView.tariff, () => {
                this.props.parent.editActivation(idx);
            });
        } else {
            this.props.parent.editActivation(idx);
        };
    };
};

export default ActivationSummary;