import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import Rot_ErwerbNichtMoeglich from '../../../assets/icons/trafficLight_noVVL.svg';
import Gruen_ErwerbMoeglich from '../../../assets/icons/trafficLight_VVL.svg';
import Gelb_ErwerbMoeglichMitTarifwechsel from '../../../assets/icons/trafficLight_VVLTW.svg';
import ErwerbMitAddedRuntimeMoeglich from '../../../assets/icons/trafficLight_vVVL.svg';
import { IBaseComponentState } from '../../../interfaces';
import { EControlType, ECustomerActionPurchase, ELButtonType, OrderDataWrapper } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { IContract } from '../../../models/existing/contract';
import { Brand } from '../../../models/ordering/brand';
import CustomerScreen from '../../../screens/existing/customer';
import { BrandKeys } from '../../../utils/Defaults';
import { CustomerTexts, MainTexts } from '../../../utils/messages';
import BrandThumb, { EBrandThumbStatus, EBrandThumbType } from '../../ordering/brandThumb';
import { LButton, LControl } from '../../page';
import BaseBlock from '../../page/block';

export interface IExistingCustomerContractProps {
    parent: CustomerScreen;
};

export interface IExistingCustomerContractState {

};

export class ExistingCustomerContract extends React.Component<IExistingCustomerContractProps, IBaseComponentState> {
    private contract: IContract;
    private red: boolean = false;
    private green: boolean = false;
    private yellow: boolean = false;
    private noColor: boolean = false;
    private showFN: boolean = false;
    private showTV: boolean = false;

    constructor(props: IExistingCustomerContractProps) {
        super(props);

        this.state = {
            changed: false

        };

        this.contract = this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0] : {};
        
        if (this.contract.purchase) {
            switch (this.contract.purchase) {
                case 1: {
                    this.red = true;
                    break;
                }
                case 2: {
                    this.yellow = true;
                    break;
                }
                case 3: {
                    this.green = true;
                    break;
                }
                case 4: {
                    this.noColor = true;
                    break;
                }
            }
        }
        if (this.red) {
            var dateToday = new Date();
            if (this.contract.actions !== undefined) {
                this.contract.actions.forEach(x => {
                    var dateServer = new Date(x.possibleFrom);

                    if (x.purchase === ECustomerActionPurchase.addedRuntime && dateToday.getFullYear() === dateServer.getFullYear() && dateToday.getMonth() === dateServer.getMonth() && dateToday.getDate() === dateServer.getDate()) {

                        this.red = false;
                        this.noColor = true;
                    }
                })
            }
        }
        if (this.contract.brandKey === BrandKeys.vodafone) {
            this.gigakombiCheck()
        }
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: undefined,
            transparent: false,
            style: '',
            styleContainer: ''
        };

        let ctrl =
            <Container>
                <Row>
                    <Col md={3}>
                        <BrandThumb ckey={'bt-brand-' + this.props.parent.brand.id} item={Brand.fromInterface(this.props.parent.brand)} onClick={undefined} type={EBrandThumbType.square} status={EBrandThumbStatus.active} />
                    </Col>
                    <Col md={4}>
                        <div className="text-h1">{this.props.parent.data.customer.firstname} {this.props.parent.data.customer.surname}</div>
                        <div className="spacer" />
                        {
                            //this.props.parent.brand.id == BrandKeys.otelo ?
                            //<LControl type={EControlType.info} title={MainTexts.contractId} label={this.contract.contractId} style="merged"/>
                            //:
                            <LControl type={EControlType.info} title={MainTexts.customerId} label={this.contract.customerId} style="merged" />
                        }

                        <div className="spacer" />
                        <LControl type={EControlType.info} title={MainTexts.msisdn} label={OrderDataWrapper.getMsisdnFromContract(this.props.parent.data)} style="merged" />
                        <div className="spacer" />
                        <LControl type={EControlType.info} title={MainTexts.contract} label={OrderDataWrapper.getProductFromContract(this.props.parent.brand, this.props.parent.data)} style="merged" />
                    </Col>


                    {this.contract.brandKey === BrandKeys.vodafone ? <Col xs={3} md={3} >
                        <Row>
                            <Col xs={12} md={12}>
                                <img style={{ width: '100%' }} src="/images/GigaKombiBooked.svg" />
                            </Col>
                        </Row>

                        <Row>

                            <Col xs={{ span: 1, offset: 10 }}>
                                {!this.showFN || !this.showTV ? <LButton
                                    icon="info" id="gk" tooltip={MainTexts.gigakombiTooltip} type={ELButtonType.linkWithIconLeft} /> : <></>}
                            </Col>

                        </Row>

                        <Row>
                            <Col xs={12} md={12}>
                                {!this.showFN ? <LButton
                                    icon="ok-disabled" id="gk" text="GigaKombi Festnetz" type={ELButtonType.linkWithIconLeft} />
                                    :
                                    <LButton
                                        icon="ok-green" text="GigaKombi Festnetz" type={ELButtonType.linkWithIconLeft} />}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12}>
                                {!this.showTV ? <LButton
                                    icon="ok-disabled" id="gk2" text="GigaKombi TV" type={ELButtonType.linkWithIconLeft} />
                                    :
                                    <LButton
                                        icon="ok-green" text="GigaKombi TV" type={ELButtonType.linkWithIconLeft} />}
                            </Col>
                        </Row>


                    </Col> : <></>}


                    <Col md={2}>
                        {this.contract.brandKey === BrandKeys.vodafone ? 
                        <>
                            {this.red ? <img style={{ width: '190px', height: '190px' }} src={Rot_ErwerbNichtMoeglich} alt={"contractRed"} /> : <></>}
                            {this.green ? <img style={{ width: '190px', height: '190px' }} src={Gruen_ErwerbMoeglich} alt={"contractGreen"} /> : <></>}
                            {this.yellow ? <img style={{ width: '190px', height: '190px' }} src={Gelb_ErwerbMoeglichMitTarifwechsel} alt={"contractYellow"} /> : <></>}
                            {this.noColor ? <img style={{ width: '190px', height: '190px' }} src={ErwerbMitAddedRuntimeMoeglich} alt={"contractNoColor"} /> : <></>}

                            {
                                this.contract.churner ? 
                                <div className="text-desc tar">
                                    {CustomerTexts.churner}
                                </div>
                                : null
                            }
                        </>
                            : <></>
                        }
                    </Col>
                </Row>
            </Container>;

        ctrl = <BaseBlock config={cfg}>
            {ctrl}
        </BaseBlock>;

        return ctrl;
    };

    private gigakombiCheck() {
        if (this.contract.bookedProduct) {
            if (this.contract.bookedProduct.services.findIndex(x => x === 'VFAIO2MRK' || x === 'VFGSUK10') > -1) {
                this.showFN = true;
            }
            if (this.contract.bookedProduct.services.findIndex(x => x === 'GKTVMRK') > -1) {
                this.showTV = true;
            }
        }


    }
};

export default ExistingCustomerContract;