import * as React from 'react';
import BaseBlock from '../../page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import ProcessNewScreen from '../../../screens/ordering/process';
import { Row, Col, Container } from 'react-bootstrap';
import * as page from '../../page/index';
import * as m from '../../../models/index';
import { MainTexts, ValidationMessages } from '../../../utils/messages';
import { ECreditDebitType, EActivationType, EOrderProcessView, ENumberPortingType } from '../../../models/ordering/order';
import { BrandKeys } from '../../../utils/Defaults';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import Tel from '../global/tel';
import { Tel as TelModel } from '../../../models/ordering/tel';
import { IControlValue, IKeyText } from '../../../models/page/interfaces';
import Global from '../../../utils/Global';
import StorageHelper from '../../../utils/storage';
import * as linq from 'linq';
import { IAvailabilityBrand } from '../../../models/ordering/availability';

export interface IPortingOptionsProps {
    parent: ProcessNewScreen;
    onChange: any;
    //parentCallback: any;
    //refreshParent: any;
};

export interface IPortingOptionsState {
    changed: boolean;
    individualNumber: boolean;
    multipleNumbers: boolean;
};

export class PortingOptions extends React.Component<IPortingOptionsProps, IPortingOptionsState> {
    public numbersToPort: number;
    public portType:ENumberPortingType = ENumberPortingType.single;
    private brandKey: string = '';
    private avl: m.Availability;

    constructor(props: IPortingOptionsProps) {
        super(props);

        this.onChange = this.onChange.bind(this);

        this.avl = StorageHelper.getAvailability();

        this.numbersToPort = this.getFromNumbers();

        this.state = {
            changed: false,
            individualNumber: true,
            multipleNumbers: false
        };
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: MainTexts.portingInformation,
            transparent: false
        };

        this.brandKey = this.props.parent.current ?  this.props.parent.current.brandKey : '';

        if(this.props.parent.data.customer.mnp){
            this.portType = this.props.parent.data.customer.mnp.numberPortingType ? this.props.parent.data.customer.mnp.numberPortingType: 1; 
            this.numbersToPort = this.props.parent.data.customer.mnp.countNumbers ? this.props.parent.data.customer.mnp.countNumbers: this.getFromNumbers();
        };

        let multiplePortingAvailable: boolean = true;

        if(this.props.parent.current && this.props.parent.current.brandKey == BrandKeys.dsl) {
            if(this.avl.hasRegioOnly(BrandKeys.dsl, this.props.parent.current.productId)) { // && !this.avl.hasBasic(BrandKeys.dsl, this.props.parent.current.productId)) {
                multiplePortingAvailable = false;
            };
        };

        if(this.props.parent.current && this.props.parent.current.brandKey == BrandKeys.kabel) {
            if(!this.avl.hasKip()) {
                multiplePortingAvailable = false;
            };
        };        

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={6} md={6}>
                            <page.LControl type={m.EControlType.radio}
                                onChange={this.onChange}
                                value={ENumberPortingType.single}
                                checked={this.portType == ENumberPortingType.single ? true: false}
                                name="portOption_1"
                                id="portOption_1" label={MainTexts.individualPorting} />
                        </Col>

                        {
                            multiplePortingAvailable ? 
                            <Col xs={6} md={6}>
                                <page.LControl type={m.EControlType.radio}
                                    onChange={(v)=>{
                                        this.onChange(v);
                                    }}
                                    value={ENumberPortingType.multiple}
                                    checked={this.portType == ENumberPortingType.multiple ? true: false}
                                    name="portOption_2"
                                    id="portOption_2" label={MainTexts.multiplePorting} 
                                    hint={this.props.parent.current && (this.props.parent.current.brandKey == BrandKeys.dsl || this.props.parent.current.brandKey == BrandKeys.kabel) ? "* + 9,99  einmalig" : ""}/>
                            </Col>
                            : null
                        }

                        {
                            multiplePortingAvailable && this.brandKey === BrandKeys.kabel ? 
                            <Col xs={6} md={6}>
                                <page.LControl type={m.EControlType.radio}
                                    onChange={this.onChange}
                                    value={ENumberPortingType.later}
                                    checked={this.portType == ENumberPortingType.later ? true: false}
                                    name="portOption_3"
                                    id="portOption_3" label={MainTexts.laterPorting} />
                            </Col>
                            : null
                        }
                    </Row>
                    {this.portType===2 ? <> <div className="spacer"></div>
                        <div className="spacer"></div>
                        <div className="spacer"></div>
                        <Row>
                            <Col xs={6} md={6}>
                                <page.LControl 
                                    type={m.EControlType.dropdown}
                                    value={this.numbersToPort}
                                    onChange={this.onChange}
                                    name="numberOfItemsToPort" 
                                    id="numberOfItemsToPort_id" 
                                    title={MainTexts.numberOFPhonesToBePorted}
                                    options={this.getNumberCount()} />
                            </Col>
                        </Row>
                    </>
                        : <></>
                    }

                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };

    private getFromNumbers() {
        let start: number = 2;

        if(this.brandKey == BrandKeys.dsl && this.props.parent.current && this.avl.hasBasic(BrandKeys.dsl, this.props.parent.current.productId)) {
            start = 3;
        };

        return start;
    };
 
    private getNumberCount(): IKeyText[] {
        let opt: IKeyText[] = [];
        let start: number = this.getFromNumbers();

        for (var _i = start; _i <= 10; _i++) {
            opt.push({ key: _i.toString(), text: _i.toString() });
        }

        return opt;
    };

    private applyCablePortation(type: ENumberPortingType) {
        if(this.brandKey == BrandKeys.kabel && this.props.parent.current) {
            let svc = this.props.parent.current.services;
            //.push('rufnummernmitnahme-x');
            Global.removeElementFromArray(svc, 'rufnummernmitnahme-1');
            Global.removeElementFromArray(svc, 'rufnummernmitnahme-x');

            if(type == ENumberPortingType.multiple) {
                this.props.parent.current.addRemoveService('rufnummernmitnahme-x', this.props.parent.current.selection.children[0].id)
                svc.push('rufnummernmitnahme-x');
            } else {
                this.props.parent.current.addRemoveService('rufnummernmitnahme-1', this.props.parent.current.selection.children[0].id)
                svc.push('rufnummernmitnahme-1');
            };

            this.props.parent.current.services = svc;
        };
    };

    private onChange(e: IControlValue) {
        if(this.props.parent.data.customer.mnp) {
            if (e.type === m.EControlType.radio) {
                let sa = e.name.split('_');

                this.portType = Number.parseInt(sa[1]);
                this.props.parent.data.customer.mnp.numberPortingType = Number.parseInt(sa[1]);
                
                this.applyCablePortation(this.portType);
                
                if(this.portType == ENumberPortingType.single) {
                    this.numbersToPort = this.props.parent.data.customer.mnp.countNumbers = 1;
                } else if(this.portType == ENumberPortingType.multiple) { 
                    this.numbersToPort = this.props.parent.data.customer.mnp.countNumbers = this.getFromNumbers();
                };
            } else {
                this.numbersToPort = this.props.parent.data.customer.mnp.countNumbers = e.value;
            };
        };

        StorageHelper.addOrder(this.props.parent.data);

        this.props.parent.refresh();
    }
};

export default PortingOptions;