import * as React from 'react';
import Popup, { EPopupType } from '../../components/dialogs/popup';
import Global from '../../utils/Global';
import { ERequestType, ELButtonType, EControlType } from '../../models';
import { MainTexts } from '../../utils/messages';
import { IBlockConfiguration } from '../../models/block/configuration';
import { Container, Row, Col } from 'react-bootstrap';
import { PopupBody, LButton, LClear, LControl, Inner1C } from '../../components';
import BaseBlock from '../../components/page/block';
import { IControlValue } from '../../models/page/interfaces';
import { IApiResponse, IApiSummary } from '../../interfaces';
import Api from '../../utils/api';
import { Roles } from '../../utils/Defaults';


export interface ISpaceProps {
}

export interface ISpaceState {
  
}

export default class Spice extends React.Component<ISpaceProps, ISpaceState> {
    
    public static containerId: string = 'SpiceScreen';

    constructor(props) {

        super(props);

       
        Global.easyApp.setTitle("Spice");


        this.state = {
            
        };
    }

    render() {
        const cfg: IBlockConfiguration = {
            title: MainTexts.mnpStatus,
            transparent: false,
            styleContainer: ''
        };

       
        return <div id={Spice.containerId}>
           
                  
                    <BaseBlock config={cfg}>
                        <Container className="container-action">
                            <Row>
                                <Col xs="12" md="12">
                                <iframe src={'https://apps.unitymedia.de/SpiceExternal'} frameBorder="0" style={{ width: '100%', height: '1800px', border: 0 }} />
                                </Col>
                            </Row>
                        </Container>
                    </BaseBlock>
         </div>
    }

   
}