
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { EControlType } from '../../models/page/diverse';
import { IControlValue } from '../../models/page/interfaces';
import Global from '../../utils/Global';
import { MainTexts } from "../../utils/messages";
import LControl from './control';



export interface ILTimeProps {
    value?: string;
    onChange?: any;
    id: string;
    name?: string;
};

export interface ILTimeState {
    changed: boolean;
};

export class LTime extends React.Component<ILTimeProps, ILTimeState> {
    private value: string = ''
    private hh: string = '';
    private min: string = '';
   
    private valueControl: IControlValue = { id: '', name: '', type: EControlType.undefined };

    constructor(props: ILTimeProps) {
        super(props);
          let test = this.props.value? this.props.value.split(":") : [];
        this.hh = this.props.value? test[0] : '';
        this.min = this.props.value? test[1] : '';

        this.changeValue = this.changeValue.bind(this);
        
        this.state = {
            changed: false,
        };
    };

    public componentWillReceiveProps(next: ILTimeProps) {
        let test = next.value? next.value.split(":") : [];
        this.hh = next.value? test[0] : '';
        this.min = next.value? test[1] : '';
       
        this.setState({ changed: true});
    };

    public render() {
        return this.renderControl(this.value);
    };

    private renderControl(value: string): JSX.Element {
       
        let ctrl =
            (
                <Container className={"oe-control"}>
                    <>
                        <Row>
                            {/* <Col xs={12} md={12} className="np">
                                    <page.LControl type={EControlType.text} title={MainTexts.telCallback}
                                        value={del.callbackTime}
                                        onChange={this.props.onChange} name="delivery.callbackTime" id="delivery-callbackTime"
                                        label='z.b. gegen 9:00' />
                                </Col> */}
                            <Col xs={6} md={6}>
                                <LControl
                                    title={MainTexts.telCallback}
                                    type={EControlType.dropdown}
                                    value={this.hh}
                                    onChange={(e)=>{this.hh = e.value ;this.changeValue()}}
                                    name={"delivery.callbackTimeSplitted.hour"}
                                    isChild={true}
                                    id={"delivery-callback-h"}
                                    options={Global.getHours()} />
                            </Col>
                            <Col xs={6} md={6}>
                                <LControl
                                    type={EControlType.dropdown}
                                    value={this.min}
                                    onChange={(e)=>{this.min = e.value ;this.changeValue()}}
                                    name={"delivery.callbackTimeSplitted.minutes"}
                                    id={"delivery-callback-m"}
                                    isChild={true}
                                    title={''}
                                    options={Global.getMinutes()} />
                            </Col>
                        </Row>
                    </>
                </Container >
            );

        return ctrl;
    };

    public getValue() {
        return {
            type: EControlType.time,
            name: this.props.id ?  this.props.id : '',
            id: this.props.id ?  this.props.id : '',
            value: this.hh + ':' + this.min            
        };        
    };

    private changeValue() {
        let val = this.getValue();
        this.props.onChange(val);
    };

};

export default LTime;