import * as React from 'react';
import BaseBlock from '../../../components/page/block';
import ProcessNewScreen from '../../../screens/ordering/process';
import IActivation from '../../../models/ordering/activation';
import BrandThumb, { EBrandThumbType, EBrandThumbStatus } from '../brandThumb';
import StorageHelper from '../../../utils/storage';
import IBrand from '../../../models/ordering/brand';
import linq from 'linq';

export interface IActivationContractBlockProps {
    parent: ProcessNewScreen;
    activation: IActivation
};

export class ActivationContractBlock extends React.Component<IActivationContractBlockProps,any> {
    constructor(props: IActivationContractBlockProps) {
        super(props);
    };

    public render() {
        let br = linq.from<IBrand>(StorageHelper.getBrands())
            .where(x=>x.id == this.props.activation.brandKey)
            .first();

        let ctrl = (
            <div className="block-contract">
                <div className="block block-act-contract">
                    <BrandThumb ckey={'bt-brand-' + br.id} item={br} onClick={undefined} type={EBrandThumbType.blister} status={EBrandThumbStatus.static}/>
                    <div className="block-inner">
                        Aktivierung
                        <br/>MSISDN: 0172
                        <br/>Code
                        <br/>Sofort aktivierung
                    </div>
                </div>
            </div>
        );

        return ctrl;
    };
};

export default ActivationContractBlock;