import * as React from 'react';
import LButton from '../page/button';

export interface IBlockInfoProps {
    title: string;
    children?: JSX.Element[];
    button?: JSX.Element;
    style?: string;
};

export class BlockInfo extends React.Component<IBlockInfoProps, any> {
    constructor(props) {
        super(props);
    };

    public render() {
        let ctrl = <></>;

        let css = 'block-info';

        if(this.props.style) {
            css += ' ' + this.props.style;
        }

        ctrl = (
            <div className={css}>
                <div className="title text-h2">{this.props.title}</div>
                <div className="content text-body">{this.props.children}</div>
                <div className="action">{this.props.button}</div>
            </div>
        );
        return ctrl;
    };
};

export default BlockInfo;