import * as linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBaseComponentState } from '../../../interfaces';
import { ERequestType, ICustomerAction, OrderDataWrapper } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { IContract } from '../../../models/existing/contract';
import { EActivationType, EBusinessCase, EIDCardType } from '../../../models/ordering/order';
import CustomerScreen from '../../../screens/existing/customer';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { ActionTexts, Hints, MainTexts, OrderingTexts } from '../../../utils/messages';
import EasyRouter from '../../../utils/router';
import StorageHelper from '../../../utils/storage';
import { EPopupType, IPopupConfig } from '../../dialogs/popup';
import TariffThumb from '../../ordering/tariffThumb';
import BaseBlock from '../../page/block';

export interface IExistingCustomerActivitiesProps {
    parent: CustomerScreen;
};

export interface IExistingCustomerActivitiesState extends IBaseComponentState {
};

export class ExistingCustomerActivities extends React.Component<IExistingCustomerActivitiesProps, IExistingCustomerActivitiesState> {
    private contract: IContract;
    constructor(props: IExistingCustomerActivitiesProps) {
        super(props);

        this.state = {
            changed: false
        };

        this.selectActivity = this.selectActivity.bind(this);
        this.addNextContract = this.addNextContract.bind(this);

        this.contract = this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0] : {};
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.activities,
            transparent: false,
            style: '',
            styleContainer: '',
            collapsible: true,
            collapsed: false
        };

        let act: JSX.Element = <></>;
        let act2: JSX.Element = <></>;
        let info: JSX.Element = <></>;

        if (this.contract.actions) {
            let aa = linq.from<ICustomerAction>(this.contract.actions)
                .where(x => !x.active)
                .toArray();

            if (aa && aa.length) {
                info = (
                    <ul>
                        {aa.map((i, idx) =>
                            (<li>{i.name} {Global.toGermanDate(i.possibleFrom).endsWith('1900') ? null : ' mglich ab: ' + Global.toGermanDate(i.possibleFrom)}</li>)
                        )}
                    </ul>
                );
            };

            aa = linq.from<ICustomerAction>(this.contract.actions)
                .where(x => x.active)
                .toArray();

                

            act = (
                <>
                    {aa.map((i, idx) =>
                        (<TariffThumb
                            text={i.name }
                            id={'action-' + (this.contract.actions ? this.contract.actions.indexOf(i) : -1)}
                            idx={idx}
                            key={idx}
                            cssStyle="thumb-w2"
                            brandKey={''}
                            selected={false}
                            styleBrand={true}
                            onClick={this.selectActivity} />)
                    )}
                </>
            );
        };

        act2 = <>
            <TariffThumb
                        text={ActionTexts.belateporting}
                        id={'action-' + 101}
                        idx={101}
                        key={101}
                        cssStyle="thumb-w2"
                        brandKey={''}
                        selected={false}
                        styleBrand={true}
                        onClick={() => {
                            EasyRouter.route({ url: '/customer/belatedporting/' + this.props.parent.data.id, title: ActionTexts.belateporting });
                        }} />   

            <TariffThumb
                text={ActionTexts.addImei}
                id={'action-' + 100}
                idx={100}
                key={100}
                cssStyle="thumb-w2"
                brandKey={''}
                selected={false}
                styleBrand={true}
                onClick={() => {
                    let msisdn;
                    if (this.props.parent.data.customer.contracts) {
                        msisdn = this.props.parent.data.customer.contracts[0].msisdn;
                    }
                    this.props.parent.openImei(msisdn);
                }} />

            <TariffThumb
                text={ActionTexts.orderSim}
                id={'action-' + 102}
                idx={102}
                key={102}
                cssStyle="thumb-w2"
                brandKey={''}
                selected={false}
                styleBrand={true}
                onClick={() => {
                    EasyRouter.route({ url: '/customer/sim/' + this.props.parent.data.id, title: ActionTexts.orderSim });
                }} />

            <TariffThumb
                text={MainTexts.cancellation}
                id={'action-' + 101}
                idx={101}
                key={101}
                cssStyle="thumb-w2"
                brandKey={''}
                selected={false}
                styleBrand={true}
                onClick={() => {
                    let cfg: IPopupConfig = {
                        props: {
                            id: 'dynamicPopup',
                            title: 'Stornoprozess wird vorbereitet',
                            visible: true,
                            closable: false,
                            type: EPopupType.pleaseWait
                        },
                        closeOnDone: true,
                        //callback: ()=> { },
                        content: <div>{OrderingTexts.customerData}</div>
                    };

                    let t = {
                        type: ERequestType.customerGetCanellation,
                        brandKey: this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0].brandKey : '',
                        id: this.props.parent.data.id
                    };

                    this.props.parent.processCustomer(() => {
                        Global.hub.send(JSON.stringify(t),
                            ERequestType.customerGetCanellation,
                            undefined);
                    }, cfg);
                    //EasyRouter.route({ url: '/customer/cancellation/' + this.props.parent.data.id, title: ActionTexts.cancel });
                }} />

            <TariffThumb
                text={ActionTexts.addNextContract}
                id={'action-' + 102}
                idx={102}
                key={102}
                cssStyle="thumb-w2"
                brandKey={''}
                selected={false}
                styleBrand={true}
                onClick={() => {
                    this.addNextContract()
                }} />                
        </>;

       // act2 = <></>

        let ctrl =
            <Container>
                <Row>
                    <Col md={12} className="np">
                        {info}
                    </Col>
                </Row>
                <Row>
                    <Col md={12} className="np">
                        {act}
                    </Col>
                </Row>
                {
                    this.props.parent.data.customer.contracts && this.props.parent.data.customer.contracts[0].brandKey === BrandKeys.otelo ?
                        <Row>
                            <Col md={12} className="np">
                                {act2}
                            </Col>
                        </Row>
                        : null
                }

                {
                    this.props.parent.data.customer.contracts && this.props.parent.data.customer.contracts[0].brandKey === BrandKeys.vodafone ?
                        <Row>
                            <Col md={12} className="np">
                                <span className="text-h1 red">*</span> {Hints.hintVfVllOldTariff}
                            </Col>
                        </Row>
                        : null
                }
            </Container>;

        ctrl = <BaseBlock config={cfg}>
            {ctrl}
        </BaseBlock>;

        return ctrl;
    };

    private selectActivity(value: string) {
        let id = Number.parseInt(value.replace('action-', ''));
       
        if (this.contract && this.contract.actions) {
            let a = this.contract.actions[id];

            // if(this.props.parent.data.customer.payment){
            //     this.props.parent.data.customer.payment.iban = '';
            // }

            let d = OrderDataWrapper.createProcess(a.bc, EActivationType.existingCustomer, this.props.parent.data, a);
            this.props.parent.refresh();          
            EasyRouter.route({ url: '/customer/tariff/' + d.id, title: a.name + ' ' + OrderDataWrapper.getMsisdnFromContract(d) });
        }
    };

    private addNextContract() {
        let d = OrderDataWrapper.createProcess(EBusinessCase.bnt, EActivationType.actDefault, this.props.parent.data, undefined);
       
        d.activations.map((i)=>{
            i.nextContract = true;
            i.id = Global.getUniqueId();
            i.processId = d.id;
            i.brandKey = BrandKeys.otelo;
            i.contractId = '';
           // i.productId = productId? productId : '3'
            i.msisdn = '';
            i.code = '';
           
             
        });
        d.customer.nationality = 'DEU';
        d.customer.legitimation = { id: '', type: EIDCardType.german, authority: d.customer.address ? d.customer.address.city : ''};
        StorageHelper.addOrder(d);
        Global.categoryWrapper.switchCat(this.props.parent.data.currentCat);
       
        //this.props.parent.refresh();
        EasyRouter.route({ url: '/ordering/new/tariff/' + d.id, title: ActionTexts.addNextContract });
        this.setState({changed: true});
    };
};

export default ExistingCustomerActivities;