import * as React from 'react';
import linq from 'linq';
import ProcessNewScreen from '../../../screens/ordering/process';
import { Row, Col, Container } from 'react-bootstrap';
import TariffThumb from '../tariffThumb';
import IService, { IServiceRule } from '../../../models/ordering/service';
import IBrand from '../../../models/ordering/brand';
import * as cmp from '../../index';
import { EPopupType } from '../../dialogs/popup';
import Global from '../../../utils/Global';
import IProduct, { ISubConfig, ISubServiceConfig } from '../../../models/ordering/product';
import OrderingHelper from '../../../utils/OrderingHelper';
import { EServiceType, EBusinessCase, EActivationType, EServiceRuleType, EBonusType } from '../../../models/ordering/order';
import { IActivation, ELButtonType } from '../../../models';
import ProductWrapper from '../../../models/ordering/productWrapper';
import { MainTexts } from '../../../utils/messages';
import { BrandKeys } from '../../../utils/Defaults';
import ISub from '../../../models/ordering/sub';
import GlobalJSX from '../../../utils/GlobalJSX';
import StorageHelper from '../../../utils/storage';

export interface IActivationServiceSelectorProps {
    activation: IActivation;
    preselected?: string[];
    brand: IBrand;
    onChanged: any;
    bc: EBusinessCase;
    canEdit: boolean;
    blockAddRemove?: boolean;
};

export interface IActivationServiceSelectorState {
    changed: boolean;
    popupVisible: boolean;
    activation:IActivation;
};

export class ActivationServiceSelector extends React.Component<IActivationServiceSelectorProps, IActivationServiceSelectorState> {
    private alreadyBooked:IService[] = [];
    private inclusive: IService[] = [];
    private product?: IProduct;
    private sub?:ISubConfig;
    private serviceRules: IServiceRule[] = [];

    constructor(props: IActivationServiceSelectorProps) {
        super(props);

        this.onDismissSelector = this.onDismissSelector.bind(this);
        this.onDone = this.onDone.bind(this);
        this.thumbClick = this.thumbClick.bind(this);

        this.serviceRules = this.props.brand.serviceRules;

        this.state = {
            changed: false,
            popupVisible: false,
            activation: this.props.activation
        };
    };

    private onDismissSelector() {
        this.setState({popupVisible: false});
    };

    private onDone() {
        this.setState({popupVisible: false});
    };

    private renderSelection() : JSX.Element {
        let ctrl : JSX.Element[] = [];

        let prod : IProduct = linq.from<IProduct | any>(this.props.brand.products)
            .where(x=>x.id === this.state.activation.productId)
            .firstOrDefault();
        
        if(!prod) { return <></>; };
        
        let pw = new ProductWrapper(this.state.activation.brandKey, prod);

        let all = pw.getServices(this.state.activation, this.props.bc, false);

        let availableSvc : IService[] = linq.from<IService>(this.props.brand.services)
            .where(x=> all.indexOf(x.id)>=0)
            .where(x=>!x.mandatory) 
            .toArray();

        //reverse excludes - remove all services which are notbookable with already selected services
        let reverseExcludes: string [] = [];
        for(let i = 0; i < availableSvc.length; i++) {
            let s = availableSvc[i];
           
            if(!s.notbookableWith) { continue;}

            for(let j =0; j < this.state.activation.services.length; j++){

                if(s.notbookableWith.indexOf(this.state.activation.services[j]) >= 0) {
                    reverseExcludes.push(s.id);
                    continue;
                };
            };
        };

        //for vvl/tw remove already booked
        if(this.state.activation.activationType == EActivationType.existingCustomer) {
            let abids = linq.from<IService>(this.alreadyBooked).select(x=>x.id).toArray();
            availableSvc = linq.from<IService>(availableSvc).where(x=>abids.indexOf(x.id) < 0).toArray();
        };
      
        availableSvc = linq.from<IService>(availableSvc).where(x=>reverseExcludes.indexOf(x.id)<0).toArray();

        let available : string[] = linq.from<IService>(availableSvc)
            .select(x=>x.id)
            .toArray();

        let excludes : string[] = [];

        availableSvc.map((i,idx) => {
            if(this.state.activation.services.indexOf(i.id) >= 0) {
                if(i.notbookableWith && i.notbookableWith.length > 0) {
                    i.notbookableWith.map((j)=>{ 

                        //one of the notbookable is already selected
                        if(this.state.activation.services.indexOf(j) >=0 ) {  

                        } else {
                            if(excludes.indexOf(j) < 0 ) { if(i.id !== j) { excludes.push(j);} }
                        }
                    });
                };
            };            
        });

        let svc:IService[] = linq.from<IService>(this.props.brand.services)
            .where(x=>available.indexOf(x.id) >= 0 && !x.hidden)
            .toArray(); 
            
        let groups : string[] = linq.from<IService>(svc).orderBy(x=>x.fmailySort).select(x=>x.familyName).distinct().toArray();
   
        for(let i : number = 0; i < groups.length; i++ ) {
            let svcGroup : IService[] = linq.from<IService>(this.props.brand.services)
                .where(x=>x.familyName === groups[i] && available.indexOf(x.id)>=0)
                .toArray();
            let groupItems : JSX.Element[] = []; 

            svcGroup.map((i, idx)=> {
                //inclusive promos/options shouldnt be selectable
                if(i.type == EServiceType.inclusive) {
                    return;
                };

                //services coming from NBA are not offered for selection
                if(this.state.activation.isNba() 
                    && this.state.activation.action 
                    && this.state.activation.action.services
                    && this.state.activation.action.services.indexOf(i.id) >= 0) {
                        return;
                    };

                let sel: boolean = false;
                if(this.state.activation.services.indexOf(i.id) >= 0) {
                    sel = true;
                };

                let css: string = '';

                if(this.props.preselected && this.props.preselected.indexOf(i.id) >=0 ){
                    css=' insel';
                };

                if(excludes.indexOf(i.id)<0) { 
                    groupItems.push(this.renderThumb(i, this.thumbClick, sel, idx, css, false ));
                };
            });

            let group = <Container key={'service-group-' + groups[i]}>
                    <Row>
                        <Col md={12}>{groups[i]}</Col>                
                    </Row>
                    <Row>
                        <Col md={12}>
                            {groupItems}
                        </Col>
                    </Row>
                </Container>;
            
            if(groupItems.length > 0) {
                ctrl.push(group);
            };
        }

        return <>{ctrl}</>;
    };

    private getInclusiveOptions() : IService[] {
        if(!this.product) { return []; };
 
        let pw = new ProductWrapper(this.state.activation.brandKey, this.product);

        //from prod.sub.service
        let all = pw.getAllServicesFromConfig(this.state.activation.sub, this.props.bc);

        let available : string[] = linq.from<IService>(this.props.brand.services)
            .where(x=> all.indexOf(x.id)>=0) //  && x.type === EServiceType.inclusive) 
            .select(x=>x.id)
            .toArray();

        let svc:IService[] = linq.from<IService>(this.props.brand.services)
            .where(x=>available.indexOf(x.id) >= 0)
            .toArray();

        if(svc.length == 0) {return [];}

        let groupItems : IService[]  = [];

        svc.map( (i, idx)=> {
            let subSvc = linq.from<ISubServiceConfig>(this.sub!.sl).where(x=>x.id === i.id).first();

            if(!subSvc) { return;};

            if(i.type === EServiceType.inclusive || (subSvc.m && !i.hidden)) {
                groupItems.push(i); 

                //mandatory not hidden are added to activation
                if(subSvc.m && !i.hidden && i.type !== EServiceType.inclusive) {
                    this.state.activation.addService(i.id, undefined);
                };
            };
        });

        return groupItems;
    };    

    private getAlreadyBooked() : IService[] {
        let parent = StorageHelper.getParentOfProcess(this.state.activation.processId);

        if(!this.product) { return []; };

        let available : string[] = linq.from<IService>(this.props.brand.services)
            .where(x=> parent.customer.contracts![0].bookedProduct!.services.indexOf(x.id)>=0) //  && x.type === EServiceType.inclusive) 
            .select(x=>x.id)
            .toArray();

        let svc:IService[] = linq.from<IService>(this.props.brand.services)
            .where(x=>available.indexOf(x.id) >= 0)
            .toArray();

        return svc;
    }; 

    private thumbClick(id: string) {
            if(this.state.activation.hasService(id, undefined)) {
                this.state.activation.removeService(id, undefined);
            } else {
                this.state.activation.addService(id, undefined);
            }

            this.props.onChanged();
            //this.props.parent.setState({changed: true});

            //this.setState({changed: true});
    };

    private renderThumb(service: IService, 
        clickHandler: any, 
        selected: boolean, 
        idx: number,
        css: string,
        checkRules: boolean) : JSX.Element {
        
        let clickable: boolean = true;
        let ruleCss: string = '';

        if(checkRules) {
            let insub = linq.from<ISubServiceConfig>(this.sub!.sl).where(x=>x.id == service.id).firstOrDefault()
            let rul = linq.from<IServiceRule>(this.serviceRules).where(x=>x.id == service.id).firstOrDefault();

            if(!insub) {
                ruleCss = ' autoremove';
                clickable = false;
            };

            if(rul && rul.type == EServiceRuleType.canBeRemoved) {
                ruleCss = ' removable';
                clickable = true;
            };

            if(rul && rul.type == EServiceRuleType.notRemovable) {
                ruleCss = ' notremovable';
                clickable = false;
            };

            if(insub && insub.m) {
                ruleCss = '';
                clickable = false;
            };
        };

        if(this.state.activation.servicesRemove.indexOf(service.id)>=0) {
            ruleCss = '';
            css = css.replace('selected', '');
            selected = false;
        };

        css += ruleCss;

        let ctrl : JSX.Element = <TariffThumb
            key={service.id + '_' + Global.getUniqueId()} 
            idx={idx}
            text={service.name}
            tooltip={service.tooltip}
            tooltipModel={service}
            selected={selected}
            id={service.id}
            brandKey={this.props.brand.id}
            styleBrand={false}
            cssStyle={css}
            onClick={(e)=>{
                if(clickable) {
                    if(checkRules) {
                        this.state.activation.addRemoveServiceRemove(e, this.state.activation.productId);
                        this.setState({changed: true});
                    } else {
                        if(clickHandler) {
                            clickHandler(e);
                        }
                    };
                }
            }}/>

        return ctrl;
    };

    private renderSelected() : JSX.Element {
        let ctrl : JSX.Element[] = [];

        let prod : IProduct = linq.from<IProduct | any>(this.props.brand.products)
            .where(x=>x.id === this.state.activation.productId)
            .firstOrDefault();

        let sub = linq.from<ISubConfig>(prod.subs).where(x=>x.id === this.state.activation.sub && x.b == this.props.bc).first();

        let svc : IService[] = linq.from<IService>(this.props.brand.services)
            .toArray();

            let svcs: string[] = this.state.activation.services;

            for(let j: number = 0 ; j < svcs.length; j++) {
                let svcSelected : IService = linq.from<IService | any>(svc)
                    .where(x=>x.id === svcs[j])
                    .firstOrDefault();

                let current = linq.from<IService>(svc).where(x=>x.id === svcs[j]).firstOrDefault();

                if(current && current.hidden) { continue; };

                if(svcSelected) { 
                    let subSvc = linq.from<ISubServiceConfig>(sub.sl).where(x=>x.id === svcs[j]).firstOrDefault();

                    //mandatory are shown in inclusiveoptions area
                    if(!subSvc || subSvc.m) {
                       if(!svcSelected.bonusType) {
                        continue;
                       };
                    };

                    ctrl.push(this.renderThumb(svcSelected, undefined, true, j, '', false)); } else { console.log('svc' + svcs[j] + ' not found');
                }
            }

        return <>{ctrl}</>
    };

    public componentWillReceiveProps(next: IActivationServiceSelectorProps) {
        this.setState({activation: next.activation});
    };

    public render() {
        let ctrl = <></>;

        if(!this.state.activation.productId || !this.state.activation.sub) {
            return ctrl;
        };

        this.product = linq.from<IProduct>(this.props.brand.products)
            .where(x=>x.id === this.state.activation.productId)
            .firstOrDefault();

        this.sub = linq.from<ISubConfig>(this.product!.subs)
            .where(x=>x.id === this.state.activation.sub && x.b == this.props.bc)
            .first();

        if(!this.props.canEdit) { return ctrl;};

        this.alreadyBooked = this.state.activation.activationType == EActivationType.existingCustomer ? this.getAlreadyBooked() : [];
        this.inclusive = this.getInclusiveOptions();

        ctrl = 
        <>
            <Container>
                <Row key="row1">
                    <Col md={12}>
                        <div className="subtitle">{MainTexts.optionsAdditional}</div>
                    </Col>
                </Row>
                <Row key="row2">
                    <Col md={9}>
                        {this.renderSelected()}
                    </Col>
                    <Col md={3}>
                        {
                            this.props.blockAddRemove ? <></> : 
                            <TariffThumb
                                idx={-1}
                                key={'serviceselector'} 
                                text={'Hinzufgen / Bearbeiten'}
                                selected={false}
                                id={'serviceselector'}
                                brandKey={'vf'}
                                styleBrand={false}
                                onClick={()=>{ this.setState({popupVisible: true}); }}/>
                        }
                    </Col>                
                </Row>

                {
                    this.inclusive.length > 0 ? 
                    <>
                        <Row key="row3">
                            <Col md={12}>
                                <div className="subtitle">{MainTexts.optionsInclusive} </div>
                            </Col>
                        </Row>                    
                        <Row key="row4">
                            <Col md={9}>
                            {
                                    this.inclusive.map((i, idx)=>{
                                       return this.renderThumb(i, undefined, true, idx, '', false)
                                    })
                                }
                            </Col>
                            <Col md={3}/>          
                        </Row>
                    </>
                    : null
                }

                {
                    this.alreadyBooked.length > 0 ? 
                    <>
                        <Row key="row3">
                            <Col md={12}>
                                <div className="subtitle">{MainTexts.optionsAlreadyBooked} {GlobalJSX.getInfoTooltip('info-', 'Bitte beachte, dass einige Optionen aus technischen Grnde nicht abgewhlt werden knnen.')} </div>
                            </Col>
                        </Row>                    
                        <Row key="row4">
                            <Col md={9}>
                                {
                                    this.alreadyBooked.map((i, idx)=>{
                                       return this.renderThumb(i, this.thumbClick, true, idx, '', true)
                                    })
                                }
                            </Col>
                            <Col md={3}/>          
                        </Row>   
                        <Row>
                             <Col xs={12} md={12}>
                                <div className="thumb-small-desc">Legende:</div>
                            </Col>
                            <Col xs={12} md={12}>
                                <div className="removable thumb thumb-tariff thumb-small"></div>
                                <div className="thumb-small-desc">Option ist abwhlbar</div>
                            </Col>
                            <Col xs={12} md={12}>
                                <div className="notremovable thumb thumb-tariff thumb-small"></div>
                                <div className="thumb-small-desc">Option nicht abwhlbar</div>
                            </Col> 
                            <Col xs={12} md={12}>
                                <div className="selected thumb thumb-tariff thumb-small"></div>
                                <div className="thumb-small-desc">Pflichtoption</div>
                            </Col>                                                        
                        </Row>     
                    </>            
                    : null
                }
            </Container>

            <cmp.Popup type={EPopupType.info} onDismiss={this.onDismissSelector} 
                closable={true}
                visible={this.state.popupVisible} id="confirm" title={MainTexts.optionsAdditional} height={600} style="wide">
                    <cmp.PopupBody>
                        {this.renderSelection()}
                        <></>
                    </cmp.PopupBody>
                    
                    <cmp.Action>
                        <cmp.LButton text={MainTexts.ready} css="fr" icon="ok" type={ELButtonType.buttonWithIconRight} onClick={this.onDone} />
                        <></>
                    </cmp.Action>
                </cmp.Popup> 
        </>;
        
        return ctrl;
    };
};

export default ActivationServiceSelector;