import linq from 'linq';
import * as React from 'react';
import { Alert } from 'react-bootstrap';
import { ERequestType } from '../../../models';
import IActivation from '../../../models/ordering/activation';
import { ICustomer } from '../../../models/ordering/customer';
import { EActivationType, EBillingType, ECreditDebitType, EEvnType, EIDCardType, EOrderProcessView, ETargetNumber } from '../../../models/ordering/order';
import { CombiWrapper } from '../../../models/support/ISupportWrapper';
import * as s from '../../../screens/insex';
import { ValidationWrapper } from '../../../utils';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import InputWrapper from '../../page/inputWrapper';
import * as c from '../customer/index';

export interface IActivationCustomerDataProps {
    parent: s.ProcessNewScreen;
    customer: ICustomer;
};

export interface IActivationCustomerDataState {
    changed: boolean;
};

export class ActivationCustomerData extends React.Component<IActivationCustomerDataProps, IActivationCustomerDataState> {
    private customer: ICustomer;
    private hasDsl: boolean;
    private activation: IActivation;
    constructor(props: IActivationCustomerDataProps) {
        super(props);

        this.onNext = this.onNext.bind(this);
        this.onBack = this.onBack.bind(this);
        this.onInput = this.onInput.bind(this);

        this.customer = this.props.customer;
        this.customer.legitimation = this.props.customer.legitimation ? this.props.customer.legitimation : {type: EIDCardType.german, id: '', authority: ''};

        this.hasDsl = linq.from<IActivation>(this.props.parent.data.activations)
            .where(x => x.brandKey === BrandKeys.dsl)
            .count() > 0;

        this.activation = this.props.parent.data.activations[0];

        if (this.activation.brandKey === BrandKeys.vodafone && this.customer.evn === null && this.activation.activationType === EActivationType.partnerCard) {
            this.customer.evn =  {billingType: EBillingType.online,evnType: EEvnType.shortEvn,targetNumberType: ETargetNumber.complete}
        }




        this.state = {
            changed: false
        };
    };

    public componentDidMount() {
        ValidationWrapper.validateFormular(this.props.parent.dataWrapper, false);

        window.scrollTo(0, 0);


    };

    public render() {
        let prov = this.props.parent.data.customer.mnp ? this.props.parent.data.customer.mnp.provider : '';

        let hasUnityOnly = this.props.parent.dataWrapper.hasUnityOnly();

        let inCdChange = this.props.parent.dataWrapper.hasCdChange();

        let ctrl = (
            <>
                <div className="gigakombiAlert">
                    {CombiWrapper.combisWereAdded ? <Alert variant={'success'}>
                        Hinweis:<br />
                        Da du mehrere Gigakombi fhige Produkte in den Warenkorb gelegt hast, haben wir die fehlenden GigaKombi-Marker automatisch hinzugefgt.
                     </Alert> : <></>}
                </div>

                <InputWrapper>
                    <c.CustomerDataMain parent={this} data={this.props.parent.data} customer={this.props.customer}
                        onChange={this.onInput} />
                    <></>
                </InputWrapper>

                {
                    this.hasDsl ?
                        <>
                            <InputWrapper>
                                <c.CustomerDesiredSchedule parent={this} customer={this.props.customer} onChange={this.onInput} />
                            </InputWrapper>
                        </>
                        :
                        <></>
                }

                {
                    (this.props.parent.dataWrapper.inActMnp || inCdChange) && !this.props.parent.dataWrapper.hasUnityOnly() ?
                        <InputWrapper>
                            <c.CustomerMnp parent={this} customer={this.props.customer} onChange={this.onInput} preview={false} />
                        </InputWrapper>
                        : null
                }

                {
                    this.hasDsl ?
                        <>
                            <InputWrapper>
                                <c.CustomerDSL customer={this.props.customer} parent={this}
                                    showAddress={!(prov == '1' || prov == '2')}
                                    showTel={true}
                                    house={this.props.customer.house} onChange={this.onInput} />
                            </InputWrapper>
                        </>
                        :
                        <></>
                }

                {
                    this.topUPageValidation() ? <InputWrapper>
                        <c.CustomerTopup parent={this} topup={this.props.customer.topup} onChange={this.onInput} />
                    </InputWrapper> : <></>
                }

                <InputWrapper>
                    <c.CustomerLegitimation parent={this} onChange={this.onInput}
                        preview={false}
                        legitimation={this.customer.legitimation}
                        payment={this.customer.payment} />
                </InputWrapper>

                <InputWrapper>
                    <c.CustomerPayment parent={this} onChange={this.onInput} payment={this.customer.payment} />
                </InputWrapper>

                {
                    !hasUnityOnly && this.props.parent.current && this.props.parent.current.creditDebitType === ECreditDebitType.postpaid ?
                        <InputWrapper>
                            <c.CustomerEVN parent={this} onChange={this.onInput} evn={this.customer.evn} />
                        </InputWrapper>
                        : null
                }

                <InputWrapper>
                    <c.CustomerConsulting parent={this} onChange={this.onInput} bew={this.customer.bew} />
                </InputWrapper>

                <c.CustomerAction parent={this} />
            </>
        );

        return ctrl;
    };

    public onNext() {
        if (ValidationWrapper.validateFormular(this.props.parent.dataWrapper, true, 'container-main')) {
            this.props.parent.onCreateProcess(ERequestType.create);
        }
    };

    public onBack() {
        this.props.parent.gotoView(EOrderProcessView.tariff);
    };
 
    private onInput(target: any) {
        
        Global.onInput(this.customer, target, () => {
            if(target) {
                if(target.name == 'payment.owner') {
                    this.customer.payment!.ownerChanged = true;
                }

                if(target.id === 'type_1'){
                    this.customer.legitimation!.type = EIDCardType.german;
                }else if (target.id === 'type_1'){
                    this.customer.legitimation!.type = EIDCardType.foreign;
                }
                
                // set the name of bank owner
                if(this.customer.payment && !this.customer.payment.ownerChanged && (target.name == 'firstname' || target.name == 'surname')) {
                    this.customer.payment!.owner = (this.customer.firstname || '') + ' ' + (this.customer.surname || '');
                };

                if(target.id === 'delivery-callbackTime'){
                    if(target.value==='0:0'){
                        this.customer.delivery!.callbackTime = '';
                    }else{
                        this.customer.delivery!.callbackTime = target.value
                    }              
                }
            };
            this.props.parent.applyCustomer(this.customer);
            this.setState({ changed: true });
        });
    };

    private topUPageValidation() {
        let canbook: boolean = true;

        if (this.activation.brandKey === BrandKeys.otelo) {
            let ageOfCustomer = Global.getAgeFromDate(this.customer.birthday);

            if (this.activation!.creditDebitType == ECreditDebitType.prepaid && ageOfCustomer < 18) {
                this.customer.topup = undefined
                canbook = false;
            }
        }

        return canbook;
    }
};

export default ActivationCustomerData;