import * as linq from 'linq';
import * as React from 'react';
import * as m from '../../../models/index';
import { ERequestStatus } from '../../../models/index';
import { AvailabilityProduct, AvailabilityWrapper, EAvailabilityResultStatus, EElementTypeUi, IAvailabilityBrand, IAvailabilityResultItem, IAvailabilityServiceSelection } from '../../../models/ordering/availability';
import IBrand from '../../../models/ordering/brand';
import { EActivationType } from '../../../models/ordering/order';
import IService from '../../../models/ordering/service';
import { IKeyText } from '../../../models/page/interfaces';
import { ProductTree, TreeItem } from '../../../models/support/tree';
import ProcessNewScreen from '../../../screens/ordering/process';
import Api from '../../../utils/api';
import { BrandKeys, ConstantKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import GlobalJSX from '../../../utils/GlobalJSX';
import { MainTexts } from '../../../utils/messages';
import StorageHelper from '../../../utils/storage';
import { EPopupType, IPopupConfig } from '../../dialogs/popup';
import { LButton } from '../../page';
import TariffThumb from '../tariffThumb';
import ActivationTariff from './activationTariff';

export interface IActivationOptionsProps {
    parent: ProcessNewScreen;
    tariff: ActivationTariff;
    onChange: any;
};

export interface IActivationOptionsState {
    changed: boolean;
    productId: string;
    sub: string;
    //inCheck?: boolean;
};

export interface IActivationOption {
    productId: string;
    id: string;
    path: string[];
    level: number;
    type?:string;
    mandatory?: boolean;
};

export class ActivationOptions extends React.Component<IActivationOptionsProps, IActivationOptionsState> {
    public static componentId = 'activationOptions';
    private act: m.IActivation;
    private avSelection: AvailabilityWrapper;
    private availability: m.Availability;
    private brand: IBrand;
    private current: IActivationOption;
    private checkAvailability: m.ERequestStatus = m.ERequestStatus.undefined;
    private checkResult: JSX.Element = <></>;
    private selectionOk: boolean = false;

    constructor(props: IActivationOptionsProps) {
        super(props);

        this.onChange = this.onChange.bind(this);

        this.check = this.check.bind(this);

        this.act = this.props.parent.current ? this.props.parent.current : new m.IActivation(this.props.parent.selectedBrand);

        this.avSelection = new AvailabilityWrapper(StorageHelper.getAvailability());

        this.brand = StorageHelper.getBrand(BrandKeys.kabel);

        this.availability = StorageHelper.getAvailability();

        this.availability.prepareCableServices(0, []);

        this.current = { id: '', path: [], level: 0, productId: '' };

        this.init(undefined);

        //this.getvailabilityCheckMessage(this.act.cart);

        this.state = {
            changed: false,
            productId: this.act.productId,
            sub: this.act.sub
        };
    };

    public static getMe() {
        let cmp = Global.GetComponent<ActivationOptions>(ActivationOptions.componentId);

        return cmp;
    };

    public componentWillReceiveProps(next: IActivationOptionsProps) {
        this.avSelection = AvailabilityWrapper.ensure(this.avSelection, this.availability);

        this.act = next.parent.current!;

        this.avSelection.applyChanges(this.act);

        this.setState({ changed: true });
    };

    public render() {

        if (!this.selectionOk) {
            return <></>
        }

        let ctrl = <div id={ActivationOptions.componentId}>

            {
                this.renderOptions()
            }

            {this.checkPopup()}

            <div className="spacer clear"></div>

            {
                <div>
                    {this.checkResult}
                </div>
            }
        </div>

        return ctrl;
    };

    initSelection = (product: string | undefined) => {
        this.avSelection.init(this.act, product);
    };

    init(callback: any) {

        try {
            if (this.avSelection) {
                this.avSelection.reset();
            };

            this.avSelection = AvailabilityWrapper.ensure(this.avSelection, this.availability);

            this.initSelection(undefined);

            this.avSelection.applyChanges(this.act);

            if (this.act.selection.children.length > 0) {

                this.act.selection.children.map((j) => {

                    j.children.map((i) => {

                        this.avSelection.inout(j.id, [i.id], true);

                        if (j.id.indexOf('kip') >= 0) {
                            if (this.act.activationType === EActivationType.actDefault) {
                                this.avSelection.inout(j.id, ['rufnummernneuvergabe'], true);
                            } else {
                                this.avSelection.inout(j.id, ['rufnummernmitnahme-1'], true);
                            };
                        };

                        if (this.act.services && this.act.services.length > 0) {
                            this.act.services.map((i) => {
                                this.avSelection.inout(j.id, i.split('/'), true);
                            });
                        };
                    });
                });
            };

            if (callback) {
                callback();
            };
        } catch (err) {
            if (this.act.selection.children[0].children.length > 0) {
                this.selectionOk = true;
                return;
            } else {
                this.selectionOk = false;
                return;
            }
        };
        this.selectionOk = true;
    };

    checkAvailabilityStart(act: m.IActivation) {
        this.checkAvailability = ERequestStatus.running;
        this.setState({ changed: true }, () => {
            this.check();
        })
    };

    private checkPopup() {
        //console.log('checkPopup ' + this.checkAvailability);
        if (this.checkAvailability != m.ERequestStatus.running) { return <></> }

        let cfg: IPopupConfig = {
            props: {
                id: 'dynamicPopup',
                title: 'Warenkorb',
                visible: true,
                closable: false,
                type: EPopupType.pleaseWait
            },
            closeOnDone: true,
            content: <div>Warenkorb wird geprft</div>,
            callback: (d) => {
                //this.setState({changed: true});
            }
        };

        return GlobalJSX.GetPopup(cfg.props, cfg.content, cfg.action);
    };

    private check() {
        let avl = StorageHelper.getAvailability();

        let model = {
            orderId: avl.id,
            brand: BrandKeys.kabel,
            selection: this.act.selection //this.avSelection.getSelectionTree()
        };

        this.act.cart = [{ id: '' }]

        this.checkAvailability = m.ERequestStatus.running;

        // this.checkResult = <div className="timer">
        //     <div className="bg"></div>
        //     <div className="inner">Warenkorb wird geprft...</div>
        // </div>

        Api.fetch('activation/availability', model, "POST", (api) => {
            var sum = Api.parseSummary(api, undefined);
            if (sum.status == m.ERequestStatus.done || sum.status == m.ERequestStatus.running) {
                if (Global.trackSession) {
                    Global.trackSession(api);
                }; 

                StorageHelper.updateAvailability(BrandKeys.kabel, api.data);

                console.log('check res', model.selection);
                console.log('check res', api.data);

                this.applyAutoSelection(this.act.selection, api.data);

                this.avSelection.prepare();

                this.availability.prepareCableServices(0, []);

                this.checkAvailability = m.ERequestStatus.done;

                let res = this.avSelection.validateAvailability(this.act, api.data);

                this.act.cart = linq.from<IAvailabilityResultItem>(res).where(x => x.status != EAvailabilityResultStatus.ok).toArray();

                this.getvailabilityCheckMessage(this.act);

                this.act.updateSelection(model.selection);

                StorageHelper.updateActivation(this.act);

                this.props.parent.setState({ changed: true });
            } else {
                this.checkResult = <>{MainTexts.textError}</>
                this.checkAvailability = m.ERequestStatus.failed;
            }

            this.setState({ changed: true });
        });
    };

    private applyAutoSelection(sel: TreeItem, 
        brand: IAvailabilityBrand) {
            if(!brand || !brand.products) { return; };

            sel.children.map((i)=>{
                //let uprod = linq.from<AvailabilityProduct>(this.avSelection.products).where(x=>x.productId == i.id).firstOrDefault();
                let bprod = linq.from<AvailabilityProduct>(brand.products).where(x=>x.productId == i.id).firstOrDefault();
                if(bprod) {
                    bprod =  AvailabilityProduct.ensure(bprod);

                    if(bprod.elements) {
                        bprod.elements.map((j)=>{
                            let insel = i.find(j.metaname);

                            //element is delivered as selected but is not in the basket,
                            //so put it in selection
                            if(j.available && j.mandatory && j.type != ConstantKeys.devices && j.selected == ConstantKeys.user && !insel) {
                                if(i.getChildrenIds().indexOf(j.metaname) < 0) {
                                    TreeItem.addRemovePath(i, [j.metaname]);
                                };
                            };
                        });
                    };
                };
            });
    };

    private renderTvConnectCable(prod: AvailabilityProduct) : JSX.Element {
        let options : JSX.Element[] = [];

        for(let j =0; j < prod.dropedServices.length; j++ ) {
            let sel:boolean = false;

            let tt = linq.from<IService>(this.brand.services)
                .where(x => x.id == prod.dropedServices[j])
                .firstOrDefault();

            if(this.props.parent.current!.hasService(prod.dropedServices[j], prod.productId)) {
                sel = true;
            };

            let name = tt ? tt.name : prod.dropedServices[j];
            let tooltip = tt ? tt.tooltip : prod.dropedServices[j];

            if (!tt) {
                let svc = prod.getService(prod.dropedServices[j], undefined);

                if (svc) {
                    name = svc.name;
                    tooltip = svc.name;
                };
            };

            if(name == 'n.a') {
                name = prod.dropedServices[j];
            };

            let op = <TariffThumb
                id={'op-tvc-' + prod.dropedServices[j]}
                text={name}
                key={'op-tvc-' + prod.dropedServices[j]}
                idx={j}
                tooltip={tooltip}
                brandKey={this.brand.id}
                selected={false}
                styleBrand={false}
                cssStyle={ sel ? 'selected sub ' + this.brand.id : 'sub ' + this.brand.id}
                clickModel={{ id: prod.dropedServices[j], productId: prod.productId , path: [prod.productId, prod.dropedServices[j]], level: 1 }}
                onClick={(i)=>{
                    i.path.shift();
                    this.handleSelection(i);
                }} />
            
            //we show just one options, so reset array and exit
            if(sel) {
                options = [op];
                break;
            } else {
                options.push(op);
            }
        };

        let ctrl = (
            <>
                <div className="clear spacer"></div>
                <div className="subtitle" id="hardwareSelectiontvc">TV Connect Optionen</div>
                {options}
            </>
        );

        return ctrl;
    };

    private getvailabilityCheckMessage(act: m.IActivation) {
        if (!act.hasHardware()) {
            return <></>
        };

        if (act.cart.length == 0) {
            this.checkResult = <>Prfung erfolgreich.</>
        } else {
            let arr: JSX.Element[] = [];

            arr.push(<div>Bitte Warenkorb korrigieren:</div>);

            act.cart.map((i) => { arr.push(<div key={'checkmsg-' + act.cart.indexOf(i)}>{i.text}</div>); });

            arr.push(<div><LButton type={m.ELButtonType.buttonWithIconRight} text="Warenkorb korrigieren" css="a1 fl" onClick={this.correctCart} /></div>);

            this.checkResult = <>{arr}</>;
        };
    };

    private correctCart = () => {
        if (this.act.cart) {
            this.act.cart.map((i) => {
                TreeItem.addRemovePath(this.props.parent.current!.selection, i.path!);
                //this.avSelection.inout(i.path![0], i.path!.slice(1), i.status == EAvailabilityResultStatus.add);
            });

            this.check();

            this.setState({ changed: true });
        }
    };

    private renderOptions() {
        let all: JSX.Element[] = [];
        let tvConnect: JSX.Element = <></>

        this.avSelection.products.map((j, jdx) => {
            if(!this.props.parent.current!.hardwareNecesseryAndSelected(j.productId)){
                return;
            };

            if (this.props.parent.current!.brandKey == BrandKeys.kabel) {
                if(j.hasDroppedServices()) {
                    tvConnect = this.renderTvConnectCable(j);
                }
            };

            let ui: JSX.Element[] = [];

            all.push(<div key={'act-opt-' + j.productId} className="subtitle">{MainTexts.tariffOptions + ': ' + j.productId}</div>)

            let op: IAvailabilityServiceSelection[] = [];

            j.prepareElements(EElementTypeUi.selectable, op, 1, ProductTree.FromTree(this.act.selection), [j.productId], undefined);

            op.map((i, idx) => {
                let tt = linq.from<IService>(this.brand.services)
                    .where(x => x.id === i.metaname)
                    .firstOrDefault();
                i.sort = tt ? tt.sort : 0;
            }); 

            op = linq.from<IAvailabilityServiceSelection>(op).orderBy(x => x.sort).toArray();

            op.map((i, idx) => {
                let tt = linq.from<IService>(this.brand.services)
                    .where(x => x.id === i.metaname)
                    .firstOrDefault();

                let name = tt ? tt.name : i.metaname;
                let tooltip = tt ? tt.tooltip : i.metaname;

                if (!tt) {
                    let svc = j.getService(i.metaname, undefined);

                    if (svc) {
                        name = svc.name;
                        tooltip = svc.name;
                    };
                };

                if(name == 'n.a') {
                    name = i.metaname;
                };

                let cur = <TariffThumb
                    id={'sop' + i.metaname}
                    text={name }
                    key={idx}
                    idx={idx}
                    tooltip={tooltip}
                    tooltipModel={i}
                    brandKey={this.brand.id}
                    selected={i.selected}
                    styleBrand={false}
                    cssStyle={i.selected ? 'selected opt ' + this.brand.id : ' opt ' + this.brand.id}
                    clickModel={{ id: i.metaname, productId: j.productId, path: [i.metaname], level: 1 }}
                    onClick={this.onChange} />

                //todo: not sure if correct
                // if(name != 'n.a') {
                ui.push(cur);
                //  };
            });

            let temp = <>
                {ui}
                <div className="spacer-big clear"></div>
                {this.renderAdditional(j, op)}
            </>

            all.push(temp);

            all.push(<div className="spacer-big clear"></div>);
        });

        return <>
            <div>{tvConnect}</div>
            <div className="spacer-big clear"></div>
            {all}
        </>
    };

    private renderAdditional(prod: AvailabilityProduct, parents: IAvailabilityServiceSelection[]) {
        let res: JSX.Element[] = [];
        
        let sel = this.act.selection.toArray(false, prod.productId);  

        for (let i: number = 0; i < parents.length; i++) {
            let cur = parents[i];
            //not selected in l1 so continue
            if (sel.indexOf(cur.metaname) < 0) {
                //console.log('not in sel:' + cur.metaname, sel);
                continue;
            }
            let svc = linq.from<IService>(this.brand.services).where(x => x.id === cur.metaname).firstOrDefault();
            let el = prod.getElement([cur.metaname]);

            //console.log('to render with children', el);

            if (svc && el && el.elements && el.elements.length > 0) {
                res.push(this.renderSecond(prod, svc, cur));
                res.push(<div className="spacer clear"></div>);
            };
        };

        return res;
    };

    private renderSecond(prod: AvailabilityProduct, svc: IService, parent: IAvailabilityServiceSelection) {
        let op: IAvailabilityServiceSelection[] = [];

        let el = prod.getElement([parent.metaname]);

        if (!el || !el.elements || el.elements.length == 0) {
            return <></>
        }

        prod.prepareElements(EElementTypeUi.children, op, 1, ProductTree.FromTree(this.act.selection), [prod.productId, svc.id], el.elements);

        let ui: JSX.Element[] = [];

        op.map((i, idx) => {
            //console.log('additional:', i);
            let tt = linq.from<IService>(this.brand.services).where(x => x.id === i.metaname).firstOrDefault();

            let name = tt ? tt.name : i.metaname;
            let tooltip = tt ? tt.tooltip : undefined;

            if (name.indexOf('-') > 0 ) {
                let cname = linq.from<IKeyText>(this.availability.cableServices).firstOrDefault(x => x.key == i.metaname);
                if (cname) {
                    name = cname.text;
                }
            };

            if(name == 'n.a') {
                name = i.metaname;
            };

            let cur = <TariffThumb
                id={'soc' + i}
                text={name}
                key={parent.metaname}
                idx={idx}
                tooltip={tooltip}
                tooltipModel={i}
                brandKey={this.brand.id}
                selected={false}
                styleBrand={false}
                cssStyle={i.selected ? 'selected sub opt ' + this.brand.id : ' sub opt ' + this.brand.id}
                clickModel={{ id: i.metaname, productId: prod.productId, path: [parent.metaname, i.metaname], level: 2 }}
                onClick={this.handleSelection} />

            ui.push(cur);
        });

        let ctrl = <>
            <div className="subtitle">{MainTexts.optionsAdditional + ': ' + svc.name}</div>
            {ui}
        </>;

        return ctrl;
    };

    private onChange(e: IActivationOption) {
        this.handleSelection(e);
    }; 

    handleSelection = (e: IActivationOption) => {
        console.log('element clicked', e);

        TreeItem.addRemoveTo(e.productId, this.act.selection, e.path);

        let el = this.act.selection.findPath([e.productId, e.path[0]]);
        
        if(e.mandatory && el) {
            //options mandatory for the product
            let man = this.avSelection.getMandatory(e.productId);
            man.map((i)=>{
                TreeItem.addRemoveTo(e.productId, this.act.selection, [i]);
            });

            //options mandatory for the hardware
            let manHw = this.avSelection.getMandatoryForElement(e.productId, e.id);
            manHw.map((i)=>{
                TreeItem.addRemoveTo(e.productId, this.act.selection, [e.id, i]);
            });
        };

        this.check();

        this.props.parent.setState({ changed: true })
    };
};

export default ActivationOptions;