import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBaseComponentState } from '../../../interfaces';
import { EControlType, EFormMode, ELButtonType, ERequestType } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { IContract } from '../../../models/existing/contract';
import { ICustomer } from '../../../models/ordering/customer';
import { ECustomerEmailStatus, ERequestStatus } from '../../../models/page/diverse';
import CustomerScreen from '../../../screens/existing/customer';
import { ValidationWrapper } from '../../../utils';
import Api from '../../../utils/api';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { MainTexts, OrderingTexts, ValidationMessages } from '../../../utils/messages';
import StorageHelper from '../../../utils/storage';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import { EPopupType, IPopupConfig } from '../../dialogs/popup';
import { LControl } from '../../page';
import BaseBlock from '../../page/block';
import * as page from '../../page/index';
import ProtectionAndConsent from './protectionAndConsent';


export interface IExistingCustomerDataState extends IBaseComponentState {
    mode: EFormMode;
    modePassword: EFormMode;
}

export interface IExistingCustomerDataProps {
    parent: CustomerScreen;

};

export class ExistingCustomerData extends React.Component<IExistingCustomerDataProps, IExistingCustomerDataState> {
    private contract: IContract;
    private customer: ICustomer;
    private password: string = '';
    private password2: string = '';
    public static containerId: string = 'existingCustomerData';
    private emailStatusControl: JSX.Element;
    private emailStatus: ECustomerEmailStatus;

    constructor(props: IExistingCustomerDataProps) {
        super(props);

        this.state = {
            changed: false,
            modePassword: EFormMode.view,
            mode: EFormMode.view
        };

        this.onInput = this.onInput.bind(this);
        this.switchMode = this.switchMode.bind(this);

        this.customer = JSON.parse(JSON.stringify(this.props.parent.data.customer));
        this.contract = this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0] : {};

        this.emailStatus = this.props.parent.data.customer.emailStatus ? this.props.parent.data.customer.emailStatus : ECustomerEmailStatus.UNDEFIEND;
        this.emailStatusControl = <></>;

        if (this.props.parent.data.customer.contracts && this.props.parent.data.customer.contracts[0].brandKey == BrandKeys.otelo) {
            if (this.emailStatus == ECustomerEmailStatus.CREATED) {
                this.showEMailStatusPopup();
                this.emailStatusControl = <span className="orange">{MainTexts.customerEMailCreated}</span>;
            } else if (this.emailStatus == ECustomerEmailStatus.VALIDATED) {
                this.emailStatusControl = <span className="green">{MainTexts.customerEMailValidated}</span>;
                this.showConsentPopup();
            } else {
                this.showEMailStatusPopup();
                this.emailStatusControl = <span className="red">{MainTexts.customerEMailNotValidated}</span>;
            };
        };
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.customerData,
            transparent: false,
            style: '',
            styleContainer: '',
            collapsible: true,
            collapsed: false
        };

        const preview = this.state.mode === EFormMode.view;
        let canEdit = this.props.parent.data.customer.contracts && this.props.parent.data.customer.contracts[0].brandKey === BrandKeys.otelo ? true : false;

        let ctrl =
            <div>
                <Container>
                    <Row>
                        <Col md={4}>
                            <LControl preview={true}
                                type={EControlType.text} style="merged"
                                title={MainTexts.firstname}
                                id={"firstname"}
                                name={"firstname"}
                                onChange={this.onInput}
                                value={this.customer.firstname} />
                        </Col>
                        <Col md={4}>
                            <LControl 
                                preview={true} 
                                type={EControlType.text} 
                                style="merged"
                                title={MainTexts.surname}
                                id={"surname"}
                                name={"surname"}
                                onChange={this.onInput}
                                value={this.customer.surname} />
                        </Col>
                        <Col md={4}>
                            <LControl preview={true} 
                                style="merged"
                                type={EControlType.text}
                                title={MainTexts.dateOfBirth}
                                id={"birthday"}
                                name={"birthday"}
                                value={Global.toGermanDate(this.customer.birthday)} />
                        </Col>                        
                        <Col md={8}>
                            <LControl preview={preview} 
                                type={EControlType.text} style="merged"
                                title={MainTexts.email}
                                id={"email"}
                                name={"email"}
                                onChange={this.onInput}
                                value={this.customer.email} />
                        </Col>
                    </Row>

                    <Row>
                        <Col md={12} className="subtitle">{MainTexts.address}</Col>
                    </Row>

                    <Row>
                        <Col md={4}>
                            <LControl preview={preview} type={EControlType.text} style="merged"
                                title={MainTexts.street}
                                id={"street"}
                                name={"address.street"}
                                onChange={this.onInput}
                                value={this.customer.address ? this.customer.address.street : MainTexts.na} />
                        </Col>
                        <Col md={4}>
                            <LControl preview={preview} type={EControlType.text} style="merged"
                                id={"nr"}
                                name={"address.nr"}
                                onChange={this.onInput}
                                title={MainTexts.houseNumber}
                                value={this.customer.address ? this.customer.address.nr : MainTexts.na} />
                        </Col>
                        <Col md={4}>
                            <LControl preview={preview} type={EControlType.text} style="merged"
                                id={"city"}
                                name={"address.city"}
                                onChange={this.onInput}
                                title={MainTexts.city}
                                value={this.customer.address ? this.customer.address.city : MainTexts.na} />
                        </Col>
                        <Col md={4}>
                            <LControl
                                preview={preview}
                                id={"zip"}
                                name={"address.zip"}
                                onChange={this.onInput}
                                type={EControlType.text} style="merged" title={MainTexts.zip}
                                value={this.customer.address ? this.customer.address.zip : MainTexts.na} />
                        </Col>
                        <Col md={4}></Col>
                        <Col xs={12} md={12} >
                            {
                                canEdit ?
                                    <>
                                        {this.state.mode === EFormMode.view ?
                                            <page.LButton type={ELButtonType.buttonWithIconRight}
                                                css="a1 fr"
                                                id="buttonEditCustomer"
                                                text={MainTexts.edit}
                                                onClick={() => {
                                                    this.setState({ mode: EFormMode.edit });
                                                }} /> :
                                            <>
                                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                                    css="a1 fr"
                                                    id="buttonRequestContract"
                                                    text={MainTexts.save}
                                                    onClick={() => {
                                                        let cfg: IPopupConfig = {
                                                            props: {
                                                                id: 'dynamicPopup',
                                                                title: 'Kundendaten ndern',
                                                                visible: true,
                                                                closable: false,
                                                                type: EPopupType.pleaseWait
                                                            },
                                                            content: <div>{OrderingTexts.customerData}</div>
                                                        };

                                                        let t = {
                                                            type: ERequestType.customerChangeData,
                                                            brandKey: this.customer.contracts ? this.customer.contracts[0].brandKey : '',
                                                            id: this.props.parent.data.id,
                                                            data: this.customer
                                                        };
                                                        this.props.parent.processCustomer(() => {
                                                            Global.hub.send(JSON.stringify(t),
                                                                ERequestType.customerChangeData,
                                                                undefined);
                                                        }, cfg);
                                                    }} />
                                                <page.LButton type={ELButtonType.linkSimple}
                                                    css="action3 fr"
                                                    id="buttonCancel"
                                                    text={MainTexts.cancel}
                                                    onClick={() => {
                                                        this.setState({ mode: EFormMode.view });
                                                    }} />
                                            </>
                                        }
                                    </>
                                    : null
                            }
                        </Col>
                    </Row>

                    {
                        this.customer.contracts && this.customer.contracts[0].brandKey === BrandKeys.otelo ?
                            <>
                                <Row>
                                    <Col>
                                        <div className="title">{MainTexts.customerEMailValidation}</div>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <div className="">Status: {this.emailStatusControl}</div>
                                    </Col>
                                </Row>
                                {
                                    this.emailStatus == ECustomerEmailStatus.VALIDATED ? null :
                                        <Row>
                                            <Col>
                                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                                    css="a1 fr"
                                                    id="buttonExecRequestEmail"
                                                    text={MainTexts.request}
                                                    onClick={() => {

                                                        let cfg: IPopupConfig = {
                                                            props: {
                                                                id: 'dynamicPopup',
                                                                title: 'E-Mail Validierung',
                                                                visible: true,
                                                                closable: true,
                                                                type: EPopupType.pleaseWait
                                                            },
                                                            content: <div>{OrderingTexts.customerData}</div>
                                                        };

                                                        let t = {
                                                            type: ERequestType.customerValidateEMail,
                                                            brand: this.customer.contracts ? this.customer.contracts[0].brandKey : '',
                                                            id: this.props.parent.data.id
                                                        };

                                                        this.props.parent.processCustomer(() => {
                                                            Api.fetch('existing/ValidateEmail', t, 'post', (api) => {
                                                                var sum = Api.parseSummary(api, undefined);

                                                                if (sum.status == ERequestStatus.done) {
                                                                    this.props.parent.data.customer.emailStatus = ECustomerEmailStatus.CREATED;
                                                                    StorageHelper.addOrder(this.props.parent.data);
                                                                    this.setState({ changed: true });
                                                                };

                                                                cfg.props.type = EPopupType.custom;
                                                                cfg.content = <div>{sum.message}</div>;

                                                                this.props.parent.processCustomer(() => { }, cfg);
                                                            });
                                                        }, cfg);
                                                    }}
                                                />
                                            </Col>
                                        </Row>
                                }
                            </>
                            : null
                    }

                    <Row>
                        <Col>
                            <div className="title">Passwort</div>
                            {
                                this.state.modePassword === EFormMode.view ?
                                    <LControl preview={true}
                                        type={EControlType.text}
                                        style="merged"
                                        title={MainTexts.customerPassword}
                                        id={"password"}
                                        name={"password"}
                                        value={"******"} />
                                    :
                                    <Container id="passwordContainer">
                                        <Row>
                                            <Col md={6} className="np">
                                                <LControl preview={false} type={EControlType.password} style="merged"
                                                    title={MainTexts.customerPassword}
                                                    id={"password"}
                                                    name={"password"}
                                                    onChange={(e) => {
                                                        this.password = e.value;
                                                        this.setState({ changed: true });
                                                    }}
                                                    value={this.password}
                                                    validationOptions={[
                                                        new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                                        new ValidationOption({ type: EValidationOption.password, message: ValidationMessages.errorPassword })
                                                    ]} />
                                            </Col>
                                            <Col md={6}>
                                                <LControl preview={false} type={EControlType.password} style="merged"
                                                    title={MainTexts.passwordRepeat}
                                                    id={"password2"}
                                                    name={"password2"}
                                                    onChange={(e) => {
                                                        this.password2 = e.value;
                                                        this.setState({ changed: true });
                                                    }}
                                                    value={this.password2}
                                                    validationOptions={[
                                                        new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                                        new ValidationOption({ type: EValidationOption.password, message: ValidationMessages.errorPassword }),
                                                        new ValidationOption({ type: EValidationOption.match, message: ValidationMessages.errorPasswordMatch, targetControl: 'password' })
                                                    ]} />
                                            </Col>
                                        </Row>
                                    </Container>
                            }
                        </Col>
                        <Col xs={12} md={12} >
                            {
                                canEdit ?
                                    <>
                                        {this.state.modePassword === EFormMode.view ?
                                            <page.LButton type={ELButtonType.buttonWithIconRight}
                                                css="a1 fr"
                                                id="buttonEditPassword"
                                                text={MainTexts.edit}
                                                onClick={() => {
                                                    this.password2 = this.password = '';
                                                    this.setState({ modePassword: EFormMode.edit });
                                                }} /> :
                                            <>
                                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                                    css="a1 fr"
                                                    id="buttonExecChangePassword"
                                                    text={MainTexts.save}
                                                    onClick={() => {
                                                        let res = ValidationWrapper.validateFormular(undefined, true, "passwordContainer", undefined);

                                                        if (!res) {
                                                            return;
                                                        };

                                                        let cfg: IPopupConfig = {
                                                            props: {
                                                                id: 'dynamicPopup',
                                                                title: 'Passwort ndern',
                                                                visible: true,
                                                                closable: false,
                                                                type: EPopupType.pleaseWait
                                                            },
                                                            content: <div>{OrderingTexts.customerData}</div>
                                                        };

                                                        let t = {
                                                            type: ERequestType.customerChangePassword,
                                                            brandKey: this.customer.contracts ? this.customer.contracts[0].brandKey : '',
                                                            id: this.props.parent.data.id,
                                                            ecpassword: this.password
                                                        };

                                                        this.props.parent.processCustomer(() => {
                                                            Global.hub.send(JSON.stringify(t),
                                                                ERequestType.customerChangePassword,
                                                                undefined);
                                                        }, cfg);
                                                    }} />
                                                <page.LButton type={ELButtonType.linkSimple}
                                                    css="action3 fr"
                                                    id="buttonCancelPassword"
                                                    text={MainTexts.cancel}
                                                    onClick={() => {
                                                        this.setState({ modePassword: EFormMode.view });
                                                    }} />
                                            </>
                                        }
                                    </>
                                    : null
                            }
                        </Col>
                    </Row>
                </Container>
            </div>;

        ctrl = <div id={ExistingCustomerData.containerId}>
            <BaseBlock config={cfg}>
                {ctrl}
            </BaseBlock>
        </div>;

        return ctrl;
    };

    public switchMode(newMode: EFormMode) {
        this.setState({ mode: newMode, modePassword: newMode });
    };

    private onInput(target: any) {
        Global.onInput(this.customer, target, () => {
            this.setState({ changed: true });
        });
    };

    private showEMailStatusPopup() {
        let msg = MainTexts.customerEMailNotValidatedText;
        if (this.props.parent.data.customer) {
            msg = msg.replace('$email', this.props.parent.data.customer.email ? this.props.parent.data.customer.email : 'k.A');
        };

        let cfg: IPopupConfig = {
            props: {
                id: 'dynamicPopupEMail',
                title: MainTexts.customerEMailNotValidatedTitle,
                visible: true,
                closable: false,
                type: EPopupType.custom,
                textPositive: MainTexts.next
            },
            action: <>
                <page.LButton text={MainTexts.next} css="fr" icon="ok"
                    type={ELButtonType.buttonWithIconRight}
                    onClick={() => {
                        this.props.parent.resetDynamicPopup(undefined);

                        this.showConsentPopup();
                    }} />
            </>,
            content: <div dangerouslySetInnerHTML={{ __html: msg }}></div>
        };

        let t = {
            type: ERequestType.customerChangePassword,
            brandKey: this.customer.contracts ? this.customer.contracts[0].brandKey : '',
            id: this.props.parent.data.id,
            ecpassword: this.password
        };

        this.props.parent.processCustomer(() => { }, cfg);
    };

    private showConsentPopup() {
        if (
            (this.customer.bew && this.customer.bew.consulting1)
            || (this.customer.contracts && this.customer.contracts[0].brandKey != BrandKeys.otelo)) {
            return <></>
        };

        let msg = '<div class="tal">Bitte beraten Sie Ihren Kunden bezglich der Vorteile der Beratungseinwilligung (BEW) Nur dann ist es mglich:<ul><li>aktives Bestandskundenmanagement fr die Zukunft zu gestalten.</li><li>Kundenroutings z.B. ber Elsbeth durchzufhren.</li><li>Ihrem Kunden besondere Angebote zu machen.</li><li>Tarife auf dem aktuellsten Stand zu halten.</li></ul><div>';

        let cfg: IPopupConfig = {
            props: {
                id: 'dynamicPopupBEW',
                title: 'Hinweis Beratungseinwilligung',
                visible: this.props.parent.brand.id.toLowerCase() === BrandKeys.otelo ? true : false,
                closable: false,
                type: EPopupType.custom,
                textPositive: MainTexts.next,
                height: 300
            },
            action: <>
                <page.LButton text={'Beratungseinwilligung anpassen'} css="fl" icon="ok"
                    type={ELButtonType.buttonWithIconRight}
                    onClick={() => {
                        this.props.parent.resetDynamicPopup(() => {
                            let cont = Global.GetComponent<ProtectionAndConsent>(ProtectionAndConsent.containerId);
                            Global.FocusComponent(ProtectionAndConsent.containerId);
                            cont.collapse(false);
                        });
                    }} />
                <page.LButton text={'Nein jetzt nicht'} css="fr" icon="ok"
                    type={ELButtonType.linkSimple}
                    onClick={() => { this.props.parent.resetDynamicPopup(undefined); }} />
            </>,
            content: <div dangerouslySetInnerHTML={{ __html: msg }}></div>
        };



        let t = {
            type: ERequestType.customerChangePassword,
            brandKey: this.customer.contracts ? this.customer.contracts[0].brandKey : '',
            id: this.props.parent.data.id,
            ecpassword: this.password
        };

        this.props.parent.processCustomer(() => { }, cfg);
    };
};

export default ExistingCustomerData;