import * as React from 'react';
import * as m from '../../models';
import { EPreviewState } from './preview/contract';

export interface IBrandThumbProps {
    item: m.InfoItem;
    //info?: m.IInfoItem;
    onClick: any;
    type: EBrandThumbType;
    status: EBrandThumbStatus;
    activationStatus?: EPreviewState;
    withoutInfo?: boolean;
    ckey: string;
    multiBasketId?: string;
};

export enum EBrandThumbType {
    blister = 'blister', // used for brand selection
    square = 'square',  // used for secondary selection
    squareSmall = 'squareSmall'
};

export enum EBrandThumbStatus {
    undefined = 0,
    active = 1,
    inactive = 2,
    selected = 4,
    static = 8,
    loading = 16
};

export interface IBrandThumbState {
    status: EBrandThumbStatus;
};

export class BrandThumb extends React.Component<IBrandThumbProps, IBrandThumbState> {
    constructor(props: IBrandThumbProps) {
        super(props);
        
        this._onClick= this._onClick.bind(this);
        
        this.state = {
            status: this.props.status
        };
    }

    public componentWillReceiveProps(next: IBrandThumbState){
        this.setState(next);
    };

    public render() {
        let cls: string = 'thumb-inner ' + this.props.item.id;
        if(this.props.multiBasketId){
            cls = 'thumb-inner ' + this.props.multiBasketId;
        }
        if((this.state.status & EBrandThumbStatus.selected) != 0){
            cls += ' selected';
        };
        
        if((this.state.status & EBrandThumbStatus.inactive) != 0){
            cls += ' inactive';
        };        

        if((this.state.status & EBrandThumbStatus.static) != 0){
            cls += ' static';
        };        
        
        if((this.state.status & EBrandThumbStatus.loading) != 0){
            cls += ' loading';
        };  

        //cls += ' ' + this.props.brand.style;

        let clst: string = 'thumb thumb-brand-' + this.props.type.toString() + ' ' + (this.props.item ? this.props.item.css : '');
        
        let clin: string = 'info ' + (this.props.item ? this.props.item.css : '');

        let ctrl = (
            <div key={this.props.ckey} className={clst} onClick={this._onClick}>
                <div className={cls} />

                {
                    this.props.item && this.props.type === EBrandThumbType.blister && !this.props.withoutInfo ? 
                    <div className={clin}><span>{this.props.item.text}</span></div>
                    : null
                }

                {
                    this.props.activationStatus && this.props.activationStatus === EPreviewState.activationDone ? 
                    <div className="markok"/>
                    : null
                }
            </div>
        );

        return ctrl;
    };

    private _onClick() {
        if((this.state.status & EBrandThumbStatus.inactive) != 0){
            return;
        };         

        if(this.props.onClick) {
            this.props.onClick(this.props.item.id);
        }
    };
};

export default BrandThumb;