import * as React from 'react';
import ReactTooltip from 'react-tooltip';
import { EPopupType, IPopupConfig, IPopupProps } from '../components/dialogs/popup';
import * as cmp from '../components/index';
import { ErrorBlock, LControl } from '../components/index';
import { EStatusState } from '../components/support/status';
import { IApiResponse } from '../interfaces';
import { EControlType, ERequestStatus } from '../models';
import Api from './api';
import Global from './Global';
import { ValidationMessages } from './messages';
import * as val from './validation/validationOption';


export class GlobalJSX {
    public static GetLoader(): JSX.Element {
        let com = (
            <div className="loader">
                <cmp.Status status={EStatusState.inprocess} id="loader" />
                {/* <img src="/images/loader.gif" width="150" height="150"/> */}
            </div>
        );

        return com;
    };

    public static getKeyFromPrefix(prefix: string, name: string, idOrName: boolean): string {
        let pref: string = prefix;
        let extra: boolean = false;
        if (pref.startsWith('+')) {
            pref = pref.substring(1);
            extra = true;
        };

        if (idOrName) {
            return pref ? pref + '-' + name : name;
        } else {
            if (extra) {
                return name;
            } else {
                return pref ? pref + '.' + name : name;
            }
        }
    };

    public static renderErrorBlock(api: IApiResponse | undefined): JSX.Element {
        let ctrl = <></>;

        if (api) {
            let sum = Api.parseModelState(api);

            if (sum.status === ERequestStatus.failed) {
                ctrl = <ErrorBlock visible={true} title={ValidationMessages.errorGeneral} message={sum.message} />;
            }
        }

        return ctrl;
    };

    public static renderInner(
        prefix: string,
        source: any,
        onChange: any,
        field: string,
        title: string | undefined,
        label: string,
        previewMode: boolean,
        fixedValue?: any,
        disabled?: boolean,
        validationOptions?: val.ValidationOption[],
        maxLength?: number,minLength?: number): JSX.Element {

        let id = GlobalJSX.getKeyFromPrefix(prefix, field, true);
        let name = GlobalJSX.getKeyFromPrefix(prefix, field, false);
        let sname = name;

      
    
        // if(sname.startsWith(prefix + '.')) {
        //     sname = sname.substring(prefix.length + 1);
        // };

        //if prefix is empty
        if (sname.startsWith('.') && !prefix) {
            sname = sname.substring(1);
        };

        let value = fixedValue ? fixedValue : Global.getValueByPath(source, sname);

        let ctrl = <LControl type={EControlType.text}
            value={value}
            onChange={onChange} title={title}
            maxLength={maxLength? maxLength: undefined}
            minLength={minLength? minLength: undefined}
            hideTitle={!title}
            name={name} id={id} label={label}
            disabled={disabled}
            validationOptions={validationOptions? validationOptions: undefined}
            preview={previewMode ? previewMode : false} />;

         

        return ctrl;
    };

    public static GetLoaderWithText(text: string): JSX.Element {
        let com = (
            <div className="tloader">
                <cmp.Status status={EStatusState.inprocess} id="loaderWithText" />
                <div className="text">
                    {text}
                </div>
            </div>
        );

        return com;
    };

    public static GetPopupAuto(cfg:IPopupConfig) {
        return GlobalJSX.GetPopup(cfg.props, cfg.content, cfg.action);
    };

    public static GetPopup(config: IPopupProps,
        content: JSX.Element,
        action: JSX.Element | undefined): JSX.Element {

        let ctrl = <>
            <cmp.Popup {...config}>
                <cmp.PopupBody>
                    {content}
                    <></>
                </cmp.PopupBody>
                {
                    action ?
                        <cmp.Action>
                            {action}
                            <></>
                        </cmp.Action>
                        : <></>
                }
            </cmp.Popup>
        </>;

        return ctrl;
    };

    public static GetPleaseWait(text: string,
        onDismiss: any,
        type: EPopupType,
        visible: boolean,
        closable: boolean,
        content: JSX.Element): JSX.Element {
        let ctrl = <>
            <cmp.Popup closable={closable}
                type={type}
                visible={visible}
                id="popupWait"
                title={text}
                height={200} onDismiss={onDismiss}>
                <cmp.PopupBody>
                    {content}
                    <></>
                </cmp.PopupBody>
                <></>
            </cmp.Popup>
        </>;

        return ctrl;
    };

    public static getInfoTooltip(id: string, 
        tt: any) {
        let ctrl = 
            <>
            <ReactTooltip place="top" type="light" effect="solid" id={'tt-' + id}>
                <div dangerouslySetInnerHTML={{ __html: tt }} />
            </ReactTooltip>

            <div data-for={'tt-' + id} data-tip={tt} className="iicon icon info link inline"></div>
            </>

        return ctrl;
    };
};

export default GlobalJSX;