import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { Inner1C, LButton, LClear, LControl, PopupBody } from '../../components';
import Popup, { EPopupType } from '../../components/dialogs/popup';
import BaseBlock from '../../components/page/block';
import { IApiResponse, IApiSummary } from '../../interfaces';
import { EControlType, ELButtonType, ERequestType } from '../../models';
import { IBlockConfiguration } from '../../models/block/configuration';
import { IControlValue } from '../../models/page/interfaces';
import Api from '../../utils/api';
import Global from '../../utils/Global';
import { MainTexts } from '../../utils/messages';


export interface IMnpStatusProps {
}

export interface IMnpStatusState {
    msisdn: string;
    statusMsg?: string;

    popupTitle: string;
    processing: boolean;
    closblePopup: boolean;
    apiMessage: string;
}

export class MnpStatusScreen extends React.Component<IMnpStatusProps, IMnpStatusState> {
    private processPopupState: EPopupType = EPopupType.pleaseWait;
    public static containerId: string = 'mnpStatusScreen';

    constructor(props) {

        super(props);

        // if (Global.validateAccessRoles([Roles.XTND_Nutzerverwaltung])) {

        // }

        Global.easyApp.setTitle(MainTexts.mnpStatusPageTitle);

        this._loadData = this._loadData.bind(this);
        this._onMsisdnChange = this._onMsisdnChange.bind(this);

        this.state = {
            msisdn: '',
            processing: false,
            closblePopup: false,
            popupTitle: '',
            apiMessage: ''
        };
    }

    render() {
        const cfg: IBlockConfiguration = {
            title: MainTexts.mnpStatus,
            transparent: false,
            styleContainer: ''
        };

        const cfg2: IBlockConfiguration = {
            title: 'Status',
            transparent: false,
            styleContainer: ''
        };

        const statusDataHtml =
            <LControl type={EControlType.hint}
                value={MainTexts.status}
                preview={true}
                name="status" id="status"
                label={this.state.statusMsg || ''} />;

        return <div id={MnpStatusScreen.containerId}>
            <Inner1C>
                <div>
                    <Popup
                        type={this.processPopupState}
                        visible={this.state.processing || this.state.closblePopup}
                        closable={this.state.closblePopup}
                        id="wait"
                        title={this.state.popupTitle}
                        height={200}
                        onDismiss={this._onDismissProcessPopup}>
                        <PopupBody>
                            <div>{this.state.apiMessage}</div>
                            <></>
                        </PopupBody>
                        <></>
                    </Popup>

                    <BaseBlock config={cfg}>
                        <Container className="container-action">
                            <Row>
                                <Col xs={6} md={6} >
                                    <LControl type={EControlType.text}
                                        value={this.state.msisdn}
                                        onChange={this._onMsisdnChange}
                                        preview={false}
                                        name="msisdn" id="msisdn"
                                        label={MainTexts.msisdn} />
                                </Col>
                                <Col xs={6} md={6} >
                                    <LButton type={ELButtonType.buttonWithIconRight}
                                        css="ai fr"
                                        id="buttonSearch"
                                        text={MainTexts.mnpStatus}
                                        onClick={this._loadData} />
                                </Col>
                            </Row>
                        </Container>
                        <LClear />
                    </BaseBlock>

                    <BaseBlock config={cfg2}>
                        <Container className="container-action">
                            <Row>
                                <Col xs="12" md="12">
                                    {statusDataHtml}
                                </Col>
                            </Row>
                        </Container>
                    </BaseBlock>
                </div>
            </Inner1C>
        </div>;
    }

    socketCallback(res: IApiResponse) {
        if (res.error) {
            let sum: IApiSummary = Api.parseSummary(res, undefined);
            this.processPopupState = EPopupType.custom;
            this.setState({ processing: false, apiMessage: sum.message, closblePopup: true });
        } else if (res.data) {
            this.setState({ processing: false, statusMsg: res.data.data });
        }
    };

    _loadData = () => {
        this.processPopupState = EPopupType.pleaseWait;

        this.setState({
            processing: true
        });

        const model = { id: this.state.msisdn };

        Global.hub.send(JSON.stringify(model),
            ERequestType.loadPortingStatus,
            undefined);
    }

    _onDismissProcessPopup = () => {
        this.setState({
            processing: false
        });

    }

    _onMsisdnChange = (e: IControlValue) => {
        this.setState({ msisdn: e.value });
    }
}