import * as React from 'react';
import IBrand  from '../../models/ordering/brand';
import IProduct  from '../../models/ordering/product';
import ReactTooltip from 'react-tooltip';
import TooltipWrapper, { ETooltipType } from '../page/tooltip';
import Global from '../../utils/Global';
import {ITooltipModel, ETooltipDataType} from '../../models/global/tooltip';
import { Hints } from '../../utils/messages';
import { ILinkConfig } from '../existing/data/services';

export interface ITariffThumbProps {
    text: string;
    detail?: string;
    action?: ILinkConfig;

    id: string;
    idx: number;
    tooltip?: string;
    brandKey: string;
    selected: boolean;
    onClick: any;
    clickModel?: any;
    styleBrand: boolean;
    cssStyle?: string;
    tooltipModel?: any;

    custom?: boolean;
};

export interface ITariffThumbState {
    selected: boolean;
};

export class TariffThumb extends React.Component<ITariffThumbProps, ITariffThumbState> {

    constructor(props: ITariffThumbProps) {
        super(props);
        this._onClick = this._onClick.bind(this);
        this.state = {
            selected: this.props.selected
        };
    }

    public componentWillReceiveProps(next: ITariffThumbProps) {
        this.setState({selected: next.selected});
    };

    public render() {
        let cls: string = 'thumb thumb-tariff ' + (this.props.styleBrand ? this.props.brandKey : '');

        cls += this.props.cssStyle ? this.props.cssStyle : '';

        if(this.state.selected) {
            cls += ' selected';
        };
        
        let uid = Global.getUniqueId();

        let ttm: ITooltipModel = {type: ETooltipDataType.html, body:'', style: ''};
        
        try {
            if(this.props.tooltip && this.props.tooltip.indexOf("{")>=0) {
                ttm = this.props.tooltip ? JSON.parse(this.props.tooltip) : {};
            } else {
                if(this.props.tooltip) { 
                    ttm = { type: ETooltipDataType.formated, body: this.props.tooltip, style: '' };
                } else {
                    ttm = { type: ETooltipDataType.html, body: Hints.noMoreInfo, style: ''}
                }
            }
        } catch(e) {}

        let ctrl = (
            <>
            {
                ttm && ttm.body ? 
                <ReactTooltip place="top" type="light" effect="solid" id={'tt' + uid}>
                    <div dangerouslySetInnerHTML={{__html: ttm.body}}/>
                    {/* <TooltipWrapper brand={this.props.brandKey} type={ETooltipType.product} model={this.props.tooltipModel} /> */}
                </ReactTooltip>
                : <></>
            }
            <div className={cls} onClick={this._onClick} data-tip={this.props.tooltip} data-for={'tt' + uid}>
                <span className="text">
                    {this.props.text}
                </span>
                {
                    this.props.action ? 
                    <div className="action" onClick={
                         (e)=>{
                                if(this.props.action) {
                                    this.props.action.onClick(this)
                                    e.preventDefault();
                                };
                            }
                         }>
                        {this.props.action.text}
                    </div>
                    : null
                }
            </div>
            </>
        );

        return ctrl;
    };

    private _onClick() {
        if(this.props.onClick){
            if(this.props.clickModel) {
                this.props.onClick(this.props.clickModel);
            } else {
                this.props.onClick(this.props.id);
            };
        }
    };
};

export default TariffThumb;