import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import ReactTooltip from 'react-tooltip';
import imageInfo from '../../../assets/icons/info-circle.svg';
import imageInfoIdCard from '../../../assets/images/idcard.jpg';
import BaseBlock from '../../../components/page/block';
import { Address } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { ILegitimation } from '../../../models/ordering/legitimation';
import { EActionAddressType, EIDCardType, EPaymentType } from '../../../models/ordering/order';
import { IPayment } from '../../../models/ordering/payment';
import { EControlType } from '../../../models/page/diverse';
import { CustomerTexts, MainTexts, ValidationMessages } from '../../../utils/messages';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import * as page from '../../page/index';
import ActivationCustomerData from '../process/activationCustomerData';

export interface ICustomerLegitimationProps {
    parent: ActivationCustomerData;
    onChange?: any;
    legitimation?: ILegitimation;
    payment?: IPayment;
    preview: boolean;
};

export interface ICustomerLegitimationState {
    changed: boolean;
};

export class CustomerLegitimation extends React.Component<ICustomerLegitimationProps, ICustomerLegitimationState> {
    private legitimation:ILegitimation;
    private payment: IPayment;
   

    constructor(props: ICustomerLegitimationProps) {
        super(props);

         this.legitimation = this.props.legitimation ? this.props.legitimation : {type: EIDCardType.german, id: '', authority: ''};
        this.payment = this.props.payment ? this.props.payment : { paymentType: EPaymentType.bank, addressType: EActionAddressType.sameAddress, address: new Address() };

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: any) {
        
     
        this.setState({changed: true});
    };
    
    public render() {
        //if cable only then no legitimation is needed
        if(this.props.parent.props.parent.dataWrapper.hasKabelOnly()) {
           return <></>
        };

        let cfg: IBlockConfiguration = {
            title: CustomerTexts.legitimation,
            transparent: false
        };

        //let idText = this.legitimation.type === EIDCardType.german ? MainTexts.idGerman : MainTexts.idNotGerman;

        let hintIdCard = <div className="contentTooltip">
            <ReactTooltip style={{ width: '700px', height: '300px', maxWidth: 'fit-content' }} place="top" type="light" effect="solid" id={'infoTooltipIDCard'}>
                <div><img  src={imageInfoIdCard}/></div>
            </ReactTooltip>
            <span>Unterscheidung zwischen DE- und EU-Personalausweis</span>
            <img src={imageInfo} className="icon action4" data-tip={"bbbbbb"} data-for="infoTooltipIDCard"/>
            {/* <page.LButton type={ELButtonType.iconImage} 
                icon="trash" 
                id="infoTooltipIDCard"
                onClick={()=>{

                }} />   */}
        </div>

        let ctrl = (
            <BaseBlock config={cfg}>
                {/* Don't remove !!!!!! */}
                {/* <BaseBlockAction>
                    {
                        this.props.preview ? <></> : <page.LControl type={EControlType.toggle} hideTitle={true} hideHint={true} id="legitimation.forAll" label="bernehmen fr alle Vertrge"/>
                    }
                    <></>
                </BaseBlockAction> */}
                {
                    this.props.parent.props.parent.dataWrapper.hasVodafoneOrDsl() ? 
                    <Container>
                        <Row>
                            <Col xs={6} md={6} className="np">
                                <page.LControl preview={this.props.preview}
                                    checked={this.legitimation.type == EIDCardType.german ? true : false}
                                    type={EControlType.radio} onChange={this.props.onChange} 
                                    name="legitimation.type" id="type_1" label={MainTexts.idGerman}/>
                            </Col>
                            <Col xs={6} md={6} className="npr">
                                <page.LControl 
                                    preview={this.props.preview}
                                    checked={this.legitimation.type == EIDCardType.foreign ? true : false}
                                    type={EControlType.radio} onChange={this.props.onChange} 
                                    name="legitimation.type" id="type_2" label={MainTexts.idNotGerman}/>
                            </Col>                        
                        </Row>
                    </Container>
                    : null 
                }
                <Container>
                    <Row> 
                        <Col xs={6} md={6} className="np">
                            {
                                this.legitimation && this.legitimation.type == EIDCardType.german ?
                                <page.LControl preview={this.props.preview}
                                    value={this.legitimation ? this.legitimation.id : ''} 
                                    type={EControlType.text} onChange={this.props.onChange} name="legitimation.id" id="idCard" maxLength={10}
                                    validationOptions = {[
                                        new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                       // new ValidationOption({ type: EValidationOption.alphanumericWithoutGermanChars, message: ValidationMessages.alphanumericNoUmlauts })                                      
                                    ]}
                                    label={MainTexts.personalIdNr}
                                    hintObject={hintIdCard}/>
                                :
                                <page.LControl preview={this.props.preview}
                                    value={this.legitimation ? this.legitimation.id : ''} 
                                    type={EControlType.text} onChange={this.props.onChange} name="legitimation.idInt" targetName="legitimation.id" id="idCard" 
                                    label={MainTexts.personalIdNr}
                                    hintObject={hintIdCard}/>                                    
                            }
                        </Col>
                        <Col xs={6} md={6} className="npr">
                            <page.LControl 
                                preview={this.props.preview}
                                maxLength={20}
                                value={this.legitimation ? this.legitimation.authority : ''} 
                                validationOptions={ 
                                    [
                                        new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                        new ValidationOption({type: EValidationOption.alphanumeric, message: ValidationMessages.alphanumeric })
                                    ]
                                } 
                                type={EControlType.text} onChange={this.props.onChange} name="legitimation.authority" id="idAuthority" label="Ausstellende Behrde"/>
                        </Col>                        
                    </Row>
                    {
                        
                        this.props.parent && !this.props.parent.props.parent.dataWrapper.hasOtelo() && this.props.parent.props.parent.dataWrapper.hasVodafoneOrDsl() && this.legitimation.type == EIDCardType.foreign ?

                        <>
                            <Row>
                                <Col xs={6} md={6} className="np">
                                    <page.LControl preview={this.props.preview}
                                        value={this.payment ? this.payment.ccNumber : ''} 
                                        maxLength = {16}
                                        type={EControlType.text} onChange={this.props.onChange} 
                                        name="payment.ccNumber" id="payment-ccNumber" label={MainTexts.legitimationCreditCard}
                                        validationOptions={ 
                                            [
                                                new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                                new ValidationOption({type: EValidationOption.numeric, message: ValidationMessages.errorNumeric })
                                            ]
                                        } 
                                        />
                                </Col>
                                <Col xs={6} md={6} className="npr">
                                    <page.LControl preview={this.props.preview}
                                            value={this.payment ? this.payment.ccBank : ''} 
                                            type={EControlType.text} onChange={this.props.onChange} 
                                            name="payment.ccBank" id="payment-ccBank" label={MainTexts.legitimationCreditCardAt}/>
                                </Col>
                            </Row>
                            <Row>
                                <Col xs={6} md={6} className="np">
                                    <page.LControl preview={this.props.preview}
                                        value={this.payment ? this.payment.ccMonth : ''} 
                                        type={EControlType.text} onChange={this.props.onChange} 
                                        maxLength = {2}
                                        name="payment.ccMonth" id="payment-ccMonth" label={MainTexts.month}
                                        validationOptions={ 
                                            [
                                             new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                             new ValidationOption({type: EValidationOption.monthRange, message: ValidationMessages.errorMonthFormat })
                                            ]
                                        }
                                        />
                                </Col> 
                                <Col xs={6} md={6} className="npr">
                                    <page.LControl preview={this.props.preview}
                                            maxLength = {2}
                                            value={this.payment ? this.payment.ccYear : ''} 
                                            type={EControlType.text} onChange={this.props.onChange} 
                                            validationOptions={ 
                                                [
                                                 new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                                 new ValidationOption({type: EValidationOption.yearFieldValidity, message: ValidationMessages.errorYearFormat })
                                                ]
                                            }
                                            name="payment.ccYear" id="payment-ccYear" label={MainTexts.year}/>
                                </Col>                        
                            </Row>                            
                        </>
                       : null
                    }

                    {
                        this.props.parent && this.props.parent.props.parent.dataWrapper.hasOteloPrepaid() ?
                        <Row> 
                            <Col xs={6} md={6} className="np">
                                <page.LDate
                                    onChange={this.props.onChange}
                                    datePicker={true}
                                    value={this.legitimation ? this.legitimation.validFrom : new Date()} 
                                    key={"legitimation.validFrom"}
                                    name={"legitimation.validFrom"} 
                                    id={"validFrom"}
                                    title={MainTexts.validFrom}/>
                            </Col>
                            <Col xs={6} md={6} className="npr">
                                <page.LDate
                                        onChange={this.props.onChange}
                                        datePicker={true}
                                        value={this.legitimation ? this.legitimation.validTo : new Date()} 
                                        key={"legitimation.validTo"}
                                        name={"legitimation.validTo"} 
                                        id={"validTo"}
                                        title={MainTexts.validTo}/>
                            </Col>                        
                        </Row>             
                        : <></>
                    }

                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };    
};

export default CustomerLegitimation;