import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import BaseBlock from '../../../components/page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { ICustomer } from '../../../models/ordering/customer';
import { ESalutation } from '../../../models/ordering/order';
import { EControlType } from '../../../models/page/diverse';
import Global from '../../../utils/Global';
import { Hints, MainTexts, OrderingTexts } from '../../../utils/messages';
import * as page from '../../page/index';
import ActivationPreview from '../process/activationPreview';

export interface IPreviewActivationsProps {
    parent: ActivationPreview;
    customer: ICustomer;
};

export interface IPreviewActivationsState {
    changed: boolean;
};

export class PreviewCustomerData extends React.Component<IPreviewActivationsProps, IPreviewActivationsState> {
    constructor(props: IPreviewActivationsProps) {
        super(props);
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.customerData,
            transparent: false
        };

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>

                    <Row>
                        <Col xs={4} md={4} className="np">
                            <page.LControl type={EControlType.info}
                                label={this.props.customer.salutation ? (this.props.customer.salutation == ESalutation.mr ? MainTexts.mr : MainTexts.mrs) : ''}
                                title="Anrede" />
                        </Col>
                        <Col xs={4} md={4}>
                            <page.LControl type={EControlType.info} label={this.props.customer.title ? this.props.customer.title : ''} title="Titel" />
                        </Col>
                        <Col xs={4} md={4} className="npr">
                            <page.LControl type={EControlType.info} label={this.props.customer.nationality ? this.props.customer.nationality : ''} title={MainTexts.nationality} />
                        </Col>
                    </Row>

                    <div className="spacer" />

                    <Row>
                        <Col xs={4} md={4} className="np">
                            <page.LControl type={EControlType.info} label={this.props.customer.firstname ? this.props.customer.firstname : ''} title="Vorname" />
                        </Col>
                        <Col xs={4} md={4}>
                            <page.LControl type={EControlType.info} label={this.props.customer.surname ? this.props.customer.surname : ''} title="Nachname" />
                        </Col>
                    </Row>

                    <div className="spacer" />

                    <Row>
                        <Col xs={8} md={8} className="np">
                            <page.LControl type={EControlType.info} label={this.props.customer.address ? this.props.customer.address.street : ''} title="Strae" />
                        </Col>
                        <Col xs={4} md={4}>
                            <page.LControl type={EControlType.info} label={this.props.customer.address ? this.props.customer.address.nr : ''} title="Hausnummer" />
                        </Col>
                    </Row>

                    <div className="spacer" />

                    <Row>
                        <Col xs={4} md={4} className="np">
                            <page.LControl type={EControlType.info} label={this.props.customer.address ? this.props.customer.address.zip : ''} title={MainTexts.zip} />
                        </Col>
                        <Col xs={4} md={4}>
                            <page.LControl type={EControlType.info} label={this.props.customer.address ? this.props.customer.address.city : ''}
                                title={MainTexts.city} />
                        </Col>
                    </Row>

                    <div className="spacer" />

                    <Row>
                        <Col xs={4} md={4} className="np">
                            <page.LControl type={EControlType.info} label={this.props.customer.birthday ? Global.toGermanDate(this.props.customer.birthday) : ''}
                                title={MainTexts.dateOfBirth} />
                        </Col>
                        <Col  xs={4} md={4}>
                            <page.LControl type={EControlType.info} label={this.props.customer.email ? this.props.customer.email : ''} title="E-Mail-Adresse" />
                        </Col>
                    </Row>

                    {
                        this.props.customer.address && this.props.customer.address.corrected ? 
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl hideTitle={true} labelStyle={'tc-hint mwa'} type={EControlType.hint} label={Hints.addressCorrected} />
                            </Col>
                        </Row>    
                        : null
                    }

                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };
};

export default PreviewCustomerData;