import { IApiError } from "./IApiError";
import { IApiSummary } from "./IApiStep";

export enum EApiResponseStatus {
    undefined = 0,
    ok = 1,
    failed = 2
};

export interface IApiValidationItem {
  Key: string;
  Value: string;
};

export interface IApiValidation {
  items: IApiValidationItem[];
};

export interface IApiResponse {
  error: IApiError;
  validation: IApiValidation;
  summary?: IApiSummary;
  data: any;
  status: EApiResponseStatus;
  isAuthenticated?: boolean;
  lifetime: number;
  success: boolean;
};

export default {};