import * as React from 'react';
import { LTimer } from '../page';
import { number } from 'prop-types';

export interface IStatusProps {
    status: EStatusState;
    stepCallback?: any;
    tickCallback?: any;
    completedCallback?: any;
    counter?: number;
    id: string;
    autoRestart?: boolean;
};

export interface IStatusState {
    status: EStatusState;
    changed: boolean;
};

export enum EStatusState {
    undefined = 0,
    inprocess = 1,
    done = 2,
    failed = 4,
    inprocessTimer = 8,
    warning = 16
};

export class Status extends React.Component<IStatusProps, IStatusState> {
    private counter: number;
    private counterDefault: number = 99;
    public static defaultTimer: number = 99;
    constructor(props: IStatusProps) {
        super(props);

        this.state = {
            status: this.props.status,
            changed: false
        };

        this.counterDefault = Status.defaultTimer;

        this.timerTick = this.timerTick.bind(this);

        this.timerCompleted = this.timerCompleted.bind(this);

        this.counter = this.props.counter ? this.props.counter : this.counterDefault;

        if(this.state.status === EStatusState.inprocessTimer) {

        }
    };

    public componentWillReceiveProps(next: IStatusProps) {
        if(next.counter) {
            this.counter = 99;
        }
        //this.counter = 99;
        this.setState({status: next.status});
    };

    public render() {
        //we force to not show timer
        let css: string = 'statusbox print-hide st' + (this.state.status == EStatusState.inprocessTimer ? EStatusState.inprocess : this.state.status );
        let ctrl = (
            <div id={this.props.id}>
                {
                    false && this.props.status === EStatusState.inprocessTimer ?
                        this.renderTimer()
                    :
                    <div className={css} style={ {background: "url()" }}>
                        <div className="icon"/>
                    </div>
                }
            </div>
        );

        return ctrl;
    };

    public resetTimer() {
        this.counter = this.counterDefault;
        this.setState({changed: true});
    };

    public renderTimer() : JSX.Element {
        //let procVal = this.counter / Status.defaultTimer 
        let proc: number = 2.6 * (Status.defaultTimer - this.counter) ;
        let cssplus: string = this.counter < 10 ? ' single' : '';

        let svg = '<div class="info ' + cssplus + '">' + this.counter + '</div><div class="bg"><svg class="circle-chart" viewbox="0 0 94 94" width="94" height="94" xmlns="http://www.w3.org/2000/svg"><defs><linearGradient id="grad1" x1="0%" y1="0%" x2="100%" y2="0%"><stop offset="0%" style="stop-color:rgb(170, 179, 56);stop-opacity:1" /><stop offset="100%" style="stop-color:rgb(223, 154, 54);stop-opacity:1" /></linearGradient></defs><circle class="circle-chart__background" stroke="#efefef" stroke-width="5" fill="none" cx="47" cy="47" r="42" /><circle class="circle-chart__circle" stroke="url(#grad1)" stroke-width="5" stroke-dasharray="' + proc + ',534" stroke-linecap="round" fill="none" cx="47" cy="47" r="42"/></svg></div>';
        
        let ctrl = (
            <>
                {/* <div className="loadersvg" dangerouslySetInnerHTML={{__html: svg}} /> */}
                <LTimer 
                    count={this.counter} 
                    step={5} 
                    completed={this.timerCompleted} 
                    showUI={false} 
                    autoRestart={this.props.autoRestart}
                    tickCallback={this.timerTick} 
                    stepCallback={this.props.stepCallback} /> 
            </>
        );

        return ctrl;
    };

    public timerCompleted() {
        if(this.props.completedCallback) {this.props.completedCallback();}
    };    

    public timerTick(e: number) {
        if(this.props.autoRestart && this.counter<=2) {
            this.counter = this.counterDefault;
        };
    
        this.counter--;

        this.setState({changed:true});

        if(this.props.tickCallback) {this.props.tickCallback();}
    };
};

export default Status;