import * as linq from 'linq';
import * as React from 'react';
import ReactTooltip from 'react-tooltip';
import { EControlType, EValueType } from '../../models/page/diverse';
import { IControlValue, IKeyText } from '../../models/page/interfaces';
import { IField } from '../../models/support/field';
import Global from '../../utils/Global';
import { MainTexts } from '../../utils/messages';
import * as val from '../../utils/validation/validationOption';

export interface ILControlProps {
    type: EControlType;
    valueType?: EValueType;
    id?: string;
    name?: string;
    targetName?: string;

    label?: string;
    labelStyle?: string;
    checked?: boolean;
    disabled?: boolean;
    visible?: boolean;
    hint?: string;
    hintObject?: any;
    error?: string;
    title?: string;
    titleDesc?: string;
    placeholder?: string;
    autoComplete?: string;
    maxLength?: number;
    minLength?: number;

    hideLabel?: boolean;
    hideTitle?: boolean;
    hideHint?: boolean;
    options?: IKeyText[];
    value?: any;
    style?: string;

    trimValue?: boolean;

    icon?: string;
    controlStlye?: string;

    onChange?: any;
    onClick?: any;
    onEnter?: any;

    validationOptions?: val.ValidationOption[];
    preview?: boolean;

    tooltip?: string;
    isHtml?: boolean;
    overflowEnabled?: boolean;
    isChild?: boolean;
    pasteDisabled?: boolean;
};

export interface ILControlState {
    changed: boolean;
    disabled?: boolean;
    hidden?: boolean;
    error?: string;
    visible?: boolean;
};

export class LControl extends React.Component<ILControlProps, ILControlState> {
    private value: IControlValue = { id: '', name: '', type: EControlType.undefined };
    private rules: val.ValidationOption[] = [];

    constructor(props: ILControlProps) {
        super(props);

        //this.onChange = this.onChange.bind(this);
        this.disable = this.disable.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);
        this.hideShow = this.hideShow.bind(this);
        this.validate = this.validate.bind(this);
        this.setError = this.setError.bind(this);

        this.state = {
            changed: false,
            visible: this.props.visible != undefined ? this.props.visible : true,
            error: this.props.error
        };

        this.value = {
            id: this.props.id ? this.props.id : '',
            name: this.props.name ? this.props.name : '',
            type: this.props.type,
            value: this.props.value,
            checked: this.props.checked,
            targetName: this.props.targetName
        };

        if (this.props.type === EControlType.checkbox) {
            
            this.value.value = this.value.checked ? 1 : 0;
        };
    };

    public componentWillReceiveProps(next: ILControlProps) {
        
    };

    public getKey(): string | undefined {
        return this.props.name ? this.props.name : undefined;
    };

    public render() {
        this.value = {
            id: this.props.id ? this.props.id : '',
            name: this.props.name ? this.props.name : '',
            type: this.props.type,
            value: this.props.value,
            checked: this.props.checked
        };

        if (this.props.type === EControlType.checkbox) {
            this.value.value = this.value.checked ? 1 : 0;
        };

        if (this.state.hidden) {
            return <></>
        };

        if (this.props.type === EControlType.title) {
            return this.renderTitle();
        }
        if (this.props.type === EControlType.hint) {
            return this.renderHint();
        }

        let cssHint = "hint";
        let hintText: any = this.props.hint;

        if(this.props.hintObject) {
            hintText = this.props.hintObject;
        }

        if (this.state.error ) {
            cssHint += ' hint-error';
            hintText = this.state.error;
        }

        //needed for birthday manual error
        if (this.props.error) {
            cssHint += ' hint-error';
            hintText = this.props.error;
        }

        let id = 'oec';

        if (this.props.id) {
            id += this.props.id;
        } else if (this.props.name) {
            id += this.props.name;
        } else {
            id += Global.getUniqueId();
        }

        let css = "oe-control " + this.props.style;

        if (!this.state.visible) {
            css += ' dn';
        };

        return <div className={css} id={id}>
            {this.props.hideTitle ? null : <div className="title">{this.props.title ? this.props.title : '\u00A0'}</div>}
            {this.renderAuto()}
            {
                this.props.type === EControlType.info || this.props.hideHint || !hintText ? null : 
                <>
                
                <div className={cssHint}>
                    {hintText}
                </div>
                <div className="spacer"/>
                </>
            }
        </div>
    };

    public getId() {
        return this.props.id;
    };

    public hideShow(state: boolean) {
        this.setState({ hidden: state });
    };

    public disable() {
        this.setState({ changed: true });
    };

    public getValue(): IControlValue {
        return this.value;
    };

    public setValue(val: any) {
        this.value.value = val;
        this.value.targetName = this.props.targetName;
        this.setState({changed: true});
    };

    public getValidationOptions() : val.ValidationOption[] {
        return this.rules ? this.rules : [];
    };

    public setValidationOptions(value : val.ValidationOption[]) {
        this.rules = value;
    };    

    public validate(field: IField | undefined,
        showErrors: boolean): boolean {
        
        //reset due dynamic rules in existing customer search
        if(this.rules.length > 0) {
            this.rules = [];
        };

        if (this.rules.length === 0) {
            if (this.props.validationOptions) {
                this.props.validationOptions.map((i, idx) => {
                    this.rules.push(i);
                });
            }

            if (field && field.config.length > 0) {
                field.config.map((i, idx) => {
                    i.rules.map((ii, iidx) => {
                        this.rules.push(new val.ValidationOption(ii));
                    });
                });
            };
        }

        if (this.rules.length > 0) {
            for (let i = 0; i < this.rules.length; i++) {
                let msg: string = this.rules[i].isValid(this);
                if (msg && msg.length > 0) {
                    if (showErrors) {
                        this.setState({ error: msg });
                    }
                    return false;
                }
            };
        };

       
        this.setState({ error: '' });

        return true;
    };

    public setError(message: string) {    
        this.setState({ error: message });
    };

    private renderAuto(): JSX.Element {
        let ctrl: JSX.Element = <></>;

        if (this.props.type === EControlType.password) {
            ctrl = this.renderPassword();
        } else if (this.props.type === EControlType.text) {
            ctrl = this.renderTextbox();
        } else if (this.props.type === EControlType.radio) {
            ctrl = this.renderRadio();
        } else if (this.props.type === EControlType.checkbox) {
            ctrl = this.renderCheckbox();
        } else if (this.props.type === EControlType.dropdown) {
            ctrl = this.renderDropDown();
        } else if (this.props.type === EControlType.info) {
            ctrl = this.renderInfo();
        } else if (this.props.type === EControlType.toggle) {
            ctrl = this.renderToggle(); 
        }

        return ctrl;
    };

    private renderHint(): JSX.Element {
        const additionalClasses = this.props.labelStyle || '';
        return <div className="oe-control">
            <div className={"hint hint-huge " + additionalClasses} dangerouslySetInnerHTML={{ __html: this.props.label ? this.props.label : '' }} />
        </div>
    };

    private renderInfo(): JSX.Element {
        let css: string = 'info noov';

        if(this.props.overflowEnabled) {
            css = 'info';
        };
 
        if (this.props.style) {
            css += ' ' + this.props.style;
        }
        return <div className="oe-info">
            <div className={css} title={this.props.label} dangerouslySetInnerHTML={{ __html: (this.props.label ? this.props.label : '')}}>
                {/* {
                    this.props.isHtml ? 
                        <div dangerouslySetInnerHTML={{ __html: (this.props.label ? this.props.label : '')}}/> 
                        : this.props.label
                } */}
            </div>
        </div>
    };

    private renderTitle(): JSX.Element {
        let css: string = 'title';
        if (this.props.style) {
            css += ' ' + this.props.style;
        }
        return <div className="oe-control">
            <div className={css}>
                <div className="text">{this.props.title}</div>
                {this.props.titleDesc ? <div className="desc">{this.props.titleDesc}</div> : null}
            </div>
        </div>
    };

    private renderPassword(): JSX.Element {

        return <div className="input-group">
            <input type="password" onChange={this.onChange}
                className="form-control"
                id={this.props.id}
                name={this.props.name}
                value={this.props.value}
                onKeyDown={this.onKeyDown}
                placeholder={this.props.placeholder ? this.props.placeholder : ''}
                autoComplete={this.props.autoComplete ? this.props.autoComplete : ''} />

            {
                this.props.hideLabel ? <></> :
                    <>
                        <span className="bar"></span>
                        <label className="oe-label">{this.props.label}</label>
                    </>
            }
        </div>;
    };

    private renderTextbox(): JSX.Element {
        //console.log('render text:' + this.props.id + '=' + this.props.disabled);
        let ctrl =
            this.props.preview ?
                <LControl id={this.props.id} type={EControlType.info} label={this.props.value} title={this.props.label} />
                : (
                    <div className="input-group">
                        <input type="text"
                            id={this.props.id}
                            value={this.props.value}
                            name={this.props.name}
                            disabled={this.props.disabled}
                            maxLength = {this.props.maxLength? this.props.maxLength: undefined}
                            minLength = {this.props.minLength? this.props.minLength: undefined}
                            className="form-control"
                            placeholder={this.props.placeholder}
                            onKeyDown={this.onKeyDown}
                            onPaste={(e)=>{ 
                                if(this.props.pasteDisabled) {
                                    e.preventDefault(); 
                                    return false;
                                }
                            }}
                            autoComplete={this.props.autoComplete ? this.props.autoComplete : ''}
                            onChange={this.onChange} 
                            onClick={()=>{ if(this.props.onClick) {this.props.onClick(this);}}}/>

                        {
                            this.props.hideLabel ? <></> :
                                <>
                                    <span className="bar"></span>
                                    <label className="oe-label">{this.props.label}</label>
                                </>
                        }
                    </div>
                );

        return ctrl;
    };

    private renderToggle(): JSX.Element {
        let css: string = 'oe-toggle';
        if (this.props.disabled) {
            css += ' disabled';
        }

        let ctrl = (
            <div className={css} >
                <input type="checkbox" className="oe-checkbox" id={this.props.id} name={this.props.name} checked={this.props.checked} onChange={this.onChange} />
                <label htmlFor={this.props.id}></label>
                <span className={this.props.labelStyle}>{this.props.label}</span>
                <div className="oe-border" />
            </div>
        );

        return ctrl;
    };

    private renderCheckbox(): JSX.Element {
        let css: string = 'oe-checkbox';

        if (this.props.disabled) {
            css += ' disabled';
        }
        
        let ctrl = 
            <div className={css}>
                {
                    this.props.tooltip ? 
                    <ReactTooltip place="top" type="light" effect="solid" id={'ttctrl' + this.props.id}>
                        <div dangerouslySetInnerHTML={{__html: this.props.tooltip}}/>
                    </ReactTooltip>
                    : null
                }
                
                <input type="checkbox" className="oe-checkbox" 
                    id={this.props.id} 
                    name={this.props.name} 
                    checked={this.props.checked} 
                    //onClick={this.onChange}
                    onChange={this.onChange} 
                    />
                <label htmlFor={this.props.id}>
                    <span className={this.props.labelStyle} data-tip={this.props.tooltip} data-for={'ttctrl' + this.props.id} dangerouslySetInnerHTML={{__html: this.props.label ? this.props.label : '' }} />
                </label>
                <div className="oe-border" />
            </div>;

        return ctrl;
    };

    private renderRadio(): JSX.Element {
        let ctrl = (
            <div className="oe-radio">
                <input type="radio" id={this.props.id} name={this.props.name} checked={this.props.checked} onClick={this.onChange} onChange={this.onChange} />
                <label htmlFor={this.props.id}>
                    <span className={this.props.labelStyle}>{this.props.label}</span>
                </label>
                <div className="oe-border" />
            </div>
        );
        return ctrl;
    };

    private renderDropDown(): JSX.Element {
        let op: IKeyText[] = this.props.options ? this.props.options : new Array<IKeyText>();

        let val:IKeyText|undefined = this.props.options ? 
            linq.from<IKeyText>(this.props.options).where(x=>x.key == this.props.value).firstOrDefault() : undefined;

        let ctrl = (
            this.props.preview ?
                <LControl id={this.props.id} type={EControlType.info} label={val ? val.text : MainTexts.na} title={this.props.label} />
                :            
                <div className="oe-select">
                    <select name={this.props.name}
                        defaultValue={this.props.value}
                        id={this.props.id} className="selectbox" onChange={this.onChange}>
                        {op.map((i, idx) => 
                            <option selected={i.key === this.props.value} value={i.key} key={idx}>{i.text}</option>
                        )}
                    </select>
                </div>
        );

        return ctrl;
    };

    private onKeyDown(e: React.KeyboardEvent<HTMLDivElement>) {
        if (e.key == "Enter") {
            if (this.props.onEnter) {

                this.props.onEnter(e);
                e.preventDefault();
                e.stopPropagation();
                return;
            }
        }
    };

    private onChange = (e: any) => {
        if (this.props.disabled) {
            return;
        };

        let val: IControlValue = {
            id: this.props.id ? this.props.id : '',
            name: this.props.name ? this.props.name : '',
            type: this.props.type,
            targetName: this.props.targetName
        };

        if (this.props.type === EControlType.checkbox || this.props.type === EControlType.radio || this.props.type ===EControlType.toggle) {
            val.value = e.target.checked ? "1" : "0";
            val.checked = e.target.checked;
        } else {
            val.value = e.target.value;
        };

        if(this.props.trimValue) {
            val.value = val.value.toString().trim();
        };

        if(this.props.valueType) {
            if(this.props.valueType == EValueType.numberInt) {
                val.value = Number.parseInt(val.value);
            }
        };

        this.value = val;

        if (this.rules.length > 0) {
            if (!this.validate(undefined, true)) { }
        };

        if (this.props && this.props.onChange) {
            this.props.onChange(val); //e.target);
            //e.stopPropagation();
            //e.preventDefault();
        };
    };
};

export default LControl;