import * as React from 'react';
import { IBlockConfiguration } from '../../models/block/configuration';
import BlockBase from '../../components/page/block';
import { Container, Row, Col } from 'react-bootstrap';
import * as page from '../../components/page/index';
import { MainTexts } from '../../utils/messages';
import { ELButtonType, EControlType, IFilter } from '../../models';
import EasyRouter from '../../utils/router';
import { IControlValue, IKeyText, IKeyTextNumber } from '../../models/page/interfaces';
import { ELElementStatus, EValueType } from '../../models/page/diverse';
import Global from '../../utils/Global';
import { Lists } from '../../utils/Defaults';
import { EDateInputMode } from './date';

export enum EFilterType {
    default = 0
};

export interface IFilterProps {
    id: string;
    type: EFilterType;
    onChange?: any;
    onApply?: any;
    optionalButtons?: true;
    optionalButtonSelection?: any;
    title: string;
    to?: Date;
    from?: Date;
    orderManagerFilter?: boolean;
    placeholderSearch?: string;
    filter: IFilter;
    hideDateFilter?: boolean;
    actionButtons?: JSX.Element | JSX.Element[],
    searchTitle?:string;
};

export interface IFilterState {
    filter: IFilter;
    inProcessing: boolean;
    completed: boolean;
    all: boolean;

};

export class Filter extends React.Component<IFilterProps, IFilterState>{

    constructor(props) {
        super(props);

        this.onChange = this.onChange.bind(this);
        this.onApply = this.onApply.bind(this);
        this.onButtonChange = this.onButtonChange.bind(this);

         this.state = {
            filter: this.props.filter ? this.props.filter : { needle: '' },
             inProcessing: false,
             completed: false,
             all: false
         };
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: this.props.title,
            transparent: false
        };

        let to: Date = new Date();
        let from: Date = new Date();



        let filter = (
            <BlockBase config={cfg}>
                <Container>
                    {false && this.props.orderManagerFilter===true? 
                    <>
                        <Row>
                            <Col xs={2} md={2} >
                                <page.LLabel text={"Brand:"}/>
                            </Col>

                            <Col xs={2} md={2} >
                            <page.LButton type={ELButtonType.normal}
                                        css="a1 fr"
                                        status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                        id="otelo"
                                        text={"Otelo"} 
                                        onClick={""} />
                            </Col>
                            <Col xs={2} md={2} >
                            <page.LButton type={ELButtonType.normal}
                                        css="a1 fr"
                                        status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                        id="Kabel"
                                        text={"Kabel"} 
                                        onClick={""} />
                            </Col>
                            <Col xs={2} md={2} >
                            <page.LButton type={ELButtonType.normal}
                                        css="a1 fr"
                                        status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                        id="Vodafone"
                                        text={"Vodafone"} 
                                        onClick={""} />
                            </Col>
                            <Col xs={2} md={2} >
                            <page.LButton type={ELButtonType.normal}
                                        css="a1 fr"
                                        status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                        id="DSL"
                                        text={"DSL"} 
                                        onClick={""} />
                            </Col>
                            <Col xs={2} md={2} >
                            <page.LButton type={ELButtonType.normal}
                                        css="a1 fr"
                                        status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                        id="Alle"
                                        text={"Alle"} 
                                        onClick={""} />
                            </Col>
                        </Row>
                    
                        <Row>
                        <Col xs={2} md={2} >
                            <page.LLabel text={"Status:"}/>
                        </Col>

                        <Col xs={3} md={3} >
                        <page.LButton type={ELButtonType.normal}
                                    css="a1 fr"
                                    status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                    id="Offen"
                                    text={"Offen"} 
                                    onClick={""} />
                        </Col>
                        <Col xs={3} md={3} >
                        <page.LButton type={ELButtonType.normal}
                                    css="a1 fr"
                                    status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                    id="abgeschlossen"
                                    text={"Abgeschlossen"} 
                                    onClick={""} />
                        </Col>
                        <Col xs={3} md={3} >
                        <page.LButton type={ELButtonType.normal}
                                    css="a1 fr"
                                    status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                    id="Alle"
                                    text={"Alle"} 
                                    onClick={""} />
                        </Col>

                        </Row>
                    
                        <Row>
                    <Col xs={2} md={2} >
                    <page.LLabel text={"Type:"}/>
                    </Col>

                    <Col xs={2} md={2} >
                    <page.LButton type={ELButtonType.normal}
                                css="a1 fr"
                                status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                id="bnt"
                                text={"BNT"} 
                                onClick={""} />
                    </Col>
                    <Col xs={2} md={2} >
                    <page.LButton type={ELButtonType.normal}
                                css="a1 fr"
                                status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                id="mnp"
                                text={"MNP"} 
                                onClick={""} />
                    </Col>
                    <Col xs={2} md={2} >
                    <page.LButton type={ELButtonType.normal}
                                css="a1 fr"
                                status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                id="vvl"
                                text={"VVL/TW"} 
                                onClick={""} />
                    </Col>
                    <Col xs={2} md={2} >
                    <page.LButton type={ELButtonType.normal}
                                css="a1 fr"
                                status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                id="optionsbuchung"
                                text={"Optionsbuchung"} 
                                onClick={""} />
                    </Col>
                    <Col xs={2} md={2} >
                    <page.LButton type={ELButtonType.normal}
                                css="a1 fr"
                                status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                id="Alle"
                                text={"Alle"} 
                                onClick={""} />
                    </Col>

                    </Row>
                    </>
                    : <></>}
                  { this.props.optionalButtons===true ? 
                    <Row>
                        <Col xs={4} md={4} >
                            <page.LButton type={ELButtonType.normal}
                                css="a1 fr"
                                status={this.state.inProcessing? ELElementStatus.selected: ELElementStatus.normal}
                                id="In Bearbeitung"
                                text={"In Bearbeitung"} 
                                onClick={()=>{this.onButtonChange(event)}} />
                        </Col>
                        <Col xs={4} md={4} >
                            <page.LButton type={ELButtonType.normal}
                                css="a1 fr"
                               status={this.state.completed? ELElementStatus.selected: ELElementStatus.normal}
                                id="Abgeschlossen"
                                text={"Abgeschlossen"}
                                onClick={()=>{this.onButtonChange(event)}} />
                        </Col>
                        <Col xs={4} md={4} >
                            <page.LButton type={ELButtonType.normal}
                                css="a1 fr"
                                status={this.state.all? ELElementStatus.selected: ELElementStatus.normal}
                                id="Alle"
                                text={"Alle"}
                                onClick={()=>{this.onButtonChange(event)}} />
                        </Col>
                    </Row>
                    : <></>
                }

                    <Row>
                        {this.props.hideDateFilter === true ? <></> :
                            <Col xs={6} md={6} className="np">
                                <Container>
                                    <Row>
                                        <Col xs={5} md={5} className="np">
                                            <page.LDate value={this.props.filter.from}
                                                datePicker = {true}
                                                    onChange={this.onChange}
                                                    title={MainTexts.periodFrom}
                                                    id={this.props.id + '.from'}
                                                    name="from"
                                                    style="middle" />
                                        </Col>
                                        <Col xs={2} md={2}></Col>
                                        <Col xs={5} md={5} className="np">
                                            <page.LDate 
                                                datePicker={true}
                                                value={this.props.filter.to}
                                                onChange={this.onChange}
                                                title={MainTexts.to}
                                                style="middle"
                                                id={this.props.id + '.to'}
                                                name="to" />
                                        </Col>   
                                    </Row>    
                                    {
                                        this.props.orderManagerFilter ? 
                                        <Row>
                                            <Col>
                                                <page.LControl type={EControlType.dropdown} 
                                                    value={this.props.filter.timeFilter}
                                                    onChange={this.onChange} 
                                                    valueType={EValueType.numberInt}
                                                    preview={false}
                                                    name="timeFilter" 
                                                    id="timeFilter" 
                                                    title={MainTexts.timeFilter}
                                                    options={this.getTimeFilterOptions()} />                                        
                                            </Col>
                                        </Row>   
                                        : null                                     
                                    }
                                </Container>                         
                            </Col>
                        }
                        <Col xs={1} md={1} className="npr"></Col>
                        <Col xs={5} md={5} className="npr">
                            <Container>
                                <Row>
                                    <Col className="npr">
                                        <page.LControl
                                            value={this.props.filter.needle}
                                            type={EControlType.text}
                                            onChange={this.onChange}
                                            onEnter={this.onApply}
                                            title={this.props.searchTitle !== undefined ? this.props.searchTitle : MainTexts.searchTitle}
                                            style="roundinner"
                                            label={undefined}
                                            hideLabel={true}
                                            placeholder={this.props.placeholderSearch}
                                            id={this.props.id + '.needle'}
                                            name="needle" />
                                    </Col>
                                </Row>
                                {
                                    this.props.orderManagerFilter ? this.renderCats()  : null
                                }
                            </Container>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={6} md={6} className="np">
                            
                        </Col>
                        <Col xs={6} md={6} className="np">
                            {this.props.actionButtons !== undefined ? <div className="filterAction">{this.props.actionButtons}</div> :
                                <div className="filterAction">
                                    <page.LButton text={MainTexts.toDashboard}
                                        css="fr"
                                        onClick={() => { EasyRouter.route({ url: '/' }); }}
                                        type={ELButtonType.buttonWithIconRight}
                                        icon="arrow-right" />
                                    <page.LButton text={this.props.optionalButtons? "Filtern": MainTexts.apply}
                                        css="fr"
                                        onClick={this.onApply}
                                        type={ELButtonType.buttonWithIconRight}
                                        icon="expand" />
                                </div>
                            }
                        </Col>
                    </Row>
                </Container>
                <></>
            </BlockBase>
        );

        let ctrl = (
            <page.Inner1C>
                <page.Inner2CLeft>
                    <>
                        {filter}
                    </>
                </page.Inner2CLeft>
                <></>
            </page.Inner1C>
        );

        return ctrl;
    };

    private renderCats() {
        console.log('filter', this.props.filter);
        let all:JSX.Element[] = [];

        Lists.brandCats.map((i, idx)=>{
            let cb = <page.LControl 
                type={EControlType.checkbox}
                checked={this.props.filter.cats && this.props.filter.cats.indexOf(i.key)>=0}
                label={i.text}
                hideTitle={true}
                name=""
                value={i.key}
                onChange={this.onChange}
                key={"cb-cat-" + i.key}
                id={"cats-" + i.key}/>

            all.push(cb);
        })

        return all;
    };

    private getTimeFilterOptions(): IKeyTextNumber[] {
        let op: IKeyTextNumber[] = new Array();
        op.push({ key: 0, text: "Alle" });
        op.push({ key: 1, text: "Aktuelle Woche" });
        op.push({ key: 2, text: "Aktueller Monat" });
        op.push({ key: 3, text: "Letzter Monat" });
        op.push({ key: 4, text: "Letzte drei Monate" });

        return op;
    };

    private onChange(val: IControlValue) {
        let f = this.props.filter;
        if (val.type === EControlType.date) {
            f[val.name] = val.dateValue;
        } else {
            f[val.name] = val.value;
        }

        if(val.id.startsWith('cats')) {
            let cats = f.cats;
            let id = val.id.replace('cats-', '');
            let idx = cats!.indexOf(id);

            if(idx >=0 ) {
                cats!.splice(idx, 1);
            } else {
                cats!.push(id);
            }

            f['cats'] = cats;
        };

        this.setState({ filter: f });

        if (this.props.onChange) {
            this.props.onChange(f);
        };
    };

    private onButtonChange(e: any){
        switch(e.target.id) {
            case 'In Bearbeitung':
                this.props.optionalButtonSelection(e.target.id);
               this.setState({inProcessing:true,completed:false,all:false});
              break;
            case 'Abgeschlossen':
                this.props.optionalButtonSelection(e.target.id);
                this.setState({completed:true,inProcessing:false,all:false});
              break;
            case 'Alle':
                this.props.optionalButtonSelection(e.target.id);
                this.setState({all:true,completed:false,inProcessing:false});
            break;
          }
    }

    private onApply() {
        if (this.props.onApply) {
            this.props.onApply(this.props.filter);
        };
    };
};

export default Filter;