    public static containsAny<T>(target: T[], source: T[]): boolean {
        if (target && source) {
            for (let i = 0; i < source.length; i++) {
                if (target.indexOf(source[i]) >= 0) {
                    return true;
                };
            };
        };

        return false;
    };

    public static getAgeFromDate(dateOfBirth: any): number {
        let today = new Date();
        let bday = new Date(dateOfBirth);
        let age: number = Math.floor((today.getTime() - new Date(dateOfBirth).getTime()) / 3.15576e+10);

        if (age === 18 && bday.getMonth() + 1 > today.getMonth() + 1 && bday.getDate() > today.getDate()) {
            age--;
        } else if (age === 18 && bday.getMonth() + 1 === today.getMonth() + 1 && bday.getDate() > today.getDate()) {
            age--;
        }else if(age === 18 && bday.getMonth() + 1 > today.getMonth() + 1 && bday.getDate() === today.getDate()){
            age--;
        }

        if (age === 16 && bday.getMonth() + 1 > today.getMonth() + 1 && bday.getDate() > today.getDate()) {
            age--;
        } else if (age === 16 && bday.getMonth() + 1 === today.getMonth() + 1 && bday.getDate() > today.getDate()) {
            age--;
        }else if(age === 16 && bday.getMonth() + 1 > today.getMonth() + 1 && bday.getDate() === today.getDate()){
            age--;
        }

        // if (age === 27 && bday.getMonth() + 1 > today.getMonth() + 1 && bday.getDate() > today.getDate()) {
        //     debugger
        //     age--;
        // } else if (age === 27 && bday.getMonth() + 1 === today.getMonth() + 1 && bday.getDate() > today.getDate()) {
        //     debugger
        //     age--;
        // }else if(age === 27 && bday.getMonth() + 1 > today.getMonth() + 1 && bday.getDate() === today.getDate()){
        //     debugger
        //     age--;
        // }
        return age;
    }

