    public static inRole(role: string): boolean {
        let usr = StorageHelper.getUser();
        if (usr) {
            let r = linq.from<string>(usr.roles).where(x => x === role).firstOrDefault();

            return r ? true : false;
        }
        return false;
    };

    public static inAnyRole(roles: string[]): boolean {
        let usr = StorageHelper.getUser();

        if (usr) {
            let r = linq.from<string>(usr.roles).where(x => roles.indexOf(x) >= 0).toArray();

            return r && r.length > 0 ? true : false;
        }
        return false;
    };

    public static checkAccessRoles(roles: string[]): boolean {
        if (!Global.inAnyRole(roles)) {
            return false;
        };

        return true;
    };

    public static validateAccessRoles(roles: string[]): boolean {
        const result = Global.checkAccessRoles(roles);

        if (!result) {
            App.setContainer(<ErrorScreen data={{ code: 403 }} />, AppIds.container);
        };

        return result;
    };

    public static isDelaer(redirect: boolean): boolean {
        const result = Global.inAnyRole([VodafonePositions.Vertriebsbeauftragter, 
            VodafonePositions.Vertriebsleiter, 
            VodafonePositions.Niederlassungsleiter, 
            VodafonePositions.Niederlassungssupport,
            VodafonePositions.Hotline,
            VodafonePositions.Backoffice, 
            VodafonePositions.Hauptabteilungsleiter
        ]);

        if (result && redirect) {
            App.setContainer(<ErrorScreen data={{ code: 403.1 }} />, AppIds.container);
        };

        return !result;
    };

