    public static getProductById(id: string): IProduct | undefined {
        let brands = StorageHelper.getBrands();

        for (let i = 0; i < brands.length; i++) {
            var p = linq.from<IProduct>(brands[i].products)
                .where(x => x.id === id)
                .firstOrDefault();

            if (p !== null) {
                return p;
            }
        }

        return undefined;
    };

    public static getDaysInMonth(value: Date): number {
        let m: number = value.getMonth() + 1;
        if (m == 2) {
            if (Global.isLeapYear(value.getFullYear())) {
                return 29;
            } else {
                return 28;
            };
        } else if (m == 4 || m == 6 || m == 9 || m == 11) {
            return 30;
        } else {
            return 31;
        };
    };

    public static isLeapYear(year) {
        return ((year % 4 == 0) && (year % 100 != 0)) || (year % 400 == 0);
    }

