    public static toGermanDate(value: Date | undefined): string {
        if (!value) { return '' };

        let inner: Date = new Date(value);
        let dd: number = inner.getDate();
        let mm: number = inner.getMonth() + 1;
        let yyyy: number = inner.getFullYear();
        let d: string = dd.toString();
        let m: string = mm.toString();

        if (dd < 10) {
            d = '0' + dd;
        }

        if (mm < 10) {
            m = '0' + mm;
        }

        return d + '.' + m + '.' + yyyy;
    };

    public static toGermanDateL(value: DateValue): string {
        let d = new Date(value.year, value.month, value.day);
        return Global.toGermanDate(d);
    };

    public static fromGermanDateString(value: string): Date | undefined {
        try {
            let sa = value.split('.');

            if (sa.length != 3) {
                throw new Error('not a date');
            };

            let d = new Date(Number.parseInt(sa[2]), Number.parseInt(sa[1]) - 1, Number.parseInt(sa[0]));

            return d;
        } catch (e) {
            console.error(e);
            return undefined;
        }
    };

    public static formatNumber(value: number, n: number, x: number, unit: string) {
        var re = '\\d(?=(\\d{' + (x || 3) + '})+' + (n > 0 ? '\\.' : '$') + ')';
        var res = value.toFixed(Math.max(0, ~~n)).replace(new RegExp(re, 'g'), '$&,');
        if (unit) {
            res += ' ' + unit;
        }
        return res;
    };

    public static removeElementFromArray(arr: string[], value: string) {
        if (arr) {
            let idx = arr.indexOf(value);
            if (idx >= 0) {
                arr.splice(idx, 1);
            }
        }
    };

