import { OrderingBase } from "..";
import ModelState from "./modelState";
import * as m from '../../models/index';
import ValidationWrapper from "./validationWrapper";
import OptionGroup from "../../components/process/optionGroup";
import Global from "../Global";
import { MainTexts, ValidationMessages } from "../messages";

export class OrderingCable extends OrderingBase {
    public validateOrder(act: m.IActivation, modelState: ModelState, data: m.IOrderData | undefined) {
        super.validate(act, modelState, data);

        let groups = ValidationWrapper.getControls('container-main', 'optiongroup');

        for(let i = 0; i<groups.length; i++) {
            let gr:OptionGroup = Global.FindReact(groups[i]);

            if(!gr.validate()) {
                Global.FocusComponent('hardwareSelection');

                modelState.errorMessage = ValidationMessages.errorOption;
                break;
            };
        };
        
        if(act.services.indexOf('kip-aio-marker')>=0) {
            if(act.services.indexOf('kip-aio-marker/mobile-euro-flat') < 0 && act.services.indexOf('kip-aio-marker/mobile-international-flat') < 0 && act.services.indexOf('kip-aio-marker/mobile-international-allnet-flat') < 0 ) {
                modelState.errorMessage = "Einer der Optionen ('Mobile & Euro-Flat', 'Mobile & International-Flat' oder 'Mobile & International-Allnet-Flat') muss ausgewhlt sein.";
            };
        };

        if(act.cart.length > 0) {
            modelState.errorMessage = ValidationMessages.errorCart;
        };
    };
};

export default OrderingCable;