import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { PopupBody } from '../../components';
import Popup, { EPopupType } from '../../components/dialogs/popup';
import BlockBase from '../../components/page/block';
import { EFilterType } from '../../components/page/filter';
import * as page from '../../components/page/index';
import { EPagingType, IColumn } from '../../components/page/table';
import { IApiResponse, IApiSummary } from '../../interfaces';
import IAppDefinition from '../../interfaces/app/IAppDefinition';
import { IBlockConfiguration } from '../../models/block/configuration';
import * as m from '../../models/index';
import { ELButtonType } from '../../models/index';
import Api from '../../utils/api';
import { Roles } from '../../utils/Defaults';
import Global from '../../utils/Global';
import { MainTexts } from '../../utils/messages';
import DealerScreen, { DealerModel } from '../user/dealer';
import { EditUserScreen } from './editUser';

export enum AdministrationEditType {
    none,
    editAccount,
    editContact
}

export interface IAdministrationScreenProps {

}
export interface IAdministrationScreenState {
    changed: boolean;
    expanded: boolean;
    api?: IApiResponse
    processing: boolean;
    apiMessage: string;
    accountMainPreviewData?: DealerModel;
    editType: AdministrationEditType;
    editContactId: number;
    closablePopup: boolean;
}

export class AdministrationScreen extends React.Component<IAdministrationScreenProps, IAdministrationScreenState> {
    public static containerId: string = 'administrationScreen';
    private processPopupState: EPopupType = EPopupType.pleaseWait;

    readonly globalCfg: IAppDefinition = Global.getConfiguration();
    readonly cfg: IBlockConfiguration = {
        title: MainTexts.mainData,
        transparent: false,
        styleContainer: ''
    };
    readonly cfg2: IBlockConfiguration = {
        title: MainTexts.filter,
        transparent: false,
        styleContainer: ''
    };

    cols: IColumn[] = [
        {
            label: "Name",filterable: true, binding: 'name', size: 3, sortable: true, style: 'text-button', renderer: (r, c) => {
                return <>
                    <div className="contactsGrid-contact-icon" />
                    <div className="text-button">{r.name}</div>

                </>;
            }
        },
        { label: "Void", filterable: true, binding: 'voId', size: 2, sortable: true },
        {
            label: MainTexts.email, filterable: true, binding: 'email', size: 3, sortable: true, renderer: (r, c) => {
                return <a href={"mailto:" + r.email}>{r.email}</a>;
            }
        },
        { label: MainTexts.mobilePhoneNumber, filterable: true, binding: 'mobile', size: 3, sortable: true },
        {
            label: '', binding: '', size: 1, sortable: true,
            renderer: (c, r) => {
                if (Global.checkAccessRoles([Roles.XTND_ShopManager,Roles.XTND_Nutzerverwaltung])) {
                    return <>
                        <page.LButton type={ELButtonType.iconImage}
                            icon="edit"
                            clickModel={c}
                            onClickEvent={(e, m) => {
                                e.preventDefault();
                                e.stopPropagation();
                                this.editContact(m);
                                return false;
                            }} />
                    </>;
                } else {
                    return <></>;
                }

            },
            rendererHead: (c) => {
                return <>
                    <page.LButton type={ELButtonType.iconImage}
                        icon="expand"
                        onClick={this.expand} />
                </>;
            }
        },];

    readonly filterActionButtons: JSX.Element[] = [<page.LButton text={MainTexts.doFilter}
        css="fr"
        onClick={() => {
            this.applyFilter(this.filter);
        }}
        type={ELButtonType.buttonWithIconRight} />];

    private filter: m.IFilter = { needle: '', from: new Date(), to: new Date() };

    constructor(props) {
        super(props);

        this.applyFilter = this.applyFilter.bind(this);
        this.expand = this.expand.bind(this);
        this.contactsRecieved = this.contactsRecieved.bind(this);
        this.loadAccountCallback = this.loadAccountCallback.bind(this);
        this.edit = this.edit.bind(this);
        this.loadAccount = this.loadAccount.bind(this);
        this.closeEditScreen = this.closeEditScreen.bind(this);
        this._onNewUserClick = this._onNewUserClick.bind(this);

        Global.easyApp.setTitle(MainTexts.administration);

        this.state = { changed: false, expanded: false, processing: true, apiMessage: '', editType: AdministrationEditType.none, editContactId: 0,closablePopup:false };

        if (Global.validateAccessRoles([Roles.XTND_Nutzerverwaltung,Roles.XTND_ShopManager])) {
            Global.hub.send('', m.ERequestType.getContacts, undefined);
            this.loadAccount();
        }
    }

    render() {
        if (Global.checkAccessRoles([Roles.XTND_ShopManager,Roles.XTND_Nutzerverwaltung])) {
            if (this.state.editType === AdministrationEditType.editAccount) {
                return <DealerScreen parent={this} model={undefined} />;
            } else if (this.state.editType === AdministrationEditType.editContact) {
                return <EditUserScreen contactId={this.state.editContactId} />;
            }
        }

        let newUsrBtn = <></>;

        if (Global.checkAccessRoles([Roles.XTND_ShopManager,Roles.XTND_Nutzerverwaltung])) {
            newUsrBtn = <div className="col-md-12 col-12">
                <page.LButton
                    text={MainTexts.newUser}
                    onClick={this._onNewUserClick}
                    type={ELButtonType.buttonWithIconRight}
                    css={"action fr"}
                    icon="chevron" />
            </div>;
        }

       

        const filter = <page.Filter
            searchTitle={''}
            actionButtons={this.filterActionButtons}
            filter={this.filter}
            id="filter"
            hideDateFilter={true}
            type={EFilterType.default}
            title={MainTexts.employee}
            onApply={this.applyFilter}
            placeholderSearch="z.B. Name, VOID, E-Mail oder Rufnummer" />
        // data={this.state && this.state.api ? this.state.api.data.data : undefined} />;

        let pagingProps = { page: 1, count: this.globalCfg.pageSize, type: EPagingType.none }; // no paging by pptx
        let table = <page.Table coulmns={this.cols} id={AdministrationScreen.containerId + 'Table'}
            expnaded={this.state.expanded}
            // itemClick={this.itemClick}
            pagingProps={pagingProps}
            filter={this.filter}
            data={this.state.api !== undefined && this.state.api.data !== undefined && this.state.api.data!==null ? this.state.api.data.data : undefined} />;

        return <div id={AdministrationScreen.containerId}>
            {this._renderPopup()}
            {this.state.accountMainPreviewData !== undefined ? <page.Inner1C>
                <BlockBase config={this.cfg}>
                    <Container>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                {this.state.accountMainPreviewData.address.additional}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                {this.state.accountMainPreviewData.address.street} {this.state.accountMainPreviewData.address.nr}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                {this.state.accountMainPreviewData.address.zip} {this.state.accountMainPreviewData.address.city}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                {this.state.accountMainPreviewData.tel.countryCode} {this.state.accountMainPreviewData.tel.prefix} {this.state.accountMainPreviewData.tel.number}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={4} md={4}>
                                <page.LControl type={m.EControlType.text}
                                    value={this.state.accountMainPreviewData.voId}
                                    name="voId" id="voId"
                                    label={"VOID"}
                                    preview={true} />
                            </Col>
                            <Col xs={4} md={4}>
                                <page.LControl type={m.EControlType.text}
                                    value={this.state.accountMainPreviewData.fnvoid}
                                    name="fnvoid" id="fnvoid"
                                    label={MainTexts.fnvoid}
                                    preview={true} />
                            </Col>
                            <Col xs={4} md={4}>
                                <page.LControl type={m.EControlType.text}
                                    value={this.state.accountMainPreviewData.vpkn}
                                    name="vpkn" id="vpkn"
                                    label={MainTexts.vpkn}
                                    preview={true} />
                            </Col>
                        </Row>
                        {Global.checkAccessRoles([Roles.XTND_ShopManager,Roles.XTND_Nutzerverwaltung]) ?
                            <Row>
                                <Col xs={12} md={12} >
                                    <page.LButton text={MainTexts.edit}
                                        css="fr"
                                        onClick={this.edit}
                                        type={ELButtonType.buttonWithIconRight} />
                                </Col>
                            </Row> : <></>}

                    </Container>
                </BlockBase>
            </page.Inner1C> : <></>}

            {filter}



            <div className={'c1 container ' + (this.state.expanded ? 'wide' : '')}>
                <div className="row">
                    {newUsrBtn}
                </div>
                <div className="row">
                    <div className="col-md-12 col-12">
                        {table}
                    </div>
                </div>
            </div>
        </div>;
    };

    contactsRecieved(res: IApiResponse) {
        if (res.error) {
            let sum: IApiSummary = Api.parseSummary(res, undefined);
            this.processPopupState = EPopupType.custom;
            this.setState({ changed: true, processing: true,closablePopup:true, api: res, apiMessage: sum.message });
        } else {
            this.setState({ changed: true, processing: false,closablePopup:false ,api: res });
        }
    };

    loadAccountCallback(res: IApiResponse) {
        if (res.error) {
            let sum: IApiSummary = Api.parseSummary(res, undefined);
            this.processPopupState = EPopupType.custom;
            this.setState({ changed: true, processing: false, api: res, apiMessage: sum.message });
        } else {
            const dataRaw: any = res.data.data;
            const dealer = dataRaw.dealer;

            let model: DealerModel = {
                tel: { countryCode: dealer.tel.cc, prefix: dealer.tel.ndc, number: dealer.tel.sn, telType: m.ETelType.home },
                address: { city: dealer.address.city, zip: dealer.address.zip, street: dealer.address.street, nr: dealer.address.nr, house_number: '', additional: '', corrected: false },
                fnvoid: dataRaw.fnvoid,
                vpkn: dataRaw.vpkn,
                voId: dataRaw.voId
            };

            this.setState({ accountMainPreviewData: model, changed: true, processing: false });
        }
    };

    closeEditScreen() {
        this.setState({ editType: AdministrationEditType.none });
    }

    private _onNewUserClick(ev) {
        this.setState({ editType: AdministrationEditType.editContact, editContactId: 0 });
    }

    private applyFilter(f) {
        this.filter = f;
        this.setState({ changed: true });
    }

    private expand() {
        this.setState({ expanded: !this.state.expanded });
    }

    private editContact(record) {
        if (record && record.id) {
            this.setState({ editType: AdministrationEditType.editContact, editContactId: record.id });
        }
    }

    private edit() {
        if (this.state.accountMainPreviewData !== undefined) {
            this.setState({ editType: AdministrationEditType.editAccount });
        }
    };

    _renderPopup = () => {
        return <Popup
            type={this.processPopupState}
            visible={this.state.processing || this.state.closablePopup}
            closable={this.state.closablePopup}
            id="wait"
            title={MainTexts.pleaseWait}
            height={200}
            onDismiss={this._onDismissPopup}>
            <PopupBody>
                <div>{this.state.apiMessage}</div>
                <></>
            </PopupBody>
            <></>
        </Popup>
    }

    _onDismissPopup = () => {
        this.setState({ processing: false, closablePopup: false });
    }


    private loadAccount() {
        const model = { id: '' };
        Global.hub.send(JSON.stringify(model), m.ERequestType.loadAccount, undefined);
    }
}

export default AdministrationScreen;