import linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBaseComponentState } from '../../../interfaces';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { ICustomer } from '../../../models/ordering/customer';
import { ESalutation } from '../../../models/ordering/order';
import { EControlType } from '../../../models/page/diverse';
import { IControlValue, IKeyText, IListItem } from '../../../models/page/interfaces';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { MainTexts, OrderingTexts } from '../../../utils/messages';
import StorageHelper from '../../../utils/storage';
import BelatedPorting from '../../existing/data/Belatedporting';
import BaseBlock from '../../page/block';
import { EDateType } from '../../page/date';
import * as page from '../../page/index';
import Person from '../global/person';
import ActivationCustomerData from '../process/activationCustomerData';

export interface ICustomerMnpProps {
    parent: ActivationCustomerData | BelatedPorting;
    onChange: any;
    //mnp?: m.IMnp;
    customer: ICustomer;
    preview: boolean;
};

export interface ICustomerMnpState extends IBaseComponentState {
    customer: ICustomer;
}

export class CustomerMnp extends React.Component<ICustomerMnpProps, ICustomerMnpState> {
    //private mnp: m.IMnp;
    private all: IListItem[];

    constructor(props: ICustomerMnpProps) {
        super(props);

        this.onChange = this.onChange.bind(this);

        this.all = StorageHelper.getSupport().networks;

        //this.mnp = this.props.mnp ? this.props.mnp : { provider: '', network: '', differentCustomer: false, birthday: new Date() };

        let customer = this.props.customer;

        if (!customer.mnp) {
            customer.mnp = { provider: '', network: '', differentCustomer: false, birthday: new Date() };
            customer.mnp.differentCustomer = false;
            customer.mnp.birthday = new Date();
            customer.mnp.cancellationDate = new Date();
            customer.mnp.contractEndDate = new Date();


            customer.bew
        };

        if(customer.mnp && !customer.mnp.cancellationDate){
            customer.mnp.cancellationDate = new Date();
        }
        if(customer.mnp && !customer.mnp.contractEndDate){
            customer.mnp.contractEndDate = new Date();
        }

        this.state = {
            changed: false,
            customer: customer
        };
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.mnpContract,
            transparent: false
        };

        let portBrand = this.props.parent.props.parent.dataWrapper.getPortationBrand();

        let inCdChange = this.props.parent.props.parent.dataWrapper.hasCdChange();

        let network = <></>;

        if (portBrand === BrandKeys.otelo && this.props.parent instanceof ActivationCustomerData) {
            network = <page.LControl type={EControlType.dropdown} value={this.state.customer.mnp ? this.state.customer.mnp.network : ''}
                preview={this.props.preview}
                onChange={this.onChange} name="mnp.network" id="network" title={OrderingTexts.mnpNetwork}
                options={this.getNetworks(portBrand)} />
        } else if (this.props.parent instanceof BelatedPorting) {
            network = <page.LControl type={EControlType.dropdown} value={this.state.customer.mnp ? this.state.customer.mnp.network : ''}
                preview={this.props.preview}
                onChange={this.onChange} name="mnp.network" id="network" title={OrderingTexts.mnpNetwork}
                options={this.getNetworks(portBrand)} />
        };

        const ctrl =
            <>
                <Row>
                    {network}

                    <Col xs={6} md={6} className="npr">
                        <page.LControl type={EControlType.dropdown} value={this.state.customer.mnp ? this.state.customer.mnp.provider : ''}
                            preview={this.props.preview}
                            onChange={this.onChange} name="mnp.provider" id="provider" title={OrderingTexts.mnpProvider}
                            options={this.getProviders(portBrand)} />
                    </Col>
                </Row>

                {
                    (portBrand !== BrandKeys.dsl && portBrand !== BrandKeys.kabel) && this.props.parent instanceof ActivationCustomerData ?
                        <Row>
                            <Col xs={6} md={6} className="np">
                                <page.LControl type={EControlType.radio}
                                    checked={this.state.customer.mnp && !this.state.customer.mnp.differentCustomer}
                                    preview={this.props.preview}
                                    onChange={this.props.onChange} name="mnp.differentCustomer" id="differentCustomer_0"
                                    title={OrderingTexts.mnpSameCustomer} />
                            </Col>
                            <Col xs={6} md={6} className="npr">
                                <page.LControl type={EControlType.radio}
                                    checked={this.state.customer.mnp && this.state.customer.mnp.differentCustomer}
                                    preview={this.props.preview}
                                    onChange={this.props.onChange} name="mnp.differentCustomer" id="differentCustomer_1"
                                    title={OrderingTexts.mnpDiffCustomer} />
                            </Col>
                        </Row>
                        : null
                }

                {
                    (portBrand !== BrandKeys.dsl) && this.props.parent instanceof ActivationCustomerData && (this.props.parent.props.parent.dataWrapper.hasVodafoneWithMnp() || this.props.parent.props.parent.dataWrapper.hasCableWithMnp()) ?
                        <Row>
                            {
                                portBrand == BrandKeys.kabel && this.state.customer.mnp && this.state.customer.mnp.provider !== 'DEU.VFDE' ?
                                    null :
                                    <Col xs={6} md={6} className="np">
                                        <page.LControl type={EControlType.text}
                                            value={this.state.customer.mnp ? this.state.customer.mnp.customerNr : ''}
                                            preview={this.props.preview}
                                            onChange={this.onChange} name="mnp.customerNr" id="customerNr"
                                            title={OrderingTexts.mnpCustomerNr}
                                            options={this.getProviders(portBrand)} />
                                    </Col>
                            }
                            {
                                portBrand !== BrandKeys.kabel ?
                                    <>
                                        <Col xs={6} md={6} className="npr">
                                            {this.props.parent.props.parent.dataWrapper.hasVodafoneWithMnpANDPortierungandASAP() ? <></> : <page.LDate onChange={this.props.onChange}
                                                preview={this.props.preview}
                                                datePicker={true}
                                                value={this.state.customer.mnp ? this.state.customer.mnp.contractEndDate : new Date()}
                                                from={Global.dateOnly(Global.addDays(new Date(), 0))}
                                                to={Global.dateOnly(Global.addDays(new Date(), 720))}
                                                type={EDateType.fixedStart}
                                                name="mnp.contractEndDate" id="contractEndDate"
                                                title={OrderingTexts.mnpContractEnd} />
                                            }
                                        </Col>
                                        {
                                            inCdChange ? null :
                                                <Col xs={6} md={6} className="np">
                                                    <page.LDate onChange={this.props.onChange}
                                                        preview={this.props.preview}
                                                        datePicker={true}
                                                        type={EDateType.fixedStart}
                                                        //  from={Global.dateOnly(Global.addDays(new Date(), 0))}
                                                        from={undefined}
                                                        to={Global.dateOnly(Global.addDays(new Date(), 720))}
                                                        value={this.state.customer.mnp ? this.state.customer.mnp.cancellationDate : new Date()}
                                                        name="mnp.cancellationDate" id="cancellationDate" title={OrderingTexts.mnpCancellationDate} />
                                                </Col>
                                        }

                                    </> : null
                            }
                        </Row>
                        : null
                }

                {
                    (portBrand !== BrandKeys.dsl && portBrand !== BrandKeys.kabel) && this.props.parent instanceof ActivationCustomerData && this.state.customer.mnp && this.state.customer.mnp.differentCustomer ?
                        <Person prefix="mnp"
                            title='Kunde'
                            showBirthday={true}
                            onChange={this.props.onChange}
                            source={this.state.customer}
                            salutation={this.state.customer.mnp && this.state.customer.mnp.salutation ? this.state.customer.mnp.salutation : ESalutation.mr}
                            firstname={this.state.customer.mnp && this.state.customer.mnp.firstname ? this.state.customer.mnp.firstname : ''}
                            surname={this.state.customer.mnp && this.state.customer.mnp.surname ? this.state.customer.mnp.surname : ''}
                            birthday={this.state.customer.mnp ? this.state.customer.mnp.birthday : new Date()} />
                        : null
                }
            </>;

        let container = <>{ctrl}</>;

        if (!(this.props.parent instanceof BelatedPorting)) {
            container = <BaseBlock config={cfg}>
                <Container>
                    {ctrl}
                </Container>
                <page.LClear />
            </BaseBlock>
        };

        return container;
    };

    private onChange(e: IControlValue) {
        let customer = this.state.customer;

        if (e && e.id == 'network' && customer.mnp) {
            customer.mnp.provider = '';
        };

        Global.onInput(customer, e, () => {
            this.setState({ customer: customer });
        });

        this.props.onChange(e);
    };

    private getNetworks(brand: string): IKeyText[] {
        let res: IKeyText[] = [{ key: "", text: MainTexts.pleaseSelect }];

        let res1 = linq.from<IListItem>(this.all)
            .where(x => x.value !== BrandKeys.vodafone)
            .orderByDescending(x => x.text)
            .select((x, i) => {
                return { key: x.value, text: x.text };
            })
            .cast<IKeyText>()
            .toArray();

        res1.map((i, idx) => { res.push(i); });

        return res;
    };

    private getProviders(brand: string): IKeyText[] {
        if (this.state.customer.mnp && !this.state.customer.mnp.network) {
            this.state.customer.mnp.network = this.all[0].value;
        };

        if ((brand === BrandKeys.vodafone || brand === BrandKeys.dsl || brand === BrandKeys.kabel) && this.state.customer.mnp) {
            this.state.customer.mnp.network = brand; //linq.from<IListItem>(this.all).where(x=>x.text === brand).first();
        };

        let res: IKeyText[] = [{ key: "", text: MainTexts.pleaseSelect }];

        let net = linq.from<IListItem>(this.all)
            .where(x => x.value == (this.state.customer.mnp ? this.state.customer.mnp.network : ''))
            .first();

        let res1 = linq.from<IListItem>(net.children)
            .select((x, i) => {
                return { key: x.value, text: x.text };
            })
            //.cast<IKeyText>()
            .toArray();

        res1.map((i, idx) => { res.push(i); });

        return res;
    };
};

export default CustomerMnp;