import * as React from 'react';
import ActivationPreview from '../process/activationPreview';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { Row, Col, Container } from 'react-bootstrap';
import BaseBlock from '../../../components/page/block';
import * as page from '../../page/index';
import { EControlType } from '../../../models/page/diverse';
import { MainTexts } from '../../../utils/messages';
import Global from '../../../utils/Global';
import { IOrderData, Address } from '../../../models';
import { ICustomer } from '../../../models/ordering/customer';
import AddressControl from '../global/address';
import PersonAndAddress from '../../../models/ordering/personWithAddress';
import { EActionAddressType } from '../../../models/ordering/order';
import Tel from '../global/tel';

export interface IPreviewDeliveryProps {
    parent: ActivationPreview;
};

export class PreviewDelivery extends React.Component<IPreviewDeliveryProps, any> {
    private delivery: PersonAndAddress;
    constructor(props: IPreviewDeliveryProps) {
        super(props);

        let customer = this.props.parent.props.parent.data.customer;
        this.delivery = new PersonAndAddress();

        if(customer.delivery) {
            if(customer.deliveryType == EActionAddressType.differentAddress) {   
                this.delivery.address = {
                    street: customer.delivery.address.street,
                    nr: customer.delivery.address.nr,
                    zip: customer.delivery.address.zip,
                    city: customer.delivery.address.city,
                    corrected: false,
                    house_number: customer.delivery.address.nr,
                    additional: ''
                };                  
            } else {
                if(customer.address)  {
                    this.delivery.address = {
                            street: customer.address.street,
                            nr: customer.address.nr,
                            zip: customer.address.zip,
                            city: customer.address.city,
                            corrected: false,
                            house_number: customer.address.nr,
                            additional: ''
                        };
                    };
            };

            this.delivery.callbackTime = customer.delivery.callbackTime;
            this.delivery.deliveryDate = customer.delivery.deliveryDate;

            this.delivery.tel = customer.delivery.tel;
        }
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: MainTexts.deliveryData,
            transparent: false
        };

        let ctrl = (
            <BaseBlock config={cfg}>
                    <Container>
                        <AddressControl prefix="address" source={this.delivery} onChange={undefined} preview={true}/>

                        <Tel prefix="tel" source={this.delivery} onChange={undefined} preview={true}/>
                    </Container>

                    <Container>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.info} title={MainTexts.telCallback}
                                        label={this.delivery.callbackTime}
                                        preview={true}
                                        onChange={undefined} name="delivery.callbackTime" id="delivery-callbackTime" />
                            </Col> 
                        </Row>                         
                        <Row>
                            <Col xs={6} md={6} className="np">
                                <page.LDate title={MainTexts.deliverydate} 
                                        datePicker = {true}
                                        from = {Global.dateOnly(Global.addDays(new Date(), 0))}
                                        to = {Global.dateOnly(Global.addDays(new Date(), 1827))}
                                        id={'deliveryDate'} 
                                        name={'delivery.deliveryDate'} 
                                        onChange={undefined}
                                        preview={true}
                                        value={this.delivery.deliveryDate}/>
                            </Col>                               
                        </Row>                     
                    </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };
};

export default PreviewDelivery;