import * as React from 'react';
import linq from 'linq';
import StorageHelper from '../../utils/storage';
import { IOrder } from '../../models/ordering/order';
import { IOrderData, IActivation } from '../../models';
import { ActivationPreview } from '../../components/ordering/process';

export interface IPreviewScreenProps {
    activationId: string;
    orderId: string;
};

export class PreviewScreen extends React.Component<IPreviewScreenProps, any> {
    constructor(props) {
        super(props);
    };

    public render() {
        let ctrl = <div>Preview</div>;

        let order = linq.from<IOrderData>(StorageHelper.getOrders())
            .where(x=>x.id == this.props.orderId)
            .firstOrDefault();
        
        if(order) {
            let act = linq.from<IActivation>(order.activations)
                .where(x=>x.id == this.props.activationId)
                .firstOrDefault()
            
            if(act) {
            }
        };

        return ctrl;
    };
};

export default PreviewScreen;