import * as React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { MainTexts, OrderingTexts, OrderProductStages } from '../../utils/messages';
import BaseBlock from '../../components/page/block';
import { IBlockConfiguration } from '../../models/block/configuration';
import { EControlType, ELButtonType, IOrderProductViewModel } from '../../../src/models';
import * as page from '../../../src/components/page/index';
import EasyRouter from '../../utils/router';
import Global from '../../utils/Global';
import * as m from '../../models/index';
import StorageHelper from '../../utils/storage';
import { IProcessStatus, IApiResponse } from '../../interfaces';
import * as linq from 'linq';
import { EOrderProcessView } from '../../models/ordering/order';
import { ActivationProgress } from '../../components/ordering/process';
import ErrorMessage from '../../components/dialogs/errorMessage/errorMessage';
import { IColumn, EPagingType } from '../../components/page/table';
import IUser from '../../models/environment/user';
import { Popup, PopupBody } from '../../components';
import { EPopupType } from '../../components/dialogs/popup';


export interface IOrderProps {
    onChange: any;
};

export interface IOrderState {
    changed: boolean;
    model: any
    progress: EOrderProcessView;
    table1: boolean;
    table2: boolean;
    data2: any[];
    dataDetails: any[];
    orderTotal: string;
    fullAmountProducts: string;
    noAmountFound: boolean;
    errorAmount: boolean;
    details: boolean;

};

export interface IDataTable2 {
    Region: string,
    Status: string,
    Produkte: string,
    PriceTotal: string,
};
export interface IDataTable1 {
    ProductGroup: string,
    Picture: string,
    Id: string,
    Name: string,
    ProductAvailability: string,
    PriceString: string,
    Amount: any
};

export class OrderProduct extends React.Component<IOrderProps, IOrderState> {
    private processPopupState: EPopupType = EPopupType.custom;
    public static containerId: string = 'orderProducts';
    public dataTable1: IDataTable1[] = [];
    public dataTable2: IDataTable2[] = [];
    public orderProductsModel: IOrderProductViewModel = { products: [], address: new m.Address() };
    public priceTotal: number = 0.00;
    public currentProduct: IDataTable1 = { ProductGroup: '', Picture: '', Id: '', Name: '', ProductAvailability: '', PriceString: '', Amount: '' };
    public user?: IUser;
    public totalAmountProducts: string = '';
    public totalPriceEverything: string = '';
    public currentAddressByOrder: m.Address = new m.Address();
    ;
    constructor(props: any) {
        super(props);

        this.state = {
            changed: false,
            progress: EOrderProcessView.selectProduct,
            table1: true,
            table2: false,
            data2: [],
            model: undefined,
            orderTotal: '0',
            fullAmountProducts: '0',
            noAmountFound: false,
            errorAmount: false,
            details: false,
            dataDetails: []
        };

        this.user = StorageHelper.getUser();

        this.socketCallback = this.socketCallback.bind(this);
        this.validateInput = this.validateInput.bind(this);
        this.getOrderDetails = this.getOrderDetails.bind(this);
        this.calculateAmount = this.calculateAmount.bind(this);


        let t = {
            Void: this.user !== undefined ? this.user.voId : ''
        };

        Global.hub.send(JSON.stringify(t), m.ERequestType.getOrderProducts, undefined);

        this.onChange = this.onChange.bind(this);
    };



    public render() {
        let cfg = Global.getConfiguration();
        const pagingProps = { page: 1, count: cfg.pageSize, type: EPagingType.local };

        let colsTable2: IColumn[] = [
            { label: MainTexts.orderFrom, binding: 'Region', size: 3, sortable: true },
            { label: MainTexts.status, binding: 'Status', size: 3, sortable: true },
            { label: MainTexts.numberOfProducts, binding: 'Produkte', size: 3, sortable: true },
            { label: MainTexts.totalPriceEuro, binding: 'PriceTotal', size: 2, sortable: true },
            {
                label: '', binding: '', size: 1, sortable: false,
                renderer: (c, r) => {
                    return <>
                        <page.LButton type={ELButtonType.iconImage}
                            icon="list"
                            clickModel={c}
                            onClickEvent={(e, m) => {
                                e.preventDefault();
                                e.stopPropagation();
                                this.getOrderDetails(c.I_id)
                                return false;
                            }} />
                    </>;
                },
                rendererHead: (c) => {
                    return <>
                        <page.LButton type={ELButtonType.iconImage}
                            icon="expand"
                            onClick={''} />
                    </>;
                }
            },

        ];

        let colsTable1: IColumn[] = [
            { label: MainTexts.productGroup, binding: 'ProductGroup', size: 3, sortable: true },
            { label: MainTexts.picture, binding: 'Picture', size: 1, sortable: true },
            { label: MainTexts.surname, binding: 'Name', size: 2, sortable: true },
            { label: MainTexts.availability, binding: 'ProductAvailability', size: 2, sortable: true },
            { label: MainTexts.priceInEuro, binding: 'PriceString', size: 2, sortable: true },
            this.state.details ? { label: MainTexts.amount, binding: 'Amount', size: 2, sortable: true } : {
                label: MainTexts.amount, binding: 'Amount', size: 2, sortable: true,
                renderer: (c, r) => {
                    return <>
                        {/* {  <page.LControl type={EControlType.text}
                            id={c}
                            style='productAmount'
                            value={this.currentProduct.Amount}
                            hideHint
                            hideLabel
                            hideTitle
                            onChange={(e, m) => {
                                this.onChange(e)
                                return;
                            }} */}
                     {/* <div className="input-round-products">
                        <input
                            type="text"
                            onChange={(event) => {
                                this.onChange(event, c);
                            }
                            }
                            value={this.currentProduct.Amount}
                            style={{ marginTop: '2px' }} /> 
                            </div>
                    </>;  */}

                       <div className="input-round orderProducts">
                            <page.LControl onChange={(e, m) => {
                                this.onChange(e)
                                }}
                                hideHint
                                id={c}
                                type={EControlType.text}
                                hideTitle={true} style=""
                                controlStlye="form-control"
                                autoComplete="nope1"
                                placeholder="menge"
                                hideLabel={true}
                                value={this.currentProduct.Amount} />
                        </div>
                        </> 
                },
                rendererHead: (c) => {
                    return <>
                        <page.LLabel text={MainTexts.amount}
                        />
                    </>;
                }
            },
        ];

        let cfg1: IBlockConfiguration = {
            title: this.state.table2 ? MainTexts.yourOrders : MainTexts.yourOrder,
            transparent: false,
            style: '',
            styleContainer: 'block-orderProducts',
            collapsible: false,
            collapsed: false
        };

        let cfg2: IBlockConfiguration = {
            title: MainTexts.details,
            transparent: false,
            style: '',
            styleContainer: 'block-orderProducts',
            collapsible: false,
            collapsed: false
        };

        let ctr = <></>;

        ctr = <div id={OrderProduct.containerId}>

            <page.Inner1C style="   ">
                {this.renderProgress()}
            </page.Inner1C>

            <>
            <Popup
                    type={this.processPopupState}
                    visible={this.state.errorAmount}
                    closable={true}
                    id="areYouSure"
                    title={"Warnung"}
                    height={200}
                    onDismiss={this._onDismissProcessPopup}>
                    <PopupBody>
                        <h5>Bitte geben Sie die Menge der zu bestellenden Sim-Karten ein.</h5>
                        <></>
                    </PopupBody>
                    <></>
                </Popup>
            </>

            <BaseBlock config={cfg1}>
                {
                    <Container>
                        {this.state.table2 ? <></> : <ErrorMessage visible={true} text={OrderingTexts.orderProductsNotification} />
                        }
                        <div className="spacer"></div>
                        {/* {
                            this.state.errorAmount ? <ErrorMessage visible={true} text={OrderingTexts.errorAmountText} /> : <></>
                        } */}
                        <div className="spacer"></div>

                        <Row>
                            <Col xs={4} md={4} >
                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                    css="a1 fr"
                                    id=""
                                    text={MainTexts.orderProducts}
                                    onClick={() => {
                                        this.setState({ table1: true, table2: false, details: false });

                                    }} />
                            </Col>
                            <Col xs={4} md={4} >
                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                    css="a1 fr"
                                    id=""
                                    text={MainTexts.yourOrders}
                                    onClick={() => {

                                        let t = {
                                            id: 'test',
                                            url: 'test'
                                        };

                                        Global.hub.send(JSON.stringify(t), m.ERequestType.getOrdersList, undefined);
                                    }} />
                            </Col>
                        </Row>

                        <Row>
                            <Col >
                                {this.state.table1 ? <page.Table coulmns={colsTable1}
                                    id={'1'}
                                    expnaded={false}
                                    pagingProps={pagingProps}
                                    data={this.dataTable1} /> : <></>
                                }
                                {this.state.table2 ? <page.Table coulmns={colsTable2}
                                    id={'1'}

                                    expnaded={false}
                                    pagingProps={pagingProps}
                                    data={this.state.data2} /> : <></>
                                }
                            </Col>
                        </Row>

                        {this.state.table2 ? <></> : <Row>
                            <Col xs={8} md={8}>
                            </Col>
                            <Col xs={2} md={2}>
                                <page.LLabel text={MainTexts.orderText + ':  ' + this.priceTotal.toFixed(2).toString() + " "} />
                            </Col>
                            <Col xs={2} md={2}>
                                <page.LLabel text={MainTexts.amount + ':  ' + this.state.orderTotal} />
                            </Col>
                        </Row>
                        }

                        {this.state.table2 ? <></> : <Col xs={12} md={12} >
                            <page.LButton type={ELButtonType.buttonWithIconRight}
                                css="a1 fr"
                                id=""
                                text={MainTexts.next}
                                onClick={() => {

                                    if (this.orderProductsModel.products.length === 0) {
                                        this.setState({ errorAmount: true });
                                    } else {
                                        this.orderProductsModel.products.forEach(product => {
                                            if (product.Amount === '0' || product.Amount === '') {
                                                this.orderProductsModel.products.splice(this.orderProductsModel.products.findIndex(pr => pr.Id === product.Id), 1);
                                            }
                                        })

                                        if (this.orderProductsModel.products.length === 0) {
                                            this.setState({ errorAmount: true });
                                        } else {
                                            StorageHelper.addOrderProducts(this.orderProductsModel);
                                            EasyRouter.route({ url: '/apps/orderproducts/selectdeliveryaddress', title: MainTexts.selectDeliveryAddress });
                                        }
                                    }
                                }} />
                        </Col>
                        }
                    </Container>
                }
            </BaseBlock>


            {this.state.details ? <BaseBlock config={cfg2}>
                {
                    <Container>

                        <Row>
                            <Col>
                                <page.Table coulmns={colsTable1}
                                    id={'1'}
                                    expnaded={false}
                                    pagingProps={pagingProps}
                                    data={this.state.dataDetails} />
                            </Col>
                        </Row>


                        <Row>
                            <Col xs={8} md={8}>
                            </Col>
                            <Col xs={2} md={2}>
                                <page.LLabel text={MainTexts.orderText + ':  ' + this.totalPriceEverything} />
                            </Col>
                            <Col xs={2} md={2}>
                                <page.LLabel text={MainTexts.amount + ':  ' + this.totalAmountProducts} />
                            </Col>
                        </Row>



                        <Row>
                            <Col md={4}>
                                <page.LLabel text={OrderProductStages.deliveryAddress} />
                            </Col>
                        </Row>

                        <Row>
                            <Col md={4}>
                                <page.LLabel text={this.currentAddressByOrder.name} />
                            </Col>
                        </Row>


                        <Row>
                            <Col md={4}>
                                <page.LLabel text={this.currentAddressByOrder.house_number + ", " + this.currentAddressByOrder.zip + ", " + this.currentAddressByOrder.city} />
                            </Col>
                        </Row>


                    </Container>
                }
            </BaseBlock> : <></>
            }
        </div>
        return ctr;
    };

    _onDismissProcessPopup = () => {
        this.setState({ errorAmount: false });
    }

    private renderProgress(): JSX.Element {
        let cancelProgress: JSX.Element =
            <ActivationProgress
                progress={this.state.progress}
                states={[OrderProductStages.selectProducts, OrderProductStages.deliveryAddress, OrderProductStages.overviewOrder]} />;

        if (Global.easyApp.head) {
            Global.easyApp.head.setTitle(this.state.table2 ? MainTexts.yourOrders : MainTexts.yourOrder);
            Global.easyApp.head.appendSub(this.state.table2 ? <></> : cancelProgress);
        }
        return <></>;
    }

    public socketCallback(res: IApiResponse) {
        this.dataTable1 = res.data;
    }

    public socketCallbackOrdersList(res: IApiResponse) {
        this.setState({ table2: true, table1: false, data2: res.data.Items, details: false });
    }

    public getOrderDetails(orderId: string) {

        let t = {
            id: orderId
        };

        Global.hub.send(JSON.stringify(t), m.ERequestType.getOrderDetail, undefined);
    }

    public socketCallbackDetails(res: IApiResponse) {

        this.totalAmountProducts = this.calculateAmount(res.data.products)
        this.currentAddressByOrder.city = res.data.address.City;
        this.currentAddressByOrder.zip = res.data.address.ZIP;
        this.currentAddressByOrder.street = res.data.address.Street;
        this.currentAddressByOrder.name = res.data.address.Name;
        this.currentAddressByOrder.house_number = res.data.address.HouseNumber;

        this.setState({ dataDetails: res.data.products, details: true });
    }

    onChange(target: any) {
     
        target.value = this.validateInput(target.value);

        this.currentProduct = target.id;
        this.currentProduct.Amount = target.value;

        const index = this.orderProductsModel.products.findIndex(product => product.Id === this.currentProduct.Id);
        if (index > -1) {
            this.orderProductsModel.products[index].Amount = target.value;
        } else {
            this.orderProductsModel.products.push(this.currentProduct);
        }

        this.orderProductsModel.products.forEach(product => {
            var priceFormat: string = product.PriceString.substring(0, 3);
            this.priceTotal += Number(product.Amount) * Number(priceFormat);
        })

        this.setState({ changed: true, orderTotal: target.value })
    }

    private calculateAmount(data: any) {
        let count: number = 0;
        let countPrice: number = 0;
        data.forEach(product => {
            count += Number(product.Amount);
            countPrice += Number(product.Amount) * product.Price;
        })
        this.totalPriceEverything = countPrice.toFixed(2).toString() + " ";
        return count.toString();
    }


    private validateInput(input: string) {
        const regex = /^(?:[1-9][0-9]*|0)$/gm;
        const str = input;
        let m = regex.exec(str) !== null;

        if (m) {
            return input;
        } else {
            return input = '';
        }
    }
};

export default OrderProduct;