import * as React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { MainTexts, OrderProductStages, ValidationMessages } from '../../utils/messages';
import BaseBlock from '../../components/page/block';
import { IBlockConfiguration } from '../../models/block/configuration';
import { EControlType, ELButtonType, ERequestType, ERequestStatus, IOrderProductViewModel } from '../../../src/models';
import * as page from '../../../src/components/page/index';
import Global from '../../utils/Global';
import * as m from '../../models/index';
import StorageHelper from '../../utils/storage';
import { IProcessStatus, IApiStep, IApiSummary, IApiResponse } from '../../interfaces';
import IUser from '../../models/environment/user';
import { EOrderProcessView } from '../../models/ordering/order';
import { ActivationProgress } from '../../components/ordering/process';
import { IKeyTextNumber, IControlValue } from '../../models/page/interfaces';
import EasyRouter from '../../utils/router';
import { StorageKeys } from '../../utils/Defaults';
import { ValidationWrapper, ModelStete } from '../../utils';
import ValidationOption, { IValidationOptionControl, EValidationOption } from '../../utils/validation/validationOption';
import Address from '../../components/ordering/global/address';

export interface IOrderProps {
    onChange: any;
    address?: m.Address;
};

export interface IOrderState {
    changed: boolean;
    progress: EOrderProcessView;
    savedAddress: boolean;
    newAddress: boolean;
};

export class DeliveryAddress extends React.Component<IOrderProps, IOrderState> {
    public static containerId: string = 'deliveryAddress';
    public savedAddressFullString: string = '';
    public orderProductsModel: IOrderProductViewModel = { products: [], address: new m.Address() };
    public modelState: ModelStete = new ModelStete();
    private address: m.Address;
    private valop: IValidationOptionControl[] = [];
    public user?: IUser;

    constructor(props: any) {
        super(props);

        this.state = {
            changed: false,
            progress: EOrderProcessView.deliveryAddress,
            savedAddress: true,
            newAddress: false
        };

        this.address = this.props.address ? this.props.address : new m.Address();
        var data = sessionStorage.getItem(StorageKeys.keyOrdersProducts);

        if (data !== null) {
            this.orderProductsModel = JSON.parse(data);
        }

        this.valop.push(
            {
                id: 'city',
                options: [
                    new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                ]
            },
            {
                id: 'zip',
                options: [
                    new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                    new ValidationOption({ type: EValidationOption.numeric, message: ValidationMessages.numericError }),
                    new ValidationOption({ type: EValidationOption.exactLength, targetValue: 5, message: 'Max 5 Zeichen' })
                ]
            },
            {
                id: 'street',
                options: [
                    new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                ]
            },
            {
                id: 'nr',
                options: [
                    new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                    new ValidationOption({ type: EValidationOption.numeric, message: ValidationMessages.numericError }),
                ]
            },
            {
                id: 'street',
                options: [
                    new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                ]
            },
        );

        this.user = StorageHelper.getUser();
        
        this.onChange = this.onChange.bind(this);
    };

    public render() {
        let a: JSX.Element = <>
            <page.LButton type={ELButtonType.linkSimple}
                text={MainTexts.back}
                css="action3 fr"
                onClick={() => {
                    EasyRouter.route({ url: '/apps/orderproducts', title: MainTexts.orderProductsTitle });
                }} />
        </>;

        let cfg1: IBlockConfiguration = {
            title: MainTexts.yourOrder,
            transparent: false,
            style: '',
            styleContainer: 'block-orderProducts',
            collapsible: false,
            collapsed: false,
            action: a
        };

        let ctr = <></>;

        ctr = <div id={DeliveryAddress.containerId}>
            <page.Inner1C style="   ">
                {this.renderProgress()}
            </page.Inner1C>

            <BaseBlock config={cfg1}>
                {
                    <Container id="newAddressValidation">

                        <Row>
                            <Col xs={4} md={4} >
                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                    css="a1 fr"
                                    id=""
                                    text={MainTexts.orderProducts}
                                    onClick={() => {
                                        EasyRouter.route({ url: '/apps/orderproducts', title: MainTexts.orderProductsTitle });

                                    }} />
                            </Col>
                            <Col xs={4} md={4} >
                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                    css="a1 fr"
                                    id=""
                                    text={MainTexts.yourOrders}
                                    onClick={() => {

                                        let t = {
                                            id: 'test',
                                            url: 'test'
                                        };

                                        Global.hub.send(JSON.stringify(t), m.ERequestType.getOrdersList, undefined);
                                        EasyRouter.route({ url: '/apps/orderproducts', title: MainTexts.yourOrders });
                                    }} />
                            </Col>
                        </Row>

                        <Row>
                            <Col md={4} xs='auto'>
                                <page.LControl type={EControlType.radio}
                                    checked={this.state.savedAddress}
                                    onChange={this.onChange}
                                    name="saveAddress"
                                    id={'savedAddress'}
                                  
                                    label={MainTexts.savedAddress} title={''} />
                            </Col>
                            <Col md={2}>
                                <page.LControl type={EControlType.radio}
                                    checked={this.state.newAddress}
                                    onChange={this.onChange}
                                    name="newAddress"
                                    id={'newAddress'}
                                    label={MainTexts.newAddress} title={''} />
                            </Col>
                        </Row>

                        {this.state.savedAddress ? <>
                            <Row >
                                <Col md={12}>
                                    <page.LControl type={EControlType.dropdown}
                                        value={'1'}
                                        onChange={this.onChange}

                                        name="dealer" id="" title={MainTexts.dealerName}
                                        options={this.getStoredDeliveryAddreses()} />
                                </Col>
                            </Row>
                        </> : <></>}

                        {this.state.newAddress ? <>

                            <Address
                                source={this.address}
                                addressAdditionalFields={true}
                                onChange={this.onChange} preview={false}
                                prefix={'+'} />

                        </> : <></>}

                        <Col xs={12} md={12} >
                            <page.LButton type={ELButtonType.buttonWithIconRight}
                                css="a1 fr"
                                id=""
                                text={MainTexts.next}
                                onClick={() => {
                                    let res = ValidationWrapper.validateFormular(undefined,
                                        true,
                                        DeliveryAddress.containerId,
                                        this.valop);

                                    if (res) {
                                        if (this.state.savedAddress) {
                                            this.orderProductsModel.address.additional = '';
                                            this.orderProductsModel.address.city = this.user === undefined ? '' : this.user.voCity;
                                            this.orderProductsModel.address.house_number = '';
                                            this.orderProductsModel.address.name = this.user === undefined ? '' : this.user.voName;
                                            this.orderProductsModel.address.street = this.user === undefined ? '' : this.user.voStreet;
                                            this.orderProductsModel.address.zip = this.user === undefined ? '' : this.user.voZIP;

                                            StorageHelper.addOrderProducts(this.orderProductsModel);
                                        } else {
                                            this.orderProductsModel.address = this.address;
                                            StorageHelper.addOrderProducts(this.orderProductsModel);
                                        }
                                        EasyRouter.route({ url: '/apps/orderproducts/selectdeliveryaddress/orderOverview', title: MainTexts.orderOverviewTitle });
                                    }
                                }} />
                        </Col>
                    </Container>

                }
            </BaseBlock>
        </div>
        return ctr;
    };

    public getStoredDeliveryAddreses(): IKeyTextNumber[] {
        let op: IKeyTextNumber[] = new Array();

        var retailerName = this.user === undefined ? '' : this.user.voName;
        var street = this.user === undefined ? '' : this.user.voStreet;
        var zip = this.user === undefined ? '' : this.user.voZIP;
        var city = this.user === undefined ? '' : this.user.voCity;

        op.push({ key: 1, text: retailerName + ", " + street + ", " + zip + " " + city });

        return op;
    };

    private renderProgress(): JSX.Element {
        let cancelProgress: JSX.Element =
            <ActivationProgress
                progress={this.state.progress}
                states={[OrderProductStages.selectProducts, OrderProductStages.deliveryAddress, OrderProductStages.overviewOrder]} />;

        if (Global.easyApp.head) {
            Global.easyApp.head.setTitle(MainTexts.yourOrder);
            Global.easyApp.head.appendSub(cancelProgress);
        }
        return <></>;
    }

    onChange(e: IControlValue) {
        if (e.id === 'newAddress') {
            this.setState({ newAddress: true, savedAddress: false })
        } else if (e.id === 'savedAddress') {
            this.setState({ newAddress: false, savedAddress: true })
        } else {
            Global.setValueByPath(this.address, e.id, e.value);
            this.setState({ changed: true })
        }
    }
};

export default DeliveryAddress;