import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { LClear } from '../../components';
import { EPopupType } from '../../components/dialogs/popup';
import * as cmp from '../../components/index';
import BaseBlock from '../../components/page/block';
import * as page from '../../components/page/index';
import { IApiResponse } from '../../interfaces';
import { EControlType, ELButtonType, ERequestType } from '../../models';
import { IBlockConfiguration } from '../../models/block/configuration';
import { IControlValue } from '../../models/page/interfaces';
import { ValidationWrapper } from '../../utils';
import Global from '../../utils/Global';
import { Hints, MainTexts, UserMessages, ValidationMessages } from '../../utils/messages';
import EasyRouter from '../../utils/router';
import ValidationOption, { EValidationOption } from '../../utils/validation/validationOption';

export interface IEditPasswordProps {

}
export interface IEditPasswordState {
    password: string;
    newPassword: string;
    newPasswordConfirm: string;

    popupTitle: string;
    processing: boolean;
    closblePopup: boolean;
    apiMessage: string;
}

class EditPasswordScreen extends React.Component<IEditPasswordProps, IEditPasswordState> {

    private processPopupState: EPopupType = EPopupType.pleaseWait;
    public static containerId: string = 'editPassword';
    readonly cfg: IBlockConfiguration = {
        title: UserMessages.passwordEdit,
        transparent: false,
        styleContainer: ''
    }

    constructor(props) {
        super(props);

        this._onSave = this._onSave.bind(this);
        this._onChange = this._onChange.bind(this);
        this.onDismissProcessPopup = this.onDismissProcessPopup.bind(this);
        this.showPopup = this.showPopup.bind(this);

        this.state = { password: '', newPassword: '', newPasswordConfirm: '', closblePopup: false, apiMessage: '', processing: false, popupTitle: '' };
    }

    render() {
        return <div id={EditPasswordScreen.containerId}>
            <cmp.Popup
                type={this.processPopupState}
                visible={this.state.processing || this.state.closblePopup}
                closable={this.state.closblePopup}
                id="wait"
                title={this.state.popupTitle}
                height={200}
                onDismiss={this.onDismissProcessPopup}>
                <cmp.PopupBody>
                    <div>{this.state.apiMessage}</div>
                    <></>
                </cmp.PopupBody>
                <></>
            </cmp.Popup>
            <page.Inner1C>
                <BaseBlock config={this.cfg}>
                    <Container className="container-action">
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.hint} label={UserMessages.passwordEditDesc} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.password}                               
                                    value={this.state.password? this.state.password: ''}
                                    validationOptions={
                                        [
                                            new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError })
                                        ]
                                    }
                                    onChange={this._onChange}
                                    name="password" id="password"
                                    label={UserMessages.currentPassword} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.password}
                                    value={this.state.newPassword? this.state.newPassword: ''}
                                    validationOptions={
                                        [
                                            new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                            new ValidationOption({ type: EValidationOption.passwordOutsideCustomerData, message: ValidationMessages.errorPasswordOutsideCustomerData })
                                        ]
                                    }
                                    onChange={this._onChange}
                                    name="newPassword" id="newPassword"
                                    label={UserMessages.newPassword} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl hideTitle={true} labelStyle={'tc-hint mwa'} type={EControlType.hint} label={Hints.passwordOutsideCustomerDataHint} />
                                <page.LClear />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.password}
                                    value={this.state.newPasswordConfirm? this.state.newPasswordConfirm: ''}
                                    validationOptions={
                                        [
                                            new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError })
                                        ]
                                    }
                                    onChange={this._onChange}
                                    name="newPasswordConfirm" id="newPasswordConfirm"
                                    label={UserMessages.newPasswordConfirm} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={1.5} md={1.5} xl={1.5} className="np">
                                <page.LButton type={ELButtonType.linkSimple}
                                    css="action3 fr"
                                    id="buttonCancel"
                                    text={MainTexts.cancel}
                                    onClick={() => {
                                        EasyRouter.route({ url: '/' })
                                    }} />
                            </Col>

                            <Col xs={10} md={10} >
                                <page.LButton
                                    onClick={this._onSave}
                                    text={MainTexts.save}
                                    type={ELButtonType.linkSimple}
                                    css="action fr"
                                    icon="chevron" />
                            </Col>
                        </Row>
                    </Container>
                    <LClear />
                </BaseBlock>
            </page.Inner1C>
        </div>;
    }

    public socketCallback(api: IApiResponse) {
        if (api.data.status===2) {
            this.showPopup(EPopupType.info, MainTexts.textError, api.data.notes[0].exception.Message, true);
        } else {
            this.setState({ password: '', newPassword: '', newPasswordConfirm: '' }); // reset fields
            this.showPopup(EPopupType.info, MainTexts.saveSuccess, '', true);
        }
    }

    public showPopup(type: EPopupType, title: string, message: string, inprocess: boolean) {
        this.processPopupState = type;
        this.setState({
            apiMessage: message,
            closblePopup: true,
            processing: inprocess,
            popupTitle: title
        });
    }

    private onDismissProcessPopup() {
        this.setState({ processing: false, closblePopup: false });
    }

    private _onChange(target: IControlValue) {
        let model = this.state;
        Global.onInput(model, target, () => {
            this.setState({ password: model.password,newPasswordConfirm: model.newPasswordConfirm,newPassword: model.newPassword });
        });
    }

    private _onSave() {
        const model = this.state;
        let res = ValidationWrapper.validateFormular(undefined,
            true,
            EditPasswordScreen.containerId
        );

        if (res) {
            Global.hub.send(JSON.stringify(model),
                ERequestType.changePassword,
                undefined);
        }

    }


}

export default EditPasswordScreen;
