import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBaseComponentState } from '../../../interfaces/index';
import { ESalutation } from '../../../models/ordering/order';
import { EControlType } from '../../../models/page/diverse';
import GlobalJSX from '../../../utils/GlobalJSX';
import { MainTexts, ValidationMessages } from '../../../utils/messages';
import ValidationOption, { EValidationOption, IValidationOptionControl } from '../../../utils/validation/validationOption';
import * as page from '../../page/index';



export interface IPersonProps {
    prefix: string;
    onChange: any;
    source: any;
    //componentTitle: string;

    salutation: ESalutation;
    title: string;
    firstname: string;
    surname: string;
    hideTitle?: boolean;
    validationOptions?: ValidationOption[];

    birthday?: Date;
    showBirthday?: boolean;
};

export class Person extends React.Component<IPersonProps, IBaseComponentState> {
    private valop: IValidationOptionControl[] = [];
    constructor(props: IPersonProps) {
        super(props);

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: IPersonProps) {
        this.setState({changed: true});
    };

    public render() {
        let bdid = GlobalJSX.getKeyFromPrefix(this.props.prefix, 'birthday', true);
        let bdname = GlobalJSX.getKeyFromPrefix(this.props.prefix, 'birthday', false);

        let sal = this.props.source ? this.props.source.salutation : 0;

        if(this.props.salutation != ESalutation.undefined) {
            sal = this.props.salutation;
        }

        let ctrl = (<>
                <Container  className="np">
                    <Row>
                        <Col xs={6} md={6}>
                            <Row>
                                
                                <Col xs={6} md={6} className="">
                                    <page.LControl type={EControlType.radio} 
                                        onChange={this.props.onChange} 
                                        name={GlobalJSX.getKeyFromPrefix(this.props.prefix, "salutation", false)} 
                                        id={GlobalJSX.getKeyFromPrefix(this.props.prefix, "salutation_1", true)} 
                                        checked={sal == ESalutation.mr}
                                        validationOptions={
                                            [

                                                new ValidationOption({ targetControl: 'salutation', source: this.props.source, type: EValidationOption.oneOf, targetValue: ['1', '2'],  message: MainTexts.genderError}),
                                            ]
                                        }
                                        label={MainTexts.mr} title={MainTexts.salutation}/>
                                </Col>
                                <Col xs={6} md={6}>
                                    <page.LControl type={EControlType.radio} onChange={this.props.onChange} 
                                        name={ GlobalJSX.getKeyFromPrefix(this.props.prefix, "salutation", false)} 
                                        id={ GlobalJSX.getKeyFromPrefix(this.props.prefix, "salutation_2", true)} 
                                        checked={sal == ESalutation.mrs}
                                        label={MainTexts.mrs}/>
                                </Col>
                               
                            </Row>
                        </Col>
                        <Col xs={6} md={6} className="npr">
                            {this.props.hideTitle? <></> : GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'title', undefined, MainTexts.title, false,undefined,undefined,undefined,16)}
                        </Col>
                    </Row>

                    <Row>
                        <Col xs={6} md={6} className="np">
                            {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'firstname', undefined, MainTexts.firstname, false,undefined,undefined,
                                            [
                                                 new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                                new ValidationOption({type: EValidationOption.validateNames,  message: "Der Vorname beginnt mit einem Grobuchstaben und ist zwischen 2 und 30 Zeichen lang. Nur Buchstaben, Bindestriche und Leerzeichen sind erlaubt"})
                                            ],30
                                        )}
                        </Col>
                        <Col xs={6} md={6} className="npr">
                            {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'surname', undefined, MainTexts.surname, false,undefined,undefined, this.props.validationOptions? this.props.validationOptions : [
                                                new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                                new ValidationOption({type: EValidationOption.validateSurname,  message: "Es sind nur Buchstaben, Bindestriche und Leerzeichen zulssig und die Lnge liegt zwischen 2 und 30 Zeichen."})
                                            ],30)}
                        </Col>                        
                    </Row>     

                    {
                        this.props.showBirthday ? 
                        <Row>
                            <Col xs={6} md={6} className="np">
                                <page.LDate title={MainTexts.dateOfBirth} 
                                datePicker = {false}
                                    id={bdid} 
                                    name={bdname} 
                                    onChange={this.props.onChange}
                                    value={this.props.birthday}/>
                            </Col>
                        </Row>
                        : null
                    }               
                </Container>
                <page.LClear />
            </>
        );

        return ctrl;
    };
};

export default Person;