    public inCdChange: boolean = false;
    public inDsl: boolean = false;
    public inOtelo: boolean = false;
    public inVodafone: boolean = false;
    public inKabel: boolean = false;

    constructor(input: IOrderData) {
        this.data = input;

        if(!this.brands || this.brands.length === 0) {
            this.brands = StorageHelper.getBrands();
        }

        this.inPrepaid = this.hasPrepaid();
        this.inPostpaid = this.hasPostpaid();
        this.inActDefault = this.hasActDefault();
        this.inActMnp= this.hasActMnp();
        this.inDsl = this.hasDsl();
        this.inOtelo = this.hasOtelo();
        this.inVodafone = this.hasVodafone();
        this.inKabel = this.hasKabel(); 
        this.inCdChange = this.hasCdChange();

        //console.log('data', this);
    };

    public canEditActivation(index: number):boolean {
        let act = this.data.activations[index];
        if(act.processStatus && act.processStatus.steps){
            let step: IApiStep = act.getStep(ERequestType.activation);
            if(step) {
                return step.status !== ERequestStatus.done;
            }
        }

        return true;
    };

    public static ensure(order: IOrderData) {
        //console.log((new Error()).stack);
        if(order.activations) {
            let aa:IActivation[] = [];

            //**if existing */
            if(order.customer.contracts && order.customer.contracts.length>0 && !order.isCrossSelling) {
                order.currentBrand = order.activations[0].brandKey;
                //** 2020-11-24 sm only if selection is null, because otelo.weiteren vertrag does not work correctly (https://lightds.atlassian.net/browse/VAS-502) */
                if(!order.activations[0].selection) {
                    let sel = new TreeItem('root');
                    sel.add(order.customer.contracts[0].bookedProduct!.id);
                    order.activations[0].selection = sel;
                }
            };

            order.activations.forEach((i,idx)=>{
                let aaa:IActivation = OrderDataWrapper.ensureActivationIsObject(i); // Object.assign(new IActivation(i.brandKey), i);
                aaa.selection = TreeItem.ensure(aaa.selection);
                aa.push(aaa);
            });
            order.activations = aa;
        }         
    };

    public static subHasOption(sub: ISub, option: ESubOptions) : boolean {
        if(!sub.keyNecessary) {
            return false;
        }

        return (sub.keyNecessary & option) === option;
    };

    public getAllExcludesForSelectedServices(brand: string, selectedServices: string[]) : string[] {
        let res: string[] = [];
        let br:IBrand = linq.from<IBrand>(this.brands).where(x=>x.id == brand).first();

        selectedServices.map(i=>{
           
            let svc = linq.from<IService>(br.services).where(x=>x.id == i).firstOrDefault();

            if(svc && svc.notbookableWith) {
                
                svc.notbookableWith.map(j=>{
                    if(res.indexOf(j)<0){
                        res.push(j);
                    };
                });
            };
        });

        return res;
    };

    public static ensureActivationIsObject(act:IActivation) : IActivation {
        return Object.assign(new IActivation(act.brandKey), act);
    };

    public getCurrentActivation() : IActivation | undefined {
        if(this.data.activations && this.data.activations.length > 0) {
            return this.data.activations[this.data.activations.length - 1];
        };

        return undefined;
    };

    public resetUnfinishedActivations() {
        for(let i = 0; i < this.data.activations.length; i++) {
            if(!this.data.activations[i].isActivated()) {
                this.data.activations[i].resetSteps(ERequestType.print);
            };

            StorageHelper.addOrder(this.data);
        };
    };

    public getAllActivationsDone() : boolean {
        for(let i = 0; i < this.data.activations.length; i++) {
            if(!this.data.activations[i].isActivated()) {
                return false;
            };
        };

        return true;
    };

    //**will deliver all tariff/subs/services as tree */
    public getProducts() : ProductTree {
        let ti = new ProductTree('', '');
        
        this.data.activations.map((i)=>{
            ti.addChildren(i.getProductsTree());
        });

        return ti;
    };

