    public getActivationByProduct(productId: string) {
        for(let i: number = 0; i< this.data.activations.length; i++) {
            if(this.data.activations[i].hasProduct(productId)) {
                this.data.activations[i].idx = i;
                return this.data.activations[i];
            };
        };
    };

    public getActivationByBrand(brand: string) {
        for(let i: number = 0; i< this.data.activations.length; i++) {
            if(this.data.activations[i].brandKey == brand) {
                this.data.activations[i].idx = i;
                return this.data.activations[i];
            };
        };
    };

