    public static createProcess(bc: EBusinessCase, 
        at: EActivationType,
        d: IOrderData,
        a: ICustomerAction|undefined) : IOrderData{
        return OrderDataWrapper.createProcessFromData(bc, at, d, a);
    };

    public static createProcessFromData(bc: EBusinessCase, 
        at: EActivationType,
        d: IOrderData,
        a: ICustomerAction | undefined) : IOrderData {
        let newid = Global.getUniqueId();

        let data : IOrderData = {
            currentCat: '',
            id: newid,
            type: bc,
            customer: d.customer,
            currentBrand: d.currentBrand,
            parentId: d.id,
            
            activations: JSON.parse(JSON.stringify(d.activations))
        };

        data.customer.bew = { };

        if(at === EActivationType.partnerCard) {
            data.customer.payment = {
                paymentType: EPaymentType.iban,
                addressType: EActionAddressType.sameAddress,
                address: new Address()
            };
        };

        data.customer.action = a;
        
        data.activations.forEach(x=>{
            if(bc != EBusinessCase.bookService) {
                if(d.customer.contracts && d.customer.contracts[0].bookedProduct) {
                    x.services = d.customer.contracts[0].bookedProduct.services;
                };
            };
            
            if(!x.services) {x.services = [];};
            x.processId = newid;

            x.activationType = at;

            if(at === EActivationType.partnerCard) {
                x.creditDebitType = ECreditDebitType.postpaid;
                x.processStatus = {steps:[]};
            };
            
            //action type is only given when delivered from epos for vvl/tw
            if(a) {
                x.action = a;
                if(a.nbaId) {
                    x.productId = a.product!;
                    x.sub = a.sub!;

                    //x.nbaId = a.nbaId;
                    x.services = a.services || [];
                };
                x.portationType = a.type == ECustomerActionType.ART ? EPortationType.asap : EPortationType.contractEnd;
            };

            x.id = Global.getUniqueId();
        });

        if(a && a.bc == EBusinessCase.bookService && data.activations[0].brandKey === BrandKeys.vodafone) {
            data.activations[0].activationType = EActivationType.bookService;
            data.activations[0].productId = a.products[0].id;
            data.activations[0].sub = a.products[0].subs[0].id;
            data.activations[0].services = linq.from<ISubServiceConfig>(a.products[0].subs[0].sl).select(x=>x.id).toArray();
        };

        OrderDataWrapper.ensure(data);

        StorageHelper.addOrder(data);

        return data;
    };

    public static previewAvailable(act: IActivation[]) : boolean {
       
        for(let i: number = 0 ; i < act.length; i++) {
            
            if(!act[i].productId) {
                return false;
            };
        };
        return true;
    };

    public static previewAvailableForAtLeastOne(act: IActivation[]) : boolean {
        for(let i: number = 0 ; i < act.length; i++) {
            if(act[i].productId) {
                return true;
            };
        };
        return false;
    };    

