    public hasPartnerGroups() : boolean {

        let res: boolean = false;
        if(this.data.customer.contracts) {
            let con = this.data.customer.contracts[0];

            if(con.groups && con.groups.length > 0) {
                res = true;
            };
        }

        return res;
    };        

    public hasNumberProtability() : boolean {
        let res: boolean = false;
        
        if(this.data.activations) {
            let orders = linq.from<IActivation>(this.data.activations)
                .where(x=>x.brandKey === BrandKeys.kabel)
                .toArray();
            
            if(orders.length >0){
                for(let i = 0; i< orders.length; i++) {
                    if(orders[i].services.indexOf('rufnummernmitnahme-1')>=0){
                        res = true;
                        break;
                    };
                };
            };
        }

        return res;
    };    

    public hasCategory(cat: string) : boolean {
        let res: boolean = false;
        if(this.data.activations) {
            res = linq.from<IActivation>(this.data.activations)
                .where(x=>x.catKey == cat)
                .count() > 0;

            if(!res && (cat == CatKeys.tv || cat == CatKeys.fn))  {
                this.data.activations.map((i)=>{
                    if(!res && i.selection && i.selection.children) {
                        for(let c = 0; c < i.selection.children.length ; c++) {
                            let prcat = Global.categoryWrapper.getCategoryKeyByProduct(i.selection.children[c].id);
                            if(prcat == cat) {
                                res = true;
                            }
                        };
                    };
                });
            };
        }
        
        return res;
    };     

