    public hasDslOnly() : boolean {
        let res: boolean = false;
        if(this.data.activations) {
            res = linq.from<IActivation>(this.data.activations)
                .where(x=>x.brandKey === BrandKeys.dsl)
                .count() > 0;

            let other = linq.from<IActivation>(this.data.activations)
                .where(x=>x.brandKey !== BrandKeys.dsl)
                .count() > 0;         
                
            if(other) {
                res = false;
            };
        }

        return res;
    };   

