import {IBrand} from '../ordering/brand';
import { BrandKeys } from '../../utils/Defaults';
import * as linq from 'linq';
import IProduct from '../ordering/product';

export class TariffAndSub {
    id:string = '';
    sub: string = '';
    productName: string = '';

    public static createInstanceFromId(brand: IBrand, productId:string) {
        let pr = new TariffAndSub();
        pr.id = productId;
        
        let prod = this.createInstanceFromJson(pr, brand);

        if(!prod.productName) {
            prod.productName = productId;
        };

        return prod;
    };

    public static createInstanceFromJson(pr: TariffAndSub|undefined, brand: IBrand) : TariffAndSub {
        if(pr) {
            if(brand.id === BrandKeys.vodafone) {
                for(let i =0;i<brand.subs.length;i++) {
                    if(pr.id.endsWith(brand.subs[i].id)) {
                        pr.productName = pr.id;
                        pr.id = pr.id.substr(0, pr.id.length - brand.subs[i].id.length);
                        pr.sub = brand.subs[i].id;
                        let bprod = linq.from<IProduct>(brand.products).where(x=>x.id == pr.id).firstOrDefault();

                        if(bprod) {
                            pr.productName = bprod.name;
                        };

                        break;
                    };
                };
            };

            return Object.assign(new TariffAndSub(), pr);
        };

        return new TariffAndSub();
    };
};