import de from "date-fns/locale/de"; // the locale you want
import * as linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import DatePicker, { registerLocale } from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { EControlType } from '../../models/page/diverse';
import { IControlValue, IKeyText } from '../../models/page/interfaces';
import { IField } from '../../models/support/field';
import Global from '../../utils/Global';
import { ValidationMessages } from '../../utils/messages';
import * as val from '../../utils/validation/validationOption';
import ValidationOption, { EValidationOption, IValidationOptionControl } from '../../utils/validation/validationOption';
import LClear from './clear';
import LControl from './control';
registerLocale("de", de);

export enum EDateType {
    default = 0,
    fixedStart = 1
};

export enum EDateInputMode {
    undefined = 0,
    withToggle = 1,
    textInput = 2
};

export interface ILDateProps {
    value?: Date;
    onChange?: any;
    id: string;
    name?: string;
    title: string;
    preview?: boolean;
    style?: string;
    type?: EDateType;
    from?: Date;
    to?: Date;
    mode?: EDateInputMode;
    datePicker?: boolean;
    validationOptions?: val.ValidationOption[];
    hasKabel?: boolean;
    minimumBdayYear?: number;
};

export interface ILDateState {
    changed: boolean;
    startDate: Date;
};

export class LDate extends React.Component<ILDateProps, ILDateState> {
    private value: Date = new Date();
    private type: EDateType = EDateType.default;
    private datePicker: boolean = false;
    private textFieldDate: string = '';
    private errorMessage: string | undefined;
    private rules: val.ValidationOption[] = [];
    private valueControl: IControlValue = { id: '', name: '', type: EControlType.undefined };
    private valop: IValidationOptionControl[] = [];
    private mode: EDateInputMode;

    constructor(props: ILDateProps) {
        super(props);

        this.onChangeD = this.onChangeD.bind(this);
        this.onChangeM = this.onChangeM.bind(this);
        this.onChangeY = this.onChangeY.bind(this);
      
        if (this.props.type) { this.type = this.props.type; };
        let v = Global.toDate(this.props.value);

      

        this.value = v ? v : new Date();

        if (this.props.from) {
            this.value = this.props.from;
        };

        if (this.props.datePicker && this.props.datePicker === true) {
            this.datePicker = true;
        };

        this.mode = this.props.mode ? this.props.mode : EDateInputMode.undefined;

        if ((this.mode & EDateInputMode.withToggle) === EDateInputMode.withToggle) {
            this.textFieldDate = Global.toGermanDate(this.value);
        };

        this.valop.push(
            {
                id: 'birthday',
                options: [
                    new ValidationOption({ type: EValidationOption.regEx, message: ValidationMessages.errorDateFormat }),
                ]
            });

        this.state = {
            changed: false,
            // startDate: this.props.value ? new Date(this.props.value) : new Date()
            startDate:  this.props.value ? new Date(this.props.value) : new Date()
        };
    };

    public render() {
        let v = Global.toDate(this.value);

        this.value = v ? v : new Date();

       

        if (!this.props.preview && this.props.type !== EDateType.default) {
            this.applyValue();
        };

        if (this.props.preview) {
            return this.renderPreview(this.value);
        } else {
            return this.renderControl(this.value);
        }
    };

    private renderPreview(value: Date): JSX.Element {
        let ctrl =
            (
                <Container>
                    <Row>
                        <Col xs={3} md={3} className="np">
                            <LControl type={EControlType.info}
                                label={Global.toGermanDate(this.value)}
                                onChange={this.onChangeD} name={this.props.id + 'Day'}
                                style={this.props.style}
                                id={this.props.id + '-day'} title={this.props.title} />
                        </Col>
                    </Row>

                </Container>

            );

        return ctrl;
    };

    private renderControl(value: Date): JSX.Element {
        let dateWrapper = <LControl onClick={() => { }} type={EControlType.text} onChange={onchange} label={this.props.title} value={value}></LControl>;


        let ctrl =
            (
                <Container className={"oe-control"}>
                    {
                        this.props.mode && this.props.mode === EDateInputMode.withToggle ?
                            <Row>
                                <LControl type={EControlType.toggle}
                                    onChange={(e) => {
                                        if ((this.mode & EDateInputMode.textInput) === EDateInputMode.textInput) {
                                            this.mode &= ~EDateInputMode.textInput;
                                        } else {
                                            this.mode |= EDateInputMode.textInput;
                                        };

                                        if ((this.mode & EDateInputMode.withToggle) === EDateInputMode.withToggle) {
                                            this.textFieldDate = Global.toGermanDate(this.value);
                                        };

                                        this.setState({ changed: true });
                                    }}
                                    hideTitle={true}
                                    hideHint={true}
                                    id="switch"
                                    label="Benutzereingabemodus" />
                            </Row>
                            : null
                    }
                    <Row>
                        {(this.mode & EDateInputMode.textInput) === EDateInputMode.textInput ?
                            <>
                                <LControl
                                    id={'bdayfield'}
                                    onClick={() => { }}
                                    name={'birthday'}
                                    type={EControlType.text}
                                    onChange={e => { this.handleChangeTextInput(e) }}
                                    label={this.props.title}
                                    value={this.textFieldDate}
                                    validationOptions={
                                        [
                                            new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                            new ValidationOption({ type: EValidationOption.bdayValidation, message: '' })
                                        ]
                                    }
                                ></LControl>

                                <LControl hideTitle={true}
                                    labelStyle={'tc-hint mwa'}
                                    type={EControlType.hint}
                                    label="Die Eingabe muss im Format TT.MM.JJJJ erfolgen." />
                                <LClear />
                            </>

                            : <></>}

                        {(this.mode & EDateInputMode.textInput) === EDateInputMode.textInput ? <></> : <>
                            {
                                this.datePicker === true ?
                                    <>
                                        <DatePicker
                                            id={this.props.id}
                                            popperClassName="some-custom-class"
                                            locale="de"
                                            dateFormat={Global.toGermanDate(this.state.startDate)}
                                            onChange={this.handleChange}
                                            selected={this.state.startDate}
                                            showYearDropdown
                                            showMonthDropdown
                                            maxDate={this.props.to ? this.props.to : new Date(2500, 1, 1)}
                                            minDate={this.props.from ? this.props.from : new Date(1900, 1, 1)}
                                            popperModifiers={{
                                                offset: {
                                                    enabled: true,
                                                    offset: "60px, 10px"
                                                },
                                                preventOverflow: {
                                                    enabled: true,
                                                    escapeWithReference: false,
                                                    boundariesElement: "viewport"
                                                }
                                            }}
                                            showDisabledMonthNavigation
                                            popperPlacement="right"
                                            customInput={dateWrapper} />
                                            <Row>
                                                <Col xs={12} md={12} className="np">
                                                    <LControl  type={EControlType.date} id={"deliveryDateValidationKabel"} value={this.state.startDate} visible={this.props.hasKabel&& this.props.id==='deliveryDate'} validationOptions={
                                                        [
                                                            new ValidationOption({ type: EValidationOption.errorDeliveryDate, message: ValidationMessages.errorDeliveryDate }),
                                                        ]
                                                    } />
                                                </Col>
                                            </Row>
                                            
                                    </>
                                    :
                                    <>

                                        <Col xs={3} md={3} className="np">
                                            <LControl
                                                type={EControlType.dropdown}
                                                value={value.getDate().toString()}
                                                style={this.props.style}
                                                onChange={this.onChangeD}
                                                name={this.props.id + 'Day'}
                                                id={this.props.id + '-day'}
                                                title={this.props.title}
                                                isChild={true}
                                                options={this.getDynamicDays()} />
                                        </Col>

                                        <Col xs={5} md={5} >
                                            <LControl type={EControlType.dropdown}
                                                value={(value.getMonth() + 1)}
                                                style={this.props.style}
                                                onChange={this.onChangeM}
                                                name={this.props.id + 'Month'}
                                                id={this.props.id + '-month'}
                                                isChild={true}
                                                options={this.getDynamicMonths()} />
                                        </Col>

                                        <Col xs={4} md={4} className="np">
                                            <LControl type={EControlType.dropdown}
                                                value={value.getFullYear().toString()}
                                                style={this.props.style}
                                                onChange={this.onChangeY}
                                                name={this.props.id + 'Year'}
                                                id={this.props.id + '-year'}
                                                isChild={true}
                                                options={this.getDynamicYears()} />
                                        </Col>
                                        <Row>
                                            <Col xs={12} md={12} className="np">
                                                <LControl type={EControlType.date} id={"bdaydropdownfulldate"} value={this.value} visible={true} validationOptions={
                                                    [
                                                        new ValidationOption({ type: EValidationOption.bdayValidationVeryPrecise, message: '' }),
                                                    ]
                                                } />
                                            </Col>
                                        </Row>
                                    </>
                            }
                        </>
                        }
                    </Row>
                </Container>
            );

        return ctrl;
    };

    handleChange = date => {
        this.value.setDate(date);

        if (this.props.onChange) {
            this.props.onChange({ type: EControlType.date, name: this.props.name, id: this.props.id, dateValue: Global.dateToString(date) });
        };
        this.setState({
            changed: true,
            startDate: date
        });
    };

    public handleChangeTextInput(e: any) {
        var d = Global.fromGermanDateString(e.value);

        this.textFieldDate = e.value;

        if (d && Global.dateIsValid(d)) {
            this.value = d;
            this.props.onChange({ type: EControlType.date, name: this.props.name, id: this.props.id, dateValue: Global.dateToString(this.value) });
        };

        this.setState({ changed: true });
    };

    private onChangeD(e: IControlValue) {
        this.value.setDate(parseInt(e.value ? e.value : "1"));
        if (this.props.onChange) {
            this.props.onChange({ type: EControlType.date, name: this.props.name, id: this.props.id, dateValue: Global.dateToString(this.value) });
        };
        this.setState({ changed: true });
    };

    private onChangeM(e: IControlValue) {
        let val = parseInt(e.value ? e.value : "1") - 1;
        this.value.setMonth(val);
        if (this.props.onChange) {
            this.props.onChange({ type: EControlType.date, name: this.props.name, id: this.props.id, dateValue: Global.dateToString(this.value) });
        };
        this.setState({ changed: true });
    };

    public validate(field: IField | undefined,
        showErrors: boolean): boolean {

        if (this.rules.length === 0) {
            if (this.props.validationOptions) {
                this.props.validationOptions.map((i, idx) => {

                    this.rules.push(i);
                });
            }

            if (field && field.config.length > 0) {
                field.config.map((i, idx) => {
                    i.rules.map((ii, iidx) => {
                        this.rules.push(new val.ValidationOption(ii));
                    });
                });
            };
        }

        if (this.rules.length > 0) {
            for (let i = 0; i < this.rules.length; i++) {
                let msg: string = this.errorMessage !== undefined ? this.errorMessage : '';
                if (msg && msg.length > 0) {
                    if (showErrors) {
                        this.setState({ changed: true });
                    }
                    return false;
                }
            };
        };
        return false;
    }

    public getId() {
        return this.props.id;
    };

    public getValue(): IControlValue {
        return this.valueControl;
    };

    private onChangeY(e: IControlValue) {
        this.value.setFullYear(parseInt(e.value ? e.value : "1"));
        if (this.props.onChange) {
            this.props.onChange({ type: EControlType.date, name: this.props.name, id: this.props.id, dateValue: Global.dateToString(this.value) });
        };
        this.setState({ changed: true });
    };

    private applyValue() {
        if (this.props.type === EDateType.fixedStart && this.props.from && this.props.to) {
            if (this.props.value && (this.value < this.props.from || this.value > this.props.to)) {
                this.value = new Date(this.value.getFullYear(), this.props.from.getMonth(), this.props.from.getDate());
            }
        }
        // var m = this.value.getMonth() + 1;
        // var months = linq.from<IKeyText>(this.getDynamicMonths()).select(x=>x.key).toArray();
        // if(months.indexOf(m) <= 0){
        //     this.value = new Date(this.value.getFullYear(), months[0] - 1, this.value.getDate());
        // };
    };

    private getDynamicDays() {
        if (this.type === EDateType.default) { return Global.getDays(1, Global.getDaysInMonth(this.value)); }

        let res: number[] = [];
        let from = this.props.from ? this.props.from : new Date();
        let to = this.props.to ? this.props.to : new Date();
        let days = Global.getDaysInMonth(this.value);

        for (let i = 1; i <= days; i++) {
            let ch: Date = new Date(this.value.getFullYear(), this.value.getMonth(), i);
            if (this.props.id === 'contractEndDate') {
            }

            if (ch >= from && ch <= to) {
                res.push(i);
            }
        };

        return linq.from<number>(res)
            .select((x) => {
                return { key: x.toString(), text: x.toString() }
            }).toArray();
    };

    private getDynamicMonths(): IKeyText[] {
        if (this.type === EDateType.default) { return Global.getMonths(1); }

        let res: number[] = [];
        let from = this.props.from ? this.props.from : new Date();
        let to = this.props.to ? this.props.to : new Date();
        for (let i = 0; i <= 11; i++) {
            let ch: Date = new Date(this.value.getFullYear(), i, this.value.getDate());
            if (ch >= from && ch <= to) {
                res.push(i + 1);
            };

            if (ch >= from && ch <= to) {
                res.push(i + 1);
            }
        };

        let ml = Global.getMonths(1);

        let tmp = linq.from<number>(res)
            .join(ml, a => a, b => b.key, (a, b) => { return { N: a, M: b } })
            .toArray();

        return linq.from(tmp).select((x) => {
            return { key: x.M.key.toString(), text: x.M.text }
        }).toArray();
    };

    private getDynamicYears(): IKeyText[] {
        if (this.type === EDateType.default) { return Global.getYears(1900, (this.props.minimumBdayYear? this.props.minimumBdayYear: (new Date()).getFullYear())); }

        let res: number[] = [];
        let from = this.props.from ? this.props.from : new Date();
        let to = this.props.to ? this.props.to : new Date();

        for (let i = from.getFullYear(); i <= to.getFullYear(); i++) {
            let ch: Date = new Date(i, this.value.getMonth(), this.value.getDate());
            if (res.indexOf(i) < 0) {
                res.push(i);
            };
        };

        return linq.from<number>(res)
            .select((x) => {
                return { key: x.toString(), text: x.toString() }
            }).toArray();
    };
};

export default LDate;