import * as React from 'react';
import BaseBlock from '../../../components/page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import ProcessNewScreen from '../../../screens/ordering/process';
import { Row, Col, Container } from 'react-bootstrap';
import * as page from '../../page/index';
import * as m from '../../../models/index';
import { MainTexts, ValidationMessages } from '../../../utils/messages';
import { ECreditDebitType, EActivationType } from '../../../models/ordering/order';
import { BrandKeys } from '../../../utils/Defaults';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';

export interface IActivationMsisdnProps {
    parent: ProcessNewScreen;
    onChange: any;
};

export interface IActivationMsisdnState {
    msisdn: string;
    code: string;
    businessType: ECreditDebitType;
};

export class ActivationMsisdn extends React.Component<IActivationMsisdnProps,IActivationMsisdnState> {
    constructor(props: IActivationMsisdnProps) {
        super(props);
        
        this.state = {
            msisdn: '', code : '', 
            businessType: this.props.parent.selection ? this.props.parent.selection.creditDebitType : ECreditDebitType.postpaid
        };
    };
    
    public render() {
        let businessType:ECreditDebitType = this.props.parent.selection ? this.props.parent.selection.creditDebitType : ECreditDebitType.postpaid;

        let cfg: IBlockConfiguration = {
            title: MainTexts.msisdn,
            transparent: false
        };

        let act: m.IActivation = this.props.parent.current ? this.props.parent.current : new m.IActivation(this.props.parent.selectedBrand);

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={6} md={6}>
                            <Row>
                                <Col xs={12} md={12}>
                                    <page.LControl 
                                        preview={!this.props.parent.dataWrapper.canEditActivation(this.props.parent.currentIndex)}
                                        type={m.EControlType.text} 
                                        value={act.msisdn} 
                                        label="MSISDN" 
                                        id="msisdn" 
                                        name="msisdn" 
                                        trimValue={true}
                                        onChange={this.props.onChange}
                                        validationOptions={ 
                                            [
                                                new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                                new ValidationOption({type: EValidationOption.maxLength, maxLength: 20, message: ValidationMessages.errorMaxLength }),
                                                new ValidationOption({type: EValidationOption.numeric, message: ValidationMessages.errorNumeric })
                                            ]
                                        }/>
                                </Col>
                                {
                                    act.activationType === EActivationType.actDefault ?
                                    <Col xs={12} md={12}>
                                    <page.LControl 
                                        preview={!this.props.parent.dataWrapper.canEditActivation(this.props.parent.currentIndex)}
                                        type={m.EControlType.text} value={act.code} label={businessType === ECreditDebitType.prepaid? 'SIM-Seriennummer' : 'Aktivierungscode'} id="code" name="code" onChange={this.props.onChange}
                                        validationOptions={ 
                                            [
                                                new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                                new ValidationOption({type: EValidationOption.maxLength, maxLength: 15, message: ValidationMessages.errorMaxLength }),
                                                new ValidationOption({type: EValidationOption.numeric, message: ValidationMessages.errorNumeric })
                                            ]
                                        }/>
                                    </Col>  
                                    : null
                                }                              
                            </Row>
                        </Col>
                        {
                            act.activationType === EActivationType.actDefault ?
                            <Col xs={6} md={6}><img className="fit" src={"/images/act-" + act.brandKey + "-" + act.creditDebitType + ".jpg"} /></Col> 
                            : null
                        }
                    </Row>                                
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };
};

export default ActivationMsisdn;