import * as React from 'react';

import '../test/progressBar.scss';

export interface IProgressBarProps {
    maxValue: number;
    initialValue: number;
    
    label: string;
};

export class ProgressBar extends React.Component<IProgressBarProps> {
    constructor(props: IProgressBarProps) {
        super(props);

        this.state = {
            maxValue: '',
            initialValue: '',          
            label: '',
        };
    };



    public render() {

        let ctr = <></>;

        ctr = (
            <div>
           
            <progress id="bar" max={this.props.maxValue} value={this.props.initialValue}></progress>
            <label className="lowerLabel" htmlFor="bar">   {this.props.label}  </label>
            </div>
     );

        return ctr;
    }



};

export default ProgressBar;