import React, { Component, Children } from 'react';
import * as ReactDOM from 'react-dom';
import * as page from '../../components/page';
import { AppIds } from '../../utils/Defaults';
import { ERequestStatus, IDefCon } from '../../models';
import BaseBlock from '../../components/page/block';
import { Container, Row, Col } from 'react-bootstrap';
import { IBlockConfiguration } from '../../models/block/configuration';
import { DefConTexts } from '../../utils/messages';

export interface IDefConScreenProps {
    dc?: IDefCon;
};

export class DefConScreen extends React.Component<any, any> {

    constructor(props) {
        super(props);
    };

    public render() {
        let ctrl = (
            <> 
                <page.Inner1C style="tiny">
                    <>
                        {this.renderData()}
                    </>
                </page.Inner1C>
            </>
        );

        return ctrl;
    };

    public renderData() {
        let cfg: IBlockConfiguration = {
            title: DefConTexts.underConstruction,
            transparent: false
        };

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={12} md={12}>
                            <div className="etitle">Beschreibung</div>
                        </Col>                        
                    </Row>                                
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };    
};

export default DefConScreen;