import * as React from 'react';
import ProcessNewScreen from '../../../screens/ordering/process';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { Row, Col, Container } from 'react-bootstrap';
import BaseBlock from '../../../components/page/block';
import ActivationCustomerData from '../process/activationCustomerData';
import * as page from '../../page/index';
import { EControlType } from '../../../models/page/diverse';
import { IEvn } from '../../../models/ordering/evn';
import { EBillingType, EEvnType, ETargetNumber } from '../../../models/ordering/order';
import { OrderingTexts, MainTexts } from '../../../utils/messages';

export interface ICustomerEVNProps {
    parent: ActivationCustomerData;
    evn?: IEvn;
    onChange: any;
};

export interface ICustomerEVNState {
    changed: boolean;
};

export class CustomerEVN extends React.Component<ICustomerEVNProps, ICustomerEVNState> {
    constructor(props: ICustomerEVNProps) {
        super(props);

        this.state = {
            changed: false
        };
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.customerBills,
            transparent: false
        };

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={12} md={12} className="np">
                            <page.LControl type={EControlType.title} 
                                title={OrderingTexts.billType}/>
                        </Col>
                    </Row>

                    {
                        this.props.parent.props.parent.dataWrapper.hasVodafone() || this.props.parent.props.parent.dataWrapper.hasDsl()  ? 
                        <Row>
                            <Col xs={6} md={6}>
                                <page.LControl onChange={this.props.onChange} checked={this.props.evn != null && this.props.evn.billingType == EBillingType.online} 
                                type={EControlType.radio} hideTitle={true} name="evn.billingType" id="billingType_2" label="Online"/>
                            </Col>
                        </Row>
                        :
                        <Row>
                            <Col xs={6} md={6} className="np">
                                <page.LControl onChange={this.props.onChange} checked={this.props.evn != null && this.props.evn.billingType == EBillingType.email} 
                                    type={EControlType.radio} hideTitle={true} name="evn.billingType" id="billingType_1" label="per E-Mail "/>
                            </Col>
                            <Col xs={6} md={6}>
                                <page.LControl onChange={this.props.onChange} checked={this.props.evn != null && this.props.evn.billingType == EBillingType.online} 
                                type={EControlType.radio} hideTitle={true} name="evn.billingType" id="billingType_2" label="Online und App"/>
                            </Col>
                        </Row>                        
                    }

                    <Row>
                        <Col xs={12} md={12} className="np">
                            <page.LControl hideTitle={true} type={EControlType.hint} label={OrderingTexts.evnDesc}/>
                            <page.LClear />
                        </Col>
                    </Row> 

                    { 
                        this.props.parent.props.parent.dataWrapper.hasOtelo() || this.props.parent.props.parent.dataWrapper.hasVodafone() ?
                        <>
                            <Row>
                                <Col xs={12} md={12} className="np">
                                    <page.LControl type={EControlType.title} title={OrderingTexts.evn}/>
                                </Col>
                            </Row>                        
                            <Row>
                                <Col xs={4} md={4} className="np">
                                    <page.LControl onChange={this.props.onChange} checked={this.props.evn != null && this.props.evn.evnType == EEvnType.fullEvn} 
                                        type={EControlType.radio} name="evn.evnType" id="evnType_1" hideTitle={true}
                                        label={this.props.parent.props.parent.dataWrapper.hasVodafone() ? OrderingTexts.evnVf1 : OrderingTexts.evn1}/>
                                </Col>
                                <Col xs={4} md={4} >
                                    <page.LControl onChange={this.props.onChange} 
                                        checked={this.props.evn != null && this.props.evn.evnType == EEvnType.shortEvn} 
                                        type={EControlType.radio} name="evn.evnType" id="evnType_2" hideTitle={true}
                                        label={this.props.parent.props.parent.dataWrapper.hasVodafone() ? OrderingTexts.evnVf2 : OrderingTexts.evn2}/>
                                </Col>        
                                <Col xs={4} md={4} className="npr">
                                    <page.LControl onChange={this.props.onChange} 
                                        checked={this.props.evn != null && this.props.evn.evnType == EEvnType.noEvn} 
                                        type={EControlType.radio} name="evn.evnType" id="evnType_3" hideTitle={true}
                                        label={this.props.parent.props.parent.dataWrapper.hasVodafone() ? OrderingTexts.evnVf3 : OrderingTexts.evn3}/>
                                </Col>                                          
                            </Row>
                        </>
                        : null
                    }

                    { 
                        this.props.parent.props.parent.dataWrapper.hasVodafone() && (this.props.parent.props.parent.dataWrapper.data.customer.evn && this.props.parent.props.parent.dataWrapper.data.customer.evn.evnType != EEvnType.noEvn)?
                        <>
                            <Row>
                                <Col xs={12} md={12} className="np">
                                    <page.LControl type={EControlType.title} title={OrderingTexts.targetNumber}/>
                                </Col>
                            </Row>                        
                            <Row>
                                <Col xs={4} md={4} className="np">
                                    <page.LControl onChange={this.props.onChange} checked={this.props.evn != null && this.props.evn.targetNumberType == ETargetNumber.complete} 
                                        hideTitle={true}
                                        type={EControlType.radio} name="evn.targetNumberType" id="targetNumberType_1" 
                                        label={MainTexts.complete}/>
                                </Col>
                                <Col xs={4} md={4} >
                                    <page.LControl onChange={this.props.onChange} 
                                        hideTitle={true}
                                        checked={this.props.evn != null && this.props.evn.targetNumberType == ETargetNumber.shortened} 
                                        type={EControlType.radio} name="evn.targetNumberType" id="targetNumberType_2" 
                                        label={MainTexts.shortened}/>
                                </Col>                                         
                            </Row>
                        </>
                        : null
                    }                    
                    
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };    
};

export default CustomerEVN;