export class ModelStateError {
    public key: string = '';
    public value: string = '';
    public path: string = '';

    public constructor(k:string, v: string, p: string ) {
        this.key = k;
        this.value = v;
        this.path = p;
    };
};

export class ModelState {
    public errors: ModelStateError[] = [];
    public specialCase: boolean = false;

    public errorMessage: string = '';

    public add(err: ModelStateError) {
        this.errors.push(err);
    };

    public addValues(key: string, value: string, path: string) {
        this.add(new ModelStateError(key, value, path));
    };

    public isValid() : boolean {
        return !(this.errors.length > 0) && (this.errorMessage === '');
    };

    public reset() {
        this.errors = [];
        this.errorMessage = '';
    };
};

export default ModelState;