import * as React from 'react';

export interface IPopupBodyProps {
    children?: JSX.Element[];
};

export class PopupBody extends React.Component<IPopupBodyProps, any> {
    constructor(props: IPopupBodyProps) {
        super(props);
    };

    public render() {
        let ctrl:JSX.Element =
        (
        <div>
            {this.props.children}
        </div>
        );

        return ctrl;
    };
};

export default PopupBody;