import * as linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import * as page from '../../../components/page/index';
import { EApiResponseStatus, IApiResponse, IApiSummary, IProcessStatus, ISocketPushModel } from '../../../interfaces';
import { EFormMode, ELButtonType, OrderDataWrapper } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import * as m from '../../../models/index';
import IBrand from '../../../models/ordering/brand';
import { EActivationType, EBusinessCase, EServiceType } from '../../../models/ordering/order';
import IProduct, { ISubConfig, ISubServiceConfig } from '../../../models/ordering/product';
import IService from '../../../models/ordering/service';
import CustomerScreen from '../../../screens/existing/customer';
import Api from '../../../utils/api';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import GlobalJSX from '../../../utils/GlobalJSX';
import { ActionTexts, MainTexts, OrderingTexts } from '../../../utils/messages';
import EasyRouter from '../../../utils/router';
import StorageHelper from '../../../utils/storage';
import { EPopupType, IPopupConfig, IPopupProps } from '../../dialogs/popup';
import TariffThumb from '../../ordering/tariffThumb';
import { LClear } from '../../page';
import BaseBlock from '../../page/block';

export interface IExistingCustomerServicesProps {
    parent: CustomerScreen;
};

export interface IServiceConfig {
    id: string;
    name: string;
    booked: boolean;
    tooltip: string;
    mandatory?: boolean;
    disabled?: boolean;
};

export interface ILinkConfig {
    text: string;
    onClick: any;
};

export class ServiceConfigUpdate {
    booked: IServiceConfig[] = [];
    bookable: IServiceConfig[] = [];

    add: string[] = [];
    remove: string[] = [];

    public canUpdate() {
        return this.add.length > 0 || this.remove.length > 0;
    };
};

export interface IServiceUpdate {
    id: string;
    brandKey: string;
    addServices: string[];
    removeServices: string[];
    type: m.ERequestType;
};

export interface IExistingCustomerServicesState {
    changed: boolean;
    status: m.ERequestStatus;
    confirmAddRemove: boolean;
    mode: EFormMode;
};

export class ExistingCustomerServices extends React.Component<IExistingCustomerServicesProps, IExistingCustomerServicesState> {
    public static containerId = 'existingCustomerServices';
    //private brandKey: string;
    private lastApi?: IApiResponse;
    private activtion: m.IActivation;
    private serviceUpdate: ServiceConfigUpdate = new ServiceConfigUpdate();
    private removedBookableOptions: string[] = [];

    constructor(props: IExistingCustomerServicesProps) {
        super(props);

        this.state = {
            changed: false,
            status: m.ERequestStatus.undefined,
            confirmAddRemove: false,
            mode: EFormMode.view
        };

        this.addRemoveService = this.addRemoveService.bind(this);
        this.onDismissPopup = this.onDismissPopup.bind(this);
        this.onSave = this.onSave.bind(this);
        this.socketCallback = this.socketCallback.bind(this);
        this.renderPopupConfirmService = this.renderPopupConfirmService.bind(this);
        this.onConfirm = this.onConfirm.bind(this);
        this.switchMode = this.switchMode.bind(this);

        this.activtion = this.props.parent.data.activations[0];

        this.serviceUpdate = this.prepareServices();
    };

    public render() {
        this.activtion = this.props.parent.data.activations[0];

        let cfg: IBlockConfiguration = {
            title: OrderingTexts.services,
            id: ExistingCustomerServices.containerId,
            transparent: false,
            style: '',
            styleContainer: '',
            collapsible: true,
            collapsed: false
        };

        let ctrl =
            <div >
                {this.renderPopup()}
                {this.renderPopupConfirmService()}
                <Container>
                    <Row>
                        <Col md={12} className="np">
                            {this.renderServices()}
                        </Col>
                    </Row>
                </Container>
            </div>;

        ctrl = <BaseBlock config={cfg}>
            {ctrl}
        </BaseBlock>

        return ctrl;
    };

    public switchMode(newMode: EFormMode) {
        this.setState({ mode: newMode });
    };

    private renderServices(): JSX.Element {
        let ctrl: JSX.Element = <></>;
        //let serviceConfig = this.prepareServices();

        ctrl = (

            <Container className="container-action">
                <Row>
                    <Col md={12}>
                        <div className="subtitle">{MainTexts.bookedServices}</div>
                        <div>
                            {
                                this.serviceUpdate.booked.map((i, idx) => (this.renderThumb(i, this.addRemoveService, this.isSelected(i.id), idx, true)))
                            }
                            <LClear />
                        </div>
                        <div className="spacer" />
                        {
                            this.props.parent.data.customer.contracts ? // && this.props.parent.data.customer.contracts[0].brandKey === BrandKeys.otelo ? 
                                <>
                                    <div className="subtitle">{MainTexts.bookableServices}</div>
                                    <div>
                                        {
                                            this.serviceUpdate.bookable.map((i, idx) => (this.renderThumb(i, this.addRemoveService, this.isSelected(i.id), idx, false)))
                                        }
                                        <LClear />
                                    </div>
                                </>
                                : null
                        }
                    </Col>
                </Row>

                {
                    this.props.parent.data.customer.contracts ? // && this.props.parent.data.customer.contracts[0].brandKey === BrandKeys.otelo ?                
                        <Row>
                            <Col xs={12} md={12} >
                                {
                                    this.state.mode === EFormMode.view ?
                                        <page.LButton
                                            onClick={() => {
                                                this.setState({ mode: EFormMode.edit });
                                            }}
                                            text={MainTexts.change}
                                            type={ELButtonType.buttonWithIconRight}
                                            css="action fr"
                                            icon="arrow-right" />
                                        :
                                        <>
                                            <page.LButton
                                                text={MainTexts.saveOptions}
                                                onClick={this.onConfirm}
                                                type={ELButtonType.buttonWithIconRight}
                                                css={"action fr"}
                                                icon="arrow-right" />

                                            <page.LButton
                                                onClick={() => {
                                                    this.serviceUpdate = this.prepareServices();
                                                    this.setState({ mode: EFormMode.view });
                                                    //this.setState({changed: true});
                                                }}
                                                text={MainTexts.cancel}
                                                type={ELButtonType.linkSimple}
                                                css="action3 fr"
                                                icon="chevron" />
                                        </>
                                }

                            </Col>
                        </Row>
                        : null
                }
            </Container>
        );

        return ctrl;
    };

    private renderThumb(service: IServiceConfig, clickHandler: any, selected: boolean, idx: number, booked: boolean): JSX.Element {
        let link: ILinkConfig | undefined = {
            text: selected ? MainTexts.remove : MainTexts.book, onClick: (t) => {
                //this.addRemoveService(t.props.id);
                //this.setState({changed: true});
                //t.preventDefault();
                //t.stopPropagation();
                //return false;
            }
        };

        let css: string = selected ? ' sel ' : '';

        css += (this.state.mode === EFormMode.view ? '' : 'thumb-service');

        //configure thumb
        if (this.activtion.brandKey === BrandKeys.vodafone) {
            if (booked) {
                //booked services cannot be removed for vodafone
                link = undefined;
                css = ' sel ';
            };
        };

        //links not visible in view mode
        if (this.state.mode === EFormMode.view) {
            link = undefined;
        };

        let ctrl: JSX.Element = service.disabled ? <></> : <TariffThumb
            key={service.id + '_' + Global.getUniqueId()}
            idx={idx}
            text={service.name}
            tooltip={service.tooltip}
            tooltipModel={service}
            selected={selected}
            id={service.id}
            brandKey={this.activtion.brandKey}
            styleBrand={false}
            cssStyle={css}
            action={link}
            onClick={(e) => {
                this.addRemoveService(e);
                this.setState({ changed: true });
            }} />

        return ctrl;
    };

    private isSelected(e: any): boolean {
        let bookedAll: string[] = linq.from<IServiceConfig>(this.serviceUpdate.booked).select(x => x.id).toArray();
        let toAdd: boolean = this.serviceUpdate.add.indexOf(e) >= 0;
        let toRemove: boolean = this.serviceUpdate.remove.indexOf(e) >= 0;
        let booked: boolean = bookedAll.indexOf(e) >= 0;

        return (toAdd || booked) && (!toRemove);
    };

    private onConfirm() {
        if (this.activtion.brandKey === BrandKeys.vodafone) {
            let c = this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0] : {};
            this.props.parent.data.activations[0].activationType = EActivationType.bookService;
            this.props.parent.data.activations[0].productId = c.bookedProduct ? c.bookedProduct.id : '';
            this.props.parent.data.activations[0].sub = c.bookedProduct ? c.bookedProduct.sub : '';
            this.props.parent.data.activations[0].services = this.serviceUpdate.add;

            let d = OrderDataWrapper.createProcess(EBusinessCase.bookService, EActivationType.bookService, this.props.parent.data, undefined);
            this.props.parent.onNext(d);
            //this.props.parent.refresh();

            EasyRouter.route({ url: '/customer/done/' + d.id, title: 'Dienste aendern ' + OrderDataWrapper.getMsisdnFromContract(d) });
            return;
        };

        this.setState({ confirmAddRemove: true });
    };

    private onSave() {
        let cfg: IPopupConfig = {
            props: {
                id: 'dynamicPopup',
                title: 'Dienste ndern',
                visible: true,
                closable: false,
                type: EPopupType.pleaseWait
            },
            content: <div>{OrderingTexts.customerData}</div>
        };

        let update: IServiceUpdate = {
            type: m.ERequestType.customerChangeAddRemoveServices,
            brandKey: this.activtion.brandKey,
            id: this.props.parent.data.id,
            addServices: this.serviceUpdate.add,
            removeServices: this.serviceUpdate.remove
        };

        this.props.parent.processCustomer(() => {
            Global.hub.send(JSON.stringify(update),
                m.ERequestType.customerChangeAddRemoveServices,
                undefined);
        }, cfg);
    };

    private onDismissPopup() {
        this.lastApi = undefined;
        this.setState({ status: m.ERequestStatus.undefined });
    };

    private addRemoveService(e: any) {
        if (this.state.mode === EFormMode.view) { return };

        let bookedAll: string[] = linq.from<IServiceConfig>(this.serviceUpdate.booked).select(x => x.id).toArray();
        let toAdd: boolean = this.serviceUpdate.add.indexOf(e) >= 0;
        let toRemove: boolean = this.serviceUpdate.remove.indexOf(e) >= 0;
        let booked: boolean = bookedAll.indexOf(e) >= 0;


        //remove vf services disabled
        if (booked && this.props.parent.data.activations[0].brandKey == BrandKeys.vodafone) {
            return;
        };
        
        //element is in added array so remove it
        if (toAdd) {
            Global.removeElementFromArray(this.serviceUpdate.add, e);
        } else if (!toAdd && !booked) {
            this.serviceUpdate.add.push(e);
        } else if (booked && !toRemove) {
            this.serviceUpdate.remove.push(e);
        } else if (booked && toRemove) {
            Global.removeElementFromArray(this.serviceUpdate.remove, e);
        };

        let svcExc = this.props.parent.dataWrapper.getAllExcludesForSelectedServices(BrandKeys.vodafone, [e]);

        if (svcExc) {
            if (!toAdd && !booked) {
                svcExc.forEach(x => {
                    let index = this.serviceUpdate.bookable.findIndex(y => y.id == x);
                    if (index !== -1) {
                        this.serviceUpdate.bookable[index].disabled = true;
                    }
                })
            } else if (toAdd) {
                this.serviceUpdate.bookable.forEach(x => {
                    x.disabled = false;
                })
            }

        }
        //this.setState({changed: true});
    };

    private prepareServices(): ServiceConfigUpdate {
        let res: ServiceConfigUpdate = new ServiceConfigUpdate();

        let brand = linq.from<IBrand>(StorageHelper.getBrands())
            .where(x => x.id == this.activtion.brandKey)
            .first();

        if (this.props.parent.data.customer.contracts
            && this.props.parent.data.customer.contracts[0].bookedProduct
            && this.props.parent.data.customer.contracts[0].bookedProduct.services) {
            let prodId = this.props.parent.data.customer.contracts[0].bookedProduct.id;
            let subId = this.props.parent.data.customer.contracts[0].bookedProduct.sub;
            let bookableServices: string[] = this.props.parent.data.customer.contracts[0].bookableServices || [];

            let bookedServices = this.props.parent.data.customer.contracts[0].bookedProduct.services;
            let prod = linq.from<IProduct>(brand.products)
                .where(x => x.id == prodId)
                .firstOrDefault();

            let bookable: IServiceConfig[] = [];
            /*product not fonund so its old otelo tarif - so product is not found*/
            if (prod) {
                let sub = linq.from<ISubConfig>(prod.subs).where(x => x.id === subId).firstOrDefault();



                if (brand.id == BrandKeys.vodafone) {
                    sub = linq.from<ISubConfig>(prod.subs).where(x => x.id === subId && x.b == EBusinessCase.bookService).firstOrDefault();

                };

                if (sub) {
                    let svcIds = linq.from<IService>(brand.services)
                        .where(x => x.type === EServiceType.option)
                        .select(x => x.id)
                        .toArray();

                    bookable = linq.from<ISubServiceConfig>(sub.sl)
                        .where(x => bookedServices.indexOf(x.id) == -1 && bookableServices.indexOf(x.id) >= 0)
                        .select(x => {
                            let svc = linq.from<IService>(brand.services)
                                .where(y => y.id == x.id) // && !x.m)
                                .first();

                            let s: IServiceConfig = {
                                id: svc.id,
                                name: svc.name,
                                booked: false,
                                mandatory: svc.mandatory,
                                tooltip: svc && svc.tooltip ? svc.tooltip : ''
                            };

                            return s;
                        })
                        .where(x => !x.mandatory)
                        .toArray();

                    /** */
                    if (brand.id === BrandKeys.vodafone) {

                        let selected = this.props.parent.data.customer.contracts && this.props.parent.data.customer.contracts[0].bookedProduct ?
                            this.props.parent.data.customer.contracts[0].bookedProduct.services : [];

                        let svc64 = linq.from<ISubServiceConfig>(sub.sl).select(x => x.id).toArray();
                        let svcExc = this.props.parent.dataWrapper.getAllExcludesForSelectedServices(BrandKeys.vodafone, selected);

                        //  bookable = linq.from<IServiceConfig>(bookable).where(x=>svc64.indexOf(x.id)>=0).toArray();
                        if (svc64) {
                            svc64.forEach(x => {
                                if (selected.indexOf(x) == -1 && svcExc.indexOf(x) == -1) {

                                    let svc = linq.from<IService>(brand.services)
                                        .where(y => y.id == x)
                                        .firstOrDefault() || {name: '', tooltip: ''};

                                    let temp: IServiceConfig = {
                                        id: x,
                                        name: svc ? svc.name : x,
                                        booked: false,
                                        tooltip: svc.tooltip ? svc.tooltip : ''
                                    };

                                    bookable.push(temp)
                                }
                            })
                        }

                        //  bookable = linq.from<IServiceConfig>(bookable).where(x=>svcExc.indexOf(x.id)<0).toArray();
                    };
                };
            }

            bookedServices.map((i, idx) => {
                let svc = linq.from<IService>(brand.services)
                    .where(x => x.id == i)
                    .firstOrDefault();

                let temp: IServiceConfig = {
                    id: i,
                    name: svc ? svc.name : i,
                    booked: true,
                    tooltip: svc && svc.tooltip ? svc.tooltip : ''
                };

                res.booked.push(temp);
            });

            if (brand.id === BrandKeys.otelo && !prod) {
                bookable = linq.from<IService>(brand.services)
                    .where(x => bookedServices.indexOf(x.id) == -1 && bookableServices.indexOf(x.id) >= 0)
                    .select(x => {
                        let s: IServiceConfig = {
                            id: x.id,
                            name: x.name,
                            booked: false,
                            mandatory: x.mandatory,
                            tooltip: x && x.tooltip ? x.tooltip : ''
                        };

                        return s;
                    })
                    .where(x => !x.mandatory)
                    .toArray();
            };

            bookable.map((i, idx) => {
                res.bookable.push(i);
            });
        }

        return res;
    };

    private renderPopup(): JSX.Element {
        let cfg: IPopupProps = {
            title: ActionTexts.requestInProcess,
            closable: this.state.status !== m.ERequestStatus.running,
            visible: this.state.status !== m.ERequestStatus.undefined && this.state.status !== m.ERequestStatus.done,
            id: "existingCustomerServiceUpdate",
            type: EPopupType.pleaseWait,
            onDismiss: this.onDismissPopup
        };

        let content: JSX.Element = <></>;

        if (this.lastApi && this.lastApi.status != EApiResponseStatus.ok) {
            let pr: IProcessStatus = JSON.parse(this.lastApi.data);
            let sum: IApiSummary = Api.parseSummary(this.lastApi, pr);

            cfg.closable = true;
            cfg.visible = true;
            cfg.type = EPopupType.info;
            content = <div>{sum.message}</div>
        };

        let ctrl = GlobalJSX.GetPopup(cfg, content, undefined);

        return ctrl;
    };

    private renderPopupConfirmService(): JSX.Element {
        let cfg: IPopupProps = {
            title: MainTexts.serviceAddRemove,
            closable: true,
            id: "addRemoveServicesPopup",
            visible: false,
            height: 200,
            textPositive: MainTexts.yes,
            textNegative: MainTexts.no,
            type: EPopupType.confirmCustom,
            onDismiss: () => {
                this.setState({ confirmAddRemove: false })
            },
            onNegative: () => {
                this.setState({ confirmAddRemove: false })
            },
            onPositive: () => {
                if (this.serviceUpdate.canUpdate()) {
                    this.setState({ confirmAddRemove: false }, () => {
                        this.onSave();
                    });
                } else {
                    this.setState({ confirmAddRemove: false })
                };
            }
        };

        let content: JSX.Element = <div>Buchen von Optionen ist Kostenpflichtig.</div>;

        if (!this.serviceUpdate.canUpdate()) {
            cfg.textPositive = "OK";
            cfg.textNegative = '';
            content = <div>Keine Korrekturen</div>;
        };

        if (this.state.confirmAddRemove) {
            cfg.visible = true;
        };

        let ctrl = GlobalJSX.GetPopup(cfg, content, undefined);

        return ctrl;
    };

    public socketCallback(sock: ISocketPushModel) {
        let api: IApiResponse = JSON.parse(sock.data);
        this.lastApi = api;
        let inner = api.data;

        if (api.error) {
            this.setState({ status: m.ERequestStatus.failed });
            return;
        };

        if (inner == null) {
            return;
        };

        let wrapper = JSON.parse(inner.data);

        let customer = JSON.parse(wrapper.steps[0].message);

        StorageHelper.addOrder(this.props.parent.data);

        this.setState({ status: m.ERequestStatus.done });
    };
};

export default ExistingCustomerServices;