import * as React from 'react';
import { App } from "../../App";
import * as constants from '../../constants';
import { ERAPIAction, IApiResponse, ISocketPushModel } from "../../interfaces";
import { ERequestType } from "../../models";
import ISignalrMessage from '../../models/global/ISignalrMessage';
import { EditUserScreen } from '../../screens/am/editUser';
import { MnpStatusScreen } from '../../screens/am/mnpStatus';
import * as scr from '../../screens/insex';
import { BelatedPorting, StatusPorting } from '../../screens/insex';
import { AppIds, StorageKeys } from "../../utils/Defaults";
import Global from "../../utils/Global";
import { MainTexts } from '../../utils/messages';
import EasyRouter from '../../utils/router';
import StorageHelper from '../../utils/storage';
import ExistingPartnerCard from '../existing/data/partnerCard';
import PreviewContract from "../ordering/preview/contract";
import { ActivationAvailability, ActivationSummary } from "../ordering/process";
import ToolSearchExisting from './searchExisting';

const buildEnv = constants.build;


declare global {
    interface Window { jQuery: any; }
};

window.jQuery = window.jQuery || {};

let apiProxy = {};




/// <reference path="..\..\SignalR.Client.JS\Scripts\jquery-1.6.4.js" />
/// <reference path="jquery.signalR.js" />
(function ($, window, undefined) {
    /// <param name="$" type="jQuery" />
    "use strict";

    if (typeof ($.signalR) !== "function") {
        throw new Error("SignalR: SignalR is not loaded. Please ensure jquery.signalR-x.js is referenced before ~/signalr/js.");
    }

    let signalR = $.signalR;

    function makeProxyCallback(hub, callback) {
        return function () {
            // Call the client hub method
            callback.apply(hub, $.makeArray(arguments));
        };
    }

    function registerHubProxies(instance, shouldSubscribe) {
        let key, hub, memberKey, memberValue, subscriptionMethod;

        for (key in instance) {
            if (instance.hasOwnProperty(key)) {
                hub = instance[key];

                if (!(hub.hubName)) {
                    // Not a client hub
                    continue;
                }

                if (shouldSubscribe) {
                    // We want to subscribe to the hub events
                    subscriptionMethod = hub.on;
                } else {
                    // We want to unsubscribe from the hub events
                    subscriptionMethod = hub.off;
                }

                // Loop through all members on the hub and find client hub functions to subscribe/unsubscribe
                for (memberKey in hub.client) {
                    if (hub.client.hasOwnProperty(memberKey)) {
                        memberValue = hub.client[memberKey];

                        if (!$.isFunction(memberValue)) {
                            // Not a client hub function
                            continue;
                        }

                        // Use the actual user-provided callback as the "identity" value for the registration.
                        subscriptionMethod.call(hub, memberKey, makeProxyCallback(hub, memberValue), memberValue);
                    }
                }
            }
        }
    }

    function _registerSubscribedHubs() {
        let connection = $.hubConnection;
        if (!connection._subscribedToHubs) {
            connection._subscribedToHubs = true;
            connection.starting(function () {
                // Set the connection's data object with all the hub proxies with active subscriptions.
                // These proxies will receive notifications from the server.
                let subscribedHubs = [] as any;

                $.each(connection.proxies, function (key) {
                    if ($.hubConnection.hasSubscriptions()) {
                        subscribedHubs.push({ name: key });
                        connection.log("Client subscribed to hub '" + key + "'.");
                    }
                });

                if (subscribedHubs.length === 0) {
                    connection.log("No hubs have been subscribed to.  The client will not receive data from hubs.  To fix, declare at least one client side function prior to connection start for each hub you wish to subscribe to.");
                }

                connection.data = connection.json.stringify(subscribedHubs);
            });
        }
    };


    const cnnDev: string = 'https://api.easy3.com/api/hub';

    let cnn: string = cnnDev; // dev by default

    if (buildEnv === constants.BuildEnv.Dev) {
        cnn = cnnDev;
    } else if (buildEnv === constants.BuildEnv.Int) {
        cnn = 'https://easy3.tradelight.net/r/api/hub';
    } else if (buildEnv === constants.BuildEnv.Prod) {
        cnn = 'https://api.vf-easy.de/r/api/hub';
    } else if (buildEnv === constants.BuildEnv.DevInt) {
        cnn = 'https://easy3.tradelight.net/r/api/hub';
    }

    console.log('hub:' + cnn);

    signalR.hub = $.hubConnection(cnn, { useDefaultPath: false });

    $.extend(signalR, signalR.hub._registerSubscribedHubs());

    $.hubConnection.prototype.createHubProxies = function () {
        console.log('proxies created');
        let proxies = {};
        this.starting(function () {
            // Register the hub proxies as subscribed
            // (instance, shouldSubscribe)
            registerHubProxies(proxies, true);

            signalR.hub._registerSubscribedHubs();
        }).disconnected(function () {
            // Unsubscribe all hub proxies when we "disconnect".  This is to ensure that we do not re-add functional call backs.
            // (instance, shouldSubscribe)
            registerHubProxies(proxies, false);
        });

        proxies['trustlightApiHub'] = this.createHubProxy('trustlightApiHub');
        proxies['trustlightApiHub'].client = {};

        proxies['trustlightApiHub'].server = {
            onSend: function (message) {
                return proxies['trustlightApiHub'].invoke.apply(proxies['trustlightApiHub'], $.merge(["onSend"], $.makeArray(arguments)));
            }
        };

        apiProxy = proxies['trustlightApiHub'];

        return proxies;
    };

    $.extend(signalR, signalR.hub.createHubProxies());

}(window.jQuery, window));

export class SignalRClient {
    //private callback: any;
    private hub: any;
    private id: string;
    public connectionId: string = '';

    public started: boolean = false;

    constructor(
        //callback: any,
        autostart: boolean,
        id: string) {
        //this.callback = callback;
        this.started = false;
        this.id = id;

        //this.callback = this.callback.bind(this);
        this.push = this.push.bind(this);
        this.auto = this.auto.bind(this);

        if (autostart) { this.start(); }

        this.send = this.send.bind(this);
    };

    public send(message: string, requestType: ERequestType, callback: any) {
       
        try {
            let h = ($.connection as any).trustlightApiHub;
            let m: ISignalrMessage = {
                type: requestType,
                connectionId: this.connectionId,
                data: message,
                url: window.location.href //Api.getAppUrl()
            };
            
            if(buildEnv != constants.BuildEnv.Prod) { console.log('hub send: ' + message.length, m); }
            h.server.onSend(JSON.stringify(m));
           
            if (callback) { callback(); };
            
        } catch (e) {
            console.log('!!!!!!!!!!!!!!!!!errror!!!!!!!!!!!!!!!!!!!!');
            
            console.log(e);
            if (callback) { callback(e); };
        }
    };

    public start() {
        this.hub = ($.connection as any).trustlightApiHub;

        $.connection.hub.disconnected(function () {
            console.log('!!!!!!!!!!!hub disconnected!!!!!!!!!!!!!');
            setTimeout(function () {
                $.connection.hub.start();
            }, 1000); // Restart connection after 5 seconds.
        });

        console.log('hub started');

        this.hub.on("pushResponse", (o) => { });

        this.hub.client.pushResponse = this.push;

        $.connection.hub.start({ pingInterval: 60000, transport: ['webSockets', 'longPolling'] })
            .done((e) => {
                this.started = true;
                this.connectionId = this.hub.connection.id;
                console.log('hub started: ' + this.connectionId);
            })
            .fail(function (e) {
                console.log('!!!!!!!!!!!!!!!!!hub error!!!!!!!!!!!!!!!!!!!!!!');
                console.log(e);
            });
    };

    private push(u: string) {
        this.auto(u);
    };

    public stop() {
        console.log('hub stopped');
        this.started = false;
        $.connection.hub.stop();
    };

    private auto(u: string) {
        Global.parseSocketAndApiResponse(u, (a: IApiResponse, s: ISocketPushModel) => {
            if (!s.data) {
                return;
            };

            if(Global.trackSession) {
                Global.trackSession(a);
            };

            if (a.error && a.error.action) {
                if (a.error.action.type === ERAPIAction.login) {
                    window.location.href = a.error.action.url;
                    return;
                }
            };

            if (a.error) {
                if (a.error.code == '403' || a.error.code == 'Forbidden') {
                    App.setContainer(<scr.ErrorScreen data={{ code: 403 }} />, AppIds.container);

                    return;
                };

                if (a.error.code == '404' || a.error.code == 'NotFound') {
                    App.setContainer(<scr.ErrorScreen data={{ code: 404 }} />, AppIds.container);

                    return;
                };
            };

            console.log('socket receive:' + s.type);
            if(buildEnv != constants.BuildEnv.Prod) { console.log('hub receive: ' + s.type, s); }

            if (s.type === ERequestType.loadDraft) {
                let order = JSON.parse(a.data.data);
                order.customer = JSON.parse(order.model);
                StorageHelper.resetOrders();
                
                order.currentBrand = order.activations[0].brandKey;
                order.currentCat = order.activations[0].brandKey;

                StorageHelper.addOrder(order);

                if(a.data.avl) {
                    a.data.avl.id = a.data.avl.brands[0].id;
                    StorageHelper.set(StorageKeys.keyAvailability, a.data.avl); 
                };
               
                let u = '/ordering/new/tariff/' + order.Id;
                EasyRouter.route({ url: u, title: MainTexts.draft });
            } else if (s.type === ERequestType.defconChanged) {
                let receiver = Global.GetComponent<App>(App.containerId);
                if (receiver) { receiver.socketCallback(s); }
            } else if (s.type === ERequestType.customerGroupGet || s.type === ERequestType.customerGroupCreate) {
                let receiver = Global.GetComponent<ExistingPartnerCard>(ExistingPartnerCard.containerId);
                if (receiver) { receiver.socketCallback(s); }
            } else if (s.type === ERequestType.loadCustomer) {
                let receiver = Global.GetComponent<ToolSearchExisting>(ToolSearchExisting.containerId);
                if (receiver) { receiver.socketCallback(s); }
            } else if (s.type === ERequestType.checkAvailability) {
                StorageHelper.parseAvailability(s);
                let receiverAvl = Global.GetComponent<ActivationAvailability>(ActivationAvailability.containerId);
                if (receiverAvl) { receiverAvl.socketCallback(s); }

                let receiverCust = Global.GetComponent<scr.CustomerScreen>(scr.CustomerScreen.containerId);
                if (receiverCust) { receiverCust.applyAvailability(); }
            } else if (s.type === ERequestType.startApp) {
                let receiver = Global.GetComponent<scr.InitScreen>(scr.InitScreen.containerId);
                if (receiver) { receiver.socketCallback(a); }
            } else if (s.type === ERequestType.getDrafts) {
                let receiver = Global.GetComponent<scr.DraftsScreen>(scr.DraftsScreen.containerId);
                if (receiver) { receiver.socketCallback(a); }
            } else if (s.type === ERequestType.getContracts) {
                let receiver = Global.GetComponent<scr.ContractManagerScreen>(scr.ContractManagerScreen.containerId);
                if (receiver) { receiver.socketCallback(a); }
            } else if (s.type === ERequestType.create || s.type === ERequestType.checkMsisdn) {
                let receiver = Global.GetComponent<scr.ProcessNewScreen>(scr.ProcessNewScreen.containerId);
                if (receiver) { receiver.socketCallback(s); }
            } else if (s.type === ERequestType.createCustomerProcess
                || s.type === ERequestType.customerChangePayment
                || s.type === ERequestType.customerChangeData
                || s.type === ERequestType.customerChangePassword
                || s.type === ERequestType.customerChangeAddRemoveServices
                || s.type === ERequestType.customerGetCanellation
                || s.type === ERequestType.customerSetCancellation
                || s.type === ERequestType.createCancellationProcess
                || s.type === ERequestType.customerOrderSim
                || s.type === ERequestType.customerUpdateConsents) {
                let receiver = Global.GetComponent<scr.CustomerScreen>(scr.CustomerScreen.containerId);
                if (receiver) { receiver.socketCallback(s); }
            } else if (s.type == ERequestType.customerChangeAddCombi){
                let receiver = Global.GetComponent<PreviewContract>(s.receiverId);
                if (receiver) { receiver.socketCallback(a); } else { console.log('receiver:' + s.receiverId + ' noyt found!!!!!');}
            } else if (s.type === ERequestType.print || s.type === ERequestType.activation || s.type === ERequestType.contractSummaryPost || s.type === ERequestType.loadCustomerRefresh) {
                let receiver = Global.GetComponent<PreviewContract>(s.receiverId);
                if (!receiver) { return; }
                if (s.type === ERequestType.loadCustomerRefresh) {
                    receiver.socketCallbackCustomer(s);
                } else {
                    if (receiver) { receiver.socketCallback(a); }
                };
            } else if (s.type === ERequestType.saveDraft) {
                
                let receiver = Global.GetComponent<ActivationSummary>(ActivationSummary.containerId);
                
                if (receiver) { receiver.socketCallback(s); }
            } else if (s.type === ERequestType.getOrderDetail) {

                let receiver = Global.GetComponent<scr.OrderProduct>(scr.OrderProduct.containerId);
                receiver.socketCallbackDetails(a);

            } else if (s.type === ERequestType.saveOrderPoducts) {
                let receiver = Global.GetComponent<scr.OrderOverview>(scr.OrderOverview.containerId);
                if (receiver) { receiver.socketCallback(a); }
            }
            else if (s.type === ERequestType.changePassword) {
                let receiver = Global.GetComponent<scr.EditPasswordScreen>(scr.EditPasswordScreen.containerId);
                if (receiver) { receiver.socketCallback(a); }
            } else if (s.type === ERequestType.loadProfile) {
                let receiver = Global.GetComponent<scr.EditProfileScreen>(scr.EditProfileScreen.containerId);
                if (receiver) { receiver.loadProfileCallback(a); }
            } else if (s.type === ERequestType.saveProfile) {
                let receiver = Global.GetComponent<scr.EditProfileScreen>(scr.EditProfileScreen.containerId);
                if (receiver) { receiver.saveProfileCallback(a); }
            } else if (s.type === ERequestType.loadAccount) {
                let receiver = Global.GetComponent<scr.DealerScreen>(scr.DealerScreen.containerId) || Global.GetComponent<scr.AdministrationScreen>(scr.AdministrationScreen.containerId);
                if (receiver) { receiver.loadAccountCallback(a); }
            } else if (s.type === ERequestType.saveAccount) {
                let receiver = Global.GetComponent<scr.DealerScreen>(scr.DealerScreen.containerId);
                if (receiver) { receiver.saveAccountCallback(a); }
            } else if (s.type === ERequestType.getContacts) {
                let receiver = Global.GetComponent<scr.AdministrationScreen>(scr.AdministrationScreen.containerId);
                if (receiver) { receiver.contactsRecieved(a); }
            } else if (s.type === ERequestType.getContractDetails) {
                let receiver = Global.GetComponent<scr.ContractManagerDetailsScreen>(scr.ContractManagerDetailsScreen.containerId);
                if (receiver) { receiver.socketCallback(a); }
            } else if (s.type === ERequestType.saveImei || s.type === ERequestType.saveImeiList) {
                let receiver = Global.GetComponent<scr.ImeiScreen>(scr.ImeiScreen.containerId);
                if (receiver) { receiver.socketCallback(a); }
            } else if (s.type === ERequestType.loadContact) {
                let receiver = Global.GetComponent<EditUserScreen>(EditUserScreen.containerId);
                receiver.loadUserCallback(a);
            } else if (s.type === ERequestType.getOrderProducts) {
                let receiver = Global.GetComponent<scr.OrderProduct>(scr.OrderProduct.containerId);
                receiver.socketCallback(a);
            } else if (s.type === ERequestType.getOrdersList) {
                let receiver = Global.GetComponent<scr.OrderProduct>(scr.OrderProduct.containerId);
                receiver.socketCallbackOrdersList(a);
            } else if (s.type === ERequestType.saveContact) {
                let receiver = Global.GetComponent<EditUserScreen>(EditUserScreen.containerId);
                if (receiver) { receiver.saveUserCallback(a); }
            } else if (s.type === ERequestType.activateUser) {
                let receiver = Global.GetComponent<EditUserScreen>(EditUserScreen.containerId);
                if (receiver) { receiver.activateUserCallback(a); }
            } else if (s.type === ERequestType.deactivateUser) {
                let receiver = Global.GetComponent<EditUserScreen>(EditUserScreen.containerId);
                if (receiver) { receiver.deactivateUserCallback(a); }
            } else if (s.type === ERequestType.belatedPortation) {
                let receiver = Global.GetComponent<BelatedPorting>(BelatedPorting.containerId);
                if (receiver) { receiver.socketCallback(a); }
            } else if (s.type === ERequestType.getMnpPortingStatus) {
                let receiver = Global.GetComponent<StatusPorting>(StatusPorting.containerId);
                if (receiver) { receiver.socketCallback(a); }
            }
            else if (s.type === ERequestType.getMnpPortingStatusDetails) {
                let receiver = Global.GetComponent<StatusPorting>(StatusPorting.containerId);
                if (receiver) { receiver.socketCallbackDetails(a); }
            }
            else if (s.type === ERequestType.notify) {
                alert('notify:' + s.data);
            } else if (s.type === ERequestType.loadPortingStatus) {
                let receiver = Global.GetComponent<MnpStatusScreen>(MnpStatusScreen.containerId);
                if (receiver) { receiver.socketCallback(a); }
            }
        });
    };
}