import LControl from '../../components/page/control';
import { IActivation, IOrderData } from '../../models';
import { ICustomer } from '../../models/ordering/customer';
import { ECreditDebitType, EIDCardType } from '../../models/ordering/order';
import { EControlType } from '../../models/page/diverse';
import { IControlValue } from '../../models/page/interfaces';
import Global from '../Global';
import StorageHelper from '../storage';

export enum EValidationOption {
    undefined = 0,
    required = 1,
    minLength = 2,
    exactLength = 3,
    maxLength = 4,
    notBefore = 5,
    notAfter = 6,
    notEqual = 7,
    email = 8,
    inRange = 9,
    numeric = 10,
    exactValue = 11,
    match = 12,
    password = 13,
    regEx = 14,
    bdayValidation = 15,
    inputRange = 16,
    number = 17,
    oneOf = 18,
    monthRange = 19,
    yearFieldValidity = 20,
    dateMax = 21,
    dateMin = 22,
    nationalityError = 23,
    errorDeliveryDate = 24,
    bdayValidationVeryPrecise = 25,
    alphanumeric = 26,
    alphanumericWithoutGermanChars = 27,
    germanIBAN = 28,
    validateNames = 29,
    leftAndRightTrim = 30,
    validateSurname = 31,
    validateSurnameDSL = 32,
    passwordOutsideCustomerData = 33

};

export interface IValidationOption {
    type: EValidationOption;
    minValue?: number;
    maxValue?: number;
    minLength?: number;
    maxLength?: number;
    targetValue?: any;
    targetMatch?: string;
    targetControl?: string;
    message: string;
    nullable?: boolean;
    source?: any;
    blockInputOnError?: boolean;
};

export interface IValidationOptionControl {
    id: string;
    options: ValidationOption[];
};

export class ValidationOption {
    public option: IValidationOption;

    constructor(op: IValidationOption) {
        this.option = op;
    };

    public isValid(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();

        if (this.option) {
            if (this.option.nullable && !val.value) {
                return '';
            };

            if (this.option.nullable && !val.value) {
                return '';
            };

            if (this.option.type === EValidationOption.required) {
                return this.isValidRequired(ctrl);
            } else if (this.option.type === EValidationOption.exactLength) {
                return this.isValidExactLength(ctrl);
            } else if (this.option.type === EValidationOption.maxLength) {
                return this.isValidMaxLength(ctrl);
            } else if (this.option.type === EValidationOption.minLength) {
                return this.isValidMinLength(ctrl);
            } else if (this.option.type === EValidationOption.numeric) {
                return this.isValidNumeric(ctrl);
            } else if (this.option.type === EValidationOption.exactValue) {
                return this.isExactValue(ctrl);
            } else if (this.option.type === EValidationOption.notEqual) {
                return this.isNotEqual(ctrl);
            } else if (this.option.type === EValidationOption.email) {
                return this.isEmail(ctrl);
            } else if (this.option.type === EValidationOption.match) {
                return this.isMatch(ctrl);
            } else if (this.option.type === EValidationOption.password) {
                return this.isPassword(ctrl);
            } else if (this.option.type === EValidationOption.bdayValidation) {
                return this.isDateValid(ctrl);
            } else if (this.option.type === EValidationOption.inputRange) {
                return this.isInputInRange(ctrl);
            } else if (this.option.type === EValidationOption.oneOf) {
                return this.isOneOf(ctrl);
            } else if (this.option.type === EValidationOption.monthRange) {
                return this.isMonthInRange(ctrl);
            } else if (this.option.type === EValidationOption.yearFieldValidity) {
                return this.isYearInRange(ctrl);
            } else if (this.option.type === EValidationOption.dateMax || this.option.type === EValidationOption.dateMin) {
                return this.isDateMinMaxValid(ctrl);
            } else if (this.option.type === EValidationOption.nationalityError) {
                return this.isNationalitySameAsIDcard(ctrl);
            }
            else if (this.option.type === EValidationOption.errorDeliveryDate) {
                return this.deliveryDateValidation(ctrl);
            } else if (this.option.type === EValidationOption.bdayValidationVeryPrecise) {
                return this.validateBdayPrecise(ctrl)
            } else if (this.option.type === EValidationOption.alphanumeric) {
                return this.validateAlphanumeric(ctrl)
            }else if (this.option.type === EValidationOption.alphanumericWithoutGermanChars) {
                return this.validateAlphanumericWithoutGermanChars(ctrl)
            }else if (this.option.type === EValidationOption.germanIBAN) {
                return this.validateGermanIBAN(ctrl)
            }else if (this.option.type === EValidationOption.validateNames) {
                return this.validateNames(ctrl)
            }else if (this.option.type === EValidationOption.leftAndRightTrim) {
                return this.trimWhitespaces(ctrl)
            }else if (this.option.type === EValidationOption.validateSurname) {
                return this.validateSurname(ctrl)
            }else if (this.option.type === EValidationOption.validateSurnameDSL) {
                return this.validateSurnameDSL(ctrl)
            }else if (this.option.type === EValidationOption.passwordOutsideCustomerData) {
                return this.passwordOutsideCustomerData(ctrl)
            }

        }


        return '';
    };

    // ^[^\s]+(\s+[^\s]+)*$
    private isOneOf(ctrl: LControl): string {

        if (ctrl.props) {
            if (ctrl.props.id) {
                let field = this.option.targetControl ? this.option.targetControl : '---';
                let val = this.option.source ? this.option.source[field] : '';

                if (this.option.targetValue && this.option.targetValue.indexOf(val.toString()) < 0) {
                    return this.option.message;
                };
            }
        }
        return '';
    };

    private validateAlphanumeric(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        const regex = new RegExp('^[A-Z][a-zA-Z0-9 ]*$');
        let m = regex.test(val.value);

        if (!m) {
            return this.option.message.replace('$0', val.name);

        };

        return '';
    };

    private trimWhitespaces(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        const regex = new RegExp('^[^\s]+(\s+[^\s]+)*$');
        let m = regex.test(val.value);

        if (!m) {
            return this.option.message.replace('$0', val.name);

        };

        return '';
    };

    private validateNames(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        const regex = new RegExp('^[A-Z][a-zA-Z -]{1,}/*$');
       
        let m = regex.test(val.value);

        if (!m) {
            return this.option.message.replace('$0', val.name);

        };

        return '';
    };

    private validateSurname(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        const regex = new RegExp('^[a-zA-Z -]{2,}/*$');
       
        let m = regex.test(val.value);

        if (!m) {
            return this.option.message.replace('$0', val.name);

        };

        return '';
    };

    private validateSurnameDSL(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        const regex = new RegExp('^[A-Z][A-Za-z -]{2,}/*$');
       
        let m = regex.test(val.value);

        if (!m) {
            return this.option.message.replace('$0', val.name);

        };

        return '';
    };

    private validateGermanIBAN(ctrl: LControl): string{
        let val: IControlValue = ctrl.getValue();
        const regex = new RegExp('^DE\d{2}\s?([0-9a-zA-Z]{4}\s?){4}[0-9a-zA-Z]{2}$');
        let m = regex.test(val.value);

        if (!m) {
            return this.option.message.replace('$0', val.name);

        };

        return '';
    }

    private validateAlphanumericWithoutGermanChars(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        const regex = new RegExp('^[a-zA-Z0-9]*$');
        let m = regex.test(val.value);

        if (!m) {
            return this.option.message.replace('$0', val.name);

        };

        return '';
    };


    private isDateMinMaxValid(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        if (
            ((ctrl.props.type === EControlType.date)
                && (val.value === undefined || val.value === '' || val.value.length === 0))
        ) {
            return this.option.message;
        }

        return '';
    };

    private validateBdayPrecise(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        let orders: IOrderData[] = StorageHelper.getOrders();
        let ageNeeded: number = 18;

          let oteloFound: boolean = false;

            StorageHelper.getOrders().forEach(or => {
                if (or.currentBrand === "otelo") {
                    if (or.activations[0].creditDebitType === ECreditDebitType.prepaid) {
                        oteloFound = true;
                    }
                }
            });

            let minimumAgeNeeded: number = oteloFound ? 16 : 18;
            let maximumAgeNeeded: number = 200;
            let order: IOrderData[] = StorageHelper.getOrders();
            if (order.length !== 0) {

                order[0].activations.forEach(act => {
                    if (act.brandKey === 'vf') {
                        act.services.forEach(ser => {
                            // if (ser === 'YOUABIAGE') {
                            //    maximumAgeNeeded = 17;
                            //    minimumAgeNeeded = 17;
                            if (ser === 'YOUAGEVAL') {
                                maximumAgeNeeded = 27;
                                minimumAgeNeeded = 18;
                            }
                        })
                    } else if (act.brandKey === 'otelo') {
                        if (act.familyId === 8) {
                            maximumAgeNeeded = 27;
                            minimumAgeNeeded = 18;
                        }
                    }
                })
            }

            let age = Global.getAgeFromDate(val.value!);
            
            if (age < minimumAgeNeeded) {

                return `Das Alter des Kunden darf nicht unter ${minimumAgeNeeded} Jahren liegen.`;
            } else if (age > maximumAgeNeeded) {
                return `Das Alter des Kunden darf nicht ber ${maximumAgeNeeded} Jahren liegen.`
            }
            else {
                return '';
            }
    }

    private deliveryDateValidation(ctrl: LControl) {
        let val: IControlValue = ctrl.getValue();
        let order: IOrderData[] = StorageHelper.getOrders();
        let orderKabel: IOrderData | undefined = order.find(x => x.currentBrand === 'kabel');
        let activation: IActivation | undefined;
        let activationDate: Date;

        if (orderKabel) {
            activation = orderKabel.activations.find(x => x.brandKey === 'kabel')
        }

        if (val.value !== undefined && order.length !== 0 && activation !== undefined) {
            activationDate = new Date(activation.activationDate);

            var howManyMonths = this.monthDiff(activationDate, val.value)

            if (howManyMonths > 6) {
                return this.option.message;
            }
        }
        return '';
    }

    private monthDiff(d1, d2) {

        var months;
        months = (d2.getFullYear() - d1.getFullYear()) * 12;
        months -= d1.getMonth();
        months += d2.getMonth();
        return months <= 0 ? 0 : months;
    }

    private isNationalitySameAsIDcard(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        let orders: IOrderData[] = StorageHelper.getOrders();
        let customer: ICustomer;
        if (orders !== undefined) {
            customer = orders[0].customer;
            if (customer.legitimation && customer.legitimation.type == EIDCardType.foreign && customer.nationality && customer.nationality == 'DEU') {
                return this.option.message;
            } else if (customer.legitimation && customer.legitimation.type == EIDCardType.german && customer.nationality && customer.nationality !== 'DEU') {
                return this.option.message;
            }
        }


        return '';
    }

    private isValidRequired(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();

        if (
            ((ctrl.props.type === EControlType.password || ctrl.props.type === EControlType.text || ctrl.props.type === EControlType.dropdown)
                && (!val || !val.value || val.value.toString().trim().length === 0))
        ) {
            return this.option.message;
        }

        return '';
    };

    private isValidExactLength(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        if (
            (ctrl.props.type === EControlType.text && (val.value === undefined || val.value.length != this.option.targetValue))
        ) {
            return this.option.message.replace('$0', val.name).replace('$1', (this.option.targetValue ? this.option.targetValue : 0).toString());
        }

        return '';
    };

    private isValidMaxLength(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        if (
            (ctrl.props.type === EControlType.text && (val.value === undefined || val.value.length > (this.option.maxLength ? this.option.maxLength : 1)))
        ) {
            return this.option.message.replace('$0', val.name).replace('$1', (this.option.maxLength ? this.option.maxLength : 0).toString());
        }

        return '';
    };

    private isValidMinLength(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();

        if (
            (ctrl.props.type === EControlType.text && (val.value === undefined || val.value.length < (this.option.minLength ? this.option.minLength : 1)))
        ) {
            return this.option.message.replace('$0', val.name).replace('$1', (this.option.minLength ? this.option.minLength : 0).toString());
        }

        return '';
    };

    private isValidNumeric(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        let regexp = new RegExp('^\\d+$');

        let test = regexp.test(val.value ? val.value : '');

        if (
            (val.value === undefined || !test)
        ) {
            if (this.option.blockInputOnError) {
                this.blockInput(ctrl);
            };

            return this.option.message.replace('$0', val.name);
        }

        return '';
    };

    private isInputInRange(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();

        const regex = new RegExp('^[0-9].{' + (this.option.minLength ? this.option.minLength - 1 : 0) + ',' + (this.option.maxLength ? this.option.maxLength - 1 : 0) + '}$'); ///^[0-9_@].{4,8}$/gm;

        let m = regex.test(val.value);

        if (!m) {
            return this.option.message.replace('$0', val.name);

        };

        return '';
    };

    private isYearInRange(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        let valueToCompare: string = new Date().getFullYear().toString().substring(2, 4);
        if (Number(val.value) >= 20 && Number(val.value) <= 99) {
            return '';
        } else {
            return this.option.message;
        }
    };

    private blockInput(ctrl: LControl) {
        let val: IControlValue = ctrl.getValue();

        let svalue: string = val.value ? val.value : '';

        if (svalue.length > 0) {
            ctrl.setValue(svalue.substring(0, svalue.length - 1));
        }
    };

    private isMonthInRange(ctrl: LControl) {
        let val: IControlValue = ctrl.getValue();
        const regex = /0[1-9]|1[0-2]/gm;
        let m = regex.exec(val.value);

        if (m !== null) {
            return '';
        } else {
            return this.option.message;
        }

    }

    private isMatch(ctrl: LControl): string {
        //let source = StorageHelper.getOrder
        let val: IControlValue = ctrl.getValue();

        //let match = Global.getValueByPath(source, this.option.targetMatch ? this.option.targetMatch : '___');
        let match = '';

        if (this.option.targetValue) {
            match = this.option.targetValue;
        } else if (this.option.targetControl) {
            let ctrl = Global.GetComponent<LControl>(this.option.targetControl);
            if (ctrl) {
                match = ctrl.getValue().value;
            };
        } else {
            match = Global.getValueByPath(this.option.source, this.option.targetMatch ? this.option.targetMatch : '___');
        };
        //console.log('match:' + val.value + '=' + match);
        if (
            (val && match) && (val.value == undefined || (match.toString() != val.value.toString()))
        ) {
            return this.option.message;
        }

        return '';
    };

    private isEmail(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if (
            (!val.value || !re.test(val.value.toLowerCase()))
        ) {
            return this.option.message.replace('$0', val.name);
        }

        return '';
    };

    private isPassword(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        var re = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[A-Z])(?!)[A-Za-z\d_@.\/#%&-]{8,10}$/gm;

        if (
            (!val.value || !re.test(val.value))
        ) {
            return this.option.message.replace('$0', val.name);
        }

        return '';
    };

    private passwordOutsideCustomerData(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        var re = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?!.*?[;,])(?!.*?[]).{8,30}$/gm;

        if (
            (!val.value || !re.test(val.value))
        ) {
            return this.option.message.replace('$0', val.name);
        }

        return '';
    };

    private isDateValid(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();

        if (val.id === "bdaydropdownfulldate") {
            let tempDate: Date = val.value;

            val.value = ('0' + tempDate.getDate()).slice(-2) + '/'
                + ('0' + (tempDate.getMonth() + 1)).slice(-2) + '/'
                + tempDate.getFullYear();
        }

        const regex = /^(0[1-9]|[12][0-9]|3[01])[- \/.](0[1-9]|1[012])[- \/.](19|20)\d\d$/gm;
        let m = regex.exec(val.value);
        if (m !== null) {
            var dateParts = val.value.replace(/\//gm, ".").split(".");

            // month is 0-based, that's why we need dataParts[1] - 1
            var dateObject = new Date(+dateParts[2], dateParts[1] - 1, +dateParts[0]);
            if (val.id === "bdaydropdownfulldate") {
                val.value = dateObject;
            }

            var dateFromValue = dateObject;
            let oteloFound: boolean = false;

            StorageHelper.getOrders().forEach(or => {
                if (or.currentBrand === "otelo") {
                    if (or.activations[0].creditDebitType === ECreditDebitType.prepaid) {
                        oteloFound = true;
                    }
                }
            });

            let minimumAgeNeeded: number = oteloFound ? 16 : 18;
            let maximumAgeNeeded: number = 200;
            let order: IOrderData[] = StorageHelper.getOrders();
            if (order.length !== 0) {

                order[0].activations.forEach(act => {
                    if (act.brandKey === 'vf') {
                        act.services.forEach(ser => {
                            // if (ser === 'YOUABIAGE') {
                            //    maximumAgeNeeded = 17;
                            //    minimumAgeNeeded = 17;
                            if (ser === 'YOUAGEVAL') {
                                maximumAgeNeeded = 27;
                                minimumAgeNeeded = 18;
                            }
                        })
                    } else if (act.brandKey === 'otelo') {
                        if (act.familyId === 8) {
                            maximumAgeNeeded = 27;
                            minimumAgeNeeded = 18;
                        }
                    }
                })
            }

            let age = Global.getAgeFromDate(dateObject);

            if (age < minimumAgeNeeded) {

                return `Das Alter des Kunden darf nicht unter ${minimumAgeNeeded} Jahren liegen.`;
            } else if (age > maximumAgeNeeded) {
                return `Das Alter des Kunden darf nicht ber ${maximumAgeNeeded} Jahren liegen.`
            }
            else {
                return '';
            }

        } else {
            return "Geben Sie ein gltiges Format ein";
        }
    };

    private isExactValue(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();

        if (
            val.value == undefined || (this.option.targetValue != undefined && this.option.targetValue.toString() != val.value.toString())
        ) {
            return this.option.message;
        }

        return '';
    };

    private isNotEqual(ctrl: LControl): string {
        let val: IControlValue = ctrl.getValue();
        if (
            (ctrl.props.type === EControlType.dropdown && (!val.value || val.value != this.option.targetValue))
        ) {
            return this.option.message;
        }

        return '';
    };
};

export default ValidationOption;