import * as React from 'react';

export interface IInner2CRightProps {
    children: React.ReactChild
};

export class Inner2CRight extends React.Component<IInner2CRightProps, any>{
    constructor(props: IInner2CRightProps){
        super(props);
    }

    public render() {
        let ctrl = (
        <>
            {this.props.children}
        </>
        );

        return ctrl;
    }

    public render1() {
        
        return <div className="inner2-right col col-3">
            {this.props.children}
        </div>
    }
};

export default Inner2CRight;