import * as React from 'react';
import { MainTexts } from '../../utils/messages';
import Global from '../../utils/Global';
import { IControlValue } from '../../models/page/interfaces';
import { EPreviewState } from '../../components/ordering/preview/contract';
import * as page from '../../../src/components/page/index';
import { IApiResponse } from '../../interfaces';
import { EPopupType } from '../../components/dialogs/popup';
import * as m from '../../models/index';
import DraftsScreen from '../am/drafts';
import { EFilterType } from '../../components/page/filter';
import { IColumn, EPagingType } from '../../components/page/table';
import * as cmp from '../../components/index';
import { Row, Col } from 'react-bootstrap';
import { EControlType } from '../../models';
import IUser from '../../models/environment/user';
import StorageHelper from '../../utils/storage';

export interface IStatusPortingProps {
    onChange: any;
    // parent: CustomerScreen;
}

export interface IStatusPortingState {
    changed: boolean;
    api?: IApiResponse;
    api2?: IApiResponse;
    expanded: boolean;
    apiMessage?: string;
    processing: boolean;
    closablePopup: boolean;
    confirmRemove: boolean;
    selectedRecord?: any;
    showPopup: boolean;
}

export class StatusPorting extends React.Component<IStatusPortingProps, IStatusPortingState> {
    public static containerId: string = 'statusPorting';
    public buttonStatus: string = '';
    private filter: m.IFilter = { needle: '' };
    private processPopupState: EPopupType = EPopupType.custom;
    public user?: IUser;
  
    readonly cfg = Global.getConfiguration();


    constructor(props: any) {
        super(props);

        this.applyFilter = this.applyFilter.bind(this);
        this.expand = this.expand.bind(this);
        this.onDismissPopup = this.onDismissPopup.bind(this);
        this.socketCallback = this.socketCallback.bind(this);
        this.renderDetailsSection = this.renderDetailsSection.bind(this);
      
        this.user = StorageHelper.getUser();

        let to: Date = new Date();
        let from: Date = new Date();
        from = new Date(from.setMonth(from.getMonth() - 1)) as Date;

        this.filter = {
            needle: '',
            from: from,
            to: to
        };

        this.state = {
            expanded: false,
            changed: false,
            processing: true,
            closablePopup: false,
            confirmRemove: false,
            selectedRecord: undefined,
            showPopup: false
        };

        this.onChange = this.onChange.bind(this);
    };



    public render() {

        let ctrl = <div id={StatusPorting.containerId}>
            <page.Filter
                filter={this.filter}
                id="filter"
                type={EFilterType.default} title={MainTexts.filterCriteria}
                onApply={this.applyFilter}
                optionalButtonSelection={() => { this.getButtonState(event) }}
                optionalButtons={true}
                placeholderSearch="Status, MSISDN, contract, customer and tariff" />
            {
                !this.state.processing ?
                    <>
                        {this.renderData()}
                    </>
                    : null
            }

            {this.state.showPopup ? this.renderPopup() : <></>}

        </div>;

        return ctrl;

    };

    private renderData(): JSX.Element {
        let cfg = Global.getConfiguration();

        let cols: IColumn[] = [
            { label: "Registrierung", binding: 'Status', size: 2, sortable: true },
            {
                label: "Start Date", filterable: true, style: 'text-sub', binding: 'StartDate', size: 1, sortable: true, renderer: (r, c) => {
                    return <>{Global.toGermanDate(r[c.binding])}</>;
                }
            },
            {
                label: "End Date", filterable: true, style: 'text-sub', binding: 'EndDate', size: 1, sortable: true, renderer: (r, c) => {
                    return <>{Global.toGermanDate(r[c.binding])}</>;
                }
            },
            { label: "Aktueller Status", filterable: true, binding: 'LastStatus', size: 3, sortable: true, style: '' },
            { label: MainTexts.msisdn, filterable: true, style: 'text-sub', binding: 'MSISDN', size: 2, sortable: false },
            { label: MainTexts.customer, filterable: true, binding: 'Customer', size: 1, sortable: true, style: '' },
            { label: MainTexts.tariff, filterable: true, binding: 'Tarif', size: 1, sortable: true, style: '' },
            {
                label: '', binding: '', size: 1, sortable: true,
                renderer: (c, r) => {
                    return <>
                        <page.LButton type={m.ELButtonType.iconImage}
                            icon="list"
                            clickModel={c}
                            onClickEvent={(e, m) => {
                                e.preventDefault();
                                e.stopPropagation();
                                this.selectRecord(m);
                                return false;
                            }} />
                    </>;
                },
                rendererHead: (c) => {
                    return <>
                        <page.LButton type={m.ELButtonType.iconImage}
                            icon="expand"
                            onClick={this.expand} />
                    </>;
                }
            },
        ];

        const pagingProps = { page: 1, count: cfg.pageSize, type: EPagingType.local };


        let ctrl = <div id={DraftsScreen.containerId} className={'c1 container ' + (this.state.expanded ? 'wide' : '')}>
            <div className="row">
                <div className="col-md-12 col-12">
                    <page.Table coulmns={cols}
                        id={StatusPorting.containerId + 'Table'}
                        expnaded={this.state.expanded}
                        pagingProps={pagingProps}
                        filter={this.filter}
                        data={this.state.api ? this.state.api.data : undefined} />
                </div>
            </div>
        </div>;

        return ctrl;
    };

    private selectRecord(record) {
        setTimeout(() => {

            let t = {
                Void: this.user===undefined? '': this.user.voId,
                ContractId: record.ContractId,
                total: '0',
                page: '1',
                count: '500',

            };

            Global.hub.send(JSON.stringify(t), m.ERequestType.getMnpPortingStatusDetails, undefined);
        }, 1000);



        const selectedRecord: any = { Kunde: record.Customer, msisdn: record.MSISDN, contractId: record.ContractId, tarif: record.Tarif };

        this.setState({ selectedRecord: selectedRecord, processing: true });
    }

    private renderPopup(): JSX.Element {
        return <cmp.Popup
            type={this.processPopupState}
            visible={this.state.showPopup}
            closable={true}
            id="wait"
            title={''}
            height={600}
            width={1000}
            onDismiss={this.onDismissPopup}>
            <cmp.PopupBody>
                <div>{this.renderDetailsSection()}</div>
                <></>
            </cmp.PopupBody>
            <></>
        </cmp.Popup>
    };

    private onDismissPopup() {
        this.setState({ showPopup: false });
    };



    private applyFilter() {
        this.loadData(this.filter.from, this.filter.to, this.filter.needle, this.buttonStatus);

    };


    private renderDetailsSection(): JSX.Element {
        let details: JSX.Element = <></>;
        const { selectedRecord } = this.state;
        if (selectedRecord !== undefined) {
            details = <>
                <Row>
                    <Col md={3} >
                        <page.LControl type={EControlType.info} label={selectedRecord.Kunde} disabled title={'Kunde'} />
                    </Col >
                    <Col md={3} >
                        <page.LControl type={EControlType.info} label={selectedRecord.msisdn} disabled title={'MSISDN'} />
                    </Col>
                    <Col md={3} >
                        <page.LControl type={EControlType.info} label={selectedRecord.contractId} disabled title={'Vertag'} />
                    </Col>
                    <Col md={3} >
                        <page.LControl type={EControlType.info} label={selectedRecord.tarif} disabled title={'Tarif'} />
                    </Col>
                </Row>
            </>;
        }

        let colsPopup: IColumn[] = [
            { label: "Status", binding: 'Status', size: 3, sortable: true },
            {
                label: "Datum", filterable: true, style: 'text-sub', binding: 'StartDate', size: 3, sortable: true, renderer: (r, c) => {
                    return <>{Global.toGermanDate(r[c.binding])}</>;
                }
            },

            { label: "Details", filterable: true, binding: 'Description', size: 4, sortable: true, style: '' },

            {
                label: '', binding: '', size: 2, sortable: true,
                renderer: (c, r) => {
                    return <>
                        <page.LButton type={m.ELButtonType.iconImage}
                            icon=""
                            clickModel={c}
                            onClickEvent={(e, m) => {
                                e.preventDefault();
                                e.stopPropagation();

                                return false;
                            }} />
                    </>;
                },
                rendererHead: (c) => {
                    return <>
                        <page.LButton type={m.ELButtonType.iconImage}
                            icon="expand"
                            onClick={this.expand} />
                    </>;
                }
            },
        ];

        const pagingProps = { page: 1, count: this.cfg.pageSize, type: EPagingType.local };

        const ctrl = <div id={StatusPorting.containerId} className={'c1 container ' + (this.state.expanded ? 'wide' : '')}>
            <div className="row">
                {details}
                <div className="col-md-12 col-12">
                    <page.Table coulmns={colsPopup}
                        id={StatusPorting.containerId + 'Table'}
                        expnaded={this.state.expanded}
                        pagingProps={pagingProps}
                        dateColumn='StartDate'
                        filter={undefined}
                        data={this.state && this.state.api2 ? this.state.api2.data : undefined} />
                </div>
            </div>

        </div>;

        return ctrl;
    }

    onDismissDetailsScreen = () => {
        this.setState({ selectedRecord: undefined });
    }



    private getButtonState(event: any) {
        this.componentDidMount();
        this.buttonStatus = event.target.id;
    };

    public componentDidMount() {

    };

    public socketCallback(api: IApiResponse) {
        this.setState({ changed: true, processing: false, api: api }); 
    };


    public socketCallbackDetails(api: IApiResponse) {
        this.setState({ changed: true, processing: false, api2: api, showPopup: true });
    };



    private loadData(from: any, to: any, search: any, portingStatus: any) {
        setTimeout(() => {
            Global.easyApp.setTitle(MainTexts.portingStatus);

            let t = {
                Void: this.user===undefined? '': this.user.voId,
                from: from,
                to: to,
                LastStatus: portingStatus,
                total: '0',
                page: '1',
                count: '500',
                search: search
            };

            Global.hub.send(JSON.stringify(t), m.ERequestType.getMnpPortingStatus, undefined);
        }, 1000);
    };

    private expand() {
        this.setState({ expanded: !this.state.expanded });
    };


    private onChange(e: IControlValue) {


    };




};

export default StatusPorting;