import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import * as page from '../../../src/components/page/index';
import { EPopupType, IPopupConfig } from '../../components/dialogs/popup';
import * as cmp from '../../components/index';
import { EStatusState } from '../../components/support/status';
import { EApiResponseStatus, IApiSummary } from '../../interfaces';
import IUser from '../../models/environment/user';
import * as m from '../../models/index';
import Api from '../../utils/api';
import Global from '../../utils/Global';
import GlobalJSX from '../../utils/GlobalJSX';
import { MainTexts } from '../../utils/messages';
import StorageHelper from '../../utils/storage';
import ScreenCapture from './ScreenshotTool/Screenshot';
import './ScreenshotTool/style.css';




// import html2canvas from 'html2canvas';

export interface IUserFeedbackProps {
    // onChange: any;
    // parent: any;
    // processId: any;

    summary: IApiSummary;
    parent?: any;
}

export interface IUserFeedbackState {
    changed: boolean;
    hideShowArea: boolean;
    screenCapture: string;
    successFeedback: boolean;
    screenCaptureVisibility: boolean;
    canvas: boolean;
    screenshotStarted: boolean;


}

export interface IFeedback {
    orderId: string;
    notes: string;
    screenshot: string;
    username: string;
    error_message: string;
}

export class UserFeedback extends React.Component<IUserFeedbackProps, IUserFeedbackState> {
    public static containerId: string = 'userFeedback';
    private processPopupState: EPopupType = EPopupType.custom;
    public user?: IUser;
    public model: IFeedback;

    readonly cfg = Global.getConfiguration();

    constructor(props: any) {
        super(props);

        this.user = StorageHelper.getUser();
        let ord = StorageHelper.getOrders();
        this.model = { orderId: ord !== undefined && ord.length >0 ? ord[0].id : '', notes: '', screenshot: '', username: this.user ? this.user.username : '', error_message: this.props.summary.message };

        this.state = {
            hideShowArea: false,
            changed: false,
            screenCapture: '',
            successFeedback: false,
            screenCaptureVisibility: false,
            canvas: false,
            screenshotStarted: false
        };
    };

    public render() {

        let ctrl =
            (
            
                <>
                    {this.state.successFeedback ? this.renderThankYouPopUp() : null}
                    {this.renderBugReporter()}
                   
                    <ScreenCapture onEndCapture={this.handleScreenCapture}>
                        {({ onStartCapture }) => (  
                           this.state.screenCaptureVisibility?  this.screenShotTool(onStartCapture) : <></>
                        ) }
                    </ScreenCapture>
                </>
            )
        return ctrl;
    };

    private renderBugReporter(): JSX.Element {
        let ctrl = (
            <Container className="full">
                <Row>
                    <Col xs={12} md={12} className="np">
                        <h2 className="text-muted font-weight-bold" >Es ist ein Fehler aufgetreten</h2>
                    </Col>
                </Row>

                <Row>
                    <Col xs={10} md={10}>
                        <div id={"capture"}>
                            {
                                this.props.summary.answerError ?
                                    <>
                                        <Row>
                                            <Col xs={8} md={8}>
                                                <h4 className="text-muted font-weight-bolder mb-0">Das ist passiert:</h4>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col xs={8} md={8}>
                                                <p className="text-muted font-weight-lighter mt-0">
                                                    {this.props.summary.answerError.message}
                                                </p>
                                            </Col>
                                        </Row>
                                    </>
                                    :
                                    <Row>
                                        <Col xs={8} md={8}>
                                            <p id="errorcanvas" className="text-muted font-weight-lighter mt-0">
                                                {this.props.summary.message}
                                            </p>
                                        </Col>
                                    </Row>
                            }
                        </div>

                        {
                            this.props.summary.answerError ?
                                <>
                                    <Row>
                                        <Col xs={8} md={8}>
                                            <h4 className="text-muted font-weight-bolder mb-0">Was kannst du tun?</h4>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col xs={8} md={8}>
                                            <p className="text-muted font-weight-lighter mt-0">
                                                {this.props.summary.answerError.suggestion}
                                            </p>
                                        </Col>
                                    </Row>
                                </>
                                : null
                        }

                    </Col>
                    <Col>
                        <Row>
                            <Col xs={2} md={2}>
                                <cmp.Status status={EStatusState.failed} id="stfail" />
                            </Col>
                        </Row>
                    </Col>
                </Row>

                <Row>
                    <Col xs={12} md={12} className="np">
                        <page.LButton
                            type={m.ELButtonType.linkWithIconRight}
                            css="action4 right"
                            icon={this.state.hideShowArea ? 'chevron-up-small' : 'chevron-down-small'}
                            text="Fehler melden"
                            onClick={() => {
                                this.setState({ hideShowArea: !this.state.hideShowArea });
                            }} />
                        {/* <h5 className="mb-0">
                            <button className="btn btn-link" data-toggle="collapse" style={{ color: "#9400D3" }} onClick={() => { this.setState({ hideShowArea: this.state.hideShowArea === 'show' ? 'hide' : 'show' }) }} data-target="#collapseOne" aria-expanded="false" aria-controls="collapseOne">
                                Fehler melden! {this.state.hideShowArea === "show" ? '' : ''}
                            </button>
                        </h5> */}
                    </Col>

                    {
                        this.state.hideShowArea ?
                            <div id="collapseOne" aria-labelledby="headingOne" data-parent="#accordion">
                                <div className="">
                                    Nutze unsere Meldefunktion, wenn dir unser Tip nicht helfen konnte. Nehme einen Screenshot auf und fge deine Beschreibung an. Wir schauen uns deinen Fehler schnellstmligch an.
                            </div>
                                <Row className="justify-content-md-left">
                                    <Col xs={12} md={12} >
                                        <page.LButton type={m.ELButtonType.buttonWithIconRight}
                                            css="a1 fr"
                                            id=""
                                            text={"Fehler melden"}
                                            onClick={() => {
                                                this.setState({ screenCaptureVisibility: true })
                                            }} />
                                    </Col>
                                </Row>
                                <Row>


                                </Row>
                            </div>
                            : null
                    }
                </Row>


            </Container>
        )

        return <div id={UserFeedback.containerId}>
            {ctrl}
        </div>;
    }



    private screenShotTool(onStartCapture: any): JSX.Element {

        const { screenCapture, screenCaptureVisibility } = this.state

        // let cfg: IBlockConfiguration = {
        //     title: "",
        //     transparent: false,
        //     style: '',
        //     styleContainer: '',
        //     collapsible: false,
        //     id: "",
        //     collapsed: false,
        // };

        // return <div id={UserFeedback.containerId}>
        //     <BaseBlock config={cfg}>
        //         {ctrl}
        //     </BaseBlock>
        // </div>;       


        let ctrl = (
            <Container className="full tal">
                <Row>
                    <Col xs={12} md={12}>
                        <p className="text-muted font-weight-lighter mt-0">Ein Screenshot von deinem Bildschirm wurde erfolreich aufgenommen.</p>
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} md={12}>


                        <page.LButton
                            type={m.ELButtonType.linkWithIconRight}
                            css="action4 right"
                            text="Screenshot anzeigen"
                            onClick={onStartCapture} />
                        {/* <button className="btn btn-link " style={{ color: "#9400D3", textAlign: "left" }} onClick={this.screenshot} >
                            Screenshot anzeigen
                        </button> */}

                        <img src={this.state.screenCapture !== null ? this.state.screenCapture : ''}></img>

                    </Col>

                </Row>

                <Row>
                    <Col xs={12} md={12}>
                        <p className="text-muted font-weight-lighter mt-0">Beschreibung:</p>
                    </Col>
                </Row>
                <Row>
                    <Col md={12} xs={12}>
                        <div className="form-group">
                            <textarea id='notes' style={{ backgroundColor: '#DCDCDC', borderRadius: '15px' }}
                                onChange={(e) => { this.model.notes = e.target.value }}
                                className="form-control"
                                rows={5}
                            />
                        </div>
                    </Col>
                </Row>
            </Container>
        )

        let cfg: IPopupConfig = {
            props: {
                id: 'dynamicPopup',
                title: 'Fehler melden',
                visible: true,
                closable: false,
                style: 'tal',
                type: EPopupType.custom,
                height: 500,
                width: 800
            },
            content: ctrl,
            action: <>
                <page.LButton type={m.ELButtonType.buttonWithIconRight}
                    css="a1 fl"
                    id=""
                    text={MainTexts.cancel}
                    onClick={() => {
                        this.setState({ screenCaptureVisibility: false });
                    }} />
                <page.LButton type={m.ELButtonType.buttonWithIconRight}
                    css="a1 fr"
                    id=""
                    text={"Absenden"}
                    onClick={() => {
                        Api.fetch('activation/saveFeedback', this.model, "POST", (api) => {
                            if(api.status === EApiResponseStatus.ok  )
                            this.setState({ screenCaptureVisibility: false, successFeedback: true })
                        });

                    }} />
            </>
        };

        return GlobalJSX.GetPopup(cfg.props, cfg.content, cfg.action);
    };

    handleScreenCapture = (screenCapture: string) => {
        
        this.model.screenshot = screenCapture;
        this.setState({
            screenCapture: screenCapture,
            changed: true,
            screenCaptureVisibility: true
        })
    }







    public screenshot() {
        var el = document.getElementById('capture');

        if (el) {

            // html2canvas(el).then(function(canvas) {
            //     canvas.id="captured"
            //  document.body.appendChild(canvas);

            // });   
        }
        // const { screenCapture } = this.state;
        // <ScreenCapture onEndCapture={this.handleScreenCapture}>
        //     {({ onStartCapture }) => (
        //         <React.Fragment>

        //             <button onClick={onStartCapture}>Capture</button>

        //             <img src={screenCapture} />
        //   </Fragment>
        //     )}
        // </ScreenCapture>





    };

    private renderThankYouPopUp(): JSX.Element {
        let cfg: IPopupConfig = {
            props: {
                id: 'dynamicPopup',
                title: 'Danke fr dein Feedback!',
                visible: true,
                closable: false,
                style: 'tal',
                type: EPopupType.custom
            },
            content: <Container className="full">
                <Row>
                    <Col xs={12} md={12}>
                        <p className="text-muted font-weight-lighter mt-0">Deine Fehlermeldung wurde erfolgreich bermittelt.</p>
                    </Col>
                </Row>
            </Container>,
            action: <>
                <page.LButton type={m.ELButtonType.buttonWithIconRight}
                    css="a1 fr"
                    id=""
                    text={"OK"}
                    onClick={() => {
                        this.setState({ screenCaptureVisibility: false, successFeedback: false })
                    }} />
            </>
        };

        return GlobalJSX.GetPopup(cfg.props, cfg.content, cfg.action);
        // return <cmp.Popup
        //     type={this.processPopupState}
        //     visible={this.state.successFeedback}
        //     closable={false}
        //     id="thankYou"
        //   height={300}
        //     onDismiss={() => { }}>
        //     <cmp.PopupBody >
        //         <Container>
        //         <Row className="justify-content-md-center">
        //             <Col xs={12} md={12}>
        //                 <h2 className="text-muted font-weight-bold" >Danke fr dein Feedback!</h2>
        //             </Col>
        //         </Row>
        //             <Row className="justify-content-md-center">
        //                 <Col xs={12} md={12}>
        //                     <p className="text-muted font-weight-lighter mt-0">Deine Fehlermeldung wurde erfolgreich bermittelt.</p>
        //                 </Col>
        //             </Row>
        //             <Row>
        //             </Row>
        //             <Row className="justify-content-md-center">
        //                 <Col xs={4} md={4} >
        //                     <page.LButton type={m.ELButtonType.buttonWithIconRight}
        //                         css="a1"
        //                         id=""
        //                         text={MainTexts.ok}
        //                         onClick={() => {
        //                             this.setState({ successFeedback: false })
        //                         }} />
        //                 </Col>
        //             </Row>
        //         </Container>
        //         <></>
        //     </cmp.PopupBody>
        //     <></>
        // </cmp.Popup>
    };
};

export default UserFeedback;