import { OPEN_POPUP, OPEN_SCREEN } from "./types";

export const openPopup = (jsx: any) => {  
  console.log('action1!!!!');
  return {
    type: OPEN_POPUP,
    payload: jsx
  }
};

export const openScreen = (jsx: any) => {  
  console.log('action2!!!!');
  return {
    type: OPEN_SCREEN,
    payload: jsx
  }
};