export enum BuildEnv {
    Dev = 0,
    Int = 1,
    Prod = 2,
    DevInt = 3
}

// SET THE BUILD ENV HERE:
export const build: BuildEnv = window.location.href.indexOf('//localhost') > 0 ? BuildEnv.Dev :
    window.location.href.indexOf('vf-easy') > 0 ? BuildEnv.Prod : BuildEnv.Int;

export const debug: boolean = window.location.href.indexOf('//localhost') > 0 ? true : false;