import linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import ReactTooltip from 'react-tooltip';
import * as constants from '../../../constants';
import * as e3c from '../../../constants';
import * as i from '../../../interfaces/index';
import { EApiResponseStatus, IApiResponse, IApiStep, IApiSummary, IProcessStatus, ISocketPushModel } from '../../../interfaces/index';
import IUser from '../../../models/environment/user';
import * as m from '../../../models/index';
import { IOrderData } from '../../../models/index';
import { ActivationHelper, ActivationUI } from '../../../models/ordering/activation';
import { ICustomer } from '../../../models/ordering/customer';
import { EActivationFileType, EActivationType, EBusinessCase } from '../../../models/ordering/order';
import IService from '../../../models/ordering/service';
import { EControlType, ELButtonType, ERequestStatus, ERequestType } from '../../../models/page/diverse';
import { IKeyValue } from '../../../models/page/interfaces';
import Api from '../../../utils/api';
import ActivationApi from '../../../utils/apiActivation';
import { BrandKeys, CatKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import GlobalJSX from '../../../utils/GlobalJSX';
import { ActivationMessages, Hints, MainTexts, OrderingTexts } from '../../../utils/messages';
import EasyRouter from '../../../utils/router';
import StorageHelper from '../../../utils/storage';
import { EPopupType, IPopupProps } from '../../dialogs/popup';
import * as page from '../../page/index';
import { EStatusState } from '../../support/status';
import BrandThumb, { EBrandThumbStatus, EBrandThumbType } from '../brandThumb';
import ActivationResponseStatus from '../process/activationResponseStatus';

export interface IPreviewContractProps {
    preview: boolean;
    customer: ICustomer;
    ah: ActivationHelper;
    refreshParent: any;
    bc: EBusinessCase;
};

export interface IPreviewContractState {
    status: EPreviewState;
    mounted: boolean;
    changed: boolean;
    loading: boolean;
    confirmSigned: boolean;
    confirmBoni: boolean;
};

export enum EPreviewState {
    undefined = 0,
    requestNeeded = 1,
    contractRequested = 2,
    requestReady = 4,
    printReady = 8,
    printNeeded = 16,
    activationStarted = 32,
    activationDone = 64,
    apiFailed = 128,
    printFailed = 256,
    timeout = 512,
    printReadyBoni = 1024
    // ,executeDirect = 512
    // ,executeStarted = 1024
    // ,executeFailed = 2048
    // ,executeDone = 4096
};

export class PreviewContract extends React.Component<IPreviewContractProps, IPreviewContractState> {
    private failedOnce: boolean = false;
    private api?: i.IApiResponse;
    private prevStatus: EPreviewState = EPreviewState.undefined;
    private downloads: IKeyValue[] = [];
    /**
     * true if the customer data was refreshed after an vvl/tw
     */
    private customerUpdated: boolean = false;

    constructor(props: IPreviewContractProps) {
        super(props);

        this.processCallback = this.processCallback.bind(this);
        this.socketCallback = this.socketCallback.bind(this);
        this.socketCallbackCustomer = this.socketCallbackCustomer.bind(this);
        this.timerCompletedCallback = this.timerCompletedCallback.bind(this);

        let tempStatus = EPreviewState.requestNeeded;

        if (this.props.ah.activation.isActivated()) {
            tempStatus = EPreviewState.activationDone;
        } else if (this.props.ah.activation.isTimedout()) {
            tempStatus = EPreviewState.timeout;
        }else {
            this.props.ah.activation.resetSteps(ERequestType.activation);
        };

        this.state = {
            status: tempStatus,
            mounted: false,
            changed: false,
            loading: false,
            confirmSigned: false,
            confirmBoni: false
        };
    };

    public componentDidMount() {
        this.setState({ mounted: true });
        this.checkStatus();
    };

    public render() {
        if (!this.state.mounted) {
            return <></>
        };

        return this.renderMultiple();
    };

    public renderMultiple() {
        
        let item=Global.categoryWrapper.getCategoryByProduct(this.props.ah.activation.productId);
        let catKey = item.id;
      
        if(this.props.ah.activation.selection.children.length>1){
           
            let categoryOne=Global.categoryWrapper.getCategoryByProduct(this.props.ah.activation.selection.children[0].id);
            let categoryTwo=Global.categoryWrapper.getCategoryByProduct(this.props.ah.activation.selection.children[1].id); 
            
            if((categoryOne.id == CatKeys.tv && categoryTwo.id == CatKeys.fn) || (categoryOne.id == CatKeys.fn && categoryTwo.id == CatKeys.tv)){
                catKey = 'tv_fn';
            }
        }

        let ctrl = (
            <div className="block-contract" id={this.props.ah.activation.id} key={'prevc-' + this.props.ah.activation.id}>
                {this.renderPopup()}

                {this.renderPopupConfirmSigned()}
                {this.renderPopupConfirmBoniSigned()}

                <div className="action-top">
                    {this.renderActionCustomer()}
                </div>

                <div className="action">
                    {this.renderAction()}
                </div>

                <div className="block block-act-contract block-act-preview">
                    <BrandThumb 
                        ckey={'bt-cat-' + catKey}
                        item={item}
                        multiBasketId={catKey}
                        onClick={undefined} 
                        type={EBrandThumbType.blister}
                        withoutInfo={true} 
                        status={EBrandThumbStatus.static | EBrandThumbStatus.selected}
                        activationStatus={this.state.status} />

                    {
                        this.props.ah.items.map((i)=>{
                            return this.renderMain(i)
                        })
                    }

                    {this.renderPrice()}

                    {this.renderStatusBlock()}

                    <page.LClear />
                </div>
            </div>
        );

        return ctrl;
    };

    private renderStatusBlock() {
        return this.props.preview || this.props.ah.activation.inCurrent ?
            <Container>
                <Row>
                    <Col xs={12} md={12} className="np">
                        <div className="spacer-medium" />
                        <div className="spacer-line" />
                        <div className="spacer-medium" />
                    </Col>
                </Row>
                <Row>
                    <Col md={12} className="np">
                        {this.renderStatus()}
                    </Col>
                </Row>
            </Container>
    :
        <Container>
            <Row>
                <Col xs={12} md={12} className="np">
                    <div className="spacer-medium" />
                    <div className="spacer-line" />
                    <div className="spacer-medium" />
                </Col>
            </Row>

            {
                this.props.ah.activation.brandKey === BrandKeys.vodafone && this.props.bc === EBusinessCase.bnt ?
                    <>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                {Hints.hintMandatoryServicesVodafone}
                            </Col>
                        </Row>
                        <div className="spacer-medium" />
                        <div className="spacer-line" />
                        <div className="spacer-medium" />
                    </>
                    : null
            }

            <Row>
                <Col md={12} className="np">
                    {this.renderStatus()}
                </Col>
            </Row>
        </Container>
    };

    private renderMain(act: ActivationUI) {
        let user: IUser | undefined = StorageHelper.getUser();

        let ctrl = <Container>
            {
                this.props.ah.activation.activationType === EActivationType.existingCustomer && this.props.preview ?
                    <Row>
                        <Col xs={12} md={12} className="np">
                            <div className="title">{act.getActionName()}</div>
                        </Col>
                    </Row>
                    :
                    null
            }

            {
                this.props.ah.activation.inCurrent ?
                    <Row>
                        <Col xs={12} md={12} className='title'>
                            {ActivationMessages.prolongateWithCurrent}
                        </Col>
                    </Row>
                    : null
            }

            {
                this.props.ah.activation.brandKey == BrandKeys.unity ?
                    <Row>
                        <Col xs={12} md={12} className='text-hint np'>
                            {Hints.dataCorrectCheck} 
                            <br/> &nbsp;
                        </Col>
                    </Row>
                    : null
            }

            <Row>
                <Col xs={6} md={6} className="np">
                    <page.LControl type={EControlType.info} title="Tarif" label={act.getProductName()} />
                </Col>
                {
                    this.props.preview ?
                        <>
                            <Col xs={6} md={6} className="np">
                                <page.LControl type={EControlType.info} title="VOID" label={ user ? user.username : ''} />
                            </Col>
                            <div className="spacer" />
                            {
                                this.props.ah.activation.activationType == EActivationType.bookService ? null :
                                    <Col xs={6} md={6} className="np">
                                        <page.LControl type={EControlType.info} title={MainTexts.contractType} label={act.getContractType()} />
                                    </Col>
                            }
                        </>
                        : null
                }
            </Row>

            {
                this.props.ah.activation.brandKey == BrandKeys.otelo ?
                    <Row>
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.info} label={act.getMsisdn()} title="MSISDN" />
                        </Col>

                        {
                            this.props.customer.contracts ? 
                            <Col xs={6} md={6} className="np">
                                <page.LControl type={EControlType.info} label={act.getCustomerId()} 
                                    title={MainTexts.customerId} />
                            </Col>
                            : 
                            <Col xs={6} md={6} className="np">
                                <page.LControl type={EControlType.info} label={act.getContractIdUi()} title={MainTexts.contractId} />                     
                            </Col>
                        }

                        <div className="spacer" />
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.info}
                                label={Global.toGermanDate(act.activationDate)}
                                title={act.activationDateTitleUI} />
                        </Col>
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.info} title={"IMEI"} label={this.props.ah.activation.imei} />
                        </Col>
                    </Row>
                    : null
            }

            {
                this.props.ah.activation.brandKey == BrandKeys.dsl ?
                    <Row>
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.info} label={act.getContractId()} title="Barcode" />
                        </Col>
                    </Row>
                    : null
            }

            {
                this.props.ah.activation.brandKey == BrandKeys.kabel ?
                    <Row>
                        <Col xs={12} md={12} className="np">
                            <page.LControl type={EControlType.info} label={act.getContractIdUi()} title={MainTexts.webOrderId} />	                            
                        </Col>
                        {
                            act.isDone() || true ? 
                                <Col xs={12} md={12} className="np">
                                    <page.LControl style="text-hint" type={EControlType.info} label={"Bitte flle das Feld Weborder-ID, in der oberen rechten Ecke des Vertragsdokumentes, manuell aus."} />
                                </Col>
                            : null
                        }                        
                    </Row>
                    : null
            }

            {/* vf sim msisdn */}
            {
                this.props.ah.activation.brandKey == BrandKeys.vodafone ?
                    <Row>
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.info}
                                label={act.getMsisdn()}
                                title="MSISDN" />
                        </Col>
                        {
                            this.props.ah.activation.activationType === EActivationType.existingCustomer || this.props.ah.activation.activationType === EActivationType.bookService || this.props.ah.activation.activationType === EActivationType.cdChange ? null :
                                <Col xs={6} md={6} className="np">
                                    <page.LControl type={EControlType.info} label={act.getSim()} title="SIM" />
                                </Col>
                        }
                        {
                            this.props.ah.activation.activationType === EActivationType.bookService || this.props.ah.activation.activationType === EActivationType.cdChange ? null :
                                <Col xs={6} md={6} className="np">
                                    <page.LControl type={EControlType.info} title={"IMEI"} label={this.props.ah.activation.imei} />
                                </Col>
                        }
                    </Row>
                    : null
            }

            {
                this.props.ah.activation.brandKey == BrandKeys.vodafone && this.props.preview && !this.props.ah.activation.inCurrent ?
                    <Row>
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.info} label={!this.props.customer.contracts? act.customerId : act.getCustomerId()} title={MainTexts.customerId} />
                        </Col>
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.info}
                                label={Global.toGermanDate(act.activationDate)}
                                title={act.activationDateTitleUI} />
                        </Col>
                    </Row>
                    : null
            }

            <div className="spacer" />
            <Row>
                {
                    this.props.ah.activation.activationType == EActivationType.bookService || this.props.ah.activation.inCurrent ? null :
                        <Col xs={6} md={6} className="np">
                            <page.LControl type={EControlType.info} label={act.subUI} title={OrderingTexts.sub} />
                        </Col>
                }
                {
                    this.props.ah.activation.brandKey !== BrandKeys.vodafone ?
                        <Col xs={6} md={6} className="npr">
                            <page.LControl type={EControlType.info} label={act.optionsUI} title={MainTexts.bookedOptions} />
                        </Col>
                        : null
                }
            </Row>
            <div className="spacer" />
            {/* vf services */}
            {
                this.props.ah.activation.brandKey === BrandKeys.vodafone && !this.props.ah.activation.inCurrent ?
                    <>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl type={EControlType.info} title={MainTexts.bookedServices} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                {act.optionsUI}
                            </Col>
                        </Row>
                    </>
                    :
                    <>
                        {
                            this.props.ah.activation.inCurrent ? null :
                                <Row>
                                    <Col xs={12} md={12} className="np">
                                        <page.LControl type={EControlType.info} label={act.promotionsUI} title={MainTexts.bookedPromotions} />

                                    </Col>
                                </Row>
                        }
                    </>
            }
        </Container>

        return ctrl;
    };

    private renderPrice() {
        let ctrl =  this.props.ah.activation.inCurrent ? <></> :
                <Container>
                    <Row>
                        <Col xs={12} md={12} className="np">
                            <div className="spacer-medium" />
                            <div className="spacer-line" />
                            <div className="spacer-medium" />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={12} className="np">
                            <div className="title">{this.props.ah.activation.activationType === EActivationType.bookService ? MainTexts.costsAll : MainTexts.costs}</div>
                        </Col>
                    </Row>
                    <Row>
                        {
                            this.props.ah.activation.activationType === EActivationType.existingCustomer || this.props.ah.activation.activationType === EActivationType.bookService ? null
                                :
                                <Col xs={4} md={4} className="np">
                                    <page.LControl type={EControlType.info} style="big" label={Global.feeToString(this.props.ah.getActivationFee())} title={MainTexts.feeActivation} />
                                </Col>
                        }

                        <Col xs={4} md={4} >
                            <page.LControl type={EControlType.info}
                                label={Global.feeToString(this.props.ah.getOptionMonthlyFee())}
                                title={this.props.ah.activation.activationType === EActivationType.bookService ? MainTexts.costsBookedOptions : MainTexts.bookedServices}
                                style="big" />
                        </Col>

                        {
                            this.props.ah.activation.activationType === EActivationType.bookService ? null :
                                <Col xs={4} md={4} className="npr">
                                    <page.LControl type={EControlType.info} label={Global.feeToString(this.props.ah.getMonthlyFee())} title={MainTexts.feeMonthly} style="big" />
                                </Col>
                        }
                    </Row>
                </Container>
        
        return ctrl;
    };

    public makeServicesRenderable(allServices: IService[]): JSX.Element {
        let ctr = <></>;

        ctr = (<ul>
            {allServices.map(i => {
                return <li key={'allsvc-' + i.id}>{i.name}</li>;
            })}
        </ul>
        )
        return ctr;
    };

    /**
     * Renders the action like Anfordern und Aktivieren
     */
    public renderAction() {
        let ctrl = <></>;
        let bcss: string = 'a1 disabled';
        let msg: string = 'Bitte besttigen Sie zuerst die Einverstndniserklrung';
        let consentSet: boolean = false;

        if (this.props.preview) {
            ctrl = <page.LButton type={ELButtonType.buttonWithIconRight} icon="print" css={"no-print"}
                text={ActivationMessages.print} onClick={this.onPrintDocument} />
            return ctrl;
        };

        if (this.props.customer.bew && this.props.customer.bew.consent) {
            bcss = 'a1';
            consentSet = true;
        };

        if (    this.state.status === EPreviewState.contractRequested 
            ||  this.state.status === EPreviewState.activationStarted
            ||  this.state.status === EPreviewState.timeout) {
            ctrl = (
                <></>
                // <page.LTimer count={3000} step={5} stepCallback={this.processCallback} showUI={true} />
            );
        } else if (this.state.status == EPreviewState.requestNeeded || this.state.status == EPreviewState.printReadyBoni) {
            if(this.props.ah.activation.brandKey == BrandKeys.unity && this.state.status != EPreviewState.printReadyBoni) {
                if(!this.props.ah.activation.hasBoniFile()) {
                    ctrl = <page.LButton type={ELButtonType.buttonWithIconRight} 
                        icon="print"
                        css={bcss}
                        text={'Bonittsprfung starten'}
                        onClick={() => { this.checkBoni() }} /> 
                } else {
                    var f = this.props.ah.activation.getFile(EActivationFileType.boni);
                    ctrl = <page.LButton type={ELButtonType.buttonWithIconRight} 
                        icon="print"
                        css={bcss}
                        text={'Bonittsprfung ausdrucken'}
                        onClick={() => { this.onDownload(f!.Name, EPreviewState.printReadyBoni) }} />                     
                }
            } else {
                ctrl = (
                    <>
                        {
                            consentSet ? null :
                                <ReactTooltip place="top" type="light" effect="solid" id={'tt_buttonRequestContract'}>
                                    <div>{msg}</div>
                                </ReactTooltip>
                        }
                        <span data-for="tt_buttonRequestContract" data-tip={msg}>
                            <page.LButton
                                type={ELButtonType.buttonWithIconRight}
                               // icon="print"
                                css={bcss}
                                id="buttonRequestContract"
                                text={this.props.ah.activation.activationType == EActivationType.bookService ? ActivationMessages.requestContractOptions : ActivationMessages.requestContract}
                                onClick={(e)=> {
                                    if(this.props.ah.activation.brandKey == BrandKeys.unity) {
                                        this.setState({confirmBoni: true});
                                    } else {
                                        this.onRequestPrint();
                                    }
                                }} />
                        </span>
                    </>
                );
            }
        } else if (this.state.status === EPreviewState.printNeeded) {
            var step = this.props.ah.activation.getStep(ERequestType.print);

            let info = JSON.parse(step.message);

            this.downloads = linq.from<string>(info.Binary.split(';'))
                .select(x => {
                    return {
                        Key: x,
                        Value: '0'
                    }
                }).toArray();
            
            if (this.props.ah.activation.brandKey === BrandKeys.kabel && this.downloads.length > 1) {
                ctrl = (
                    <>
                        <div>Ausdrucken:</div>
                        {
                            this.downloads.map((i, idx)=>{
                                let s = MainTexts.contract;
                                
                                if(i.Key.toLowerCase().indexOf('kabelkip')>0) {
                                    s = MainTexts.contractFn;
                                } else if(i.Key.toLowerCase().indexOf('tvkabel')>0) {
                                    s = MainTexts.contractTv;
                                } else if(idx > 0) {
                                    s = MainTexts.sepa;
                                }

                                return <page.LButton type={ELButtonType.buttonWithIconRight}
                                    icon="print" css={bcss}
                                    text={s}
                                    onClick={() => { this.onDownloadNotReady(i.Key) }} />
                            })
                        }
                    </>
                );
            } else {
                ctrl = (
                    <>
                        {
                            <page.LButton 
                                type={ELButtonType.buttonWithIconRight} 
                                icon="print" 
                                css={bcss}
                                text={ActivationMessages.print} 
                                onClick={()=>
                                    {this.onDownload('', EPreviewState.printReady);}
                                } />
                        }

                    </>
                );
            }
        } else if (this.state.status === EPreviewState.printReady) {
            ctrl = (
                <page.LButton type={ELButtonType.buttonWithIconRight}
                     css={bcss}
                    text={this.props.ah.activation.activationType == EActivationType.bookService ? ActivationMessages.completeContractOptions : ActivationMessages.completeContract}
                    onClick={this.onActivateConfirm} />
            );
        } else if (this.state.status === EPreviewState.activationDone) {
            ctrl = (
                <>
                    <page.LButton type={ELButtonType.buttonWithIconRight} icon="print" css={bcss}
                        text={ActivationMessages.overview}
                        onClick={() => { this.onOverview() }} />
                </>
            );
        };

        let ctrldbg: JSX.Element = <></>;

        if (e3c.build !== constants.BuildEnv.Prod) {
            ctrldbg = (
                <>
                    <page.LButton type={ELButtonType.buttonWithIconRight} icon="print"
                        css={bcss}
                        text={'Set Done'}
                        onClick={() => { this.setActivationDone() }} />                       
                </>
            );
        };

        return [ctrl, ctrldbg];
    };

    /**
     * 
     * @param api will be called after an print or activation action
     */
    public socketCallback(api: IApiResponse) {
        this.api = api;

        if (api.error) {
            this.setState({ status: this.prevStatus });
        } else {
           
            let steps = ActivationApi.getSteps(api);
            if(api.data.responseObject) {
                this.props.ah.activation.files = api.data.responseObject.Files;
            };
            this.props.ah.activation.updateSteps(steps);

            this.checkStatus();
        }
    };

    /**
     * will be called after loadCustomerRefresh
     */
    public socketCallbackCustomer(sock: ISocketPushModel) {
        let api = JSON.parse(sock.data);

        if (!api.error) {
            let inner = api.data;

            let proc = JSON.parse(inner.data);
            let step: IApiStep = proc.steps[0];


            if (step.status === ERequestStatus.done) {
                let process = JSON.parse(step.message);

                StorageHelper.addOrderAndReset(process);

                this.api = undefined;
                this.setState({ loading: false });

                EasyRouter.route({ url: '/customer/dashboard/' + process.id, title: MainTexts.existingCustomerActivation });
            }
        }

        {
            this.api = api;
            this.setState({ loading: false });
        }
    };

    /**
     * will create an checkBoni request and sends it via api to backend
     */
    private checkBoni = () => {
        let model = {
            brand: this.props.ah.activation.brandKey,
            orderId: this.props.ah.activation.id,
            type: ERequestType.checkBoni
        };

        this.setState({loading: true});
        this.api = undefined;

        Api.fetch('activation/checkBoni', model, "POST", (api: IApiResponse) => {
            if(api.status == EApiResponseStatus.ok) {
                this.props.ah.activation.files = api.data.files;
                StorageHelper.updateActivation(this.props.ah.activation);
                this.setState({loading: false});
            } else {
                var sum = Api.parseSummary(api, undefined);
                this.api = api;
                this.setState({changed: true});
            };
        });
    };

    private onPrintDocument=()=> {
        window.print();
    };

    /**
     * will set current activation to status done
     * so use only for debug
     */
    private setActivationDone=()=> {
        if (!e3c.debug) { console.error('Use only on local'); return; };

        this.props.ah.activation.activateManually();
        StorageHelper.updateActivation(this.props.ah.activation);
        this.setState({ changed: true, status: EPreviewState.activationDone });
    };

    /**
     * will be called when the button "Zu den Kundendaten" is being clicked
     */
    private onBackToCustomer=()=> {
        if (!this.customerUpdated && this.props.ah.activation.isActivated()) {
            var parent = StorageHelper.getParentOfProcess(this.props.ah.activation.processId);

            Global.hub.send('{ "type": ' + m.ERequestType.loadCustomerRefresh + ', "receiverId":"' + this.props.ah.activation.id + '", "id":"' + Global.getUniqueId() + '", "parentId": "' + parent.id + '", "brandKey": "' + this.props.ah.activation.brandKey + '" }',
                m.ERequestType.loadCustomerRefresh,
                () => {
                    this.setState({ changed: true, loading: true });
                });
        } else {
            this.props.ah.activation.activateManually();
            this.setState({ changed: true, status: EPreviewState.activationDone });
        };
    };

    private renderPopup(): JSX.Element {
        let cfg: IPopupProps = {
            title: 'Anforderung wird verarbeitet',
            closable: false,
            id: "existingRefreshPopup",
            visible: false,
            type: EPopupType.pleaseWait
        };

        if (this.state.loading) {
            cfg.visible = true;
        };

        let content: JSX.Element = <></>;

        if (this.api && this.api.status != EApiResponseStatus.ok) {
            let pr: IProcessStatus = JSON.parse(this.api.data);
            let sum: IApiSummary = Api.parseSummary(this.api, pr);

            cfg.closable = true;
            cfg.visible = true;
            cfg.type = EPopupType.info;
            content = <div>{sum.message}</div>
        };

        let ctrl = GlobalJSX.GetPopup(cfg, content, undefined);

        return ctrl;
    };

    private renderPopupConfirmSigned=():JSX.Element => {
        let cfg: IPopupProps = {
            title: MainTexts.printFile,
            closable: true,
            id: "newconfirmPopup",
            visible: false,
            height: 200,
            textPositive: MainTexts.yes,
            textNegative: MainTexts.no,
            type: EPopupType.confirmCustom,
            onDismiss: () => {
                this.setState({ confirmSigned: false })
            },
            onNegative: () => {
                this.setState({ confirmSigned: false })
            },
            onPositive: () => {
                this.setState({ confirmSigned: false }, () => {
                    this.onActivate();
                });
            }
        };

        if (this.state.confirmSigned) {
            cfg.visible = true;
        };

        let content: JSX.Element = <div>{Hints.customerSignedDocument}</div>;

        let ctrl = GlobalJSX.GetPopup(cfg, content, undefined);

        return ctrl;
    };

    private renderPopupConfirmBoniSigned=():JSX.Element => {
        let cfg: IPopupProps = {
            title: MainTexts.hint,
            closable: true,
            id: "newconfirmPopup",
            visible: false,
            height: 200,
            textPositive: MainTexts.yes,
            textNegative: MainTexts.no,
            type: EPopupType.confirmCustom,
            onDismiss: () => {
                this.setState({ confirmBoni: false })
            },
            onNegative: () => {
                this.setState({ confirmBoni: false })
            },
            onPositive: () => {
                this.setState({ confirmBoni: false }, () => {
                    this.onRequestPrint();
                });
            }
        };

        if (this.state.confirmBoni) {
            cfg.visible = true;
        };

        let content: JSX.Element = <div>{Hints.customerSignedBoni}</div>;

        let ctrl = GlobalJSX.GetPopup(cfg, content, undefined);

        return ctrl;
    };

    private renderActionCustomer(): JSX.Element {
        if ((this.props.ah.activation.activationType !== EActivationType.existingCustomer)
            || !this.props.ah.activation.isActivated()
            || this.props.preview) {
            return <></>;
        };

        let ctrl = <>
            <page.LButton text={MainTexts.backToCustomer}
                onClick={this.onBackToCustomer}
                type={ELButtonType.linkSimple}
                css="action3"
                icon="chevron" />
        </>;

        return ctrl;
    };

    private onDownload = (file: string, state: EPreviewState) => {
        this.setState({ status: state });

        ActivationApi.download(this.props.ah.activation, file, (api: i.IApiResponse) => { });
    };

    private onDownloadNotReady(file: string) {
        let f = linq.from<IKeyValue>(this.downloads).where(x => x.Key == file).first();
        f.Value = '1';
        let a = linq.from<IKeyValue>(this.downloads).where(x => x.Value == '1').count();

        ActivationApi.download(this.props.ah.activation, file, (api: i.IApiResponse) => { });

        if (a == this.downloads.length) {
            this.setState({ status: EPreviewState.printReady });
        };
    };

    private processCallback() {
        return;
    };

    private onRequestPrint = () => {
        Global.hub.send('{ "processId": "' + this.props.ah.activation.processId + '", "orderId": "' + this.props.ah.activation.id + '" }',
            m.ERequestType.print,
            undefined);

        this.props.ah.activation.resetSteps(ERequestType.print);

        this.prevStatus = this.state.status;

        this.setState({ status: EPreviewState.contractRequested });
    };

    private onActivateConfirm=()=> {
        this.setState({ confirmSigned: true });
    };

    private onActivate = () => {
        this.api = undefined;

        Global.hub.send('{ "processId": "' + this.props.ah.activation.processId + '", "orderId": "' + this.props.ah.activation.id + '" }',
            m.ERequestType.activation,
            undefined);

        this.props.ah.activation.resetSteps(ERequestType.activation);

        this.prevStatus = this.state.status;

        this.setState({ status: EPreviewState.activationStarted });
    };

    private onOverview=()=> {
        let url = '/ordering/preview/' + this.props.ah.activation.processId + '/' + this.props.ah.activation.id;
        let w = window.open(url, '_blank');
    };

    private checkStatus() {
        try {
            let tempStatus = EPreviewState.requestNeeded;

            var stepPrint: i.IApiStep = this.props.ah.activation.getStep(ERequestType.print);
            var stepAct: i.IApiStep = this.props.ah.activation.getStep(ERequestType.activation);

            if (!stepPrint || !stepAct) { return; };

            if (stepPrint && stepAct.status == ERequestStatus.undefined) {
                if (stepPrint.status === ERequestStatus.done) {
                    tempStatus = EPreviewState.printNeeded;
                } else if (stepPrint.status === ERequestStatus.failed || stepPrint.status === ERequestStatus.invalid) {
                    tempStatus = EPreviewState.requestNeeded;
                    this.failedOnce = true;
                } else if (stepPrint.status === ERequestStatus.running) {
                    tempStatus = EPreviewState.contractRequested;
                }
            };

            if (stepAct) {
                if (stepAct.status != ERequestStatus.running) {
                    if (stepAct.status === ERequestStatus.done) {
                        tempStatus = EPreviewState.activationDone;
                        this.checkSuccess(stepAct);
                    } else if (stepAct.status === ERequestStatus.failed || stepAct.status === ERequestStatus.invalid) {
                        tempStatus = EPreviewState.printReady;
                        this.failedOnce = true;
                    } else if (stepAct.status === ERequestStatus.timeout) {
                        tempStatus = EPreviewState.timeout;
                    };
                } else {
                    tempStatus = EPreviewState.activationStarted;
                }
            };

            this.setState({ status: tempStatus });
        } catch (e) {
            console.error(e);
        }
    };

    private checkSuccess(step: i.IApiStep) {
        let info: m.IAnswerInfo = JSON.parse(step.message);

        this.props.ah.activation.msisdn = info.MobileNumber;

        StorageHelper.updateActivation(this.props.ah.activation);

        /**
         * otelo customer is being refreshed directly after vvl/tw
         */
        if (step.type == ERequestType.activation &&
            step.status == ERequestStatus.done &&
            this.props.ah.activation.brandKey === BrandKeys.otelo &&
            this.props.ah.activation.activationType === EActivationType.existingCustomer) {
            try {
                let inner = JSON.parse(step.message);
                let proc = StorageHelper.getParentOfProcess(this.props.ah.activation.processId);

                let data: IOrderData = inner.uiUpdate;

                if (this.props.customer.contracts && data.customer.contracts) {
                    this.props.customer.contracts[0].actions = data.customer.contracts[0].actions;
                    this.customerUpdated = true;
                    StorageHelper.updateCustomer(proc.id, data.customer);
                };
                this.customerUpdated = true;
            } catch (e) {
                console.error(e);
            };
        };

        if (this.props.refreshParent) {
            this.props.refreshParent();
        };
        //this.props.parent.refresh();
        this.setState({ changed: true });
    };

    private timerCompletedCallback() {
        this.setState({ status: this.prevStatus });
    };

    private renderStatus(): JSX.Element {
        let status: EStatusState = EStatusState.undefined;
        let message = '';

        if (this.failedOnce == false &&
            (this.state.status === EPreviewState.printReadyBoni || this.state.status === EPreviewState.printReady || this.state.status === EPreviewState.printNeeded || this.state.status === EPreviewState.undefined || this.state.status === EPreviewState.requestNeeded)) {
            return <></>;
        }

        if (this.state.status == EPreviewState.contractRequested
            || this.state.status == EPreviewState.activationStarted) {
            status = EStatusState.inprocessTimer;
        } else if (this.state.status == EPreviewState.activationDone) {
            status = EStatusState.done;
        } else if (this.state.status == EPreviewState.timeout) {
            status = EStatusState.warning;
            message = 'berprfe den Status deines Auftrags zu einem spteren Zeitpunkt im Auftragsmanager'
        } else {
            status = EStatusState.failed;
        };

        if (this.api && this.api.error) {
            status = EStatusState.failed;
        };

        let ctrl = <ActivationResponseStatus
            apiState={status}
            title="Deine Anfrage wurde bermittelt."
            message={message}
            api={this.api}
            resetTimer={true}
            autoRestart={true}
            completedCallback={this.timerCompletedCallback}
            process={this.props.ah.activation.processStatus}
            processCallback={this.processCallback} />

        return ctrl;
    };
};

export default PreviewContract;