import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import BaseBlock from '../../../components/page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { ActivationWrapper } from '../../../models/ordering/activation';
import { IOrderData, OrderDataWrapper } from '../../../models/ordering/data';
import { EBusinessCase, EOrderProcessView } from '../../../models/ordering/order';
import { EControlType } from '../../../models/page/diverse';
import ProcessNewScreen from '../../../screens/ordering/process';
import Global from '../../../utils/Global';
import { MainTexts } from '../../../utils/messages';
import StorageHelper from '../../../utils/storage';
import CustomVodafoneGigaCombi from '../../brand/gigaCombi';
import * as page from '../../page/index';
import CustomerConsent from '../customer/consent';
import PreviewContract from '../preview/contract';
import PreviewDelivery from '../preview/delivery';
import PreviewCustomerData from '../preview/newCustomerData';

export interface IActivationPreviewProps {
    parent: ProcessNewScreen;
    order: IOrderData;
    preview: boolean;
};

export interface IActivationPreviewState {
    changed: boolean;
};

export class ActivationPreview extends React.Component<IActivationPreviewProps, IActivationPreviewState> {
    constructor(props: IActivationPreviewProps) {
        super(props);
        
        this.onNext = this.onNext.bind(this);
        this.onBack = this.onBack.bind(this);

        this.state = {
            changed: false
        };
    };

    public componentDidMount() {
        window.scrollTo(0,0);
    };

    public render() {
        let aw = new ActivationWrapper(this.props.order);

        let ctrl = (
            <>
                {
                    this.props.preview ? null
                    :   <>
                            {this.renderHint()}

                            {
                                OrderDataWrapper.hasGiga(this.props.order) ? <CustomVodafoneGigaCombi/> : <></>
                            }  

                            <CustomerConsent 
                                customer={this.props.parent.data.customer} 
                                parentRefresh={()=>{
                                    StorageHelper.addOrder(this.props.parent.data);
                                    this.props.parent.refresh();
                                }}/>
                        </>
                }            

                {
                    this.props.preview ? 
                    <div className="text-h2">Aktivierungsbesttigung fr</div>
                    : null 
                }
                
                {aw.items.map((i, idx)=>
                    (<PreviewContract 
                            key={"pc-" + idx}
                            bc={this.props.order.type}
                            customer={this.props.parent.data.customer}
                            ah={i}
                            preview={this.props.preview} 
                            refreshParent={()=>{this.props.parent.refresh();}}/> )
                )}

                {
                    this.props.order.type == EBusinessCase.bnt ? this.renderSummaryPrice(aw) : null
                }

                <PreviewCustomerData parent={this} customer={this.props.order.customer}/>

                {
                    this.props.parent.props.preview && this.props.parent.dataWrapper.hasKabel() ?
                    <PreviewDelivery parent={this}/>
                    : null
                }
            </>
        );

        return ctrl;
    };

    public renderHint() {
        let cfg: IBlockConfiguration = {
            title: 'Grundgebhr wird im Voraus berechnet.',
            styleContainer: 'clue',
            transparent: false,
        };

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={12} md={12} className="np">
                            <div className="subtitle">Hinweis: Bitte Kunden aufklren!</div>
                        </Col> 
                    </Row>    
                    
                    <Row>
                        <Col xs={12} md={12} className="np">
                            <page.LControl type={EControlType.hint} label="Weisen Sie Ihren Kunden bitte darauf hin, dass bei Vodafone die Grundgebhr im Voraus berechnet wird. Ihr Kunde bekommt kurz nach Aktivierung eine Erstrechnung, die den Anschlusspreis und die anteiligen Gebhren des Aktivierungsmonats enthlt. <br/><br/>Die erste regulre Rechnung enthlt dann die Nutzungskosten des Aktivierungsmonats sowie die Grundgebhr des kommenden Monats. (Ausnahme zur Erstrechnung siehe Klartext zur vorflligen Grundgebhr)"/>
                        </Col> 
                    </Row>                             
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };        

    public renderSummaryPrice(prw: ActivationWrapper) {
        let cfg: IBlockConfiguration = {
            title: 'Gesamtkosten',
            styleContainer: 'hot print-hide',
            transparent: false
        }; 

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        {
                            this.props.parent.data.type === EBusinessCase.bnt ? 
                            <Col xs={4} md={4} className="np">
                                    <page.LControl type={EControlType.info} style="big" label={Global.feeToString(prw.getActivationFee())} title={MainTexts.feeActivation}/>
                            </Col>    
                            : null
                        }
                        <Col xs={4} md={4} className="">
                                <page.LControl type={EControlType.info} style="big" label={Global.feeToString(prw.getOptionMonthlyFee())} title={MainTexts.feeBookedOptions}/>
                        </Col>  
                        <Col xs={4} md={4} className="npr">
                                <page.LControl type={EControlType.info} style="big" label={Global.feeToString(prw.getMonthlyFee())} title=""/>
                        </Col>                                                                          
                    </Row>                             
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };

    public onNext() {
        this.props.parent.gotoView(EOrderProcessView.done);
    };

    public onBack() {
 
        this.props.parent.gotoView(EOrderProcessView.customer);
    };      
};

export default ActivationPreview;