import * as React from 'react';
import ProcessNewScreen from '../../../screens/ordering/process';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { Row, Col, Container } from 'react-bootstrap';
import BaseBlock from '../../../components/page/block';
import ActivationCustomerData from '../process/activationCustomerData';
import * as page from '../../page/index';
import { ELButtonType } from '../../../models/page/diverse';
import { MainTexts } from '../../../utils/messages';

export interface ICustomerActionProps {
    parent: ActivationCustomerData
};

export interface ICustomerActionState {
    changed: boolean;
};

export class CustomerAction extends React.Component<ICustomerActionProps, ICustomerActionState> {
    constructor(props: ICustomerActionProps) {
        super(props);

        this.state = {
            changed: false
        };
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: '',
            transparent: true,
        };

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container className="container-action">
                    <Row>
                        <Col xs={4} md={4}>
                            <page.LButton text={MainTexts.back} 
                                onClick={this.props.parent.onBack} 
                                type={ELButtonType.linkSimple} 
                                css="action3"
                                icon="chevron"/>
                        </Col>
                        <Col xs={8} md={8} className="toright">                      
                            <page.LButton text={MainTexts.next}
                                css="fr"
                                onClick={this.props.parent.onNext} 
                                type={ELButtonType.buttonWithIconRight} 
                                icon="arrow-right"/>      
                            {/* <page.LButton text={MainTexts.save}
                                type={ELButtonType.linkSimple} 
                                css="action fr"
                                icon="chevron"/>                                                             */}
                        </Col>
                    </Row>                                
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };    
};

export default CustomerAction;