import * as React from 'react';
import BaseBlock from '../../components/page/block';
import { ELButtonType, OrderDataWrapper } from '../../models';
import { IBlockConfiguration } from '../../models/block/configuration';
import { IOrderData } from '../../models/ordering/data';
import { CatKeys } from '../../utils/Defaults';
import Global from '../../utils/Global';
import { MainTexts, OrderingTexts } from '../../utils/messages';
import EasyRouter from '../../utils/router';
import { LButton, LClear } from '../page';
import { Category, ECategoryStatus } from '../tools/categoryWrapper';
import BrandTariffList from './brandTariffs';
import BrandThumb, { EBrandThumbStatus, EBrandThumbType } from './brandThumb';

export interface IBrandsListProps {
    block: IBlockConfiguration;
    //brands: IBrand[];
    withTariffs?: boolean;
    selected: string;
    style?: string;
    thumbType: EBrandThumbType;
    thumbSelectable: boolean;
    clickHandler?: any;
    hideProducts?: boolean;
    needSelection?: boolean;
    forceVisible?: boolean;
    crossSelling?: boolean;
    checkAvailabilityProductsUponClick?: boolean;
};

export interface IBrandsListState {
    selected?: string;
    changed: boolean;
};

export class BrandsList extends React.Component<IBrandsListProps, IBrandsListState> {
    private current: Category | any;
    private onlyOne: boolean;
    private oneClick: boolean = false;

    constructor(props: IBrandsListProps) {
        super(props);

        let sel = this.props.selected;

        this.onlyOne = Global.categoryWrapper.onlyOneBrandAvailable();

        this.current = Global.categoryWrapper.getSelected();

        this.state = {
            changed: true,
            selected: Global.categoryWrapper.cat
        };
    };

    public componentWillReceiveProps(next: IBrandsListProps) {

        if (!this.oneClick) {
            this.setState({ selected: next.selected });
        } 
    };

    public render() {
        this.current = Global.categoryWrapper.getSelected();

        if(!this.current) { return <></> }

        let subtitle: string = '';

        let cfg: IBlockConfiguration = {
            title: this.onlyOne ? '' : this.props.block.title,
            transparent: this.props.block.transparent,
            style: this.props.block.style,
            styleContainer: window.location.pathname == '/' ? 'middle' : ''
        };

        Global.categoryWrapper.reset();

        let act = <div>
            <LButton text={MainTexts.allTariffs}
                onClick={this.onSelectProduct}
                css="fr uc text-h2"
                type={ELButtonType.linkSimple}
                icon="arrow-right" />
            <LClear />
        </div>;

        this.current.id === CatKeys.vodafone || this.current.id === CatKeys.fn || this.current.id === CatKeys.tv ? act : act = <></>

        let cats = this.props.crossSelling ?
            Global.categoryWrapper.getCrossSellingCats()
            :
            Global.categoryWrapper.availableCats;

        let ctrl = (
            <>
                <BaseBlock config={cfg}>
                    {cats.map((i, idx) =>
                        this.renderThumb(i, idx)
                    )}
                </BaseBlock>
                {this.props.withTariffs && cats.length > 0 ?
                    <BrandTariffList
                        products={Global.categoryWrapper.getCategoryBrandProducts(true)}
                        key="xxx"
                        styleContainer={window.location.pathname == '/' ? 'middle' : ''}
                        brandKey={this.current.id}
                        onClick={this.onSelectProduct}
                        block={{ title: MainTexts.favoriteTariffs, subTitle: subtitle, transparent: true, action: act }}
                    />
                    : null
                }
            </>
        );

        return ctrl;
    };

    private renderThumb(cat: Category, idx: number): JSX.Element {
        if (
            cat.status != ECategoryStatus.available
            && cat.status != ECategoryStatus.inProcess
            && !this.props.forceVisible) { return <></>; }

        let sta: EBrandThumbStatus = EBrandThumbStatus.undefined;

        if (!this.props.thumbSelectable) {
            sta = sta | EBrandThumbStatus.inactive;
        }
        //console.log('selected:' + cat.id + '(' + cat.status + ')=' + this.current.id);
        if (cat.id === this.current.id) {
            sta = sta | EBrandThumbStatus.selected;
        }

        if (cat.status == ECategoryStatus.inProcess) {
            sta = sta | EBrandThumbStatus.loading;
        }
        //console.log('brand thumb ' + cat.id + ' = ' + cat.status);
        let ctrl = (
            <BrandThumb
                key={'bt-' + idx}
                ckey={'bt-' + idx}
                item={cat}
                status={sta}
                type={this.props.thumbType}
                onClick={(k) => {
                    if (cat.status != ECategoryStatus.available) { return; };
                    this.onSelectCat(k);
                }} />
        );

        return ctrl;
    };

    private onSelectCat = (key: string) => {
        Global.categoryWrapper.switchCat(key);

        if (this.props.clickHandler) {
            this.props.clickHandler(key);
        } else {
            this.setState({ changed: true })
        }
    };

    private onSelectProduct = (key: string) => {
        if (!this.current) { return; };

        if (!Global.easyApp.checkDefCon()) {
            return;
        };

        //all tarifs
        if (!key) {
            key = Global.categoryWrapper.getFirstProductInCategory();
        };

        let order: IOrderData = OrderDataWrapper.createOrder(Global.getUniqueId(), this.current.id,
            Global.categoryWrapper.getBrandByProduct(key),
            key);

        let processUrl: string = '/ordering/new/tariff/' + order.id;

        EasyRouter.route({ url: processUrl, title: OrderingTexts.activation + ": " + OrderingTexts.activationBNTTariff });
    };
};

export default BrandsList;