import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import * as page from '../../../components/page';
import BaseBlock from '../../../components/page/block';
import { Address } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { EActionAddressType, ECreditDebitType, EPaymentType } from '../../../models/ordering/order';
import { IPayment } from '../../../models/ordering/payment';
import { EControlType } from '../../../models/page/diverse';
import Global from '../../../utils/Global';
import { OrderingTexts, ValidationMessages } from '../../../utils/messages';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import { ActivationCustomerData } from '../process';



export interface ICustomerPaymentProps {
    parent: ActivationCustomerData;
    onChange: any;
    payment?: IPayment;
};

export interface ICustomerPaymentState {
    changed: boolean;
};

export class CustomerPayment extends React.Component<ICustomerPaymentProps, ICustomerPaymentState> {
    private payment: IPayment;
    constructor(props: ICustomerPaymentProps) {
        super(props);

        this.payment = this.props.payment ? this.props.payment : { owner: "", paymentType: EPaymentType.bank, addressType: EActionAddressType.sameAddress, address: new Address()};
        this.payment.iban = '';

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: any) {
        if(next.payment) {
            this.payment = next.payment;
        }
        this.setState({changed: true});
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.payment,
            transparent: false
        };

        let unityOnly = this.props.parent.props.parent.dataWrapper.hasUnityOnly();
        let oteloOnly = this.props.parent.props.parent.dataWrapper.hasOteloOnly();
        let oteloPrepaid = this.props.parent.props.parent.dataWrapper.hasOteloPrepaid();
        let age = Global.getAgeFromDate(this.props.parent.props.parent.data.customer.birthday);

        let canIban = true;

        if(oteloPrepaid && age < 18){
            canIban = false;
        }

        if(!oteloOnly && !canIban) {
            return <></>
        }

        if(!canIban) {
            this.props.payment!.paymentType = EPaymentType.cash;
        }

        if(unityOnly) {
            if(!this.props.parent.props.parent.data.customer.payment) {
                this.props.parent.props.parent.data.customer.payment = this.props.payment;
            };

            this.props.parent.props.parent.data.customer.payment!.owner = 
                this.props.parent.props.parent.data.customer.firstname + ' ' + this.props.parent.props.parent.data.customer.surname
        }

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        {
                            canIban ? 
                            <Col xs={4} md={4} className="np">
                                <page.LControl type={EControlType.radio} hideTitle={true}
                                        checked={this.payment.paymentType === EPaymentType.iban}
                                        onChange={this.props.onChange} name="payment.paymentType" 
                                        id="paymentType_1" label={OrderingTexts.payment1}/>
                            </Col>
                            : null
                        }


                        {
                            this.props.parent.props.parent.current 
                            && this.props.parent.props.parent.current.creditDebitType === ECreditDebitType.prepaid 
                            && (!this.props.parent.props.customer.topup || (this.props.parent.props.customer.topup && this.props.parent.props.customer.topup.amount == 0))
                            ?
                            <Col xs={4} md={4} className="np">
                                <page.LControl type={EControlType.radio} 
                                        hideTitle={true}
                                        checked={this.payment.paymentType === EPaymentType.cash}
                                        onChange={this.props.onChange} name="payment.paymentType" 
                                        id="paymentType_4" label={OrderingTexts.payment4}/>
                            </Col>                                          
                            : null
                        }
                    </Row>                    

                    <Row>
                        {
                            canIban && this.payment.paymentType === EPaymentType.iban ?
                            <Col xs={6} md={6} className="npr">
                                <page.LControl type={EControlType.text} onChange={this.props.onChange} 
                                maxLength={22}
                                  validationOptions={ 
                                    [
                                        new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                        
                                    ]
                                } 
                                    name="payment.iban" id="iban" label={OrderingTexts.payment1} value={this.payment.iban}/>
                            </Col>  
                            : null
                        }   
                        {
                            canIban && (this.payment.paymentType === EPaymentType.bank || this.payment.paymentType === EPaymentType.iban) ?
                            <Col xs={6} md={6} className="np">
                                <page.LControl type={EControlType.text} onChange={this.props.onChange} 
                                maxLength={32}
                                    preview={unityOnly}
                                    name="payment.owner" id="owner" label="Kontoinhaber" 
                                    validationOptions={[
                                        new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                        new ValidationOption({ type: EValidationOption.validateNames, message: 'Der Kontoinhaber beginnt mit einem Grobuchstaben. Die Eingabe sollte nur aus Buchstaben, Bindestrichen und Leezeichen bestehen' })
                                    ]}
                                    value={this.payment.owner}/>
                            </Col>
                            : null
                        }
                            

                        {
                            canIban && this.payment.paymentType === EPaymentType.bank ?
                            <>
                                <Col xs={6} md={6} className="npr">
                                    <page.LControl type={EControlType.text} onChange={this.props.onChange} 
                                        name="payment.bankId" id="bankId" label="BLZ" value={this.payment.bankId}/>
                                </Col>  
                                <Col xs={6} md={6} className="np">
                                    <page.LControl type={EControlType.text} onChange={this.props.onChange} 
                                        name="payment.accountId" id="accountId" label="Kontonummer" value={this.payment.accountId}/>
                                </Col>
                            </>
                            : null
                        }                                                               
                    </Row>

                    
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };    
};

export default CustomerPayment;