import * as React from 'react';
import * as m from '../../models/index';

export interface LTimerProps {
    count:number;
    step?:number;
    stepCallback?: any;
    tickCallback?: any;
    completed?:any;
    apiModel?: m.ApiModel; 
    showUI: boolean;
    autoRestart?: boolean;
};

export interface LTimerState {
    count: number;
};

class LTimer extends React.Component<LTimerProps, LTimerState> {
    private timer: any;

    constructor (props:LTimerProps) {
      super(props);

      this.state = {count: this.props.count};

      this.startTimer();
    }

    componentWillUnmount () {
      clearInterval(this.timer);
    };

    tick () {
      if(this.state.count <= 0) {
        if(this.props.completed) {
            this.stopTimer();
            if(this.props.completed) {
              this.props.completed();
            }
        };

        return;
      } else {     
        if(this.props.tickCallback) {
          this.props.tickCallback();
        }
        if(this.props.step) {
          if(this.state.count % this.props.step == 0) {
            if(this.props.stepCallback) {
              this.props.stepCallback(this.state.count);
            }
          }
        }
      }

      let cnt = this.state.count - 1;
      if(this.props.autoRestart && this.state.count<=1) {
          cnt = this.props.count;
      };

      this.setState({count: cnt});
    };

    startTimer () {
      clearInterval(this.timer)
      this.timer = setInterval(this.tick.bind(this), 1500)
    };

    stopTimer () {
      clearInterval(this.timer)
    }

    render () {
      if(this.props.showUI) {
        return (
          <div className='timer'>
              <div className="bg"/>
              <div className="inner">{this.state.count}</div>
          </div>
        )
      } else {
        return <div/>
      }
    }
  };

export default LTimer;