import { BrandKeys } from "../../utils/Defaults";
import ApiProcess from "../global/process";
import { EExistingCustomerLoginMode } from "../page/diverse";

export class ExistingCustomerLogin extends ApiProcess {
    ecusername: string = '';
    birthday: string = ''; //Date = new Date();
    brandKey: string = '';
    ecpassword: string ='';
    additional: string = '';
    
    temp1: string = '';
    temp2: string = '';
    customerId: string = '';
    withAvailability: number = 0;
    mode: EExistingCustomerLoginMode = EExistingCustomerLoginMode.password;

    public reset() {
        this.withAvailability = 0;
        this.ecusername = '';
        this.ecpassword = '';
        this.brandKey = '';
        this.additional  = '';
        this.birthday = '';
        this.temp1 = '';
        this.temp2 = '';
        this.customerId = '';
        this.brandKey = BrandKeys.vodafone;
        this.mode = EExistingCustomerLoginMode.password;
    };
};

export default ExistingCustomerLogin;