import * as React from 'react';
import * as page from '../../components/page/index';
import { IApiResponse } from '../../interfaces';
import { IAppSettings } from '../../interfaces/app/IAppSettings';
import { ELButtonType } from '../../models/page/diverse';
import * as s from '../../screens/insex';
import Api from '../../utils/api';
import { Roles, StorageKeys, Themes } from '../../utils/Defaults';
import Global from '../../utils/Global';
import { MainTexts } from '../../utils/messages';
import EasyRouter from '../../utils/router';
import StorageHelper from '../../utils/storage';
import ToolSearchExisting from '../tools/searchExisting';
import LButton from './button';

interface IUserPannelState {
    expanded: boolean;
    userOpen: boolean;
    changed: boolean;
};

interface IUserPannelProps {
    parent: s.MainScreen;
};

interface ISessionTrack {
    date: Date;
    lifetime: number;
    min: number;
};

export class UserPannel extends React.Component<IUserPannelProps, IUserPannelState> {
    private appSettings: IAppSettings;
    public static containerId: string = 'userPanel';
    private session: ISessionTrack;
    private interval: any;

    constructor(props: IUserPannelProps) {
        super(props);

        this._onExpand = this._onExpand.bind(this);
        this._logout = this._logout.bind(this);
        this._onExpandUser = this._onExpandUser.bind(this);
        this._onSwitchTheme = this._onSwitchTheme.bind(this);
        this.onLogout = this.onLogout.bind(this);
        this.trackSession = this.trackSession.bind(this);

        this.appSettings = StorageHelper.getAppSettings();

        Global.trackSession = this.trackSession;

        this.interval = setInterval(() => {
            this.session.min--;
            if (this.session.min <= 0) {
                clearInterval(this.interval);
                Global.easyApp.expiredSessionVisibility(true);
            };

            this.setState({ changed: true });
        }, 60000);

        this.session = {
            date: new Date(),
            lifetime: 60,
            min: 60
        };

        this.state = {
            expanded: this.appSettings.expanded,
            userOpen: false,
            changed: false
        };
    };

    public render() {
        let user = StorageHelper.getUser();
        let cfg = Global.getConfiguration();
        let styles = {};

        if (user) {
            styles = {
                backgroundImage: 'url(' + cfg.apiUrl + user.pictureUrl + ')'
            };
        };

        let cssu: string = 'user-container';
        if (this.state.userOpen) {
            cssu += ' open';
        };

        let css: string = 'sidepannel';
        if (this.state.expanded) {
            css += ' open';
        }

        let theme = localStorage.getItem(StorageKeys.keyTheme);

        let ctrl = (
            <div id={UserPannel.containerId}>
                <div className={css}>
                    <div className="tool-container">
                        <div className="action open">
                            <div className="icon arrow-left" onClick={this._onExpand} />
                        </div>
                        <div className="tool tool-name">
                            <div className="personal">{user ? user.firstname + ' ' + user.surname : ''}</div>
                            <div className="company">{user ? user.voName : ''}</div>
                        </div>

                        <div className={'tool '}>
                            <LButton cssText='np' text={MainTexts.logout} type={ELButtonType.buttonWithIconRight} onClick={this.onLogout} />
                        </div>

                        <div className="spacer"></div>

                        <ToolSearchExisting parent={this.props.parent} />
                        {/* <ToolCheckAvailability />
                        <ToolChecPortation /> */}
                    </div>

                    <div className={'tool ' + (this.state.expanded ? 'dn' : 'open')}>
                        <LButton cssText='np' text={MainTexts.logout} type={ELButtonType.buttonWithIconRight} onClick={this.onLogout} />
                    </div>


                    <div className="inner">
                    </div>
                    <div className="navi-container">

                        <div className="row">
                            <div className="col-md-6">
                                <page.LButton type={ELButtonType.linkWithIconLeft} icon="pepper"
                                    text={"Spice"}
                                    onClick={ ()=>{window.open('https://apps.unitymedia.de/SpiceExternal')}} />
                            </div>
                            <div className="col-md-6">
                                <page.LButton type={ELButtonType.linkWithIconLeft} icon="iconHA"
                                    text={"HA portal"}
                                    onClick={()=>{ window.open('https://secure.kabeldeutschland.de/auth/realms/bsso/protocol/openid-connect/auth?response_type=code&redirect_uri=https%3A%2F%2Fsecure.kabeldeutschland.de%2Fhandel%2Foidc%2Fauthenticate&client_id=handel&state=e7fec05684cfd32e7eb72bfc93862b9d&scope=openid+profile')}} />
                            </div>
                        </div>

                        <div className="sep"></div>

                        {!Global.isDelaer(false) ? <></> :
                            <>
                                <div className="row">
                                    <div className="col-md-6">
                                        {/* <page.LButton type={ELButtonType.linkWithIconLeft} icon="time" text="IMEI Nachmel."/> */}
                                        <page.LButton type={ELButtonType.linkWithIconLeft} icon="doc" text={MainTexts.drafts}
                                            onClick={() => { EasyRouter.route({ title: MainTexts.drafts, url: '/am/drafts' }); }} />

                                        {/* <page.LButton type={ELButtonType.linkWithIconLeft} icon="scan" text="Ausweisscanner"/> */}
                                        {/* <page.LButton type={ELButtonType.linkWithIconLeft} icon="list" text="Port. Listenabfrage"/> */}
                                    </div>
                                    <div className="col-md-6">
                                        <page.LButton type={ELButtonType.linkWithIconLeft} icon="doc" text={MainTexts.documentScanner}
                                            onClick={() => { EasyRouter.route({ title: MainTexts.documentScanner, url: '/am/idscanner' }); }} />
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-6">
                                        <page.LButton type={ELButtonType.linkWithIconLeft} icon="date" text={MainTexts.contractManager}
                                            onClick={() => { EasyRouter.route({ title: MainTexts.contractManager, url: '/am/contractmanager' }); }} />
                                    </div>
                                    {
                                        Global.inAnyRole([Roles.E3_User, Roles.XTND_ShopManager]) ? <div className="col-md-6">
                                            <page.LButton type={ELButtonType.linkWithIconLeft} icon="doc" text={MainTexts.mnpStatusLinkText}
                                                onClick={() => { EasyRouter.route({ title: MainTexts.contractManager, url: '/am/mnp/status' }); }} />
                                        </div> : <></>
                                    }
                                    {
                                        Global.inAnyRole([Roles.E3_User, Roles.XTND_ShopManager]) ? <div className="col-md-6">
                                            <page.LButton type={ELButtonType.linkWithIconLeft} icon="doc" text={MainTexts.mnpPortingStatusLinkText}
                                                onClick={() => { EasyRouter.route({ title: MainTexts.contractManager, url: '/am/mnp/portingstatus' }); }} />
                                        </div> : <></>
                                    }

                                    {
                                        Global.inAnyRole([Roles.XTND_Produktbestellung, Roles.XTND_ShopManager]) ? <div className="col-md-6">
                                            <page.LButton type={ELButtonType.linkWithIconLeft} icon="doc" text={MainTexts.orderProductLink}
                                                onClick={() => { EasyRouter.route({ title: MainTexts.contractManager, url: '/apps/orderproducts' }); }} />
                                        </div> : <></>
                                    }
                                    {
                                        user && user.accountLevel == 1 && Global.inAnyRole([Roles.Stornofreigabe, Roles.XTND_ShopManager]) ? <div className="col-md-6">
                                            <page.LButton type={ELButtonType.linkWithIconLeft} icon="doc" text={MainTexts.level1Cancellation}
                                                onClick={() => { EasyRouter.route({ title: MainTexts.cancellations, url: '/am/cancellation' }); }} />
                                        </div> : <></>
                                    }
                                </div>
                                <div className="row">
                                    {
                                        Global.inAnyRole([Roles.XTND_Nutzerverwaltung, Roles.XTND_ShopManager]) ? <div className="col-md-6">
                                            <page.LButton type={ELButtonType.linkWithIconLeft} icon="people"
                                                text={MainTexts.administration}
                                                onClick={() => { EasyRouter.route({ title: MainTexts.administration, url: '/am/administration' }); }} />  </div>
                                            : null
                                    }

                                    <div className="col-md-6">
                                        <page.LButton type={ELButtonType.linkWithIconLeft} icon="doc"
                                            text={MainTexts.imeiMenuItem}
                                            onClick={() => { EasyRouter.route({ title: MainTexts.imeiMenuItem, url: '/am/imei' }); }} />  </div>
                                </div>
                            </>
                        }

                        <div className="sep"></div>
                        <div className="row">
                            {/* <div className="col-md-6">
                                <page.LButton type={ELButtonType.linkPanel} icon="time" text="FAQ" />
                                <page.LButton type={ELButtonType.linkPanel} icon="doc" text="News" />
                                <page.LButton type={ELButtonType.linkPanel} icon="scan"
                                    text="Downloads" />
                            </div> */}
                            <div className="col-md-12">
                                <page.LButton type={ELButtonType.linkPanel} icon="date" text="AGB"
                                    onClick={() => { window.open("https://www.vodafone.de/agb.html", '_blank'); }} />
                                <page.LButton type={ELButtonType.linkPanel} icon="people" text="Datenschutz"
                                    onClick={() => { window.open("https://www.vodafone.de/unternehmen/soziale-verantwortung/datenschutz-privatsphaere.html", '_blank'); }} />
                                <page.LButton type={ELButtonType.linkPanel} icon="orders"
                                    text="Impressum"
                                    onClick={() => { window.open("https://www.vodafone.de/impressum.html", '_blank'); }} />
                            </div>
                        </div>
                    </div>
                </div >

                <div className={cssu}>
                    <div className="close">
                        <div className="icon close" onClick={this._onExpandUser} />
                    </div>
                    <div className="inside">
                        <div className="user" style={styles} onClick={this._onExpandUser} />
                        <div className="lifetime">
                            min.
                            <br />
                            <span className="min">{this.session.min > 10 ? this.session.min : '0' + this.session.min}</span>
                        </div>
                        <div className="tools">
                            <page.LButton type={ELButtonType.linkWithIconLeft} icon="profile" text="Profil bearbeiten" onClick={this._onProfileEditClick} />
                            <page.LButton type={ELButtonType.linkWithIconLeft} icon="lock" text="Passwort ndern" onClick={this._onPasswordEditClick} />
                            {
                                theme === Themes.light ?
                                    <page.LButton type={ELButtonType.linkWithIconLeft} icon="moon" text="Dunkles Farbschema" onClick={this._onSwitchTheme} />
                                    :
                                    <page.LButton type={ELButtonType.linkWithIconLeft} icon="moon" text="Standard Farbschema" onClick={this._onSwitchTheme} />
                            }

                        </div>
                        {/* <div className="sep" />
                        <div className="tools">
                            <page.LButton type={ELButtonType.linkWithIconLeft} css="action2" icon="logout" text={MainTexts.logout} onClick={this._logout} />
                        </div> */}
                    </div>

                    <page.LClear />

                </div>

            </div >
        );

        return ctrl;
    };

    private trackSession(api: IApiResponse) {
        if (api.lifetime > 0) {
            this.session.lifetime = this.session.min = api.lifetime;
            this.session.date = new Date();

            this.setState({ changed: true });
        };
    };

    private onLogout() {
        Global.easyApp.visibilityLogout(true);
    };



    private _onProfileEditClick() {
        EasyRouter.route({ url: '/user/profile', title: MainTexts.editProfile });
        //window.location.href = '/user/profile';
    };

    private _onPasswordEditClick() {
        EasyRouter.route({ url: '/user/password', title: MainTexts.changePassword });
    };

    private _onSwitchTheme() {
        let theme = localStorage.getItem(StorageKeys.keyTheme);
        if (theme === Themes.light) {
            theme = Themes.dark;
        } else {
            theme = Themes.light;
        }

        localStorage.setItem(StorageKeys.keyTheme, theme);

        window.location.href = '/';
    };

    private _onExpand() {
        this.appSettings.expanded = !this.state.expanded;
        StorageHelper.set(StorageKeys.keyApp, this.appSettings);
        Global.easyApp.expand(this.appSettings.expanded);

        this.setState({ expanded: this.appSettings.expanded });
    };

    private _onExpandUser() {
        
        if (!Global.easyApp.checkDefCon()) {
            return;
        };

        this.setState({ userOpen: !this.state.userOpen });
    };

    private _logout() {
        Api.logout();
    };
};

export default UserPannel;