import * as React from 'react';
import ProcessNewScreen from '../../../screens/ordering/process';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { Row, Col, Container } from 'react-bootstrap';
import BaseBlock from '../../../components/page/block';
import ActivationCustomerData from '../process/activationCustomerData';
import * as page from '../../page/index';
import { EControlType } from '../../../models/page/diverse';
import { IBew } from '../../../models/ordering/bew';

export interface ICustomerConsultingProps {
    parent: ActivationCustomerData;
    onChange: any;
    bew?: IBew;
};

export interface ICustomerConsultingState {
    changed: boolean;
};

export class CustomerConsulting extends React.Component<ICustomerConsultingProps, ICustomerConsultingState> {
    private bew: IBew;
    constructor(props: ICustomerConsultingProps) {
        super(props);

        this.bew = this.props.bew ? this.props.bew  : {consulting1: false, consulting2: false};

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: ICustomerConsultingProps) {
        this.bew = this.props.bew ? this.props.bew  : {consulting1: false, consulting2: false};
        this.setState({changed: true});
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: 'Beratungseinwilligung',
            transparent: false
        };

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    {/* fix for kabel not displaying */}
                    <div className="dn">  
                            <page.LControl 
                                onChange={this.props.onChange} 
                                checked = { this.bew && this.bew.consulting1 } 
                                type={EControlType.checkbox} hideTitle={true} 
                                style="check"
                                visible={true}
                                name="bew.consulting1" id="consulting1_true" label=""/>   
                            <page.LControl onChange={this.props.onChange} 
                                checked = { this.bew && this.bew.consulting2 } 
                                type={EControlType.checkbox} hideTitle={true} 
                                style="check"
                                visible={true}
                                name="bew.consulting2" id="consulting2_true" label=""/>                                                                     
                    </div>
                    <Row>
                        <Col xs={1} md={1} className="np">
                            <page.LControl 
                                onChange={this.props.onChange} 
                                checked = { this.bew && this.bew.consulting1 } 
                                type={EControlType.checkbox} hideTitle={true} 
                                style="check"
                                visible={true}
                                name="bew.consulting1" id="consulting1_true" label=""/>                                                         
                        </Col>
                        <Col xs={11} md={11} className="npr">
                            <page.LControl type={EControlType.hint} label="Ja, ich willige ein, dass die Vodafone GmbH* <br/>als Inhaberin der Marke otelo und die Vodafone Kabel Deutschland GmbH* meine Bestandsdaten untereinander austauschen, um mich individuell zu beraten, fr ihre Angebote zu werben und Marktforschung zu betreiben. Meine Bestandsdaten sind Angaben rund um mein Vertragsverhltnis, z.B. Name, Adresse und Geburtsdatum sowie Tarife, Optionen und Services. Ja, ich willige ein, dass meine Nutzungsdaten und meine Verkehrsdaten hchstens 6 Monate verwendet werden, um Telekommunikations- und Telemedien-Dienste bedarfsgerecht und individuell zu gestalten und zu vermarkten sowie Dienste mit Zusatznutzen fr mich bereitzustellen. Meine Nutzungsdaten sind Informationen ber Art, Umfang und Zeitpunkt der Nutzung der Webseiten von otelo/Vodafone, Vodafone Kabel Deutschland oder Kooperations- und Werbepartnern. Die Daten identifizieren mich oder mein Gert direkt und sind zum Teil in meinem Gert gespeichert, z.B. als Cookies. Meine Verkehrsdaten sind z.B. die Art des genutzten Telekommunikationsdienstes, Anfang, Ende und Dauer der Verbindungen, das genutzte Datenvolumen, an der Kommunikation beteiligte Personen, Gerte oder Anschlsse sowie deren Standorte. Ich kann meine Einwilligung jederzeit ganz oder teilweise mit Wirkung fr die Zukunft widerrufen. *Die Vodafone GmbH, Ferdinand-Braun-Platz 1, 40549 Dsseldorf (Vodafone) und die Vodafone Kabel Deutschland GmbH, Betastrae 6-8, 85774 Unterfhring (Vodafone Kabel Deutschland) sind wirtschaftlich ein Unternehmen und bieten Telekommunikationsdienstleistungen an, die sich ergnzen."/>
                        </Col>                        
                    </Row>

                    <Row>
                        <Col xs={1} md={1} className="np">
                            <page.LControl onChange={this.props.onChange} 
                                checked = { this.bew && this.bew.consulting2 } 
                                type={EControlType.checkbox} hideTitle={true} 
                                style="check"
                                visible={true}
                                name="bew.consulting2" id="consulting2_true" label=""/>                                                            
                        </Col>
                        <Col xs={11} md={11} className="npr">
                            <page.LControl type={EControlType.hint} label="Ja, ich willige ein, dass Vodafone und Vodafone Kabel Deutschland mich telefonisch, per E-Mail, Messenger, SMS und MMS ber Angebote von otelo/Vodafone und Vodafone Kabel Deutschland informieren. Ich kann meine Einwilligung jederzeit ganz oder teilweise mit Wirkung fr die Zukunft widerrufen."/>
                        </Col>                        
                    </Row>
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };    
};

export default CustomerConsulting;