import * as React from 'react';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { Row, Col, Container } from 'react-bootstrap';
import BaseBlock from '../../page/block';
import ActivationCustomerData from '../process/activationCustomerData';
import * as page from '../../page/index';
import { EControlType } from '../../../models/page/diverse';
import * as m from '../../../models/index';
import StorageHelper from '../../../utils/storage';
import linq from 'linq';
import { IListItem, IKeyText } from '../../../models/page/interfaces';
import { MainTexts, OrderingTexts } from '../../../utils/messages';
import { ICustomer } from '../../../models/ordering/customer';
import { EPhonebookType } from '../../../models/ordering/order';

export interface ICustomerPhonebookProps {
    parent: ActivationCustomerData;
    customer: ICustomer;
    onChange: any;
};

export interface ICustomerPhonebookState {
    changed: boolean;
};

export class CustomerPhonebook extends React.Component<ICustomerPhonebookProps, ICustomerPhonebookState> {
    constructor(props: ICustomerPhonebookProps) {
        super(props);

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: ICustomerPhonebookProps) {
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.phoneBookEntry,
            titleDesc:OrderingTexts.forDSL,
            transparent: false
        };

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>            
                    <div className="spacer-dim2"></div>
                    <Row>
                        <Col className="np">
                            <page.LControl type={EControlType.hint} label="Telefonbucheintrag gewnscht?"/>
                        </Col>
                    </Row>
                    <div className="spacer-dim2"></div>
                    <Row>
                        <Col xs={4} md={4} className="np">
                            <page.LControl type={EControlType.radio} 
                                    checked={this.props.customer.phonebook === EPhonebookType.no}
                                    onChange={this.props.onChange} name="phonebook" id="phonebook_0" title={MainTexts.no}/>
                        </Col>
                        <Col xs={4} md={4} className="npr">
                            <page.LControl type={EControlType.radio} 
                                    checked={this.props.customer.phonebook === EPhonebookType.yesDefault}
                                    onChange={this.props.onChange} name="phonebook" id="phonebook_1" title={OrderingTexts.phoneBook1}/>
                        </Col>
                        {/* <Col xs={4} md={4} className="npr">
                            <page.LControl type={EControlType.radio} 
                                    checked={this.props.customer.phonebook === EPhonebookType.yesIndividual}
                                    onChange={this.props.onChange} name="phonebook" id="phonebook_2" title={OrderingTexts.phoneBook2}/>
                        </Col>                                            */}
                    </Row>            
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };        
};

export default CustomerPhonebook;