import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBaseComponentState } from '../../../interfaces';
import { EControlType, EFormMode, ELButtonType, ERequestType } from '../../../models';
import { IContract } from '../../../models/existing/contract';
import { ICustomer } from '../../../models/ordering/customer';
import { EBillingType, EPaymentType } from '../../../models/ordering/order';
import { IKeyText } from '../../../models/page/interfaces';
import CustomerScreen from '../../../screens/existing/customer';
import { ValidationWrapper } from '../../../utils';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { Hints, MainTexts, OrderingTexts, ValidationMessages } from '../../../utils/messages';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import { EPopupType, IPopupConfig } from '../../dialogs/popup';
import { LControl } from '../../page';
import * as page from '../../page/index';

export interface IExistingCustomerPaymentState extends IBaseComponentState {
    mode: EFormMode
}

export interface IExistingCustomerPaymentProps {
    parent: CustomerScreen;
    contract?: IContract;
};

export class ExistingCustomerPayment extends React.Component<IExistingCustomerPaymentProps, IExistingCustomerPaymentState> {
    private customer: ICustomer;
    public static containerId = 'existingCustomerPayment';

    constructor(props: IExistingCustomerPaymentProps) {
        super(props);

        this.switchMode = this.switchMode.bind(this);

        this.state = {
            changed: false,
            mode: EFormMode.view
        };

        this.onInput = this.onInput.bind(this);

        this.customer = JSON.parse(JSON.stringify(this.props.parent.data.customer));
    };

    public render() {
        const preview = this.state.mode === EFormMode.view;

        let optionsBillType: IKeyText[] = [];

        let billTypeDesc = Hints.paymentHintOPL;

        if (this.customer.evn && this.customer.evn.billingType == EBillingType.email) {
            billTypeDesc = Hints.paymentHintOPS.replace('$email', (this.customer.email ? this.customer.email : MainTexts.na));
        };



        if (!this.props.contract) {
            return <></>
        } else {
            if (this.props.contract.brandKey === BrandKeys.otelo) {
                optionsBillType = [
                    { key: '1', text: 'Rechnung per E-Mail' },
                    { key: '2', text: 'Rechnung Online / App' },
                ];
            };

            let ctrl =
                <div id={ExistingCustomerPayment.containerId}>
                    <Container>
                        <Row>
                            <Col xs={6} md={6} className="np">
                                <page.LControl onChange={this.onInput}
                                    preview={preview}
                                    checked={this.customer.evn != null && this.customer.evn.billingType == EBillingType.online}
                                    type={EControlType.dropdown}
                                    value={this.customer.evn ? this.customer.evn.billingType.toString() : ''}
                                    options={optionsBillType}
                                    hideTitle={true}
                                    label={OrderingTexts.billType}
                                    name="evn.billingType"
                                    id="billingType_dd" />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} className="np">
                                <page.LControl
                                    type={EControlType.info}
                                    hideTitle={true}
                                    isHtml={true}
                                    overflowEnabled={true}
                                    label={billTypeDesc} />
                            </Col>
                        </Row>

                        <Row>
                            <Col>
                                <page.LControl type={EControlType.radio} title={OrderingTexts.payment}
                                    checked={this.customer.payment && this.customer.payment.paymentType === EPaymentType.iban}
                                    name="payment.paymentType"
                                    id="paymentType_1" label={OrderingTexts.payment1} />
                            </Col>
                        </Row>
                        <Row>
                            <Col md={6} className="np">
                                <LControl preview={preview}
                                    type={EControlType.text}
                                    style="merged"
                                    title={OrderingTexts.bankOwner}
                                    id={"owner"}
                                    name={"payment.owner"}
                                    onChange={this.onInput}
                                    maxLength={27}
                                    validationOptions={ 
                                        [
                                            new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                            new ValidationOption({type: EValidationOption.alphanumeric, message: ValidationMessages.alphanumeric })
                                        ]
                                    }                                    
                                    value={this.customer.payment ? this.customer.payment.owner : ''}
                                    />
                            </Col>
                            <Col md={6} className="">
                                <LControl preview={preview}
                                    type={EControlType.text}
                                    style="merged"
                                    title={'IBAN'}
                                    id={"iban"}
                                    name={"payment.iban"}
                                    onChange={this.onInput}
                                    value={this.customer.payment ? (preview ? this.hideIBAN(this.customer.payment.iban) : this.customer.payment.iban) : ''}
                                    validationOptions={ 
                                        [
                                            new ValidationOption({type: EValidationOption.required, message: ValidationMessages.requiredError }),
                                            new ValidationOption({type: EValidationOption.germanIBAN, message: ValidationMessages.germanIBANerror })
                                        ]
                                    }   
                                    />
                            </Col>
                        </Row>

                        <Row>
                            <Col md={4}></Col>
                            <Col xs={12} md={12} >
                                {this.state.mode === EFormMode.view ?
                                    <page.LButton type={ELButtonType.buttonWithIconRight}
                                        css="a1 fr"
                                        id="buttonEdit"
                                        text={MainTexts.edit}
                                        onClick={() => {
                                            if (this.customer.payment === undefined) {
                                                return;
                                            }
                                            
                                            this.customer.payment!.iban = '';

                                            this.setState({ mode: EFormMode.edit });
                                        }} /> :
                                    <>
                                        <page.LButton type={ELButtonType.buttonWithIconRight}
                                            css="a1 fr"
                                            id="buttonSave"
                                            text={MainTexts.save}
                                            onClick={() => {
                                                let cfg: IPopupConfig = {
                                                    props: {
                                                        id: 'dynamicPopup',
                                                        title: 'Zahlungsdaten ndern',
                                                        visible: true,
                                                        closable: false,
                                                        type: EPopupType.pleaseWait
                                                    },
                                                    content: <div>{OrderingTexts.customerData}</div>
                                                };

                                                if(ValidationWrapper.validateFormular(undefined, true, ExistingCustomerPayment.containerId)){
                                                    ValidationWrapper.resetFormular(ExistingCustomerPayment.containerId);

                                                    this.props.parent.processCustomer(() => {
                                                        Global.hub.send('{ "type": ' + ERequestType.customerChangePayment + ', "brandKey": "' + (this.customer.contracts ? this.customer.contracts[0].brandKey : '') + '", "iban": "' + (this.customer.payment ? this.customer.payment.iban : '') + '", "owner": "' + (this.customer.payment ? this.customer.payment.owner : '') + '", "billingType": ' + (this.customer.evn ? this.customer.evn.billingType : 0) + ', "id": "' + this.props.parent.data.id + '", "orderId": "' + this.props.parent.data.activations[0].id + '" }',
                                                            ERequestType.customerChangePayment,
                                                            undefined);
                                                    }, cfg);
                                                }
                                            }} />
                                        <page.LButton type={ELButtonType.linkSimple}
                                            css="action3 fr"
                                            id="buttonCancel"
                                            text={MainTexts.cancel}
                                            onClick={() => {
                                                this.customer = JSON.parse(JSON.stringify(this.props.parent.data.customer));
                                                this.setState({ mode: EFormMode.view });
                                            }} />
                                    </>
                                }
                            </Col>
                        </Row>
                    </Container>
                </div>;

            return ctrl;
        }
    };

    public hideIBAN(iban: string | undefined) {
        if (iban === undefined || iban === null) {
            return '';
        } else if (this.state.mode === EFormMode.edit) {
            if (this.customer.payment !== undefined) {
                this.customer.payment.iban = iban;
            }
        } else {
            if(iban.length > 4) {
                iban = iban.substring(iban.length - 4);
            }

            iban = '****' + iban;
            return iban;
        }
    };

    public switchMode(newMode: EFormMode) {
        this.setState({ mode: newMode });
    };

    private onInput(target: any) {
        Global.onInput(this.customer, target, () => {
            this.setState({ changed: true });
        });
    };
};

export default ExistingCustomerPayment;