import * as React from 'react';
import { IBlockConfiguration } from '../../models/block/configuration';
import { LClear, LButton } from '.';
import { ELButtonType } from '../../models';

export interface IBlockBaseProps {
    children: JSX.Element[] | JSX.Element;
    config: IBlockConfiguration;
};

export interface IBlockBaseState {
    changed: boolean;
    //collapsed: boolean;
};

export interface IBaseBlockActionProps {
    children: JSX.Element[];
};

export class BaseBlockAction extends React.Component<IBaseBlockActionProps, any> {
    constructor(props:any) {
        super(props);
    };

    public render() {
        if(!this.props.children || this.props.children.length === 0) {return <></>;}
        
        return <div className="block-action">
                {this.props.children}
            </div>
    };
};

export class BlockBase extends React.Component<IBlockBaseProps, IBlockBaseState>{    
    private collapsed: boolean = false;
    constructor(props: IBlockBaseProps){
        super(props);

        this.collapsed =  this.props.config.collapsed ? this.props.config.collapsed : false;

        this.state = {
            changed: false,
            //collapsed: this.collapsed //this.props.config.collapsed ? this.props.config.collapsed : false
        };
    };

    public componentWillReceiveProps(next: IBlockBaseProps) {
        this.collapsed = next.config.collapsed ? next.config.collapsed : false;

        this.setState({changed: true});
    };

    public render() {
        let cls : string = 'block block-inner page-break-avoid';
        let clsc: string = 'block-container';

        if(this.props.config.transparent) {
            cls += ' block-trans';
        }

        if(this.props.config.styleContainer) {
            cls += ' ' + this.props.config.styleContainer;
        }

        if(this.props.config.style) {
            clsc += ' ' + this.props.config.style;
        }

        if(this.collapsed) {
            clsc += ' dn';
        }


        let ctrl = ( 
        <div className={cls} id={this.props.config.id}>
            {
                this.props.config.teaser ? this.props.config.teaser : null
            }

            {                
                this.props.config.title && this.props.config.title.length > 0 ? 
                <div className="title">
                    <span>{this.props.config.title}</span>
                    {this.props.config.titleDesc ? <div className="desct">{this.props.config.titleDesc}</div> : null}
                    {
                            this.props.config.action ?  
                            <div className="action">
                                {this.props.config.action}
                            </div>
                            : null
                    }                    
                    <div className="spacer-dim2"></div>
                </div>
                 : null
            }
            {
                this.props.config.collapsible ? this.renderCollapse() : null
            }
            <LClear/>
            {this.props.config.subTitle ? <div className="subtitle">{this.props.config.subTitle}</div> : null}
            <div className={clsc}>
                {this.props.children}
            </div>
            <div className="clear"></div>
        </div>
        );

        return ctrl;
    };

    private renderCollapse() : JSX.Element {
        let ctrl = <BaseBlockAction>
            <LButton type={ELButtonType.iconImage}
                        css="chevron"
                        icon={this.collapsed ? "chevron-down" : "chevron-up"}
                        onClickEvent={(e, m) => {
                            e.preventDefault();
                            e.stopPropagation();
                            this.collapsed = !this.collapsed;

                            this.setState({changed: true}, ()=>{
                                if(this.props.config.onCollapse) {
                                    this.props.config.onCollapse(this.collapsed);
                                };                                
                            });

                            return false;
                        }} />
            <></>
        </BaseBlockAction>;

        return ctrl;
    };
};

export default BlockBase;