import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBaseComponentState } from '../../../interfaces/index';
import { EControlType } from '../../../models/page/diverse';
import Global from '../../../utils/Global';
import GlobalJSX from '../../../utils/GlobalJSX';
import { MainTexts } from '../../../utils/messages';
import * as page from '../../page/index';

export interface IAddressProps {
    prefix: string;
    onChange: any;
    source: any;
    withAditional?: boolean;
    addressAdditionalFields?: boolean;
    preview: boolean;
    showStrnAdditional?: boolean

};

export interface ICustomerDataMainState {
    changed: boolean;
};

export class Address extends React.Component<IAddressProps, IBaseComponentState> {
    constructor(props: IAddressProps) {
        super(props);

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: IAddressProps) {
        this.setState({ changed: true });
    };

    public render() {
        let ctrl = (<>
            <Container className="np">
                <Row>
                    <Col xs={7} md={7} className="np">
                        {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'street', MainTexts.address, MainTexts.street, this.props.preview,undefined,undefined,undefined,30)}
                    </Col>
                    <Col xs={3} md={3} className="">
                        {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'nr', " ", MainTexts.stnr, this.props.preview,undefined,undefined,undefined,5)}
                    </Col>
                    {this.props.showStrnAdditional ? <Col xs={2} md={2} className="npr">
                        {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'nre', " ", MainTexts.stnre, this.props.preview)}
                    </Col> : <></>}
                </Row>

                <Row>
                    <Col xs={3} md={3} className="np">
                        {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'zip', undefined, MainTexts.zip, this.props.preview,undefined,undefined,undefined,5)}
                    </Col>
                    <Col xs={9} md={9} className="npr">
                        {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'city', undefined, MainTexts.city, this.props.preview,undefined,undefined,undefined,24)}
                    </Col>
                </Row>

                {
                    this.props.addressAdditionalFields ? <Row>
                        <Col xs={3} md={3} className="np">
                            {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'Name', undefined, MainTexts.nameAddress, this.props.preview)}
                        </Col>
                        <Col xs={9} md={9} className="npr">
                            {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'Adresszusatz', undefined, MainTexts.addressAdditional, this.props.preview)}
                        </Col>
                    </Row> : <></>
                }

                {
                    this.props.withAditional ?
                        <Row>
                            <Col xs={12} md={12} className="np">
                                {GlobalJSX.renderInner(this.props.prefix, this.props.source, this.props.onChange, 'additional', undefined, MainTexts.adrAdditional, this.props.preview)}
                            </Col>
                        </Row>
                        : <></>
                }
            </Container>
            <page.LClear />
        </>
        );

        return ctrl;
    };



    private renderInner(field: string, title: string | undefined, label: string): JSX.Element {
        let id = this.props.prefix + '_' + field;
        let name = this.props.prefix + '.' + field;
        let value = Global.getValueByPath(this.props.source, name);

        let ctrl = <page.LControl type={EControlType.text} value={value}
            onChange={this.props.onChange} title={title}
            hideTitle={!title}
            name={name} id={id} label={label} />;

        return ctrl;
    };
};

export default Address;