export class ActivationPrice {
    //priceSub?: number;
    //priceOptions: number=0;
    //pricePromotions: number=0;

    monthlyFeeProduct: number = 0;
    monthlyFeeSub: number = 0;
    monthlyFeeOptions: number = 0;

    activationFeeProduct: number = 0;
    activationFeeSub: number = 0;
    activationFeeOptions: number = 0;

    public add(pr: ActivationPrice) {
        this.monthlyFeeProduct += pr.monthlyFeeProduct;
        this.monthlyFeeSub += pr.monthlyFeeSub;
        this.activationFeeProduct += pr.activationFeeProduct;
        this.activationFeeSub += pr.activationFeeSub;
        this.monthlyFeeOptions += pr.monthlyFeeOptions;
    };

    public getActivationFee() {
        var fee: IFee = {
            price: (this.activationFeeSub || 0) + (this.activationFeeProduct || 0) + (this.activationFeeOptions || 0),
            unit: MainTexts.euro
        };

        return fee;
    };

    public getOptionsFeeMonthly() {
        var fee: IFee = {
            price: (this.monthlyFeeOptions || 0),
            unit: MainTexts.euro
        };

        return fee;
    };

    public getMonthlyFee() {
        var fee: IFee = {
            price: (this.monthlyFeeProduct || 0) + (this.monthlyFeeOptions || 0) + (this.monthlyFeeSub || 0),
            unit: MainTexts.euro
        };

        return fee;
    };
};

export class ActivationWrapper {
    public items: ActivationHelper[] = [];
    public priceTotal: ActivationPrice = new ActivationPrice();

    constructor(data: IOrderData) {
        let br = StorageHelper.getBrands();
        data.activations.map((i) => {
            let b = linq.from<IBrand>(br).where(x => x.id == i.brandKey).first();
            let ah = new ActivationHelper(i, b, data.customer);
            this.priceTotal.add(ah.priceTotal);
            this.items.push(ah);
        });
    };

    getMonthlyFee(): IFee {
        let pr: IFee = {
            price: 0,
            unit: MainTexts.euro
        };

        this.items.map((i)=>{
            pr.price += (i.getMonthlyFee().price);
        });

        return pr;
    };
 
    getActivationFee(): IFee {
        let pr: IFee = {
            price: 0,
            unit: MainTexts.euro
        };

        this.items.map((i)=>{
            pr.price += (i.getActivationFee().price);
        });

        return pr;
    };

    getOptionMonthlyFee(): IFee {
        let pr: IFee = {
            price: 0,
            unit: MainTexts.euro
        };

        this.items.map((i)=>{
            pr.price += (i.getOptionMonthlyFee().price);
        });

        return pr;
    };

    getActivationByIndexes(act: number, inner: number) : ActivationUI {
        return this.items[act].items[inner];
    };
};

//** activation helper to handle more than one activation */
export class ActivationHelper {
    public items: ActivationUI[] = [];
    public activation: IActivation;
    private brand: IBrand;

    private customer: ICustomer;    
    public priceTotal: ActivationPrice = new ActivationPrice();

    constructor(act: IActivation, 
        brand: IBrand,
        customer: ICustomer) {
        this.brand = brand;
        this.customer = customer;
        this.activation = act;

        if (act.selection.children.length > 0 && brand.id == BrandKeys.kabel) {
            act.selection.children.map((i) => {
                let a = IActivation.fromSelection(act.brandKey, i)
                a.activationType = act.activationType;
                a.activationDate = act.activationDate;
                a.contractId = act.contractId;
                a.processStatus = act.processStatus;

                this.add(ActivationUI.fromActivation(a, brand, customer));
            });
        } else {
            this.add(ActivationUI.fromActivation(act, brand, customer));
        }

        this.init();

        this.calculatePrices();
    };

    getMonthlyFee(): IFee {
        let pr: IFee = {
            price: 0,
            unit: MainTexts.euro
        };

        this.items.map((i)=>{
            pr.price += (i.getMonthlyFee().price);
        });

        return pr;
    };

    getOptionMonthlyFee(): IFee {
        let pr: IFee = {
            price: 0,
            unit: MainTexts.euro
        };

        this.items.map((i)=>{
            pr.price += (i.getOptionMonthlyFee().price);
        });

        return pr;
    };

    getActivationFee(): IFee {
        let pr: IFee = {
            price: 0,
            unit: MainTexts.euro
        };

        this.items.map((i)=>{
            pr.price += (i.getActivationFee().price);
        });

        return pr;
    };

    hasMultiple() {
        return this.items && this.items.length > 1;
    };

    private init() {
        this.items.map((i:any) => {
            i.productUI = linq.from<ISub>(this.brand.products)
                .where(x => x.id === i.productId)
                .select(x => x.name)
                .firstOrDefault();

            i.subUI = linq.from<ISub>(this.brand.subs)
                .where(x => x.id === i.sub)
                .select(x => x.name)
                .firstOrDefault();

            i.promotionsUI = linq.from<string>(i.services)
                .join(this.brand.services, a => a, s => s.id, (a, s) => { return { A: a, S: s } })
                .where(x => x.S.type === EServiceType.promotion)
                .select(x => x.S.name)
                .toArray()
                .join(', ');

            i.optionsUI = linq.from<string>(i.services)
                .join(this.brand.services, a => a, s => s.id, (a, s) => { return { A: a, S: s } })
                .where(x => x.S.type === EServiceType.option)
                .select(x => x.S.name)
                .toArray()
                .join(', ');

            i.activationDateTitleUI = i.activationType === EActivationType.existingCustomer ? OrderingTexts.prolongationDate :
                i.activationType === EActivationType.actDefault ? OrderingTexts.activationDate : OrderingTexts.portationDate;
        })
    };

    private add(act: ActivationUI) {
        this.items.push(act);
    };

    private calculatePrices() {
        this.items.map((a) => {
            //this.calculateSingle(a)
            this.priceTotal.add(a.price);
        });
    };

    private getOptionPriceMonthly(act: IActivation): IFee {
        let res: number = 0;
        for (let i = 0; i < act.services.length; i++) {
            let opt = linq.from(this.brand.services)
                .where(x => x.id === act.services[i] && x.type === EServiceType.option)
                .firstOrDefault();

            if (opt) {
                if (this.brand.id === BrandKeys.kabel && opt.id.indexOf('bereitstellungsentgelt') >= 0) {
                    continue;
                };

                if (opt && opt.monthlyFee) {
                    res += opt.monthlyFee.price;
                };
            };

            //2020-06-18
            //special case - otelo, option is delivered as promotions
            //if monthlyfee then add it to the option price
            if (act.brandKey === BrandKeys.otelo) {
                let prom = linq.from(this.brand.services)
                    .where(x => x.id === act.services[i] && x.type === EServiceType.promotion)
                    .firstOrDefault();

                if (prom && prom.monthlyFee) {
                    res += prom.monthlyFee.price;
                };
            };
        };

        let f: IFee = {
            price: res,
            unit: Defaults.currencyUnit
        };

        return f;
    };

    private getOptionPriceActivation(act: IActivation): IFee {
        let res: number = 0;
        for (let i = 0; i < act.services.length; i++) {
            let oid = act.getOptionUniqueId(act.services[i]);
            let opt = linq.from(this.brand.services)
                .where(x => x.id === oid && x.type === EServiceType.option)
                .firstOrDefault();

            if (opt) {
                if (act.brandKey === BrandKeys.kabel && opt.id.indexOf('bereitstellungsentgelt') >= 0) {
                    continue;
                };

                if (opt && opt.activationFee) {
                    res += opt.activationFee.price;
                };
            };
        };

        let f: IFee = {
            price: res,
            unit: Defaults.currencyUnit
        };

        return f;
    };

    private getProductPrice(act: IActivation, product: IProduct): IFee {
        let fee: IFee = JSON.parse(JSON.stringify(product.monthlyFee));
        if (act.brandKey === BrandKeys.vodafone) {
            if (act.services.indexOf('SOHOV15_1') >= 0) {
                fee.price = fee.price * 0.9;
            };
        };

        return fee;
    };

    private getPromoPrice(act: IActivation) {
        let res: number = 0;
        for (let i = 0; i < act.services.length; i++) {
            let opt = linq.from(this.brand.services)
                .where(x => x.id === act.services[i] && x.type === EServiceType.promotion)
                .firstOrDefault();
            if (opt && opt.activationFee) {
                res += opt.activationFee.price;
            };
        }

        let f: IFee = {
            price: res,
            unit: Defaults.currencyUnit
        };

        return f;
    };
};

export interface IActivationFile {
    id: string;
    OrderId: string;
    Name: string;
    Type: EActivationFileType;
};

//** the base activation object */
export class IActivation {
    public id: string = Global.getUniqueId();
    public idx: number = 0;
    public processId: string = '';
    public catKey: string = '';
    public brandKey: string = '';
    public familyId: number = 0;
    public sub: string = '';
    public productId: string = '';
    public services: string[] = [];
    public servicesRemove: string[] = [];
    // public activationDate: Date = new Date();
    public msisdn: string = '';
    public code: string = '';
    public imei: string = '';
    public barcode: string = '';
    public contractId: string = '';
    public activationDate: Date = Global.convertUTCDateToLocalDate(new Date());
    public activationTime: EActivationTime = EActivationTime.now;
    public creditDebitType: ECreditDebitType = ECreditDebitType.postpaid;
    public activationType: EActivationType = EActivationType.actDefault;
    public availability: Availability = new Availability();
    public portationType: EPortationType = EPortationType.undefined;
    public portationDate: Date = new Date();
    public availabilityId: string = '';
    public inCurrent: boolean = false;
    public draftId?: string = '';

    //public nbaId?: number;

    public cart: IAvailabilityResultItem[] = [];

    //cable multiple
    public selection: TreeItem = new TreeItem('root');

    public additional: boolean = false;
    //** used for otelo to mark an "weiteren Vertrag" */
    public nextContract: boolean = false;
    public customerId: string = '';
    public customerPassword: string = '';

    public isSimulation: boolean = false;

    public price: ActivationPrice = new ActivationPrice();
 
    public processStatus: i.IProcessStatus = { steps: [] };

    public action?: ICustomerAction;

    public files?: IActivationFile[];

    constructor(brand: string) {
        this.brandKey = brand;
        this.validateService = this.validateService.bind(this);
        this.getActivationDate = this.getActivationDate.bind(this);
    };

    isMultiple = () =>{
        if(this.selection && this.selection.children.length > 1) {
            return true;
        };

        return false;
    };

    isNba = () =>{
        return this.action && this.action.nbaId ? true : false;
    };

    getFile = (type: EActivationFileType) => {
        if(!this.files) { return undefined; }

        return linq.from<IActivationFile>(this.files).where(x=>x.Type == type).firstOrDefault();
    };

    hasBoniFile = () => {
        return this.getFile(EActivationFileType.boni) != undefined;
    }; 

    hasContractFile = () => {
        return this.getFile(EActivationFileType.contractMain) != undefined;
    }; 

    public static createInstance(brand: string,
        processId: string,
        catKey: string): IActivation {
        let act: IActivation = new IActivation(brand);
        act.activationType = EActivationType.actDefault;
        act.portationType = EPortationType.undefined;
        act.processId = processId;
        act.catKey = catKey;
        act.id = Global.getUniqueId();
        return act;
    };

    //**used to create a new Activation object from selection.TreeItem */
    public static fromSelection(brandKey: string, ti: TreeItem): IActivation {
        let a = new IActivation(brandKey);
        a.productId = ti.id;

        let br = StorageHelper.getAvailabilityForBrand(brandKey);
        
        let pr = linq.from<AvailabilityProduct>(br.products).where(x => x.productId == ti.id).firstOrDefault();

        let sub = linq.from<TreeItem>(ti.children)
            .join(pr!.elements, i => i.id, p => p.metaname, (i, p) => {
                return { I: i, P: p };
            })
            .where(x => x.P.type == ConstantKeys.devices)
            .firstOrDefault();

        if (sub) {
            a.sub = sub.P.metaname;

            a.services = linq.from<TreeItem>(ti.children)
                .join(pr!.elements, i => i.id, p => p.metaname, (i, p) => {
                    return { I: i, P: p };
                })
                .where(x => x.P.type != ConstantKeys.devices)
                .select(x => x.I.id)
                .toArray();
        }

        return a;
    };

    public static clone(a: IActivation) {
        let act = JSON.parse(JSON.stringify(a));
        return IActivation.ensure(act);
    };

    public static ensure(a: IActivation) {
        let aaa:IActivation = Object.assign(new IActivation(a.brandKey), a);
        aaa.selection = TreeItem.ensure(aaa.selection);
        return aaa;
    };

    public updateSelection(sel: TreeItem) {
        this.selection = sel;
    };

    public getProductsTree() : ProductTree[] {
        let ti = new ProductTree(this.brandKey, this.catKey);

        ti.familyId = this.familyId;
        ti.id = this.productId;
        ti.brand = this.brandKey;

        if(this.brandKey == BrandKeys.kabel) {
            let res:ProductTree[] = [];
            let br = StorageHelper.getBrand(this.brandKey);

            this.selection.children.map((i, idx)=>{
                let p = linq.from<IProduct>(br.products).where(x=>x.id == i.id).first();

                ti = new ProductTree(this.brandKey, p.catKey);

                ti.familyId = p.familyId;
                ti.id = p.id;
                ti.brand = this.brandKey;

                let a = ProductTree.FromTree(i);
                a.idx = idx;
                ti.children.push(a);

                res.push(ti);
            });

            return res;
        } else {
            let sub = new ProductTree(this.sub, '');

            this.services.map((i)=>{
                sub.add(i);
            });

            ti.addChildren([sub]);
        }

        return [ti];
    };

    public addRemoveProduct(id: string) {
        if (this.brandKey != BrandKeys.kabel) {
            this.selection = new TreeItem('root');
        };

        //remove products form the same category
        let insel = this.selection.getChildrenIds();
        let currentInSel = insel.indexOf(id);
        let thisCat = Global.categoryWrapper.getCategoryByProduct(id);
        insel.map((i) => {
            let c = Global.categoryWrapper.getCategoryByProduct(i);

            if (c == thisCat) {
                //console.log('remove cat:' + thisCat + ' / ' + c + '   ' + i);
                this.selection.remove(i);
            };
        });
       
        //console.log('proceed');
        if (currentInSel == -1) {
            //console.log('insel add ' + thisCat + ':' + id);
            this.selection.addOrRemove(id);
        };

        this.productId = id;
    };

    public addRemoveService(id: string, productId: string | undefined) {
        if(this.hasService(id, productId)) {
            this.removeService(id, undefined);
        } else {
            this.addService(id, productId);
        };
    };

    public addRemoveServiceRemove(id: string, productId: string | undefined) {
        let i = this.servicesRemove.indexOf(id);
        if (i >= 0) {
            this.servicesRemove.splice(i, 1);
        } else {
            this.servicesRemove.push(id);
        };
        StorageHelper.updateActivation(this);
    };

    public removeService(service: string, productId: string | undefined) {
        let i = this.services.indexOf(service);
        if (i >= 0) {
            this.services.splice(i, 1);
            StorageHelper.updateActivation(this);
        };
    };

    public addService(service: string, productId: string  | undefined) {
        if(this.hasService(service, productId)) { return; };

        if(this.brandKey == BrandKeys.kabel) {
            var p = linq.from<TreeItem>(this.selection.children).where(x=>x.id == productId).firstOrDefault();

            if(p) {
                p.addOrRemove(service);
            }
        } else {
            this.services.push(service);
        }
        
        StorageHelper.updateActivation(this);
    };

    public reset() {
        this.productId = this.sub = '';
        this.services = [];
        this.servicesRemove = [];
        this.selection.reset();
    };

    public getProducts() {
        return this.selection.getChildrenIds();
    };

    public hasProduct(id: string) {
        if(this.brandKey == BrandKeys.kabel) {
            return this.selection.getChildrenIds().indexOf(id) >= 0;
        } else {
            return this.productId == id;
        };
    };

    public getSubs(productId: string) {
        let el = this.selection.find(productId);
        return el!.getChildrenIds();
        //return this.subs.join(';');
    };

    public addRemoveSub(productId: string, id: string) {
        let el = this.selection.find(productId);
        if (this.brandKey != BrandKeys.kabel) {
            this.selection.children = [];
        }

        this.sub = id;
        el!.addOrRemove(id);
    };

    public isActivated(): boolean {
        if (!this.processStatus || !this.processStatus.steps) { return false; }

        let s = this.getStep(ERequestType.activation);

        if (s) {
            return s.status === ERequestStatus.done;
        };

        return false;
    };

    public isTimedout(): boolean {
        if (!this.processStatus || !this.processStatus.steps) { return false; }

        let s = this.getStep(ERequestType.activation);

        if (s) {
            return s.status === ERequestStatus.timeout;
        };

        return false;
    };

    public getProductPrice(product: IProduct): IFee {
        let fee: IFee = JSON.parse(JSON.stringify(product.monthlyFee));
        if (this.brandKey === BrandKeys.vodafone) {
            if (this.services.indexOf('SOHOV15_1') >= 0) {
                fee.price = fee.price * 0.9;
            };
        };

        let found: boolean = false;


        return fee;
    };

    public getMonthlyFee(): IFee {
        let br = StorageHelper.getBrand(this.brandKey);

        var prBrand = linq.from<IProduct>(br.products).where(x => x.id === this.productId).first();
        var sub = linq.from<ISub>(br.subs).where(x => x.id === this.sub).firstOrDefault();

        let pr: IFee = {
            price: prBrand.monthlyFee.price,
            unit: MainTexts.euro
        };

        if (sub && sub.monthlyFee) {
            pr.price += sub.monthlyFee.price;
        };

        return pr;
    };

    public activateManually() {
        if (!this.processStatus || !this.processStatus.steps) { return false; }

        this.processStatus.steps.forEach((i, idx) => {
            i.status = ERequestStatus.done;
        });

        StorageHelper.updateActivation(this);
    };

    public isPrinted(): boolean {
        if (!this.processStatus || !this.processStatus.steps) { return false; }

        let s = this.getStep(ERequestType.print);

        if (s) {
            return s.status === ERequestStatus.done;
        };

        return false;
    };

    public getStep(type: ERequestType): i.IApiStep {
        //console.log('getstep');
        //console.log(this.processStatus); 

        if (!this.processStatus) {
            this.processStatus = { steps: [] };
        }

        var st = linq.from<i.IApiStep | any>(this.processStatus.steps)
            .where(x => x.type == type)
            .firstOrDefault();

        return st;
    };

    public resetSteps(type: ERequestType) {
        let step = this.getStep(type);

        if (!step) { return; }

        step.status = ERequestStatus.undefined;
        step.message = '';

        this.updateStep(step);
    };

    public updateStep(step: i.IApiStep) {
        var st = linq.from<i.IApiStep | any>(this.processStatus.steps)
            .where(x => x.type == step.type)
            .firstOrDefault();

        this.processStatus.steps[this.processStatus.steps.indexOf(st)] = step;

        StorageHelper.updateActivation(this);
    };

    public updateSteps(steps: i.IApiStep[]) {
        this.processStatus.steps = steps;

        StorageHelper.updateActivation(this);
    };

    public isDone(): boolean {
        if (this.processStatus && this.processStatus.steps && this.processStatus.steps.length > 0) {
            var done = linq.from<i.IApiStep>(this.processStatus.steps)
                .where(x => x.status === ERequestStatus.done)
                .count();

            return done == this.processStatus.steps.length;
        };

        return false;
    };

    public resetSeetedsubsAndServices() {
        this.sub = '';
        this.services = [];
        this.servicesRemove = [];
    };

    public isWithHardware(): boolean {
        // some kabel products can be booked without hardware e.g. Apps, so this
        // rule applies only for kabel 
        if (this.brandKey != BrandKeys.kabel) {
            return true;
        };

        let br = linq.from<IBrand>(Global.categoryWrapper.bra).where(x => x.id == this.brandKey).first();

        let p = linq.from<IProduct>(br.products).where(x => x.id == this.productId).first();

        if (!p.subs || p.subs.length == 0) {
            return false;
        } else {
            return true;
        };
    };

    //** will compare the selection children with the brands and return TRUE if all of them have HW selected */
    public hardwareNecesseryAndSelected(productId: string | undefined) {
        if (this.brandKey != BrandKeys.kabel) {
            return true;
        };

        let br = linq.from<IBrand>(Global.categoryWrapper.bra).where(x => x.id == this.brandKey).first();
        let ok : number = 0;

        //iterate products
        this.selection.children.map((i)=>{
            if(!productId || productId && i.id == productId) { 
                let p = linq.from<IProduct>(br.products).where(x => x.id == this.productId).first();

                //no subs required so this one is fine
                if (!p.subs || p.subs.length == 0) {
                    ok++;
                } else {
                    let avlp = AvailabilityProduct.ensure(Global.categoryWrapper.avl.getProduct(this.brandKey, i.id));
                    if((Global.indexOfAny(i.getChildrenIds(), avlp.getServices()))) {
                        ok++; 
                    }
                };
            };
        });

        return productId ? ok == 1 : ok == this.selection.children.length;
    };

    /** 
     * will check if an activation has service, compatible with SELECTION
    */
    public hasService(service: string, productId: string | undefined): boolean {
        if(this.brandKey == BrandKeys.kabel) {
            var p = linq.from<TreeItem>(this.selection.children).where(x=>x.id == productId).firstOrDefault();

            if(p) {
                return p.getChildrenIds().indexOf(service) >= 0;
            }
        } else {
            return this.services.indexOf(service) >= 0;
        }

        return false;
    };

    /** will check if a service can be added to an existing activation
     * - v1. excludes are beining checked 
    */
    checkServiceCompatibility(service: string, brand: IBrand) : string[] {
        for (let i = 0; i < this.services.length; i++) {
            var s = linq.from<IService>(brand.services).where(x=>x.id == this.services[i]).firstOrDefault();

            if(s && s.notbookableWith && s.notbookableWith.indexOf(service) >= 0) {
                return [s.id];
            };
        };

        return [];
    };

    public hasHardware() : boolean {
        var hw:string[] = [];
        var hwbr:string[] = linq.from<ISub>(StorageHelper.getBrand(this.brandKey).subs).select(x=>x.id).toArray();

        if(this.brandKey == BrandKeys.kabel) {
            //for cabl one of the keys is the hardware 
            this.selection.children.map((i)=>{
                hw = hw.concat(i.getChildrenIds());
            })
        } else {
            hw.push(this.sub);
        };

        return Global.containsAny<string>(hwbr, hw);
    };

    public removeServiceLike(pattern: string) {
        let all = linq.from<string>(this.services)
            .where(x => x.indexOf(pattern) >= 0)
            .toArray();
        all.map((i) => {
            this.removeService(i,undefined);
        });
    };

    public getOptionUniqueId(id: string) {
        if (id.indexOf('/') > 0) {
            let sa = id.split('/');
            return sa[sa.length - 1];
        } else {
            return id;
        };
    };

    public getOptionPriceActivation(brand: IBrand): IFee {
        let res: number = 0;
        for (let i = 0; i < this.services.length; i++) {
            let oid = this.getOptionUniqueId(this.services[i]);
            let opt = linq.from(brand.services)
                .where(x => x.id === oid && x.type === EServiceType.option)
                .firstOrDefault();

            if (opt) {
                if (brand.id === BrandKeys.kabel && opt.id.indexOf('bereitstellungsentgelt') >= 0) {
                    continue;
                };

                if (opt && opt.activationFee) {
                    res += opt.activationFee.price;
                };
            };
        };

        let f: IFee = {
            price: res,
            unit: Defaults.currencyUnit
        };

        return f;
    };

    public getPromoPrice(brand: IBrand) {
        let res: number = 0;
        for (let i = 0; i < this.services.length; i++) {
            let opt = linq.from(brand.services)
                .where(x => x.id === this.services[i] && x.type === EServiceType.promotion)
                .firstOrDefault();
            if (opt && opt.activationFee) {
                res += opt.activationFee.price;
            };
        }

        let f: IFee = {
            price: res,
            unit: Defaults.currencyUnit
        };

        return f;
    };

    public validateService(id: string): boolean {
        let brand = linq.from(StorageHelper.getBrands()).where(x => x.id == this.brandKey).first();

        let svc = linq.from(brand.services)
            .where(x => x.id === id)
            .firstOrDefault();

        // self 
        if (svc) {
            if (svc.notbookableWith) {
                let which = this.services.some(x => svc!.notbookableWith.indexOf(x) >= 0);
                if (which && this.services.indexOf(svc.id)>=0) {
                    return true;
                }
            }
        }

        // other
        for (let i = 0; i < this.services.length; i++) {
            if (this.services[i] === id) { continue; }
            let svc = linq.from(brand.services)
                .where(x => x.id === this.services[i])
                .firstOrDefault();

            if (svc && svc.notbookableWith) {
                if (svc.notbookableWith.indexOf(id) >= 0  && this.services.indexOf(svc.id)>=0) {
                    return true;
                }
            }
        }

        return false;
    };

    public getBarcode(): string | undefined {
        if (this.processStatus && this.processStatus.steps) {
            let step = linq.from<IApiStep>(this.processStatus.steps)
                .where(x => x.type == ERequestType.activation)
                .firstOrDefault();

            if (step && step.message) {
                let info: IAnswerInfo = JSON.parse(step.message);

                if (info.ContractId) { return info.ContractId; }
            }
        }

        return undefined;
    };

    public getContractId(): string | undefined {
        if (this.processStatus && this.processStatus.steps) {
            let step = linq.from<IApiStep>(this.processStatus.steps)
                .where(x => x.type === ERequestType.activation)
                .firstOrDefault();

            if (step && step.message) {
                let info: IAnswerInfo = JSON.parse(step.message);
                if (info.ContractId) { return info.ContractId; }
            }
        }

        return undefined;
    };

    public getActivationDate(): string {
        if (this.activationType === EActivationType.portDefault) {
            return Global.toGermanDate(this.portationDate);
        } else {
            return Global.toGermanDate(this.activationDate);
        }
    };

    public getCustomerId(): string | undefined {
        if (this.processStatus && this.processStatus.steps) {
            let step = linq.from<IApiStep>(this.processStatus.steps)
                .where(x => x.type === ERequestType.activation)
                .firstOrDefault();

            if (step && step.message) {
                let info: IAnswerInfo = JSON.parse(step.message);
                if (info.CustomerId) { return info.CustomerId; }
            }
        }

        return undefined;
    };

    public getSim(): string | undefined {
        // if(this.activationType == EActivationType.actDefault && this.brandKey == BrandKeys.vodafone) {
        //     return this.msisdn;
        // };

        if (this.processStatus && this.processStatus.steps) {
            let step = linq.from<IApiStep>(this.processStatus.steps)
                .where(x => x.type === ERequestType.activation)
                .firstOrDefault();

            if (step && step.message) {
                let info: IAnswerInfo = JSON.parse(step.message);
                if (info.Sim) { return info.Sim; }
            }
        }

        return MainTexts.unkonwnAtThisTime;
    };
};

export class ActivationUI extends IActivation {
    public product?: IProduct;
    public hardware?: ISub;
    public options?: IService[];

    public brand?: IBrand;

    public ttProduct?: string;
    public ttHardware?: string;
    public ttOptions?: string;
    public ttActivation?: string;

    private customer: ICustomer;

    //UI only
    public subUI: string = '';
    public productUI: string = '';
    public optionsUI: string = '';
    public promotionsUI: string = '';
    public activationDateUI: string = '';
    public msisdnUI: string = MainTexts.unkonwnAtThisTime;
    public barcodeUI: string = MainTexts.unkonwnAtThisTime;
    public simUI: string = MainTexts.unkonwnAtThisTime;
    public customerIdUI: string = MainTexts.unkonwnAtThisTime;
    public activationDateTitleUI: string = '';

    public constructor(brand: string, customer: ICustomer) {
        super(brand);

        this.customer = customer;
    };

    public static fromActivation(act: IActivation, brand: IBrand, customer: ICustomer): ActivationUI {
        let a = act as ActivationUI;
        a = ActivationUI.ensureUi(a, customer);
        a.init(brand);
        a.processStatus = act.processStatus;
        a.customer = customer;
        return a;
    };

    static ensureUi(act: ActivationUI, customer: ICustomer) : ActivationUI {
        let a = Object.assign(new ActivationUI(act.brandKey, customer), act);
        a.customer = customer;
        return a;
    };

    init(br: IBrand) {
        this.brand = br;
        if(this.productId) {
            this.product = linq.from<IProduct>(br.products).where(x=>x.id == this.productId).firstOrDefault();

            if(this.product) {
                this.ttProduct = this.product!.tooltipExtra;

                if(this.sub) {
                    this.hardware = linq.from<ISub>(br.subs).where(x=>x.id == this.sub).firstOrDefault();
        
                    if(this.hardware) {
                        this.ttHardware = this.hardware!.tooltipExtra;
        
                        if(this.services && this.services.length) {
                            this.options = linq.from<IService>(br.services).where(x=>this.services.indexOf(x.id)>=0).toArray();
                            this.ttOptions = this.getTooltipOptions();

                            this.ttActivation = this.getTooltipActivation();
                        };
                    }
                };
            } else {
                let t = {
                    id: this.productId, 
                    name: this.customer.contracts![0].bookedProduct!.name
                };

                this.product = t as IProduct;
            }
        };
    };

    getActivationFee(): IFee {
        let br = StorageHelper.getBrand(this.brandKey);
        let avl = StorageHelper.getAvailabilityForBrand(this.brandKey);
        var prBrand = linq.from<IProduct>(br.products).where(x => x.id === this.productId).firstOrDefault();

        var fee: IFee;

        if (!prBrand) {
            return {
                price: 0,
                unit: MainTexts.euro
            };
        };

        if (this.brandKey === BrandKeys.kabel) {
            let prAvl = linq.from<AvailabilityProduct>(avl.products).where(x => x.productId === this.productId).firstOrDefault();

            let bse = linq.from<string>(this.services).where(x => x.indexOf("bereitstellungsentgelt") >= 0).firstOrDefault();
            if (bse) {
                let el = linq.from<IAvailabilityService>(prAvl!.elements).where(x => x.metaname === bse).firstOrDefault();

                if (el) {
                    return {
                        price: el.prices[0].amount * 100,
                        unit: MainTexts.euro
                    };
                };
            };
        };

        fee = {
            price: prBrand.activationFee ? prBrand.activationFee.price : 0,
            unit: MainTexts.euro
        };

        if (this.options) {
            this.options.map((i) => {
                //if (i.id == 'rufnummernmitnahme-x') {
                    //let svc = linq.from<IService>(br.services).where(x => x.id == i.id).firstOrDefault();

                    if (i && i.activationFee) {
                        fee.price += i.activationFee.price;
                    };
                //};
            });
        };

        return fee;
    };    

    getSubMonthlyFee() : IFee {
        if(this.hardware) {
            return this.hardware.monthlyFee;
        };

        return Global.getEmptyFee();
    };

    getProductMonthlyFee() : IFee {
        if(this.product) {
            return this.product.monthlyFee;
        };

        return Global.getEmptyFee();
    };

    getOptionMonthlyFee() : IFee {
        let all: number = 0;
        if(this.options) {
            all = linq.from<IService>(this.options).select(x=>x.monthlyFee.price).sum();
        };

        let fee = Global.getEmptyFee();
        fee.price = all;
        return fee;
    };

    getOptionActivationFee() : IFee {
        let all: number = 0;
        if(this.options) {
            all = linq.from<IService>(this.options).select(x=>x.activationFee.price).sum();
        };

        let fee = Global.getEmptyFee();
        fee.price = all;
        return fee;
    };

    getProductActivationFee() : IFee {
        if(this.product) {
            return this.product.activationFee;
        };

        return Global.getEmptyFee();
    };

    getMonthlyFee() : IFee {
        let f = Global.getEmptyFee();

        f.price += this.getProductMonthlyFee().price;
        f.price += this.getSubMonthlyFee().price;
        f.price += this.getOptionPriceMonthly().price;

        return f;
    };

    public getOptionPriceMonthly(): IFee {
        let res: number = 0;
        for (let i = 0; i < this.services.length; i++) {
            let opt = linq.from(this.brand!.services)
                .where(x => x.id === this.services[i] && x.type === EServiceType.option)
                .firstOrDefault();

            if (opt) {
                if (this.brand!.id === BrandKeys.kabel && opt.id.indexOf('bereitstellungsentgelt') >= 0) {
                    continue;
                };

                if (opt && opt.monthlyFee) {
                    res += opt.monthlyFee.price;
                };
            };

            //2020-06-18
            //special case - otelo, option is delivered as promotions
            //if monthlyfee then add it to the option price
            if (this.brand!.id === BrandKeys.otelo) {
                let prom = linq.from(this.brand!.services)
                    .where(x => x.id === this.services[i] && x.type === EServiceType.promotion)
                    .firstOrDefault();

                if (prom && prom.monthlyFee) {
                    res += prom.monthlyFee.price;
                };
            };
        };

        let f: IFee = {
            price: res,
            unit: Defaults.currencyUnit
        };

        return f;
    };

    private getTooltipOptions(): string {
        if (this.options) {
            let res = linq.from<IService>(this.options)
                //options with activation fee are being rendered in Einmalbetrag
                .where(x=>!x.activationFee || x.activationFee.price == 0)
                .select(x => x.tooltipExtra ? '<li>' + x.tooltipExtra + '</li>' : '')
                .toArray();

            return res.join('');
        };

        return '';
    };

    private getTooltipActivation(): string {
        if (this.options) {
            let res = linq.from<IService>(this.options)
                .where(x=>x.activationFee!=undefined && x.activationFee.price != 0)
                .select(x => x.tooltipExtra ? '<li>' + x.tooltipExtra + '</li>' : '')
                .toArray();

            return res.join('');
        };

        return '';
    };

    getCustomerName() {
        return this.customer.firstname + " " + this.customer.surname;
    };

    getContractType() : string {
        let s: string = MainTexts.contractTypeBnt;    
        if(this.activationType == EActivationType.actDefault) {
            s = MainTexts.contractTypeBnt;
        } else if (this.activationType === EActivationType.portDefault) {
            s = MainTexts.contractTypePort;
        } else if (this.activationType === EActivationType.existingCustomer) {
            s = MainTexts.contractTypeProlong;
        } else if (this.activationType === EActivationType.bookService) {
            this.activationDateTitleUI = OrderingTexts.bookDate;
            this.customerIdUI = this.customer.contracts && this.customer.contracts[0].customerId ? this.customer.contracts[0].customerId : MainTexts.unkonwnAtThisTime;
            document.title = this.customerIdUI + ' ' + Global.toGermanDate(new Date());
        };

        return s;
    };

    getActionName() {
        return MainTexts.thankYouFor + ' ' + (this.customer.action ? this.customer.action.name : MainTexts.na);
    };

    getCustomerId() {
        let s = MainTexts.unkonwnAtThisTime;

        s = this.customer.contracts && this.customer.contracts[0]  ? (this.customer.contracts[0].customerId || MainTexts.na) : MainTexts.na;

        return s;
    };

    getMsisdn(): string | undefined {
        if (this.processStatus && this.processStatus.steps) {
            let step = linq.from<IApiStep>(this.processStatus.steps)
                .where(x => x.type === ERequestType.activation)
                .firstOrDefault();

            if (step && step.message) {
                let info: IAnswerInfo = JSON.parse(step.message);
                if (info.MobileNumber) { return info.MobileNumber; }
            }
        }

        if(this.inCurrent || this.activationType == EActivationType.existingCustomer) {
            return this.customer.contracts && this.customer.contracts[0] ? this.customer.contracts[0].msisdn : MainTexts.na
        };

        if(this.activationType == EActivationType.portDefault) {
            return this.msisdn;
        };

        return MainTexts.unkonwnAtThisTime;
    };

    getProductName() {
        return this.product ? this.product.name : MainTexts.na;
    };

    getContractIdUi() : string {
        let u = StorageHelper.getUser();
        let s = this.contractId ? this.contractId : MainTexts.unkonwnAtThisTime;

        if(this.activationType == EActivationType.portDefault && this.brandKey == BrandKeys.otelo) {
            var step = this.getStep(ERequestType.activation);
            if(step && step.message) {
                var data = JSON.parse(step.message);
                if(data) {
                    s = data.ContractId;
                }
            };
        } else if(this.isDone()) {
            let step = this.getStep(ERequestType.activation);

            let info = JSON.parse(step.message);

            s = info.ContractId;
        }

        if(s && s.endsWith(u!.username.replace('-',''))) {
            s = MainTexts.unkonwnAtThisTime;
        };

        return s;
    };
};

export default IActivation;