import * as React from 'react';
import ReactTooltip from 'react-tooltip';
import { ETooltipDataType, ITooltipModel } from '../../models/global/tooltip';
import { ELButtonType, ELElementStatus } from '../../models/page/diverse';
import { ILElementProps, ILElementState } from '../../models/page/interfaces';

export interface ILButonProps extends ILElementProps {
    type?: ELButtonType;
    icon?: string;
    inline?: boolean;
    css?: string;
    cssText?: string;
    id?: string;
    onClick?: any;
    tooltip?: any;
};

export class LButton extends React.Component<ILButonProps, ILElementState> {
    constructor(props: ILButonProps) {
        super(props);

        this._onClick = this._onClick.bind(this);

        this.state = {
            status: this.props.status ? this.props.status : ELElementStatus.normal
        };
    };

    public render() {
        let type = this.props.type ? this.props.type : ELButtonType.normal;

        if (type === ELButtonType.normal) {
            return this.renderNormal();
        } else if (type === ELButtonType.icon) {
            return this.renderIcon();
        } else if (type === ELButtonType.buttonWithIconRight) {
            return this.renderButtonIcon();
        } else if (type === ELButtonType.iconImage) {
            return this.renderImageIcon();
        } else if (type === ELButtonType.textWithIconLeft) {
            return this.renderTextIcon();
        } else if (type === ELButtonType.linkWithIconLeft || type === ELButtonType.linkWithIconRight) {
            return this.renderLinkIcon();
        } else if (type === ELButtonType.linkSimple) {
            return this.renderLinkIcon();
        } else if (type === ELButtonType.linkPanel) {
            return this.renderLink();
        }


        return <></>
    };

    public renderIcon(): JSX.Element {
        let css: string = 'bicon';

        if (this.props.status === ELElementStatus.selected) {
            css += ' selected';
        } else if (this.props.status === ELElementStatus.disabled) {
            css += ' disabled';
        } else if (this.props.status === ELElementStatus.ready) {
            css += ' ready';
        }

        if (this.props.inline) {
            css += ' inline';
        }

        let cssi = "icon " + this.props.icon;
        let ctrl = (
            <div className={css}>
                <div className={cssi}></div>
                <div className="text">{this.props.text}</div>
            </div>
        );

        return ctrl;
    };

    public renderLink(): JSX.Element {
        let css: string = 'link';

        if (this.props.css) {
            css += ' ' + this.props.css;
        }

        let ctrl = (
            <button className={css} onClick={this._onClick}>
                <div className="text">{this.props.text}</div>
            </button>
        );

        return ctrl;
    };

    public renderLinkIcon(): JSX.Element {
        let css: string = 'licon';

        if (this.props.status === ELElementStatus.selected) {
            css += ' selected';
        } else if (this.props.status === ELElementStatus.disabled) {
            css += ' disabled';
        }

        if (this.props.css) {
            css += ' ' + this.props.css;
        }

        if (this.props.type === ELButtonType.linkSimple) {
            css += ' noicon';
        }

        let cssi = "icon " + this.props.icon;
        let icon = "/assets/icons/" + this.props.icon;
        let ttm: ITooltipModel = { type: ETooltipDataType.html, body: this.props.tooltip, style: '' };
        let ctrl = (
            <div>
             <ReactTooltip place="top" type="light" effect="solid" id={this.props.id}>
                 <div dangerouslySetInnerHTML={{ __html: ttm.body }} />
             </ReactTooltip>
            <button className={css} onClick={this._onClick} data-tip={this.props.tooltip} data-for={this.props.id}>
                {
                    this.props.type === ELButtonType.linkWithIconLeft ?
                        <>

                            <div className="text">{this.props.text}</div>
                            <div className={cssi} />
                        </>
                        : null
                }

                {
                    this.props.type === ELButtonType.linkWithIconRight ?
                        <>

                            <div className={cssi + ''} />
                            <div className="text">{this.props.text}</div>
                        </>
                        : null
                }

                {
                    this.props.type === ELButtonType.linkSimple ?
                        <div className="text">{this.props.text}</div> : null
                }
            </button>
            </div>
        );

        return ctrl;
    };

    public renderTextIcon(): JSX.Element {
        let css: string = 'ticon';

        if (this.props.status === ELElementStatus.selected) {
            css += ' selected';
        } else if (this.props.status === ELElementStatus.disabled) {
            css += ' disabled';
        }

        if (this.props.css) {
            css += ' ' + this.props.css;
        }

        let cssi = "icon " + this.props.icon;
        let icon = "/assets/icons/" + this.props.icon;
        let ctrl = (
            <button className={css} onClick={this._onClick}>
                <div className="text">{this.props.text}</div>
                <div className={cssi} />
            </button>
        );

        return ctrl;
    };

    public renderButtonIcon(): JSX.Element {
        let css: string = 'aicon';

        if (this.props.status === ELElementStatus.selected) {
            css += ' selected';
        } else if (this.props.status === ELElementStatus.disabled) {
            css += ' disabled';
        }

        if (this.props.css) {
            css += ' ' + this.props.css;
        }

        let cssi = "icon " + this.props.icon;
        let ctrl = (
            <button className={css} onClick={this._onClick} id={this.props.id}>
                <div className={'text ' + this.props.cssText} dangerouslySetInnerHTML={{ __html: this.props.text || '' }} />
                <div className={cssi} />
            </button>
        );

        return ctrl;
    };

    public renderNormal(): JSX.Element {
        let css: string = '';

        if (this.props.status === ELElementStatus.selected) {
            css += ' selected';
        } else if (this.props.status === ELElementStatus.disabled) {
            css += ' disabled';
        }

        let ctrl = (
            <button className={css} onClick={this._onClick} id={this.props.id}>
                {this.props.text}
            </button>
        );

        return ctrl;
    };



    public renderImageIcon(): JSX.Element {
        let cssi = "icon iicon red " + this.props.icon;
        if (this.props.css) {
            cssi += ' ' + this.props.css;
        }
        let ctrl = (
            <div className={cssi} onClick={this._onClick} id={this.props.id} />
        );

        return ctrl;
    };

    private _onClick(ev: any) {
        if (this.props.css && this.props.css.indexOf('disabled') >= 0) {
            return;
        };

        if (this.props.onClickEvent) {
            this.props.onClickEvent(ev, this.props.clickModel);
        };

        if (this.props.onClick) {
            this.props.onClick(this.props.clickModel);
        }
    };
};

export default LButton;