import * as React from 'react';
import BaseBlock from '../../page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { ECreditDebitType, EBusinessCase, ECustomerType, EActivationType, EMnpType } from '../../../models/ordering/order';
import IProduct from '../../../models/ordering/product';
import ProcessNewScreen from '../../../screens/ordering/process';
import LButton from '../../page/button';
import LLabel from '../../page/label';
import LClear from '../../page/clear';
import { Row, Col, Container } from 'react-bootstrap';
import { ELElementStatus, ELButtonType } from '../../../models/page/diverse';
import linq from 'linq';
import IBrand from '../../../models/ordering/brand';
import StorageHelper from '../../../utils/storage';
import { IActivationBlockProps, IActivationBlockState, IActivationBlockOptions} from './activationBlock';
import { MainTexts, OrderingTexts } from '../../../utils/messages';
import { BrandKeys, CatKeys } from '../../../utils/Defaults';
import EasyRouter from '../../../utils/router';
import { CustomerScreen } from '../../../screens/insex';
import { threadId } from 'worker_threads';
import Global from '../../../utils/Global';

export class ActivationBlockSimple extends React.Component<IActivationBlockProps, IActivationBlockState> {
    private mounted: boolean = false;
    private config: IActivationBlockOptions;
    public static componentId = 'activationBlock';

    constructor(props: IActivationBlockProps) {
        super(props);
        
        this.handlePayment = this.handlePayment.bind(this);
        this.handleActivationType = this.handleActivationType.bind(this);
        this.handleCusomerType = this.handleCusomerType.bind(this);

        this.config = { canMnpPredate: false, canMnpStandard: false, canSoho: false, canPrepaid: false };

        this.state = {
            businessType: ECreditDebitType.postpaid,
            businessCase: EBusinessCase.bnt,
            customerType: ECustomerType.personal,
            activationType: this.props.parent.current ? this.props.parent.current.activationType : EActivationType.actDefault
        };
    }

    public componentDidMount() {
        this.mounted = true;
    };

    public componentWillReceiveProps(next: any){
        this.setState(next);
    };

    public render() {
        let act:JSX.Element = this.props.parent.dataWrapper.hasNextContract() ?
            <LButton text={MainTexts.backToCustomer} css="fr" icon="ok"
                type={ELButtonType.linkSimple}
                onClick={() => {
                    EasyRouter.route({ url: CustomerScreen.uri + this.props.parent.data.id, title: MainTexts.existingCustomerActivation });
                }} />
        : <></>;

        let cfg: IBlockConfiguration = {
            title: MainTexts.productSelection,
            transparent: false,
            id: ActivationBlockSimple.componentId,
            action: act
        };

        this.prepare();

        let cfgCategory: IBlockConfiguration = {
            transparent: true,
            action: act
        };        

        let brand = Global.categoryWrapper.getCategoryBrandKey(this.props.parent.data);
        let nextBrands = Global.categoryWrapper.getOtherBrandsAvailable(brand);

        let ctrl = <>
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={12} md={12}>
                            <LLabel text={MainTexts.businessCase}/>
                        </Col>
                    </Row>               

                    {
                        this.props.parent.current && (this.props.parent.current.activationType == EActivationType.partnerCard || this.props.parent.current.activationType == EActivationType.partnerCardPortation) ? 
                        <Row>
                            <Col xs={6} md={6}>                         
                                <LButton 
                                        text={OrderingTexts.pcBooking}
                                        onClick={this.handleActivationType}
                                        clickModel={EActivationType.partnerCard}
                                        status={this.state.activationType === EActivationType.partnerCard ? ELElementStatus.selected : ELElementStatus.normal}/>
                            </Col>      
                            <Col xs={6} md={6}>                         
                                <LButton 
                                        text={OrderingTexts.pcPortation}
                                        onClick={this.handleActivationType}
                                        clickModel={EActivationType.partnerCardPortation}
                                        status={this.state.activationType === EActivationType.partnerCardPortation ? ELElementStatus.selected : ELElementStatus.normal}/>
                            </Col>                           
                        </Row>
                        : null
                    }

                    {
                         this.props.parent.current && (this.props.parent.current.activationType != EActivationType.partnerCard && this.props.parent.current.activationType != EActivationType.partnerCardPortation) ?     
                        <Row>
                            <Col xs={6} md={6}>
                                <LButton text={OrderingTexts.activation}
                                        onClick={this.handleActivationType}
                                        clickModel={EActivationType.actDefault}
                                        status={this.state.activationType === EActivationType.actDefault ? ELElementStatus.selected : ELElementStatus.normal}/> 
                            </Col>      
                            
                            {
                                    this.config.canMnpStandard  ?       
                                    <Col xs={6} md={6}>                         
                                        <LButton text={OrderingTexts.portation}
                                                onClick={this.handleActivationType}
                                                clickModel={EActivationType.portDefault}
                                                status={this.state.activationType === EActivationType.portDefault ? ELElementStatus.selected : ELElementStatus.normal}/>
                                    </Col>     
                                    : null
                            }
                        </Row> 
                        : null
                    }

                    <Row>
                        {
                            this.props.parent.current 
                            && this.props.parent.current.brandKey == BrandKeys.vodafone 
                            && (this.props.parent.current.activationType != EActivationType.partnerCard && this.props.parent.current.activationType != EActivationType.partnerCardPortation) ?
                            <Col xs={6} md={6}>                         
                                <LButton text={OrderingTexts.cdChange}
                                        onClick={this.handleActivationType}
                                        clickModel={EActivationType.cdChange}
                                        status={this.state.activationType === EActivationType.cdChange ? ELElementStatus.selected : ELElementStatus.normal}/>
                            </Col>
                            : null
                        }
                    </Row>
                    
                    {
                        this.props.parent.current && (this.props.parent.current.brandKey !== BrandKeys.unity && this.props.parent.current.brandKey !== BrandKeys.dsl && this.props.parent.current.brandKey !== BrandKeys.kabel) ? 
                        <>
                        <Row>
                            <Col xs={12} md={12}>
                                <LLabel text={MainTexts.productSelection}/>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={6} md={6}>
                                <LButton 
                                    text="Postpaid" 
                                    onClick={this.handlePayment}
                                    clickModel={ECreditDebitType.postpaid}
                                    status={this.props.parent.selection.creditDebitType === ECreditDebitType.postpaid ? ELElementStatus.selected : ELElementStatus.normal}/>
                            </Col>
                            <Col xs={6} md={6}> 
                                {
                                    this.config.canPrepaid ?
                                    <LButton text="Prepaid"
                                        onClick={this.handlePayment}
                                        clickModel={ECreditDebitType.prepaid}
                                        status={this.props.parent.selection.creditDebitType === ECreditDebitType.prepaid ? ELElementStatus.selected : ELElementStatus.normal}/>
                                    : null
                                }
                            </Col>                   
                        </Row>       
                        </>                    
                        : null
                    }                                   
                </Container>
                <LClear />
            </BaseBlock>
            
            {
                Global.categoryWrapper.canSwitchCat(this.props.parent.data) ?
                <BaseBlock config={cfgCategory}>
                    <Container>
                        <Row>
                            <Col xs={12} md={12}>
                                <LButton
                                    text={ Global.categoryWrapper.all  ? "- Alle" : "+ Alle"} 
                                    onClick={()=>{
                                        Global.categoryWrapper.all = !Global.categoryWrapper.all;
                                        this.props.parent.refresh();
                                    }} 
                                    css="fl uc text-h2"
                                    type={ELButtonType.linkSimple} 
                                    icon="arrow-right"/>    

                                {
                                    nextBrands.length == 0 ? null :
                                    nextBrands.map((i, idx)=>{
                                        return <LButton
                                            text={"-> " + i.key + " Tarife"} 
                                            onClick={()=>{
                                                this.handleCategoryBrand( i.key );
                                            }} 
                                            css="fr uc text-h2"
                                            type={ELButtonType.linkSimple} 
                                            icon="arrow-right"/>   
                                    })                             
                                }
                            </Col>
                        </Row>                                      
                    </Container>
                    <LClear />
                </BaseBlock>  
                : null
            }      
        </>

        return ctrl;
    };
 
    private handleCategoryBrand = (id: string) =>{
        this.props.parent.changeCatBrand(id);
    };

    private prepare() {
        var brand = linq.from<IBrand>(StorageHelper.getBrands())
            .where(x=>x.id == this.props.parent.selectedBrand)
            .first();

        this.config.canPrepaid = linq.from<IProduct>(brand.products)
            .where(x=>x.creditDebitType === ECreditDebitType.prepaid)
            .count() > 0;

        this.config.canSoho = linq.from<IProduct>(brand.products)
            .where(x=> (x.customerType & ECustomerType.soho) === ECustomerType.soho) 
            .count() > 0;   

        this.config.canMnpStandard = linq.from<IProduct>(brand.products)
            .where(x=> (x.activationType & EActivationType.portDefault) == EActivationType.portDefault) 
            .count() > 0;       
        
        if(brand.id === BrandKeys.kabel) {
             this.config.canMnpStandard=true;
        }
    };

    private handleCusomerType(value: ECustomerType) {
        if(this.mounted) {
            this.setState({customerType: value});
            this.props.parent.applyCustomerType(value);
        }
    };

    private handleActivationType(value: EActivationType) {
        if(this.mounted) {
            if(!this.props.parent.canEditActivation()) {return;};

            this.setState({activationType: value});
            
            this.props.parent.applyActivationType(value);
        }
    };

    private handlePayment(value: ECreditDebitType) {
        if(this.mounted) {
            if(!this.props.parent.canEditActivation()) {return;};
            this.setState({businessType: value});
            this.props.parent.applyPayment(value);
        }
    };
};

export default ActivationBlockSimple;