import linq from 'linq';
import { IApiResponse, IApiStep, ISocketPushModel } from "../interfaces";
import { EAppTheme, IAppSettings } from '../interfaces/app/IAppSettings';
import { Address, ERequestStatus, ERequestType, IActivation, IDefCon, OrderDataWrapper } from "../models";
import { IUser } from '../models/environment/user';
import Availability, { AvailabilityProduct, EElementType, IAvailabilityBrand, PriceWrapper } from "../models/ordering/availability";
import { IBrand } from '../models/ordering/brand';
import { ICustomer } from "../models/ordering/customer";
import { IOrderData, IOrderProductViewModel } from "../models/ordering/data";
import IProduct from '../models/ordering/product';
import IService from '../models/ordering/service';
import ISub from '../models/ordering/sub';
import { CombiWrapper, ISupportWrapper } from '../models/support/ISupportWrapper';
import { TreeItem } from "../models/support/tree";
import { BrandKeys, CatKeys, StorageKeys } from "./Defaults";
import Global from "./Global";

export class StorageHelper {
    public static user?: IUser;

    public static getOrders(): IOrderData[] {
        let data = sessionStorage.getItem(StorageKeys.keyOrders);
        let items: IOrderData[] = [];
        if (data != null) {
            items = JSON.parse(data);
        } else {
            items = [];
            StorageHelper.set(StorageKeys.keyOrders, items);
        }

        return items;
    };

    public static updateActivation(act: IActivation) {
        let order = StorageHelper.getOrder(act.processId);

        var st = linq.from<IActivation>(order.activations)
            .where(x => x.id == act.id)
            .firstOrDefault();

        if (st) {
            order.activations[order.activations.indexOf(st)] = act;

        } else {
            order.activations.push(act);
        };

        StorageHelper.addOrder(order);
    };

    public static updateCustomer(processId: string, cust: ICustomer) {
        console.log('update c', cust);
        let order = StorageHelper.getOrder(processId);

        order.customer = cust;


        StorageHelper.addOrder(order);

    };

    public static getCustomer(processId: string): ICustomer {
        let order = StorageHelper.getOrder(processId);

        return order.customer;
    };

    public static resetStep(act: IActivation, type: ERequestType, status: ERequestStatus) {
        let step = linq.from<IApiStep>(act.processStatus.steps)
            .where(x => x.type == type)
            .firstOrDefault();
        if (step) {
            step.status = status;
            step.message = '';
            StorageHelper.updateActivation(act);
        }
    };

    public static getOrder(id: string): IOrderData {
        let orders: IOrderData[] = StorageHelper.getOrders();
        let order = linq.from<IOrderData>(orders)
            .where(x => x.id.toLowerCase() == id.toLowerCase())
            .firstOrDefault();

        //todo remove in prod
        //   if(!order) {
        //       order = OrderDataWrapper.createOrder("undefined Process", BrandKeys.vodafone, 'SM2S');
        // };

        if(!order) {
            //window.location.href = '/error/404';
            return OrderDataWrapper.createOrder("undefined Process", CatKeys.vodafone, BrandKeys.vodafone, 'SM2S');;
        };

        OrderDataWrapper.ensure(order);

        return order;
    };

    public static removeOther(id: string) {
        //console.log('removeOther' + id);

        let o: IOrderData[] = [];

        o.push(StorageHelper.getOrder(id));

        StorageHelper.set(StorageKeys.keyOrders, o);
    };

    public static getParentOfProcess(id: string): IOrderData {
        let orders: IOrderData[] = StorageHelper.getOrders();
        let order = linq.from<IOrderData>(orders)
            .where(x => x.id == id)
            .first();

        return StorageHelper.getOrder(order.parentId ? order.parentId : '');
    };

    public static toActivation(a: IActivation): IActivation {
        let newOne: IActivation = Object.assign(new IActivation(a.brandKey), a);
        return newOne;
    };

    public static addOrder(order: IOrderData, callback?: any) {
        if (!order['Id'] && !order.id) { return; }

        if (order['Id']) { order.id = order['Id']; }

        let ord = StorageHelper.getOrders();
        let found = linq.from<IOrderData>(ord)
            .where(x => x.id == order.id)
            .firstOrDefault();

        //services should be an array
        order.activations.map((i, idx) => {
            if (!i.selection) {
                i.selection = new TreeItem('root');
                if (order.customer.contracts && !order.isCrossSelling) {
                    i.catKey = i.brandKey == BrandKeys.vodafone ? CatKeys.vodafone :
                        i.brandKey == BrandKeys.otelo ? CatKeys.otelo : CatKeys.fn;
                    i.selection.add(order.customer.contracts![0].bookedProduct!.id);
                } else {
                    let bpm = Global.categoryWrapper.getCategoryBrandMapByProduct(i.productId);
                    i.catKey = bpm.catKey;
                    i.brandKey = i.brandKey;
                    i.selection.add(i.productId);
                }
            };

            if (!i.catKey) {
                i.catKey = Global.categoryWrapper.getCategoryKeyByProduct(i.selection.children[0].id)
            };

            if (!i.services) {
                i.services = [];
            };

            if (!i.servicesRemove) {
                i.servicesRemove = [];
            };
        });

        //catkey is not stored in BE so get it
        if (!order.currentCat && order.activations.length > 0) {
            order.currentCat = order.activations[0].catKey;
        };
        if (!order.currentBrand && order.activations.length > 0) {
            order.currentBrand = order.activations[0].brandKey;
        };

        if (found) {
            let idx = ord.indexOf(found);
            
            ord[idx] = order;
        } else {
            ord.push(order);
        }

        StorageHelper.set(StorageKeys.keyOrders, ord);

        if (callback) {
            callback();
        };
    };

    public static addOrderProducts(order: IOrderProductViewModel) {

        StorageHelper.set(StorageKeys.keyOrdersProducts, order);
    };

    public static resetAvailability(adr: Address | undefined,
        targetStatus: ERequestStatus) {
        let avl = StorageHelper.getAvailability();
        avl.brands = [
            { id: BrandKeys.kabel, key: BrandKeys.kabel, products: [], data: '' },
            { id: BrandKeys.dsl, key: BrandKeys.dsl, products: [], data: '' },
        ];

        avl.id = Global.getUniqueId();
        avl.executed = targetStatus;
        if (adr) {
            avl.address = adr;
        }

        StorageHelper.set(StorageKeys.keyAvailability, avl);

        return avl;
    };

    public static parseAvailability(sock: ISocketPushModel) {
        let api: IApiResponse = JSON.parse(sock.data);

        let inner = api.data;

        if (api.error != null) {
            return;
        };

        if (inner == null) {
            return;
        };

        try {
            let step: IApiStep = inner.steps[0];// ActivationApi.processStep(api, state);

            if (step.status == ERequestStatus.done || step.status == ERequestStatus.tempResult) {
                let avl: Availability = JSON.parse(step.message);
                let avlStore = StorageHelper.getAvailability();

                avl.brands.map((i) => {
                    //todo: in separated availability the request is delivered as allbrands id
                    avl.id = i.id || '';
                    let data = JSON.parse(i.data);
                    if (data && data.products) {
                        i.products = JSON.parse(i.data).products
                    }
                });

                avl.executed = step.status; // == ERequestStatus.done;
                avl.address = avlStore.address;

                StorageHelper.set(StorageKeys.keyAvailability, avl);
            } else if (step.status === ERequestStatus.corrected) {

            } else if (step.status === ERequestStatus.failed || step.status === ERequestStatus.invalid) {

            };
        } catch (e) {
            console.error(e);
        }
    };

    public static addOrderAndReset(order: IOrderData) {
        StorageHelper.resetOrders();
        StorageHelper.addOrder(order);
    };

    public static removeOrder(id: string, callback: any) {
        let ord = StorageHelper.getOrders();
        let other = linq.from<IOrderData>(ord)
            .where(x => x.id != id)
            .toArray();

        StorageHelper.set(StorageKeys.keyOrders, other);

        if (callback) {
            callback();
        };
    };

    public static cleanAndPreserve(id: string) {
        let ord = StorageHelper.getOrders();
        let cur = linq.from<IOrderData>(ord)
            .where(x => x.id === id)
            .toArray();

        StorageHelper.set(StorageKeys.keyOrders, cur);
    };

    public static resetOrders() {
        let ord: IOrderData[] = [];
        StorageHelper.set(StorageKeys.keyOrders, ord);
    };

    public static clear() {
        sessionStorage.clear();
        if (StorageHelper.user) {
            StorageHelper.user = undefined;
        };
    };

    public static set(key: string, model: any) {
        if (key === StorageKeys.keyUser) {
            let cfg = Global.getConfiguration();
            if (cfg.debug) {
                sessionStorage.setItem(key, JSON.stringify(model));
            } else {
                StorageHelper.user = model;
            }
        } else {
            sessionStorage.setItem(key, JSON.stringify(model));

            if(key == StorageKeys.keyBrands) {
                let avl = StorageHelper.getAvailability();
                StorageHelper.syncAvailabilityPrices(avl);
            };
        }
    };

    //** some brands have dynamic prices so we need to sync them with the local brands array */
    public static syncAvailabilityPrices(model: any) {
        var data = model as Availability;
        let brands = StorageHelper.getBrands();
        
        if(data && data.brands) {
            data.brands.map((i)=>{
                if(i.products && (i.key == BrandKeys.unity || i.key == BrandKeys.kabel)) {
                    let b = linq.from<IBrand>(brands).where(x=>x.id == i.key).first();
 
                    i.products.map((j)=>{ 
                        let p = linq.from<IProduct>(b.products).where(x=>x.id == j.productId).firstOrDefault();
                        if(p) {
                            var pw = PriceWrapper.createInstance(p.id, i.key, j, EElementType.product);
                            //console.log('found p: ' + p.id + ' (' + p.name + ') (' + i.key + ')' + pw.getMonthlyFee().price + ' === ' + j.price);
                            p.monthlyFee = pw.getMonthlyFee(); //.price == 0 ? {price: (j.price || 0), unit: Defaults.currencyUnit} : pw.getMonthlyFee();
                        };

                        /*deactivated for unity*/
                        if(i.key != BrandKeys.unity && j.subs) {
                            j.subs.map((k)=>{
                                let s = linq.from<ISub>(b.subs).where(x=>x.id == k.id).firstOrDefault();
                                if(s) {
                                    //console.log('found s: ' + s.id + ' ' + s.activationFee.price + ' === ' + k.price);
                                    //bundles does not have prices
                                    //s.monthlyFee = {price: (k.price || 0), unit: Defaults.currencyUnit};
                                };

                                if(k.options) {
                                    k.options.map((l)=>{
                                        let o = linq.from<IService>(b.services).where(x=>x.id == l.id).firstOrDefault();
                                        if(o) {
                                            var pw = PriceWrapper.createInstance(o.id, i.key, l, EElementType.service);
                                            //console.log('found o: ' + o.id + ' ' + o.activationFee.price + ' === ' + l.price);
                                            o.monthlyFee = pw.getMonthlyFee(); //{price: (l.price || 0), unit: Defaults.currencyUnit}; 
                                            o.activationFee = pw.getActivationFee();
                                        };
                                    });
                                };
                            });
                        };
                    });
                };
            });

            //store direct to preven cycle
            sessionStorage.setItem(StorageKeys.keyBrands, JSON.stringify(brands));
        };
    };

    public static remove(key: string, model: any) {
        if (key === StorageKeys.keyUser) {
            let cfg = Global.getConfiguration();
            if (cfg.debug) {
                sessionStorage.removeItem(key);
            } else {
                StorageHelper.user = undefined;
            }
        } else {
            sessionStorage.removeItem(key);
        }
    };

    public static removeOrderAndGotoParent() {

    };

    public static getAppSettings(): IAppSettings {
        let tmp = sessionStorage.getItem(StorageKeys.keyApp);
        let set: IAppSettings | undefined = undefined;
        if (!tmp) {
            set = { expanded: false, theme: EAppTheme.light };
            StorageHelper.set(StorageKeys.keyApp, set)
        } else {
            set = JSON.parse(tmp) as IAppSettings;
        };

        return set;
    };

    public static getBrands(): IBrand[] {
        let data = sessionStorage.getItem(StorageKeys.keyBrands);
        let items: IBrand[] = [];
        if (data != null) {
            items = JSON.parse(data);
        }

        return items;
    };

    public static getCombis(): CombiWrapper {
        let data = sessionStorage.getItem(StorageKeys.keyCombis);
        let cw: CombiWrapper = new CombiWrapper();
        if (data != null) {
            cw.items = JSON.parse(data);
        }

        return cw;
    };

    public static getBrand(id: string): IBrand {
        let data = StorageHelper.getBrands();

        return linq.from<IBrand>(data).where(x => x.id === id).first();
    };

    public static getDefCon(): IDefCon {
        let sup: IDefCon = new IDefCon();

        try {
            let data = sessionStorage.getItem(StorageKeys.keyDefCon);

            if (data != null) {
                sup = JSON.parse(data);
            }
        }
        catch (e) { }

        sup = Object.assign(new IDefCon(), sup);

        // sup.brands.map((i)=>{
        //     i.available = true;
        // });

        // StorageHelper.set(StorageKeys.keyDefCon, sup);

        return sup;
    };

    public static oteloOnlyBookable(): boolean {
        let dc = StorageHelper.getDefCon();

        if (!dc || !dc.brands) {
            return false;
        };

        let ot = linq.from<IBrand>(dc.brands).where(x => x.id == BrandKeys.otelo && x.available).firstOrDefault();

        if (!ot) {
            return false;
        } else {
            let cnt = linq.from<IBrand>(dc.brands).where(x => x.available).count();

            return cnt == 1;
        };
    };

    public static needAvailability(dc: IDefCon) {
        let avl = linq.from<IBrand>(dc.brands).where(x => x.available).toArray();

        let dsl = linq.from<IBrand>(avl).where(x => x.id == BrandKeys.dsl).firstOrDefault();
        let cab = linq.from<IBrand>(avl).where(x => x.id == BrandKeys.kabel).firstOrDefault();

        if (dsl || cab) {
            return true;
        }

        return false;
    };

    public static getSupport(): ISupportWrapper {
        let data = sessionStorage.getItem(StorageKeys.keySupport);
        let sup: ISupportWrapper = {
            countries: [],
            networks: [],
            fields: [],
            boxes: []
        };

        if (data != null) {
            sup = JSON.parse(data);
        }

        return sup;
    };

    public static getAvailability(): Availability {
        let data = sessionStorage.getItem(StorageKeys.keyAvailability);
        let items: Availability = new Availability();
        if (data != null) {
            items = JSON.parse(data);

            items = Object.assign(new Availability(), items);

            items.brands.map((i) => {
                if (i.data) {
                    i.products = JSON.parse(i.data).products;
                };
            });
        }

        return items;
    };

    public static updateAvailability(brand: string, avl: IAvailabilityBrand) {
        let data = this.getAvailability();

        for (let i: number = 0; i < data.brands.length; i++) {
            if (data.brands[i].key == brand) {
                data.brands[i] = avl;
                data.brands[i].key = brand;
                break;
            };
        };

        StorageHelper.syncAvailabilityPrices(data);

        this.set(StorageKeys.keyAvailability, data);
    };

    public static getAvailabilityForBrand(brand: string): IAvailabilityBrand {
        let data = StorageHelper.getAvailability();

        if (data != null) {
            return linq.from<IAvailabilityBrand | any>(data.brands).where(x => x.key === brand).firstOrDefault();
        }

        return { key: brand, products: [], data: '' };
    };

    public static getAvailabilityProduct(brand: string, productId: string): AvailabilityProduct | undefined {
        let data = StorageHelper.getAvailabilityForBrand(brand);

        if (data != null && data.products != null) {
            var p = linq.from<AvailabilityProduct>(data.products).where(x => x.productId === productId).firstOrDefault();

            return p;
        }

        return undefined;
    };

    public static getUser(): IUser | undefined {
        let cfg = Global.getConfiguration();
        if (cfg.debug) {
            let data = sessionStorage.getItem(StorageKeys.keyUser);
            let items: IUser | undefined;
            if (data != null) {
                items = JSON.parse(data);
            }

            return items;
        }

        return StorageHelper.user;
    };
};

export default StorageHelper;