import { OrderingBase } from "..";
import ModelState from "./modelState";
import * as m from '../../models/index';
import Global from "../Global";
import { ValidationMessages } from "../messages";
import { EActivationType, ENumberPortingType } from "../../models/ordering/order";

export class OrderingDsl extends OrderingBase {
    public validateOrder(act: m.IActivation, modelState: ModelState, data: m.IOrderData | undefined) {
        super.validate(act, modelState, data);
        
        if(act.activationType === EActivationType.portDefault) {
            if(data && data.customer.mnp && data.customer.mnp.numberPortingType === ENumberPortingType.multiple && act.services.indexOf('svc_dsl_comfort') < 0) {
                modelState.errorMessage = ValidationMessages.multipleDslComfortError;
                modelState.specialCase = true;
            }
        };
    };
}; 

export default OrderingDsl;