import * as React from 'react';
import { IBaseComponentState } from '../../interfaces/index';

export interface IInputWrapperProps {
    //children: JSX.Element[];
};

export class InputWrapper extends React.Component<IInputWrapperProps, IBaseComponentState> {
    constructor(props:IInputWrapperProps) {
        super(props);

    };

    public render() {
        let ctrl= <div className="iwrap">
            {this.props.children}
        </div>;

        return ctrl;
    };
};

export default InputWrapper;