import { AppState, OPEN_POPUP, OPEN_SCREEN } from "./types";

const appState: AppState = {
    popup: null,
    screen: null
};

export function appReducer(state = appState, { type, payload }: any): AppState {
    switch (type) {
      case OPEN_POPUP: {
        return {
          ...state,
          popup: payload
        };
      }
      case OPEN_SCREEN: {
        return {
          ...state,
          screen: payload
        };
      }      
      default: {
        return state;
      }
    }
  }