import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { EPopupType } from '../../components/dialogs/popup';
import * as cmp from '../../components/index';
import { Inner1C } from '../../components/index';
import BaseBlock from '../../components/page/block';
import * as page from '../../components/page/index';
import { IApiResponse, IApiSummary } from '../../interfaces';
import { EControlType, ELButtonType, ERequestType } from '../../models';
import { IBlockConfiguration } from '../../models/block/configuration';
import { IControlValue } from '../../models/page/interfaces';
import Api from '../../utils/api';
import Global from '../../utils/Global';
import { MainTexts, ValidationMessages } from '../../utils/messages';
import ValidationOption, { EValidationOption } from '../../utils/validation/validationOption';
import CustomerScreen from '../existing/customer';

export enum ImeiUploadType {
    Manual,
    List
}

export interface ImeiScreenProps {
    msisdn?: string;
    onCancel?: any;
    parent?: CustomerScreen;
    uploadType: ImeiUploadType;
}

export interface IImeiScreenState {
    msisdn: string;
    imei: string;
    file?: any;

    popupTitle: string;
    processing: boolean;
    closblePopup: boolean;
    apiMessage: string;
}

export class ImeiScreen extends React.Component<ImeiScreenProps, IImeiScreenState> {
    private processPopupState: EPopupType = EPopupType.pleaseWait;
    public static containerId: string = 'imeiScreen';

    constructor(props) {
        super(props);

        this.state = {
            msisdn: this.props.msisdn || '', imei: '',
            closblePopup: false, apiMessage: '',
            processing: false, popupTitle: ''
        };

        if(this.props.uploadType === ImeiUploadType.List) {
            Global.easyApp.setTitle(MainTexts.imeiPageTitle);
        }

        this._onChange = this._onChange.bind(this);
        this._onFileChange = this._onFileChange.bind(this);
        this._onSave = this._onSave.bind(this);
        this._showPopup = this._showPopup.bind(this);
        this._onDismissProcessPopup = this._onDismissProcessPopup.bind(this);
    }

    render() {
        let a: JSX.Element = <></>;

        if (this.props.parent) {
            a = <page.LButton type={ELButtonType.linkSimple}
                text={MainTexts.backToCustomerDataSheet}
                css="action3 fr"
                onClick={() => {
                    if (this.props.parent) {
                        this.props.parent.onBackToCustomer(false);
                    }
                }} />;
        }

        let cfg: IBlockConfiguration = {
            title: this.props.uploadType === ImeiUploadType.List ? MainTexts.imeiListUpload : MainTexts.imeiPageTitle,
            transparent: false,
            styleContainer: '',
            action: a
        };

        let ctrl = <></>;

        if (this.props.uploadType === ImeiUploadType.List) {
            ctrl = this._renderList(cfg);
        } else {
            ctrl = this._renderManual(cfg);
        }

        return ctrl;
    }

    socketCallback(res: IApiResponse) {
        this._onDismissProcessPopup();

        if (res.error) {
            let sum: IApiSummary = Api.parseSummary(res, undefined);
            this.processPopupState = EPopupType.custom;

            this._showPopup(EPopupType.custom, '', sum.message, false, true);
        } else {
            this._showPopup(EPopupType.info, MainTexts.saveSuccess, '', true, true);
        }
    }

    private _renderManual = (cfg: IBlockConfiguration) => {
        return <div id={ImeiScreen.containerId}>
            <BaseBlock config={cfg}>
                <cmp.Popup
                    type={this.processPopupState}
                    visible={this.state.processing || this.state.closblePopup}
                    closable={this.state.closblePopup}
                    id="wait"
                    title={this.state.popupTitle}
                    height={200}
                    onDismiss={this._onDismissProcessPopup}>
                    <cmp.PopupBody>
                        <div>{this.state.apiMessage}</div>
                        <></>
                    </cmp.PopupBody>
                    <></>
                </cmp.Popup>
                <Container className="container-action">
                    <Row>
                        <Col xs={12} md={12}>
                            <page.LControl type={EControlType.hint} label={MainTexts.imeiManualDesc} />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={6} md={6}>
                            <page.LControl type={EControlType.text}
                                value={this.state.msisdn}
                                validationOptions={
                                    [
                                        new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError })
                                    ]
                                }
                                onChange={this._onChange}
                                name="msisdn" id="msisdn"
                                label={MainTexts.msisdn} />
                        </Col>
                        <Col xs={6} md={6}>
                            <page.LControl type={EControlType.text}
                                value={this.state.imei}
                                validationOptions={
                                    [
                                        new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError })
                                    ]
                                }
                                onChange={this._onChange}
                                name="imei" id="imei"
                                label={MainTexts.imei} />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={6} md={6} >

                        </Col>
                        <Col xs={6} md={6} >
                            <page.LButton type={ELButtonType.buttonWithIconRight}
                                css="ai fr"
                                id="buttonSave"
                                text={MainTexts.save}
                                onClick={this._onSave} />
                        </Col>
                    </Row>
                </Container>
                <page.LClear />
            </BaseBlock>
        </div>;
    }

    private _renderList = (cfg: IBlockConfiguration) => {
        return <div id={ImeiScreen.containerId}>
            <Inner1C>
                <BaseBlock config={cfg}>
                    <cmp.Popup
                        type={this.processPopupState}
                        visible={this.state.processing || this.state.closblePopup}
                        closable={this.state.closblePopup}
                        id="wait"
                        title={this.state.popupTitle}
                        height={200}
                        onDismiss={this._onDismissProcessPopup}>
                        <cmp.PopupBody>
                            <div>{this.state.apiMessage}</div>
                            <></>
                        </cmp.PopupBody>
                        <></>
                    </cmp.Popup>
                    <Container className="container-action">
                        <Row>
                            <Col xs={12} md={12}>
                                <page.LControl type={EControlType.hint} label={MainTexts.imeiListUploadDesc} />
                            </Col>
                        </Row>

                        <Row>
                            <Col xs={12} md={12}>
                                <input type="file" name="file" id="file" accept=".csv" onChange={this._onFileChange} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={6} md={6} >

                            </Col>
                            <Col xs={6} md={6} >
                                <page.LButton type={ELButtonType.buttonWithIconRight}
                                    css="ai fr"
                                    id="buttonSave"
                                    text={MainTexts.save}
                                    onClick={this._onSave} />
                            </Col>
                        </Row>
                    </Container>
                    <page.LClear />
                </BaseBlock>
            </Inner1C>
        </div>;
    }

    private _onChange(e: IControlValue) {
        let st = this.state;
        Global.onInput(st, e, () => {
            this.setState(st);
        });
    }

    private getBase64(file) {
        const promise = new Promise<string | ArrayBuffer | null>((resolve, reject) => {
            let reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = function () {
                resolve(reader.result)
            };
            reader.onerror = function (error) {
                reject(error);
            };
        });

        return promise;
    }

    private _onSave() {
        this._showPopup(EPopupType.pleaseWait, '', '', true, false);
        if (this.props.uploadType === ImeiUploadType.Manual) {
            const model = { msisdn: this.state.msisdn, imei: this.state.imei };
            Global.hub.send(JSON.stringify(model),
                ERequestType.saveImei,
                undefined);
        } else { // upload imei file
            this.getBase64(this.state.file).then(result => {
                const model = { file: result, fileName: this.state.file.name };
                Global.hub.send(JSON.stringify(model),
                    ERequestType.saveImeiList,
                    undefined);
            });
        }
    }

    private _showPopup(type: EPopupType, title: string, message: string, inprocess: boolean, closblePopup: boolean) {
        this.processPopupState = type;
        this.setState({
            apiMessage: message,
            closblePopup: closblePopup,
            processing: inprocess,
            popupTitle: title
        });
    }

    private _onDismissProcessPopup() {
        this.setState({ processing: false, closblePopup: false });
    }

    _onFileChange = event => {
        this.setState({ file: event.target.files[0] });
    }
}

export default ImeiScreen;