import * as linq from 'linq';
import { BrandKeys, ConstantKeys, Defaults } from '../../utils/Defaults';
import Global from '../../utils/Global';
import StorageHelper from '../../utils/storage';
import * as m from '../index';
import { ERequestStatus } from '../page/diverse';
import { IKeyText } from '../page/interfaces';
import { ProductTree, TreeItem } from '../support/tree';
import Address from './../ordering/address';
import IActivation from './activation';
import IProduct from './product';
import IService from './service';

export interface IAvailabilityServiceSelection {
    metaname: string;
    selected: boolean;
    sort?: number;

    productId: string;
};

export interface IAvailabilityService {
    metaname: string;
    available: boolean;
    mandatory: boolean;
    sort: number;
    prices: IAvailabilityPrice[];
    excludes: string[];
    elements: IAvailabilityService[];
    selected?: string;
    type: string;
    name: string;
};

export enum EElementType {
    undefined = 0,
    product = 1,
    sub = 2,
    service = 3
};

export class PriceWrapper {
    public brand: string = '';
    public id: string = '';
    public type: EElementType = EElementType.product;
    public prices: IAvailabilityPrice[] = [];
    public price?: any;

    constructor(id: string, brand: string, type: EElementType){
        this.id = id;
        this.brand = brand;
        this.type = type;
    };

    static createInstance(id:string, brand: string, source: any, type: EElementType) {
        let p: PriceWrapper = new PriceWrapper(id, brand, type);
        if(source) {
            if(source.prices) {
                p.prices = source.prices;
            } else if (source.price) {
                p.price = source.price;
            }
        };

        return p;
    };

    getMonthlyFee() {
        let f = Global.getEmptyFee();
        if(this.prices && this.prices.length) {
            if(this.brand == BrandKeys.kabel) {
                let p = linq.from<IAvailabilityPrice>(this.prices)
                    .where(x=>x.billingFrequency == BillingFrequency.monthly && !x.periodEnd)
                    .firstOrDefault();

                if(p) {
                    f.price = p.amount * 100;
                };
            } else if(this.brand == BrandKeys.unity) {
                //f = { price: Number.parseInt(this.price!.toString()), unit: Defaults.currencyUnit};
                f = this.price!;
            };
        } else { 
            if(this.brand == BrandKeys.unity) {
                if(this.price){
                    f = { price: Number.parseInt(this.price!.toString()), unit: Defaults.currencyUnit};
                };
            };            
        }

        return f;
    };

    getActivationFee() {
        let f = Global.getEmptyFee();
        if(this.prices && this.prices.length) {
            if(this.brand == BrandKeys.kabel) {
                let p = linq.from<IAvailabilityPrice>(this.prices)
                    .where(x=>x.billingFrequency == BillingFrequency.once)
                    .firstOrDefault();

                if(p) {
                    f.price = p.amount * 100;
                };
            } else if(this.brand == BrandKeys.unity) {
                f = this.price!;
            };
        };

        return f;
    };
};

export interface IAvailabilityPrice {
    amount: number;
    billingFrequency: string;
    periodStart?: number;
    periodEnd?: number;
};

export class BillingFrequency {
    public static monthly = 'MONTHLY';
    public static once = 'ONCE';
};

export enum EElementTypeUi
{
    undefined = 0,
    mandatory = 1,
    selectable = 2,
    optional = 4,
    selectedMandatory = 8,
    children = 16,
    childrenSelected = 32
};

export enum EAvailabilityResultStatus {
    undefined = 0,
    ok = 1,
    remove = 2,
    add = 4
};

export interface IAvailabilityResultItem {
    status?: EAvailabilityResultStatus;
    id: string;
    text?: string;
    path?: string[];
};

export class AvailabilityWrapper {
    products: AvailabilityProduct[] = [];
    
    private avlBrand: IAvailabilityBrand;

    constructor(avl: Availability) {
        this.avlBrand = linq.from<IAvailabilityBrand>(avl.brands).first(x=>x.key == BrandKeys.kabel);
    };

    applyChanges(act: IActivation) {
        let res:AvailabilityProduct[] = [];
        let sel = act.selection;

        if(sel.children.length > 0) {
            sel.children.map((i)=>{
                let local = linq.from<AvailabilityProduct>(this.products).where(x=>x.productId == i.id).firstOrDefault();

                if(!local) {
                    local = linq.from<AvailabilityProduct>(this.avlBrand.products).where(x=>x.productId == i.id).first();

                    local = AvailabilityProduct.ensure(local);
                }

                local.init(i.toArray(false, undefined));

                res.push(local);
            });
        };

        this.products = res;

        this.applyAutoSelection(act);
    };

    getExcludes() {
        this.products.map((i)=>{
            //console.log('excludes for ' + i.productId, i.exc);
        });
    };

    applyAutoSelection(act: IActivation) {
        act.selection.children.map((i)=>{
            let p = linq.from<AvailabilityProduct>(this.products).where(x=>x.productId == i.id).first();
            this.applyElements(i, p.elements);
        });
    };

    applyElements(item: TreeItem, elements: IAvailabilityService[]) {
        item.children.map((i)=>{
            let el = linq.from<IAvailabilityService>(elements).where(x=>x.metaname == i.id).firstOrDefault();
            if(el) {
                el.selected = ConstantKeys.user;

                if(el.elements) {
                    this.applyElements(i, el.elements);
                }
            }
        });
    };

    init(act:IActivation, productId: string | undefined) {
        this.products.map((i)=>{
            if(!productId || productId == i.productId) { 
                if(productId) {
                    i.reset(undefined);
                };

                i.init(act.selection.toArray(false, productId));
            };
        });
    };

    prepareElements(type: EElementTypeUi, sel: ProductTree) : IAvailabilityServiceSelection[] {
        let res:IAvailabilityServiceSelection[] = [];
        this.products.map((i)=>{
            let op:IAvailabilityServiceSelection[] = [];
            i.prepareElements(type, op, 1, sel, [i.productId], undefined);
            op.map((j)=>{
                res.push(j);
            })
        });

        return res;
    };

    prepare() {
        this.products.map((i)=>{
            i.prepare();
        });
    };

    reset() {
        this.products.map((i)=>{
            i.reset(undefined);
        });
    };

    validateAvailability(act:IActivation, brand: IAvailabilityBrand) {
        let res:IAvailabilityResultItem[] = [];
        this.products.map((i)=>{
            i = linq.from<AvailabilityProduct>(brand.products).where(x=>x.productId == i.productId).first();
            i = AvailabilityProduct.ensure(i);
            let tmp: IAvailabilityResultItem[] = i.validateAvailability(act, brand);
            res = res.concat(tmp);
        });
        return res;
    };

    getElement(productId: string, path: string[]) {
        let prod = linq.from<AvailabilityProduct>(this.products).first(x=>x.productId == productId);
        return prod.getElement(path);
    };

    getMandatory(productId: string) : string[] {
        let prod = linq.from<AvailabilityProduct>(this.products).first(x=>x.productId == productId);
        return prod.getMandatory();
    };

    getMandatoryForElement(productId: string, id: string) : string[] {
        let prod = linq.from<AvailabilityProduct>(this.products).first(x=>x.productId == productId);
        return prod.getMandatoryForElement(id);
    };

    inout(productId: string, path: string[], selected: boolean) {
        let prod = linq.from<AvailabilityProduct>(this.products).first(x=>x.productId == productId);
        prod.inout(path, selected);
    };

    static ensure(source:AvailabilityWrapper, avl : Availability) : AvailabilityWrapper{
        let aw =  Object.assign(new AvailabilityWrapper(avl), source);
        aw.products = [];

        source.products.map((i)=>{
            aw.products.push(AvailabilityProduct.ensure(i));
        });

        return aw;
    };
};

export interface IAvailabilityElement {
    id: string;
    name: string;
    price: number;
    selected: boolean;
    mandatory: boolean;

    options: IAvailabilityElement[];
};

export class AvailabilityProduct {
    //kabel
    ////public key: string = '';
    //public prices: IAvailabilityPrice[] = [];
    public elements: IAvailabilityService[] =[];

    //dsl
    public productId: string = '';
    public connectionTypes: string[] = [];
    public asb: string = '';
    public areaCode: string = '';
    public offeredAccessTechnology: string = '';
    public bandwidth: string = '';
    public hasComfort: boolean = false;
    public speed: number = 0;
    public speedText: string = '';

    //
    public dropedServices:string[] = ['kas-an-opt', 'tv-connect'];

    public price?: number;

    public exc : string[] = [];
    //private chi : string[] = [];

    public subs: IAvailabilityElement[] = [];

    private br = StorageHelper.getBrand(BrandKeys.kabel);

    public static ensure(source: AvailabilityProduct) : AvailabilityProduct {
        return Object.assign(new AvailabilityProduct(), source);
    };

    public getService(meta: string, source: IAvailabilityService[] | undefined) : IAvailabilityService | undefined {
        let src = source;

        if(!src) {
            src = this.elements;
        };
        
        for(let i = 0; i < src.length; i++) {
            if(src[i].metaname == meta) {
                return src[i];
            };

            if(src[i].elements && src[i].elements.length >0) {
                let res = this.getService(meta, src[i].elements);

                if(res) {
                    return;
                };
            };
        };

        return undefined;
    };

    //** will deliver all eleemnts of type DEVICE - kabel only */
    getServices() : string[] {
        if(this.elements) {
            return linq.from<IAvailabilityService>(this.elements).where(x=>x.type == ConstantKeys.devices).select(x=>x.metaname).toArray();
        };

        return [];
    };

    public init(selectionArray: string[]) {
        var sel = selectionArray; //this.getSelection();
        var excludes: string[] = [];
        //this.elements = linq.from<IAvailabilityService>(this.elements).where(x=>x.metaname != 'kip-be-3p-REMOVED!!!!').toArray();
        if(sel.length == 0) {
            this.elements.map((i)=>{
                if(i.type != ConstantKeys.devices) {
                    if(i.mandatory && i.metaname.indexOf('rufnummer') < 0) {
                        if(excludes.indexOf(i.metaname) < 0) {
                            if(this.dropedServices.indexOf(i.metaname) < 0) {
                                this.inout([i.metaname], true);
                            }

                            excludes = excludes.concat(i.excludes);
                        }
                    };
                };
            });
        };
    };

    public hasDroppedServices() : boolean {
        let cnt = 0;

        this.dropedServices.map((i)=>{
            if(linq.from<IAvailabilityService>(this.elements).where(x=>x.metaname == i).count() > 0) {
                cnt++;
            }
        });

        return cnt == this.dropedServices.length;
    };

    public reset(el: IAvailabilityService[] | undefined) {
        let inner = el ? el : this.elements;

        for(let i:number = 0; i<inner.length; i++){
            inner[i].selected = ConstantKeys.no;
            if(inner[i].elements) {
                this.reset(inner[i].elements);
            }
        };
    };

    //** will add or remove a service */
    public inout(path: string[], 
        selected: boolean) {
        let el: IAvailabilityService[] = this.elements;

        for(let i: number =0;i<path.length; i++){
            var e = linq.from<IAvailabilityService>(el)
                .where(x=>x.metaname == path[i])
                .first();
            if(i != path.length - 1) {
                el = e.elements;
            } else {
                if(e.type == ConstantKeys.devices) {
                    //this.deselectOtherDevices();
                    
                    if(selected) {
                        this.selectMandatoryForParent(e);
                    } else {
                        this.reset(undefined);
                    }
                };

                e.selected = selected ? ConstantKeys.user : ConstantKeys.no;

                if(e.elements && e.elements.length >0 && !selected) {
                    e.elements.map((j)=>{
                        j.selected = ConstantKeys.no;
                    });
                }
            };
        };

        this.prepare();
    };

    //** will fill a local array with services which are not bookable, according to other selection */
    public getExcludes(exc:string[], elem: IAvailabilityService[]) {
        let el: IAvailabilityService[] = elem;

        el.map((i)=>{
            if(i.excludes && i.excludes.length >0 && i.selected == ConstantKeys.user) {
                i.excludes.map((j)=>{
                    if(exc.indexOf(j)<0) { 
                        //console.log('excludes for: ' + i.metaname + ' drops ' + j);
                        exc.push(j); 
                    };
                })

                if(i.elements && i.elements.length >0){
                    i.elements.map((j)=>{
                        this.getExcludes(exc, j.elements);
                    });
                };
            };
        });
    };  

    public getElement(path: string[]) {
        //console.log('getelement', path);
        let el: IAvailabilityService[] = this.elements;

        for(let i: number =0;i<path.length; i++){
            var e = linq.from<IAvailabilityService>(el)
                .where(x=>x.metaname == path[i])
                .firstOrDefault();
            if(e) {
                if(i == path.length - 1) {
                    return e;
                } else {
                    el = e.elements;
                }
            }
        };
    };

    public getDevice() : IAvailabilityService {
        let d = linq.from<IAvailabilityService | any>(this.elements).where(x=>x.type == ConstantKeys.devices && x.selected == ConstantKeys.user).firstOrDefault();

        return d;
    };

    public appendElements(tree: TreeItem, elements: IAvailabilityService[]) {
        let all = linq.from<IAvailabilityService>(elements).where(x=>x.selected == ConstantKeys.user && x.type != ConstantKeys.devices).toArray();

        all.map((i)=>{
            let newparent = tree.add(i.metaname);

            if(i.elements && i.elements.length > 0 ) {
                this.appendElements(newparent, i.elements);
            }
        });
    };

    public prepareElements(type: EElementTypeUi, 
        target: IAvailabilityServiceSelection[],
        level: number,
        sel: ProductTree,
        path: string[],
        source: IAvailabilityService[] | undefined) {
        let res:IAvailabilityServiceSelection[] = [];

        let src: IAvailabilityService[] = source ? source : this.elements;

        src.map((i)=>{
            let currentPath = new Array().concat(path);
            currentPath.push(i.metaname);
            
            let el = sel.findPath(currentPath);
            
            if(!el) {
                i.selected = ConstantKeys.no;
            } else {
                i.selected = ConstantKeys.user;
            }

            if(type === EElementTypeUi.selectable || type === EElementTypeUi.children) {
                let man : IAvailabilityServiceSelection[] = [];
                this.prepareElements(EElementTypeUi.selectedMandatory, man, 1, sel, path, undefined);

                if(i.type != ConstantKeys.devices) {
                    if(i.metaname.indexOf('rufnummer') < 0 && this.dropedServices.indexOf(i.metaname) < 0) {
                        if(type === EElementTypeUi.selectable && this.exc.indexOf(i.metaname) < 0) { 
                            res.push(this.toElementUi(i));
                        } else if(type === EElementTypeUi.children) {
                            res.push(this.toElementUi(i));
                        };  
                    };
                };
            } else if (type === EElementTypeUi.childrenSelected ) {
                if(i.type != ConstantKeys.devices) {
                    if(i.metaname.indexOf('rufnummer') < 0 && this.dropedServices.indexOf(i.metaname) < 0) {
                        //no more children
                        if((!i.elements || i.elements.length == 0) && level > 1) {
                            if(i.selected == ConstantKeys.user) {
                                res.push(this.toElementUi(i));
                                //puch the excludes too so they are not available in 1 level
                                if(i.excludes && i.excludes.length > 0) {
                                    i.excludes.map((j)=>{
                                        res.push(this.toElementUiString(j, false));
                                    });
                                };
                            };
                        } else {
                            this.prepareElements(type, target, level + 1, sel, currentPath, i.elements);
                        };
                    };
                };
            } else if(type === EElementTypeUi.selectedMandatory) {
                if(i.type != ConstantKeys.devices && i.mandatory && i.selected == ConstantKeys.user) {
                    res.push(this.toElementUi(i));
                };

                if(i.elements && i.elements.length > 0) {
                    this.prepareElements(type, target, level + 1, sel, path, i.elements);
                };
            };
        });

        if(res.length > 0) {
            res.map((i)=>{ 
                //i.productId = this.key;
                target.push(i);
            })
        };
    };

    public deselectOtherDevices() {
        this.elements.map((i)=>{
            if(i.type == ConstantKeys.devices) {
                i.selected = ConstantKeys.no;

                this.deselectElements(i.elements);
            };
        });
    };

    public deselectElements(el: IAvailabilityService[]) {
        el.map((i)=>{
            i.selected = ConstantKeys.no;
            if(i.elements && i.elements.length >0){
                this.deselectElements(i.elements);
            };
        })
    };

    public selectMandatoryForParent(el: IAvailabilityService) {
        if(el.elements) {
            el.elements.map((i)=>{
                if(i.mandatory) {
                    if(this.dropedServices.indexOf(i.metaname) < 0 ) {
                        i.selected = ConstantKeys.user;
                    };
                };
            });
        };
    }; 

    public getSubs() {
        let res:string[] = [];
        let br = StorageHelper.getBrand(BrandKeys.kabel);
        let subs = linq.from(br.subs).select(x=>x.id).toArray();

        this.elements.map((i)=>{
            if(subs.indexOf(i.metaname)>=0) {
                res.push(i.metaname);
            };
        });

        return res;
    };

    public getMandatory() {
        let res: string[] = [];
        this.elements.map((i)=>{
            if(i.mandatory && i.metaname.indexOf('rufnummer') < 0 && i.type == ConstantKeys.options && this.exc.indexOf(i.metaname) < 0 && this.dropedServices.indexOf(i.metaname) < 0) {
                res.push(i.metaname);
            };
        });
        return res;
    };

    public getMandatoryForElement(id: string) {
        let res: string[] = [];
        if(this.elements) {
            let el = linq.from<IAvailabilityService>(this.elements).where(x=>x.metaname == id).firstOrDefault();
            if(el && el.elements) {
                el.elements.map((i)=>{
                    if(i.mandatory && i.type != ConstantKeys.devices && this.exc.indexOf(i.metaname) < 0 && this.dropedServices.indexOf(i.metaname) < 0) {
                        res.push(i.metaname);
                    };
                });
            };
        }
        return res;
    };

    public validateAvailability(act: IActivation, avl: IAvailabilityBrand) : IAvailabilityResultItem[] {
        let res:IAvailabilityResultItem[]=[];

        let prod = linq.from<AvailabilityProduct>(avl.products).where(x=>x.productId == this.productId).firstOrDefault();

        if(prod) {
            let sel = act.selection.toArray(true, this.productId); //this.getSelection();
            this.validateElements('', prod.elements, res, sel);
        };

        //filter
        //1. remove rufnummer
        res=linq.from<IAvailabilityResultItem>(res).where(x=>x.id.indexOf('rufnummern') < 0).toArray();
        //2. remove excluded items
        //let subs = this.getSubs();
        let subs = linq.from<IAvailabilityService>(prod!.elements).where(x=>x.type == ConstantKeys.devices).select(x=>x.metaname).toArray();

        subs.map((i)=>{
            res=linq.from<IAvailabilityResultItem>(res).where(x=>x.id.indexOf(i)<0).toArray();
        });

        return linq.from<IAvailabilityResultItem>(res).orderBy(x=>x.id).toArray();
    };

    private validateElements(path: string, 
        elements: IAvailabilityService[],
        target: IAvailabilityResultItem[],
        sel: string[]) {
            elements.map((i)=>{
                target.push(this.getElementStatus(path, i, sel));

                let spath = (path ? path + '/' : '') + i.metaname;

                if(i.elements && i.elements.length > 0) {
                    this.validateElements(spath, i.elements, target, sel);
                };
            });
    };

    private getElementStatus(path: string, item: IAvailabilityService, sel: string[]) : IAvailabilityResultItem {
        let res: IAvailabilityResultItem = {
            status: EAvailabilityResultStatus.ok,
            id: this.productId + '/' + (path ? path + '/' : '') + item.metaname
        };

        ////console.log('element ' + item.metaname, item);
        ////console.log('selection', sel);

        if(this.dropedServices.indexOf(item.metaname) >= 0) {
            res.status = EAvailabilityResultStatus.ok;
        } else if(item.mandatory && item.available) {
            // if in excludes then other element has driped it out so no error message to ui
            if(sel.indexOf(res.id) < 0 && this.exc.indexOf(res.id) < 0) {
                res.status = EAvailabilityResultStatus.add;
                res.text = "Hinzufgen: " + this.getUserFriendlyPath(res.id);
            };
        } else if (!item.mandatory && item.available){
            // in selection but avl.returns it with NO
            // gigakombi excluded - logic dont works with other rules
            if(sel.indexOf(res.id) >= 0 && item.selected == ConstantKeys.no) { //&& item.metaname.indexOf('-aio-') < 0) { 
                res.status = EAvailabilityResultStatus.remove;
                res.text = "Entfernen: " + this.getUserFriendlyPath(res.id);
            } else {
                res.status = EAvailabilityResultStatus.ok;
            }
        } else if (!item.available) {
            if(sel.indexOf(res.id) >= 0) {
                res.status = EAvailabilityResultStatus.remove;
                res.text = "Entfernen: " + this.getUserFriendlyPath(res.id);
            };
        };

        res.path = this.getFullPath(res.id);

        ////console.log('res: ' + res.path, item);

        return res;
    };

    private getFullPath(p: string) {
        let path = p.split('/');
        let res: string[] = [];

        path.map((i)=>{
            let svc = linq.from<IService>(this.br.services).where(x=>x.id == i).firstOrDefault();
            if(svc) {
                res.push(svc.id);
            } else {
                let svc2 = this.getService(i, undefined);
                if(svc2) {
                    res.push(svc2.metaname);
                } else {
                    res.push(i);
                }
                
            };
        });

        return res;
    };

    private getUserFriendlyPath(p: string) {
        let path = p.split('/');
        let res: string[] = [];
        path.shift();

        path.map((i)=>{
            let svc = linq.from<IService>(this.br.services).where(x=>x.id == i).firstOrDefault();
            if(svc) {
                res.push(svc.name );
            } else {
                let svc2 = this.getService(i, undefined);
                if(svc2) {
                    if(svc2.name == 'n.a') {
                        res.push(svc2.metaname);    
                    } else {
                        res.push(svc2.name);
                    }
                } else {
                    res.push(i);
                }
                
            };
        });

        return res.join(' \\ ');
    };

    private toElementUi(el:IAvailabilityService) : IAvailabilityServiceSelection {
        return {
            productId: this.productId,
            metaname: el.metaname,
            selected: el.selected == ConstantKeys.user,
            sort: el.sort
        };
    };

    private toElementUiString(metaname: string, selected: boolean) : IAvailabilityServiceSelection {
        return {
            productId: this.productId,
            metaname: metaname,
            selected: selected ,        
        }; 
    };

    prepare() {
        this.exc = [];
        this.getExcludes(this.exc, this.elements);

        //this.prepareElements(EElementTypeUi.childrenSelected, children, 1, undefined);
    };
};

export interface IAvailabilityBrand {
    key: string;
    id?: string; 
    products: AvailabilityProduct[];
    address?: Address;
    data:string;
    available?: boolean;
    selection?: TreeItem;
};

export class Availability {
    public address: m.Address = new m.Address();
    public id: string = '';
    public isCrossSelling: boolean = false;
    public executed: ERequestStatus = ERequestStatus.undefined;

    public brands: IAvailabilityBrand[] = [];

    public warning: string = '';

    public cableServices: IKeyText[] = [];

    static oneOfIsAvaialble(brands: IAvailabilityBrand[]) : boolean {
        let res : boolean = false;
        brands.map((i)=>{
            if(i.products && i.products.length >0) {
                res = true;
            }
        });
        return res;
    };

    static getProducts(brand: IAvailabilityBrand) : string[] {
        return linq.from<AvailabilityProduct>(brand.products).select(x=>x.productId).toArray();
    };

    public forUI() : string {
        let s: string = '';
        if(this.address) {
            s = this.address.street + ' ' + this.address.nr + ', ' + this.address.zip + ' ' + this.address.city;
        };
        return s;
    };
 
    public hasKip() {
        let kav = linq.from<IAvailabilityBrand>(this.brands).where(x=>x.key === BrandKeys.kabel).firstOrDefault();

        if(kav) {
            let pr = linq.from<AvailabilityProduct>(kav.products)
                .where(x=>x.productId.indexOf("kip-")>=0).toArray();
                //.where(x=>x.productId.indexOf("tv-giga-wechselgarantie")>=0).toArray();

            if(pr) {
                return pr.length > 0;
            };
        };

        return false;
    };

    public getExecuted() {
        return this.executed == ERequestStatus.done || this.executed == ERequestStatus.tempResult;
    };

    public hasKabel() {
        let cab = linq.from<IAvailabilityBrand>(this.brands).where(x=>x.key == BrandKeys.kabel).firstOrDefault();

        if(cab) {
            if(cab.products && cab.products.length >0) return true;
        };

        return false;
    };  

    public hasDSL() {
        let cab = linq.from<IAvailabilityBrand>(this.brands).where(x=>x.key == BrandKeys.dsl).firstOrDefault();

        if(cab) {
            if(cab.products && cab.products.length >0) return true;
        };

        return false;
    };  


    public getBrand(brandKey: string) : IAvailabilityBrand {
        let br: IAvailabilityBrand = linq.from<IAvailabilityBrand | any>(this.brands)
            .where(x=>x.key === brandKey)
            .firstOrDefault();
        
        if(!br) {
            br =  {
                key: brandKey,
                products: [],
                data: ''
            };
        };

        return br;
    };

    public getBrandProducts(brandKey: string) : AvailabilityProduct[] {
        return this.getBrand(brandKey).products;
    };

    public getBrandProductIds(brandKey: string) : string[] {
        
        let pr = this.getBrand(brandKey).products;

        return linq.from<AvailabilityProduct>(pr).select(x=>x.productId).toArray();
    };

    public getAvailableProducts(brandKey: string, avlBrand: IAvailabilityBrand) : string[] {
        let prod:string[] = [];
        let br = StorageHelper.getBrand(brandKey); // this.getBrand(brandKey);
        let avl = linq.from<AvailabilityProduct>(avlBrand.products).select(x=>x.productId).toArray();

        if(brandKey === BrandKeys.kabel || brandKey === BrandKeys.unity) {
            prod = linq.from<IProduct>(br.products)
                .where(x=>avl.indexOf(x.id)>=0)
                .select(x=>x.id).toArray();            
        } else {
            let brdsl = this.getBrand(brandKey);
            prod = linq.from<AvailabilityProduct>(brdsl.products).select(x=>x.productId).toArray();
        };

        return prod;
    };

    public getProduct(brand: string, productId: string) : AvailabilityProduct {
        let br = this.getBrand(brand);

        let p = brand === BrandKeys.kabel || brand === BrandKeys.unity ?
            linq.from<AvailabilityProduct>(br.products).where(x=>x.productId === productId).firstOrDefault()
            :
            linq.from<AvailabilityProduct>(br.products).where(x=>x.productId === productId).firstOrDefault();

        if(!p) {
            p = new AvailabilityProduct()
        };

        return p;
    };

    public hasRegioOnly(brandKey: string, productId: string) : boolean {
        let br = this.getBrand(brandKey);
        let res : boolean = false;
        if(br){
            let pr = linq.from<AvailabilityProduct>(br.products).where(x=>x.productId === productId).firstOrDefault();
            if(pr) {
                res = linq.from<string>(pr.connectionTypes).where(x=>!x.endsWith('_Regio')).count() == 0;
            };
        };

        return res;
    };

    public hasBasic(brandKey: string, productId: string) : boolean {
        let br = this.getBrand(brandKey);
        let res : boolean = false;
        if(br){
            let pr = linq.from<AvailabilityProduct>(br.products).where(x=>x.productId === productId).firstOrDefault();
            if(pr) {
                res = linq.from<string>(pr.connectionTypes).where(x=>x.startsWith('basic')).count() > 0;
            };
        };

        return res;
    };

    //** will fill the local array of cable servies with all possible so the UI can get a name wich is not in the EPM */
    public prepareCableServices(level: number, elements: IAvailabilityService[]) {
        if(level == 0) {
            this.cableServices = [];
            
            var br = linq.from<IAvailabilityBrand>(this.brands).first(x=>x.key == BrandKeys.kabel);
            br.products.map((i)=>{
                if(i.elements && i.elements.length > 0) {
                    this.prepareCableServices(level + 1, i.elements)
                };
            })
        } else {
            elements.map((i)=>{
                var isin = linq.from<IKeyText>(this.cableServices).firstOrDefault(x=>x.key == i.metaname);
                if(!isin) {
                    this.cableServices.push({key: i.metaname, text: i.name});
                }

                if(i.elements && i.elements.length > 0) {
                    this.prepareCableServices(level + 1, i.elements)
                };                
            })
        }
    }
};

export default Availability;