import * as linq from 'linq';
import { Availability, ERequestStatus, IDefCon, InfoItem, IOrderData } from "../../models";
import { AvailabilityProduct, IAvailabilityBrand } from "../../models/ordering/availability";
import IBrand from "../../models/ordering/brand";
import IProduct from "../../models/ordering/product";
import { TreeItem } from "../../models/support/tree";
import { BrandKeys, CatKeys } from "../../utils/Defaults";
import Global from '../../utils/Global';
import { MainTexts } from "../../utils/messages";
import StorageHelper from "../../utils/storage";

export enum ECategoryStatus {
    undefined = 0,
    available = 1,
    notAvailable = 2,
    inProcess = 3
};

export class Category extends InfoItem {
    public id: string = '';
    public status: ECategoryStatus = ECategoryStatus.notAvailable;

    public constructor(id: string, status: ECategoryStatus) {
        super();
        this.id = id;
        this.status = status;
    };

    setInfo(i: InfoItem) {
        this.text = i.text;
        this.css = i.css;

        this.visible = i.visible;
        this.loading = i.loading;
    };
};

export interface ICategoryBrandProduct {
    catKey: string;
    brandKey: string;
    productId: string;
    available: boolean;
};

export class CategoryWrapper {
    public static categories: TreeItem = new TreeItem('cat');
    public static brandsFnTv: string[] = [BrandKeys.kabel, BrandKeys.dsl, BrandKeys.unity];
    public cat: string = '';
    //**if true no filtering by cat */
    public all: boolean = false;
    public avl: Availability;
    public dc: IDefCon;
    public bra: IBrand[] = [];

    public availableCats: Category[] = [];
    public availableBrands: string[] = [];
    public catBrandMap: ICategoryBrandProduct[] = [];

    public constructor() {
        this.avl = StorageHelper.getAvailability();
        this.bra = StorageHelper.getBrands();
        this.dc = StorageHelper.getDefCon();

        this.reset();
    };

    switchCat(cat: string) {
       
        this.cat = cat;

        this.reset();
    };

    switchCatForProduct(id: string) {
        let map = linq.from<ICategoryBrandProduct>(this.catBrandMap).where(x => x.productId == id).first();

        this.switchCat(map.catKey);
    };

    getCrossSellingCats(): Category[] {
        return linq.from<Category>(this.availableCats).where(x => x.id == CatKeys.fn || x.id == CatKeys.tv).toArray();
    };

    onlyOneBrandAvailable() {
        let avl = linq.from<IBrand>(this.dc.brands).where(x => x.available).toArray();

        return avl.length == 1;
    };

    canSwitchCat(data: IOrderData) {
        // if(data.currentBrand == BrandKeys.unity) {
        //     return false;
        // };
        
        if (data.currentCat == CatKeys.fn) {
            return this.allBrandsInCategoryAvailable(data.currentCat);
        };

        return false;
    };

    //**will find the next brand within a category*/
    getOtherBrandsAvailable(currentBrand: string) : IAvailabilityBrand[]  {
        let c = linq.from<TreeItem>(CategoryWrapper.categories.children).where(x=>x.id == this.cat).first();
        let res : IAvailabilityBrand[] = [];

        for(let i: number = 0; i < c.children.length; i++){
            if(c.children[i].id == currentBrand) { continue;};
            //check if its available
            let b = linq.from<IAvailabilityBrand>(this.avl.brands).where(x=>x.key == c.children[i].id).firstOrDefault();

            if(b && b.products && b.products.length>0) {
                let allInCat = linq.from<ICategoryBrandProduct>(this.catBrandMap).where(x=>x.catKey == this.cat && x.brandKey == b!.key).select(x=>x.productId).toArray();
                let allAvl = linq.from<AvailabilityProduct>(b.products).select(x=>x.productId).toArray();
                if(allInCat.length > 0 && Global.containsAny(allAvl, allInCat)) {
                    res.push(b);
                };
            }
        };
        
        return res;
    };

    resetCategory() {
        //todo: get default cateogry fr dealer
        this.cat = CatKeys.vodafone;
    };

    private allBrandsInCategoryAvailable(catKey: string) {
        let c = linq.from<TreeItem>(CategoryWrapper.categories.children).where(x => x.id == catKey).first();

        let res = false;

        c.children.map((i) => {
            if (this.availableBrands.indexOf(i.id) >= 0) {
                res = true;
            };
        });

        return res;
    };

    getCategoryBrandProducts(withBlister: boolean): IProduct[] {
        
        let brandKey = this.getCategoryBrandKey(undefined);

        let bdc = linq.from<IBrand>(this.dc.brands).where(x=>x.id == brandKey).first();
        if(!bdc.categories || bdc.categories.indexOf(this.cat) < 0) {
            return [];
        };

        let prod = linq.from<IBrand>(this.bra).where(x => x.id == brandKey).first().products;

        prod = linq.from<IProduct>(prod).where(x => x.catKey == this.cat).toArray();

        /* !!!! check disable because of unity !!!! dont remove
        if(withBlister) {
            prod = linq.from<IProduct>(prod).where(x=> (x.blister != null && x.blister!= '' && x.blister.length > 0)).toArray();
        };
        */

        prod = this.applyAvailabilityToProducts(brandKey, prod);

       

        return prod;
    };

    private applyAvailabilityToProducts(brandKey: string, products: IProduct[]): IProduct[] {
       
        if (brandKey == BrandKeys.vodafone || brandKey == BrandKeys.otelo) {
            return products;
        };
      
        let pr = this.avl.getBrandProductIds(brandKey);

        let filteredPr: IProduct[] = [];
        filteredPr = linq.from<IProduct>(products).where(x => pr.indexOf(x.id) >= 0).toArray();
        return filteredPr;
    };

    private getCategoryProductsAvailable(catKey: string, brandsAvl: IAvailabilityBrand[]) {
        let res = false;
        if (catKey == CatKeys.vodafone || catKey == CatKeys.otelo) {
            res = true;
        } else {
            brandsAvl.map((i) => {
                if (i.products) {
                    let b = linq.from<IBrand>(this.bra).where(x => x.id == i.key).first();

                    let ca = linq.from<AvailabilityProduct>(i.products)
                        .join(b.products, ip => ip.productId, bp => bp.id, (ip, bp) => { return { IP: ip, BP: bp } })
                        .where(x => x.BP.catKey == catKey)
                        .select(x => x.BP.id)
                        .toArray();

                    if (ca.length > 0) {
                        res = true;
                    };
                };
            });
        }

        return res;
    };

    //**will add custom information to the categories e.g. loading or available */
    private applyAvailability() {
        for (let i = 0; i < CategoryWrapper.categories.children.length; i++) {
            try {
                let cat: Category = new Category(CategoryWrapper.categories.children[i].id, ECategoryStatus.notAvailable); //this.availableCats[i];

                console.log('avl', this.avl);
                let info: InfoItem = { id: '', text: MainTexts.available, css: 'main' };

                info.visible = true;
                info.loading = false;

                var brandsAvl = linq.from<IAvailabilityBrand>(this.avl.brands)
                    .where(x => CategoryWrapper.categories.children[i].getChildrenIds().indexOf(x.key) >= 0)
                    .toArray();

                let oneIsAvailable = Availability.oneOfIsAvaialble(brandsAvl);
                let productsAvailable = this.getCategoryProductsAvailable(cat.id, brandsAvl);

                if (cat.id == CatKeys.otelo || cat.id === CatKeys.vodafone) {
                    if (cat.status == ECategoryStatus.available) {
                        info.loading = false;
                        info.visible = true;
                        info.css = 'main';
                        info.text = MainTexts.available;
                    }
                    //defcon appliesd in reset so do nothing
                } else if (
                    (this.avl.executed == ERequestStatus.running || this.avl.executed == ERequestStatus.tempResult)
                    && (cat.id === CatKeys.fn || cat.id === CatKeys.tv)) {
                    //console.log('case 1');
                    //** processing availability */
                    info.loading = true;
                    info.visible = true;
                    info.css = productsAvailable ? 'main' : 'high';
                    info.text = productsAvailable ? MainTexts.available : MainTexts.inCheck;
                    cat.status = productsAvailable ? ECategoryStatus.available : ECategoryStatus.inProcess;
                } else if (
                    !(this.avl.executed == ERequestStatus.done || this.avl.executed == ERequestStatus.tempResult)
                    && (cat.id === CatKeys.fn || cat.id === CatKeys.tv)) {
                    cat.visible = false;
                } else if (
                    oneIsAvailable
                    && (this.avl.executed == ERequestStatus.done)
                    && (cat.id === CatKeys.fn || cat.id === CatKeys.tv)) {
                    info.visible = true;
                    info.loading = false;
                    info.css = "main";
                    info.text = MainTexts.available;
                    cat.status = ECategoryStatus.available;
                } else {
                    info.css = "bad theme-dark";
                    info.text = MainTexts.notAvailable;
                };

                if (!productsAvailable && cat.status != ECategoryStatus.inProcess) {
                    cat.status = ECategoryStatus.notAvailable;
                    info.css = "bad theme-dark";
                    info.text = MainTexts.notAvailable;
                }

                cat.setInfo(info);

            } catch(e) {

            };
        };

    };

    private isCategoryAvailableInDefCon(cat: string) : boolean {
        return linq.from<IBrand>(this.dc.brands).where(x=>x.categories && x.categories.indexOf(cat)>=0).count() > 0;
    };

    private isBrandAvailableInDefCon(brand: string) : boolean {
        return linq.from<IBrand>(this.dc.brands).where(x=>x.id == brand && x.available).count() > 0;
    }    

    getCategory(catKey: string) {
        return linq.from<Category>(this.availableCats).where(x => x.id == catKey).firstOrDefault();
    };

    //**delivers the top 1 prio brand in the category */
    getCategoryBrandKey(data: IOrderData | undefined) {
        if(linq.from<Category>(this.availableCats).select(x=>x.id).toArray().indexOf(this.cat) < 0) {
            this.cat = this.availableCats[0].id;
        }

        if (this.cat == CatKeys.vodafone || this.cat == CatKeys.otelo) {
            return linq.from<TreeItem>(CategoryWrapper.categories.children).where(x => x.id == this.cat).first().children[0].id;
        } else {
            //**despite the new category prio, an user is capble to switch the brand in a cat manually so return the one selected */
            if (data) {
                return data.currentBrand;
            };

            let children = linq.from<TreeItem>(CategoryWrapper.categories.children)
                .where(x => x.id == this.cat).first()
                .children;

            let distinctBrandsWithProducts = linq.from<ICategoryBrandProduct>(this.catBrandMap)
                .where(x => x.catKey == this.cat && x.available)
                .select(x => x.brandKey)
                .distinct()
                .toArray();

            let cb = linq.from<TreeItem>(children)
                .where(x => this.availableBrands.indexOf(x.id) >= 0 && distinctBrandsWithProducts.indexOf(x.id) >= 0)
                .orderByDescending(x => x.prio).first().id;

            return cb;
        }
    };

    getCategoryBrand(data: IOrderData) {
        //this.cat = data.currentCat;

        let b = this.getCategoryBrandKey(data);

        return linq.from<IBrand>(this.bra).first(x => x.id == b);
    };

    //**returns the brandid of the given product id */
    getBrandByProduct(id: string) {
        return linq.from<ICategoryBrandProduct>(this.catBrandMap).first(x => x.productId == id).brandKey;
    };

    getCategoryBrandMapByProduct(id: string): ICategoryBrandProduct {
        return linq.from<ICategoryBrandProduct>(this.catBrandMap)
            .where(x => x.productId == id)
            .first();
    };

    //**needed for ALL TARIFFS Link */
    getFirstProductInCategory() {
        let map = linq.from<ICategoryBrandProduct>(this.catBrandMap)
            .where(x => x.catKey == this.cat)
            .toArray();
        
        let ids = linq.from<ICategoryBrandProduct>(map).where(x=>x.available === true).select(x => x.productId).toArray();
        // let b = this.getCategoryBrandKey(undefined);
        let br = linq.from<IBrand>(this.bra).where(x => x.id == map[0].brandKey).first();
        let brp = linq.from<IProduct>(br.products).where(x => ids.indexOf(x.id) >= 0).toArray();
        
        let p = this.applyAvailabilityToProducts(map[0].brandKey, brp);

        return p.length>0? p[0].id : ids[0]
        
    };

    getCategoryKeyByProduct(id) {
        let p = linq.from<ICategoryBrandProduct>(this.catBrandMap).firstOrDefault(x => x.productId == id);
        return p ? p.catKey : '';
    };

    getCategoryByProduct(id) {
       
        let p = linq.from<ICategoryBrandProduct>(this.catBrandMap).firstOrDefault(x => x.productId == id);
        
        if(p) {
            
            return linq.from<Category>(this.availableCats).first(x => x.id == p!.catKey);
        } else {
           
            return linq.from<Category>(this.availableCats).first(x => x.id == this.cat);
        }
    };

    getSelected = () => {
        if(!this.cat) {
            var c  = linq.from<Category>(this.availableCats).where(x=>x.status == ECategoryStatus.available).firstOrDefault();
            if(c) {
                this.cat = c.id;
            }
        };
        return linq.from<Category>(this.availableCats).where(x => x.id == this.cat).firstOrDefault();
    };

    reset = () => {
        this.avl = StorageHelper.getAvailability();

        if(!this.dc) {
            this.dc = StorageHelper.getDefCon();
        };

        CategoryWrapper.categories.children = [
            TreeItem.createInstance(CatKeys.vodafone, [new TreeItem(BrandKeys.vodafone)]),
            TreeItem.createInstance(CatKeys.otelo, [new TreeItem(BrandKeys.otelo)]),
            TreeItem.createInstance(CatKeys.fn, [TreeItem.createInstance(BrandKeys.kabel, [], 100), TreeItem.createInstance(BrandKeys.unity, [], 50), TreeItem.createInstance(BrandKeys.dsl, [], 0)]),
            TreeItem.createInstance(CatKeys.tv, [new TreeItem(BrandKeys.kabel)])
           
        ];

        this.availableCats = [];
        this.availableBrands = [];

        CategoryWrapper.categories.children.map((i) => {
            let c = new Category(i.id, ECategoryStatus.undefined);

            let dc = linq.from<IBrand>(this.dc.brands).firstOrDefault(x => x.id == i.id);

            if (dc) {
                if (dc.available) {
                    c.status = ECategoryStatus.available;
                    this.availableBrands.push(i.id);
                };
            };

            if (linq.from<string>(i.getChildrenIds()).any(x => CategoryWrapper.brandsFnTv.indexOf(x) >= 0)) {
                i.children.map((j) => {
                    c.status = ECategoryStatus.undefined;

                    let av = linq.from<IAvailabilityBrand>(this.avl.brands).firstOrDefault(x => x.key == j.id);
                    if (av && av.products && av.products.length > 0 && this.isBrandAvailableInDefCon(j.id)) {
                        c.status = ECategoryStatus.available;
                        this.availableBrands.push(j.id);
                    }
                });
            };

            if(c.status == ECategoryStatus.available) {
                this.availableCats.push(c);
            };
        });

        if (!this.cat) {
            let fa = linq.from<Category>(this.availableCats).firstOrDefault(x => x.status == 1);
            if (fa) {
                this.cat = fa.id;
            } else {
                this.cat = this.availableCats.length > 0 ? this.availableCats[0].id : '';
            }
        };

        this.bra.map((i) => {
            i.products.map((j) => {
                let a = false;
                if (j.catKey == CatKeys.otelo || j.catKey == CatKeys.vodafone) {
                    a = true;
                } else {
                    if (this.avl && this.avl.brands) {
                        let avlBrand = linq.from<IAvailabilityBrand>(this.avl.brands).where(x => x.key == i.id).firstOrDefault();
                        if (avlBrand) {
                            a = Availability.getProducts(avlBrand).indexOf(j.id) >= 0;
                        }
                    }
                }

                this.catBrandMap.push({ brandKey: i.id, catKey: j.catKey, productId: j.id, available: a });
            })
        });

        this.applyAvailability();

        this.availableBrands = linq.from<string>(this.availableBrands).distinct().toArray();

        console.log('reseted', this);
    };
};

export default CategoryWrapper;