import * as linq from 'linq';
import * as m from '../../models/index';
import { EActivationType, ECreditDebitType, ENumberPortingType, EValidationGroupType } from '../../models/ordering/order';
import IService from '../../models/ordering/service';
import IServiceValidationGroup from '../../models/ordering/validationGroup';
import { BrandKeys } from '../Defaults';
import { ValidationMessages } from '../messages';
import StorageHelper from '../storage';
import ModelState from './modelState';

export class OrderingBase {
    public validate(act: m.IActivation, modelState: ModelState, data: m.IOrderData | undefined) {
        
        if (!act.productId) {
            
            modelState.errorMessage = ValidationMessages.errorProd;
            return;
        };

        if (!this.validateServiceGroups(act, modelState, data)) {
            return;
        };

        //sub validate
        // - if creditdebit in bnt
        // - or Vf and VVL
        if ((!act.sub || act.sub === '') &&
            (act.creditDebitType === ECreditDebitType.postpaid || (act.activationType === EActivationType.existingCustomer && act.brandKey === BrandKeys.vodafone))) {
            if (act.isWithHardware()) {
                if (act.brandKey === BrandKeys.dsl) {
                    modelState.errorMessage = ValidationMessages.errorHardware;
                } else if (act.brandKey === BrandKeys.kabel) {
                    if(!act.hardwareNecesseryAndSelected(undefined)) {
                        modelState.errorMessage = ValidationMessages.errorHardware;                    
                    }
                } else {
                    modelState.errorMessage = ValidationMessages.errorSub;
                };
            }
        };

        if (act.brandKey === BrandKeys.vodafone && !act.msisdn) {
            modelState.errorMessage = ValidationMessages.errorSim;
        };

        if (act.brandKey === BrandKeys.vodafone && act.activationType === EActivationType.actDefault && !this.validateSim(act.msisdn)) {
            modelState.errorMessage = ValidationMessages.errorSimNumeric14;
        };

        if (act.activationType === EActivationType.portDefault && !this.validateRequired(act.msisdn) && act.brandKey!==BrandKeys.unity) {
            modelState.errorMessage = ValidationMessages.requiredError + act.msisdn;
        };

        if (act.activationType === EActivationType.portDefault && !this.validateNumeric(act.msisdn) && act.brandKey!==BrandKeys.unity) {
            modelState.errorMessage = ValidationMessages.errorMsisdnNumeric + act.msisdn;

            //2020-04-01 sm if kabel and later mnp then reset the error, msisdn is not needed
            if(data && act.brandKey === BrandKeys.kabel && data.customer.mnp && data.customer.mnp.numberPortingType == ENumberPortingType.later) {
                modelState.errorMessage = '';
            };
        };

       

        if (act.activationType === EActivationType.actDefault && act.imei && !this.validateNumeric(act.imei)) {
            modelState.errorMessage = ValidationMessages.errorImeiNumeric;
        };

        if (act.activationType === EActivationType.existingCustomer 
            && act.sub != 'OS'
            && act.brandKey === BrandKeys.vodafone 
            && !act.imei) {
            modelState.errorMessage = ValidationMessages.errorImei;
        };

        if (act.imei && act.imei.length != 15) {
            modelState.errorMessage = ValidationMessages.errorImei15;
        };

        if (act.brandKey === BrandKeys.otelo) {
            if (
                ((!act.msisdn || !act.code) && act.activationType === EActivationType.actDefault)
                ||
                ((!act.msisdn) && act.activationType === EActivationType.portDefault)
            ) {
                modelState.errorMessage = ValidationMessages.errorMsisndCode;
            }
        };
    };

    public validateRequired(val: string): boolean {
        let hasErrors: boolean = true;

        if (val.indexOf(";") > 0) {
            let allNumbers = val.split(";");
            let splitted: string[] = [];
            for (let i = 0; i < allNumbers.length; i++) {
                splitted = allNumbers[i].split("-");
                if (splitted[0] === "" || splitted[1] === "") {
                    hasErrors = false;
                    break;
                }
            }

        } else {
            let numberInfo = val.split("-");
            if (numberInfo[0] === "" || numberInfo[1] === "") {
                hasErrors = false;
              
            }
        }
        return hasErrors;

    };

    public validateSim(val: string): boolean {
        let regexp = new RegExp("\\b\\d{14}\\b", "g");
        let res = regexp.test(val);
        return res;
    };

    public validateNumeric(val: string): boolean {
        let errorFound: boolean = false;
        if (val.indexOf(";") >= 0) {
            let tempArray = val.split(";");

            for (let i = 0; i < tempArray.length; i++) {
                let oneNumber = tempArray[i].split("-");

                let regexp = new RegExp("\\b\\d+\\b", "g");
                let res = regexp.test(oneNumber[0] + oneNumber[1]);
                errorFound = res;
                if (!res) {
                    errorFound = res;
                    break;
                }
            }
        } else {
            let regexp = new RegExp("\\b\\d+\\b", "g");
            let res = regexp.test(val);
            errorFound = res;
            return res;
        }

        return errorFound;
    };

    public validateServiceGroups(act: m.IActivation, modelState: ModelState, data: m.IOrderData | undefined): boolean {
        var br = StorageHelper.getBrand(act.brandKey);

        if (br.serviceGroups) {
            var gr = linq.from<IServiceValidationGroup>(br.serviceGroups)
                .where(x => x.productId === act.productId && x.subId === act.sub && x.activationType === act.activationType)
                .toArray();

            for (let i = 0; i < gr.length; i++) {
                let g = gr[i];

                if (g.type === EValidationGroupType.oneMandatory) {
                    let svc = linq.from<string>(act.services).where(x => g.services.indexOf(x) >= 0).toArray();

                    if (svc.length === 0) {
                        let svcNames: string[] = linq.from<IService>(br.services)
                            .where(x => g.services.indexOf(x.id) >= 0)
                            .select(x => x.name)
                            .toArray();

                        let msg: string = ValidationMessages.errorOneMandatoryOption.replace('{0}', svcNames.join(', '));

                        modelState.errorMessage = msg;

                        return false;
                    };
                };
            };
        };

        return true;
    };
};

export default OrderingBase;