import { OrderingBase } from "..";
import * as linq from 'linq';
import ModelState from "./modelState";
import * as m from '../../models/index';
import Global from "../Global";
import { ValidationMessages } from "../messages";
import { EActivationType, ESubOptions } from "../../models/ordering/order";
import { OrderDataWrapper } from "../../models/index";
import StorageHelper from "../storage";
import { BrandKeys } from "../Defaults";
import ISub from "../../models/ordering/sub";

export class OrderingVodafone extends OrderingBase {
    public validateOrder(act: m.IActivation, 
        modelState: ModelState, 
        data: m.IOrderData | undefined) {
        super.validate(act, modelState, data);
        
        if(!act.imei) {
            var br = StorageHelper.getBrand(BrandKeys.vodafone);
            var sub = linq.from<ISub>(br.subs).where(x=>x.id == act.sub).firstOrDefault();
            if(sub && OrderDataWrapper.subHasOption(sub, ESubOptions.ImeiRequired)) {
                modelState.errorMessage = ValidationMessages.errorImei;
            }
        }

        if(!act.msisdn || act.msisdn === '') {
            modelState.addValues('msisdn', ValidationMessages.requiredError, Global.getPropertyPath('msisdn', act));
        };
    };
};

export default OrderingVodafone;