import linq, * as Enumerable from 'linq';
import * as React from 'react';
import * as act from '../../../src/components/ordering/process/index';
import * as page from '../../../src/components/page/index';
import { EPopupType, IPopupConfig } from '../../components/dialogs/popup';
import * as cmp from '../../components/index';
import BrandsList from '../../components/ordering/brandList';
import { EBrandThumbType } from '../../components/ordering/brandThumb';
import { IActivationBlockState } from '../../components/ordering/process/activationBlock';
import ActivationCustomerData from '../../components/ordering/process/activationCustomerData';
import * as i from '../../interfaces/index';
import { IApiResponse, IApiSummary, ISocketPushModel } from '../../interfaces/index';
import IProcessNewSelection from '../../interfaces/ordering/IProcessNewSelection';
import * as m from '../../models/index';
import { IDefCon } from '../../models/index';
import IActivation from '../../models/ordering/activation';
import IBrand from '../../models/ordering/brand';
import { ICustomer } from '../../models/ordering/customer';
import { IOrderData, OrderDataWrapper } from '../../models/ordering/data';
import { EActivationType, EBusinessCase, ECreditDebitType, ECustomerType, ENumberPortingType, EOrderProcessView } from '../../models/ordering/order';
import { IProduct } from '../../models/ordering/product';
import { ERequestType } from '../../models/page/diverse';
import { IFocusBox } from '../../models/page/interfaces';
import Api from '../../utils/api';
import { BrandKeys, CatKeys, StorageKeys } from '../../utils/Defaults';
import Global from '../../utils/Global';
import GlobalJSX from '../../utils/GlobalJSX';
import * as u from '../../utils/index';
import { MainTexts, OrderingTexts, OrderStages, ValidationMessages } from '../../utils/messages';
import OrderingHelper from '../../utils/OrderingHelper';
import EasyRouter from '../../utils/router';
import StorageHelper from '../../utils/storage';
import { GigacombiBookOptions, GiggacombiBanners } from './advertisement';

export interface IProcessNewScreenProps {
    id: string;
    brandKey?: string;
    productId?: string;
    preview: boolean;
};

export interface IProcessNewScreenState {
    //currentBrand: string;
    changed: boolean;
    progress: EOrderProcessView;
    processing: boolean;
    loading: boolean;
    apiMessage: string;

    closablePopup: boolean;
    numbersToPort: number;
    individualPorting: boolean;
};

export class ProcessNewScreen extends React.Component<IProcessNewScreenProps, IProcessNewScreenState> {
    public modelState: u.ModelStete = new u.ModelStete();
    public currentBookedOption: GigacombiBookOptions = GigacombiBookOptions.undefined;

    private lastApi?: IApiResponse;
    public data: IOrderData;
    public dataWrapper: OrderDataWrapper;
    private defcon: IDefCon;
    private product?: IProduct;
    public selection: IProcessNewSelection;
    public current?: IActivation;
    public static uri: string = '/ordering/new/';
    public static title: string = 'Aktivierung';
    public selectedBrand: string = '';
    public selectedProduct: string = '';
    public currentIndex: number = 0;
    private dynamicPopup: IPopupConfig | undefined;
    public numbersToPort: string = '';
    private loading: boolean = false;

    public static containerId: string = "process";

    private mounted: boolean = false;

    private processPopupState: EPopupType = EPopupType.pleaseWait;

    constructor(props: IProcessNewScreenProps) {
        super(props);

        this.handleActivation = this.handleActivation.bind(this);
        //this.updateActivation = this.updateActivation.bind(this);
        this.startNewActivation = this.startNewActivation.bind(this);
        this.applyPayment = this.applyPayment.bind(this);
        this.applyActivationType = this.applyActivationType.bind(this);
        this.applyCustomerType = this.applyCustomerType.bind(this);
        this.applyCustomer = this.applyCustomer.bind(this);
        this.onCreateProcess = this.onCreateProcess.bind(this);
        this.onDismissProcessPopup = this.onDismissProcessPopup.bind(this);
        this.onDismissValidation = this.onDismissValidation.bind(this);
        this.onInputData = this.onInputData.bind(this);
        this.changeBrand = this.changeBrand.bind(this);
        this.refresh = this.refresh.bind(this);
        this.activationChanged = this.activationChanged.bind(this);
        this.socketCallback = this.socketCallback.bind(this);

        this.defcon = StorageHelper.getDefCon();

        if (this.props.preview) {
            this.loading = true;
            this.loadProcess();
        };

        this.data = this.prepareData();

        this.dataWrapper = new OrderDataWrapper(this.data);

        OrderDataWrapper.ensure(this.data);

        let pr = this.getProgress();

        this.selectedBrand = this.data.currentBrand;
        

        //initialbanner logic
        if (this.selectedBrand == BrandKeys.vodafone || this.selectedBrand == BrandKeys.otelo) {
            this.currentBookedOption = GigacombiBookOptions.mobilfunk_doppel_kombi;
        } else if (this.selectedBrand == BrandKeys.dsl) {
            this.currentBookedOption = GigacombiBookOptions.internet_Phone_Doppel_Kombi;
        } else if (this.selectedBrand == BrandKeys.kabel) {
            this.currentBookedOption = GigacombiBookOptions.tv_doppel_kombi;
        }

        //if(Global.easyApp.head) {
        let title: string = MainTexts.newCustomerActivation
        if (this.data.activations.length > 0 && this.data.activations[0].activationType === EActivationType.partnerCard) {
            title = OrderingTexts.bookPartner;
        };

        Global.easyApp.setTitle(title);
        //}


        this.selection = {
            businessCase: EBusinessCase.bnt,
            creditDebitType: this.data.activations && this.data.activations.length > 0 && this.data.activations.length < 2 ? this.data.activations[0].creditDebitType : ECreditDebitType.postpaid,
            activationType: EActivationType.actDefault,
            customerType: ECustomerType.personal
        };

        this.state = {
            changed: false,
            progress: pr,
            processing: false,
            closablePopup: false,
            loading: this.loading,
            apiMessage: "",
            numbersToPort: 2,
            individualPorting: true
        };
    };

    public componentDidMount() {
        this.initData();

        this.mounted = true;

        this.setState({ changed: true });
    };

    private initData() {
        this.data = this.prepareData();
        

        this.dataWrapper = new OrderDataWrapper(this.data);

        OrderDataWrapper.ensure(this.data);
    };

    componentWillReceiveProps(next: IProcessNewScreenProps) {
        this.setState({ changed: true });
    };

    public refresh() {
        this.setState({ changed: true });
    };

    public getId() {
        let s = window.location.href;
    };

    public validate(): boolean {
        this.modelState.reset();

        if (this.current) {
            this.modelState = u.ValidationWrapper.startActivationValidation(this.current, this.dataWrapper);
        }

        if (!this.modelState.isValid()) {
            this.setState({ changed: true });
            return false;
        }

        return true;
    };

    public handleActivation(model: IActivationBlockState) {
        this.setState({ changed: true });
    };

    public applyPayment(pay: ECreditDebitType) {
        if (this.current) {
            this.current.creditDebitType = pay;

            StorageHelper.updateActivation(this.current);
        };

        if (this.selection) {
            this.selection.creditDebitType = pay;
        }

        this.setState({ changed: true });
    };

    public applyCustomerType(type: ECustomerType) {
        if (this.selection) {
            this.selection.customerType = type;
        }

        this.setState({ changed: true });
    };

    public applyActivationType(act: EActivationType) {
        if (this.selection) {
            if (this.current) {
                this.current.activationType = act;
                if (this.current.activationType === EActivationType.actDefault) {
                    this.current.portationType = m.EPortationType.undefined;
                } else {
                    this.current.portationType = m.EPortationType.contractEnd;
                }
            }
            this.selection.activationType = act;
        }
        this.setState({ changed: true });
    };

    public checkPopup() {
        let cfg: IPopupConfig = {
            props: {
                id: 'dynamicPopup',
                title: 'Warenkorb',
                visible: true,
                closable: false,
                type: EPopupType.pleaseWait
            },
            closeOnDone: true,
            content: <div>Warenkorb wird geprft</div>,
            callback: (d) => {
                //this.setState({changed: true});
            }
        };

        return GlobalJSX.GetPopup(cfg.props, cfg.content, cfg.action);
    };

    public render() {
        if (!this.mounted || this.state.loading) { return <></>; }

        if (this.props.preview) {
            return this.renderPreviewPrint();
        } else {
            return this.renderProcess();
        }
    };

    public getProgress(): EOrderProcessView {

        if (window.location.href.indexOf('/' + OrderStages.done + '/') > 0) {
            return EOrderProcessView.done;
        } else if (window.location.href.indexOf('/' + OrderStages.customer + '/') > 0) {
            return EOrderProcessView.customer;
        } else {
            return EOrderProcessView.tariff;
        };
    };

   public onCategoryChange = (catkey: string) => {
        if (this.validate()) {
            this.dynamicPopup = undefined;

            if (this.current!.brandKey == BrandKeys.kabel && (catkey == CatKeys.tv || catkey == CatKeys.fn)) {

                Global.categoryWrapper.all = true; //!Global.categoryWrapper.all;

                document.getElementById(act.ActivationBlockSimple.componentId)!.scrollIntoView({ behavior: 'smooth' });
            }

            Global.categoryWrapper.switchCat(catkey);
            this.data.currentCat = catkey;
            this.selectedBrand = this.data.currentBrand = Global.categoryWrapper.getCategoryBrandKey(undefined);

            //fix cable in the wrong order
            if (this.selectedBrand == BrandKeys.kabel) {
                if (!this.dataWrapper.hasKabel()) {
                    this.startNewActivation();
                } else {
                    let a = linq.from<IActivation>(this.data.activations).where(x => x.brandKey == BrandKeys.kabel).first();
                    let idx = this.data.activations.indexOf(a);
                    this.current = this.data.activations[idx];
                    this.currentIndex = idx;
                    Global.categoryWrapper.all = true;
                }
                this.setState({ changed: true });
                return;
            }

            //if current is cable then add the next one to same basket
            if (!(this.current!.brandKey == BrandKeys.kabel && (catkey == CatKeys.tv || catkey == CatKeys.fn))) {

                this.current = this.startNewActivation();
            } else if (this.selectedBrand === BrandKeys.dsl) {

                this.current = this.startNewActivation();
            }

            this.setState({ changed: true });
        }
    }

    public onCreateProcess(type: ERequestType) {
        this.lastApi = undefined;

        this.setState({ processing: true });

        let avl = StorageHelper.getAvailability();
         
        if (avl) {
            
            this.data.activations.forEach((i, idx) => {
                
                i.availability = avl;
                i.availabilityId = avl.id;
            });

            StorageHelper.addOrder(this.data);
            
        }
        
        //reset availability because its to big
        let toSend: IOrderData = JSON.parse(JSON.stringify(this.data));
        toSend.activations.forEach((i, idx) => {
            i.availability = new m.Availability();
        });

    
        if (type === ERequestType.create) {
            this.data.activations.map((i, idx) => {
              
                if (!i.isPrinted()) { i.resetSteps(ERequestType.print); };
                if (!i.isActivated() && !i.isTimedout()) { i.resetSteps(ERequestType.activation); };
            });
        };
       
        let pw = this.dataWrapper.getPasswordForAdditionalContract();
        if (pw) {
            this.data.customer.password = pw;
            StorageHelper.addOrder(this.data);
        };
        
        Global.hub.send(JSON.stringify(toSend), type, undefined); //m.ERequestType.create, undefined );

        this.setState({ closablePopup: false });

        this.processPopupState = EPopupType.pleaseWait;
    };

    public onInputData(target: any) {
        Global.onInput(this.current, target, () => {
            if (this.current) { StorageHelper.updateActivation(this.current); }
            this.setState({ changed: true });
        });
    };

    public applyCustomer(value: ICustomer) {
        
        this.data.customer = value;
        StorageHelper.addOrder(this.data);
        this.setState({ changed: true });

    };

    public removeActivation(index: number) {

        this.selectedProduct = '';
        this.data.activations.splice(index, 1);
        StorageHelper.addOrder(this.data);

        this.currentIndex = index > 0 ? index - 1 : -1;
        this.current = this.data.activations[this.currentIndex];

        this.setState({ changed: true });
    };

    public editActivation(index: number) {
        this.data.currentBrand = this.data.activations[index].brandKey;
        this.data.currentCat = this.data.activations[index].catKey;
        this.currentIndex = index;
        this.current = this.data.activations[this.currentIndex];

        this.setState({ changed: true });
    };

    public canEditActivation() {
        if (!this.dataWrapper.canEditActivation(this.currentIndex)) {
            this.modelState.errorMessage = ValidationMessages.errorEditDone;
            this.setState({ changed: true });
            return false;
        };

        return true;
    };

    public showPopup(type: EPopupType, message: string, inprocess: boolean) {
        this.processPopupState = type;
        this.setState({
            changed: true,
            apiMessage: message,
            closablePopup: true,
            processing: inprocess
        });
    };

    private prepareData = (): IOrderData => {
        if (this.loading) {
            return { id: 'dummy', type: EBusinessCase.bnt, activations: [], currentCat: '', currentBrand: '', customer: {} }
        };

        let sa: string[] = window.location.pathname.split('/');

        if (this.props.preview) {
            
            return StorageHelper.getOrder(sa[4]);
        } else {
           
            return StorageHelper.getOrder(sa[4]);
        }
    };

    private activationChanged() {
        if (!this.current) {
            this.current = this.startNewActivation();
        };

        StorageHelper.addOrder(this.data);
        this.setState({ changed: true });
    };

    private startNewActivation(apply?: any): IActivation {
        this.data.activations = linq.from<IActivation>(this.data.activations)
            .where(x => (x.productId !=undefined && x.productId.length > 0))
            .toArray();

        let act: IActivation = IActivation.createInstance(this.selectedBrand,
            this.data.id,
            Global.categoryWrapper.cat);

        act.availability = StorageHelper.getAvailability();

        if (apply) {
            apply(act);
        };

        this.data.activations.push(act);
        this.currentIndex = this.data.activations.length - 1;

        this.current = act;
        StorageHelper.addOrder(this.data);
        this.setState({ changed: true });
        return act;
    };

    private getTitle(pr: EOrderProcessView) {
        let t: string = pr == EOrderProcessView.tariff ? OrderingTexts.activationBNTTariff :
            pr == EOrderProcessView.customer ? OrderingTexts.activationBNTCustomer : OrderingTexts.activationBNTOverview;

        return t;
    };

    public gotoView(value: EOrderProcessView, callback?: any) {
        let pr = this.getProgress();

        if (pr === EOrderProcessView.tariff) {
            if (!this.validate()) { return; };
        }

        this.setState({ progress: value });

        let prname = value === EOrderProcessView.tariff ? OrderStages.tariff
            : value === EOrderProcessView.customer ? OrderStages.customer : OrderStages.done;

        let s: string = ProcessNewScreen.uri + prname + '/' + this.data.id;

        EasyRouter.route({ url: s, title: OrderingTexts.activation + ": " + this.getTitle(value) });

        if (callback) { callback(); };
    };

    public socketCallback(sock: ISocketPushModel) {
        let api: i.IApiResponse = JSON.parse(sock.data);
  
        this.lastApi = api;

        if (api && !api.error) {
            if (sock.type === ERequestType.checkMsisdn) {
                this.setState({ processing: false });
                this.gotoView(EOrderProcessView.customer);
            } else {
                api.data.isCrossSelling = this.data.isCrossSelling;
                this.data = api.data;
    
                OrderDataWrapper.ensure(this.data);
                StorageHelper.addOrder(this.data);
                this.setState({ processing: false, progress: EOrderProcessView.done }, () => {
                    this.gotoView(EOrderProcessView.done, null);
                });
            }
        } else {
            let sum: IApiSummary = Api.parseSummary(api, undefined);
            this.processPopupState = EPopupType.custom;
            this.setState({
                processing: false, closablePopup: true,
                apiMessage: Global.extractErrorAndValidation(sum)
            });

            if (api.validation) {
                Api.applyValidation(api);
            };
        }
    };

    public syncOrder(order: IActivation, callback: any) {
        let avl = StorageHelper.getAvailability();

        let model = {
            availabilityId: avl.id,
            brand: this.data.currentBrand,
            order: {
                processId: order.processId,
                productId: order.productId,
                sub: order.sub,
                services: order.services,
                activationType: order.activationType
            }
        };

        let cfg: IPopupConfig = {
            props: {
                id: "dynamicPopup",
                title: 'Warenkorb',
                visible: true,
                closable: false,
                type: EPopupType.pleaseWait,
                height: 300,
                onDismiss: () => {
                    Global.easyApp.runPopup(undefined, undefined);
                }
            },
            content: <div>Warenkorb wird geprft</div>,
        };

        var pop = GlobalJSX.GetPopup(cfg.props, cfg.content, cfg.action);

        Global.easyApp.runPopup(pop, () => {
            Api.fetch('activation/syncorder', model, "POST", (api) => {
                var sum = Api.parseSummary(api, undefined);

                if (sum.status == m.ERequestStatus.done) {
                    Global.easyApp.runPopup(undefined, undefined);
                    if (callback) {
                        callback(api);
                    };
                } else {
                    var popup = Global.FindReact(document.getElementById("dynamicPopup")) as cmp.Popup;

                    popup.refresh(true, true, EPopupType.custom, <div dangerouslySetInnerHTML={{ __html: sum.message }} />);
                }
            });
        });
    };

    private onDismissProcessPopup() {
        this.setState({ processing: false, closablePopup: false });
    };

    private onDismissValidation() {
        this.modelState.reset();

        this.setState({ changed: true });
    };

    private renderPage(pr: EOrderProcessView): JSX.Element {
        let ctrl = <></>;

        if (pr === EOrderProcessView.done) {
            ctrl = (
                <>
                    <page.Inner1C>
                        <page.Inner2CLeft>
                            <>
                                {this.renderProgress()}
                            </>
                        </page.Inner2CLeft>
                        <></>
                    </page.Inner1C>
                </>);
        } else {
            ctrl = (
                <>
                    <page.Inner2C>
                        <page.Inner2CLeft>
                            <>
                                {GlobalJSX.renderErrorBlock(this.lastApi)}

                                {this.renderProgress()}
                            </>
                        </page.Inner2CLeft>
                        <page.Inner2CRight>
                            <>
                                <act.ActivationSummary parent={this} activations={this.data.activations} />
                            </>
                        </page.Inner2CRight>
                    </page.Inner2C>
                </>);
        }


        return ctrl;
    };

    private loadProcess() {
        let sa = window.location.pathname.split('/');
        let id: string = sa[4]; //EasyRouter.getQueryVariable(window.location.href, StringTokens.id, '');
        let url: string = 'activation/getprocess/' + id;

        Api.fetch(url, undefined, 'GET', (api: i.IApiResponse) => {

            var d: IOrderData = api.data as IOrderData;
            let aa: IActivation[] = [];

            d.activations.forEach((i, idx) => {
                let aaa: IActivation = Object.assign(new IActivation(i.brandKey), i);
                aa.push(aaa);
            });

            d.activations = aa;

            StorageHelper.addOrder(d);

            if (d.avl) {
                StorageHelper.set(StorageKeys.keyAvailability, d.avl);
            }

            this.data = d;
            this.setState({ changed: true, loading: false });
        });
    };

    private changeBrand(brandKey: string) {
        if (OrderDataWrapper.previewAvailable(this.data.activations)) {
            if (!this.validate()) { return; };

        };
        this.selectedBrand = this.data.currentBrand = brandKey;
        this.selection.creditDebitType = ECreditDebitType.postpaid;
        this.selection.activationType = EActivationType.actDefault;

        this.startNewActivation();
        let nextUrl = ProcessNewScreen.uri + 'tariff/' + this.data.id + '?brand=' + brandKey;
        EasyRouter.route({ url: nextUrl, title: ProcessNewScreen.title });
        this.setState({ changed: true });
    };

    private changeCat = (catKey: string) => {
       
        if (OrderDataWrapper.previewAvailable(this.data.activations)) {
            if (!this.validate()) { return; };
        }

    
        //Global.categoryWrapper.switchCat(catKey);
        this.selectedBrand = this.data.currentBrand = Global.categoryWrapper.getCategoryBrandKey(undefined);
        this.product = undefined;
        this.selection.creditDebitType = ECreditDebitType.postpaid;
        this.selection.activationType = EActivationType.actDefault;
        ////console.log('changed:' + catKey + '=' + this.selectedBrand);
        this.startNewActivation();
        let nextUrl = ProcessNewScreen.uri + 'tariff/' + this.data.id + '?brand=' + this.selectedBrand;
        EasyRouter.route({ url: nextUrl, title: ProcessNewScreen.title });
        this.setState({ changed: true });
    };

    public changeCatBrand = (brandKey: string) => {
        this.selectedBrand = this.data.currentBrand = brandKey;
        this.current!.reset();
        this.current!.brandKey = brandKey;
        StorageHelper.addOrder(this.data);
        this.setState({ changed: true });
    };

    private renderProgress(): JSX.Element {

        let pr = this.getProgress();

        if (pr == EOrderProcessView.tariff) {

            return this.renderTariff();
        } else if (pr === EOrderProcessView.customer) {
            return this.renderCustomer();
        } else if (pr === EOrderProcessView.done) {
            if (this.state.loading) { return <></> };

            return this.renderPreview();
        };

        return <></>;
    };

    private canRenderFocusBox(box: IFocusBox): boolean {
        for (let i = 0; i < box.items.length; i++) {
            //brand;productId;sub;option
            let id = box.items[i].id.split(';')[1];

            if (this.dataWrapper.hasProduct(id)) {
                return false;
            };
        };

        return true;
    };

    private renderTariff(): JSX.Element {

        let brands = StorageHelper.getBrands();
        let avl = StorageHelper.getAvailability();

        let brand = Global.categoryWrapper.getCategoryBrand(this.data);

        let boxes = StorageHelper.getSupport().boxes;
        let box = avl.hasKip() ? boxes[1] : boxes[1];

        let ctrl = (
            <>
                <BrandsList
                    thumbType={EBrandThumbType.blister}
                    thumbSelectable={false}
                    selected={this.selectedBrand}
                    block={{ title: 'Whlen Sie eine Kategorie', transparent: true, style: "block-thumbs selonly" }} />

                {this.renderAdditional(brand)}

                <div className="spacer"></div>
                <div className="text-tiny center">Weiteren Tarif hinzufgen</div>
                <div className="spacer"></div>

                <BrandsList
                    thumbType={EBrandThumbType.square}
                    thumbSelectable={true}
                    clickHandler={this.changeCat}
                    selected={this.selectedBrand}
                    block={{ transparent: true, style: "block-thumbs" }} />

                <act.ActivationActions parent={this} progress={this.getProgress()} />

            </>
        );

        return ctrl;

    };

    /**
     * Will render componets depending if some additional input is needed. EG DSL AddressCheck
     *
     * @brand {IBrand} current brand
     */
    private renderAdditional(brand: IBrand): JSX.Element {
        let ctrl = <></>;

        /**
         * show the DSL address check if current brand is DSL and check not executed
         */
        ctrl = <>
            {this.renderBrand(brand)}
        </>;

        return ctrl;
    };

    private renderBrand(brand: IBrand): JSX.Element {
        let avl = StorageHelper.getAvailability();
        let ctrl = <></>;
        let items: JSX.Element[] = [];

        if (this.current) {
            if (this.current.brandKey === BrandKeys.dsl || this.current.brandKey === BrandKeys.kabel) {
                items.push(<GiggacombiBanners bookOption={GigacombiBookOptions.undefined} parent={this} advertisementBanner={true} />)
            }

            if (this.current.brandKey == BrandKeys.otelo ||
                this.current.brandKey == BrandKeys.vodafone ||
                this.current.brandKey == BrandKeys.dsl ||
                this.current.brandKey == BrandKeys.kabel ||
                this.current.brandKey == BrandKeys.unity) {
                items.push(<act.ActivationBlockSimple product={this.product} parent={this} />);

                if ((this.current.activationType === EActivationType.portDefault) && this.current.brandKey !== BrandKeys.dsl && this.current.brandKey !== BrandKeys.unity) {
                    items.push(<act.ActivationMnp parent={this} onChange={this.onInputData} />);
                };

                //if(this.current.productId) {
                if (this.current.brandKey == BrandKeys.otelo) {
                    items.push(<act.ActivationMsisdn parent={this} onChange={this.onInputData} />);
                } else if (this.current.brandKey == BrandKeys.vodafone) {
                    items.push(<act.ActivationSim parent={this} onChange={this.onInputData} />);
                } else {
                    if (this.current.activationType === EActivationType.portDefault) {
                        if (this.current.brandKey != BrandKeys.unity) {
                            items.push(<act.PortingOptions parent={this} onChange={this.onInputData} />);

                            if (this.data.customer.mnp && this.data.customer.mnp.numberPortingType != ENumberPortingType.later) {
                                items.push(<act.ActivationTel numbersToPort={this.data.customer.mnp && this.data.customer.mnp.countNumbers ? this.data.customer.mnp.countNumbers : 1} parent={this} onChange={this.onInputData} />)
                            };
                        }
                    };
                }
                //}
            };

            if (this.current.brandKey == BrandKeys.otelo || this.current.brandKey == BrandKeys.vodafone) {
                items.push(<act.ActivationTariff
                    onChange={this.onInputData}
                    selectionChanged={this.activationChanged}
                    tariff={this.selectedProduct}
                    parent={this}
                    selection={this.selection} />);

                if (this.current.creditDebitType === ECreditDebitType.postpaid) {
                    items.push(<act.ActivationHardware preview={false} activation={this.current} onChange={this.onInputData} />);
                };

            } else {
                items.push(<act.ActivationTariff selectionChanged={this.activationChanged}
                    onChange={this.onInputData}
                    tariff={this.selectedProduct} parent={this} selection={this.selection} />);
            }

            if (avl && avl.hasKabel() || avl.hasDSL() && this.current!.brandKey != BrandKeys.unity) { // show only after avilability check and the user can book kabel and dsl
                items.push(<GiggacombiBanners data={this.dataWrapper} bookOption={this.currentBookedOption} onCategoryChanged={this.onCategoryChange} parent={this} advertisementBanner={false} />);
            }
        }
        ctrl = <>{items}</>

        return ctrl;
    };

    private renderCustomer(): JSX.Element {
        let ctrl = (
            <>
                <ActivationCustomerData parent={this} customer={this.data.customer} />
            </>
        );

        return ctrl;
    };

    private renderPreview(): JSX.Element {
        let ctrl = (
            <>
                {/* <page.Inner1C> */}
                <page.Inner2CLeft>
                    <>
                        <act.ActivationPreview parent={this} order={this.data} preview={false} />

                        <act.ActivationActions parent={this} progress={this.getProgress()} />
                    </>
                </page.Inner2CLeft>
                <></>
                {/* </page.Inner1C> */}
            </>);

        return ctrl;
    };

    private renderPreviewPrint(): JSX.Element {
        let ctrl = (
            <>
                <page.Inner1C>
                    <page.Inner2CLeft>
                        <>
                            <act.ActivationPreview parent={this} order={this.data} preview={true} />
                        </>
                    </page.Inner2CLeft>
                    <></>
                </page.Inner1C>
            </>);

        return ctrl;
    };

    private renderProcess(): JSX.Element {

        this.dataWrapper = new OrderDataWrapper(this.data);
        let cfg = Global.getConfiguration();
        this.selectedBrand = this.data.currentBrand;

        if (this.data.activations.length > 0) {
           
            this.current = this.data.activations[this.currentIndex];
           
            this.selectedBrand = !this.current ? BrandKeys.vodafone : this.current.brandKey;
            this.selectedProduct = this.current ? this.current.productId : '';

            if(this.dataWrapper.hasPartnerCard() && this.selection.activationType != EActivationType.partnerCard) {
                this.selection.activationType = EActivationType.partnerCard;
            };
        } else {
            this.current = this.startNewActivation();
        }

        //if more than one product in current selection then activate mode ALLE
        if (this.current && this.current.brandKey == BrandKeys.kabel && this.current.isMultiple()) {
            Global.categoryWrapper.all = true;
        }

        if (!this.selectedBrand) {
            this.selectedBrand = linq.from<IBrand>(this.defcon.brands).first(x => x.available).id;
        };

        let pr: EOrderProcessView = this.getProgress();

        if (pr === EOrderProcessView.tariff) {
            let brand = OrderingHelper.getBrand(this.selectedBrand);

            this.product = Enumerable.from<IProduct>(brand.products)
                .where(x => x.id === this.selectedProduct)
                .firstOrDefault();

            if (this.product) {
                Global.categoryWrapper.switchCat(this.product.catKey);
                //Global.categoryWrapper.all = this.current!.isMultiple();
                this.data.currentCat = this.product.catKey;
                this.data.currentBrand = this.selectedBrand;
            }
        }

        let sub: JSX.Element = <act.ActivationProgress progress={this.state.progress}
            states={[OrderStages.tariff, OrderStages.customer, OrderStages.done]} />;

        if (Global.easyApp.head) {
            Global.easyApp.head.appendSub(sub);
        };

        let dyn: JSX.Element = <></>;

        if (this.dynamicPopup) {
            this.dynamicPopup.props.onDismiss = () => {
                this.dynamicPopup = undefined;
                this.setState({ changed: true, processing: false, closablePopup: false });
            };

            dyn = GlobalJSX.GetPopup(this.dynamicPopup.props, this.dynamicPopup.content, this.dynamicPopup.action);
        };

        let a: JSX.Element = <cmp.Action>
            <></>
            <page.LButton text={MainTexts.close} css="fr" icon="ok" type={m.ELButtonType.buttonWithIconRight} onClick={() => {
                this.modelState.reset();
                this.setState({ changed: true });
            }} />
        </cmp.Action>

        if (this.modelState.errorMessage.indexOf('Komfort-Anschluss') > 0) {
            a = <cmp.Action>
                <page.LButton text={MainTexts.back} css="fl" icon="cancel" type={m.ELButtonType.linkWithIconLeft} onClick={() => {
                    this.modelState.reset();
                    this.setState({ changed: true });
                }} />
                <page.LButton text="Weiter mit Komfort-Anschluss" css="fr" icon="ok" type={m.ELButtonType.buttonWithIconRight} onClick={() => {
                    if (this.current) {
                        this.current.services.push('svc_dsl_comfort');
                        StorageHelper.updateActivation(this.current);
                        this.gotoView(EOrderProcessView.customer);
                    }
                }} />
            </cmp.Action>
        }

        let ctrl = (
            <div id={ProcessNewScreen.containerId}>
                {this.renderPage(pr)}

                {dyn}

                <cmp.Popup
                    type={this.processPopupState}
                    visible={this.state.processing || this.state.closablePopup}
                    closable={this.state.closablePopup}
                    id="wait"
                    title={MainTexts.pleaseWait}
                    height={200}
                    onDismiss={this.onDismissProcessPopup}>
                    <cmp.PopupBody>
                        <div dangerouslySetInnerHTML={{ __html: this.state.apiMessage }} />
                        <></>
                    </cmp.PopupBody>
                    <></>
                </cmp.Popup>

                <cmp.Popup
                    type={EPopupType.custom}
                    visible={!this.modelState.isValid()}
                    closable={true}
                    id="validate"
                    title={ValidationMessages.titleValidationError}
                    height={200}
                    onDismiss={this.onDismissValidation}>
                    <cmp.PopupBody>
                        <div>{this.modelState.errorMessage}</div>
                        <page.LClear />
                    </cmp.PopupBody>
                    {a}
                </cmp.Popup>
            </div>
        );

        return ctrl;
    };
};

export default ProcessNewScreen;