import linq from 'linq';
import * as m from '../models/index';
import StorageHelper from './storage';
import { BrandKeys } from './Defaults';
import { AvailabilityProduct, IAvailabilityBrand, IAvailabilityService } from '../models/ordering/availability';
import IProduct from '../models/ordering/product';
import IBrand from '../models/ordering/brand';
import ISub from '../models/ordering/sub';
import IService from '../models/ordering/service';
import { EServiceType, EBusinessCase, EActivationType } from '../models/ordering/order';
import { MainTexts } from './messages';

export class OrderingHelper {
    public static filterProducts(brandKey: string, 
        products: IProduct[],
        bc: EBusinessCase) : IProduct[] {
        let prod = linq.from<IProduct>(products).toArray();

        let avl: m.Availability = StorageHelper.getAvailability();
        /** 
         * for DSL, kabel, unity products we apply additional filter for the given address
        */
        if(brandKey === BrandKeys.dsl || brandKey === BrandKeys.kabel || brandKey == BrandKeys.unity) {
            let avlBrand = avl.getBrand(brandKey);
            let avlProd = avl.getAvailableProducts(brandKey, avlBrand);

            if(avl && avlProd) {
                let prid = avlProd;

                prod = linq.from<IProduct>(prod)
                    .where(x=> prid.indexOf(x.id) > -1 )
                    .toArray();

                if(brandKey === BrandKeys.kabel) {
                    prod.map((i)=>{
                        let p = linq.from<AvailabilityProduct>(avlBrand.products)
                            .where(x=>x.productId === i.id)
                            .firstOrDefault();
                        if(p) {
                            let el = linq.from<IAvailabilityService>(p.elements)
                                .where(x=>x.metaname === 'rufnummernmitnahme-1')
                                .firstOrDefault();
                            if(el) {
                                i.activationType |= EActivationType.portDefault;
                            };
                        }
                    });
                };
            } else {
                prod = [];
            }
        } else {
            prod = products;
        }

        if(bc != EBusinessCase.undefined) {
            prod = linq.from<IProduct>(prod)
                .where(x=>x.businessCases.indexOf(bc) >=0 )
                .toArray();
        };

        return prod;
    };

    public static filterServices(brand: IBrand, act: m.IActivation, type: EServiceType) : string[] {
        if(brand.id == BrandKeys.kabel) {
            return OrderingHelper.filterServicesFromAvailability(brand, act, type);
        };

        let res: string[] = [];
        var svcAll = linq.from<IService>(brand.services)
            .where(x=> ((type & x.type) === x.type))
            .toArray();

        var svcSel = linq.from<IService>(brand.services)
            .join(act.services, s=>s.id, a=>a, (s,a) => { 
                return {
                    S: s,
                    A: a
                };
             })
            //.where(x=>!x.S.hidden)
            .select(x=>x.S)
            .toArray();
            
    //console.log('svcSel', svcSel);
    //console.log('svcAll', svcAll);

        svcAll.map((i,idx)=>{
            let add: boolean = true;
            for(let a=0;a<svcSel.length;a++) {
                if(svcSel[a].notbookableWith && svcSel[a].notbookableWith.indexOf(i.id)>=0){
                    add=false;
                    break;
                }
            }

            if(add) {res.push(i.id);}
        });

        return res;
    };

    public static filterServicesFromAvailability(brand: IBrand, act: m.IActivation, type: EServiceType) : string[] {
        let res: string[] = [];
        let avl: m.Availability = StorageHelper.getAvailability();
        // var avl: IAvailabilityBrand = linq.from<IAvailabilityBrand>(StorageHelper.getAvailability().brands)
        //     .where(x=>x.key == brand.id)
        //     .firstOrDefault();

        var pr:AvailabilityProduct = avl.getProduct(brand.id, act.productId);
        // linq.from<AvailabilityProduct>(avl.getAvailableProducts(brand.id))
        //     .where(x=>x.productId == act.productId)
        //     .first();

        var svcAll = linq.from<IAvailabilityService>(pr.elements)
            .where(x=> x.available == true)
            .toArray();

        var svcSel = linq.from<IService>(brand.services)
            .join(act.services, s=>s.id, a=>a, (s,a) => { 
                return {
                    S: s,
                    A: a
                };
             })
            //.where(x=>!x.S.hidden)
            .select(x=>x.S)
            .toArray();

        svcSel.map((i,idx)=>{
            let el = linq.from<IAvailabilityService>(pr.elements).where(x=>x.metaname === i.id).firstOrDefault();
            if(el) {
                i.notbookableWith = el.excludes;
            };
        });

        svcAll.map((i,idx)=>{
            let add: boolean = true;
            for(let a=0;a<svcSel.length;a++) {
                if(svcSel[a].notbookableWith && svcSel[a].notbookableWith.indexOf(i.metaname)>=0){
                    add=false;
                    break;
                }
            }

            if(add) {res.push(i.metaname);}
        });

        return res;
    };    

    public static getSub(brand: IBrand, id: string) : ISub  {
        if(!id && brand.id === BrandKeys.vodafone) {
            id = 'OS';  
        };
        let sub = linq.from<ISub>(brand.subs)
            .where(x=>x.id === id)
            .first();

        return sub;
    };    

    public static getSubName(brand: IBrand, id: string, fallback: string ) : string  {
        if(!id && brand.id === BrandKeys.vodafone) {
            return fallback;
        };

        let sub = linq.from<ISub>(brand.subs)
            .where(x=>x.id === id)
            .firstOrDefault();

        if(sub) {
            return sub.name;
        } else {
            return MainTexts.na;
        };
    };        

    public static getBrand(key: string) : IBrand {
        return linq.from<IBrand>(StorageHelper.getBrands())
            .where(x=> x.id === key) 
            .first();        
    };
};

export default OrderingHelper;