import * as React from 'react';
import Inner2CRight from './inner2right';
import Inner2CLeft from './inner2left';
import { Row, Col, Container } from 'react-bootstrap';
import IInnerProps from './IInnerProps';

export interface IHeaderState {
    changed: boolean;
};

export class Inner2C extends React.Component<IInnerProps, any>{
    constructor(props: IInnerProps){
        super(props);
    }

    public render() {
        let ctrl = ( 
            <Container className="c2" id="container-main">
                {this.props.title ?
                    (
                    <Row>
                        <Col xs={12} md={12}>
                            <h1>{this.props.title}</h1>
                        </Col>
                    </Row>
                    ) : null 
                }
                <Row>
                    <Col xs={8} md={8}>
                        {this.props.children[0]}
                    </Col>
                    <Col xs={4} md={4}>
                        {this.props.children[1]}
                    </Col>
                </Row>
            </Container>
        );
        return ctrl;
        /*
        return <div className="wrapper">
            {this.props.children}
            <div className="clear"/>
        </div>
        */
    }
};

export default Inner2C;