import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBaseComponentState } from '../../../interfaces';
import { ConsumptionItem, EControlType, ELButtonType, OrderDataWrapper } from '../../../models';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { IContract } from '../../../models/existing/contract';
import { EActivationType, EBusinessCase } from '../../../models/ordering/order';
import CustomerScreen from '../../../screens/existing/customer';
import ProgressBar from '../../../screens/test/ProgressBar';
import { BrandKeys } from '../../../utils/Defaults';
import Global from '../../../utils/Global';
import { MainTexts, OrderingTexts } from '../../../utils/messages';
import OrderingHelper from '../../../utils/OrderingHelper';
import EasyRouter from '../../../utils/router';
import StorageHelper from '../../../utils/storage';
import { LControl } from '../../page';
import BaseBlock from '../../page/block';
import * as page from '../../page/index';
import ExistingPartnerCard from './partnerCard';

export interface IExistingContractDataProps {
    parent: CustomerScreen;
};

export class ExistingContractData extends React.Component<IExistingContractDataProps, IBaseComponentState> {
    private contract: IContract;
    constructor(props: IExistingContractDataProps) {
        super(props);

        this.state = {
            changed: false
        };

        this.contract = this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0] : {};
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.contractData,
            transparent: false,
            style: '',
            styleContainer: '',
            collapsible: true,
            collapsed: false
        };

        let consumption: ConsumptionItem | undefined = this.contract.consumption && this.contract.consumption.data ? this.contract.consumption.data : undefined;

        if (consumption) {
            consumption = Object.assign(new ConsumptionItem(), consumption);
        }

        let ctrl =
            <Container>
                {
                    this.contract.brandKey === BrandKeys.vodafone ?
                        <>
                            <Row>
                                <Col>
                                    <ExistingPartnerCard parent={this.props.parent} />
                                </Col>
                            </Row>
                            <Row>
                                <Col className="np">
                                    <div className="spacer-line" />
                                    <div className="spacer-medium" />
                                </Col>
                            </Row>
                        </>
                        : null
                }
                <Row>
                    {this.contract.brandKey === BrandKeys.otelo || this.contract.brandKey === BrandKeys.dsl  ? <Col md={4}>
                        <LControl type={EControlType.info} style="merged" title={OrderingTexts.contractStart}
                            label={Global.toGermanDate(this.contract.contractFirstActivation)} />
                    </Col> : <></>}
                    {
                        this.contract.brandKey === BrandKeys.vodafone ?
                            <>
                                <Col md={4}>
                                    <LControl type={EControlType.info} style="merged" title={OrderingTexts.contractStart}
                                        label={Global.toGermanDate(this.contract.contractStart)} />
                                </Col>
                                <Col md={4}>
                                    <LControl type={EControlType.info} style="merged" title={OrderingTexts.firstActivation}
                                        label={Global.toGermanDate(this.contract.contractFirstActivation)} />
                                </Col>
                                <Col md={4}>
                                    <LControl type={EControlType.info} style="merged" title={OrderingTexts.contractEnd}
                                        label={Global.toGermanDate(this.contract.contractEnd)} />
                                </Col>
                            </> : <></>

                    }

                    {
                        this.contract.brandKey === BrandKeys.otelo ?
                            <>
                                <Col md={4}>
                                    <LControl type={EControlType.info} style="merged" title={MainTexts.contractId}
                                        label={this.contract.contractId} />
                                </Col>
                            </>
                            : null
                    }

                    <Col md={4}>
                        <LControl type={EControlType.info} style="merged" title={OrderingTexts.sub}
                            label={OrderingHelper.getSubName(this.props.parent.brand, this.contract.bookedProduct ? this.contract.bookedProduct.sub : '', this.contract.bookedProduct ? this.contract.bookedProduct.id : MainTexts.na)} />
                    </Col>

                    {
                        this.contract.status ?
                            <Col md={4}>
                                <LControl type={EControlType.info} style="merged" title={OrderingTexts.customerStatus}
                                    label={Global.getCustomerStatus(this.contract.status.status)} />
                            </Col>
                            :
                            null
                    }

                    {
                        this.contract.brandKey === BrandKeys.vodafone ?
                            <Col md={4}>
                                <LControl type={EControlType.info} style="merged" title={OrderingTexts.countCards}
                                    label={this.contract.countCards !== undefined ? this.contract.countCards.toString() : "0"} />
                            </Col>
                            :
                            null
                    }

                    {
                        consumption ?
                            <Col md={6} className="np">
                                <div className="spacer-big" />
                                <page.LLabel text="Status Datenverbrauch" css="title" />
                                <ProgressBar maxValue={100} initialValue={consumption.getValueReverse()}
                                    label={'[' + consumption.getLabel() + ']'} />
                            </Col>
                            : null
                    }
                </Row>

                {
                    this.contract.brandKey === BrandKeys.vodafone ?
                        <>
                            <Row>
                                <Col className="np">
                                    <div className="spacer-medium" />
                                    <div className="spacer-line" />
                                    <div className="spacer-medium" />
                                </Col>
                            </Row>
                            {
                                this.props.parent.dataWrapper.hasPartnerGroups() ?
                                    <Row>
                                        <Col md={12} className="text-sub text-desc">{OrderingTexts.moreOptions}</Col>
                                        <div className="spacer" />
                                        <Col md={12} className="text-sub text-desc">
                                            {this.renderOptionalSimCards()}
                                        </Col>
                                    </Row>
                                    : null
                            }
                        </>
                        : null
                }
            </Container>;

        ctrl = <BaseBlock config={cfg}>
            {ctrl}
        </BaseBlock>;

        return ctrl;
    };

    private renderOptionalSimCards(): JSX.Element {
        let ctrl = <></>;

        if (this.contract.brandKey !== BrandKeys.vodafone) {
            return ctrl;
        };

        let but = <page.LButton type={ELButtonType.buttonWithIconRight}
            icon="print"
            css="a1"
            id="buttonRequestContract"
            text={OrderingTexts.bookNow}
            onClick={() => {
                let d = OrderDataWrapper.createProcess(EBusinessCase.bnt,
                    EActivationType.partnerCard,
                    this.props.parent.data,
                    undefined);

                d.activations[0].additional = true;
                d.activations[0].msisdn = '';
                if (this.props.parent.data.customer.contracts && this.props.parent.data.customer.contracts[0].customerId) {
                    d.activations[0].customerId = this.props.parent.data.customer.contracts[0].customerId;
                }

                StorageHelper.addOrder(d);

                this.props.parent.refresh();
                EasyRouter.route({ url: '/ordering/new/tariff/' + d.id, title: OrderingTexts.bookPartner });
                this.props.parent.refresh();
            }} />;

        ctrl = (
            <Container>
                <Row>
                    <Col md={12} className="np">
                        {/* <page.BlockInfo title={OrderingTexts.bookMulticard} 
                        style="thumb-sim"
                        button={but}>
                        <></>
                        <div>
                            {OrderingTexts.bookMulticardDesc}
                        </div>
                    </page.BlockInfo> */}
                        {
                            this.props.parent.dataWrapper.hasPartnerGroups() ?
                                <page.BlockInfo title={OrderingTexts.bookPartner}
                                    style="thumb-sim"
                                    button={but}>
                                    <></>
                                    <div>
                                        {OrderingTexts.bookMulticardDesc}
                                    </div>
                                </page.BlockInfo>
                                : <></>
                        }

                    </Col>
                </Row>
            </Container>
        );

        return ctrl;
    };
};

export default ExistingContractData;