import * as React from 'react';
import {App} from '../../App';
import CategoryWrapper from '../../components/tools/categoryWrapper';
import { IApiResponse } from '../../interfaces';
import { ERequestType } from '../../models';
import { IUIDataModel } from '../../models/environment/uidata';
import * as dc from '../../models/global/defcon';
import Api from '../../utils/api';
import { HttpMethods, StorageKeys } from '../../utils/Defaults';
import Global from '../../utils/Global';
import jsx from '../../utils/GlobalJSX';
import StorageHelper from '../../utils/storage';

export interface IInitScreenProps {
    parent: App;
};

export class InitScreen extends React.Component<IInitScreenProps, any> {
    public static containerId: string = 'screenInit';
    private interval;
    constructor(props: IInitScreenProps) {
        super(props);

        this.socketCallback = this.socketCallback.bind(this);
    };

    componentDidMount() {
        Api.init();

        Api.fetch('environment/dc', undefined, HttpMethods.get, (api: IApiResponse) => {
            Api.parseDefcon(api, (res: dc.IDefCon) => {

                StorageHelper.set(StorageKeys.keyDefCon, api.data);
                if (res && (res.state & dc.EAppState.ok) !== dc.EAppState.ok) {
                    this.props.parent.refresh({});

                    return;
                };

                Global.init();

                this.interval = setInterval(() => {
                    if (Global.hub.started) {
                        clearInterval(this.interval);
                        Global.hub.send('', ERequestType.startApp, undefined);
                    }
                }, 1000);
            })
        });
    };

    public render() {
        let ctrl = <div id={InitScreen.containerId}>
            {jsx.GetLoader()}
        </div>;

        return ctrl;
    };

    public socketCallback(api: IApiResponse) {
        let model: IUIDataModel = api.data;

        StorageHelper.set(StorageKeys.keyUser, model.user);
        StorageHelper.set(StorageKeys.keyBrands, model.brands);
        StorageHelper.set(StorageKeys.keySupport, model.support);
        StorageHelper.set(StorageKeys.keyCombis, model.combis);

        // in case of defcon allow brands for voids to test
        let d = StorageHelper.getDefCon();
        if(d.state != dc.EAppState.ok && model.user.isAuthenticated && d.voidAllowed && d.voidAllowed.indexOf(model.user.voId)>=0) {
            d.brands.map((i)=>{
                i.available = true;
            });
            StorageHelper.set(StorageKeys.keyDefCon, d);
        };

        Global.categoryWrapper = new CategoryWrapper();

        this.props.parent.refreshAndRoute({
            url: window.location.href
        });
    };
};

export default InitScreen;