import * as React from 'react';
import { IBlockConfiguration } from '../../models/block/configuration';
import { MainTexts, DealerMessages } from '../../utils/messages';
import BaseBlock from '../../components/page/block';
import { Row, Col, Container } from 'react-bootstrap';
import { LClear } from '../../components';
import * as page from '../../components/page/index';
import { ELButtonType, ETelType, ERequestType, EControlType } from '../../models';
import { IControlValue } from '../../models/page/interfaces';
import { Tel as TelModel } from '../../../src/models/ordering/tel';
import Tel from '../../components/ordering/global/tel';
import { Address as AddressModel } from '../../../src/models/ordering/address';
import Address from '../../components/ordering/global/address';
import Global from '../../utils/Global';
import { IApiResponse } from '../../interfaces';
import { EPopupType } from '../../components/dialogs/popup';
import * as cmp from '../../components/index';
import { AdministrationScreen } from '../insex';

export interface DealerModel {
    tel: TelModel;
    address: AddressModel,
    fnvoid: string;
    vpkn: string;
    voId: string;
}

export interface IDealerProps {
    model?: DealerModel;
    parent?: AdministrationScreen;
}
export interface IDealerState {
    model: DealerModel;
    popupTitle: string;
    processing: boolean;
    closblePopup: boolean;
    apiMessage: string;
}

class DealerScreen extends React.Component<IDealerProps, IDealerState> {
    private processPopupState: EPopupType = EPopupType.pleaseWait;
    public static containerId: string = 'dealerScreen';

    constructor(props) {
        super(props);

        this._onSave = this._onSave.bind(this);
        this._onChange = this._onChange.bind(this);
        this.onDismissProcessPopup = this.onDismissProcessPopup.bind(this);
        this.showPopup = this.showPopup.bind(this);

        this.state = {
            model: this.props.model || {
                tel: { prefix: '', telType: ETelType.home, number: '', countryCode: '+49' },
                address: { city: '', zip: '', street: '', nr: '', corrected: false, house_number: '', additional: '' },
                fnvoid: '',
                vpkn: '',
                voId: ''
            },
            closblePopup: false, apiMessage: '', processing: false, popupTitle: ''
        };

        if (this.props.model === undefined) {
            Global.hub.send(JSON.stringify({ id: '' }),
                ERequestType.loadAccount,
                undefined);
        }
    };

    render() {
        const cfg: IBlockConfiguration = {
            title: this.props.model === undefined ? DealerMessages.dealerEdit : "Abgleich deiner Stammdaten",
            transparent: false,
            styleContainer: ''
        };

        return <div id={DealerScreen.containerId}>
            <page.Inner1C>
                <BaseBlock config={cfg}>
                    <cmp.Popup
                        type={this.processPopupState}
                        visible={this.state.processing || this.state.closblePopup}
                        closable={this.state.closblePopup}
                        id="wait"
                        title={this.state.popupTitle}
                        height={200}
                        onDismiss={this.onDismissProcessPopup}>
                        <cmp.PopupBody>
                            <div>{this.state.apiMessage}</div>
                            <></>
                        </cmp.PopupBody>
                        <></>
                    </cmp.Popup>
                    <Container className="container-action">
                        {this.props.model === undefined ? <></> : <strong>Bitte berprfe und korrigiere Kontaktdaten sorgfltig. Daten werden auf Vertragsdokumenten verwended.</strong>}
                        <Address onChange={this._onChange} source={this.state.model} prefix="address" preview={false} />
                        <Tel countryCodeVisible={true} telTypeHidden={this.props.model !== undefined} onChange={this._onChange} source={this.state.model} prefix="tel" preview={false} />

                        <Row>
                            <Col xs={4} md={4}>
                                <page.LControl type={EControlType.text}
                                    value={this.state.model.voId}
                                    name="voId" id="voId"
                                    label={"VOID"} preview={true} />
                            </Col>
                            <Col xs={4} md={4}>
                                <page.LControl type={EControlType.text}
                                    value={this.state.model.fnvoid}
                                    onChange={this._onChange}
                                    name="fnvoid" id="fnvoid"
                                    label={MainTexts.fnvoid} preview={true} />
                            </Col>
                            <Col xs={4} md={4}>
                                <page.LControl type={EControlType.text}
                                    value={this.state.model.vpkn}
                                    onChange={this._onChange}
                                    name="vpkn" id="vpkn"
                                    label={MainTexts.vpkn} preview={true} />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={6} md={6} >
                                {this.props.parent !== undefined ?
                                    <page.LButton
                                        text={MainTexts.cancel}
                                        onClick={this.props.parent.closeEditScreen}
                                        type={ELButtonType.linkSimple}
                                        css={"fl"}
                                        icon="chevron" />

                                    : <page.LButton
                                        text={MainTexts.cancel}
                                        onClick={() => window.history.back()}
                                        type={ELButtonType.linkSimple}
                                        css={"fl"}
                                        icon="chevron" />}
                            </Col>
                            <Col xs={6} md={6} >
                                <page.LButton
                                    text={this.props.model === undefined ? MainTexts.save : MainTexts.takeOver}
                                    onClick={this._onSave}
                                    type={this.props.model === undefined ? ELButtonType.linkSimple : ELButtonType.buttonWithIconRight}
                                    css={this.props.model === undefined ? "action fr" : "fr"}
                                    icon="chevron" />
                            </Col>
                        </Row>
                    </Container>
                    <LClear />
                </BaseBlock>
            </page.Inner1C>
        </div >;
    }

    public loadAccountCallback(api: IApiResponse) {
        const dataRaw: any = api.data.data;
        const dealer = dataRaw.dealer;

        let model: DealerModel = {
            tel: { countryCode: dealer.tel.cc, prefix: dealer.tel.ndc, number: dealer.tel.sn, telType: ETelType.home },
            address: { city: dealer.address.city, zip: dealer.address.zip, street: dealer.address.street, nr: dealer.address.nr, house_number: '', additional: '', corrected: false },
            fnvoid: dataRaw.fnvoid,
            vpkn: dataRaw.vpkn,
            voId: dataRaw.voId
        };
        this.setState({ model: model });
    }

    public saveAccountCallback(api: IApiResponse) {
        if (api.error) {
            this.showPopup(EPopupType.info, MainTexts.textError, api.error.message, true);
        } else {
            this.showPopup(EPopupType.info, MainTexts.saveSuccess, '', true);
        }
    }

    private _onChange(e: IControlValue) {
        let { model } = this.state;
        Global.onInput(model, e, () => {
            this.setState({ model });
        });
    }

    private _onSave() {
        const stateModel = this.state.model;
        const model = {
            dealer: {
                tel: { cc: stateModel.tel.countryCode, ndc: stateModel.tel.prefix, sn: stateModel.tel.number },
                address: { city: stateModel.address.city, zip: stateModel.address.zip, street: stateModel.address.street, nr: stateModel.address.nr }
            },
            fnvoid: stateModel.fnvoid,
            vpkn: stateModel.vpkn,
            voId: stateModel.voId
        };

        Global.hub.send(JSON.stringify(model),
            ERequestType.saveAccount,
            undefined);
    }

    private showPopup(type: EPopupType, title: string, message: string, inprocess: boolean) {
        this.processPopupState = type;
        this.setState({
            apiMessage: message,
            closblePopup: true,
            processing: inprocess,
            popupTitle: title
        });
    }

    private onDismissProcessPopup() {
        this.setState({ processing: false, closblePopup: false });
    }
}

export default DealerScreen;
