export class MobileNumber {
    NDC:string = '';

    MSISDN:string = '';

    public toString() : string {
        return this.NDC + this.MSISDN;
    };

    public static createInstanceFromJson(mn: MobileNumber|undefined) : MobileNumber {
        if(mn) {
            return Object.assign(new MobileNumber(), mn);
        } else {
            return new MobileNumber();
        };
    };
};