import * as React from 'react';
import { ELElementStatus } from '../../models/page/diverse';
import { ILElementProps, ILElementState } from '../../models/page/interfaces';

export class LLabel extends React.Component<ILElementProps, ILElementState> {

    constructor(props: ILElementProps) {
        super(props);

        this._onClick = this._onClick.bind(this);

        this.state = {
            status: this.props.status ? this.props.status : ELElementStatus.normal
        };
    };

    public render() {
        let css = '';
        if(this.props.css) {
            css += ' ' + this.props.css;
        }
        let ctrl = (
            <label onClick={this._onClick} className={css}>
                {this.props.text}
            </label>
        );

        return ctrl;
    };

    private _onClick() {

    };
};

export default LLabel;