import { BrandKeys } from '../Defaults';
import * as o from '../../models/ordering/data';
import linq from 'linq';
import { IActivation } from "../../models";
import {IVisibilityConfiguration} from './visibilityConfiguration';
import { EActivationType } from '../../models/ordering/order';

export class VisibilityOrdering {
    private static config: IVisibilityConfiguration[] = [
        {name: "nationality", brands: [BrandKeys.otelo], activationTypes: [EActivationType.actDefault]},
        {name: "firstname", brands: [BrandKeys.otelo, BrandKeys.dsl, BrandKeys.kabel, BrandKeys.vodafone], activationTypes: [EActivationType.actDefault]}
    ];

    constructor() {
    };

    public static checkVisibility(name: string, order: o.OrderDataWrapper) : boolean {
        let cfg = linq.from<IVisibilityConfiguration>(this.config)
            .where(x=>x.name === name)
            .firstOrDefault();
        
        if(!cfg) { return true; }

        for(let a of order.data.activations) {
            if(cfg.brands.indexOf(a.brandKey)>=0)  {
                return true;
            }
        }

        return false;
    };
};

export default VisibilityOrdering;