import * as React from 'react';
import { IBaseComponentState } from '../../interfaces';
import { IBlockConfiguration } from '../../models/block/configuration';
import BaseBlock from '../../components/page/block';
import { Container, Row, Col } from 'react-bootstrap';
import LClear from './clear';

export interface IErrorBlockProps  {
    visible: boolean;
    title: string;
    message: string;
};

export class ErrorBlock extends React.Component<IErrorBlockProps, IBaseComponentState> {
    constructor(props) {
        super(props);

        this.state = {
            visible: this.props.visible,
            changed: false
        };
    };

    public componentWillReceiveProps(next: IErrorBlockProps) {
        this.setState({visible: next.visible});
    };

    public render() {
        let ctrl = <></>;

        let cfg: IBlockConfiguration = {
            title: this.props.title,
            transparent: false,
            styleContainer: 'error'
        };

        ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    <Row>
                        <Col xs={12} md={12}>
                            <div dangerouslySetInnerHTML={{__html:this.props.message}}/>
                        </Col>                      
                    </Row>                                
                </Container>
                <LClear />
            </BaseBlock>
        );

        return ctrl;
    };
};

export default ErrorBlock;