import React, { Component, Children } from 'react';
import * as ReactDOM from 'react-dom';
import * as page from '../../components/page';
import { AppIds } from '../../utils/Defaults';
import { ERequestStatus, IDefCon, EAppState } from '../../models';
import StorageHelper from '../../utils/storage';
import { DefConScreen } from '../insex';

export interface IScreenState {

};

export interface IScreenProps {
    blank: boolean;
    content?: JSX.Element;
};

export class MainScreen extends React.Component<IScreenProps, IScreenState> {
    private jsxLoader: JSX.Element = <></>;

    constructor(props) {
        super(props);

        this.state = {
            loaderVisible: false,
            loaderText : '',
            loaderState: ERequestStatus.undefined
        };
    };

    componentWillReceiveProps() {
        this.setState({changed: true});
    };

    public render() {
        let ctrl = (
            this.props.blank ?
                <div id={AppIds.container}>
                    {
                        this.props.content ? this.props.content : null
                    }
                </div> 
            :
            <>
                <page.Head {...{changed:false}}/>

                <div id={AppIds.container}>
                    {this.renderDefCon()}
                </div>                
                <page.Footer {...{changed:false}}/>
                <page.UserPannel {...{changed:false}} parent={this} />
                <div className="spacer-huge"></div>
            </>
        );

        return ctrl;
    }; 

    private renderDefCon() : JSX.Element {
        let dc: IDefCon = StorageHelper.getDefCon();

        if((dc.state & EAppState.ok) !== EAppState.ok) {
            return <DefConScreen />
        } else {
            return <></>
        }
    };
};

export default MainScreen;