import * as React from 'react';
import CustomerScreen from '../../../screens/existing/customer';
import { IBaseComponentState } from '../../../interfaces';
import { Container, Row, Col } from 'react-bootstrap';
import { IBlockConfiguration } from '../../../models/block/configuration';
import BaseBlock from '../../page/block';
import { IContract } from '../../../models/existing/contract';
import { LControl } from '../../page';
import { EControlType, OrderDataWrapper, ICustomerAction, ECustomerActionType, ECustomerActionPurchase, ERequestType, ICustomerNba } from '../../../models';
import { MainTexts, OrderingTexts, ActionTexts, Hints } from '../../../utils/messages';
import BrandThumb, { EBrandThumbType, EBrandThumbStatus } from '../../ordering/brandThumb';
import StorageHelper from '../../../utils/storage';
import * as linq from 'linq';
import IBrand from '../../../models/ordering/brand';
import Global from '../../../utils/Global';
import TariffThumb from '../../ordering/tariffThumb';
import EasyRouter from '../../../utils/router';
import { EActivationType, EBonusType, EBusinessCase, EIDCardType } from '../../../models/ordering/order';
import { BrandKeys } from '../../../utils/Defaults';
import { IPopupConfig, EPopupType } from '../../dialogs/popup';
import IProduct from '../../../models/ordering/product';
import IService from '../../../models/ordering/service';

export interface IExistingCustomerNbaProps {
    parent: CustomerScreen;
    brand: IBrand;
};

export interface IExistingCustomerNbaState extends IBaseComponentState {
};

export class ExistingCustomerNba extends React.Component<IExistingCustomerNbaProps, IExistingCustomerNbaState> {
    private contract: IContract;
    constructor(props: IExistingCustomerNbaProps) {
        super(props);

        this.state = {
            changed: false
        };

        this.contract = this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0] : {};
    };

    public render() {
        if(!this.contract.nba || this.contract.nba!.length == 0) {
            return <></>
        };

        let cfg: IBlockConfiguration = {
            title: OrderingTexts.nba,
            transparent: false,
            style: '',
            styleContainer: '',
            collapsible: true,
            collapsed: false
        };

        let act: JSX.Element = <></>;

        if (this.contract.nba) {
            let aa = linq.from<ICustomerAction>(this.contract.nba!)
                .toArray();

            act = (
                <>
                    {aa.map((i, idx) =>
                        (<TariffThumb
                            text={i.title! }
                            id={'nba-' + (i.nbaId)}
                            idx={idx}
                            key={idx}
                            tooltip={this.getTooltip(i)}
                            cssStyle={"thumb-w2 nba-" + i.category }
                            brandKey={''}
                            selected={false}
                            styleBrand={true}
                            onClick={()=>{this.selectActivity(i)}} />)
                    )}
                </>
            );
        };

        let ctrl =
            <Container>
                <Row>
                    <Col md={12} className="np">
                        {act}
                    </Col>
                </Row>

            </Container>;

        ctrl = <BaseBlock config={cfg}>
            {ctrl}
        </BaseBlock>;

        return ctrl;
    };

    private getTooltip(nba: ICustomerNba) : string {
        let p = linq.from<IProduct>(this.props.brand.products).where(x=>x.id == nba.product).first();
        let html = '';

        if(nba.services && nba.services.length > 0) {
            nba.services.map((i, idx)=>{
                let s = linq.from<IService>(this.props.brand.services).where(x=>x.id == i).first();
                if(s) {
                    let fee = s.monthlyFee && s.monthlyFee.price ? s.monthlyFee : s.activationFee;
                    let price = fee.price != 0 ? Global.feeToString(fee) : '';
                    html += '<div class=\\\"bl text-h2 tar\\\">' + price + ' ' + s.familyName + '</div>'; 
                };
            })
            
        }

        html = p.tooltip.replace('<div class=\\\"nba dn\\\">$</div>', '<div class=\\\"nba\\\">' + html + '</div>');

        return html;
    };

    private selectActivity = (value: ICustomerAction) => {
        if (this.contract && this.contract.actions) {
            let d = OrderDataWrapper.createProcess(EBusinessCase.vvl, EActivationType.existingCustomer, this.props.parent.data, value);
            this.props.parent.refresh();
            EasyRouter.route({ url: '/customer/tariff/' + d.id, title: value.title + ' ' + OrderDataWrapper.getMsisdnFromContract(d) });
        }
    };
};

export default ExistingCustomerNba;