import * as React from 'react';
import { Row, Col, Container } from 'react-bootstrap';
import IInnerProps from './IInnerProps';

export class Inner1C extends React.Component<IInnerProps, any>{
    constructor(props: IInnerProps){
        super(props);
    }

    public render() {
        let css : string = "c1";
        if(this.props.style) { css += ' ' + this.props.style; };

        const child = this.props.children instanceof Array ? this.props.children[0] : this.props.children;

        let ctrl = ( 
            <Container className={css} id="container-main">
                {this.props.title ?
                    (
                    <Row>
                        <Col xs={12} md={12}>
                            <h1>{this.props.title}</h1>
                        </Col>
                    </Row>
                    ) : null 
                }
                <Row>
                    <Col xs={12} md={12}>
                        {child}
                    </Col>
                </Row>
            </Container>
        );
        return ctrl;
    }
};

export default Inner1C;