import * as React from 'react';

import { Container, Row, Col } from 'react-bootstrap';
import { LControl } from '../..';
import { MainTexts, OrderingTexts, OrderStages, Hints } from '../../../utils/messages';
import BaseBlock from '../../page/block';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { EControlType, ELButtonType, ERequestType, IActivationSim } from '../../../models';
import * as page from '../../page/index';
import TariffThumb from '../../ordering/tariffThumb';
import { CustomerScreen } from '../../../screens/insex';
import { EPopupType, IPopupConfig } from '../../dialogs/popup';
import Global from '../../../utils/Global';
import { IContract } from '../../../models/existing/contract';

export interface ISimReplacementProps {
    parent: CustomerScreen;
};

export interface ISimReplacementState {
    simSelected: boolean,
    actByCustomer: boolean,
    actUponReceipt: boolean,
    procedeButtonState: boolean
}

export class SimReplacement extends React.Component<ISimReplacementProps, ISimReplacementState> {
    private current: IActivationSim = { activateByCustomer: 1, name: '', price: { price: 0, unit: '' }, id: '' };
    private priceFormated: string = "";

    constructor(props: any) {
        super(props);

        this.state = {
            simSelected: false,
            actByCustomer: false,
            actUponReceipt: true,
            procedeButtonState: true

        };
    };

    public render() {
        var contracts: any = this.props.parent.data.customer.contracts;

        let a: JSX.Element = <>
            <page.LButton type={ELButtonType.linkSimple}
                text={MainTexts.backToCustomerDataSheet}
                css="action3 fr"
                onClick={() => {
                    this.props.parent.onBackToCustomer(false);
                }} />
        </>;
        let cfg: IBlockConfiguration = {
            title: MainTexts.orderReplacementSIM,
            transparent: false,
            style: '',
            collapsible: false,
            collapsed: false,
            action: a
        };

        let ctr = <></>;
        let cssStyle = "a1 fr";

        if(this.state.procedeButtonState===true){
            cssStyle += " disabled"
        }else{
            cssStyle = "ai fr"
        }

        ctr = (
            <div>
                <BaseBlock config={cfg}>
                    {
                        <Container >
                            <Row>
                                <Col md={4}>
                                    <LControl type={EControlType.info} style="merged"
                                        label={MainTexts.pleaseSelectSIM} />
                                </Col>
                            </Row>

                            <Row>
                                <Col md={4}>
                                    {contracts.map((i, idx) => (
                                        this.renderSim(i)))}
                                </Col>
                            </Row>

                            <Row>
                                <Col md={10}>
                                    <page.LControl type={EControlType.textArea}
                                        name=""
                                        id=""
                                        style={"black"}
                                    />
                                    <page.LControl type={EControlType.info}
                                        label={Hints.simReplaceHint} isHtml={true} />

                                </Col>
                            </Row>

                            <Row>
                                <Col md={6}>
                                    <page.LControl type={EControlType.radio}
                                        onChange={(e) => { this.activationChange(e) }}
                                        name=""
                                        id="ByCustomer"
                                        value={''}
                                        checked={this.state.actByCustomer}
                                        label={MainTexts.activationByCustomerUponReceipt} />
                                </Col>

                                <Col md={4}>
                                    <page.LControl type={EControlType.radio}
                                        onChange={(e) => { this.activationChange(e) }}
                                        name=""
                                        id="byOperator"
                                        value={''}
                                        checked={this.state.actUponReceipt}
                                        label={MainTexts.cardActivatedSent} />
                                </Col>
                            </Row>

                            <Row>
                                <Col md={10}>
                                    <page.LControl type={EControlType.textArea}
                                        name=""
                                        id=""
                                        style={"black"}
                                    /> {'Hinweis:'}<br />{'Durch diese Auswahl wird die aktuelle Sim-Karte des Kunden nach Bestellung sofort gesperrt.'}
                                    <br />{'Diese Auswahl ist empfehlenswert bei Diebstahl oder Verlust der Sim-Karte.'}
                                </Col>
                            </Row>

                            <Row>
                                <Col xs={12} md={12} >
                                    <page.LButton type={ELButtonType.buttonWithIconRight}
                                        css={cssStyle}
                                        id="buttonOrderSim"
                                        text={MainTexts.orderReplForaFee}
                                        onClick={() => {
                                            let cfg: IPopupConfig = {
                                                props: {
                                                    id: 'dynamicPopup',
                                                    title: 'SIM Karte wird verarbeitet',
                                                    visible: true,
                                                    closable: false,
                                                    type: EPopupType.pleaseWait
                                                },
                                                content: <div>{OrderingTexts.customerData}</div>
                                            };

                                            if (this.props.parent.data.customer !== undefined) {
                                                if (this.props.parent.data.customer.contracts !== undefined) {
                                                    if (this.props.parent.data.customer.contracts[0].simCards !== undefined) {
                                                        this.props.parent.data.customer.contracts[0].simCards[0] = this.current;
                                                    }
                                                }
                                            }

                                            this.props.parent.processCustomer(() => {
                                                Global.hub.send('{ "type": ' + ERequestType.customerOrderSim + ', "sim": {"sim": "' + this.current.id + '","activated": "' + (this.current.activateByCustomer == 1 ? 1 : 2) + '"}' + ', "brandKey": "' + (this.props.parent.data.customer.contracts ? this.props.parent.data.customer.contracts[0].brandKey : '') + '", "id": "' + this.props.parent.data.id + '"  }',
                                                    ERequestType.customerOrderSim,
                                                    undefined);
                                            }, cfg);
                                        }} />
                                </Col>
                            </Row>

                        </Container>
                    }
                </BaseBlock>
            </div>
        );
        return ctr;
    }

    private renderSim(contract: IContract): JSX.Element {
        var simCards: any = contract.simCards;
        let ctr = <></>;

        for (let index = 0; index < simCards.length; index++) {
            const element = simCards[index];

            let beforeDecimal = element.price.price.toString().substring(1,2);
            let afterDecimal = element.price.price.toString().substring(1,3);
            let finalPrice = beforeDecimal+"."+afterDecimal+" "+element.price.unit;
            this.priceFormated = finalPrice;
            
        }

        simCards.map((i, idx) => (
          

            ctr = (
                <Row>
                    <Col md={4}>
                        <TariffThumb
                            key={i.id}
                            id={i.id}
                            text={i.name +'\n\xa0'+ this.priceFormated}
                            tooltip={'Bitte whlen Sie eine SIM Karte aus.'}
                            tooltipModel={''}
                            idx={idx}
                            brandKey={''}
                            selected={this.state.simSelected}
                            styleBrand={false}
                            cssStyle={this.state.simSelected ? 'selected' : ''}
                            onClick={() => this.simChange(i)} />
                    </Col>
                </Row>
            )
        ));
        return ctr;
    }

    private activationChange(event: any) {
        if (event.id === 'ByCustomer') {
            this.current.activateByCustomer = 1;
            this.setState({ actByCustomer: true,actUponReceipt: false});
        } else {
            this.current.activateByCustomer = 2;
            this.setState({ actByCustomer: false,actUponReceipt: true });
        }
    }

    private simChange(sim: IActivationSim) {
        this.current.id = sim.id;

        if (this.state.simSelected === false) {

            if (this.props.parent.data.customer !== undefined) {
                if (this.props.parent.data.customer.contracts !== undefined) {
                    if (this.props.parent.data.customer.contracts[0].simCards !== undefined) {
                        this.current = this.props.parent.data.customer.contracts[0].simCards[0];
                        this.current.activateByCustomer = 1;
                    }
                }
            }

            this.setState({ simSelected: true,procedeButtonState: false });

        } else {
            this.current = ({ activateByCustomer: 1, name: '', price: { price: 0, unit: '' }, id: '' });
            this.setState({ simSelected: false,procedeButtonState: true });
        }
    }


};

export default SimReplacement;