import * as React from 'react';
import { ELElementStatus, ELButtonType } from '../../../models/page/diverse';
import { EOrderProcessView, EcancellationProcessView } from '../../../models/ordering/order';
import Global from '../../../utils/Global';
import * as page from '../../page/index';
import { OrderStages, OrderProductStages } from '../../../utils/messages';

export interface IActivationProgressProps {
    progress: EOrderProcessView;
    states: string[];
};

export class ActivationProgress extends React.Component<IActivationProgressProps, any> {
    constructor(props: IActivationProgressProps) {
        super(props);
    };

    public render() {
        let sprog = Global.getStringFromEnum(EOrderProcessView, this.props.progress);

        let cssp: string = 'iprogress-bg ' + sprog;

        let ctrl = (
            <>
                <div className="block block-inner block-trans iprogress">
                    <div className="inner">
                        <div className={cssp} />
                        <div className="iprogress-icons">
                            {this.props.states.indexOf(OrderStages.tariff) >= 0 ? <page.LButton type={ELButtonType.icon}
                                icon="tariff" text="Tarif" status={this.getProgressStatus(EOrderProcessView.tariff)} /> : null}
                            {this.props.states.indexOf(OrderStages.customer) >= 0 ? <page.LButton type={ELButtonType.icon}
                                icon="customer" text="Kundendaten" status={this.getProgressStatus(EOrderProcessView.customer)} /> : null}
                            {this.props.states.indexOf(OrderStages.done) >= 0 ? <page.LButton type={ELButtonType.icon}
                                icon="deal" text="Abschluss" status={this.getProgressStatus(EOrderProcessView.done)} /> : null}

                            {this.props.states.indexOf(OrderStages.cancellation) >= 0 ? <page.LButton type={ELButtonType.icon}
                                icon="selection" text="AUSWAHL" status={this.getProgressStatus(EOrderProcessView.cancellation)} /> : null}
                            {this.props.states.indexOf(OrderStages.overview) >= 0 ? <page.LButton type={ELButtonType.icon}
                                icon="overview" text="BERSICHT" status={this.getProgressStatus(EOrderProcessView.overview)} /> : null}
                            {this.props.states.indexOf(OrderStages.summary) >= 0 ? <page.LButton type={ELButtonType.icon}
                                icon="summary" text="ZUSAMMENFASSUNG" status={this.getProgressStatus(EOrderProcessView.summary)} /> : null}

                            {this.props.states.indexOf(OrderProductStages.selectProducts) >= 0 ? <page.LButton type={ELButtonType.icon}
                                icon="add-sim" text="PRODUKTE AUSWHLEN" status={this.getProgressStatusForOrderProducts(EOrderProcessView.selectProduct)} /> : null}
                            {this.props.states.indexOf(OrderProductStages.deliveryAddress) >= 0 ? <page.LButton type={ELButtonType.icon}
                                icon="calls-contacts" text="LIEFERANSCHRIFT" status={this.getProgressStatusForOrderProducts(EOrderProcessView.deliveryAddress)} /> : null}
                            {this.props.states.indexOf(OrderProductStages.overviewOrder) >= 0 ? <page.LButton type={ELButtonType.icon}
                                icon="transparency" text="BERSICHT" status={this.getProgressStatusForOrderProducts(EOrderProcessView.orderOverview)} /> : null}
                            <page.LClear />
                        </div>
                    </div>
                </div>
            </>
        );

        return ctrl;
    };

    private getProgressStatus(pr: EOrderProcessView): ELElementStatus {

        if (this.props.progress < pr) {
            return ELElementStatus.disabled;
        } else if (this.props.progress === pr) {
            return ELElementStatus.selected;
        } else {
            return ELElementStatus.ready;
        }
    };


private getProgressStatusForOrderProducts(pr: EOrderProcessView): ELElementStatus {

     if (this.props.progress === pr) {
        return ELElementStatus.selected;
    } else{
        return ELElementStatus.disabled;
    } 

};

};


export default ActivationProgress;