import * as React from 'react';
import linq from 'linq';
import { ErrorMessages } from '../../utils/messages';
import BaseBlock from '../../components/page/block';
import { IBlockConfiguration } from '../../models/block/configuration';
import { Container, Row, Col } from 'react-bootstrap';
import { LClear } from '../../components';
import * as page from '../../components/page/index';
import { IDefCon, ELButtonType } from '../../models';
import Global from '../../utils/Global';
import StorageHelper from '../../utils/storage';
import ReactDOMServer from 'react-dom/server';

export interface IErrorScreenData {
    title?: string;
    message?: string;
    code?: number;
    dc?: IDefCon;
    isContent?: boolean;
    content?: any;
};

export interface IErrorScreenProps {
    data: IErrorScreenData;
};

export interface IErrorScreenState {
};

export class ErrorScreen extends React.Component<IErrorScreenProps, IErrorScreenState> {
    constructor(props) {
        super(props);

        this.state = {

        };
    };

    public render() {
        let d = this.prepare();

        let cfg: IBlockConfiguration = {
            title: d.isContent? '' : d.title,
            transparent: false
        };

        let msg:string[] = [];
        
        d.message ? d.message.split('\n').map((i,idx)=>( msg.push('<p>' + i + '</p>'))) : '';

        let ctrl = (
            <page.Inner1C>
                <page.Inner2CLeft>
                    <>            
                    <BaseBlock config={cfg}>
                        <div className="spacer-big"/>
                        <Container>
                            <Row>
                                {
                                    d.isContent ? 
                                    <Col xs={12} md={12} className="tac">
                                        <div className="title">{d.title}</div>
                                        <br/>
                                        {d.content}
                                    </Col>
                                    :
                                    <Col xs={12} md={12} className="np text tac" dangerouslySetInnerHTML={{__html: msg.join('')}}></Col>
                                }
                            </Row>
                        </Container>
                        <LClear />
                    </BaseBlock>
                    </>
                </page.Inner2CLeft>
                <></>
            </page.Inner1C>
        );

        return ctrl;
    };

    private prepare() : IErrorScreenData {
        let data: IErrorScreenData = this.props.data;

        if(data.code) {
            if(data.code === 404) {
                data.message = ErrorMessages.message404;
                data.title = ErrorMessages.title404;
            } else if (data.code == 403) {
                data.message = ErrorMessages.message403;
                data.title = ErrorMessages.title403;
            }  else if (data.code == 403.1) {
                let usr = StorageHelper.getUser();
                data.message = ErrorMessages.message4031;
                
                data.isContent = true;
                data.content = <page.LButton type={ELButtonType.buttonWithIconRight} css="aicon" text="Zum VB-Tool" onClick={()=>{
                    window.open('https://bo.vf-easy.de',  "_blank");
                }}/>

                if(usr) {
                    data.title = ErrorMessages.title4031.replace('$user', usr.firstname + ' ' + usr.surname);
                };
            };
        }

        if(data.dc) {
            data.message = ErrorMessages.message403;
            data.title = ErrorMessages.title403;    
        };

        return data;
    };
};

export default ErrorScreen;