import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import * as page from '../../../src/components/page/index';
import { EPopupType } from '../../components/dialogs/popup';
import * as cmp from '../../components/index';
import BlockBase from '../../components/page/block';
import { EFilterType } from '../../components/page/filter';
import { EPagingType, IColumn } from '../../components/page/table';
import { IApiResponse, IApiSummary } from '../../interfaces';
import { IBlockConfiguration } from '../../models/block/configuration';
import * as m from '../../models/index';
import { IFilter } from '../../models/index';
import Api from '../../utils/api';
import { BrandKeys } from '../../utils/Defaults';
import Global from '../../utils/Global';
import { MainTexts } from '../../utils/messages';
import EasyRouter from '../../utils/router';
import ContractManagerDetailsScreen from './managerDetails';

export interface IContractManagerScreen {
    api?: IApiResponse;
    changed: boolean;
    expanded: boolean;

    apiMessage?: string;
    processing: boolean;
    closablePopup: boolean;

    selectedRecord?: any;
};

/**
 * Component to handle the history of contracts done by a dealer
 */
export class ContractManagerScreen extends React.Component<any, IContractManagerScreen> {
    public static containerId: string = 'contractManager';
    private filter: m.IFilter = { needle: '' };
    private processPopupState: EPopupType = EPopupType.pleaseWait;
    readonly cfg = Global.getConfiguration();
    readonly cols: IColumn[] = [
        {
            label: MainTexts.referenceId, showTitle: false, style: 'text-sub-logo', filterable: true, binding: 'contractId', size: 2, sortable: true,
            renderer: (r, c) => {
                let txt = r['contractId'];

                if (txt != null) {
                    if (txt.length == 36) {
                        txt = MainTexts.unkonwnAtThisTime;
                    }
                }

                if (r['brand'] == BrandKeys.vodafone) {
                    txt = 'K-' + txt;
                } else if (r['brand'] == BrandKeys.kabel || r['brand'] == BrandKeys.dsl  || r['brand'] == BrandKeys.unity) {
                    txt = 'V-' + txt;
                } else if (r['brand'] == BrandKeys.otelo) {
                    if (txt) {
                        txt = 'V-' + txt;
                    } else {
                        txt = 'M-' + r['msisdn'];
                    }
                };

                return <>
                    <div className={'brandi ' + (r['brand'] ? r['brand'] : 'otelo')} />
                    <div className="text-sub-logo">{txt}</div>
                </>
            }
        },
        {
            label: MainTexts.date, filterable: true, style: 'text-sub', binding: 'orderDate', size: 1, sortable: true, renderer: (r, c) => {
                return <>{Global.toGermanDate(r[c.binding])}</>;
            }
        },
        { label: MainTexts.customerName, showTitle: true, filterable: true, binding: 'customer', size: 1, sortable: true, style: '' },
        { label: MainTexts.msisdn, showTitle: true, filterable: true, binding: 'msisdn', size: 2, sortable: true, style: '' },
        { label: MainTexts.employee, labelShort: MainTexts.employeeShort, showTitle: true, binding: 'employeeId', size: 1, sortable: true, style: 'text-sub', renderer: (r, c) => {
            return <>{r[c.binding].replace('-', '- ')}</>;
        } },
        { label: MainTexts.type, showTitle: true, filterable: true, binding: 'typeUI', size: 2, sortable: true, style: '' },
        { label: MainTexts.details, isHtml: true, showTitle: true, filterable: true, binding: 'description', size: 1, sortable: true },
        {
            label: '', binding: '', size: 2, sortable: false,
            renderer: (c, r) => {
                let ctrl = <></>;

                ctrl = (<>
                    <page.LButton type={m.ELButtonType.iconImage}
                        icon="list"
                        clickModel={c}
                        onClickEvent={(e, m) => {
                            e.preventDefault();
                            e.stopPropagation();
                            this.selectRecord(m);
                            return false;
                        }} />

                    {
                        //vvl, tv, or bnt
                        (c.transactionTypeId == 3 || c.transactionTypeId == 4 || c.transactionTypeId == 1  || c.transactionTypeId == 2  || c.transactionTypeId == 18) && c.canReprint ? 
                        <page.LButton type={m.ELButtonType.iconImage}
                            icon="print"
                            clickModel={c}
                            onClickEvent={(e, m) => {
                                e.preventDefault();
                                e.stopPropagation();
                                let cfg = Global.getConfiguration();
                                let url =  cfg.apiUrl + 'document/GetContract?activationId=' + c.orderId + '';
                                let w = window.open(url, '_blank');
                                return false;
                            }} /> 
                        : null
                    }

                    {
                        (c.transactionTypeId == 3 || c.transactionTypeId == 4 || c.transactionTypeId == 1  || c.transactionTypeId == 2  || c.transactionTypeId == 18) && c.canReprint ? 
                            <page.LButton type={m.ELButtonType.iconImage}
                                icon="overview"
                                clickModel={c}
                                onClickEvent={(e, m) => {
                                    e.preventDefault();
                                    e.stopPropagation();
                                    let url = '/ordering/preview/x/' + c.orderId + '/';
                                    let w = window.open(url, '_blank');
                                    return false;
                                }} />                          
                            : null
                    }                        
                </>)

                return ctrl;
            },
            rendererHead: (c) => {
                return <>
                    <page.LButton type={m.ELButtonType.iconImage}
                        icon="expand"
                        onClick={this.expand} />
                </>;
            }
        },
    ];

    constructor(props) {
        super(props);

        this._filterChange = this._filterChange.bind(this);
        this._applyFilter = this._applyFilter.bind(this);
        this.expand = this.expand.bind(this);
        this.onDismissPopup = this.onDismissPopup.bind(this);
        this.itemClick = this.itemClick.bind(this);

        Global.easyApp.setTitle(MainTexts.contractManager);

        this.state = {
            expanded: false,
            changed: false,
            processing: true,
            closablePopup: false
        };

        let to: Date = new Date();
        let from: Date = new Date();
        // from = new Date(from.setMonth(from.getMonth() - 1)) as Date;
        from.setDate(from.getDate() - 31);

        this.filter = {
            needle: '',
            from: from,
            to: to,
            timeFilter: 0,
            cats: ['vf', 'otelo', 'fn', 'tv']
        };

    }

    public componentDidMount() {

        setTimeout(() => {
            Global.easyApp.setTitle(MainTexts.contractManager);

            const obj = { search: this.filter.needle, from: this.filter.from, to: this.filter.to };

            Global.hub.send(JSON.stringify(obj), m.ERequestType.getContracts, undefined);
        }, 1000);
    }

    public render() {
        let ctrl = <div id={ContractManagerScreen.containerId}>
            <page.Filter
                filter={this.filter}
                id="filter"
                type={EFilterType.default}
                title={MainTexts.filter}
                orderManagerFilter = {true}
                onApply={this._applyFilter}
                onChange={this._filterChange}
                placeholderSearch="z.B. MSISDN oder Vorname" />
            {
                this.state.processing || !this.state.api || !this.state.api.data ? <></> :
                    <>
                        {this.renderData()}
                    </>
            }

            {this.renderPopup()}
        </div>;

        return ctrl;
    }

    public socketCallback(res: IApiResponse) {
        if (res.error) {
            let sum: IApiSummary = Api.parseSummary(res, undefined);
            this.processPopupState = EPopupType.custom;
            this.setState({ changed: true, processing: true, closablePopup: true, api: res, apiMessage: sum.message });
        } else {
            this.setState({ changed: true, processing: false, api: res });
        }
    }

    onDismissDetailsScreen = () => {
        this.setState({ selectedRecord: undefined });
    }

    private onDismissPopup() {
        this.setState({ processing: false });
    }

    private renderFilter(): JSX.Element {
        let cfg: IBlockConfiguration = {
            title: MainTexts.filter,
            transparent: false
        };

        let to: Date = new Date();
        let from: Date = new Date();

        let filter = (
            <BlockBase config={cfg}>
                <Container>
                    <Row>
                        <Col xs={6} md={6} className="np">
                            <page.LDate value={from}
                                datePicker={true}
                                onChange={this._applyFilter}
                                title={MainTexts.periodFrom}
                                name="filter.from"
                                style="middle"
                                id="from" />
                        </Col>
                        <Col xs={6} md={6} className="npr">
                            <page.Filter
                                filter={this.filter}
                                id="filter"
                                type={EFilterType.default} title={MainTexts.searchTitle}
                                onApply={this._applyFilter}
                                placeholderSearch="z.B. MSISDN oder Vorname" />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={6} md={6} className="np">
                            <page.LDate value={to}
                                datePicker={true}
                                onChange={this._applyFilter}
                                title={MainTexts.to}
                                style="middle"
                                name="filter.to"
                                id="to" />
                        </Col>
                        <Col xs={6} md={6} className="np">
                            <page.LButton text={MainTexts.toDashboard}
                                css="fr"
                                onClick={() => { EasyRouter.route({ url: '/' }); }}
                                type={m.ELButtonType.buttonWithIconRight}
                                icon="arrow-right" />
                        </Col>
                    </Row>
                </Container>
                <></>
            </BlockBase>
        );

        let ctrl = (
            <page.Inner1C>
                <page.Inner2CLeft>
                    <>
                        {filter}
                    </>
                </page.Inner2CLeft>
                <></>
            </page.Inner1C>
        );

        return ctrl;
    }

    private renderPopup(): JSX.Element {
        return <cmp.Popup
            type={this.processPopupState}
            visible={this.state.processing || this.state.closablePopup}
            closable={this.state.closablePopup}
            id="wait"
            title={MainTexts.pleaseWait}
            height={200}
            onDismiss={this.onDismissPopup}>
            <cmp.PopupBody>
                <div>{this.state.apiMessage}</div>
                <></>
            </cmp.PopupBody>
            <></>
        </cmp.Popup>
    }

    _filterChange = (f: IFilter) => {
        this.filter = f;
        this.setState({ changed: true });
    }

    _applyFilter = () => {
        this.setState({ processing: true });
        //const obj = { search: this.filter.needle, from: this.filter.from, to: this.filter.to };

        Global.hub.send(JSON.stringify(this.filter), m.ERequestType.getContracts, undefined);
    }

    private selectRecord(record) {
        const selectedRecord: any = { voId: record.Void, contractId: record.contractId };
        this.setState({ selectedRecord });
    }

    private expand() {
        this.setState({ expanded: !this.state.expanded });
    }

    private renderData(): JSX.Element {
        let details: JSX.Element = <></>;
        const { selectedRecord } = this.state;
        if (selectedRecord !== undefined) {
            details = <div className="row">
                <div className="col-md-12 col-12">
                    <ContractManagerDetailsScreen {...selectedRecord} parent={this} />
                </div>
            </div>;
        }

        const pagingProps = { page: 1, count: this.cfg.pageSize, type: EPagingType.local };

        const ctrl = <div id={ContractManagerScreen.containerId} className={'c1 container ' + (this.state.expanded ? 'wide' : '')}>
            <div className="row">
                <div className="col-md-12 col-12">
                    <page.Table coulmns={this.cols}
                        id={ContractManagerScreen.containerId + 'Table'}
                        expnaded={this.state.expanded}
                        itemClick={this.itemClick}
                        pagingProps={pagingProps}
                        dateColumn='OrderDate'
                        filter={undefined}
                        data={this.state && this.state.api ? this.state.api.data.data : undefined} />
                </div>
            </div>
            {details}
        </div>;

        return ctrl;
    }

    private itemClick(ev: any, id: any) {
        if (ev) { ev.preventDefault(); }
    }
};

export default ContractManagerScreen;