import * as linq from 'linq';
import * as React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { IBaseComponentState } from '../../../interfaces';
import { IBlockConfiguration } from '../../../models/block/configuration';
import { IAvailabilityBrand } from '../../../models/ordering/availability';
import { ICustomer } from '../../../models/ordering/customer';
import { House } from '../../../models/ordering/house';
import { EHomeType } from '../../../models/ordering/order';
import { EControlType, ETelType } from '../../../models/page/diverse';
import { IKeyText } from '../../../models/page/interfaces';
import { BrandKeys } from '../../../utils/Defaults';
import { MainTexts, OrderingTexts, ValidationMessages } from '../../../utils/messages';
import StorageHelper from '../../../utils/storage';
import ValidationOption, { EValidationOption } from '../../../utils/validation/validationOption';
import BaseBlock from '../../page/block';
import * as page from '../../page/index';
import { Tel } from '../global/tel';
import { ActivationCustomerData } from '../process';

export interface ICustomerDSLProps {
    parent: ActivationCustomerData;
    house?: House;
    customer: ICustomer;
    onChange: any;
    showTel: boolean;
    showAddress: boolean;
};

export class CustomerDSL extends React.Component<ICustomerDSLProps, IBaseComponentState> {
    constructor(props: ICustomerDSLProps) {
        super(props);

        this.state = {
            changed: false
        };
    };

    public componentWillReceiveProps(next: ICustomerDSLProps) {
        this.setState( {changed: true});
    };

    public render() {
        let cfg: IBlockConfiguration = {
            title: OrderingTexts.dslHouse,
            transparent: false
        };

        if(this.props.parent.props.parent.data.customer.mnp && this.props.parent.props.parent.data.customer.mnp.provider == '1') {
            cfg.title = 'Kontakt';
        };        

        let title: string = 'Rckrufnummer'; 

        let house: House = this.props.house ? this.props.house : new House();

        let detailsValOpt:ValidationOption[] = [];

        let onlyDsl = this.props.parent.props.parent.dataWrapper.hasDslOnly();

        let prefixDisabled: boolean = false;
 
        if(onlyDsl) {
            var avl = StorageHelper.getAvailability();
            let br = linq.from<IAvailabilityBrand>(avl.brands).where(x => x.key === BrandKeys.dsl).firstOrDefault();

            if(this.props.parent.props.parent.data.customer.house && this.props.parent.props.parent.data.customer.house.tel.telType == ETelType.mobile) {
                prefixDisabled = false;

                if(this.props.parent.props.customer.house && this.props.parent.props.customer.house.tel.prefix == br!.products[0].areaCode) {
                    this.props.parent.props.customer.house.tel.prefix = '';
                };
            } else {
                prefixDisabled = true;

                if (br && br.products && br.products.length > 0 && this.props.parent.props.customer.house) {
                    this.props.parent.props.customer.house.tel.prefix = br.products[0].areaCode;
                };                
            }
        };

        let ctrl = (
            <BaseBlock config={cfg}>
                <Container>
                    {
                        this.props.showAddress ?  
                        <>
                            <Row>
                                <Col xs={6} md={6} className="np">
                                    <page.LControl type={EControlType.radio} 
                                        onChange={this.props.onChange} 
                                        name="house.homeType" 
                                        id="homeType_1" 
                                        value={EHomeType.detachedHouse.toString()} 
                                        checked={house.homeType == EHomeType.detachedHouse }
                                        label="Einfamilienhaus" title="Gebudeart"/>
                                </Col>
                                <Col xs={6} md={6}>
                                    <page.LControl type={EControlType.radio} 
                                        onChange={this.props.onChange} 
                                        name="house.homeType" 
                                        id="homeType_2" 
                                        value={EHomeType.appartmentHouse.toString()} 
                                        checked={house.homeType == EHomeType.appartmentHouse }
                                        label="Mehrfamilienhaus"/>
                                </Col>
                            </Row>

                            {
                                house.homeType == EHomeType.appartmentHouse  ?
                                <>
                                    <Row>
                                        <Col xs={6} md={6} className="np">
                                            <page.LControl type={EControlType.radio} 
                                                onChange={this.props.onChange} 
                                                name="house.entranceLocation" 
                                                id="entranceLocation_Vorderhaus" 
                                                checked={house.entranceLocation == 'Vorderhaus' }
                                                label="Vorderhaus" title="Lage der Wohnung"/>
                                        </Col>
                                        <Col xs={6} md={6} className="">
                                            <page.LControl type={EControlType.radio} 
                                                onChange={this.props.onChange} 
                                                name="house.entranceLocation" 
                                                id="entranceLocation_Hinterhaus" 
                                                checked={house.entranceLocation == 'Hinterhaus' }
                                                label="Hinterhaus"/>
                                        </Col>                                
                                    </Row>
                                    <Row>
                                        <Col xs={4} md={4} className="np">
                                            <page.LControl type={EControlType.radio} 
                                                onChange={this.props.onChange} 
                                                name="house.appartmentLocation" 
                                                id="appartmentLocation_Links" 
                                                checked={house.appartmentLocation == 'Links' }
                                                label="Links" title="Lage des Eingangs"/>
                                        </Col>
                                        <Col xs={4} md={4} className="">
                                            <page.LControl type={EControlType.radio} 
                                                onChange={this.props.onChange} 
                                                name="house.appartmentLocation" 
                                                id="appartmentLocation_Mitte" 
                                                checked={house.appartmentLocation == 'Mitte' }
                                                label="Mitte"/>
                                        </Col>
                                        <Col xs={4} md={4} className="">
                                            <page.LControl type={EControlType.radio} 
                                                onChange={this.props.onChange} 
                                                name="house.appartmentLocation" 
                                                id="appartmentLocation_Rechts" 
                                                checked={house.appartmentLocation == 'Rechts' }
                                                label="Rechts"/>
                                        </Col>                                                             
                                    </Row>                             
                                    <Row>
                                        <Col xs={6} md={6} className="np">
                                            <page.LControl type={EControlType.dropdown} 
                                                value={house.floor}
                                                onChange={this.props.onChange} 
                                                name="house.floor" 
                                                id="floor" 
                                                title={MainTexts.floor}
                                                options={this.getFllors()} />                                    
                                        </Col>      
                                    </Row>
                                    <Row>
                                        <Col xs={12} md={12} className="np">
                                            <page.LControl type={EControlType.text} 
                                                onChange={this.props.onChange} name="house.details" 
                                                id="details" value={house.details} label={'Lage der Telefondose'}
                                                hint="z.B. Flur oder Wohnzimmer"/>
                                        </Col>   
                                    </Row>                        
                                </>
                                : 
                                <>
                                    <Row>
                                        <Col xs={12} md={12} className="np">
                                            <page.LControl type={EControlType.text} 
                                                onChange={this.props.onChange} name="house.details" 
                                                id="details" value={house.details} label={'Lage der Telefondose'}
                                                validationOptions={
                                                    [new ValidationOption({ type: EValidationOption.required, message: ValidationMessages.requiredError })]
                                                }
                                                hint="z.B. Flur oder Wohnzimmer"/>
                                        </Col>   
                                    </Row>  
                                </>
                            }
                            
                            <div className="spacer"/>                        
                        </>                        
                        : null
                    }
                    
                    {
                        this.props.showTel ?  
                            <Tel 
                                source={this.props.customer} 
                                onChange={this.props.onChange} 
                                prefix="house.tel" 
                                preview={false}
                                titleTel={title}
                                disablePrefix={prefixDisabled}
                                />
                            : null
                    }
                
                </Container>
                <page.LClear />
            </BaseBlock>
        );

        return ctrl;
    };   

    private getFllors() : IKeyText[] {
        let opt:IKeyText[] = [];

        opt.push({key: 'K', text:'Keller'});
        opt.push({key: 'EG', text:'EG'});
        opt.push({key: 'DG', text:'DG'});

        for(let i:number = 1; i<= 20; i++){
            opt.push({key: i, text: i.toString()});
        };

        opt.push({key: '20+', text:'20+'});

        return opt;
    };
};

export default CustomerDSL;